import { Dialog, Notify } from 'quasar';
import axios from 'axios';
import { Deploy } from 'cordova-plugin-ionic';
import { env } from './env';
import c from './constants';
import errorHandler from './error-handler';
import conversionTrackingService from './conversion-tracking-service';
import { delay } from './helpers';

const androidUrl = 'market://details?id=com.jugglestreet.app';
const iosUrl = 'itms-apps://itunes.apple.com/app/id1586742271';

let config;
let configured = false;
let activeDialog = false;
let lastFetched;
let router;

const AppUpdateManager = {
  init(s, r) {
    router = r;
  },
  async configure() {
    if (configured) return Promise.resolve();
    configured = true;

    if (env.deviceInfo.platform === 'web') {
      await AppUpdateManager.configureWebUpdates();
    } else {
      await AppUpdateManager.configureCodeUpdates();
    }

    return Promise.resolve();
  },
  showWebUpdateDialog() {
    if (activeDialog) return;
    activeDialog = true;
    Dialog.create({
      title: 'Product Update',
      message:
        'A NEW version is available. Would you like to update now?',
      cancel: {
        label: 'Later',
        color: 'secondary',
        outline: true,
      },
      ok: {
        label: 'Yes',
        outline: true,
      },
    }).onOk(() => {
      activeDialog = false;
      window.location = '/';
    }).onCancel(() => {
      activeDialog = false;
    });
  },
  checkWebVersion() {
    if (lastFetched) {
      const secondsSinceLastCheck = (new Date().getTime() - lastFetched) / 1000;

      if (secondsSinceLastCheck < 300) {
        return;
      }
    }

    axios.get('/app-info.json').then((response) => {
      const serverAppInfo = response.data;
      lastFetched = new Date();

      if (serverAppInfo.version !== env.appInfo.version) {
        AppUpdateManager.showWebUpdateDialog();
      }
    }).catch((error) => {
      errorHandler.captureException(error);
    });
  },
  async configureWebUpdates() {
    setInterval(AppUpdateManager.checkWebVersion, 300000);
  },
  showCodeUpdateDialog(force) {
    if (!force) {
      if (router.currentRoute.value.fullPath.indexOf('post-') !== -1
      || router.currentRoute.value.fullPath.indexOf('login') !== -1
      || router.currentRoute.value.fullPath.indexOf('profile-wizard') !== -1) {
        console.log('[AppUpdate] Skip Code Update Dialog...');
        return;
      }
    }

    if (activeDialog) return;
    activeDialog = true;

    Dialog.create({
      title: 'App Update',
      persistent: true,
      message:
        'We have identified a few issues which require a quick update to your app.',
      cancel: {
        label: 'Later',
        color: 'secondary',
        outline: true,
      },
      ok: {
        label: 'Update',
        outline: true,
      },
    }).onOk(async () => {
      console.log('[AppUpdate] Install and Reload...');
      conversionTrackingService.logAppEvent(c.appEventType.overTheAirUpdate);
      activeDialog = false;
      await Deploy.reloadApp();
    });
  },
  async installUpdate(force) {
    if (!force) {
      if (router.currentRoute.value.fullPath.indexOf('post-') !== -1
      || router.currentRoute.value.fullPath.indexOf('login') !== -1
      || router.currentRoute.value.fullPath.indexOf('reset-password') !== -1
      || router.currentRoute.value.fullPath.indexOf('signup') !== -1
      || router.currentRoute.value.fullPath.indexOf('profile-wizard') !== -1) {
        return;
      }
    }

    try {
      const currentVersion = await Deploy.getCurrentVersion();
      let hasUpdate = false;
      const resp = await Deploy.sync({ updateMethod: 'background' }, (percentDone) => {
        console.log(`[AppUpdate] Update is ${percentDone}% done!`);
        if (percentDone === 100) {
          hasUpdate = true;
        }
      });

      if (!currentVersion || currentVersion.versionId !== resp.versionId || hasUpdate) {
        console.log('[AppUpdate] New Update is Available!');
        Notify.create({
          message: 'Please wait...',
          group: false,
          timeout: 500,
          spinner: true,
          caption: '',
        });
        await delay(1000);

        await conversionTrackingService.logAppEvent(c.appEventType.overTheAirUpdate);
        await Deploy.reloadApp();
      } else {
        console.log('[AppUpdate] No Updates are Available!');
        if (currentVersion) {
          console.log(`[AppUpdate] Current Version: ${JSON.stringify(currentVersion)}`);
        }
      }
    } catch (error) {
      errorHandler.captureException(error);
    }
  },
  async checkForCodeUpdatesBrief() {
    const promptUser = false;
    let notif;
    try {
      console.log('[AppUpdate] Checking for Updates...');
      const updateInfo = await Deploy.checkForUpdate();

      if (!updateInfo.available) {
        console.log('[AppUpdate] No Update Available...');
        return false;
      }
      if (promptUser) {
        notif = Notify.create({
          message: 'Please wait...',
          group: false,
          timeout: 100,
          spinner: true,
          caption: '',
        });
      }

      console.log('[AppUpdate] Downloading New Update...');
      await Deploy.downloadUpdate();
      console.log('[AppUpdate] Completed Download...');

      console.log('[AppUpdate] Extracting Update...');
      await Deploy.extractUpdate();
      console.log('[AppUpdate] Completed Extraction...');
      if (promptUser) {
        notif({
          timeout: 250,
        });
      }
      return true;
    } catch (error) {
      errorHandler.captureException(error);

      if (promptUser && notif) {
        notif({
          timeout: 250,
        });
      }
    }

    return false;
  },
  async checkForCodeUpdates() {
    const notif = Notify.create({
      message: 'Checking for updates...',
      group: false,
      timeout: 0,
      spinner: true,
      caption: '',
    });
    const updateInfo = await Deploy.checkForUpdate();
    if (updateInfo.available) {
      notif({
        message: 'Downloading...',
        caption: '0%',
      });

      try {
        await Deploy.downloadUpdate((progress) => {
          console.log(`[AppUpdate] Downloading... ${progress} Complete`);
          notif({
            caption: `${progress}%`,
          });
        });

        await Deploy.extractUpdate((progress) => {
          console.log(`[AppUpdate] Extracting... ${progress} Complete`);
          notif({
            caption: `${progress}%`,
          });
        });

        notif({
          icon: 'done',
          spinner: false,
          message: 'Update downloaded successfully',
          timeout: 1000,
          caption: '',
        });
      } catch (error) {
        notif();
        errorHandler.captureException(error);
      }

      AppUpdateManager.showCodeUpdateDialog(true);
    } else {
      notif({
        icon: 'done',
        spinner: false,
        message: 'You are using the latest version of Juggle St.',
        timeout: 1000,
        caption: '',
      });
    }
  },
  async configureCodeUpdates() {
    if (!Deploy) return Promise.resolve();

    config = {
      appId: 'bbee2118',
      channel: env.deployment,
      // updateMethod: env.deployment === 'Production4' ? 'background' : 'none',
      updateMethod: 'background',
    };
    await Deploy.configure(config);

    // Manually Call checkForCodeUpdates for manual updates (updateMethod: none)
    return Promise.resolve();
  },
  requiresAppStoreUpdate(user) {
    if (user.meta.lastFetched === null) return false;

    const latestVersionCode = user.data.appSettings.latestAppVersionCode;
    const currentVersionCode = parseInt(env.versionCode, 10);
    const diff = latestVersionCode - currentVersionCode;
    const hasUpdate = diff >= 3;
    return hasUpdate;
  },
  isUnsupportedVersion(user) {
    if (user.meta.lastFetched === null) return false;

    const latestVersionCode = user.data.appSettings.latestAppVersionCode;
    const currentVersionCode = parseInt(env.versionCode, 10);
    const diff = latestVersionCode - currentVersionCode;
    const hasUpdate = diff >= 5;
    return hasUpdate;
  },
  navigateToAppStore() {
    conversionTrackingService.logAppEvent(c.appEventType.appStoreUpdate);
    const url = env.deviceInfo.operatingSystem === 'android' ? androidUrl : iosUrl;
    window.open(url, '_blank', 'location=yes');
  },
};

export default AppUpdateManager;
