<template>
  <q-page class="layout-padding">
    <div class="">
      <div class="q-mx-xs">
        Congratulations on becoming a Super Helper
      </div>
      <div class="q-mx-xs q-my-sm text-grey">
        This award recognizes your ongoing commitment to Juggle Street, the number of jobs you have completed, your responsiveness and your consistently high ratings.
      </div>
      <div class="q-mx-xs q-my-sm text-grey">
        You now have access to these settings:
      </div>
      <q-field
        label="Distance from home to work"
        stack-label
        borderless
        hide-bottom-slots
      >
        <template v-slot:prepend>
          <q-icon name="location_on" />
        </template>
        <template v-slot:control>
          <div class="full-width q-mt-sm">
            <div class="">
              <small>Would you like to limit the location of your jobs?</small>
            </div>
            <q-radio class="q-mx-sm" v-model="limitRadius" :val="true" label="Yes" />
            <q-radio class="q-mx-sm" v-model="limitRadius" :val="false" label="No" />
            <div v-if="limitRadius">
              <div style="display: flex;justify-content: space-between">
                <span>Home</span>
                <span>Default</span>
              </div>
              <div class="q-mx-md">
                <q-slider
                  v-model="visibilityLimit"
                  markers
                  :marker-labels="getMarkerLabel"
                  track-size="10px"
                  :min="0"
                  :max="10000"
                  :step="2500"
                  color="secondary" />
              </div>
              <div class="text-center">
                <q-btn label="Save" color="accent" @click="handleSetRadius" />
              </div>
            </div>
          </div>
        </template>
      </q-field>

      <q-field
        label="Minimum Hourly Rate"
        stack-label
        borderless
        hide-bottom-slots
      >
        <template v-slot:prepend>
          <q-icon name="attach_money" />
        </template>
        <template v-slot:control>
          <div class="full-width q-mt-sm">
            <div>
              <small>
                Would you like to set a minimum  hourly rate?
              </small>
            </div>
            <q-radio class="q-mx-sm" v-model="hasMinHourlyRate" :val="true" label="Yes" />
            <q-radio class="q-mx-sm" v-model="hasMinHourlyRate" :val="false" label="No" />
            <div v-if="hasMinHourlyRate" class="q-mx-md">
              <div>
                <q-input
                  style="width: 200px"
                  :error="v$.minHourlyRate.$error"
                  type="number"
                  v-model="minHourlyRate"
                  placeholder="00"
                  prefix="$"
                  suffix="per hour"
                />
                <div v-if="averageRate">
                  <small class="q-mr-xs">Note</small>
                  <small class="text-grey">{{averageRate}}</small>
                </div>
                <div class="text-center q-mt-xs">
                  <q-btn label="Save" color="accent" @click="handleSetRate" />
                </div>
              </div>
            </div>
          </div>
        </template>
      </q-field>
      <div class="q-mx-xs">
        Note
        <br/>
        <span class="text-grey">To maintain Super Helper status, you must complete at least 4 jobs a year and receive five-star reviews at least 80% of the time.</span>
      </div>
    </div>
  </q-page>
</template>

<script>
import { mapState, mapActions } from 'vuex';
import useVuelidate from '@vuelidate/core';
import { requiredIf, minLength, numeric } from '@vuelidate/validators';
import store from '../../store';
import NotificationService from '../../utils/notification-service';
import { formatNumber, formatCurrency } from '../../utils/helpers';

export default {
  name: 'PageHelperAdvancedSettings',
  data() {
    const limitRadius = store.state.userInfo.data.visibilityLimit !== 10000;
    const hasMinHourlyRate = store.state.userInfo.data.provider.minHourlyRate > 0;
    return {
      limitRadius,
      hasMinHourlyRate,
      suburbStats: null,
    };
  },
  methods: {
    ...mapActions({
      suburbSearchService: 'suburbSearch',
    }),
    getMarkerLabel(val) {
      return `${formatNumber(val / 1000)} km`;
    },
    handleSetRadius() {
      const invalidCheck = true;
      if (invalidCheck) {
        this.$q.dialog({
          title: 'JOB INVITE ALERT!',
          message: 'Default is 10km - if you change this setting you will only receive job invites from employers within this distance. This will limit the number of invites you receive.',
          ok: {
            label: 'Continue',
            outline: true,
          },
          cancel: {
            label: 'Cancel',
            color: 'secondary',
            outline: true,
          },
        }).onOk(() => {
          this.saveChanges();
        }).onCancel(() => {});
      }
    },
    handleSetRate() {
      const invalidCheck = true;
      if (invalidCheck) {
        this.$q.dialog({
          title: 'JOB INVITE ALERT!',
          message: 'If you set a minimum rate, you will only receive job invitations at, and above this hourly rate. This will limit the number of job invites you receive.',
          ok: {
            label: 'Continue',
            outline: true,
          },
          cancel: {
            label: 'Cancel',
            color: 'secondary',
            outline: true,
          },
        }).onOk(() => {
          this.saveChanges();
        }).onCancel(() => {});
      }
    },
    saveChanges() {
      this.v$.$touch();
      if (this.v$.$error) {
        NotificationService.negative('Please fix all highlighted errors.');
        return;
      }

      this.$store.dispatch('updateUserInfo').then(() => {
        NotificationService.positive('Settings updated successfully.');
      }).catch((error) => {
        NotificationService.negative(error.message);
      });
    },
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
    }),
    storeModuleName() {
      return 'userInfo';
    },
    formatVisibilityLimit() {
      return `${formatNumber(this.visibilityLimit / 1000)} km`;
    },
    visibilityLimit: {
      get() {
        return this.userInfo.visibilityLimit;
      },
      set(value) {
        this.setProperty('visibilityLimit', value);
      },
    },
    minHourlyRate: {
      get() {
        return this.userInfo.provider.minHourlyRate;
      },
      set(value) {
        this.setProperty('provider.minHourlyRate', value);
      },
    },
    averageRate() {
      if (!this.suburbStats) return '';

      return `the average rate in ${this.userInfo.defaultAddress.formattedSuburb} is ${formatCurrency(this.suburbStats.jobsAverageHourlyRate6Months)} p/h.`;
    },
  },
  setup() {
    return {
      v$: useVuelidate(),
    };
  },
  validations() {
    return {
      minHourlyRate: {
        requiredIf: requiredIf(this.hasMinHourlyRate),
        minLength: minLength(1),
        numeric,
      },
    };
  },
  watch: {
    hasMinHourlyRate() {
      this.minHourlyRate = null;
    },
    limitRadius() {
      this.visibilityLimit = 10000;
    },
  },
  async mounted() {
    if (this.country === 'nz') return;

    const term = `${this.userInfo.defaultAddress.formattedSuburb}`;
    const suburbs = await this.suburbSearchService({ term, country: this.userInfo.country });

    // Match by postcode.
    this.suburbStats = suburbs.find((s) => s.state === this.userInfo.defaultAddress.state && s.postcode === this.userInfo.defaultAddress.postCode);
    // Match by suburb name.
    if (!this.suburbStats) {
      this.suburbStats = suburbs.find((s) => s.state === this.userInfo.defaultAddress.state && s.name === this.userInfo.defaultAddress.formattedSuburb);
    }
  },
  beforeRouteLeave(to, from, next) {
    this.v$.$touch();
    if (this.v$.$error) {
      NotificationService.negative('Please fix all highlighted errors.');
      next(false);
      return;
    }

    this.$store.dispatch('updateUserInfo').then(() => {
      next();
    }).catch((error) => {
      NotificationService.negative(error.message);
      next(false);
    });
  },
};
</script>
