<template>
  <q-item
    clickable
    @click="handleClick"
  >
    <q-item-section
      v-if="icon"
      avatar
    >
      <q-icon :color="iconColor" :name="icon" :size="iconSize"/>
    </q-item-section>

    <q-item-section>
      <q-item-label>
        {{ title }}
        <b :class="hintColor">{{hint}}</b>
        <small v-if="note" class="text-red text-bold text-italic font-size-sm q-mx-sm">{{ note  }}</small>
      </q-item-label>
      <q-item-label caption v-if="caption">
        {{ caption }}
      </q-item-label>
    </q-item-section>
    <q-item-section side v-if="sideContent">
      {sideContent}}
    </q-item-section>
  </q-item>
</template>

<script>
export default {
  name: 'EssentialLink',
  props: {
    title: {
      type: String,
      required: true,
    },

    caption: {
      type: String,
      default: '',
    },

    note: {
      type: String,
      default: '',
    },

    hint: {
      type: String,
    },

    hintColor: {
      type: String,
    },

    sideContent: {
      type: String,
    },

    link: {
      type: String,
    },

    linkAction: {
      type: String,
      default: 'push',
    },

    icon: {
      type: String,
      default: '',
    },

    iconSize: {
      type: String,
      default: '',
    },

    iconColor: {
      type: String,
      default: 'primary',
    },
  },
  emits: ['click'],
  methods: {
    handleClick() {
      this.$store.dispatch('toggleLeftDrawer');
      if (this.link) {
        if (this.linkAction === 'push') {
          this.routerPush(this.link, false);
        } else {
          this.routerReplace(this.link);
        }
      }

      this.$emit('click');
    },
  },
};
</script>
