<template>
  <q-page class="">
    <div>
      <job-actions/>
      <div class="layout-padding">
        <div v-if="meta.lastFetched != null">
          <!-- <q-field v-if="completedJobs.length > 0"
            icon="assignment"
            label="Completed Jobs"/> -->
          <div class="q-mx-sm">
            <div v-for="(job, index) in completedJobs" :key="index">
              <helper-job-summary
                :key="job.id"
                :job="job">
              </helper-job-summary>
            </div>
          </div>
          <div class="text-faded" style="padding: 1rem;" v-if="completedJobs.length === 0">
            No jobs match the specified criteria.
          </div>
        </div>
      </div>
    </div>
  </q-page>
</template>

<script>
import JobActions from 'components/JobActions';
import HelperJobSummary from 'components/HelperJobSummary';
import { mapState } from 'vuex';
import c from '../../utils/constants';

export default {
  name: 'PageArchivedJobs',
  components: { JobActions, HelperJobSummary },
  data() {
    return {
    };
  },
  computed: {
    ...mapState({
      meta: (state) => state.helpers.archivedJobs.meta,
      jobs: (state) => state.helpers.archivedJobs.data,
    }),
    completedJobs() {
      return this.jobs.filter((job) => job.jobStatus === c.jobStatus.completed);
    },
  },
  methods: {
  },
  beforeRouteEnter(to, from, next) {
    next(async (vm) => {
      await vm.$store.dispatch('helpers/fetchArchivedJobs');
    });
  },
};
</script>
