<template>
  <q-page class="">
    <job-actions/>
    <div class="q-mt-sm">
      <q-chip
        clickable
        v-for="(item, index) in filterGroups" :key="item.id"
        :icon="groupFilter === item.id ? 'check_circle_outline' : 'radio_button_unchecked'"
        :color="groupFilter === item.id ? 'white' : 'white'"
        :text-color="groupFilter === item.id ? 'primary' : 'black'"
        @click="handleFilterGroupClick(index)"
        class="q-mx-xs q-my-xs q-caption profile-tab-margin"
      >
        <span>{{item.text}}</span>
        <q-badge v-if="item.id === 'unratedJobs' && unratedJobsMeta.lastFetched != null && unratedJobs && unratedJobs.length > 0" color="red" floating>
          {{unratedJobs.length}}
        </q-badge>
      </q-chip>
    </div>
    <div class="layout-padding" style="padding-top: 0">
      <div v-if="meta.lastFetched != null">
        <div v-if="groupFilter === 'upcomingJobs'">
          <div v-if="pendingJobs.length === 0 && upcomingJobs.length === 0">
            <p>No upcoming jobs</p>
          </div>
          <q-field v-if="pendingJobs.length > 0"
            borderless
            readonly
            label="Unawarded Jobs">
            <template v-slot:prepend>
              <q-icon name="assignment" />
            </template>
          </q-field>
          <div class="q-mx-sm">
            <div v-for="(job, index) in pendingJobs" :key="index">
              <client-job-summary
                :key="job.id"
                :job="job">
              </client-job-summary>
            </div>
          </div>

          <q-field v-if="upcomingJobs.length > 0"
            borderless
            readonly
            label="Awarded Jobs">
            <template v-slot:prepend>
              <q-icon name="assignment" />
            </template>
          </q-field>
          <div class="q-mx-sm">
            <div v-for="(job, index) in upcomingJobs" :key="index">
              <client-job-summary
                :key="job.id"
                :job="job">
              </client-job-summary>
            </div>
          </div>
        </div>

        <div v-if="groupFilter === 'historyJobs'">
          <div v-if="completedJobs.length === 0 && cancelledJobs.length === 0">
            <p>No jobs to display</p>
          </div>
          <q-field v-if="completedJobs.length > 0"
            borderless
            readonly
            label="Completed Jobs">
            <template v-slot:prepend>
              <q-icon name="assignment" />
            </template>
          </q-field>
          <div class="q-mx-sm">
            <div v-for="(job, index) in completedJobs" :key="index">
              <client-job-summary
                :key="job.id"
                :job="job">
              </client-job-summary>
            </div>
          </div>

          <q-field v-if="cancelledJobs.length > 0"
            borderless
            readonly
            label="Cancelled Jobs">
            <template v-slot:prepend>
            <q-icon name="assignment" />
            </template>
          </q-field>
          <div class="q-mx-sm">
            <div v-for="(job, index) in cancelledJobs" :key="index">
              <client-job-summary
                :key="job.id"
                :job="job">
              </client-job-summary>
            </div>
          </div>
        </div>
      </div>

      <div v-if="unratedJobsMeta.lastFetched != null">
        <div v-if="groupFilter === 'unratedJobs'">
          <div v-if="unratedJobs.length === 0">
            <p>No unrated jobs</p>
          </div>

          <client-unrated-job-summary
            v-for="job in unratedJobs"
            :key="job.id"
            :job="job">
          </client-unrated-job-summary>
        </div>
      </div>
    </div>
    <rate-helper-modal ref="rateHelperModal" v-on:rateHelperSuccess="handleRateHelperSuccess" />
  </q-page>
</template>

<script>
import ClientJobSummary from 'components/ClientJobSummary';
import ClientUnratedJobSummary from 'components/ClientUnratedJobSummary';
import RateHelperModal from 'components/RateHelperModal';
import JobActions from 'components/JobActions';
import { mapState } from 'vuex';
import c from '../../utils/constants';
import errorHandler from '../../utils/error-handler';

export default {
  name: 'PageFamiliesAllJobs',
  components: {
    ClientJobSummary, JobActions, ClientUnratedJobSummary, RateHelperModal,
  },
  data() {
    const filterGroups = [
      {
        id: 'upcomingJobs',
        text: 'Upcoming',
      },
      {
        id: 'historyJobs',
        text: 'History',
      },
      {
        id: 'unratedJobs',
        filterKey: 'auPairsNearMe',
        text: 'Unrated Jobs',
      },
    ];

    return {
      filterGroups,
      groupFilter: 'upcomingJobs',
    };
  },
  computed: {
    ...mapState({
      jobs: (state) => state.families.jobs.data,
      meta: (state) => state.families.jobs.meta,
      unratedJobs: (state) => state.families.unratedJobs.data,
      unratedJobsMeta: (state) => state.families.unratedJobs.meta,
    }),
    pendingJobs() {
      return this.jobs.filter((job) => job.jobStatus === c.jobStatus.pending);
    },
    upcomingJobs() {
      return this.jobs.filter((job) => job.jobStatus === c.jobStatus.awarded || job.jobStatus === c.jobStatus.filled);
    },
    completedJobs() {
      return this.jobs.filter((job) => job.jobStatus === c.jobStatus.completed || job.jobStatus === c.jobStatus.filledAndClosed);
    },
    cancelledJobs() {
      return this.jobs.filter((job) => job.jobStatus === c.jobStatus.cancelled);
    },
  },
  methods: {
    handlePostJob() {
      this.routerPush('/post-job?reset=1');
    },
    handleFilterGroupClick(index) {
      const group = this.filterGroups[index];
      if (this.groupFilter === group.id) return;

      this.groupFilter = group.id;
    },
    async fetch() {
      this.showLoader();
      await this.$store.dispatch('families/fetchJobs');
      await this.$store.dispatch('families/fetchUnratedJobs');
      this.hideLoader();
    },
    async checkUnratedJobs() {
      try {
        if (this.unratedJobs.length === 0) return;
        const hasRatedHelper = this.getPageState('hasRatedHelper');
        if (hasRatedHelper) return;

        this.setPageState('hasRatedHelper', true);
        const firstUnratedJob = this.unratedJobs[0];
        await this.$store.dispatch('families/fetchJobForRating', { id: firstUnratedJob.id, ratingFor: firstUnratedJob.awardedApplicantId, ratingMode: c.ratingMode.popup });

        if (this.$refs.rateHelperModal) {
          this.$refs.rateHelperModal.open();
        }
      } catch (err) {
        errorHandler.captureException(err);
      }
    },
    handleRateHelperSuccess() {
      this.$store.dispatch('families/fetchUnratedJobs');
    },
  },
  beforeRouteEnter(to, from, next) {
    next(async (vm) => {
      await vm.fetch();
      vm.checkUnratedJobs();
    });
  },
  async beforeRouteUpdate(to, from, next) {
    await this.fetch();
    next();
  },
};
</script>
