import * as types from '../mutation-types';
import c from '../../utils/constants';

export default {
  namespaced: true,
  state: {
    jobDetails: {},
    jobDetailsTemplate: {
      data: {
        jobType: c.jobType.unspecified,
        isBeforeSchoolCare: false,
        isAfterSchoolCare: false,
        addressId: -1,
        managedBy: c.managedBy.system,
      },
      meta: {
      },
    },
  },
  actions: {
  },
  mutations: {
    [types.POST_JOB_UPDATE_JOB_TYPE]: (state, payload) => {
      const mutation = {};
      mutation[payload.member] = payload.value;

      Object.assign(state.data, mutation);
    },
  },
  getters: {
  },
};
