<template>
<div>
    <table
      class="pricing-table"
    >
      <thead>
        <tr>
          <th class="text-left"></th>
          <th class="text-left"></th>
          <th class="text-center">Hours</th>
          <th class="text-center">Rate</th>
          <th class="text-right">Total</th>
        </tr>
      </thead>
      <tbody>
        <weekly-schedule-summary-row
          v-for="(shift, index) in weeklyScheduleEntries"
          :key="index"
          :shift="shift"
          :storeModuleName="storeModuleName"
          :editable="editable"
          :minHours="minHours"
        />
        <tr>
          <td class="text-left"></td>
          <td class="text-left"></td>
          <td class="text-center">
            <span class="text-bold">{{getHourlyTotal()}}</span>
            </td>
          <td class="text-center"></td>
          <td class="text-right">
            <span class="text-bold">
              ${{getWeeklyTotal()}}
            </span>
          </td>
        </tr>
      </tbody>
    </table>
    <div v-if="isBusiness">
      <small class="q-mr-xs">*</small>
      <small v-if="country === 'au'">Excludes Superannuation and other statutory entitlements. </small>
      <small v-if="country === 'nz'">Excludes any government statutory entitlements.</small>
    </div>
    <div v-else-if="lowHoursNotice">
      <span class="text-red">*</span>
      <span class="text-grey q-ml-sm">
        Shifts under 3 hours can be difficult to fill.
        <br/>
        Consider increasing the hours and/or increasing the hourly rate.
      </span>
    </div>
  </div>
</template>

<script>
import WeeklyScheduleSummaryRow from 'components/WeeklyScheduleSummaryRow.vue';
import useVuelidate from '@vuelidate/core';
import { getTimeDifference } from '../utils/helpers';

export default {
  name: 'WeeklyScheduleSummary',
  components: { WeeklyScheduleSummaryRow },
  data() {
    return {
    };
  },
  props: ['job', 'meta', 'storeModuleName', 'editable', 'minHours'],
  setup() {
    return {
      v$: useVuelidate({ $stopPropagation: true }),
    };
  },
  methods: {
    validate() {
      this.v$.$touch();
      return !this.v$.$error;
    },
    getHourlyTotal() {
      const weeklyTotal = this.weeklyScheduleEntries.reduce((totalAcc, entry) => {
        if (entry.shifts) {
          const shiftTotal = entry.shifts.reduce((shiftAcc, shift) => {
            shiftAcc += shift.hours;
            return shiftAcc;
          }, 0);
          totalAcc += shiftTotal;
        } else {
          const hours = getTimeDifference(entry.jobStartTime, entry.jobEndTime);
          totalAcc += hours;
        }

        return totalAcc;
      }, 0);
      return weeklyTotal;
    },
    getWeeklyTotal() {
      const weeklyTotal = this.weeklyScheduleEntries.reduce((totalAcc, entry) => {
        if (entry.shifts) {
          const shiftTotal = entry.shifts.reduce((shiftAcc, shift) => {
            shiftAcc += shift.price;
            return shiftAcc;
          }, 0);
          totalAcc += shiftTotal;
        } else {
          totalAcc += entry.price;
        }
        return totalAcc;
      }, 0);
      return weeklyTotal;
    },
  },
  computed: {
    lowHoursNotice() {
      if (!this.minHours) return false;

      const hasLowHourShifts = this.weeklyScheduleEntries.find((entry) => entry.hours < this.minHours);

      return hasLowHourShifts;
    },
    weeklyScheduleEntries() {
      if (!this.meta) return this.job.weeklySchedule.weeklyScheduleEntries;

      const { weeklyScheduleEntries } = this.meta;

      const entries = [];
      weeklyScheduleEntries.forEach((entry) => {
        entry.shifts.forEach((shift, shiftIndex) => {
          entries.push({
            dayOfWeek: entry.dayOfWeek,
            shiftIndex,
            hours: shift.hours,
            hourlyPrice: shift.hourlyPrice,
            price: shift.price,
            jobStartTime: shift.jobStartTime,
            jobEndTime: shift.jobEndTime,
          });
        });
      });

      return entries;
    },
  },
};
</script>
<style lang="sass">
.hourly-price-editor
  .q-field__suffix
    color: red
</style>
