<template>
  <q-page class="post-one-off-job-step-3">
    <div class="layout-padding">
      <q-field
        label="Job Summary"
        borderless
        readonly
        no-error-icon
        hide-bottom-space
      >
        <template v-slot:prepend>
          <q-icon name="date_range" />
        </template>
        <template v-slot:control>
          <div class="full-width q-mt-md">
            {{jobHours}} Hour {{jobTypeText}} for ${{jobPrice}}
          </div>
        </template>
      </q-field>

      <q-field
        borderless
        readonly
        hide-bottom-space
        :error="v$.willPayOvertime.$error"
      >
        <template v-slot:prepend>
          <q-icon name="add" />
        </template>
        <template v-slot:control>
          <div class="full-width q-mt-xs">
            <span class="q-field__label">{{overtimeText}}</span>
            <q-option-group
              type="radio"
              v-model="willPayOvertime"
              :options="[
                { label: 'No', value: false },
                { label: 'Yes', value: true },
              ]"
            />
            <q-input
              v-if="willPayOvertime"
              type="number"
              :error="v$.overtimeRate.$error"
              v-model="overtimeRate"
              placeholder="00"
              prefix="$"
              suffix="per hour"
              class="overtime-rate-input"
            />
          </div>
        </template>
      </q-field>

      <job-payment-method ref="jobPaymentMethod" :job="job" :storeModuleName="storeModuleName" />
    </div>
  </q-page>
</template>

<script>
import useVuelidate from '@vuelidate/core';
import { requiredIf } from '@vuelidate/validators';
import { mapState } from 'vuex';
import JobPaymentMethod from 'components/JobPaymentMethod';
import { beforeRouteEnter, beforeLeaveStep } from './multistep-helper';
import c from '../../utils/constants';
import NotificationService from '../../utils/notification-service';
import { getTimeDifference, formatNumber, jobTypeText } from '../../utils/helpers';

export default {
  name: 'PageOneOffJobStep3',
  components: { JobPaymentMethod },
  data() {
    return {};
  },
  setup() {
    return {
      v$: useVuelidate(),
    };
  },
  validations() {
    return {
      willPayOvertime: {
        required() {
          return this.willPayOvertime === true || this.willPayOvertime === false;
        },
      },
      overtimeRate: {
        requiredIf: requiredIf(this.willPayOvertime),
      },
    };
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
      job: (state) => state.postOneOffJob.jobDetails.data,
    }),
    storeModuleName() {
      return 'postOneOffJob.jobDetails';
    },
    jobTypeText() {
      return jobTypeText(this.job.jobType, this.userInfo.client.clientType);
    },
    jobHours() {
      const hours = getTimeDifference(this.job.jobStartTime, this.job.jobEndTime);
      return hours;
    },
    jobPrice() {
      let totalValue;
      if (this.paymentType === c.jobPaymentType.hourly) {
        totalValue = this.jobHours * this.job.price;
      } else {
        totalValue = this.job.price;
      }

      return formatNumber(totalValue);
    },
    paymentType: {
      get() {
        return this.job.paymentType;
      },
      set(value) {
        this.setProperty('paymentType', value);
      },
    },
    willPayOvertime: {
      get() {
        return this.job.willPayOvertime;
      },
      set(value) {
        this.setProperty('willPayOvertime', value);
      },
    },
    overtimeRate: {
      get() {
        return this.job.overtimeRate;
      },
      set(value) {
        this.setProperty('overtimeRate', value);
      },
    },
    overtimeText() {
      // const { paymentType } = this.job;
      // return paymentType === c.jobPaymentType.fixed
      //   ? 'Will you pay extra if you arrive home late?'
      //   : 'Will you continue paying if you arrive home late?';
      return 'If the job exceeds the hours above, will you increase the hourly rate for the extra hours?';
    },
  },
  methods: {
    validate() {
      this.v$.$touch();
      const hasPaymentMethoodError = !this.$refs.jobPaymentMethod.validate();
      const hasError = this.v$.$error || hasPaymentMethoodError;
      if (hasError) {
        NotificationService.negative('Please review highlighted fields.');
      }

      return !hasError;
    },
  },
  beforeRouteEnter(to, from, next) {
    beforeRouteEnter(to, from, next);
  },
  beforeRouteLeave(to, from, next) {
    beforeLeaveStep(this, to, from, next);
  },
};
</script>
<style lang="sass">
  .post-one-off-job-step-3
    .price-input
      display: inline-block
      width: 75px
    .payment-type
      display: inline-block
    .overtime-rate-input
      width: 125px
</style>
