<template>
  <q-page class="post-recurring-job-step-1">
    <div class="layout-padding">
      <q-field
        borderless
        readonly
        :error="v$.durationToStart.$error"
        hide-bottom-space
        label="Select start date">
        <template v-slot:prepend>
          <q-icon name="date_range" />
        </template>
        <template v-slot:control>
          <div class="full-width row items-center">
            <q-radio
              v-model="jobStartType"
              :val="2"
              label="Job to start within:"
            />
            <!-- <q-input :disable="jobStartType !== 2"
              v-model="durationToStart"
              :error="v$.durationToStart.$error"
              error-message="required"
              type="number"
              placeholder="00"
              :min="1"
              style="width: 25px;margin: 0 10px"
              class="inline duration-to-start"
            /> -->
            <q-select
              :disable="jobStartType !== 2"
              v-model="durationToStart"
              :options="numberList"
              option-value="value"
              option-label="label"
              emit-value
              map-options
              placeholder="00"
              style="width: 3rem"
              class="inline q-mx-sm"
            />
            <q-select
              :disable="jobStartType !== 2"
              v-model="jobStartDurationType"
              :options="durationTypes"
              option-value="value"
              option-label="label"
              emit-value
              map-options
              class="inline"
            />
          </div>
        </template>
      </q-field>
      <p class="q-my-xs q-mx-xl">OR</p>
      <q-field
        :error="v$.jobDate.$error"
        hide-bottom-space
        borderless>
        <template v-slot:prepend>
          <q-icon name="date_range" />
        </template>
        <template v-slot:control>
          <div class="full-width row items-start">
            <q-radio
              v-model="jobStartType"
              :val="1"
              label="Job to start on:"/>
            <date-picker v-if="jobStartType === 1" :autoSelect="true" :show-hint="false" :date="jobDate" v-on:change="handleJobDateChange" :min-date-today="true" />
          </div>
        </template>
      </q-field>

      <q-field
        :error="v$.duration.$error"
        borderless
        hide-bottom-space
        readonly
        label="Expected job duration"
      >
        <template v-slot:prepend>
          <q-icon name="timelapse" />
        </template>
        <template v-slot:control>
          <div class="full-width row items-center">
            <!-- <q-input
              :error="v$.duration.$error"
              error-message="required"
              v-model="duration"
              type="number"
              placeholder="00"
              :min="1"
              class="inline duration-to-start"
              style="width: 30px; margin: 0 30px"
            /> -->
            <q-select
              v-model="duration"
              :options="numberList"
              placeholder="00"
              style="width: 3rem"
              class="inline q-mx-sm"
              option-value="value"
              option-label="label"
              emit-value
              map-options
            />
            <q-select
              v-model="durationType"
              radio
              :options="durationTypes"
              class="inline"
              option-value="value"
              option-label="label"
              emit-value
              map-options
            />
          </div>
        </template>
      </q-field>

      <q-field
        class="q-my-md"
        icon="today"
        readonly
        label="Select the days required"
        :error="v$.selectedDays.$error"
        borderless
      >
        <template v-slot:prepend>
          <q-icon name="date_range" />
        </template>
        <template v-slot:control>
          <div class="full-width q-my-sm">
            <q-option-group
              type="checkbox"
              v-model="selectedDays"
              :options="allDaysOfWeek"
            />
            <span class="text-italic">Candidates will respond with their availability for each of the days you select</span>
          </div>
        </template>
      </q-field>
    </div>
  </q-page>
</template>

<script>
import useVuelidate from '@vuelidate/core';
import { required, requiredIf } from '@vuelidate/validators';
import { mapState } from 'vuex';
import DatePicker from 'components/DatePicker';
import { beforeRouteEnter, beforeLeaveStep } from './multistep-helper';
import NotificationService from '../../utils/notification-service';
import c from '../../utils/constants';
import { getDayOfWeekText } from '../../utils/helpers';

export default {
  name: 'PageRecurringJobStep1',
  components: { DatePicker },
  data() {
    const selectedDays = [...this.$store.state.postRecurringJob.jobDetails.meta.selectedDays];

    return {
      durationTypes: [
        { label: 'Week(s)', value: 1 },
        { label: 'Month(s)', value: 2 },
      ],
      numberList: c.numberList,
      selectedDays,
    };
  },
  setup() {
    return {
      v$: useVuelidate(),
    };
  },
  validations() {
    return {
      selectedDays: {
        required,
      },
      durationToStart: {
        requiredIf: requiredIf(this.jobStartType === 2),
      },
      jobDate: {
        required(value) {
          if (this.jobStartType === 1) {
            // return required(this.job.jobDate);
            if (!value) {
              return false;
            }
            const jobDate = new Date(value);
            const today = new Date();
            today.setHours(0, 0, 0, 0);
            return jobDate.getTime() >= today.getTime();
          }

          return true;
        },
      },
      duration: {
        required,
      },
    };
  },
  computed: {
    ...mapState({
      job: (state) => state.postRecurringJob.jobDetails.data,
      meta: (state) => state.postRecurringJob.jobDetails.meta,
    }),
    storeModuleName() {
      return 'postRecurringJob.jobDetails';
    },
    allDaysOfWeek() {
      const list = [
        { value: 1, label: getDayOfWeekText(1) },
        { value: 2, label: getDayOfWeekText(2) },
        { value: 3, label: getDayOfWeekText(3) },
        { value: 4, label: getDayOfWeekText(4) },
        { value: 5, label: getDayOfWeekText(5) },
      ];

      if (this.job.jobType === c.jobType.nannying) {
        list.push({ value: 6, label: getDayOfWeekText(6) });
        list.push({ value: 0, label: getDayOfWeekText(0) });
      }

      return list;
    },
    jobStartType: {
      get() {
        return this.job.jobStartType;
      },
      set(value) {
        this.setProperty('jobStartType', value);
      },
    },
    durationToStart: {
      get() {
        return this.job.durationToStart;
      },
      set(value) {
        this.setProperty('durationToStart', value);
      },
    },
    jobStartDurationType: {
      get() {
        return this.job.jobStartDurationType;
      },
      set(value) {
        this.setProperty('jobStartDurationType', value);
      },
    },
    jobDate: {
      get() {
        return this.job.jobDate;
      },
      set(value) {
        // const parsedValue = value ? new Date(value) : null;
        // this.setProperty('jobDate', parsedValue);
        this.setProperty('jobDate', value);

        if (value && value.getDate) {
          this.setProperty('jobDateDay', value.getDate());
          this.setProperty('jobDateMonth', value.getMonth() + 1);
          this.setProperty('jobDateYear', value.getFullYear());
        }
      },
    },
    duration: {
      get() {
        return this.job.duration;
      },
      set(value) {
        this.setProperty('duration', value);
      },
    },
    durationType: {
      get() {
        return this.job.durationType;
      },
      set(value) {
        this.setProperty('durationType', value);
      },
    },
  },
  methods: {
    validate() {
      this.v$.$touch();
      const hasError = this.v$.$error;
      if (hasError) {
        NotificationService.negative('Please review highlighted fields.');
      }

      return !hasError;
    },
    handleJobDateChange(value) {
      this.jobDate = value.date;
    },
  },
  watch: {
    jobStartType(value) {
      if (value === 1) this.durationToStart = '';
      else this.jobDate = null;
    },
    selectedDays(value) {
      const shiftTemplate = [];
      const { hourlyPrice } = this.job;
      const { jobType } = this.job;

      // Default shift data.
      if (jobType === c.jobType.nannying) {
        shiftTemplate.push({
          isRequired: true,
          jobStartTime: '06:00',
          jobEndTime: '18:00',
          hourlyPrice,
          price: hourlyPrice * 12,
          hours: 12,
        });
      } else if (jobType === c.jobType.beforeSchoolCare) {
        shiftTemplate.push({
          isRequired: true,
          jobStartTime: '06:00',
          jobEndTime: '09:00',
          hourlyPrice,
          price: hourlyPrice * 3,
          hours: 3,
        });
      } else if (jobType === c.jobType.afterSchoolCare) {
        shiftTemplate.push({
          isRequired: true,
          jobStartTime: '15:00',
          jobEndTime: '18:00',
          hourlyPrice,
          price: hourlyPrice * 3,
          hours: 3,
        });
      } else if (jobType === c.jobType.beforeAndAfterSchoolCare) {
        shiftTemplate.push({
          isRequired: true,
          jobStartTime: '06:00',
          jobEndTime: '09:00',
          hourlyPrice,
          price: hourlyPrice * 3,
          hours: 3,
        });

        shiftTemplate.push({
          isRequired: true,
          jobStartTime: '15:00',
          jobEndTime: '18:00',
          hourlyPrice,
          price: hourlyPrice * 3,
          hours: 3,
        });
      }

      const entries = value.map((selectedDay, index) => {
        const shifts = shiftTemplate.map((shift) => {
          const clonedShift = { ...shift };
          return clonedShift;
        });

        const entry = {
          index,
          dayOfWeek: selectedDay,
          shifts,
        };

        return entry;
      });

      this.setMetaProperty('selectedDays', [...value]);
      this.setMetaProperty('weeklyScheduleEntries', entries);
    },
  },
  beforeRouteEnter(to, from, next) {
    beforeRouteEnter(to, from, next, (vm) => {
      // This is a workaround as the current implementation of Quasar Option Groups
      // directly manipulate the array which crashes vuex in strict mode.
      const storeSelectedDays = vm.getMetaProperty('selectedDays');
      vm.localSelectedDays = [...storeSelectedDays];
    });
  },
  beforeRouteLeave(to, from, next) {
    beforeLeaveStep(this, to, from, next);
  },
};
</script>
<style lang="sass">
.post-recurring-job-step-1
  .duration-to-start
    width: 10rem
</style>
