import { Dialog } from 'quasar';
import store from '../../store';
import * as types from '../../store/mutation-types';
import c from '../../utils/constants';

export const beforeRouteEnter = (to, from, next, cb) => {
  if (from.path.indexOf('helper-profile') !== -1) {
    return next(cb);
  }

  const jobDetails = store.getters['postOneOffJob/jobDetails'];
  if (!jobDetails) {
    return next({ path: '/post-job', query: { reset: 1 }, replace: true });
  }

  const fromStep = parseInt(from.path.split('step/')[1], 10) || 1;
  const toStep = parseInt(to.path.split('step/')[1], 10) || 1;
  let skip = false;

  // Candidate selection step.
  if (toStep === 4 && jobDetails.managedBy === c.managedBy.user) {
    skip = true;
  }

  // Select helpers step.
  if (toStep === 5 && jobDetails.managedBy === c.managedBy.system) {
    skip = true;
  }

  // Payment Step
  if (toStep === 6) {
    // Payment Not Required.
    const paymentRequired = store.getters['postOneOffJob/isPaymentRequired'];
    if (!paymentRequired) {
      skip = true;
    }
  }

  if (skip) {
    const movingForward = toStep > fromStep;
    const nextStep = movingForward ? toStep + 1 : toStep - 1;
    return next({ path: `/post-one-off-job/step/${nextStep}` });
  }

  return next(cb);
};

export const beforeLeaveStep = (context, to, from, next) => {
  if (to.path.indexOf('helper-profile') !== -1) {
    next();
    return;
  }

  const toStep = parseInt(to.path.split('step/')[1], 10) || 1;
  const currentStep = context.$store.getters['postOneOffJob/currentStep'];
  // Trigger validation only when going to next step.
  if (toStep > currentStep && !context.validate()) {
    next(false);
    return;
  }

  store.commit(`postOneOffJob/${types.UPDATE_STEP}`, { toStep });

  next();
};

export const postJob = (router) => {
  const jobDetails = store.getters['postOneOffJob/jobDetails'];

  let message;
  if (jobDetails.managedBy === c.managedBy.user) {
    message = 'This job can be active for up to 14 days. During this time you will be able to chat online with invited candidates. If you don\'t find the right person quickly, you can change the job details and invite more candidates to apply. When a candidate applies for your job, they will be waiting to hear from you! Make sure to confirm your chosen candidate by clicking the AWARD button.';
  } else {
    message = 'This job can be active for up to 14 days. The Juggle Assist algorithm will now invite candidates who meet your criteria and invite them to your job. You will be notified each time a candidate applies for this job – these applicants will appear in your job shortlist.';
  }

  Dialog.create({
    title: 'Important',
    message,
    cancel: {
      label: 'Cancel',
      color: 'secondary',
      outline: true,
      handler: () => {
        // Nothing.
      },
    },
    ok: {
      label: 'Ok, Got It',
      color: 'primary',
      outline: true,
    },
  }).onOk(() => {
    store.dispatch('postOneOffJob/postOneOffJob', router).catch(() => {
      const paymentRequired = store.getters['postOneOffJob/isPaymentRequired'];
      if (paymentRequired) {
        router.replace({ path: `/post-one-off-job/step/${store.getters['postOneOffJob/currentStep'] - 1}` });
      }
    });
  }).onCancel(() => {});
};
