<template>
  <q-dialog v-model="opened" class="award-job-modal">
    <q-layout view="Lhh lpR fff" container class="bg-white">
      <q-header>
        <q-toolbar>
          <q-toolbar-title>
            Award Job
          </q-toolbar-title>
          <q-btn flat>
            <q-icon name="clear" v-close-popup />
          </q-btn>
        </q-toolbar>
      </q-header>
      <q-page-container>
        <div class="layout-padding">
          <p>
            Select the carers you would like to award the job to.
            <q-option-group
              color="primary"
              type="checkbox"
              v-model="selected"
              :options="applicants"
            />
          </p>
        </div>
      </q-page-container>
      <q-footer class="bg-white">
        <q-toolbar>
          <q-btn color="secondary" outline class="q-mx-sm" v-close-popup>Cancel</q-btn>
          <q-btn color="accent" @click="handleAwardJob()">Award</q-btn>
        </q-toolbar>
      </q-footer>
    </q-layout>
  </q-dialog>
</template>

<script>
import NotificationService from '../../utils/notification-service';

export default {
  name: 'RecurringJobAwardJobModal',
  data() {
    return {
      applicants: [],
      selected: [],
      opened: false,
    };
  },
  computed: {
    storeModuleName() {
      return 'families.pages.jobDetails';
    },
  },
  methods: {
    open({ applicants, selected }) {
      this.applicants = applicants;
      this.selected = Object.assign([], selected);
      this.opened = true;
    },
    handleAwardJob() {
      if (this.selected.length === 0) {
        NotificationService.info('Please select one or more candidates.');
        return;
      }

      this.setProperty('awardedApplicantIds', this.selected);
      this.opened = false;
      this.$store
        .dispatch('families/updateJobDetails')
        .then(() => {
          NotificationService.positive('Job awarded successfully.');
        }).catch((error) => {
          NotificationService.negative(error.message);
        });
    },
  },
};
</script>
