import * as http from '../../api/http';
import * as types from '../mutation-types';
import loader from '../../utils/loader';
import c from '../../utils/constants';

export default {
  namespaced: true,
  state: {
    data: {
      firstName: '',
      lastName: '',
      phoneNumber: '',
      phoneNumberRepeat: '',
      relationship: '',
      guardianApprovalKey: '',
      doesAcceptTermsAndConditions: false,
      doesApproveAccount: false,
      status: c.guardianStatus.pending,
    },
    meta: {
      relationships: [
        { label: 'Mum', value: 2 },
        { label: 'Dad', value: 1 },
        { label: 'Guardian', value: 3 },
      ],
      isProcessing: false,
      hasGuardian: false,
    },
  },
  actions: {
    approveAccount({ state, commit }) {
      loader.show();
      const payload = state.data;

      return http
        .patch(`guardians/${state.data.guardianApprovalKey}`, payload)
        .then((response) => {
          commit(types.GUARDIAN_APPROVAL_SUCCESS);
          loader.hide();
          return response;
        })
        .catch((error) => {
          loader.hide();
          return Promise.reject(error);
        });
    },
    fetchGuardianForApproval({ commit }, { guardianApprovalKey }) {
      commit(types.FETCH_GUARDIAN_REQUEST);

      return http
        .get(`guardians/${guardianApprovalKey}`)
        .then((response) => {
          commit(types.FETCH_GUARDIAN_SUCCESS, [response.data]);
          loader.hide();
          return response;
        })
        .catch((error) => {
          commit(types.FETCH_GUARDIAN_FAILURE);
          loader.hide();
          return Promise.reject(error);
        });
    },
    submitGuardian({ state, dispatch }) {
      loader.show();

      const payload = state.data;
      return http
        .post('guardians', payload)
        .then(async (response) => {
          await dispatch('refreshUserInfo', null, { root: true });
          loader.hide();
          return response;
        })
        .catch((error) => {
          loader.hide();
          return Promise.reject(error);
        });
    },
    fetchGuardian({ commit }) {
      loader.show();

      commit(types.FETCH_GUARDIAN_REQUEST);
      return http
        .get('guardians')
        .then((response) => {
          commit(types.FETCH_GUARDIAN_SUCCESS, response.data);
          loader.hide();
          return response;
        })
        .catch((error) => {
          commit(types.FETCH_GUARDIAN_FAILURE);
          loader.hide();
          return Promise.reject(error);
        });
    },
  },
  mutations: {
    [types.FETCH_GUARDIAN_REQUEST]: (state) => {
      state.meta.isProcessing = true;
    },
    [types.FETCH_GUARDIAN_SUCCESS]: (state, payload) => {
      state.meta.isProcessing = false;

      if (payload.length > 0) {
        const data = payload[0];
        data.phoneNumberRepeat = data.phoneNumber;

        state.data = data;
        state.meta.hasGuardian = true;
      }
    },
    [types.FETCH_GUARDIAN_FAILURE]: (state) => {
      state.meta.isProcessing = false;
    },
    [types.GUARDIAN_APPROVAL_SUCCESS]: (state) => {
      state.data.status = c.guardianStatus.approved;
    },
  },
  getters: {},
};
