import { env } from './env';
import errorHandler from './error-handler';

let intercomUserData = null;
let userInfo = null;
let adminMode = false;

const IntercomService = {
  load() {
    if (document.querySelectorAll('script[src*="intercom"]').length === 0 && !window.Intercom) {
      console.log('Loading Intercom...');
      const { hostname } = window.location;
      const isProd = env.deviceInfo.platform !== 'web' || (hostname === 'm.jugglestreet.com' || hostname === 'prem.jugglestreet.com' || hostname === 'premnz.jugglestreet.com' || hostname === 'm.jugglestreet.com.au' || hostname === 'm.jugglestreet.co.nz');
      const appId = (isProd) ? 'xolb00oc' : 'eowwcr9g';

      const intercomLinkEl = document.getElementById('intercom-link');
      intercomLinkEl.href = `mailto:${appId}@incoming.intercom.io`;

      window.intercomSettings = {
        app_id: appId,
        custom_launcher_selector: '#intercom-link',
      };

      const src = `https://widget.intercom.io/widget/${appId}`;
      const script = document.createElement('script');
      script.setAttribute('src', src);
      document.head.appendChild(script);
    }
  },
  init(user, admin) {
    userInfo = user;
    adminMode = admin;

    try {
      setTimeout(() => {
        IntercomService.load();
      }, 10000);
    } catch (e) {
      errorHandler.captureException(e);
    }
  },
  setUserInfo() {
    if (adminMode) return;

    document.getElementById('intercom-custom-container').style.display = 'none';

    if (!userInfo || !userInfo.id) return;

    const unreadCountEl = document.getElementById('intercom-unread-count');
    if (unreadCountEl) {
      window.Intercom('onUnreadCountChange', (unreadCount) => {
        if (unreadCount > 0) {
          unreadCountEl.style.visibility = 'visible';
          unreadCountEl.innerText = unreadCount;
        } else {
          unreadCountEl.style.visibility = 'hidden';
        }
      });
    }

    intercomUserData = {
      // app_id: env.intercomAppId,
      user_id: userInfo.id,
      custom_launcher_selector: '#intercom-link',
      'Mobile Site Version': window.appInfo.version,
      'Mobile Site Mode': window.navigator.standalone ? 'App Mode' : 'Site Mode',
      name: `${userInfo.firstName} ${userInfo.lastName}`,
      email: userInfo.email,
    };

    window.Intercom('boot', intercomUserData);
  },
  shutdown() {
    if (!window.Intercom) return;

    window.Intercom('shutdown');
    intercomUserData = null;
  },
  show() {
    if (!IntercomService.hasLoaded()) {
      IntercomService.load();
      setTimeout(IntercomService.show, 2000);
      return;
    }

    window.Intercom('show');
  },
  showNewMessage(content) {
    if (!IntercomService.hasLoaded()) {
      IntercomService.load();
      setTimeout(() => IntercomService.showNewMessage(content), 2000);
      return;
    }

    window.Intercom('showNewMessage', content);
  },
  hide() {
    if (!window.Intercom) return;

    window.Intercom('hide');
  },
  hasLoaded() {
    const loaded = window.Intercom;
    if (loaded && intercomUserData === null) {
      IntercomService.setUserInfo();
    }

    return loaded;
  },
  showIcon() {
    if (!IntercomService.hasLoaded()) {
      IntercomService.load();
      setTimeout(IntercomService.showIcon, 2000);
      return;
    }

    document.getElementById('intercom-custom-container').style.display = 'block';
  },
  hideIcon() {
    document.getElementById('intercom-custom-container').style.display = 'none';
  },
  // update(userInfo, adminMode) {
  //   if (adminMode) return;

  //   if (!window.Intercom) return;

  //   const data = {
  //     app_id: env.intercomAppId,
  //     user_id: userInfo.id,
  //     email: userInfo.email,
  //   };

  //   window.Intercom('update', data);
  // },
};

export default IntercomService;
