<template>
  <div class="layout-padding">
    <profile-visibility :defaultVisibility="false" ref="profileVisibility" v-on:change="handleChange"/>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex';
import ProfileVisibility from 'components/ProfileVisibility';
import beforeLeaveStep from '../before-leave-step';
import * as types from '../../../store/mutation-types';

export default {
  name: 'ProfileWizardProfileVisibility',
  components: { ProfileVisibility },
  data() {
    const userInfo = this.$store.state.userInfo.data;

    return {
      isComplete: userInfo.publicUserId !== null,
    };
  },
  methods: {
    ...mapActions([
      'updateUserInfo',
    ]),
    handleChange() {
      this.isComplete = true;
      this.$store.commit(`profileWizard/${types.UPDATE_STEP_STATUS}`, { complete: true });
    },
    validate(notify) {
      if (this.currentStep.meta.canSkip) {
        return true;
      }

      return this.$refs.profileVisibility.validate(notify);
    },
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
      currentStep: (state) => {
        const { meta } = state.profileWizard;
        const currentStep = meta.steps[meta.currentStep];
        return currentStep;
      },
      stepComplete: (state) => {
        const { meta } = state.profileWizard;
        const currentStep = meta.steps[meta.currentStep];
        return currentStep.complete;
      },
    }),
    storeModuleName() {
      return 'userInfo';
    },
  },
  beforeRouteLeave(to, from, next) {
    if (this.isComplete) {
      this.$refs.profileVisibility.updateProfile(false).then((response) => {
        console.log(response);
        beforeLeaveStep(this, to, from, next);
      });
    } else {
      beforeLeaveStep(this, to, from, next);
    }
  },
};
</script>
