<template>
  <div v-if="!isOwnProfile" style="min-height: 30px" :class="customClass">

    <q-btn size="14px" color="accent" v-if="canConnect && isApproved" @click="connect" icon="add" label="">
      <q-icon v-if="isFamily" name="favorite_border" color="white" size="24px" />
      <span v-else>Connect</span>
    </q-btn>

    <q-btn size="14px" color="accent" v-if="canConnect && isPending" @click="pendingAccountNotice" icon="add">
      <q-icon v-if="isFamily" name="favorite_border" color="white" size="24px" />
      <span v-else>Connect</span>
    </q-btn>

    <!-- <span v-if="isWaiting && isHelper" style="line-height: 35px">Waiting for RSVP</span> -->

    <div v-if="canAccept" class="text-center">
      <span style="font-size: 14px" class="text-primary text-italic text-size-sm">
        {{this.isFamily ? 'Favourite Request' : 'Connection Request'}}
      </span>
      <br/>
      <q-btn class="q-my-xs" size="12px" color="accent"  @click="accept" icon="mail" label="Accept" />
      <q-btn v-if="isFamily" class="q-my-xs text-underline" flat size="14px" color="secondary"  @click="resetConnection" label="Ignore" />
    </div>

    <q-btn size="14px" color="secondary" v-if="canReject" @click="reject" label="Unfriend" />

    <!-- <q-btn size="14px" color="accent" v-if="canChat" icon="chat" @click="chat" label="Chat" /> -->
    <q-icon name="chat" size="28px" color="black" v-if="canChat" @click="chat" />

    <span v-if="!canChat && isHelper && displayConnectionStatus && (isConnectionApproved || isWaiting)" style="line-height: 35px" class="flex justify-center">
      <q-icon name="link" size="25px" class="q-mx-xs" /> Connected
    </span>

    <!-- <q-btn color="accent" v-if="canRefer">Refer </q-btn> -->
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex';
import c from '../utils/constants';
// import NotificationService from '../utils/notification-service';

export default {
  name: 'HelperActions',
  data() {
    return {
    };
  },
  props: {
    user: {
      default: undefined,
    },
    allowUnfriendAction: {
      default: false,
    },
    allowChat: {
      default: false,
    },
    allowAccept: {
      default: true,
    },
    allowConnect: {
      default: true,
    },
    customClass: {
      default: '',
    },
    displayConnectionStatus: {
      default: true,
    },
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
    }),
    canConnect() {
      return this.user.friendStatus === c.friendStatus.none && this.allowConnect;
    },
    canAccept() {
      return this.user.friendStatus === c.friendStatus.pending && this.user.actionRequired && this.allowAccept;
    },
    isWaiting() {
      return this.user.friendStatus === c.friendStatus.pending && !this.user.actionRequired;
    },
    canReject() {
      return this.user.friendStatus === c.friendStatus.approved && this.allowUnfriendAction;
    },
    canChat() {
      if (this.isFamily) {
        // return (this.isWaiting || this.isConnectionApproved) && this.allowChat;
        return this.allowChat;
      }

      return false;
    },
    canRefer() {
      return this.user.friendStatus === c.friendStatus.approved;
    },
    isConnectionApproved() {
      return this.user.friendStatus === c.friendStatus.approved;
    },
    isOwnProfile() {
      return this.userInfo.id === this.user.id;
    },
  },
  methods: {
    ...mapActions(['sendFriendRequest']),
    async chat() {
      // Check chat with user.
      const payload = {
        helperId: this.user.id,
      };
      await this.$store.dispatch('realtime/canChatWithHelper', payload)
        .then((canChat) => {
          if (canChat) {
            this.navigateToChat();
          } else {
            this.displayChatNotice();
          }
        });
    },
    navigateToChat() {
      const url = `/chat/with/${this.user.id}`;
      this.routerPush(url);
    },
    displayChatNotice() {
      let actionLabel;
      let message;
      let route;
      if (this.userInfo.permissions.hasUnlimitedChatWithProviders) {
        message = this.isIndividual
          ? 'You need an active job post to chat with this helper.'
          : 'You can chat with helpers who initiate a conversation and candidates who apply for your job.';
        actionLabel = 'Post Job';
        route = '/post-job?reset=1';
      } else {
        message = 'You need to subscribe and post a job to chat with helpers.';
        actionLabel = 'Subscribe';
        route = '/account/pricing';
      }

      this.$q.dialog({
        title: 'Chat',
        message,
        cancel: {
          label: 'Ok',
          color: 'secondary',
          outline: true,
        },
        ok: {
          label: actionLabel,
          outline: true,
        },
      }).onOk(() => {
        this.routerPush(route);
      }).onCancel(() => {});
    },
    processFriendRequest(status) {
      const payload = {
        friendId: this.user.id,
        status,
      };

      if (this.user.requestId && this.user.requestId > 0) {
        payload.id = this.user.requestId;
      } else if (this.user.friendshipId && this.user.friendshipId > 0) {
        payload.id = this.user.friendshipId;
      }

      this.sendFriendRequest(payload);
    },
    pendingAccountNotice() {
      this.$q.dialog({
        title: `Invite ${this.user.firstName}`,
        message: `Your profile must be 100% complete before you can connect with ${this.user.firstName}`,
        cancel: {
          label: 'Later',
          color: 'secondary',
          outline: true,
        },
        ok: {
          label: 'Complete Profile',
          color: 'primary',
          outline: true,
        },
      }).onOk(() => {
        if (this.isFamily) {
          this.routerReplace('/client-profile-wizard');
        } else {
          this.routerReplace('/helper-profile-wizard');
        }
      }).onCancel(() => {});
    },
    connect() {
      let title;
      let message;
      if (this.isFamily) {
        title = 'My Favourites';
        message = `Add ${this.user.publicName} to your "My Favourites" section.`;
      } else {
        title = `Connect with ${this.user.firstName}`;
        message = `${this.user.firstName} will be moved to your "My Connections" section`;
      }

      this.$q.dialog({
        title,
        message,
        cancel: {
          label: 'Cancel',
          color: 'secondary',
          outline: true,
        },
        ok: {
          label: this.isFamily ? 'Yes' : 'Invite',
          color: 'primary',
          outline: true,
        },
      }).onOk(() => {
        this.processFriendRequest(c.friendStatus.pending);
      }).onCancel(() => {});
    },
    accept() {
      this.processFriendRequest(c.friendStatus.approved);
      // NotificationService.positive(`${this.user.firstName} has been moved to your "${this.userInfo.jugglerTypes === c.jugglerType.client ? 'My Connections' : 'Connections'}" section.`);
    },
    resetConnection() {
      this.processFriendRequest(c.friendStatus.none);
    },
    reject() {
      this.$q.dialog({
        title: `Hide ${this.user.publicName}`,
        message: `Are you sure you want to permanently hide ${this.user.publicName}?`,
        cancel: {
          label: 'Cancel',
          color: 'secondary',
          outline: true,
        },
        ok: {
          label: 'Hide',
          color: 'primary',
          outline: true,
        },
      }).onOk(() => {
        this.processFriendRequest(c.friendStatus.rejected);
      }).onCancel(() => {});
    },
  },
};
</script>
