<template>
  <q-page class="">
    <div class="layout-padding">
      <q-field
        label="References"
        borderless
        readonly
      >
        <template v-slot:prepend>
          <q-icon name="grading" />
        </template>
        <template v-slot:control>
          <div class="full-width q-mt-md">
            <p>
              Adding references will enhance your Juggle St profile.
            </p>
            <p>
              Juggle Street will give your referee details to potential employers upon request.
            </p>
            <p>
              Au Pairs – 1 reference is required to be a Juggle Street Au Pair. References can be Au Pair and non-childcare related.
            </p>
            <personal-references ref="personalReferences" :minRefs="0" />
          </div>
        </template>
      </q-field>
    </div>
  </q-page>
</template>

<script>
import PersonalReferences from 'components/PersonalReferences';
import NotificationService from '../../utils/notification-service';

export default {
  name: 'PageAccountPersonalReferences',
  components: { PersonalReferences },
  data() {
    return {};
  },
  methods: {},
  computed: {},
  beforeRouteLeave(to, from, next) {
    if (!this.$refs.personalReferences.validate(true)) {
      next(false);
      return;
    }

    this.$store.dispatch('updateUserInfo').then(() => {
      next();
    }).catch((error) => {
      NotificationService.negative(error.message);
      next(false);
    });
  },
};
</script>
