<template>
  <q-page class="">
    <div class="layout-padding">
      <div class="q-mb-sm" v-if="country === 'au' && userInfo.ageInYears >= 17">
        <wwcc-manager></wwcc-manager>
      </div>

      <div class="group" v-if="adminMode">
        <q-field
          stack-label
          borderless
          label="Helper Badges">
          <template v-slot:prepend>
            <q-icon name="badge" />
          </template>
          <template v-slot:control>
            <q-chip
              clickable
              outline
              :icon="item.selected ? 'check' : 'clear'"
              :color="item.selected ? 'primary' : 'black'"
              :text-color="item.selected ? 'white' : 'black'"
              v-for="(item, index) in userInfo.provider.helperBadges" :key="item.optionId"
              @click="handleItemClick('helperBadges', index)"
              class="q-mx-xs q-my-xs q-caption profile-tab-margin"
            >
              {{item.text}}
            </q-chip>
          </template>
        </q-field>
      </div>

      <div class="group">
        <q-field
          stack-label
          borderless
          label="Qualifications">
          <template v-slot:prepend>
            <q-icon name="cast_for_education" />
          </template>
          <template v-slot:control>
            <q-chip
              clickable
              :icon="item.selected ? 'check' : 'clear'"
              :color="item.selected ? 'primary' : 'black'"
              outline
              :text-color="item.selected ? 'white' : 'black'"
              v-for="(item, index) in userInfo.provider.qualifications" :key="item.optionId"
              @click="handleItemClick('qualifications', index)"
              class="q-mx-xs q-my-xs q-caption profile-tab-margin ellipsis"
            >
              {{item.text}}
            </q-chip>
          </template>
        </q-field>
      </div>

      <div class="group">
        <q-field
          stack-label
          borderless
          label="Tutoring Qualifications">
          <template v-slot:prepend>
            <q-icon name="school" />
          </template>
          <template v-slot:control>
            <q-chip
              clickable
              outline
              :icon="item.selected ? 'check' : 'clear'"
              :color="item.selected ? 'primary' : 'black'"
              :text-color="item.selected ? 'white' : 'black'"
              v-for="(item, index) in userInfo.provider.tutoringQualifications" :key="item.optionId"
              @click="handleItemClick('tutoringQualifications', index)"
              class="q-mx-xs q-my-xs q-caption profile-tab-margin ellipsis"
            >
              {{item.text}}
            </q-chip>
          </template>
        </q-field>
      </div>

      <div class="group">
        <q-field
          stack-label
          borderless
          label="First Aid">
          <template v-slot:prepend>
            <q-icon name="local_hospital" />
          </template>
          <template v-slot:control>
            <q-chip
              clickable
              outline
              :icon="item.selected ? 'check' : 'clear'"
              :color="item.selected ? 'primary' : 'black'"
              :text-color="item.selected ? 'white' : 'black'"
              v-for="(item, index) in userInfo.provider.firstAid" :key="item.optionId"
              @click="handleItemClick('firstAid', index)"
              class="q-mx-xs q-my-xs q-caption profile-tab-margin"
            >
              {{item.text}}
            </q-chip>
          </template>
        </q-field>
      </div>
    </div>
  </q-page>
</template>

<script>
import { mapState } from 'vuex';
import WwccManager from 'components/WwccManager';
import * as mutationTypes from '../../store/mutation-types';
import NotificationService from '../../utils/notification-service';

export default {
  name: 'PageAccountMyCertificates',
  components: { WwccManager },
  data() {
    return {
    };
  },
  methods: {
    handleItemClick(section, index) {
      const options = this.userInfo.provider[section];
      const currentOption = options[index];

      const selectedOptions = [];
      options.forEach((option) => {
        let selected = false;
        if (option.optionId === currentOption.optionId) {
          selected = !option.selected;
        } else {
          // eslint-disable-next-line
          selected = option.selected;
        }

        if (selected) {
          selectedOptions.push(option.optionId);
        }
      });

      const payload = {
        propPath: `provider.${section}`,
        value: selectedOptions,
      };

      this.$store.commit(mutationTypes.UPDATE_USER_OPTIONS, payload);
    },
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
    }),
    storeModuleName() {
      return 'userInfo';
    },
    adminMode() {
      return this.$store.state.appInfo.adminMode;
    },
  },
  beforeRouteLeave(to, from, next) {
    this.$store.dispatch('updateUserInfo').then(() => {
      next();
    }).catch((error) => {
      NotificationService.negative(error.message);
      next(false);
    });
  },
};
</script>
