<template>
  <q-page class="">
    <div v-if="usersMeta.lastFetched !== null">
      <div class="q-mb-sm">
        <q-chip
          clickable
          v-for="(item, index) in filterGroups" :key="item.id"
          :icon="groupFilter === item.id ? 'check_circle' : 'radio_button_unchecked'"
          :color="groupFilter === item.id ? 'primary' : 'white'"
          :text-color="groupFilter === item.id ? 'white' : 'black'"
          @click="handleFilterGroupClick(index)"
          class="q-mx-xs q-my-xs q-caption profile-tab-margin"
        >
          {{item.text}}
        </q-chip>
      </div>
      <p class="q-mx-sm q-ym-sm" v-if="users.length === 0 && !usersMeta.isFetching">
        You don't have invitations that match this category.
        <br />
        <q-btn color="accent" rounded class="" @click="routerReplace('/dashboard')">
          {{this.isFamily ? 'Connect with helpers near you' : 'Connect with families near you'}}
        </q-btn>
      </p>
      <div v-if="users.length > 0">
        <div>
          <div v-if="isFamily">
            <helper-profile-card
              v-for="helper in this.users"
              :key="helper.id"
              mode="view"
              filterKey="helperInvitations"
              :helper="helper" />
          </div>
          <div v-if="isHelper">
            <client-profile-card
              v-for="user in this.users"
              :key="user.id"
              mode="view"
              filterKey="familyInvitations"
              :client="user" />
          </div>
        </div>

        <div class="q-my-sm text-center" v-if="hasMore">
          <q-btn rounded outline label="Load More" color="accent" @click="loadMore"/>
        </div>
      </div>
      <q-page-scroller position="bottom-right" :scroll-offset="150" :offset="[18, 18]">
        <q-btn fab icon="keyboard_arrow_up" color="secondary" />
      </q-page-scroller>
      <!-- <q-page-sticky position="bottom-right" :offset="[18, 50]">
          <q-btn round color="secondary" @click="toggleRightPanel">
              <q-icon name="search" />
          </q-btn>
      </q-page-sticky> -->
    </div>
  </q-page>
</template>

<script>
import { mapState } from 'vuex';
import HelperProfileCard from 'components/HelperProfileCard';
import ClientProfileCard from 'components/ClientProfileCard';
import store from '../store';
import c from '../utils/constants';
import * as mutationTypes from '../store/mutation-types';

export default {
  name: 'PageInvitations',
  components: { HelperProfileCard, ClientProfileCard },
  data() {
    const filterGroups = [
      {
        id: 'recent',
        text: 'Recent',
      },
      {
        id: 'all',
        filterKey: 'All',
        text: 'All',
      },
    ];

    return {
      filterGroups,
      groupFilter: 'all',
    };
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
      users: (state) => state.filters.data,
      usersMeta: (state) => state.filters.meta,
    }),
    hasMore() {
      return this.groupFilter === 'all' && (this.pageState.pageIndex * this.pageState.pageSize < this.usersMeta.total);
    },
  },
  methods: {
    loadMore() {
      this.setPageState('pageIndex', this.pageState.pageIndex + 1);
      this.refreshFilter();
    },
    handleFilterGroupClick(index) {
      const group = this.filterGroups[index];
      if (this.groupFilter === group.id) return;

      this.groupFilter = group.id;
      this.setPageState('pageIndex', 1);
      this.refreshFilter();
    },
    toggleRightPanel() {
      this.$store.dispatch('toggleRightDrawer', true);
    },
    refreshFilter() {
      const filterType = this.isFamily ? 'helperInvitations' : 'familyInvitations';

      const invitationRange = {
        filterType,
        filterKey: 'neighbourhood',
        value: this.groupFilter === 'recent' ? c.neighbourhood.recentInvitations : c.neighbourhood.invitations,
      };
      this.$store.commit(`filters/${mutationTypes.UPDATE_FILTER_OPTION}`, invitationRange);

      const filterPayload = {
        filterKey: filterType,
        pageIndex: this.pageState.pageIndex,
        pageSize: this.pageState.pageSize,
        displayLoader: true,
      };
      this.$store.dispatch('filters/applyFilter', filterPayload);
    },
  },
  beforeRouteEnter(to, from, next) {
    next((vm) => {
      store.dispatch('setPageInfo', { title: vm.isFamily ? 'Favourite Requests' : 'Connection Requests', subtitle: '' });
      if (!vm.pageState.pageSize || !vm.pageState.pageIndex) {
        vm.setPageState('pageIndex', 1);
        vm.setPageState('pageSize', 50);
      }

      vm.$store.dispatch('filters/resetFilters');
      vm.refreshFilter();
    });
  },
};
</script>
