<template>
  <q-page class="layout-padding">
    <phone-verification
      v-on:verificationSuccess="handlePhoneVerificationSuccess"
      v-on:verificationFailure="handlePhoneVerificationFailure"
    />
  </q-page>
</template>

<script>
import { mapActions } from 'vuex';
import PhoneVerification from 'components/PhoneVerification';
import NotificationService from '../../utils/notification-service';

export default {
  name: 'PageAccountUpdatePhone',
  components: { PhoneVerification },
  data() {
    return {};
  },
  methods: {
    ...mapActions({
      refreshUserInfo: 'refreshUserInfo',
    }),
    handlePhoneVerificationSuccess() {
      NotificationService.positive('Your mobile number has been updated successfully.');
      this.refreshUserInfo();
      this.routerReplace('/account/about-me');
    },
    handlePhoneVerificationFailure(error) {
      NotificationService.negative(error.message);
    },
  },
};
</script>
