<template>
  <div class="q-mx-sm">
    <q-chip
      v-for="(item, index) in profileTabs" :key="item.id"
      clickable
      :icon="selectedTab === item.id ? 'check_circle_outline' : 'radio_button_unchecked'"
      :color="selectedTab === item.id ? 'white' : 'white'"
      :text-color="selectedTab === item.id ? 'primary' : 'black'"
      @click="handleProfileTabChange(index)"
      :class="'q-my-sm q-caption profile-tab-margin' + (selectedTab === item.id ? ' text-bold' : '')"
    >
      {{item.text}}
    </q-chip>
    <!-- <q-icon v-if="isConnected" name="favorite" color="primary" size="24px" class="float-right q-my-sm"/> -->
  </div>
</template>

<script>
import c from '../../utils/constants';

export default {
  name: 'ProfileTabs',
  props: ['helper', 'selectedTab'],
  emits: ['profileTabChange', 'scrollToReviews'],
  data() {
    return {
      mediaShown: 'image',
    };
  },
  computed: {
    isConnected() {
      return this.helper.friendStatus === c.friendStatus.approved
        || (this.helper.friendStatus === c.friendStatus.pending && !this.helper.actionRequired);
    },
    profileTabs() {
      const tabs = [
        {
          id: 'profile',
          text: 'Profile',
        },
      ];

      const distance = this.helper.distanceInKiloMetersRounded;

      if (this.interestedInChildcareJobs && this.canAccessChildcareJobs && distance <= 25) {
        tabs.push({
          id: 'childcare',
          text: 'Childcare',
        });
      }

      if (this.interestedInAuPairJobs && this.isIndividual) {
        tabs.push({
          id: 'auPair',
          text: 'Au Pair',
        });
      }

      if (this.interestedInTutoringJobs && this.canAccessTutoringJobs && (distance <= 25 || this.helper.providerInterestedInOnlineTutoring)) {
        tabs.push({
          id: 'tutor',
          text: 'Tutor',
        });
      }

      return tabs;
    },
    interestedInChildcareJobs() {
      const { interestedInJobTypes } = this.helper;

      return (interestedInJobTypes & c.jobType.babysitting
      || interestedInJobTypes & c.jobType.nannying
      || interestedInJobTypes & c.jobType.beforeSchoolCare
      || interestedInJobTypes & c.jobType.afterSchoolCare);
    },
    interestedInAuPairJobs() {
      const { interestedInJobTypes } = this.helper;

      return (interestedInJobTypes & c.jobType.auPair);
    },
    interestedInTutoringJobs() {
      const { interestedInJobTypes } = this.helper;

      return (interestedInJobTypes & c.jobType.primarySchoolTutoring
      || interestedInJobTypes & c.jobType.highSchoolTutoring);
    },
  },
  methods: {
    handleProfileTabChange(index) {
      const tabInfo = this.profileTabs[index];
      this.$emit('profileTabChange', tabInfo);
    },
    hasProfileVideo() {
      return this.hasVideo(this.helper.profileIntroVideo);
    },
    scrollToProfileReviews() {
      this.$emit('scrollToReviews');
    },
    hasVideo(video) {
      return video && video.externalHashedId && video.mediaReviewStatus === c.mediaReviewStatus.approved;
    },
  },
};
</script>
