<template>
  <div class="layout-padding">
    <helper-tutoring-job-methods ref="tutoringMethods" v-on:change="handleChange" :isMandatory="true" />
    <div v-if="suppliedTutoringMethods">
      <helper-tutoring-jobs ref="tutoringJobs" v-on:change="handleChange" :isMandatory="true" />
      <helper-tutoring-category ref="tutorCategory" v-on:change="handleChange"/>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex';
import HelperTutoringJobMethods from 'components/HelperTutoringJobMethods';
import HelperTutoringJobs from 'components/HelperTutoringJobs';
import HelperTutoringCategory from 'components/HelperTutoringCategory';
import beforeLeaveStep from '../before-leave-step';
import * as types from '../../../store/mutation-types';

export default {
  name: 'ProfileWizardTutoringCategory',
  components: { HelperTutoringJobMethods, HelperTutoringJobs, HelperTutoringCategory },
  data() {
    return {};
  },
  methods: {
    ...mapActions([
      'updateUserInfo',
    ]),
    handleChange() {
      const complete = this.$refs.tutoringMethods && this.$refs.tutoringMethods.validate(false)
        && this.$refs.tutoringJobs && this.$refs.tutoringJobs.validate(false)
        && this.$refs.tutorCategory && this.$refs.tutorCategory.validate(false);

      this.$store.commit(`profileWizard/${types.UPDATE_STEP_STATUS}`, { complete });
      this.$store.commit(`profileWizard/${types.UPDATE_STEP_STATUS}`, { canSkip: false });
    },
    validate() {
      if (this.currentStep.meta.canSkip) {
        return true;
      }

      return this.$refs.tutoringMethods.validate(true) && this.$refs.tutoringJobs.validate(true) && this.$refs.tutorCategory.validate(true);
    },
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
      currentStep: (state) => {
        const { meta } = state.profileWizard;
        const currentStep = meta.steps[meta.currentStep];
        return currentStep;
      },
      stepComplete: (state) => {
        const { meta } = state.profileWizard;
        const currentStep = meta.steps[meta.currentStep];
        return currentStep.complete;
      },
    }),
    storeModuleName() {
      return 'userInfo';
    },
    suppliedTutoringMethods() {
      return this.userInfo.provider.interestedInFaceToFaceTutoring || this.userInfo.provider.interestedInOnlineTutoring;
    },
  },
  beforeRouteLeave(to, from, next) {
    // Update user info before proceeding to the next step if step is complete
    if (this.stepComplete) {
      this.updateUserInfo().then(() => {
        beforeLeaveStep(this, to, from, next);
      });
    } else {
      beforeLeaveStep(this, to, from, next);
    }
  },
};
</script>
