import loader from '../../utils/loader';
import * as http from '../../api/http';
import c from '../../utils/constants';
import { getServerFilters } from '../../utils/helpers';

export class FilterItem {
  constructor(value, label, meta) {
    this.value = value;
    this.label = label;
    this.meta = meta;
  }
}

