<template>
  <div v-if="job.managedBy === types.managedBy.system">
    <div class="q-my-sm" v-if="(isTest || $store.state.appInfo.adminMode)">
        <div class="bg-red text-center text-white">Admin Mode</div>
        <div class="text-center text-bold">Juggle Assist - Job Analysis</div>
        <div>Status: {{managementStatus}}</div>
        <div>Invitation Batches: {{job.invitationAttempts}}/{{job.targetInvitationAttempts}}</div>
        <div>Invitations per Batch: {{job.invitationsPerAttempt}}</div>
        <div>Invited Helpers: {{job.invitationsCount}}</div>
        <div>Applications: {{shortlistedApplicants.length}}/{{job.targetApplicationsCount}}</div>
        <div>Viewed: {{getApplicantCounts(types.jobApplicationStatus.viewed)}}</div>
        <div>Unseen: {{getApplicantCounts(types.jobApplicationStatus.pending)}}</div>
        <div>Withdrawn: {{getApplicantCounts(types.jobApplicationStatus.withdrawn)}}</div>
        <div>Declined: {{applicantDeclinesCount}}</div>
        <div class="q-ml-sm" v-if="applicantDeclinesCount > 0">
          <div>Declined - Calendar clash: {{getApplicantDeclinesCountByReason(types.unavailableReason.calendarClash)}}</div>
          <div>Declined - Shifts too short: {{getApplicantDeclinesCountByReason(types.unavailableReason.shiftsTooShort)}}</div>
          <div>Declined - Too far away: {{getApplicantDeclinesCountByReason(types.unavailableReason.tooFarAway)}}</div>
          <div>Declined - Hourly rate too low: {{getApplicantDeclinesCountByReason(types.unavailableReason.priceTooLow)}}</div>
          <div>Declined - Other: {{getApplicantDeclinesCountByReason(types.unavailableReason.other)}}</div>
        </div>
        <div>
          <table class="q-table horizontal-separator">
            <thead>
              <tr>
                <th class="text-center">Batch #</th>
                <th class="text-center">Date</th>
                <th class="text-center">Batch Size</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="invitation in job.jobInvitations" :key="invitation.id">
                <td class="text-center">{{ invitation.attemptNumber }}</td>
                <td class="text-center">{{ formatDateTime(invitation.invitationDate) }}</td>
                <td class="text-center">{{ invitation.invitationsCount }}</td>
              </tr>
            </tbody>
          </table>
        </div>
        <div class="bg-red text-center text-white">Admin Mode</div>
      </div>
      <div v-if="applicantDeclinesCount > 0">
        <q-field
          label="Applicant Responses"
          borderless
          readonly
          hide-bottom-space
        >
          <template v-slot:prepend>
            <q-icon name="announcement" />
          </template>
          <template v-slot:control>
            <div class="full-width q-mt-md">
              <div>{{getUndecidedPercentage()}} - Undecided</div>
              <div>{{getDeclinedPercentage()}} - Declined job</div>
              <div v-if="applicantDeclinesCount > 0">
                <div class="q-my-xs text-grey">Reasons for declining job</div>
                <div class="q-ml-sm">
                  <div v-for="(item, index) in declineList" :key="index">
                    {{getDeclinePercentage(item.count)}} - {{getUnavailableReasonText(item.unavailableReason)}}
                  </div>
                </div>
                <!-- <div class="q-ml-sm">
                  <div>Calendar clash: {{getDeclinePercentage(getApplicantDeclinesCountByReason(types.unavailableReason.calendarClash))}}</div>
                  <div>Shifts too short: {{getDeclinePercentage(getApplicantDeclinesCountByReason(types.unavailableReason.shiftsTooShort))}}</div>
                  <div>Too far away: {{getDeclinePercentage(getApplicantDeclinesCountByReason(types.unavailableReason.tooFarAway))}}</div>
                  <div>Hourly rate too low: {{getDeclinePercentage(getApplicantDeclinesCountByReason(types.unavailableReason.priceTooLow))}}</div>
                  <div>Other: {{getDeclinePercentage(getApplicantDeclinesCountByReason(types.unavailableReason.other))}}</div>
                </div> -->
              </div>
            </div>
          </template>
        </q-field>
      </div>
    </div>
</template>

<script>
import {
  getJobManagementStatus,
  formatDateTime,
  getUnavailableReasonText,
} from '../utils/helpers';
import c from '../utils/constants';

export default {
  name: 'JuggleAssistJobAnalysis',
  props: ['job'],
  data() {
    return {
      types: {
        managedBy: c.managedBy,
        jobApplicationStatus: c.jobApplicationStatus,
        unavailableReason: c.unavailableReason,
      },
    };
  },
  computed: {
    managementStatus() {
      return getJobManagementStatus(this.job.managementStatus);
    },
    declineList() {
      return this.job.applicationSummaries.filter((a) => (a.applicationStatus === c.jobApplicationStatus.notInterested || a.applicationStatus === c.jobApplicationStatus.notAvailable));
    },
    shortlistedApplicants() {
      const applicants = this.job.applicants.filter((applicant) => applicant.applicationStatus === c.jobApplicationStatus.applied
        || applicant.applicationStatus === c.jobApplicationStatus.shortlistedBySystem
        || applicant.applicationStatus === c.jobApplicationStatus.shortlistedBySystem);

      return applicants;
    },
    applicantDeclinesCount() {
      const declines = this.job.applicationSummaries.filter((a) => (a.applicationStatus === c.jobApplicationStatus.notInterested || a.applicationStatus === c.jobApplicationStatus.notAvailable));
      let count = 0;
      declines.forEach((s) => {
        count += s.count;
      });

      return count;
    },
    applicantAccessCount() {
      const declines = this.job.applicationSummaries.filter((a) => (a.applicationStatus === c.jobApplicationStatus.viewed || a.applicationStatus === c.jobApplicationStatus.notInterested || a.applicationStatus === c.jobApplicationStatus.notAvailable));
      let count = 0;
      declines.forEach((s) => {
        count += s.count;
      });

      return count;
    },
  },
  methods: {
    formatDateTime(date) {
      return formatDateTime(date);
    },
    getUnavailableReasonText(unavailableReason) {
      return getUnavailableReasonText(unavailableReason);
    },
    getApplicantDeclinesCountByReason(unavailableReason) {
      const summary = this.job.applicationSummaries.find((a) => (a.applicationStatus === c.jobApplicationStatus.notInterested || a.applicationStatus === c.jobApplicationStatus.notAvailable)
        && a.unavailableReason === unavailableReason);

      if (!summary) return 0;

      return summary.count;
    },
    getUndecidedPercentage() {
      const count = this.getApplicantCounts(c.jobApplicationStatus.viewed);
      const perc = Math.round((count / this.applicantAccessCount) * 100);
      return `${perc}%`;
    },
    getDeclinedPercentage() {
      const count = this.applicantDeclinesCount;
      const perc = Math.round((count / this.applicantAccessCount) * 100);
      return `${perc}%`;
    },
    getDeclinePercentage(count) {
      const perc = Math.round((count / this.applicantAccessCount) * 100);
      return `${perc}%`;
    },
    getApplicantCounts(jobApplicationStatus) {
      const summary = this.job.applicationSummaries.find((a) => a.applicationStatus === jobApplicationStatus);

      if (!summary) return 0;

      return summary.count;
    },
  },
};
</script>
