<template>
  <q-field
    :label="isIndividual ? 'How will you pay the helper for this Job?' : 'Payment Method'"
    borderless
    readonly
    hide-bottom-space
    :error="v$.helperPaymentMethod.$error">
      <template v-if="icon" v-slot:prepend>
        <q-icon :name="icon" />
      </template>
      <template v-slot:control>
        <div v-if="isIndividual" class="full-width q-mt-sm">
          <q-option-group
            type="radio"
            v-model="helperPaymentMethod"
            :options="paymentMethods"
          />
          <div v-if="helperPaymentMethod === types.helperPaymentMethod.bankTransfer" class="text-grey q-ml-lg">When you award this job you will be provided with the helper’s bank details.</div>
      </div>
      <div v-else class="full-width q-mt-md">
        Direct payment via bank transfer.
        <br/>
        When you award this job, you will be provided with the candidate's bank details.
      </div>
    </template>
  </q-field>
</template>

<script>
import useVuelidate from '@vuelidate/core';
import c from '../utils/constants';

export default {
  name: 'JobPaymentMethod',
  data() {
    return {
      types: {
        helperPaymentMethod: c.helperPaymentMethod,
      },
    };
  },
  setup() {
    return {
      v$: useVuelidate(),
    };
  },
  validations: {
    helperPaymentMethod: {
      required(value) {
        return value !== c.helperPaymentMethod.unspecified;
      },
    },
  },
  props: ['job', 'storeModuleName', 'hideIcon'],
  methods: {
    valid() {
      return this.helperPaymentMethod !== c.helperPaymentMethod.unspecified;
    },
    validate() {
      this.v$.$touch();

      return !this.v$.$error;
    },
  },
  computed: {
    paymentMethods() {
      const methods = [];

      if (this.isIndividual) {
        methods.push({ label: 'Cash payment', value: c.helperPaymentMethod.cashPayment });
      }

      methods.push({ label: 'Bank transfer', value: c.helperPaymentMethod.bankTransfer });

      return methods;
    },
    icon() {
      if (this.hideIcon) return '';

      return 'monetization_on';
    },
    helperPaymentMethod: {
      get() {
        return this.job.helperPaymentMethod;
      },
      set(value) {
        this.setProperty('helperPaymentMethod', value);
      },
    },
  },
  mounted() {
    if (this.isBusiness) {
      this.helperPaymentMethod = c.helperPaymentMethod.bankTransfer;
    }
  },
};
</script>
