<template>
  <div class="layout-padding">
    <div class="">
      <!-- <q-field
        label="Membership"
        borderless
        readonly
      >
        <template v-slot:prepend>
          <q-icon name="card_membership" />
        </template>
        <template v-slot:control>
          <div class="full-width q-mt-md"> -->
            <helper-membership
              ref="helperMembership"
              v-on:paymentInfoUpdated="handleMembershipSuccess"
              v-on:membershipSuccess="handleMembershipSuccess"
              v-on:paymentFailure="handlePaymentFailure" />
          <!-- </div>
        </template>
      </q-field> -->
    </div>
    <!-- <div class="text-center q-my-sm">
      <q-btn color="secondary" @click="handleContinue">Continue</q-btn>
    </div> -->
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex';
import useVuelidate from '@vuelidate/core';
import HelperMembership from 'components/HelperMembership';
// import NotificationService from '../../../utils/notification-service';
import beforeLeaveStep from '../before-leave-step';
// import * as types from '../../../store/mutation-types';

export default {
  name: 'ProfileWizardHelperMembership',
  components: { HelperMembership },
  data() {
    return {};
  },
  methods: {
    ...mapActions('profileWizard', [
      'getStep',
    ]),
    ...mapActions([
      'updateUserInfo',
      'refreshUserInfo',
    ]),
    validate() {
      if (this.currentStep.meta.canSkip) {
        return true;
      }

      return this.$refs.helperMembership.validate();
    },
    handleMembershipSuccess() {
      // this.$store.commit(`profileWizard/${types.UPDATE_STEP_STATUS}`, { complete: true });
      this.$store.dispatch('profileWizard/getStep', { forward: true }).then((nextStep) => {
        if (nextStep !== null) {
          this.routerReplace({ path: `/helper-profile-wizard/${nextStep.path}` });
          return;
        }

        this.routerReplace('/dashboard');
      });
    },
    handlePaymentSuccess() {

    },
    handlePaymentFailure() {

    },
  },
  computed: {
    ...mapState({
      meta: (state) => state.profileWizard.meta,
      userInfo: (state) => state.userInfo.data,
      currentStep: (state) => {
        const { meta } = state.profileWizard;
        const currentStep = meta.steps[meta.currentStep];
        return currentStep;
      },
      stepComplete: (state) => {
        const { meta } = state.profileWizard;
        const currentStep = meta.steps[meta.currentStep];
        return currentStep.complete;
      },
    }),
    storeModuleName() {
      return 'userInfo';
    },
  },
  setup() {
    return {
      v$: useVuelidate(),
    };
  },
  validations: {
    terms: {
      required() {
        return true;
      },
    },
  },
  beforeRouteLeave(to, from, next) {
    // Update user info before proceeding to the next step if step is complete
    if (this.stepComplete) {
      this.updateUserInfo().then(() => {
        beforeLeaveStep(this, to, from, next);
      });
    } else {
      beforeLeaveStep(this, to, from, next);
    }
  },
};
</script>
