<template>
  <q-page class="post-tutoring-job-step-1">
    <div class="layout-padding">
      <q-field
        borderless
        hide-bottom-space
        readonly
        label="Select type of tutoring"
        :error="v$.jobDeliveryMethod.$error">
          <template v-slot:prepend>
            <q-icon name="supervised_user_circle" />
          </template>
          <template v-slot:control>
            <div class="full-width q-mt-sm">
              <q-option-group
                type="radio"
                v-model="jobDeliveryMethod"
                :options="[
                  { label: 'In-home Tutoring', value: types.jobDeliveryMethod.faceToFace },
                  { label: 'Online Tutoring', value: types.jobDeliveryMethod.online },
                ]"
              />
              <span v-if="jobDeliveryMethod === types.jobDeliveryMethod.online" class="text-grey q-my-sm">
                Juggle Street does not endorse any video or meeting platform. If you have a preference please include in Job Description, ie. prefer lessons to be delivered via Zoom.
              </span>
            </div>
        </template>
      </q-field>

      <q-field
        borderless
        hide-bottom-space
        readonly
        label="Select School Year"
        :error="v$.schoolYear.$error">
        <template v-slot:prepend>
          <q-icon name="class" />
        </template>
        <template v-slot:control>
          <div class="full-width q-mt-sm">
            <q-option-group
              type="radio"
              v-model="schoolYear"
              :options="[
                { label: 'All', value: 'all' },
                { label: 'Select School Year', value: 'specific' },
              ]"
            />
            <div class="row" style="marginLeft: 25px">
              <q-chip
                clickable
                class="col-5"
                :icon="isSchoolYearSelected(item.optionId) ? 'check_circle' : 'radio_button_unchecked'"
                :color="isSchoolYearSelected(item.optionId) ? 'primary' : 'white'"
                :text-color="isSchoolYearSelected(item.optionId) ? 'white' : 'black'"
                v-for="(item, index) in schoolYearsList" :key="item.optionId"
                @click="handleSchoolYearClick(index)"
              >
                {{item.text}}
              </q-chip>
            </div>
          </div>
        </template>
      </q-field>
    </div>
  </q-page>
</template>

<script>
import { mapState } from 'vuex';
import useVuelidate from '@vuelidate/core';
import { beforeRouteEnter, beforeLeaveStep } from './multistep-helper';
import NotificationService from '../../utils/notification-service';
import c from '../../utils/constants';
import * as mutationTypes from '../../store/mutation-types';

export default {
  name: 'PageTutoringJobStep1',
  data() {
    const job = this.$store.state.postTutoringJob.jobDetails.data;
    return {
      schoolYear: job.jobSettings.schoolYears.length === 0 ? '' : 'specific',
      types: {
        jobDeliveryMethod: c.jobDeliveryMethod,
      },
    };
  },
  setup() {
    return {
      v$: useVuelidate(),
    };
  },
  validations: {
    jobDeliveryMethod: {
      required(value) {
        return value !== c.jobDeliveryMethod.unspecified;
      },
    },
    schoolYear: {
      required(value) {
        const validSchoolYear = (value === 'all' || (value === 'specific'
        && this.schoolYears.length > 0));
        return validSchoolYear;
      },
    },
  },
  computed: {
    ...mapState({
      job: (state) => state.postTutoringJob.jobDetails.data,
      userInfo: (state) => state.userInfo.data,
    }),
    storeModuleName() {
      return 'postTutoringJob.jobDetails';
    },
    jobDeliveryMethod: {
      get() {
        return this.job.jobDeliveryMethod;
      },
      set(value) {
        this.setProperty('jobDeliveryMethod', value);
      },
    },
    filterKey() {
      if (this.job.jobDeliveryMethod === c.jobDeliveryMethod.online) {
        return 'postOnlineTutoringJob';
      }

      return 'postHomeTutoringJob';
    },
    schoolYearsList() {
      return this.job.jobType === c.jobType.primarySchoolTutoring ? this.userInfo.client.primarySchoolYears : this.userInfo.client.highSchoolYears;
    },
    schoolYears: {
      get() {
        return this.job.jobSettings.schoolYears;
      },
      set(value) {
        const selectedYears = value;
        this.setProperty('jobSettings.schoolYears', selectedYears);
      },
    },
  },
  methods: {
    handleSchoolYearClick(index) {
      this.schoolYear = 'specific';
      const options = this.schoolYearsList;
      const currentOption = options[index];
      const idx = this.schoolYears.indexOf(currentOption.optionId);
      const years = [...this.schoolYears];

      if (idx !== -1) {
        years.splice(idx, 1);
      } else {
        if (years.length === 2) {
          NotificationService.negative('Only a maximum of 2 school years can be selected.');
          return;
        }

        years.push(currentOption.optionId);
      }
      this.schoolYears = years;
    },
    isSchoolYearSelected(id) {
      return this.schoolYears.indexOf(id) !== -1;
    },
    validate() {
      this.v$.$touch();
      if (this.v$.$error) {
        NotificationService.negative('Please check highlighted fields.');
        return false;
      }

      return true;
    },
  },
  watch: {
    schoolYear(val) {
      if (val === 'all') {
        this.schoolYears = [];
      }
    },
  },
  beforeRouteEnter(to, from, next) {
    beforeRouteEnter(to, from, next);
  },
  beforeRouteLeave(to, from, next) {
    const toStep = parseInt(to.path.split('step/')[1], 10) || 1;
    if (toStep === 2) {
      const filterPayload = {
        filterType: this.filterKey,
        filterKey: 'schoolYears',
        value: this.schoolYears,
      };
      this.$store.commit(`filters/${mutationTypes.UPDATE_FILTER_OPTION}`, filterPayload);

      // filterPayload = {
      //   filterKey: this.filterKey,
      // };
      // this.$store.dispatch('filters/applyFilter', filterPayload);

      this.$store.dispatch('postTutoringJob/resetApplicants');
      this.$store.dispatch('filters/resetFilters');
    }
    beforeLeaveStep(this, to, from, next);
  },
};
</script>
<style lang="sass">
.post-tutoring-job-step-1
  .duration-to-start
    width: 10rem
</style>
