<template>
  <div>
    <div class="q-mx-sm row justify-between" style="align-items: center;">
      <div class="q-ml-md q-mt-sm inline row items-center">
        <favourite-actions v-if="!isGuardianApprovalMode && mode === 'view'" :user="helper" display-mode="icon" defaultClass="no-padding q-mr-xs"/>
        <span>{{helper.firstName}} {{helper.lastInitial}}</span>
        <span class="q-ml-sm" v-if="!helper.hideAgeDisplay">{{helper.ageInYears}} yrs</span>
      </div>
      <div class="q-mr-md q-mt-sm">
        <helper-actions :user="helper" :allowHideAction="false" :allowChat="true" :allowConnect="false" :allowAccept="false"/>
      </div>
    </div>
    <div v-if="isSuperHelper" class="q-mx-sm" style="width: 75%">
      <div class="q-ml-md q-mt-sm inline row items-center">
        <q-icon name="star" class="text-brand-gold q-mr-xs" size="22px" />
        <span>Super Helper</span>
      </div>
      <div class="q-ml-md text-italic">
        <small>{{superHelperText}}</small>
      </div>
    </div>
  </div>
</template>

<script>
import FavouriteActions from 'components/FavouriteActions';
import HelperActions from 'components/HelperActions';
import { formatCurrency } from '../../utils/helpers';

export default {
  name: 'ProfileName',
  components: { FavouriteActions, HelperActions },
  props: ['helper', 'isGuardianApprovalMode', 'mode'],
  data() {
    return {
    };
  },
  computed: {
    isSuperHelper() {
      return this.helper.isSuperProvider;
    },
    superHelperText() {
      if (this.helper.providerMinHourlyRate > 0 && this.helper.visibilityLimit !== 10000) {
        return `${this.helper.firstName} has a minimum hourly rate of ${formatCurrency(this.helper.providerMinHourlyRate)} and works within ${this.helper.visibilityLimit / 1000}km of home.`;
      }

      if (this.helper.providerMinHourlyRate > 0 && this.helper.visibilityLimit === 10000) {
        return `${this.helper.firstName} has a minimum hourly rate of ${formatCurrency(this.helper.providerMinHourlyRate)}.`;
      }

      if (!this.helper.providerMinHourlyRate && this.helper.visibilityLimit !== 10000) {
        return `${this.helper.firstName} works within ${this.helper.visibilityLimit / 1000}km of home.`;
      }

      return '';
    },
  },
  methods: {
  },
};
</script>
