<template>
  <div>
    <q-dialog v-model="opened">
      <q-layout view="Lhh lpR fff" container class="bg-white">
        <q-toolbar>
          <q-toolbar-title>
            {{ index >= 0 ? 'Edit' : 'Add' }}
          </q-toolbar-title>
          <q-btn flat>
            <q-icon name="clear" v-close-popup />
          </q-btn>
        </q-toolbar>

        <q-page-container>
          <div class="layout-padding">
            <q-field
              class="group"
              borderless
              stack-label
              hide-bottom-space
              label="Full name as it appears on WWCC"
            >
              <template v-slot:control>
                <div class="full-width">
                  <q-input
                    class="q-mx-md"
                    v-model="officialFirstName"
                    :error="v$.officialFirstName.$error"
                    placeholder="First Name"
                    hide-bottom-space
                  />
                  <q-input
                    class="q-mx-md"
                    v-model="officialMiddleName"
                    placeholder="Middle Name"
                  />
                  <q-input
                    class="q-mx-md"
                    v-model="officialLastName"
                    :error="v$.officialLastName.$error"
                    placeholder="Last Name"
                  />
                </div>
              </template>
            </q-field>

            <q-field
              label="Number"
              borderless
              stack-label
              hide-bottom-space
              :error="v$.certificate.certificateNumber.$error"
              error-message="required"
            >
              <template v-slot:control>
                <q-input
                  v-model="certificate.certificateNumber"
                  placeholder="WWCC Number"
                />
              </template>
            </q-field>

            <q-field
              label="Expiration Date"
              borderless
              stack-label
              hide-bottom-space
              :error="v$.certificate.expiryDate.$error"
              error-message="required"
            >
              <template v-slot:control>
                <date-picker :autoSelect="true" :show-hint="true" :date="certificateExpiryDate" v-on:change="handleCertificateExpiryChange" :min-date-today="true" />
              </template>
            </q-field>
            <p class="no-margin">
              <strong>Note: </strong> It is your responsibility to have the correct WWCC (paid or volunteer). Check your state guidelines.
            </p>
          </div>
        </q-page-container>

        <q-footer class="bg-white">
          <q-toolbar>
            <q-btn color="accent" @click="handleSave">
              <q-icon name="save"/>
              Save
            </q-btn>
          </q-toolbar>
        </q-footer>
      </q-layout>
    </q-dialog>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import DatePicker from 'components/DatePicker';
import useVuelidate from '@vuelidate/core';
import { required } from '@vuelidate/validators';
import NotificationService from '../utils/notification-service';
import c from '../utils/constants';

export default {
  name: 'AddCertificateModal',
  components: { DatePicker },
  emits: ['addCertificateSuccess'],
  data() {
    return {
      certificate: {},
      index: -1,
      opened: false,
    };
  },
  methods: {
    open(certificate, index) {
      this.v$.$reset();

      this.certificate = certificate;
      this.index = index;

      this.opened = true;
    },
    handleCertificateExpiryChange(value) {
      this.certificateExpiryDate = value.date;
    },
    handleSave() {
      this.v$.$touch();
      if (this.v$.$error) {
        NotificationService.negative('Please review highlighted fields.');
        return;
      }

      if (!this.certificate.certificateStatus) {
        this.certificate.certificateStatus = c.certificateStatus.pendingVerification;
      }
      if (!this.certificate.certificateTypeId) {
        this.certificate.certificateTypeId = 1;
      }
      if (!this.certificate.category) {
        this.certificate.category = 1;
      }

      this.$emit('addCertificateSuccess', {
        certificate: this.certificate,
        index: this.index,
      });
      this.opened = false;
    },
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
    }),
    storeModuleName() {
      return 'userInfo';
    },
    certificateExpiryDate: {
      get() {
        // if (!this.certificate.expiryDate) return null;

        // const expiryDate = date.formatDate(this.certificate.expiryDate, 'DD/MM/YYYY');
        // return expiryDate;
        return this.certificate.expiryDate;
      },
      set(value) {
        // this.certificate.expiryDate = date.extractDate(value, 'DD/MM/YYYY');
        this.certificate.expiryDate = value;
      },
    },
    officialFirstName: {
      get() {
        return this.userInfo.officialFirstName;
      },
      set(value) {
        this.setProperty('officialFirstName', value);
      },
    },
    officialMiddleName: {
      get() {
        return this.userInfo.officialMiddleName;
      },
      set(value) {
        this.setProperty('officialMiddleName', value);
      },
    },
    officialLastName: {
      get() {
        return this.userInfo.officialLastName;
      },
      set(value) {
        this.setProperty('officialLastName', value);
      },
    },
  },
  setup() {
    return {
      v$: useVuelidate(),
    };
  },
  validations: {
    officialFirstName: {
      required,
    },
    officialLastName: {
      required,
    },
    certificate: {
      certificateNumber: {
        required,
      },
      expiryDate: {
        required,
      },
    },
  },
};
</script>
