<template>
  <div :class="welcomeClass">
    <div class="welcome-message">
      <div v-if="isIndividual">
        <p class="text-dark text-title">Welcome to Juggle Street, {{userInfo.firstName}}!</p>
        <p class="text-dark text-subtitle2">
          Before you can chat online with your neighbours and post jobs, you need to <span  class="bold">complete your family profile</span>.
        </p>
      </div>
      <div v-if="isBusiness">
        <p class="text-dark text-title">Welcome to Juggle Street, {{userInfo.tradingName}}!</p>
        <p class="text-dark text-subtitle2">
          Before you can post jobs, you need to <span  class="bold">complete your business profile</span>.
        </p>
      </div>
      <div class="start-now-button">
        <q-btn color="secondary" outline class="q-mx-sm" @click="handleLater">Later</q-btn>
        <q-btn color="accent" @click="handleStart">Complete</q-btn>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import beforeLeaveStep from '../before-leave-step';

export default {
  name: 'ProfileWizardFamilyWelcomStep',
  data() {
    return {
    };
  },
  methods: {
    handleLater() {
      this.routerReplace('/dashboard');
    },
    handleStart() {
      this.$store.dispatch('profileWizard/getStep', { forward: true }).then((nextStep) => {
        this.routerReplace({ path: `/client-profile-wizard/${nextStep.path}` });
      });
    },
  },
  computed: {
    ...mapState({
      meta: (state) => state.profileWizard.meta,
      userInfo: (state) => state.userInfo.data,
    }),
    submitConversion() {
      return this.$route.query.success;
    },
    welcomeClass() {
      return `profile-wizard-welcome-screen ${this.isIndividual ? 'profile-wizard-family-welcome-screen' : 'profile-wizard-client-welcome-screen'} fullscreen relative-position`;
    },
  },
  beforeRouteLeave(to, from, next) {
    beforeLeaveStep(this, to, from, next);
  },
};
</script>
<style lang="sass">
.profile-wizard-family-welcome-screen
  background: url('/images/welcome-screen-background.jpg') no-repeat
  background-size: cover
.profile-wizard-client-welcome-screen
  background: url('/images/welcome-screen-background-pro.jpg') no-repeat
  background-size: cover
.profile-wizard-welcome-screen
  .welcome-message
    text-align: center
    position: absolute
    top: 6%
    left: 0
    width: 100vw
    background: rgb(238,238,238)
    opacity: 0.9
    padding: 1rem 1rem
    .title
      font-size: 1.2rem
</style>
