import { FirebaseAnalytics } from '@capacitor-community/firebase-analytics';
import { env } from './env';
import { getAmountForPaymentType, getPaymentTypeText } from './helpers';
import FacebookPixelManager from './facebook-pixel-manager';
import errorHandler from './error-handler';

const ignoreAnalyticsErrors = true;

export default {
  logPageView(path, name) {
    try {
      if (env.deviceInfo.platform === 'web') {
        const gaData = {
          hitType: 'pageview',
          page: path,
          title: name,
        };
        window.ga('send', gaData);
      } else {
        const gaData = {
          appName: 'Juggle St',
          screenName: name,
          page: path,
        };
        window.ga('send', 'screenview', gaData);

        FacebookPixelManager.trackViewEvent();

        if (FirebaseAnalytics) {
          FirebaseAnalytics.logEvent({
            name: 'screen_view',
            params: {
              screen_name: path,
            },
          });
        }
      }
    } catch (error) {
      if (ignoreAnalyticsErrors) return;

      errorHandler.captureException(error);
    }
  },
  setUser(userInfo) {
    try {
      const userId = (userInfo ? userInfo.id : '');

      if (window.ga) {
        window.ga('set', 'userId', userId);
      }

      // TODO: Function doesn't work and crashes with error 'userId property is missing'
      // even though the value is set.
      // if (env.deviceInfo.platform !== 'web' && FirebaseAnalytics) {
      //   FirebaseAnalytics.setUserId({
      //     userId,
      //   });
      // }
    } catch (error) {
      if (ignoreAnalyticsErrors) return;

      errorHandler.captureException(error);
    }
  },
  logAppDownload() {
    try {
      // Occurs automatically in Firebase.
      const gaData = {
        hitType: 'event',
        eventCategory: 'App Interactions',
        eventAction: 'firstOpen',
        eventLabel: env.deviceInfo.operatingSystem === 'android' ? 'Android' : 'iOS',
      };

      window.ga('send', gaData);
    } catch (error) {
      if (ignoreAnalyticsErrors) return;

      errorHandler.captureException(error);
    }
  },
  logLogin() {
    try {
      const gaData = {
        hitType: 'event',
        eventCategory: 'User Interactions',
        eventAction: 'login',
        eventLabel: env.deviceInfo.operatingSystem === 'android' ? 'Android' : 'iOS',
      };

      window.ga('send', gaData);

      if (env.deviceInfo.platform !== 'web' && FirebaseAnalytics) {
        FirebaseAnalytics.logEvent({
          name: 'login',
        });
      }
    } catch (error) {
      if (ignoreAnalyticsErrors) return;

      errorHandler.captureException(error);
    }
  },
  logConversion(isClient) {
    try {
      const type = isClient ? 'newFamilySignup' : 'newHelperSignup';

      const gaData = {
        hitType: 'event',
        eventCategory: 'User Actions',
        eventAction: type,
        eventLabel: isClient ? 'New Family Signup' : 'New Helper Signup',
      };

      window.ga('send', gaData);

      if (env.deviceInfo.platform !== 'web' && FirebaseAnalytics) {
        FirebaseAnalytics.logEvent({
          name: 'sign_up',
          params: {
            type,
          },
        });
      }
    } catch (error) {
      if (ignoreAnalyticsErrors) return;

      errorHandler.captureException(error);
    }
  },
  logTransaction(userInfo, paymentType) {
    try {
      if (env.isTest) {
        console.log('Test Mode - Skip Tracking');
        return;
      }

      const trnId = `${userInfo.id}-${paymentType}-${new Date().toJSON()}`;
      const currency = userInfo.country === 'au' ? 'AUD' : 'NZD';
      const total = getAmountForPaymentType(paymentType);
      const name = getPaymentTypeText(paymentType);

      const gaTrn = {
        id: trnId,
        revenue: total,
        currency,
      };

      const gaItem = {
        id: trnId,
        sku: paymentType,
        name,
        price: total,
        quantity: 1,
        currency,
      };

      window.ga('ecommerce:addTransaction', gaTrn);
      window.ga('ecommerce:addItem', gaItem);
      window.ga('ecommerce:send');

      FacebookPixelManager.trackPurchase(total, currency);

      if (env.deviceInfo.platform !== 'web' && FirebaseAnalytics) {
        const fsTrn = {
          transaction_id: trnId,
          value: total,
          currency,
        };

        FirebaseAnalytics.logEvent({
          name: 'purchase',
          params: fsTrn,
        });
      }
    } catch (error) {
      if (ignoreAnalyticsErrors) return;

      errorHandler.captureException(error);
    }
  },
};
