<template>
  <div>
    <div>
      <q-field
        readonly
        borderless
        label="Working With Children Check">
        <!-- <template v-slot:prepend>
          <q-icon name="verified_user" />
        </template> -->
        <template v-slot:control>
          <div class="full-width q-mt-lg">
            <q-btn @click="handleAddCertificate" color="accent" icon="add" v-if="canAddWWC">
              Add
            </q-btn>
            <p class="q-my-md" v-if="showNotice">
              <span class="text-red text-bold text-italic font-size-sm">{{updateNotice}}</span> - All helpers 18 years and older are required to have a WWCC to use Juggle Street.
            </p>
          </div>
        </template>
      </q-field>
      <q-card v-if="!canAddWWC" class="q-mx-md">
        <q-card-section>
          <q-field label="Number" stack-label borderless readonly hide-bottom-space>
            {{wwc.certificateNumber}}
          </q-field>
          <q-field label="Expiry Date" stack-label borderless readonly hide-bottom-space>
            {{formatDate(wwc.expiryDate)}}
          </q-field>
          <q-field label="Status" stack-label borderless readonly hide-bottom-space>
            {{getStatusText(wwc.certificateStatus)}}
          </q-field>
        </q-card-section>
        <q-card-actions>
          <q-btn
            v-if="canEditCertificate"
            color="accent"
            @click="handleEditCertificate(wwc)"
            icon="edit"
            small
          >
            Edit
          </q-btn>
          <q-btn
            v-if="canDeleteCertificate"
            color="secondary"
            outline
            @click="handleDeleteCertificate"
            icon="close"
            small
          >
            Delete
          </q-btn>
        </q-card-actions>
      </q-card>
    </div>
    <add-certificate-modal
      ref="addCertificateModal"
      v-on:addCertificateSuccess="handleAddCertificateSuccess"
    />
  </div>
</template>

<script>
import { mapState } from 'vuex';
import { date } from 'quasar';
import AddCertificateModal from 'components/AddCertificateModal';
import c from '../utils/constants';

export default {
  name: 'WwccManager',
  emits: ['complete'],
  components: { AddCertificateModal },
  data() {
    return {
    };
  },
  methods: {
    handleAddCertificateSuccess({ certificate }) {
      this.setProperty('certificates', [certificate]);
      this.$emit('complete');
    },
    handleEditCertificate(certificate) {
      this.$refs.addCertificateModal.open({ ...certificate }, 0);
    },
    handleAddCertificate() {
      this.$refs.addCertificateModal.open({
        expiryDate: null,
      }, -1);
    },
    getStatusText(status) {
      switch (status) {
        case c.certificateStatus.active:
          return 'Verified';
        case c.certificateStatus.pendingVerification:
          return 'Verification Pending';
        case c.certificateStatus.invalid:
          return 'Invalid';
        case c.certificateStatus.expired:
          return 'Expired';
        default:
          throw new Error(`Unsupported option: ${status}`);
      }
    },
    formatDate(d) {
      return date.formatDate(d, 'DD/MM/YYYY');
    },
    handleDeleteCertificate() {
      this.$q.dialog({
        title: 'Confirm',
        message: 'Are you sure you want to delete this certificate?',
        cancel: {
          label: 'No',
          color: 'secondary',
          outline: true,
        },
        ok: {
          label: 'Yes',
          outline: true,
        },
      }).onOk(() => {
        this.setProperty('certificates', []);
      }).onCancel(() => {});
    },
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
      canAddWWC: (state) => {
        const { certificates } = state.userInfo.data;
        return certificates && certificates.length === 0;
      },
      wwc: (state) => {
        const { certificates } = state.userInfo.data;
        return certificates && certificates.length > 0
          ? certificates[0]
          : {};
      },
      canEditCertificate() {
        // return this.wwc && this.wwc.certificateStatus && this.wwc.certificateStatus !== c.certificateStatus.active;
        return true;
      },
      canDeleteCertificate() {
        return this.wwc && this.wwc.certificateStatus && this.wwc.certificateStatus !== c.certificateStatus.active;
      },
      updateNotice() {
        return this.isApproved ? 'UPDATE REQUIRED' : 'NOTE';
      },
    }),
    storeModuleName() {
      return 'userInfo';
    },
    showNotice() {
      return this.canAddWWC;
    },
  },
};
</script>
