import { Dialog } from 'quasar';
import store from '../../store';
import * as types from '../../store/mutation-types';
import c from '../../utils/constants';

export const beforeRouteEnter = (to, from, next, cb) => {
  if (from.path.indexOf('helper-profile') !== -1) {
    return next(cb);
  }

  const jobDetails = store.getters['postTutoringJob/jobDetails'];
  if (!jobDetails) {
    return next({ path: '/post-job', query: { reset: 1 }, replace: true });
  }

  const fromStep = parseInt(from.path.split('step/')[1], 10) || 1;
  const toStep = parseInt(to.path.split('step/')[1], 10) || 1;
  let skip = false;

  // Candidate selection step.
  if (toStep === 7 && jobDetails.managedBy === c.managedBy.user) {
    skip = true;
  }

  // Select helpers step.
  if (toStep === 8 && jobDetails.managedBy === c.managedBy.system) {
    skip = true;
  }

  // Payment Step.
  if (toStep === 9) {
    // Payment Not Required.
    const paymentRequired = store.getters['postTutoringJob/isPaymentRequired'];
    if (!paymentRequired) {
      skip = true;
    }
  }

  if (skip) {
    const movingForward = toStep > fromStep;
    const nextStep = movingForward ? toStep + 1 : toStep - 1;
    return next({ path: `/post-tutoring-job/step/${nextStep}` });
  }

  return next(cb);
};

export const beforeLeaveStep = (context, to, from, next) => {
  if (to.path.indexOf('helper-profile') !== -1) {
    next();
    return;
  }

  const toStep = parseInt(to.path.split('step/')[1], 10) || 1;
  const currentStep = context.$store.getters['postTutoringJob/currentStep'];

  // Trigger validation only when going to next step.
  if (toStep > currentStep && !context.validate()) {
    next(false);
    return;
  }

  store.commit(`postTutoringJob/${types.UPDATE_STEP}`, { toStep });

  next();
};

export const postJob = (router) => {
  const jobDetails = store.getters['postTutoringJob/jobDetails'];

  let message;
  if (jobDetails.managedBy === c.managedBy.user) {
    message = 'This tutoring job post will be active for 30 days, during this time you will be able to chat online with invited tutors. If you don’t find the right person right away you can invite more tutors to apply, change the job details and repost the job. Remember, when a tutor applies for your job they will be waiting to hear from you!';
  } else {
    message = 'The Juggle Assist algorithm will now invite tutor who meet your criteria and invite them to your job. You will be notified each time a carer applies for this job – these applicants will appear in your job shortlist';
  }

  Dialog.create({
    title: 'Important',
    message,
    cancel: {
      label: 'Cancel',
      color: 'secondary',
      outline: true,
    },
    ok: {
      label: 'Ok, Got It',
      color: 'primary',
      outline: true,
    },
  }).onOk(() => {
    store.dispatch('postTutoringJob/postJob', router).catch(() => {
      const paymentRequired = store.getters['postTutoringJob/isPaymentRequired'];
      if (paymentRequired) {
        router.replace({ path: `/post-tutoring-job/step/${store.getters['postTutoringJob/currentStep'] - 1}` });
      }
    });
  }).onCancel(() => {});
};
