<template>
  <q-page class="layout-padding">
    <q-field borderless stack-label readonly
      label="First Name *">
        <template v-slot:control>
          <div class="self-center full-width no-outline" tabindex="0">{{userInfo.firstName}}</div>
        </template>
    </q-field>
    <q-field borderless stack-label readonly
      label="Last Name *">
        <template v-slot:control>
          <div class="self-center full-width no-outline" tabindex="0">{{userInfo.lastName}}</div>
        </template>
    </q-field>
    <q-field borderless stack-label readonly
      label="Phone Number">
        <template v-slot:control>
          <div class="self-center full-width no-outline" tabindex="0">
            {{userInfo.phoneNumber}}
            <a @click="handlePhoneNumberChange" class="q-ml-md button-link text-primary">Change Number</a>
          </div>
        </template>
    </q-field>
    <q-field borderless stack-label readonly
      label="Email">
      <template v-slot:control>
        <div class="self-center full-width no-outline" tabindex="0">
          <q-input style="width: 200px; display: inline-block" v-model="email" float-label="Email" placeholder="Email address"/>
            <!-- <q-btn class="q-mx-sm"  @click="handleChangeEmail" color="accent">
              Change
            </q-btn> -->
        </div>
      </template>
    </q-field>

    <q-field v-if="isHelper"
      stack-label
      borderless
      bottom-slots
      label="Introduce Yourself"
      :helper="aboutMeHelper"
      :error="v$.aboutMe.$error"
      class="q-mx-sm"
    >
      <template v-slot:control>
        <div class="full-width self-center no-outline" tabindex="0">
          <p>
            Tell your neighbours a bit about yourself, but leave out your name and age as this will already be shown. This introduction will be displayed on your profile card.
          </p>

          <q-input
            v-model="aboutMe"
            type="textarea"
            placeholder="EXAMPLE -  Hi mums and dad’s, I grew up in a big family with 2 younger brothers and a younger sister, so I have many years of experience looking after children! I know lots of people in the neighbourhood and look forward to meeting you."
            rows="6"
          />
        </div>
      </template>
      <template v-slot:hint>
        {{aboutMeHelper}}
      </template>
      <template v-slot:error>
        {{aboutMeHelper}}
      </template>
    </q-field>

    <q-field label="Profile Photo" v-if="isHelper"
      stack-label borderless>
      <template v-slot:control>
        <div class="self-center full-width no-outline" tabindex="0">
          <img :src="userInfo.defaultImage.scale1ImageUrl" class="q-my-sm q-mx-sm" style="height: 150px"/>
          <br/>
          <q-btn @click="handleSelectPhoto" color="accent" size="sm">
            Select Photo
          </q-btn>
          <image-uploader
            ref="image-uploader"
            @uploadSuccess="handleUploadSuccess"
            @uploadFailure="handleUploadFailure"
          />
          <q-btn class="q-mx-md" @click="handleCropPhoto" color="accent" size="sm">
            Crop
          </q-btn>
        </div>
      </template>
    </q-field>

    <q-field v-if="isHelper"
      stack-label
      borderless
      label="Date of Birth *">
      <template v-slot:control>
        <label>{{dateOfBirth}}</label>
      </template>
    </q-field>

    <q-field label="Gender" stack-label borderless>
      <template v-slot:control>
        <q-option-group
          type="radio"
          v-model="gender"
          :options="[
            { label: 'Male', value:  1},
            { label: 'Female', value: 2 },
          ]"
        />
      </template>
    </q-field>

    <!-- <q-field label="Primary Address" stack-label borderless>
      <template v-slot:control>
        <div class="self-center full-width no-outline" tabindex="0">
          <q-input v-model="addressLine1" float-label="Address Line 1" placeholder="Address Line 1" name="address1" autocomplete="billing address-line1" autocapitalize="off"  autocorrect="off" spellcheck="false" />
          <q-input v-model="addressLine2" float-label="Address Line 2" placeholder="Address Line 2" name="address2" autocomplete="billing address-line2" autocapitalize="off"  autocorrect="off" spellcheck="false" />
          <q-input v-model="suburb" float-label="Suburb" placeholder="Suburb" name="city" autocomplete="billing address-level2" autocapitalize="off"  autocorrect="off" spellcheck="false" />

          <q-select v-if="country === 'au'"
            float-label="State"
            v-model="state"
            :options="states"
            name="state"
            autocomplete="billing address-level1"
            option-value="value"
            option-label="label"
            emit-value
            map-options
            use-input
            hide-selected
            fill-input
          />
          <q-input v-if="country === 'nz'" v-model="state" float-label="Region/City" placeholder="Region/City" autocomplete="billing address-level1" />

          <q-input v-model="postCode" float-label="Post Code" placeholder="Post Code" name="postal" autocomplete="billing postal-code" autocorrect="off" spellcheck="false"/>
        </div>
      </template>
    </q-field> -->
    <small>* This field is not editable. Contact Juggle Street to update.</small>
  </q-page>
</template>

<script>
import { mapState } from 'vuex';
import useVuelidate from '@vuelidate/core';
import { date } from 'quasar';
import ImageUploader from 'components/ImageUploader';
import c from '../../utils/constants';
import NotificationService from '../../utils/notification-service';

export default {
  name: 'PageAccountAboutMe',
  components: { ImageUploader },
  data() {
    return {
      states: c.states,
    };
  },
  methods: {
    handlePhoneNumberChange() {
      this.routerReplace('/account/modal/update-phone');
    },
    handleChangeEmail() {
      this.routerReplace('/dashboard');
    },
    handleSelectPhoto() {
      this.$refs['image-uploader'].open();
    },
    handleCropPhoto() {
      this.routerPush('/account/modal/crop-image');
    },
    handleUploadSuccess() {
      this.routerPush('/account/modal/crop-image');
    },
    handleUploadFailure() {
      NotificationService.negative('An error occurred. Please try again later.');
    },
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
    }),
    storeModuleName() {
      return 'userInfo';
    },
    aboutMe: {
      get() {
        return this.getProperty('aboutMe');
      },
      set(value) {
        this.setProperty('aboutMe', value);
      },
    },
    dateOfBirth() {
      return date.formatDate(this.userInfo.dateOfBirth, 'DD/MM/YYYY');
    },
    email: {
      get() {
        return this.userInfo.email;
      },
      set(value) {
        this.setProperty('email', value);
      },
    },
    gender: {
      get() {
        return this.userInfo.gender;
      },
      set(value) {
        this.setProperty('gender', value);
      },
    },
    addressLine1: {
      get() {
        return this.userInfo.defaultAddress.addressLine1;
      },
      set(value) {
        this.setProperty('defaultAddress.addressLine1', value);
      },
    },
    addressLine2: {
      get() {
        return this.userInfo.defaultAddress.addressLine2;
      },
      set(value) {
        this.setProperty('defaultAddress.addressLine2', value);
      },
    },
    suburb: {
      get() {
        return this.userInfo.defaultAddress.suburb;
      },
      set(value) {
        this.setProperty('defaultAddress.suburb', value);
      },
    },
    postCode: {
      get() {
        return this.userInfo.defaultAddress.postCode;
      },
      set(value) {
        this.setProperty('defaultAddress.postCode', value);
      },
    },
    aboutMeHelper() {
      if (this.v$.aboutMe.$error && this.aboutMe) {
        return `required - ${100 - this.aboutMe.length} characters left`;
      }

      return 'required - min 100 characters';
    },
    state: {
      get() {
        return this.userInfo.defaultAddress.state;
      },
      set(value) {
        this.setProperty('defaultAddress.state', value);
      },
    },
  },
  setup() {
    return {
      v$: useVuelidate(),
    };
  },
  validations: {
    aboutMe: {
      required(value) {
        if (this.isFamily) return true;

        return value !== null && value.length >= 100;
      },
    },
  },
  beforeRouteLeave(to, from, next) {
    this.v$.$touch();
    if (this.v$.$error) {
      NotificationService.negative('Please fix all highlighted errors.');
      next(false);
      return;
    }

    this.$store.dispatch('updateUserInfo').then(() => {
      next();
    }).catch((error) => {
      NotificationService.negative(error.message);
      next(false);
    });
  },
};
</script>
