<template>
  <q-card
    inline
    class="client-profile-card">
    <q-card-section v-if="isIndividualProfile" @click="handleClick" style="padding: 4px 16px; height: 28px">
      <div class="text-subtitle1 ellipsis" style="max-width: 135px">
        <div class="">{{client.publicName}}</div>
      </div>
    </q-card-section>
    <q-card-section v-else @click="handleClick" style="padding: 4px 0; height: 28px">
      <div class="text-subtitle1 ellipsis" style="max-width: 135px">
        <div class="q-px-md box-primary" style="width: 80px">Business</div>
      </div>
    </q-card-section>
    <q-card-section align="center" @click="handleClick" style="padding: 8px 16px;">
      <div class="img-container row justify-center">
        <img :src="getImageUrl()"/>
      </div>
    </q-card-section>
    <q-card-section style="padding: 0 16px">
      <div :style="featuresStyle">
        <div v-if="isBusinessProfile" class="ellipsis">
          <q-icon name="business" size="18px" style="vertical-align: middle" class="text-secondary-lighter q-mr-xs" /> {{client.publicName}}
        </div>
        <div class="ellipsis">
          <q-icon name="location_pin" size="18px" style="vertical-align: middle" class="text-secondary-lighter" /> {{client.suburb}} - {{client.distanceInKm}}km
        </div>
        <div v-if="client.jobsCompleted > 0">
          <q-icon name="receipt_long" size="18px" style="vertical-align: middle" class="q-mr-xs text-secondary-lighter" />
          <span>{{client.jobsCompleted}}</span> Job{{client.jobsCompleted == 1 ? "" : "s"}}
          <span style="marginLeft: 5px" v-if="client.ratingsCount > 0"><span>{{client.ratingsCount}}</span> Rating{{client.ratingsCount == 1 ? "" : "s"}} </span>
        </div>
        <!-- <div v-if="isConnected">
          <q-icon name="link" size="18px" style="vertical-align: middle" class="text-secondary-lighter" /> Connected
        </div> -->
        <!-- <div v-if="client.lastSeenInDays <= 28 || client.jobsCompleted >= 5">
          <q-icon name="history" size="18px" style="vertical-align: middle" class="text-secondary-lighter" /> {{getLastSeenText(client.lastSeenInDays)}}
        </div> -->
      </div>
    </q-card-section>
    <q-card-actions align="center" v-if="this.mode === 'view'" style="padding: 0px 8px;">
      <client-actions v-if="isIndividualProfile" :user="client" />
      <hide-user-action :user="client" display-mode="icon"/>
    </q-card-actions>
  </q-card>
</template>

<script>
import ClientActions from 'components/ClientActions';
import HideUserAction from 'components/HideUserAction';
import { env } from '../utils/env';
import c from '../utils/constants';
import { getLastSeenNoteFromNumOfDays } from '../utils/helpers';

export default {
  name: 'ClientProfileCard',
  components: { ClientActions, HideUserAction },
  data() {
    return {

    };
  },
  props: ['client', 'mode', 'click'],
  computed: {
    featuresStyle() {
      // if (this.isConnected) return 'height: 90px; overflow: hidden';

      return 'height: 45px; overflow: hidden';
    },
    isConnected() {
      return this.client.friendStatus === c.friendStatus.approved
        || (this.client.friendStatus === c.friendStatus.pending && !this.client.actionRequired);
    },
    isIndividualProfile() {
      return this.client.accountSubtype === c.clientType.individual;
    },
    isBusinessProfile() {
      return this.client.accountSubtype === c.clientType.business;
    },
  },
  methods: {
    getImageUrl() {
      return `${env.demoMode ? '/statics/images/kelly.png' : env.storageUrl}/images/${this.client.imageSrc}`;
    },
    handleClick() {
      if (this.mode === c.clientProfileCardMode.readOnly) {
        return;
      }

      if (this.mode === c.clientProfileCardMode.view) {
        this.routerPush(`/account/client-profile/${this.client.id}`);
      } else if (this.click) {
        this.click(this.helper);
      }
    },
    getLastSeenText(days) {
      return getLastSeenNoteFromNumOfDays(days);
    },
  },
};
</script>

<style lang="sass">
.client-profile-card
  position: relative
  display: inline-block
  height: 280px
  .q-card-subtitle
    .q-rating .q-icon
      font-size: 1rem
  margin: 0
  width: 50%
  border: 1px solid white
  .img-container
    img
      height: 150px
      max-width: 150px

</style>
