import * as http from '../../api/http';
import * as types from '../mutation-types';
import NotificationService from '../../utils/notification-service';
import loader from '../../utils/loader';
import c from '../../utils/constants';
import { getHelperType } from '../../utils/helpers';
import appRatingManager from '../../utils/app-rating-manager';
import { env } from '../../utils/env';

export default {
  namespaced: true,
  state: {
    jobs: {
      data: [],
      meta: {
        isFetching: false,
        lastFetched: null,
        dirty: false,
      },
    },
    unratedJobs: {
      data: [],
      meta: {
        isFetching: false,
        lastFetched: null,
        dirty: false,
      },
    },
    upcomingJobs: {
      data: [],
      meta: {
        isFetching: false,
        lastFetched: null,
        dirty: false,
      },
    },
    archivedJobs: {
      data: [],
      meta: {
        isFetching: false,
        lastFetched: null,
        dirty: false,
      },
    },
    pages: {
      rateHelper: {},
      rateHelperTemplate: {
        data: {
          jobId: undefined,
          ratingFor: undefined,
          ratingBy: undefined,
          category1: 0,
          category2: 0,
          category3: 0,
          category4: 0,
          feedback: '',
        },
        meta: {
          job: {},
          isFetching: false,
          lastFetched: null,
          dirty: false,
        },
      },
      jobDetails: {},
      jobDetailsTemplate: {
        data: {
        },
        meta: {
          isFetching: false,
          lastFetched: null,
          dirty: false,
        },
      },
    },
  },
  actions: {
    fetchJobs: ({ commit }) => {
      commit(types.JOBS_REQUEST);
      return http
        .get('jobs/client')
        .then((response) => {
          commit(types.JOBS_SUCCESS, response.data);
          return response;
        })
        .catch((error) => {
          commit(types.JOBS_FAILURE);
          return Promise.reject(error);
        });
    },
    fetchUpcomingJobs: ({ commit }) => {
      loader.show();
      commit(types.UPCOMING_JOBS_REQUEST);
      return http
        .get('jobs/client/upcoming')
        .then((response) => {
          commit(types.UPCOMING_JOBS_SUCCESS, response.data);
          loader.hide();
          return response;
        })
        .catch((error) => {
          commit(types.UPCOMING_JOBS_FAILURE);
          loader.hide();
          return Promise.reject(error);
        });
    },
    fetchArchivedJobs: ({ commit }) => {
      loader.show();
      commit(types.ARCHIVED_JOBS_REQUEST);
      return http
        .get('jobs/client/completed')
        .then((response) => {
          commit(types.ARCHIVED_JOBS_SUCCESS, response.data);
          loader.hide();
          return response;
        })
        .catch((error) => {
          commit(types.ARCHIVED_JOBS_FAILURE);
          loader.hide();
          return Promise.reject(error);
        });
    },
    fetchUnratedJobs: ({ commit }) => {
      commit(types.UNRATED_JOBS_REQUEST);
      return http
        .get('jobs/client/unratedjobs')
        .then((response) => {
          commit(types.UNRATED_JOBS_SUCCESS, response.data);
          return response;
        })
        .catch((error) => {
          commit(types.UNRATED_JOBS_FAILURE);
          return Promise.reject(error);
        });
    },
    fetchJobForRating: async ({ dispatch, commit }, payload) => {
      loader.show();
      dispatch('resetStoreStructure', { propPath: 'families.pages.rateHelper' }, { root: true });

      commit(types.JOB_FOR_RATING_REQUEST);
      const ratingMeta = {
        ratingFor: payload.ratingFor,
        ratingMode: payload.ratingMode,
      };

      return http
        .get(`jobs/client/unratedjob/${payload.id}/${payload.ratingFor}`)
        .then((response) => {
          commit(types.JOB_FOR_RATING_SUCCESS, { job: response.data, meta: ratingMeta });
          loader.hide();
          return response;
        })
        .catch((error) => {
          commit(types.JOB_FOR_RATING_FAILURE);
          loader.hide();
          return Promise.reject(error);
        });
    },
    postHelperRating({ state }, router) {
      loader.show();

      const rating = state.pages.rateHelper.data;
      const ratingSum = rating.category1 + rating.category2 + rating.category3 + rating.category4;

      return http
        .post(`jobs/provider/${rating.jobId}/rating`, rating)
        .then(async () => {
          loader.hide();
          if (env.deviceInfo.platform !== 'web' && ratingSum === 20) {
            appRatingManager.promptForRating();
          }

          router.replace('/families/all-jobs');
        })
        .catch((error) => {
          loader.hide();
          NotificationService.negative(error.message);

          return Promise.reject(error);
        });
    },
    fetchJobDetails({ commit }, payload) {
      loader.show();
      commit(types.JOB_DETAILS_REQUEST);
      return http
        .get(`jobs/client/${payload.id}`)
        .then((response) => {
          commit(types.JOB_DETAILS_SUCCESS, response.data);
          loader.hide();
          return response;
        })
        .catch((error) => {
          commit(types.JOB_DETAILS_FAILURE);
          loader.hide();
          return Promise.reject(error);
        });
    },
    updateJobDetails({ commit, state }) {
      loader.show();
      const payload = state.pages.jobDetails.data;
      return http
        .patch(`jobs/client/${payload.id}`, payload)
        .then((response) => {
          commit(types.JOB_DETAILS_SUCCESS, response.data);
          loader.hide();
          return response;
        })
        .catch((error) => {
          loader.hide();
          return Promise.reject(error);
        });
    },
    // awardRecurringJob({ state, dispatch, commit }, { awardedApplicantIds }) {
    //   loader.show();
    //   let request = state.pages.jobDetails.data;
    //   request.awardedApplicantIds = awardedApplicantIds;

    //   return http
    //     .patch(`jobs/client/${request.id}`, request)
    //     .then(response => {
    //       commit(types.JOB_DETAILS_SUCCESS, response.data);
    //       loader.hide();
    //       return response;
    //     })
    //     .catch(error => {
    //       loader.hide();
    //       return Promise.reject(error);
    //     });
    // },
    toggleApplicant({ commit, dispatch }, helper) {
      commit(types.TOGGLE_APPLICANT, helper);
      dispatch('setPageTitle');
    },
    setPageTitle({ commit, state }) {
      const { newApplicants, jobType } = state.pages.jobDetails.data;
      // const moduleName = getJobModuleName(jobType);
      const helperType = getHelperType(jobType);

      const title = newApplicants.length > 0 ? `Selected ${newApplicants.length} Candidate${newApplicants.length > 1 ? 's' : ''}` : `Invite More ${helperType}s`;
      const subtitle = '';

      commit(types.SET_PAGE_INFO, { title, subtitle }, { root: true });
    },
  },
  mutations: {
    [types.JOBS_REQUEST]: (state) => {
      state.jobs.meta.isFetching = true;
    },
    [types.JOBS_SUCCESS]: (state, payload) => {
      state.jobs.data = payload;
      state.jobs.meta.isFetching = false;
      state.jobs.meta.lastFetched = new Date();
    },
    [types.JOBS_FAILURE]: (state) => {
      if (state.jobs) {
        state.jobs.data = undefined;
        state.jobs.meta.isFetching = false;
        state.jobs.meta.lastFetched = new Date();
      }
    },
    [types.UPCOMING_JOBS_REQUEST]: (state) => {
      state.upcomingJobs.meta.isFetching = true;
    },
    [types.UPCOMING_JOBS_SUCCESS]: (state, payload) => {
      state.upcomingJobs.data = payload;
      state.upcomingJobs.meta.isFetching = false;
      state.upcomingJobs.meta.lastFetched = new Date();
    },
    [types.UPCOMING_JOBS_FAILURE]: (state) => {
      if (state.upcomingJobs) {
        state.upcomingJobs.data = undefined;
        state.upcomingJobs.meta.isFetching = false;
        state.upcomingJobs.meta.lastFetched = new Date();
      }
    },
    [types.ARCHIVED_JOBS_REQUEST]: (state) => {
      state.archivedJobs.meta.isFetching = true;
    },
    [types.ARCHIVED_JOBS_SUCCESS]: (state, payload) => {
      state.archivedJobs.data = payload;
      state.archivedJobs.meta.isFetching = false;
      state.archivedJobs.meta.lastFetched = new Date();
    },
    [types.ARCHIVED_JOBS_FAILURE]: (state) => {
      if (state.archivedJobs) {
        state.archivedJobs.data = undefined;
        state.archivedJobs.meta.isFetching = false;
        state.archivedJobs.meta.lastFetched = new Date();
      }
    },
    [types.UNRATED_JOBS_REQUEST]: (state) => {
      state.unratedJobs.meta.isFetching = true;
    },
    [types.UNRATED_JOBS_SUCCESS]: (state, payload) => {
      state.unratedJobs.data = payload;
      state.unratedJobs.meta.isFetching = false;
      state.unratedJobs.meta.lastFetched = new Date();
    },
    [types.UNRATED_JOBS_FAILURE]: (state) => {
      if (state.unratedJobs) {
        state.unratedJobs.data = undefined;
        state.unratedJobs.meta.isFetching = false;
        state.unratedJobs.meta.lastFetched = new Date();
      }
    },
    [types.JOB_FOR_RATING_REQUEST]: (state) => {
      state.pages.rateHelper.meta.isFetching = true;
    },
    [types.JOB_FOR_RATING_SUCCESS]: (state, payload) => {
      state.pages.rateHelper.data.jobId = payload.job.id;
      state.pages.rateHelper.data.ratingBy = payload.job.jugglerId;

      state.pages.rateHelper.data.ratingFor = payload.meta.ratingFor;
      state.pages.rateHelper.data.ratingMode = payload.meta.ratingMode;

      state.pages.rateHelper.meta.job = payload.job;
      state.pages.rateHelper.meta.isFetching = false;
      state.pages.rateHelper.lastFetched = new Date();
    },
    [types.JOB_FOR_RATING_FAILURE]: (state) => {
      if (state.jobs) {
        state.pages.rateHelper.meta.job = undefined;
        state.pages.rateHelper.meta.isFetching = false;
        state.pages.rateHelper.meta.lastFetched = new Date();
      }
    },
    [types.JOB_DETAILS_REQUEST]: (state) => {
      state.pages.jobDetails.meta.isFetching = true;
    },
    [types.JOB_DETAILS_SUCCESS]: (state, payload) => {
      // state.pages.jobDetails.data = Object.assign({}, payload, { returnUpdatedJob: true });
      state.pages.jobDetails.data = { ...payload };
      state.pages.jobDetails.data.returnUpdatedJob = true;
      state.pages.jobDetails.meta.isFetching = false;
      state.pages.jobDetails.meta.dirty = false;
      state.pages.jobDetails.lastFetched = new Date();
    },
    [types.JOB_DETAILS_FAILURE]: (state) => {
      if (state.jobs) {
        state.pages.jobDetails.meta.job = undefined;
        state.pages.jobDetails.meta.isFetching = false;
        state.pages.jobDetails.meta.lastFetched = new Date();
      }
    },
    [types.TOGGLE_APPLICANT]: (state, helper) => {
      const helperIndex = state.pages.jobDetails.data.newApplicants.findIndex((applicant) => applicant.applicantId === helper.id);

      if (helperIndex >= 0) {
        state.pages.jobDetails.data.newApplicants.splice(helperIndex, 1);
      } else {
        const applicant = { applicantId: helper.id };
        state.pages.jobDetails.data.newApplicants.push(applicant);
      }
    },
  },
  getters: {
    allJobs: (state) => {
      const allJobs = state.jobs.data;

      return allJobs;
    },
    openJobs: (state) => {
      const allJobs = state.jobs.data;
      const jobs = allJobs.filter((job) => job.jobStatus === c.jobStatus.pending);
      return jobs;
    },
    upcomingJobs: (state) => {
      const allJobs = state.jobs.data;
      const jobs = allJobs.filter((job) => job.jobStatus === c.jobStatus.awarded || job.jobStatus === c.jobStatus.filled);
      return jobs;
    },
    jobHistory: (state) => {
      const allJobs = state.jobs.data;
      const jobs = allJobs.filter((job) => job.jobStatus === c.jobStatus.completed || job.jobStatus === c.jobStatus.filledAndClosed);
      return jobs;
    },
    cancelledJobs: (state) => {
      const allJobs = state.jobs.data;
      const jobs = allJobs.filter((job) => job.jobStatus === c.jobStatus.cancelled);
      return jobs;
    },
    jobById: (state) => (id) => {
      const allJobs = state.jobs.data;
      const job = allJobs.filter((j) => j.id === id);
      return job;
    },
  },
};
