<template>
  <div class="">
    <div class="group">
      <q-field
        label="Select one or both types of Tutoring"
        :error="v$.tutoringMethods.$error"
        borderless
        stack-label
      >
        <template v-slot:prepend>
          <q-icon name="face" />
        </template>
        <template v-slot:control>
          <q-checkbox v-model="interestedInFaceToFaceTutoring" label="In-home face-to-face Tutoring" />
          <br/>
          <q-checkbox v-model="interestedInOnlineTutoring" label="Online Video Tutoring" />
        </template>
      </q-field>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import useVuelidate from '@vuelidate/core';
import { sameAs } from '@vuelidate/validators';
import NotificationService from '../utils/notification-service';
import c from '../utils/constants';

export default {
  name: 'HelperTutoringJobMethods',
  emits: ['change'],
  data() {
    return {

    };
  },
  props: ['isMandatory'],
  setup() {
    return {
      v$: useVuelidate(),
    };
  },
  validations() {
    return {
      tutoringMethods: {
        sameAsRawValue: sameAs(true),
      },
    };
  },
  methods: {
    isValid() {
      if (!this.isMandatory) {
        return true;
      }

      return this.isMandatory ? this.interestedInFaceToFaceTutoring || this.interestedInOnlineTutoring : true;
    },
    validate(notify) {
      if (!this.isMandatory) {
        return true;
      }

      if (!notify) {
        return this.isValid();
      }

      this.v$.$touch();
      if (this.v$.$error) {
        NotificationService.negative('Please review highlighted fields.');
      }

      return !this.v$.$error;
    },
    triggerChange() {
      this.$emit('change', { valid: this.isValid() });
    },
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
      userMeta: (state) => state.userInfo.meta,
    }),
    storeModuleName() {
      return 'userInfo';
    },
    tutoringMethods() {
      return this.isMandatory ? this.interestedInFaceToFaceTutoring || this.interestedInOnlineTutoring : true;
    },
    interestedInFaceToFaceTutoring: {
      get() {
        return this.userInfo.provider.interestedInFaceToFaceTutoring;
      },
      set(value) {
        this.setProperty('provider.interestedInFaceToFaceTutoring', value);
        this.triggerChange();
      },
    },
    interestedInOnlineTutoring: {
      get() {
        return this.userInfo.provider.interestedInOnlineTutoring;
      },
      set(value) {
        this.setProperty('provider.interestedInOnlineTutoring', value);
        this.triggerChange();
      },
    },
  },
  mounted() {
    const { provider, accountStatus } = this.userInfo;
    const defaultValue = (accountStatus === c.accountStatus.pending);

    if (provider.interestedInFaceToFaceTutoring === null) {
      this.setProperty('provider.interestedInFaceToFaceTutoring', defaultValue);
    }

    if (provider.interestedInOnlineTutoring === null) {
      this.setProperty('provider.interestedInOnlineTutoring', defaultValue);
    }
  },
};
</script>
