<template>
  <div>
    <span class="q-mx-sm text-caption">{{formattedDate}}</span>
    <q-btn icon="event" round color="accent" :disable="disable" >
      <q-popup-proxy cover ref="datePopup" @before-show="handleBeforeShow" transition-show="scale" transition-hide="scale">
        <q-date
          :title="title"
          :subtitle="subtitle"
          v-model="proxyDate"
          :minimal="minimal"
          :options="datePickerOptions"
          :default-view="defaultView"
          :default-year-month="defaultYearMonth">
          <div class="row items-center justify-end">
            <q-btn label="Cancel" class="q-mx-sm"  flat color="secondary" v-close-popup />
            <q-btn label="OK" color="accent" @click="save" :disabled="!proxyDate" v-close-popup />
          </div>
        </q-date>
      </q-popup-proxy>
    </q-btn>
    <!-- <q-input v-model="internalDate" :rules="['date']" placeholder="Expiration Date">
      <template v-slot:append>
        <q-icon name="event" class="cursor-pointer">
          <q-popup-proxy ref="qDateProxy" transition-show="scale" transition-hide="scale">
            <q-date v-model="internalDate" minimal mask="YYYY/MM/DD">
              <div class="row items-center justify-end">
                <q-btn v-close-popup label="Close" color="accent" flat />
              </div>
            </q-date>
          </q-popup-proxy>
        </q-icon>
      </template>
    </q-input> -->
  </div>
</template>

<script>
import { date } from 'quasar';

export default {
  name: 'DateTimePicker',
  emits: ['change'],
  props: [
    'date',
    'showHint',
    'disable',
    'defaultView',
    'defaultYearMonth',
    'minDateToday',
    'minimal',
    'dateFormat',
    'autoSelect',
  ],
  data() {
    const tempDate = new Date();
    const todayDate = new Date(tempDate.getFullYear(), tempDate.getMonth(), tempDate.getDate());
    return {
      todayDate,
      proxyDate: null,
      canAutoSelect: false,
    };
  },
  computed: {
    title() {
      if (!this.proxyDate) {
        return 'Tap to select';
      }

      return date.formatDate(this.proxyDate, this.dateFormat ? this.dateFormat : 'Do MMMM');
    },
    subtitle() {
      return null;
    },
    formattedDate() {
      if (!this.date && this.showHint) {
        return 'Tap to select';
      }

      return date.formatDate(this.date, 'DD/MM/YYYY');
    },
  },
  methods: {
    datePickerOptions(dateOption) {
      if (!this.minDateToday) return true;

      const targetDate = date.extractDate(dateOption, 'YYYY/MM/DD');
      return targetDate >= this.todayDate;
    },
    handleBeforeShow() {
      if (!this.date) {
        this.canAutoSelect = true;
        return;
      }

      this.proxyDate = date.formatDate(this.date, 'YYYY/MM/DD');
      this.canAutoSelect = true;
    },
    save() {
      const parsedDate = date.extractDate(this.proxyDate, 'YYYY/MM/DD');
      this.$emit('change', { date: parsedDate });
    },
  },
  watch: {
    proxyDate() {
      if (this.canAutoSelect && this.autoSelect) {
        this.save();
        this.$refs.datePopup.hide();
      }
    },
  },
};
</script>
