<template>
  <q-page class="">
    <div class="layout-padding">
      <q-field
        borderless
        stack-label
        label="How much are you offering for this job?"
        :error="v$.price.$error"
        hide-bottom-space
      >
        <template v-slot:prepend>
          <q-icon name="attach_money" />
        </template>
        <template v-slot:control>
          <div class="full-width">
            $<q-input
              type="number"
              v-model="price"
              placeholder="00"
              style="width: 40px; margin: 0px 5px"
              class="inline"
            />per week (min $100)

            <q-field
              borderless
              label="Is this price negotiable?"
              :error="v$.isPriceNegotiable.$error"
              hide-bottom-space
            >
              <template v-slot:control>
                <div class="full-width">
                  <q-option-group
                    type="radio"
                    v-model="isPriceNegotiable"
                    :options="negotiableTypes"
                    inline
                  />
                </div>
              </template>
            </q-field>
          </div>
        </template>
      </q-field>
      <job-payment-method ref="jobPaymentMethod" :job="job" :storeModuleName="storeModuleName" :hideIcon="true" />
    </div>
  </q-page>
</template>

<script>
import useVuelidate from '@vuelidate/core';
import { required, between } from '@vuelidate/validators';
import { mapState } from 'vuex';
import JobPaymentMethod from 'components/JobPaymentMethod';
import { beforeRouteEnter, beforeLeaveStep } from './multistep-helper';
import NotificationService from '../../utils/notification-service';

export default {
  name: 'PageAuPairJobStep2',
  components: { JobPaymentMethod },
  data() {
    return {
      negotiableTypes: [
        { label: 'Yes', value: true },
        { label: 'No', value: false },
      ],
    };
  },
  setup() {
    return {
      v$: useVuelidate(),
    };
  },
  validations: {
    price: {
      required,
      between: between(100, 1000),
    },
    isPriceNegotiable: {
      required(value) {
        return value !== '';
      },
    },
  },
  computed: {
    ...mapState({
      job: (state) => state.postAuPairJob.jobDetails.data,
      meta: (state) => state.postAuPairJob.jobDetails.meta,
    }),
    storeModuleName() {
      return 'postAuPairJob.jobDetails';
    },
    isPriceNegotiable: {
      get() {
        return this.job.isPriceNegotiable;
      },
      set(value) {
        this.setProperty('isPriceNegotiable', value);
      },
    },
    price: {
      get() {
        return this.job.price;
      },
      set(value) {
        this.setProperty('price', value);
      },
    },
  },
  methods: {
    validate() {
      this.v$.$touch();
      const hasPaymentMethoodError = !this.$refs.jobPaymentMethod.validate();
      const hasError = this.v$.$error || hasPaymentMethoodError;
      if (hasError) {
        NotificationService.negative('Please review highlighted fields.');
      }

      return !hasError;
    },
  },
  beforeRouteEnter(to, from, next) {
    beforeRouteEnter(to, from, next);
  },
  beforeRouteLeave(to, from, next) {
    beforeLeaveStep(this, to, from, next);
  },
};
</script>
