<template>
  <q-page class="">
    <div class="layout-padding">
      <q-field
        label="Would you like to work as a Childminder?"
        stack-label
        borderless
        hide-bottom-slots
      >
        <template v-slot:prepend>
          <q-icon name="child_care" />
        </template>
        <template v-slot:control>
          <q-radio class="q-mx-sm" v-model="interestedInChildcareJobs" :val="true" label="Yes" />
          <q-radio class="q-mx-sm" v-model="interestedInChildcareJobs" :val="false" label="No" />
        </template>
      </q-field>
      <div v-if="interestedInChildcareJobs">
        <q-field
          stack-label
          borderless
          hide-bottom-slots
          :error="v$.helpWith.$error"
          label-slot
        >
          <template v-slot:prepend>
            <q-icon name="work_outline" />
          </template>
          <template v-slot:label>
            <span>
              Select all the childcare jobs you would be<br/> willing to do for families and businesses nearby
            </span>
          </template>
          <template v-slot:control>
            <div class="full-width q-mt-sm">
              <q-option-group
                type="checkbox"
                v-model="helpWith"
                :options="jobTypes"
              />
            </div>
          </template>
        </q-field>
        <q-field
          label="Describe your childcare experience"
          :error="v$.myExperience.$error"
          borderless
          stack-label
          bottom-slots
          hide-bottom-slots
          class="q-mx-sm"
        >
          <!-- <template v-slot:prepend>
            <q-icon name="border_color" />
          </template> -->
          <template v-slot:control>
            <div class="full-width">
              <q-input
                v-model="myExperience"
                type="textarea"
                placeholder="How long have you been looking after children for? What age children are you most comfortable with? What do you find most rewarding about childcare? Etc."
                rows="5"
              />
            </div>
          </template>
          <template v-slot:hint>
            {{childcareExperienceHelper}}
          </template>
          <template v-slot:error>
            {{childcareExperienceHelper}}
          </template>
        </q-field>
        <q-field
          borderless
          stack-label
          hide-bottom-slots
          label="Do you have special needs training?"
        >
          <template v-slot:prepend>
            <q-icon name="accessibility_new" />
          </template>
          <template v-slot:control>
            <q-option-group
              type="radio"
              class="no-margin"
              v-model="hasSpecialNeedsTraining"
              :options="[
                  { label: 'No', value: false },
                  { label: 'Yes', value: true },
              ]"
            />
          </template>
        </q-field>
        <q-field
          borderless
          bottom-slots
          v-if="hasSpecialNeedsTraining === true"
          :helper="myExperience2Helper"
          class="q-mx-sm"
          :error="v$.myExperience2.$error">
          <!-- <template v-slot:prepend>
            <q-icon name="border_color" />
          </template> -->
          <template v-slot:control>
            <div class="full-width">
              <q-input
                v-model="myExperience2"
                type="textarea"
                placeholder="Describe your special needs training & experience."
                rows="3"
              />
            </div>
          </template>
          <template v-slot:hint>
            {{myExperience2Helper}}
          </template>
          <template v-slot:error>
            {{myExperience2Helper}}
          </template>
        </q-field>
        <q-field
          stack-label
          borderless
          hide-bottom-slots
          label="Childcare Extra's">
          <template v-slot:prepend>
            <q-icon name="add_task" />
          </template>
          <template v-slot:control>
            <div class="full-width">
              <q-chip
                clickable
                outline
                :icon="item.selected ? 'check' : 'clear'"
                :color="item.selected ? 'primary' : 'black'"
                :text-color="item.selected ? 'white' : 'black'"
                v-for="(item, index) in userInfo.provider.extraSkills" :key="item.optionId"
                @click="handleItemClick('extraSkills', index)"
                class="q-mx-xs q-my-xs q-caption profile-tab-margin"
              >
                  {{item.text}}
              </q-chip>
            </div>
          </template>
        </q-field>
      </div>
    </div>
  </q-page>
</template>

<script>
import { mapState } from 'vuex';
import useVuelidate from '@vuelidate/core';
import NotificationService from '../../utils/notification-service';
import * as mutationTypes from '../../store/mutation-types';
import c from '../../utils/constants';

export default {
  name: 'PageAccountMyExperience',
  data() {
    const userInfo = this.$store.state.userInfo.data;
    const { interestedInJobTypes } = userInfo;
    const specialNeedsExp = userInfo.provider.myExperience2;

    return {
      helpWith: [],
      hasSpecialNeedsTraining: specialNeedsExp !== null && specialNeedsExp.length > 0,
      interestedInChildcareJobs: (interestedInJobTypes & 15) > 0,
    };
  },
  methods: {
    handleItemClick(section, index) {
      const options = this.userInfo.provider[section];
      const currentOption = options[index];

      const selectedOptions = [];
      options.forEach((option) => {
        let selected = false;
        if (option.optionId === currentOption.optionId) {
          selected = !option.selected;
        } else {
          // eslint-disable-next-line
          selected = option.selected;
        }

        if (selected) {
          selectedOptions.push(option.optionId);
        }
      });

      const payload = {
        propPath: `provider.${section}`,
        value: selectedOptions,
      };

      this.$store.commit(mutationTypes.UPDATE_USER_OPTIONS, payload);
    },
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
    }),
    storeModuleName() {
      return 'userInfo';
    },
    childcareExperienceHelper() {
      if (this.v$.myExperience.$error && this.myExperience) {
        return `required - ${100 - this.myExperience.length} characters left`;
      }

      return 'required - min 100 characters';
    },
    myExperience2Helper() {
      if (this.v$.myExperience2.$error && this.myExperience2) {
        return `required - ${50 - this.myExperience2.length} characters left`;
      }

      return 'required - min 50 characters';
    },
    jobTypes() {
      return c.childcareJobTypes;
    },
    myExperience2: {
      get() {
        return this.userInfo.provider.myExperience2;
      },
      set(value) {
        this.setProperty('provider.myExperience2', value);
      },
    },
    myExperience: {
      get() {
        return this.userInfo.provider.myExperience;
      },
      set(value) {
        this.setProperty('provider.myExperience', value);
      },
    },
  },
  watch: {
    helpWith(value) {
      let interestedInJobTypes = value.reduce((acc, item) => {
        acc += item;
        return acc;
      }, 0);

      if ((this.userInfo.interestedInJobTypes & c.jobType.auPair) === c.jobType.auPair) {
        interestedInJobTypes += c.jobType.auPair;
      }

      if ((this.userInfo.interestedInJobTypes & c.jobType.primarySchoolTutoring) === c.jobType.primarySchoolTutoring) {
        interestedInJobTypes += c.jobType.primarySchoolTutoring;
      }

      if ((this.userInfo.interestedInJobTypes & c.jobType.highSchoolTutoring) === c.jobType.highSchoolTutoring) {
        interestedInJobTypes += c.jobType.highSchoolTutoring;
      }

      if (this.userInfo.interestedInJobTypes !== interestedInJobTypes) {
        this.setProperty('interestedInJobTypes', interestedInJobTypes);
      }
    },
    interestedInChildcareJobs(value) {
      if (!value) {
        let interestedInJobTypes = 0;
        if ((this.userInfo.interestedInJobTypes & c.jobType.auPair) === c.jobType.auPair) {
          interestedInJobTypes += c.jobType.auPair;
        }

        if ((this.userInfo.interestedInJobTypes & c.jobType.primarySchoolTutoring) === c.jobType.primarySchoolTutoring) {
          interestedInJobTypes += c.jobType.primarySchoolTutoring;
        }

        if ((this.userInfo.interestedInJobTypes & c.jobType.highSchoolTutoring) === c.jobType.highSchoolTutoring) {
          interestedInJobTypes += c.jobType.highSchoolTutoring;
        }

        if (this.userInfo.interestedInJobTypes !== interestedInJobTypes) {
          this.setProperty('interestedInJobTypes', interestedInJobTypes);
        }

        this.helpWith = [];
      }
    },
  },
  setup() {
    return {
      v$: useVuelidate(),
    };
  },
  validations: {
    helpWith: {
      required(value) {
        if (this.interestedInChildcareJobs) {
          return value !== null && value.length > 0;
        }

        return true;
      },
    },
    myExperience: {
      required(value) {
        if (this.helpWith.length > 0) {
          return value !== null && value.length >= 100;
        }

        return true;
      },
    },
    myExperience2: {
      required(value) {
        if (this.hasSpecialNeedsTraining === false) {
          return true;
        }

        return this.hasSpecialNeedsTraining === true && value !== null && value.length >= 50;
      },
    },
  },
  mounted() {
    const { interestedInJobTypes } = this.userInfo;

    this.helpWith = c.childcareJobTypes.reduce((acc, item) => {
      if (item.value & interestedInJobTypes) {
        acc.push(item.value);
      }
      return acc;
    }, []);
  },
  beforeRouteLeave(to, from, next) {
    if (this.interestedInChildcareJobs) {
      this.v$.$touch();
      if (this.v$.$error) {
        NotificationService.negative('Please fix all highlighted errors.');
        next(false);
        return;
      }
    }

    this.$store.dispatch('updateUserInfo').then(() => {
      next();
    }).catch((error) => {
      NotificationService.negative(error.message);
      next(false);
    });
  },
};
</script>
