<template>
  <div class="response-padding">
    <div v-if="displayDetails">
      <q-field label="Your Response Time" borderless readonly hide-bottom-space>
        <template v-slot:prepend>
          <q-icon name="access_time" />
        </template>
        <template v-slot:control>
          <div class="full-width q-mt-md">
            <div>
              The best way to improve your chances of winning a job is to respond quickly to all job invites.
              <br/><br/>
              Your Response Rate is calculated on how often and how quickly you respond.
              <br/><br/>
              Your current Response Rate:
            </div>
            <div v-if="responseTime !== 0">
              <q-rating size="1.5rem" v-model="responseStars" :max="5" color="accent" icon-half="star_half" readonly />
            </div>
            <table>
              <tbody>
                <tr
                  v-for="(response, index) in responseTimeTypes"
                  :key="index"
                >
                  <td class="text-left">
                    <q-icon :name="isActiveResponseTime(response.value) ? 'check_circle' : 'radio_button_unchecked'" color="primary" size="20px"/>
                  </td>
                  <td :class="isActiveResponseTime(response.value) ? 'text-left text-primary' : 'text-left'">
                    {{response.label}}
                    <q-icon v-if="response.icon" :name="response.icon" size="18px" :color="isActiveResponseTime(response.value) ? 'primary' : ''" />
                  </td>
                </tr>
              </tbody>
            </table>

            <!-- <q-list>
              <EssentialLink
                v-for="(response, index) in responseTimeTypes"
                iconSize="md"
                :key="index"
                :title="response.label"
                :icon="entryIcon(response)"
                :iconColor="entryIconColor(response)"
                />
            </q-list> -->

          </div>
        </template>

        <!-- <p class="q-my-xs">
          * All Helpers with Response Rate less than 75% are classified as "Over 2 days"
        </p> -->
      </q-field>

      <!-- <q-field label="Your Response Rate" icon="autorenew">
        <p>
          <span style="margin-right: 5px" class="text-bold">Current Response Rate is:</span> <span :class="responseRate >=75 ? 'text-bold text-primary' : 'text-bold text-red'">{{responseRateText}}</span>
        </p>
      </q-field> -->

      <q-field label="Important Info" borderless readonly>
        <template v-slot:prepend>
          <q-icon name="info" />
        </template>
        <template v-slot:control>
          <div class="full-width q-mt-md">
            <p>
              Parents search for helpers using Response Time filters - helpers with good response rates receive more job invitations.
            </p>
            <p>
              Response Times are measured over the past 30 days.
            </p>
          </div>
        </template>
      </q-field>
    </div>
    <div v-else>
      <!-- <b>Response Time</b>
      <div style="display: table-cell; vertical-align: middle;">
        <q-rating size="1.5rem" v-model="responseStars" :max="5" color="accent" icon-half="star_half" readonly />
        <q-btn v-if="responseStars < 4.5" class="q-mx-sm" rounded label="Improve" color="primary" icon-right="done" size="12px" @click="routerPush('/helpers/response-stats')" />
        <span class="q-ml-sm" v-if="responseStars >= 4.5">Great Work...Keep It Up</span>
      </div> -->
      <q-icon name="access_time" size="24px" class="q-mr-xs" />
      <span>Response Time: {{responseTimeText}}</span>
      <q-btn flat class="q-ml-xs text-underline" @click="routerPush('/helpers/response-stats')" label="Improve" padding="none"></q-btn>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';
// import EssentialLink from 'components/EssentialLink';
import c from '../utils/constants';

export default {
  name: 'PageHelpersResponseStats',
  props: ['displayDetails'],
  // components: { EssentialLink },
  data() {
    return {
      responseTimeTypes: c.responseTimeTypes,
      icons: [
        'sentiment_very_dissatisfied',
        'sentiment_dissatisfied',
        'sentiment_dissatisfied',
        'sentiment_satisfied',
        'sentiment_very_satisfied',
      ],
    };
  },
  methods: {
    isActiveResponseTime(value) {
      return this.responseTime === value;
    },
    entryIcon(response) {
      return response.value === this.responseTime ? 'check_circle' : 'radio_button_unchecked';
    },
    entryIconColor(response) {
      return response.value === this.responseTime ? 'primary' : 'black';
    },
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
    }),
    storeModuleName() {
      return 'userInfo';
    },
    responseRate() {
      return this.userInfo.responseRate;
    },
    responseTime() {
      if (this.userInfo.validApplicationsCount === 0) return 0;

      // if (this.userInfo.responseRate < 75 && this.userInfo.responseTime > 72) return 1000;
      if (this.userInfo.responseRate < 75 || this.userInfo.responseTime === c.responseTimes.within7Days) return 1000;

      if (this.userInfo.responseTime === c.responseTimes.withinAnHour) return c.responseTimes.withinADay;

      return this.userInfo.responseTime;
    },
    responseStars() {
      switch (this.responseTime) {
        case 1:
          return 5;
        case 24:
          return 5;
        case 48:
          return 4;
        case 72:
          return 3;
        case 168:
          return 2;
        case 1000:
          return 1;
        default:
          return 1;
      }
    },
    responseTimeText() {
      return this.responseTimeTypes.find((r) => r.value === this.responseTime).label;
    },
    responseRateText() {
      return `${this.userInfo.responseRate}%`;
    },
  },
};
</script>
