<template>
  <q-page class="">
    <div class="">
      <!-- <q-card class="q-mx-sm q-my-sm">
        <q-card-section class="">
          <span class="text-subtitle2">Invitation Notifications</span>
          <br/>
          <span>Select frequency you will receive invitations from {{isFamily ? 'helpers' : 'families'}}.</span>
          <q-option-group
            type="radio"
            v-model="inviteNotificationInterval"
            :options="inviteNotificationOptions"
          />
        </q-card-section>
        <q-card-actions>
          <q-btn color="accent" @click="saveSettings(1)">Submit</q-btn>
        </q-card-actions>
      </q-card> -->

      <!-- <q-card v-if="isFamily">
        <q-card-section class="text-subtitle2">
          Chat Notifications
          <span slot="subtitle">Select frequency you will receive notifications for chat messages.</span>
        </q-card-section>
        <q-card-separator />
        <q-card-section>
          <q-option-group
            type="radio"
            v-model="chatNotificationInterval"
            :options="chatNotificationOptions"
          />
        </q-card-section>
        <q-card-separator />
        <q-card-actions>
          <q-btn color="accent" @click="saveSettings(2)">Submit</q-btn>
        </q-card-actions>
      </q-card> -->

      <q-card class="q-mx-sm q-my-sm">
        <q-card-section>
          <span class="text-subtitle2">Change Password</span>
          <q-input v-model="password.oldPassword" type="password" label="Old Password" :error="v$.password.oldPassword.$error" />
          <q-input v-model="password.newPassword" type="password" label="New Password" :error="v$.password.newPassword.$error" />
          <q-input v-model="password.confirmPassword" type="password" label="Confirm New Password" :error="v$.password.confirmPassword.$error" />
        </q-card-section>
        <q-card-actions>
          <q-btn color="accent" @click="handleChangePassword">Change Password</q-btn>
        </q-card-actions>
      </q-card>

      <q-card class="q-mx-sm q-my-sm" v-if="isApproved">
        <q-card-section>
          <span class="text-subtitle2">Hide My Account</span>
          <br />
          <span>Your account is currently active. Click on ‘Hide My Account’ to hide your account. While your account is hidden, you will be invisible in the network and won’t receive any communication. Your account will remain hidden until you log back in.</span>
        </q-card-section>
        <q-card-actions>
          <q-btn color="secondary" @click="handleHideAccount">Hide My Account</q-btn>
        </q-card-actions>
      </q-card>

      <q-card class="q-mx-sm q-my-sm">
        <q-card-section>
          <span class="text-subtitle2">Delete My Account</span>
          <br />
          <span>Request your account to be deleted. This action is irreversible, and you will be logged out permanently. </span>
        </q-card-section>
        <q-card-actions>
          <q-btn class="bg-red text-white" @click="handleRequestDeleteAccount">Delete My Account</q-btn>
        </q-card-actions>
      </q-card>

      <!-- <q-card>
        <q-card-section class="text-subtitle2">
          About
        </q-card-section>
        <q-card-separator />
        <q-card-section>
          <q-list>
            <q-item>
              Version: {{appVersion}}
            </q-item>
            <q-item>
              Framework Version: {{frameworkVersion}}
            </q-item>
            <q-item>
              Browser: {{browserDetails}}
            </q-item>
            <q-item>
              Mode: {{appMode}}
            </q-item>
          </q-list>
        </q-card-section>
      </q-card> -->
    </div>
  </q-page>
</template>

<script>
import { mapState } from 'vuex';
import useVuelidate from '@vuelidate/core';
import { minLength, required, sameAs } from '@vuelidate/validators';
import c from '../../utils/constants';
import { logout } from '../../api/auth';
import NotificationService from '../../utils/notification-service';

export default {
  name: 'PageAccountSettings',
  data() {
    return {
      inviteNotificationOptions: [
        // { label: 'Instant', value: 1 },
        { label: 'Daily', value: 2 },
        { label: 'Weekly', value: 3 },
        { label: 'Never', value: 4 },
      ],
      chatNotificationOptions: [
        { label: 'Instant', value: 1 },
        { label: 'Never', value: 4 },
      ],
      password: {
        confirmPassword: '',
        newPassword: '',
        oldPassword: '',
      },
    };
  },
  methods: {
    saveSettings(type) {
      const payload = {
        id: -1,
        jugglerId: 0,
        numericValue: type === 1 ? this.inviteNotificationInterval : this.chatNotificationInterval,
        settingType: type,
        stringValue: '',
      };
      this.$store.dispatch('settings/updateSettings', payload).then(() => {
        NotificationService.info('Settings updated successfully.');
      }).catch((error) => {
        NotificationService.negative(error.message);
      });
    },
    handleRequestDeleteAccount() {
      this.$q.dialog({
        title: 'Delete My Account',
        message: 'Deleting your account will delete all your data and historical records. This action is irreversible, and you will be logged out permanently. Are you sure you want to delete your account?',
        cancel: {
          label: 'No',
          color: 'secondary',
          outline: true,
        },
        ok: {
          label: 'Yes',
          outline: true,
        },
      }).onOk(() => {
        this.deleteAccount();
      }).onCancel(() => {});
    },
    deleteAccount() {
      this.$store.dispatch('settings/deleteAccount').then(async () => {
        await logout();
        this.routerReplace('/login');
      }).catch((error) => {
        NotificationService.negative(error.message);
      });
    },
    handleHideAccount() {
      this.$q.dialog({
        title: 'Hide My Account',
        message: 'Are you sure you want to hide your account? This will log you out.',
        cancel: {
          label: 'No',
          color: 'secondary',
          outline: true,
        },
        ok: {
          label: 'Yes',
          outline: true,
        },
      }).onOk(() => {
        this.hideAccount();
      }).onCancel(() => {});
    },
    hideAccount() {
      this.$store.dispatch('settings/hideAccount').then(async () => {
        await this.handleLogout(true);
        if (this.isWeb) {
          window.close();
        }
      }).catch((error) => {
        NotificationService.negative(error.message);
      });
    },
    handleChangePassword() {
      this.v$.password.$touch();
      if (this.v$.password.$error) {
        NotificationService.negative('Please fix all highlighted errors.');
        return;
      }

      this.$store.dispatch('settings/changePassword', this.password).then(() => {
        NotificationService.info('Password changed successfully.');
        this.password = {
          confirmPassword: '',
          newPassword: '',
          oldPassword: '',
        };
        this.v$.password.$reset();
      }).catch((error) => {
        NotificationService.negative(error.message);
      });
    },
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
      appVersion: (state) => state.appInfo.version,
      appMode: (state) => {
        const { appType } = state.appInfo;
        return appType === c.applicationType.webApp ? 'App Mode' : 'Site Mode';
      },
      browserDetails: (state) => {
        const browserName = state.appInfo.userAgent.browser.name;
        const browserVersion = state.appInfo.userAgent.browser.version;
        return `${browserName} ${browserVersion}`;
      },
      frameworkVersion() {
        return '1.0.0';
      },
    }),
    storeModuleName() {
      return 'userInfo';
    },
    chatNotificationInterval: {
      get() {
        return this.userInfo.accountProperties.chatNotificationInterval;
      },
      set(value) {
        this.setProperty('accountProperties.chatNotificationInterval', value);
      },
    },
    inviteNotificationInterval: {
      get() {
        return this.userInfo.accountProperties.inviteNotificationInterval;
      },
      set(value) {
        this.setProperty('accountProperties.inviteNotificationInterval', value);
      },
    },
  },
  setup() {
    return {
      v$: useVuelidate(),
    };
  },
  validations() {
    return {
      password: {
        oldPassword: {
          required,
          minLength: minLength(7),
        },
        newPassword: {
          required,
          minLength: minLength(7),
        },
        confirmPassword: {
          required,
          sameAsPassword: sameAs(this.password.newPassword),
        },
      },
    };
  },
};
</script>
