<template>
  <q-dialog v-model="opened" @hide="handleHide">
    <q-layout view="Lhh lpR fff" container class="bg-white">
      <q-header>
        <q-toolbar>
          <q-toolbar-title>
            {{ index >= 0 ? 'Edit Address' : 'Add Address' }}
          </q-toolbar-title>
          <q-btn flat>
            <q-icon name="clear" v-close-popup />
          </q-btn>
        </q-toolbar>
      </q-header>

      <q-page-container>
        <div class="layout-padding">
          <!-- <q-field
            :error="v$.address.firstName.$error"
            error-message="required"
            borderless
            stack-label
          >
            <template v-slot:control>
              <q-input
                v-model="address.firstName"
                float-label="First Name"
                placeholder="First Name"
              />
            </template>
          </q-field> -->

          <div v-if="addressCountry === 'au'">
            <q-select
              v-if="!address.id"
              v-model="addressCountry"
              class="q-mb-sm"
              :options="countries"
              option-value="value"
              option-label="label"
              behavior="menu"
              emit-value
              map-options
              label="Country"
            />

            <q-input v-model="address.addressLine1" label="Address Line 1" placeholder="" :error="v$.address.addressLine1.$error" name="address1" autocomplete="billing address-line1" autocapitalize="off" autocorrect="off" spellcheck="false" />
            <q-input v-model="address.addressLine2" label="Address Line 2" placeholder="" name="address2" autocomplete="billing address-line2" autocapitalize="off" autocorrect="off" spellcheck="false" />

            <q-select
              v-model="suburb"
              label="Suburb"
              placeholder="Suburb"
              :error="v$.address.suburb.$error"
              autocomplete="billing address-level2"
              autocapitalize="off"
              autocorrect="off"
              option-value="value"
              option-label="label"
              use-input
              hide-selected
              fill-input
              :options="matchingSuburbs"
              @filter="suburbSearch"
              :min-characters="4"
              :max-results="10"
              spellcheck="false">
            </q-select>

            <q-select
              label="State"
              v-model="address.state"
              :error="v$.address.state.$error"
              :options="states"
              option-value="value"
              option-label="label"
              emit-value
              map-options
            />
            <q-input v-model="address.postCode" label="Post Code" placeholder="" :error="v$.address.postCode.$error" autocomplete="billing postal-code" />
            <q-input v-if="isClient" v-model="address.addressLabel" label="Nickname" :hint="addressLabelHint"  />
          </div>
          <div v-if="addressCountry === 'nz'">
            <div>
              <q-select
                v-if="!address.id"
                v-model="addressCountry"
                class="q-mb-sm"
                :options="countries"
                option-value="value"
                option-label="label"
                behavior="menu"
                emit-value
                map-options
                label="Country"
              />
            </div>
            <div v-if="!showAddressDetails">
              <q-select
                v-model="completeAddress"
                label="Address Finder"
                placeholder="Start typing your address..."
                :error="v$.address.suburb.$error"
                autocomplete="none"
                autocapitalize="off"
                autocorrect="off"
                option-value="value"
                option-label="label"
                use-input
                hide-selected
                hide-bottom-space
                fill-input
                dense
                :options="matchingAddresses"
                @filter="addressSearch"
                :min-characters="4"
                :max-results="10"
                spellcheck="false">
              </q-select>
              <q-btn  flat color="secondary" class="text-underline" @click="handleTypeAddressManually" label="Type address manually"/>
            </div>

            <div v-if="showAddressDetails" class="">
              <q-input v-model="address.addressLine1" label="Address Line 1" placeholder="" :error="v$.address.addressLine1.$error" name="address1" autocomplete="billing address-line1" autocapitalize="off" autocorrect="off" spellcheck="false" />
              <q-input v-model="address.addressLine2" label="Address Line 2" placeholder="" name="address2" autocomplete="billing address-line2" autocapitalize="off" autocorrect="off" spellcheck="false" />
              <q-input v-model="suburb" label="Suburb" placeholder="Suburb" :error="v$.address.suburb.$error" autocomplete="none" autocapitalize="off" autocorrect="off" spellcheck="false" />
              <q-input v-model="address.state" label="Region/City" placeholder="Region/City" :error="v$.address.state.$error" autocomplete="none" />
              <q-input v-model="address.postCode" label="Post Code" placeholder="Post Code" :error="v$.address.postCode.$error" autocomplete="none" />
              <q-input v-if="isClient" v-model="address.addressLabel" label="Nickname" :hint="addressLabelHint"  />
            </div>
          </div>
        </div>
        <div class="text-center">
          <q-btn color="accent" @click="handleSave">
            <q-icon name="save"/>
            Save
          </q-btn>
        </div>
      </q-page-container>

      <!-- <q-footer class="bg-white">
        <q-toolbar>
          <q-btn color="accent" @click="handleSave">
            <q-icon name="save"/>
            Save
          </q-btn>
        </q-toolbar>
      </q-footer> -->
    </q-layout>
  </q-dialog>
</template>

<script>
import { mapActions } from 'vuex';
import useVuelidate from '@vuelidate/core';
import {
  required,
  numeric,
  maxLength,
  minLength,
} from '@vuelidate/validators';
import errorHandler from '../utils/error-handler';
import NotificationService from '../utils/notification-service';
import c from '../utils/constants';

export default {
  name: 'AddAddressModal',
  components: {},
  emits: ['hide', 'addAddressSuccess'],
  data() {
    return {
      index: -1,
      address: {},
      opened: false,
      states: c.states,
      countries: c.countryOptions,
      completeAddress: '',
      showAddressDetails: false,
      matchingSuburbs: [],
      matchingAddresses: [],
    };
  },
  methods: {
    ...mapActions({
      suburbSearchService: 'suburbSearch',
      addressSearchService: 'addressSearch',
      geocodeAddresService: 'geocodeAddress',
    }),
    handleTypeAddressManually() {
      this.showAddressDetails = true;
    },
    handleHide() {
      this.showAddressDetails = false;
      this.opened = false;
      this.$emit('hide');
    },
    open(address, index) {
      this.v$.$reset();

      this.address = address;
      this.index = index;

      if (this.addressCountry === 'nz') {
        this.showAddressDetails = this.address.id > 0;
      }

      this.opened = true;
    },
    handleSave() {
      this.v$.$touch();
      if (this.v$.$error) {
        NotificationService.negative('Please review highlighted fields.');
        return;
      }
      this.$emit('addAddressSuccess', { address: this.address, index: this.index });
      this.opened = false;
    },
    async suburbSearch(term, update, abort) {
      if (term.length < 3) {
        abort();
        return;
      }

      let results = [];
      try {
        const suburbs = await this.suburbSearchService({ term });
        results = suburbs.map((suburb) => ({
          label: `${suburb.name}, ${suburb.state.replace('-', ' ').toUpperCase()}`,
          value: suburb.name,
          meta: suburb,
        }));
      } catch (err) {
        errorHandler.captureException(err);
      }

      update(() => {
        this.matchingSuburbs = results;
      });
    },
    async addressSearch(term, update, abort) {
      if (term.length < 3) {
        abort();
        return;
      }

      let results = [];
      try {
        const addresses = await this.addressSearchService({ term });
        results = addresses.map((result) => ({
          label: result,
          value: result,
        }));
      } catch (err) {
        errorHandler.captureException(err);
      }

      update(() => {
        this.matchingAddresses = results;
      });
    },
    suburbSelected(item) {
      if (item.meta) {
        this.address.state = item.meta.state;
        this.address.postCode = item.meta.postcode;
      }
    },
    async addressSelected(term) {
      try {
        if (term.value) {
          const address = await this.geocodeAddresService({ term: term.value });
          this.showAddressDetails = true;
          this.address.addressLine1 = address.streetNumber ? address.streetNumber : '';
          this.address.addressLine1 += address.streetName ? ` ${address.streetName}` : '';
          this.address.postCode = address.postCode;
          this.address.suburb = address.suburb;
          this.address.state = address.state;
        }
      } catch (err) {
        errorHandler.captureException(err);
      }
    },
  },
  watch: {
    async completeAddress(value) {
      await this.addressSelected(value);
    },
  },
  computed: {
    addressCountry: {
      get() {
        return this.address.country;
      },
      set(value) {
        this.address.country = value;
      },
    },
    suburb: {
      get() {
        return this.address.suburb;
      },
      set(value) {
        if (value && value.meta) {
          this.address.suburb = value.meta.name;
          this.suburbSelected(value);
        } else {
          this.address.suburb = value;
        }
      },
    },
    addressLabelHint() {
      if (this.isIndividual) return 'For example: Home, Melb Work, Holiday House';

      return 'For example: Sydney-06, OOSSH-Melb, Auckland Central';
    },
  },
  setup() {
    return {
      v$: useVuelidate(),
    };
  },
  validations: {
    address: {
      addressLine1: {
        validate(value) {
          if (!value) return false;

          return (value.indexOf(' ') !== -1);
        },
      },
      suburb: {
        required,
      },
      state: {
        required,
      },
      postCode: {
        required,
        numeric,
        maxLength: maxLength(4),
        minLength: minLength(4),
      },
    },
  },
};
</script>
