import { route } from 'quasar/wrappers';
import {
  createRouter, createMemoryHistory, createWebHistory, createWebHashHistory,
} from 'vue-router';
import routes from './routes';
import errorHandler from '../utils/error-handler';
import c from '../utils/constants';
import { getRouteMetaProperty, parseQueryStringParams, getFullRoute } from '../utils/helpers';
import { isAuthorized } from '../api/auth';
import analyticsService from '../utils/analytics-service';
import intercomService from '../utils/intercom-service';

console.log('boot::router');
/*
 * If not building with SSR mode, you can
 * directly export the Router instantiation;
 *
 * The function below can be async too; either use
 * async/await or return a Promise which resolves
 * with the Router instance.
 */

export default route(({ store }) => {
  const createHistory = process.env.SERVER
    ? createMemoryHistory
    : (process.env.VUE_ROUTER_MODE === 'history' ? createWebHistory : createWebHashHistory);

  const Router = createRouter({
    scrollBehavior: () => ({ left: 0, top: 0 }),
    routes,

    // Leave this as is and make changes in quasar.conf.js instead!
    // quasar.conf.js -> build -> vueRouterMode
    // quasar.conf.js -> build -> publicPath
    history: createHistory(process.env.MODE === 'ssr' ? void 0 : process.env.VUE_ROUTER_BASE),
  });

  const checkUserInfo = async () => {
    if (isAuthorized()) {
      await store.dispatch('fetchCoreData').then(() => {
        // Do nothing.
      }).catch((e) => {
        errorHandler.captureException(e);

        // setTimeout(() => {
        //   window.location.href = 'statics/error.html';
        // }, 5000);
      });
    } else {
      intercomService.showIcon();
    }
  };

  const setPageInfo = (newRoute) => {
    let pageTitle;
    if (newRoute.meta && newRoute.meta.title) {
      if (typeof newRoute.meta.title === 'function') {
        pageTitle = newRoute.meta.title(store.getters.userInfo);
      } else {
        pageTitle = newRoute.meta.title;
      }
    } else {
      pageTitle = 'Juggle St';
    }
    // } else if (newRoute.name) {
    //   pageTitle = newRoute.name;
    // }

    store.dispatch('setPageInfo', { title: pageTitle, subtitle: '' });
  };

  const redirectQuickNavigation = async (to) => {
    if (to.path.startsWith('/qn-')) {
      const type = to.path.substring(4);
      const fullRoute = getFullRoute(type, to.query);
      const queryIdx = fullRoute.indexOf('?');
      if (queryIdx !== -1) {
        const updatedRoute = fullRoute.substring(0, queryIdx);
        const params = parseQueryStringParams(fullRoute);
        return { path: updatedRoute, query: params, replace: true };
      }
      return { path: fullRoute, replace: true };
    }

    if (to.path === '/profile-wizard') {
      if (!isAuthorized()) {
        return { path: '/login', query: { redirect: '/profile-wizard' }, replace: true };
      }

      await checkUserInfo(to);

      const updatedRoute = store.getters.userInfo.jugglerTypes === c.jugglerType.client ? 'client-profile-wizard' : 'helper-profile-wizard';
      return { path: updatedRoute, replace: true };
    }

    return false;
  };

  Router.beforeEach(async (to) => {
    // Check quick navigation.
    const qNavigate = await redirectQuickNavigation(to);
    if (qNavigate) {
      return qNavigate;
    }

    if (to.matched.some((record) => record.meta && record.meta.requiresAuth)) {
      // this route requires auth, check if logged in
      // if not, redirect to login page.
      if (!isAuthorized()) {
        return { path: '/login', query: { redirect: to.fullPath } };
      }
      await checkUserInfo(to);
    }

    setPageInfo(to);

    return true;
  });

  Router.afterEach((to) => {
    if (getRouteMetaProperty(to, 'displayIntercom')) {
      intercomService.showIcon();
    } else {
      intercomService.hideIcon();
    }

    analyticsService.logPageView(to.path, to.meta ? to.meta.title : to.name);

    // TODO: Should we update Intercom?
    // intercomService.update();
  });

  Router.onError((error) => {
    errorHandler.captureException(error);
  });

  return Router;
});
