<template>
  <q-page class="">
    <div>
      <div class="layout-padding">
        <div v-if="meta.lastFetched != null">
          <client-unrated-job-summary
            v-for="job in this.unratedJobs"
            :key="job.id"
            :job="job">
          </client-unrated-job-summary>
        </div>
      </div>
    </div>
  </q-page>
</template>

<script>
import { mapState } from 'vuex';
import ClientUnratedJobSummary from 'components/ClientUnratedJobSummary';

export default {
  name: 'PageFamiliesUnratedJobs',
  components: { ClientUnratedJobSummary },
  data() {
    return {
    };
  },
  computed: {
    ...mapState({
      unratedJobs: (state) => state.families.unratedJobs.data,
      meta: (state) => state.families.unratedJobs.meta,
    }),
  },
  methods: {
  },
  beforeRouteEnter(to, from, next) {
    next(async (vm) => {
      await vm.$store.dispatch('families/fetchUnratedJobs');
    });
  },
};
</script>
