<template>
  <div class="layout-padding">
    <phone-verification
      :phoneNumber="phoneNumber"
      v-show="!stepComplete"
      v-on:verificationSuccess="handlePhoneVerificationSuccess"
      v-on:verificationFailure="handlePhoneVerificationFailure"
    />
    <div v-show="stepComplete" class="layout-padding">
      <q-field borderless>
        <template v-slot:prepend>
          <q-icon name="done" />
        </template>
        <template v-slot:control>
          Your phone number has been successfully verified, tap Next to continue.
        </template>
      </q-field>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex';
import PhoneVerification from 'components/PhoneVerification';
import NotificationService from '../../utils/notification-service';
import beforeLeaveStep from './before-leave-step';
import * as types from '../../store/mutation-types';

export default {
  name: 'IdentityCheck',
  components: { PhoneVerification },
  data() {
    return {};
  },
  methods: {
    ...mapActions([
      'updateUserInfo',
    ]),
    handlePhoneVerificationSuccess() {
      this.setProperty('identityCheckCompleted', true);
      this.$store.commit(`profileWizard/${types.UPDATE_STEP_STATUS}`, { complete: true });
    },
    handlePhoneVerificationFailure(error) {
      NotificationService.negative(error.message);
    },
    validate() {
      if (this.stepComplete) {
        return true;
      }

      NotificationService.info('Please complete the phone verification process.');
      return false;
    },
  },
  computed: {
    ...mapState({
      stepComplete: (state) => {
        const { meta } = state.profileWizard;
        const currentStep = meta.steps[meta.currentStep];
        return currentStep.complete;
      },
      phoneNumber: (state) => state.userInfo.data.phoneNumber,
    }),
    storeModuleName() {
      return 'profileWizard';
    },
  },
  beforeRouteLeave(to, from, next) {
    // Update user info before proceeding to the next step if step is complete
    if (this.stepComplete) {
      this.updateUserInfo().then(() => {
        beforeLeaveStep(this, to, from, next);
      });
    } else {
      beforeLeaveStep(this, to, from, next);
    }
  },
};
</script>
