<template>
  <div class="">
    <div v-if="mode === 'summary'">
      Candidate Criteria:
      <br/>
      <div style="margin: 0 5px" v-for="(filter, key) in selectedFilters" :key="key">
        <span class="">
          {{filter.title}}:
        </span>
        <span>
          {{getFilterValue(filter)}}
        </span>
      </div>
    </div>
    <div v-else-if="mode === 'jobDetails'">
      <q-field
        label="Candidate Criteria"
        borderless
        readonly
        hide-bottom-space
        v-if="Object.keys(selectedFilters).length > 0"
      >
        <template v-slot:prepend>
          <q-icon name="people" />
        </template>
        <template v-slot:control>
          <div class="full-width q-mt-md">
            <div class="" v-for="(filter, key) in selectedFilters" :key="key">
              <span class="">
                {{filter.title}}:
              </span>
              <span class="text-grey">
                {{getFilterValue(filter)}}
              </span>
            </div>
          </div>
        </template>
      </q-field>
    </div>
    <div class="filters-panel" v-else>
      <q-toolbar>
        <q-toolbar-title>
          Filters
        </q-toolbar-title>
        <q-btn flat @click="togglePanel" :no-ripple="true">
          <q-icon name="close" />
        </q-btn>
      </q-toolbar>
      <div class="filters-container q-mx-sm">
        <div class="search-by-name-section">
          <q-field label="Search by Name" icon="search" v-if="enableSearchByName" class="" stack-label borderless readonly>
            <template v-slot:prepend>
              <q-icon name="search" />
            </template>
            <template v-slot:control>
              <q-select
                use-input
                hide-selected
                fill-input
                placeholder="Min 2 characters"
                style="width: 200px"
                v-model="userName"
                :input-debounce="250"
                :options="searchByNameMatches"
                @filter="search"
                :min-characters="2"
                :max-results="10"
                option-value="value"
                option-label="label"
                @update:model-value="selected">
                <template v-slot:no-option>
                  <q-item>
                    <q-item-section class="text-grey">
                      No matches
                    </q-item-section>
                  </q-item>
                </template>
                <template v-slot:option="scope">
                  <q-item
                    v-bind="scope.itemProps"
                  >
                    <q-item-section avatar>
                      <q-avatar rounded>
                        <img :src="scope.opt.avatar">
                      </q-avatar>
                    </q-item-section>
                    <q-item-section>
                      <q-item-label v-html="scope.opt.label" />
                      <q-item-label caption>{{ scope.opt.sublabel }}</q-item-label>
                    </q-item-section>
                    <q-item-section side top>
                      <q-icon name="place" color="" />
                      <q-item-label caption>{{scope.opt.stamp}}</q-item-label>
                    </q-item-section>
                  </q-item>
                </template>
              </q-select>
            </template>
          </q-field>
        </div>
        <div>
          <div class="" v-for="(filter, key) in filters" :key="key">
            <q-field class="" :label="filter.title" v-if="!filter.visible || filter.visible(jobType)" borderless readonly stack-label>
              <template v-slot:prepend>
                <q-icon :name="filter.icon" />
              </template>
              <template v-slot:control>
                <div class="" tabindex="0">
                  <q-select :disable="isProcessing" v-if="filter.type === 'select'" :options="getFilterFields(filter.fields, jobType)" v-model="filter.selected" emit-value map-options />
                  <q-option-group :disable="isProcessing" v-else size="sm" :inline="filter.inline" :options="getFilterFields(filter.fields, jobType)" :type="filter.type" v-model="filter.selected"/>
                </div>
              </template>
            </q-field>
          </div>
        </div>
        <!-- <q-btn color="accent" class="full-width" @click="handleFilterButtonClick">Apply</q-btn> -->
        <q-inner-loading :showing="isProcessing">
          <!-- <q-spinner-gears size="50px" color="secondary" /> -->
          <img src="/js-loading-animation.gif" style="height: 150px; mix-blend-mode: multiply;" />
        </q-inner-loading>
