<template>
  <q-page class="">
    <div class="layout-padding">
      <q-field :error="v$.jobDate.$error" message-label="required" label="Select the date of the job" borderless hide-bottom-space readonly>
        <template v-slot:prepend>
          <q-icon name="date_range" />
        </template>
        <template v-slot:control>
          <div class="full-width q-mt-md">
            <date-picker :autoSelect="true" :show-hint="true" :date="jobDate" v-on:change="handleJobDateChange" :min-date-today="true" />
          </div>
        </template>
      </q-field>

      <time-picker icon="access_time" :error="v$.jobStartTime.$error" error-message="End time must be greater than start time" label="Start time" :default-value="jobStartTime" :change="handleStartTimeChange" />

      <time-picker icon="timelapse" :error="v$.jobEndTime.$error" error-message="End time must be greater than start time" label="End time" :default-value="jobEndTime" :change="handleEndTimeChange" />

      <q-field :error="v$.specialInstructions.$error" label="Describe the job" borderless hide-bottom-space no-error-icon readonly>
        <template v-slot:prepend>
          <q-icon name="assignment" />
        </template>
        <template v-slot:control>
          <div class="full-width">
            <q-input :error="v$.specialInstructions.$error" error-message="required" v-model="specialInstructions" type="textarea" rows="4" :max-height="200"
            :placeholder="specialInstructionsPlaceholder" />
          </div>
        </template>
      </q-field>
    </div>
  </q-page>
</template>

<script>
import useVuelidate from '@vuelidate/core';
import { required } from '@vuelidate/validators';
import { mapState } from 'vuex';
import DatePicker from 'components/DatePicker';
import TimePicker from 'components/TimePicker';
import { beforeRouteEnter, beforeLeaveStep } from './multistep-helper';
import NotificationService from '../../utils/notification-service';
import { getTimeDifference } from '../../utils/helpers';

export default {
  name: 'PageOneOffJobStep1',
  components: { DatePicker, TimePicker },
  data() {
    return {

    };
  },
  setup() {
    return {
      v$: useVuelidate(),
    };
  },
  validations() {
    return {
      jobDate: {
        required(value) {
          if (!value) {
            return false;
          }
          const jobDate = new Date(value);
          const today = new Date();
          today.setHours(0, 0, 0, 0);
          return jobDate.getTime() >= today.getTime();
        },
      },
      jobStartTime: {
        required(value) {
          const time = getTimeDifference(value, this.jobEndTime);
          return time >= 0.25;
        },
      },
      jobEndTime: {
        required(value) {
          const time = getTimeDifference(this.jobStartTime, value);
          return time >= 0.25;
        },
      },
      specialInstructions: {
        required,
      },
    };
  },
  computed: {
    ...mapState({
      job: (state) => state.postOneOffJob.jobDetails.data,
    }),
    storeModuleName() {
      return 'postOneOffJob.jobDetails';
    },
    specialInstructionsPlaceholder() {
      if (this.isBusiness) return 'What does your childcare worker need to know about this job and working for your business?';

      return 'What does your carer need to know about this job and working for your family?';
    },
    jobDate: {
      get() {
        return this.job.jobDate;
      },
      set(value) {
        // const parsedValue = value ? new Date(value) : null;
        // this.setProperty('jobDate', parsedValue);
        this.setProperty('jobDate', value);

        if (value && value.getDate) {
          this.setProperty('jobDateDay', value.getDate());
          this.setProperty('jobDateMonth', value.getMonth() + 1);
          this.setProperty('jobDateYear', value.getFullYear());
        }
      },
    },
    jobStartTime: {
      get() {
        return this.job.jobStartTime;
      },
      set(value) {
        this.setProperty('jobStartTime', value);
      },
    },
    jobEndTime: {
      get() {
        return this.job.jobEndTime;
      },
      set(value) {
        this.setProperty('jobEndTime', value);
      },
    },
    specialInstructions: {
      get() {
        return this.job.specialInstructions;
      },
      set(value) {
        this.setProperty('specialInstructions', value);
      },
    },
  },
  methods: {
    validate() {
      this.v$.$touch();
      const hasError = this.v$.$error;
      if (hasError) {
        NotificationService.negative('Please review highlighted fields.');
      }

      return !hasError;
    },
    handleJobDateChange(value) {
      this.jobDate = value.date;
    },
    handleStartTimeChange(val) {
      this.jobStartTime = val;
    },
    handleEndTimeChange(val) {
      this.jobEndTime = val;
    },
  },
  beforeRouteEnter(to, from, next) {
    beforeRouteEnter(to, from, next);
  },
  beforeRouteLeave(to, from, next) {
    beforeLeaveStep(this, to, from, next);
  },
};
</script>
