<script>
import { mapState } from 'vuex';
import NotificationService from '../utils/notification-service';
import { env } from '../utils/env';

export default {
  name: 'ProfileVisibility',
  emits: ['success', 'change'],
  data() {
    const userInfo = this.$store.state.userInfo.data;

    if (!userInfo.publicUserId) {
      return {
        isPublicProfileAccessible: null,
        isPublicProfilePhotoAccessible: null,
      };
    }

    return {
      isPublicProfileAccessible: userInfo.isPublicProfileAccessible,
      isPublicProfilePhotoAccessible: userInfo.isPublicProfilePhotoAccessible,
    };
  },
  props: ['defaultVisibility', 'displayShareLinks'],
  methods: {
    validate(notify) {
      if (this.isPublicProfileAccessible === null) {
        if (notify) {
          NotificationService.negative('Please set your profile visibility.');
        }
        return false;
      }

      return true;
    },
    handleUpdateProfile() {
      return this.updateProfile(true);
    },
    updateProfile(notify) {
      const payload = {
        isPublicProfileAccessible: this.isPublicProfileAccessible,
        // isPublicProfilePhotoAccessible: this.isPublicProfilePhotoAccessible,
        isPublicProfilePhotoAccessible: this.isPublicProfileAccessible,
      };
      return this.$store.dispatch('settings/updateProfileVisibility', payload).then(() => {
        if (notify) {
          NotificationService.info('Privacy settings changed successfully, it may take up to five minutes for the changes to take effect.');
        }

        this.$emit('success');
      }).catch((error) => {
        NotificationService.negative(error.message);
      });
    },
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
    }),
    ageInYears() {
      return this.userInfo.ageInYears;
    },
    storeModuleName() {
      return 'userInfo';
    },
    publicUrl() {
      return `${env.marketingRoot}/profiles/${this.userInfo.publicUserId}`;
    },
    persistedIsPublicProfileAccessible() {
      return this.getProperty('isPublicProfileAccessible');
    },
    persistedIsPublicProfilePhotoAccessible() {
      return this.getProperty('isPublicProfilePhotoAccessible');
    },
  },
  watch: {
    isPublicProfileAccessible() {
      this.$emit('change');
    },
  },
};
</script>
