<template>
  <div class="layout-padding">
      <q-field
        label="References"
        borderless
        readonly
      >
        <template v-slot:prepend>
          <q-icon name="check_circle" />
        </template>
        <template v-slot:control>
          <div class="full-width q-mt-md">
            <p>
              Adding references will enhance your Juggle Street profile.
            </p>
            <p>
              Juggle Street will give your referee details to potential employers upon request.
            </p>
            <p>
              Au Pairs – 1 reference is required to be a Juggle Street Au Pair. References can be Au Pair and non-childcare related.
            </p>
            <personal-references ref="personalReferences" :minRefs="1" v-on:change="handleRefChange" v-on:complete="handleRefComplete" />
        </div>
        </template>
      </q-field>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex';
import PersonalReferences from 'components/PersonalReferences';
import beforeLeaveStep from '../before-leave-step';
import * as types from '../../../store/mutation-types';
import NotificationService from '../../../utils/notification-service';

export default {
  name: 'ProfileWizardPersonalReferences',
  components: { PersonalReferences },
  data() {
    return {

    };
  },
  methods: {
    ...mapActions([
      'updateUserInfo',
    ]),
    handleRefChange() {
      // this.$store.commit(`profileWizard/${types.UPDATE_STEP_STATUS}`, { canSkip: false });
    },
    handleRefComplete() {
      this.$store.commit(`profileWizard/${types.UPDATE_STEP_STATUS}`, { complete: true });
    },
    validate() {
      if (this.currentStep.meta.canSkip) {
        return true;
      }

      return this.$refs.personalReferences.validate(true);
    },
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
      userMeta: (state) => state.userInfo.meta,
      currentStep: (state) => {
        const { meta } = state.profileWizard;
        const currentStep = meta.steps[meta.currentStep];
        return currentStep;
      },
      stepComplete: (state) => {
        const { meta } = state.profileWizard;
        const currentStep = meta.steps[meta.currentStep];
        return currentStep.complete;
      },
    }),
    storeModuleName() {
      return 'userInfo';
    },
  },
  beforeRouteLeave(to, from, next) {
    if (this.stepComplete) {
      this.updateUserInfo().then(() => {
        beforeLeaveStep(this, to, from, next);
      }).catch((error) => {
        NotificationService.negative(error.message);
        next(false);
      });
    } else {
      beforeLeaveStep(this, to, from, next);
    }
  },
};
</script>
