<template>
  <div class="layout-padding">
    <q-field
      label="Select the jobs you would like to apply for"
      stack-label
      borderless
      hide-bottom-space
      :error="v$.validJobCategories.$error"
    >
      <template v-slot:prepend>
        <q-icon name="flag" />
      </template>
      <template v-slot:control>
        <div class="full-width">
          <q-checkbox v-model="interestedInChildcareJobs" label="Childcare" class="q-mr-sm"/> <span :class="childCareSubtitleClass">{{childCareSubtitle}}</span>
          <p class="text-faded" style="margin-left: 30px">
            Babysitting, nannying, before & after school
            <br/>
          </p>
          <q-checkbox v-model="interestedInAuPairJobs" label="Au Pair" class="q-mr-sm"/> <span :class="auPairSubtitleClass">{{auPairSubtitle}}</span>
          <p class="text-faded" style="margin-left: 30px">
            Live-in with family – provide childcare for accommodation, food & small allowance
            <br/>
          </p>
          <q-checkbox v-model="interestedInTutoringJobs" label="Tutoring" class="q-mr-sm"/> <span :class="tutoringSubtitleClass">{{tutoringSubtitle}}</span>
          <p class="text-faded" style="margin-left: 30px">
            In-home and online private lessons for Primary and High School students
            <br />
          </p>
        </div>
      </template>
    </q-field>
    <q-field
      :error="v$.aboutMe.$error"
      borderless
      hide-bottom-space
      bottom-slots
      label="Introduce Yourself"
      stack-label
    >
      <!-- <template v-slot:prepend>
        <q-icon name="border_color" />
      </template> -->
      <template v-slot:control>
        <div class="full-width">
          <p>
            Tell your neighbours a bit about yourself, but leave out your name and age as this will already be shown. This introduction will be displayed on your profile card.
          </p>
          <q-input
            v-model="aboutMe"
            class="q-my-sm"
            type="textarea"
            placeholder="EXAMPLE -  Hi mums and dad’s, I grew up in a big family with 2 younger brothers and a younger sister, so I have many years of experience looking after children! I know lots of people in the neighbourhood and look forward to meeting you."
            rows="6"
          />
        </div>
      </template>
      <template v-slot:hint>
        {{aboutMeHelper}}
      </template>
      <template v-slot:error>
        {{aboutMeHelper}}
      </template>
    </q-field>
    <q-field
      label="Do you have special needs training?"
      :error="v$.hasSpecialNeedsTraining.$error"
      stack-label
      borderless
      hide-bottom-space
    >
      <template v-slot:prepend>
        <q-icon name="accessibility_new" />
      </template>
      <template v-slot:control>
        <q-option-group
          type="radio"
          v-model="hasSpecialNeedsTraining"
          :options="[
              { label: 'No', value: false },
              { label: 'Yes', value: true },
          ]"
        />
      </template>
    </q-field>
    <q-field
      v-if="hasSpecialNeedsTraining === true"
      label="Describe your special needs training & experience"
      borderless
      stack-label
      :error="v$.myExperience2.$error">
      <!-- <template v-slot:prepend>
        <q-icon name="border_color" />
      </template> -->
      <template v-slot:control>
        <div class="full-width">
          <q-input
            v-model="myExperience2"
            type="textarea"
            placeholder=""
            rows="3"
          />
        </div>
      </template>
      <template v-slot:hint>
        {{myExperience2Helper}}
      </template>
      <template v-slot:error>
        {{myExperience2Helper}}
      </template>
    </q-field>
    <div style="height: 75px" />
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex';
import useVuelidate from '@vuelidate/core';
import { required, minLength, sameAs } from '@vuelidate/validators';
import beforeLeaveStep from '../before-leave-step';
import * as mutationTypes from '../../../store/mutation-types';
import NotificationService from '../../../utils/notification-service';
import c from '../../../utils/constants';

export default {
  name: 'ProfileWizardIntroduceYourself',
  data() {
    const specialNeedsExp = this.$store.state.userInfo.data.provider.myExperience2;
    let hasSpecialNeedsTraining;
    if (specialNeedsExp === null) {
      hasSpecialNeedsTraining = -1;
    } else {
      hasSpecialNeedsTraining = specialNeedsExp.length > 0;
    }

    return {
      hasSpecialNeedsTraining,
    };
  },
  setup() {
    return {
      v$: useVuelidate(),
    };
  },
  validations() {
    return {
      aboutMe: {
        required,
        minLength: minLength(100),
      },
      validJobCategories: {
        sameAsRawValue: sameAs(true),
      },
      hasSpecialNeedsTraining: {
        required(value) {
          return value !== -1;
        },
      },
      myExperience2: {
        required(value) {
          if (this.hasSpecialNeedsTraining === false) {
            return true;
          }

          return this.hasSpecialNeedsTraining === true && value !== null && value.length >= 50;
        },
      },
    };
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
      userMeta: (state) => state.userInfo.meta,
      currentStep: (state) => {
        const { meta } = state.profileWizard;
        const currentStep = meta.steps[meta.currentStep];
        return currentStep;
      },
      stepComplete: (state) => {
        const { meta } = state.profileWizard;
        const currentStep = meta.steps[meta.currentStep];
        return currentStep.complete;
      },
    }),
    storeModuleName() {
      return 'userInfo';
    },
    completedChildCare() {
      const { interestedInJobTypes } = this.userInfo;

      const completed = (interestedInJobTypes & c.jobType.babysitting
        || interestedInJobTypes & c.jobType.nannying
        || interestedInJobTypes & c.jobType.beforeSchoolCare
        || interestedInJobTypes & c.jobType.afterSchoolCare) > 0;

      return completed;
    },
    childCareSubtitle() {
      if (!this.interestedInChildcareJobs) return '';

      return this.completedChildCare ? 'Completed' : 'Incomplete';
    },
    childCareSubtitleClass() {
      if (!this.interestedInChildcareJobs) return '';

      const { interestedInJobTypes } = this.userInfo;
      const completed = (interestedInJobTypes & c.jobType.babysitting
        || interestedInJobTypes & c.jobType.nannying
        || interestedInJobTypes & c.jobType.beforeSchoolCare
        || interestedInJobTypes & c.jobType.afterSchoolCare) > 0;

      return completed ? 'text-primary text-italic' : 'text-red text-italic';
    },
    completedAuPair() {
      const { interestedInJobTypes } = this.userInfo;
      const completed = (interestedInJobTypes & c.jobType.auPair) > 0;
      // const hasVideo = this.userInfo.medias.length > 0;
      let nonJsRefs = 0;
      this.userInfo.vouches.forEach((v) => {
        if (v.referenceType !== c.vouchReferenceType.juggleStreet) {
          nonJsRefs++;
        }
      });

      return completed && nonJsRefs >= 1;
    },
    auPairSubtitle() {
      if (!this.interestedInAuPairJobs) return '';

      return this.completedAuPair ? 'Completed' : 'Incomplete';
    },
    auPairSubtitleClass() {
      if (!this.interestedInAuPairJobs) return '';

      return this.completedAuPair ? 'text-primary text-italic' : 'text-red text-italic';
    },
    completedTutoring() {
      const { interestedInJobTypes } = this.userInfo;
      const completed = (interestedInJobTypes & c.jobType.primarySchoolTutoring
        || interestedInJobTypes & c.jobType.highSchoolTutoring) > 0;
      const requiresVideo = !this.userInfo.provider.interestedInOnlineTutoring || this.userInfo.medias.length > 0;

      return completed && requiresVideo;
    },
    tutoringSubtitle() {
      if (!this.interestedInTutoringJobs) return '';

      return this.completedTutoring ? 'Completed' : 'Incomplete';
    },
    tutoringSubtitleClass() {
      if (!this.interestedInTutoringJobs) return '';

      return this.completedTutoring ? 'text-primary text-italic' : 'text-red text-italic';
    },
    validJobCategories() {
      return this.interestedInChildcareJobs || this.interestedInAuPairJobs || this.interestedInTutoringJobs;
    },
    myExperience2: {
      get() {
        return this.userInfo.provider.myExperience2;
      },
      set(value) {
        this.setProperty('provider.myExperience2', value);
        this.triggerChange();
      },
    },
    interestedInChildcareJobs: {
      get() {
        return this.userInfo.interestedInChildcareJobs;
      },
      set(value) {
        this.setProperty('interestedInChildcareJobs', value);
        this.triggerChange();
      },
    },
    interestedInAuPairJobs: {
      get() {
        return this.userInfo.interestedInAuPairJobs;
      },
      set(value) {
        this.setProperty('interestedInAuPairJobs', value);
        this.triggerChange();
      },
    },
    interestedInTutoringJobs: {
      get() {
        return this.userInfo.interestedInTutoringJobs;
      },
      set(value) {
        this.setProperty('interestedInTutoringJobs', value);
        this.triggerChange();
      },
    },
    aboutMeHelper() {
      if (this.v$.aboutMe.$error && this.aboutMe) {
        return `required - ${100 - this.aboutMe.length} characters left`;
      }

      return 'required - min 100 characters';
    },
    myExperience2Helper() {
      if (this.v$.myExperience2.$error && this.myExperience2) {
        return `required - ${50 - this.myExperience2.length} characters left`;
      }

      return 'required - min 50 characters';
    },
    aboutMe: {
      get() {
        return this.userInfo.aboutMe;
      },
      set(value) {
        this.setProperty('aboutMe', value);
        this.triggerChange();
      },
    },
  },
  methods: {
    ...mapActions([
      'updateUserInfo',
    ]),
    isValid() {
      return this.validJobCategories && this.aboutMe !== null && this.aboutMe.length >= 100
      && (this.hasSpecialNeedsTraining === false || (this.myExperience2 !== null && this.myExperience2.length >= 50));
    },
    validate() {
      if (this.currentStep.meta.canSkip) {
        return true;
      }

      this.v$.$touch();
      if (this.v$.$error) {
        NotificationService.negative('Please review highlighted fields.');
      }

      return !this.v$.$error;
    },
    triggerChange() {
      const canSkip = !this.validJobCategories && (this.aboutMe === null || this.aboutMe.length === 0);
      this.$store.commit(`profileWizard/${mutationTypes.UPDATE_STEP_STATUS}`, { canSkip });

      if (this.isValid()) {
        this.$store.commit(`profileWizard/${mutationTypes.UPDATE_STEP_STATUS}`, { complete: true });
      }
    },
  },
  watch: {
    hasSpecialNeedsTraining() {
      if (this.myExperience2 === null) {
        this.myExperience2 = '';
      }
    },
  },
  beforeRouteLeave(to, from, next) {
    // Update user info before proceeding to the next step if step is complete
    if (this.stepComplete) {
      this.updateUserInfo().then(() => {
        beforeLeaveStep(this, to, from, next);
      });
    } else {
      beforeLeaveStep(this, to, from, next);
    }
  },
};
</script>
