<template>
  <div>
      <q-list no-border>
        <q-item-label header class="upsell-payment-header">
          <div class="text-center q-my-xs">
            <span v-if="isSubscription">
              You currently have a {{currentPaymentTypeText}} subscription. To post this job please upgrade your subscription and you’ll receive a pro-rata discount
            </span>
            <span v-else class="text-primary">Subscriptions include</span>
          </div>
          <q-item v-if="isIndividual" class="text-left upsell-subscription-benefits">
            <q-item-section>
              <q-item-label caption><q-icon name="check" color="primary" class="upsell-check-icon"/>Post unlimited jobs</q-item-label>
              <q-item-label caption><q-icon name="check" color="primary" class="upsell-check-icon"/>In-app Chat with helpers invited to jobs</q-item-label>
              <q-item-label caption><q-icon name="check" color="primary" class="upsell-check-icon"/>Cancel subscription anytime</q-item-label>
              <q-item-label caption>
                <img class="upsell-insurance-logo" src="https://assets.jugglestreet.com/images/common/aon-insurance-logo.png" />
                <small>Insurance included</small>
              </q-item-label>
            </q-item-section>
          </q-item>
          <q-item v-else class="">
            <q-item-section>
               <!-- <span class="text-grey q-ml-sm">One-off casual jobs</span> -->
            <q-item-label class="upsell-item" v-if="canAccessChildcareJobs"><q-icon name="check" color="primary" class="q-mr-sm" size="sm"/> One-off Jobs</q-item-label>
            <q-item-label class="upsell-item" v-if="canAccessChildcareJobs"><q-icon name="check" color="primary" class="q-mr-sm" size="sm"/> Recurring Jobs</q-item-label>
            <q-item-label class="upsell-item" v-if="canAccessChildcareJobs"><q-icon name="check" color="primary" class="q-mr-sm" size="sm"/> Outside School Hours Jobs</q-item-label>
            <!-- <q-item-label class="upsell-item" v-if="canAccessTutoringJobs"><q-icon name="check" color="primary" class="q-mr-sm" size="sm"/>Primary School Tutoring</q-item-label>
            <q-item-label class="upsell-item" v-if="canAccessTutoringJobs">
              <q-icon name="check" color="primary" class="q-mr-sm" size="sm"/>
              {{country === 'au' ? 'High School' : 'Secondary School'}} Tutoring
            </q-item-label> -->
            <q-item-label class="upsell-item"><q-icon name="check" color="primary" class="q-mr-sm" size="sm"/>In-app Chat with helpers invited to jobs</q-item-label>
            </q-item-section>
          </q-item>
        </q-item-label>
        <q-item v-for="(option, index) in paymentTypes" tag="label" :key="index">
          <q-item-section side top>
            <q-radio v-model="userPaymentType" :val="option" />
          </q-item-section>
          <!-- <q-item-section>
            <q-item-label>{{getPaymentTypeText(option)}}</q-item-label>
            <q-item-label>Annual Subscription  {{getAmountForPaymentType(option)}}</q-item-label>
            <q-item-label v-if="canPostJob(option, types.jobType.babysitting)" caption><q-icon name="check" color="primary" class="upsell-check-icon"/>Babysitting</q-item-label>
            <q-item-label v-if="canPostJob(option, types.jobType.nannying)" caption><q-icon name="check" color="primary" class="upsell-check-icon"/>Nannying</q-item-label>
            <q-item-label v-if="canPostJob(option, types.jobType.nannying)" caption><q-icon name="check" color="primary" class="upsell-check-icon"/>Before & After School Care</q-item-label>
            <q-item-label v-if="canPostJob(option, types.jobType.primarySchoolTutoring)" caption><q-icon name="check" color="primary" class="upsell-check-icon"/>Primary School Tutoring</q-item-label>
            <q-item-label v-if="canPostJob(option, types.jobType.highSchoolTutoring)" caption>
              <q-icon name="check" color="primary" class="upsell-check-icon"/>
              {{country === 'au' ? 'High School' : 'Secondary School'}} Tutoring
            </q-item-label>
            <q-item-label v-if="canPostJob(option, types.jobType.auPair)" caption><q-icon name="check" color="primary" class="upsell-check-icon"/>Au Pair</q-item-label>
          </q-item-section> -->
          <q-item-section>
            <q-item-label class="text-bold">{{getPaymentTypeText(option)}}</q-item-label>
            <q-item-label class="text-bold">{{getPaymentFrequencyForPaymentType(option)}}  {{getAmountForPaymentType(option)}}</q-item-label>
            <q-item-label v-if="displaySpecialOffer(option)" :class="getSpecialOfferClass(option)">{{getSpecialOffer(option)}}</q-item-label>
            <q-item-label v-if="isIndividual" caption><q-icon :name="getJobIcon(option, types.jobType.babysitting)" :color="getJobColor(option, types.jobType.babysitting)" class="upsell-check-icon"/>One-off Babysitting</q-item-label>
            <q-item-label v-if="isIndividual" caption><q-icon :name="getJobIcon(option, types.jobType.nannying)" :color="getJobColor(option, types.jobType.nannying)" class="upsell-check-icon"/>Nannying - recurring long-term</q-item-label>
            <q-item-label v-if="isIndividual" caption><q-icon :name="getJobIcon(option, types.jobType.nannying)" :color="getJobColor(option, types.jobType.nannying)" class="upsell-check-icon"/>Before & After School Care</q-item-label>
            <q-item-label v-if="isIndividual" caption><q-icon :name="getJobIcon(option, types.jobType.primarySchoolTutoring)" :color="getJobColor(option, types.jobType.primarySchoolTutoring)" class="upsell-check-icon"/>Primary School Tutoring</q-item-label>
            <q-item-label caption v-if="isIndividual">
              <q-icon :name="getJobIcon(option, types.jobType.highSchoolTutoring)" :color="getJobColor(option, types.jobType.highSchoolTutoring)" class="upsell-check-icon"/>{{country === 'au' ? 'High School' : 'Secondary School'}} Tutoring
            </q-item-label>
            <!-- <q-item-label  v-if="canPostJob(option, types.jobType.auPair)" caption><q-icon :name="getJobIcon(option, types.jobType.auPair)" :color="getJobColor(option, types.jobType.auPair)" class="upsell-check-icon"/>Au Pair</q-item-label> -->
          </q-item-section>
        </q-item>
      </q-list>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import {
  getPaymentTypeText,
  getAmountForPaymentType,
  getUpsellOptions,
  getPaymentFrequencyForPaymentType,
  getAnnualPaymentFrequencyForPaymentType,
} from '../utils/helpers';
import c from '../utils/constants';

export default {
  name: 'PaymentType',
  emits: ['paymentTypeChanged'],
  data() {
    return {
      types: {
        jobType: c.jobType,
      },
      userPaymentType: this.paymentType,
    };
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
      paymentInfo: (state) => state.userInfo.data.paymentInfo,
      walletBalance: (state) => state.userInfo.data.walletBalanceDecimal,
    }),
    isSubscription() {
      return this.userInfo.membershipType === c.membershipType.subscription;
    },
    currentPaymentTypeText() {
      if (this.isSubscription) {
        return this.getPaymentTypeText(this.userInfo.paymentInfo.paymentType);
      }

      return 'None';
    },
    paymentTypes() {
      return getUpsellOptions(this.userInfo, this.jobType);
    },
  },
  props: ['paymentType', 'jobType'],
  methods: {
    getSpecialOfferClass(option) {
      switch (option) {
        case c.userPaymentType.annualBusinessTier1Subscription1500:
        case c.userPaymentType.annualBusinessTier2Subscription1500:
          return 'text-brand-gold';
        default:
          return '';
      }
    },
    displaySpecialOffer(option) {
      switch (option) {
        case c.userPaymentType.quarterlyBusinessTier1Subscription500:
        case c.userPaymentType.quarterlyBusinessTier2Subscription500:
        case c.userPaymentType.annualBusinessTier1Subscription1500:
        case c.userPaymentType.annualBusinessTier2Subscription1500:
          return true;
        default:
          return false;
      }
    },
    getSpecialOffer(option) {
      switch (option) {
        case c.userPaymentType.quarterlyBusinessTier1Subscription500:
        case c.userPaymentType.quarterlyBusinessTier2Subscription500:
          return `Annual Cost ${this.getAnnualAmountForPaymentType(option)}`;
        default:
          return 'SAVE 25% per annum';
      }
    },
    getPaymentTypeText(paymentType) {
      return getPaymentTypeText(paymentType);
    },
    getAmountForPaymentType(paymentType) {
      const amount = getAmountForPaymentType(paymentType);
      const cost = Math.abs(amount).toFixed(0);
      return `$${cost}`;
    },
    getAnnualAmountForPaymentType(paymentType) {
      const amount = getAmountForPaymentType(paymentType);
      const frequency = getAnnualPaymentFrequencyForPaymentType(paymentType);
      const cost = Math.abs(amount * frequency).toLocaleString();
      return `$${cost}`;
    },
    getPaymentFrequencyForPaymentType(option) {
      return getPaymentFrequencyForPaymentType(option);
    },
    canPostJob(paymentType, jobType) {
      if (paymentType === c.userPaymentType.annualBabysittingSubscription60
        || paymentType === c.userPaymentType.annualBabysittingSubscription120) {
        return jobType === c.jobType.babysitting;
      }

      if (paymentType === c.userPaymentType.annualChildcareSubscription120
        || paymentType === c.userPaymentType.annualChildcareSubscription180
        || paymentType === c.userPaymentType.quarterlyBusinessTier1Subscription500
        || paymentType === c.userPaymentType.annualBusinessTier1Subscription1500) {
        return jobType <= c.jobType.beforeAndAfterSchoolCare;
      }

      if (paymentType === c.userPaymentType.annualTutoringSubscription180
        || paymentType === c.userPaymentType.quarterlyBusinessTier2Subscription500
        || paymentType === c.userPaymentType.annualBusinessTier2Subscription1500) {
        return jobType === c.jobType.primarySchoolTutoring || jobType === c.jobType.highSchoolTutoring;
      }

      if (paymentType === c.userPaymentType.annualFamilyCareSubscription240) {
        return jobType !== c.jobType.auPair;
      }

      return true;
    },
    getJobIcon(paymentType, jobType) {
      const isJobSupported = this.canPostJob(paymentType, jobType);
      return isJobSupported ? 'check' : 'clear';
    },
    getJobColor(paymentType, jobType) {
      const isJobSupported = this.canPostJob(paymentType, jobType);
      return isJobSupported ? 'primary' : 'red';
    },
  },
  watch: {
    userPaymentType(value) {
      this.$emit('paymentTypeChanged', value);
    },
  },
};
</script>
