<template>
  <q-page class="layout-padding">
    <div class="q-my-md" v-if="userInfo.paymentInfo.frontDocumentIdVerificationStatus === 0">
      <q-field
        borderless
        label="ID Document - Front">
        <template v-slot:control>
          <div class="full-width q-my-md">
            <q-btn @click="handleUploadDocumentIdFront" color="accent">
              Upload Document
            </q-btn>
            <file-uploader
              ref="id-document-front"
              propertyName="paymentInfo.frontDocumentIdBinary"
              @uploadSuccess="handleUploadSuccess"
              @uploadFailure="handleUploadFailure"
            />
          </div>
        </template>
      </q-field>
    </div>
    <div class="q-my-md" v-if="userInfo.paymentInfo.backtDocumentIdVerificationStatus === 0">
      <q-field
        borderless
        label="ID Document - Back">
          <template v-slot:control>
            <div class="full-width q-my-md">
            <q-btn @click="handleUploadDocumentIdBack" color="accent">
              Upload Document
            </q-btn>
            <file-uploader
              ref="id-document-back"
              propertyName="paymentInfo.backDocumentIdBinary"
              @uploadSuccess="handleUploadSuccess"
              @uploadFailure="handleUploadFailure"
            />
          </div>
        </template>
      </q-field>
    </div>
    <div class="q-my-md">
      <q-field
        borderless
        label="Transfers">
        <template v-slot:control>
          <div class="full-width q-my-sm">
            <!-- <span>Recent transfers may take few hours to show up.</span> -->
            <table class="q-table horizontal-separator transfers-table" v-if="transfers && transfers.length > 0">
              <thead>
                <tr>
                  <th class="text-center">Date</th>
                  <th class="text-center">Description</th>
                  <th class="text-center">Total</th>
                </tr>
              </thead>
              <tbody>
                <tr  v-for="transfer in transfers" :key="transfer.id">
                  <td class="text-center">{{ formatDate(transfer.date) }}</td>
                  <td class="text-center">{{ transfer.description }}</td>
                  <td class="text-center">{{ formatCurrency(transfer.total) }}</td>
                </tr>
              </tbody>
            </table>
            <div v-if="transfersMeta.lastFetched !== null && transfers.length === 0">
              No transfers have been received.
            </div>
          </div>
        </template>
      </q-field>
    </div>
    <div class="q-my-md">
      <q-field
        borderless
        label="Payouts">
        <template v-slot:control>
          <div class="full-width q-my-sm">
            <!-- <span>Recent transfers may take few hours to show up.</span> -->
            <table class="q-table horizontal-separator payouts-table" v-if="payouts && payouts.length > 0">
              <thead>
                <tr>
                  <th class="text-center">Date</th>
                  <th class="text-center">Description</th>
                  <th class="text-center">Total</th>
                </tr>
              </thead>
              <tbody>
                <tr  v-for="payout in payouts" :key="payout.id">
                  <td class="text-center">{{ formatDate(payout.date) }}</td>
                  <td class="text-center">{{ payout.description }}</td>
                  <td class="text-center">{{ formatCurrency(payout.total) }}</td>
                </tr>
              </tbody>
            </table>
            <div v-if="payoutsMeta.lastFetched !== null && payouts.length === 0">
              No payouts have been mode.
            </div>
          </div>
        </template>
      </q-field>
    </div>
  </q-page>
</template>

<script>
import { mapState } from 'vuex';
import { date } from 'quasar';
import FileUploader from 'components/FileUploader';
import { formatCurrency } from '../../utils/helpers';
import NotificationService from '../../utils/notification-service';

export default {
  name: 'PageAccountHelperPayments',
  components: { FileUploader },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
      transfers: (state) => state.payments.data.transfers,
      transfersMeta: (state) => state.payments.meta.transfers,
      payouts: (state) => state.payments.data.payouts,
      payoutsMeta: (state) => state.payments.meta.payouts,
    }),
  },
  data() {
    return {

    };
  },
  methods: {
    formatDate(invoiceDate) {
      return date.formatDate(invoiceDate, 'DD/MM/YYYY');
    },
    formatCurrency(amount) {
      return formatCurrency(amount);
    },
    handleUploadDocumentIdFront() {
      this.$refs['id-document-front'].open();
    },
    handleUploadDocumentIdBack() {
      this.$refs['id-document-back'].open();
    },
    handleUploadSuccess() {
      console.log('success');
    },
    handleUploadFailure() {
      NotificationService.negative('An error occurred. Please try again later.');
    },
  },
  created() {
    this.$store.dispatch('payments/fetchTransfers');
    this.$store.dispatch('payments/fetchPayouts');
  },
};
</script>
