<template>
  <div class="">
    <profile-tabs :helper="helper" :selectedTab="selectedTab" v-on:profileTabChange="handleProfileTabChange" />
    <!-- <profile-brief :helper="helper"  v-on:scrollToReviews="scrollToProfileReviews" /> -->
    <profile-overview :displayRatings="true" :displayReviews="true" :displayJobStats="true" :displayDistance="false" :helper="helper" :mode="mode" :isGuardianApprovalMode="isGuardianApprovalMode" v-on:scrollToReviews="scrollToProfileReviews" :displayVideo="true" v-on:profileTabChange="handleProfileTabChange"/>

    <q-card-section style="padding: 4px 8px">
      <q-field
        label="Tutor Summary"
        borderless
        hide-bottom-space
        readonly>
        <template v-slot:prepend>
          <q-icon name="work" />
        </template>
        <template v-slot:control>
          <div class="full-width q-mt-md row justify-between">
            <div class="">
              <q-checkbox v-model="interestedInPrimarySchool" readonly label="Primary School" unchecked-icon="check_box_outline_blank" />
              <br />
              <q-checkbox v-model="interestedInHighSchool" readonly label="High School" unchecked-icon="check_box_outline_blank" />
            </div>

            <div v-if="interestedInHighSchool">
              <div>
                <span class="text-grey  q-mr-xs">Completed Year 12 </span>
                <span class="q-mx-sm">{{year12GraduationYear}}</span>
              </div>

              <div>
                <span class="text-grey q-mr-xs">Teaches Study Skills</span>
                <q-radio v-if="canTeachStudySkills" v-model="canTeachStudySkills" label="Yes" :val="true" readonly />
                <q-radio v-if="!canTeachStudySkills" v-model="canTeachStudySkills" label="No" :val="false" readonly />
              </div>

              <div>
                <span class="text-grey q-mr-sm">Teaches Exam Prep</span>
                <q-radio v-if="canTeachExamPreparation" v-model="canTeachExamPreparation" label="Yes" :val="true" readonly />
                <q-radio v-if="!canTeachExamPreparation" v-model="canTeachExamPreparation" label="No" :val="false" readonly />
              </div>
            </div>
          </div>
        </template>
      </q-field>

      <q-field
        label="Tutoring Experience"
        v-if="helper.providerMyExperience3"
        borderless
        readonly
        >
        <template v-slot:prepend>
          <q-icon name="school" />
        </template>
        <template v-slot:control>
          <div class="full-width q-mt-md">
            <div class="row">
              <q-radio class="col-6 q-my-xs" readonly v-model="tutoringCategory" :val="1" label="Newbie" />
              <q-radio class="col-6 q-my-xs" readonly v-model="tutoringCategory" :val="2" label="Apprentice" />
              <q-radio class="col-6 q-my-xs" readonly v-model="tutoringCategory" :val="3" label="Experienced" />
              <q-radio class="col-6 q-my-xs" readonly v-model="tutoringCategory" :val="4" label="Professional" />
            </div>
            {{providerMyExperience3}}
            <span v-if="helper.providerMyExperience3 && helper.providerMyExperience3.length > 300" style="text-decoration: underline"
                @click="displayAllExperience = !displayAllExperience">{{displayAllExperience ? 'less' : 'more'}}</span>
            </div>
          </template>
      </q-field>

      <q-field label="Qualifications" v-if="qualificationsList" borderless readonly hide-bottom-space>
        <template v-slot:prepend>
          <q-icon name="local_hospital" />
        </template>
        <template v-slot:control>
          <div class="full-width q-mt-md">
            <div v-html="qualificationsList"/>
          </div>
        </template>
      </q-field>

      <q-field icon="local_library" label="Primary School" v-if="interestedInPrimarySchool" borderless readonly hide-bottom-space>
        <template v-slot:prepend>
          <q-icon name="local_hospital" />
        </template>
        <template v-slot:control>
          <div class="full-width q-mt-md">
            <div class="row">
              <p class="col-6 no-margin" v-for="(item) in primarySchoolYears" :key="item.optionId">
                <q-checkbox v-model="item.selected" disable :label="item.text" unchecked-icon="check_box_outline_blank" />
              </p>
            </div>
            <q-field label="Subjects" class="" borderless readonly>
              <template v-slot:control>
                <div class="full-width q-mt-md row">
                  <p class="col-6  no-margin" v-for="(item) in primarySchoolSubjects" :key="item.optionId">
                    {{item.text}}
                  </p>
                </div>
              </template>
            </q-field>
          </div>
        </template>
      </q-field>

      <q-field label="High School" v-if="interestedInHighSchool" borderless readonly>
        <template v-slot:prepend>
          <q-icon name="local_library" />
        </template>
        <template v-slot:control>
          <div class="full-width q-mt-md">
            <div class="row">
              <p class="col-6 no-margin" v-for="(item) in highSchoolYears" :key="item.optionId">
                <q-checkbox v-model="item.selected" disable :label="item.text" unchecked-icon="check_box_outline_blank" />
              </p>
            </div>

            <q-field label="Subjects" borderless readonly>
              <template v-slot:control>
                <div class="full-width q-mt-md row">
                  <p class="col-6 no-margin" v-for="(item) in highSchoolSubjects" :key="item.optionId">
                    {{item.text}}
                  </p>
                </div>
              </template>
            </q-field>
          </div>
        </template>
      </q-field>

      <profile-checks :helper="helper" />

      <helper-reviews :helper="helper" ref="profileReviews"/>
    </q-card-section>
  </div>
</template>

<script>
import ProfileTabs from 'components/helper-profile/ProfileTabs';
import ProfileOverview from 'components/helper-profile/ProfileOverview';
import ProfileChecks from 'components/helper-profile/ProfileChecks';
import HelperReviews from 'components/helper-profile/HelperReviews';
import c from '../../utils/constants';
import { scrollToElement, getSelectedOptions, getOption } from '../../utils/helpers';
import datasets from '../../utils/datasets';

export default {
  name: 'TutorTab',
  components: {
    ProfileTabs, ProfileOverview, ProfileChecks, HelperReviews,
  },
  props: ['helper', 'mode', 'selectedTab', 'isGuardianApprovalMode'],
  emits: ['profileTabChange'],
  data() {
    return {
      displayAllExperience: false,
    };
  },
  computed: {
    providerMyExperience3() {
      if (!this.helper.providerMyExperience3) return '';

      if (this.helper.providerMyExperience3.length < 300 || this.displayAllExperience) {
        return this.helper.providerMyExperience3;
      }

      const idx = this.helper.providerMyExperience3.indexOf(' ', 300);
      if (idx === -1) return this.helper.providerMyExperience3;

      return `${this.helper.providerMyExperience3.substring(0, idx)}...`;
    },
    primarySchoolYears() {
      const { primarySchoolYears } = this.helper;

      return primarySchoolYears;
    },
    highSchoolYears() {
      const { highSchoolYears } = this.helper;

      return highSchoolYears;
    },
    tutoringCategory() {
      return this.helper.providerTutoringCategory;
    },
    primarySchoolSubjects() {
      const { primarySchoolSubjects } = this.helper;
      if (!primarySchoolSubjects) return '';

      const selectedSubjects = getSelectedOptions(primarySchoolSubjects);
      const subjectsText = [];

      selectedSubjects.forEach((subject) => {
        const option = getOption(subject, primarySchoolSubjects);
        subjectsText.push(option);
      });

      return subjectsText;
    },
    highSchoolSubjects() {
      const { highSchoolSubjects } = this.helper;
      if (!highSchoolSubjects) return '';

      const selectedSubjects = getSelectedOptions(highSchoolSubjects);
      const subjectsText = [];

      selectedSubjects.forEach((subject) => {
        const option = getOption(subject, highSchoolSubjects);
        subjectsText.push(option);
      });

      return subjectsText;
    },
    interestedInPrimarySchool() {
      const { interestedInJobTypes } = this.helper;

      return (interestedInJobTypes & c.jobType.primarySchoolTutoring) > 0;
    },
    interestedInHighSchool() {
      const { interestedInJobTypes } = this.helper;

      return (interestedInJobTypes & c.jobType.highSchoolTutoring) > 0;
    },
    canTeachStudySkills() {
      const { providerCanTeachStudySkills } = this.helper;

      return providerCanTeachStudySkills;
    },
    canTeachExamPreparation() {
      const { providerCanTeachExamPreparation } = this.helper;

      return providerCanTeachExamPreparation;
    },
    year12GraduationYear() {
      const { providerYear12GraduationYear } = this.helper;

      return providerYear12GraduationYear;
    },
    countriesList() {
      return datasets.countriesIso;
    },
    qualificationsList() {
      return this.getList('qualifications', '<br />');
    },
    citizenship() {
      const { nationality } = this.helper;

      if (!nationality) return null;

      const matches = this.countriesList.find((country) => country.code === nationality);

      if (!matches || matches.length === 0) return null;

      return matches[0].name;
    },
  },
  methods: {
    handleProfileTabChange(args) {
      this.$emit('profileTabChange', args);
    },
    scrollToProfileReviews() {
      scrollToElement(this.$refs.profileReviews.$el, -50);
    },
    getList(groupName, sep) {
      if (!this.helper) {
        return '';
      }

      const selectedList = [];
      const list = this.helper[groupName];
      for (let i = 0; i < list.length; i++) {
        if (list[i].selected) {
          selectedList.push(list[i].text);
        }
      }

      if (selectedList.length > 0) {
        return selectedList.join(sep);
      }

      return '';
    },
  },
};
</script>
