<template>
  <q-page class="">
    <div class="layout-padding">
      <div class="q-ml-xs">
        <filters-section :filterType="filterKey" :jobType="job.jobType"/>
      </div>
      <div class="text-center q-my-sm">
        <q-btn
          color="accent"
          @click="handleChangeCriteria"
        >
          Confirm
        </q-btn>
        <div class="q-my-sm text-bold">

        </div>
      </div>
    </div>
  </q-page>
</template>

<script>
import { mapState } from 'vuex';
import FiltersSection from 'components/FiltersSection';
import { getJobModuleName, getJuggleAssistFilters } from '../../utils/helpers';
import NotificationService from '../../utils/notification-service';
import store from '../../store';
import c from '../../utils/constants';

export default {
  name: 'PageFamiliesChangeCandidateCriteria',
  components: { FiltersSection },
  data() {
    return {
      searchText: '',
    };
  },
  computed: {
    ...mapState({
      job: (state) => state.families.pages.jobDetails.data,
      helpersMeta: (state) => state.filters.meta,
    }),
    storeModuleName() {
      return 'families.pages.jobDetails';
    },
    moduleName() {
      return getJobModuleName(this.job.jobType);
    },
    filterKey() {
      if (this.job.jobType === c.jobType.primarySchoolTutoring || this.job.jobType === c.jobType.highSchoolTutoring) {
        if (this.job.jobDeliveryMethod === c.jobDeliveryMethod.online) {
          return 'postOnlineTutoringJob';
        }

        return 'postHomeTutoringJob';
      }
      return this.moduleName;
    },
    helpers() {
      return this.filteredHelpers[this.filterKey];
    },
  },
  methods: {
    validate() {
      if (this.helpersMeta.total < 1) {
        NotificationService.negative('No helpers match specified criteria.');
        return false;
      }

      return true;
    },
    handleChangeCriteria() {
      if (!this.validate()) {
        return;
      }

      this.setProperty('actionType', c.jobActionType.changeCandidateCriteria);
      const filters = this.$store.state.filters.filterInfo[this.filterKey];
      const processedFilters = getJuggleAssistFilters(filters);
      this.setProperty('applicantFilters', processedFilters);

      this.$store.dispatch('families/updateJobDetails', this.filterKey).then(() => {
        this.routerReplace(`/families/job-details/${this.$route.params.id}`);
      });
    },
  },
  beforeRouteEnter(to, from, next) {
    const job = store.state.families.pages.jobDetails.data;
    if (!job || !job.id) {
      const path = `/families/job-details/${to.params.id}`;
      next({ path });
      return;
    }

    next();
  },
};
</script>
