<template>
  <div>
    <q-field
      label="Profile Video"
      readonly
      borderless>
        <template v-slot:prepend>
          <q-icon name="video_call" />
        </template>
    </q-field>
    <div class="q-mt-sm q-mx-sm">
      <div v-if="!hasMedia" class="q-my-sm">
        <p class="">
          <span class="text-primary text-bold">{{entryPercentage}}</span>
        </p>
        <video-uploader target="about_me" v-on:uploadStart="handleUploadStart" v-on:uploadSuccess="handleUpdateMedia" />
        <p class="q-mt-sm">
        </p>
      </div>
      <div v-else class="q-my-sm">
        <div v-html="video"></div>
      </div>
      <div class="q-my-sm text-center">
        <div v-if="hasMedia">
          {{reviewStatus}}
          <div class="row justify-center">
            <q-btn color="secondary" @click="handleDeleteVideo">Delete Video</q-btn>
          </div>
        </div>
        <div v-else>
            Upload a short video, this is the best way to introduce yourself to new families. Helpers with a video receive more job invitations.
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex';
import VideoUploader from 'components/VideoUploader';
import c from '../utils/constants';
import NotificationService from '../utils/notification-service';

export default {
  name: 'ProfileVideo',
  components: { VideoUploader },
  props: [],
  emits: ['complete', 'delete', 'change'],
  data() {
    return {
      isUploading: false,
    };
  },
  computed: {
    ...mapActions([
      'updateUserInfo',
    ]),
    storeModuleName() {
      return 'userInfo';
    },
    ...mapState({
      userInfo: (state) => state.userInfo.data,
    }),
    hasMedia() {
      return this.userInfo.medias.length > 0;
    },
    video() {
      if (this.userInfo.medias.length === 0) return '';

      const video = this.userInfo.medias[0];
      const mediaId = video.externalHashedId;

      if (!mediaId) {
        return '';
      }

      return `
        <iframe src="https://fast.wistia.net/embed/iframe/${mediaId}?playerColor=299C4A"
          width="100%" height="250px" allowtransparency="true" frameborder="0" scrolling="no"
          class="wistia_embed" name="wistia_embed" allowfullscreen mozallowfullscreen webkitallowfullscreen oallowfullscreen msallowfullscreen>
        </iframe>
      `;
    },
    reviewStatus() {
      if (this.userInfo.medias.length === 0) return '';

      const video = this.userInfo.medias[0];
      const reviewStatus = video.mediaReviewStatus;

      if (reviewStatus === c.mediaReviewStatus.approved) {
        return 'Your profile video has been approved by our team. Families visiting your profile can now see the video.';
      }

      if (reviewStatus === c.mediaReviewStatus.pending) {
        return 'Your video is being reviewed and will be approved shortly.';
      }

      if (reviewStatus === c.mediaReviewStatus.rejected) {
        return 'The profile video you have uploaded has been rejected by our team. Please upload a different video.';
      }

      return '';
    },
    pendingProfileTotalPoints() {
      let score = 0;
      this.userInfo.rankingItems.forEach((item) => {
        if (item.userManageable) {
          score += item.pointsPerEntry;
        }
      });

      return score;
    },
    entryPercentage() {
      const entry = this.userInfo.rankingItems.find((r) => r.rankingCategory === 'profile.video');
      if (!entry || entry.entriesCount > 0) return '';

      const perc = Math.round((entry.pointsPerEntry / this.pendingProfileTotalPoints) * 100);
      return `Add ${perc}% to your Profile Strength`;
    },
  },
  methods: {
    validate() {
      return !this.isUploading;
    },
    handleUpdateMedia(media) {
      const medias = [];
      if (media) {
        medias.push(media);
      }

      this.setProperty('medias', medias);
      this.updateUserInfo.then(() => {
        this.isUploading = false;
        if (medias.length > 0) {
          this.$emit('complete');
        } else {
          this.$emit('delete');
        }
      }).catch((error) => {
        NotificationService.negative(error.message);
      });
    },
    handleUploadStart() {
      this.isUploading = true;
      this.$emit('change', { valid: false });
    },
    handleDeleteVideo() {
      const self = this;
      this.$q.dialog({
        title: 'Important',
        message: 'Are you sure you want to delete this video?',
        cancel: {
          label: 'No',
          color: 'secondary',
          outline: true,
        },
        ok: {
          label: 'Yes',
          color: 'secondary',
          outline: true,
        },
      }).onOk(() => {
        self.handleUpdateMedia();
      }).onCancel(() => {});
    },
    confirmExit(done, error) {
      const self = this;
      this.$q.dialog({
        title: 'Please Wait',
        message: 'We are busy Queuing, Encoding and Uploading your video. If you exit your video will be lost. Would you like to exit?',
        cancel: {
          label: 'No',
          color: 'secondary',
          outline: true,
        },
        ok: {
          label: 'Yes',
          color: 'secondary',
          outline: true,
        },
      }).onOk(() => {
        if (window.wistiaUploader && window.wistiaUploader.cancel) {
          window.wistiaUploader.cancel();
        }
        self.isUploading = false;
        done();
      }).onCancel(() => {
        error();
      });
    },
  },
};
</script>
