import { NativeStorage } from '@ionic-enterprise/nativestorage';
import { env } from './env';
import errorHandler from './error-handler';

const nativeStorageAvailable = () => {
  if (env.deviceInfo.platform === 'web') return false;

  const isAvaiable = NativeStorage && NativeStorage.setItem && NativeStorage.getItem && NativeStorage.remove;
  return isAvaiable;
};

const setNativeStorage = async (key, value) => {
  try {
    if (!nativeStorageAvailable()) return;

    await NativeStorage.setItem(key, value);
  } catch (e) {
    errorHandler.captureException(e);
  }
};

const getNativeStorage = async (key) => {
  let value = null;
  try {
    if (!nativeStorageAvailable()) return null;

    value = await NativeStorage.getItem(key);
  } catch (e) {
    // NATIVE_WRITE_FAILED = 1
    // ITEM_NOT_FOUND = 2
    // NULL_REFERENCE = 3
    // UNDEFINED_TYPE = 4
    // JSON_ERROR = 5
    // WRONG_PARAMETER = 6

    if (e.code && e.code !== 2) {
      errorHandler.captureException(e);
    }
  }

  return value;
};

const removeNativeStorage = async (key) => {
  try {
    if (!nativeStorageAvailable()) return;

    await NativeStorage.remove(key);
  } catch (e) {
    if (e.code && e.code !== 2) {
      errorHandler.captureException(e);
    }
  }
};

const setCookie = (cname, cvalue, expiresIn) => {
  try {
    const d = new Date();
    if (expiresIn) {
      d.setTime(d.getTime() + (expiresIn * 24 * 60 * 60 * 1000));
    }
    const expires = `expires=${d.toUTCString()}`;

    let misc = '; path=/';
    if (!env.isTest) {
      misc += `;domain=${env.domain}`;
    }

    document.cookie = `${cname}=${cvalue}; ${expires}${misc}`;
  } catch (e) {
    errorHandler.captureException(e);
  }
};

const getCookie = (cname) => {
  try {
    const name = `${cname}=`;
    const ca = document.cookie.split(';');
    for (let i = 0; i < ca.length; i++) {
      let c = ca[i];
      while (c.charAt(0) === ' ') c = c.substring(1);
      if (c.indexOf(name) !== -1) return c.substring(name.length, c.length);
    }
    return '';
  } catch (e) {
    errorHandler.captureException(e);
    return '';
  }
};

const removeCookie = (cname) => {
  try {
    document.cookie = `${cname}=; expires=Thu, 01 Jan 1970 00:00:01 GMT;`;

    if (!env.isTest) {
      document.cookie = `${cname}=; expires=Thu, 01 Jan 1970 00:00:01 GMT; path=/;domain=${env.domain}`;
    }
  } catch (e) {
    errorHandler.captureException(e);
  }
};

const getStorageKeySuffix = () => {
  try {
    const hostname = window.location.hostname.toLowerCase();

    if (hostname.indexOf('devserver') === 0 || hostname.toLowerCase().indexOf('frontendserver') === 0) {
      return '-local';
    }

    if (hostname.toLowerCase().indexOf('localhost') === 0 || hostname.toLowerCase().indexOf('dm') === 0) {
      return '-dev';
    }

    if (hostname.toLowerCase().indexOf('sm') === 0 || hostname.toLowerCase().indexOf('nzsm') === 0) {
      return '-staging';
    }

    if (hostname.toLowerCase().indexOf('vnextm') === 0) {
      return '-vnext';
    }

    return '';
  } catch (e) {
    errorHandler.captureException(e);
  }

  return '';
};

const setValue = async (key, value, persist) => {
  const envKey = key + getStorageKeySuffix();

  let expiresIn;
  if (persist) {
    await setNativeStorage(envKey, value);
    expiresIn = 365;
  }

  setCookie(envKey, value, expiresIn);
};

const getValue = async (key) => {
  const envKey = key + getStorageKeySuffix();
  let value = await getNativeStorage(envKey);
  if (!value) {
    value = getCookie(envKey);
  }

  return value;
};

const removeValue = async (key) => {
  const envKey = key + getStorageKeySuffix();
  await removeNativeStorage(envKey);
  removeCookie(envKey);
};

export {
  setValue,
  getValue,
  removeValue,
};
