import axios from 'axios';
import { env } from '../utils/env';
import * as storageManager from '../utils/storage-manager';
import analyticsService from '../utils/analytics-service';

const accessTokenKey = 'jugglestreet-access-token';
const refreshTokenKey = 'jugglestreet-refresh-token';

let inMemoryAccessToken;
let inMemoryRefreshToken;

const loadTokens = async () => {
  inMemoryAccessToken = await storageManager.getValue(accessTokenKey);
  inMemoryRefreshToken = await storageManager.getValue(refreshTokenKey);
};

const setAccessToken = async (accessToken, refreshToken, persist) => {
  inMemoryAccessToken = accessToken;
  inMemoryRefreshToken = refreshToken;

  await storageManager.setValue(accessTokenKey, accessToken, persist);

  if (refreshToken) {
    await storageManager.setValue(refreshTokenKey, refreshToken, persist);
  }
};

const getAccessToken = () => inMemoryAccessToken;

const getRefreshToken = () => inMemoryRefreshToken;

const logout = async () => {
  await storageManager.removeValue(accessTokenKey);
  await storageManager.removeValue(refreshTokenKey);

  inMemoryAccessToken = undefined;
  inMemoryRefreshToken = undefined;
};

const login = (payload) => {
  const requestData = {
    userName: payload.username,
    password: payload.password,
    grantType: payload.grant_type,
    platform: env.deviceInfo.platform,
    appVersion: env.appInfo.version,
    distVersion: env.versionNumber,
  };
  const request = axios({
    method: 'POST',
    url: `${env.apiRoot}identity/token`,
    data: requestData,
  }).then(async (response) => {
    if (response.data) {
      await setAccessToken(response.data.accessToken, response.data.refreshToken, true);
    }

    analyticsService.logLogin();

    return response;
  });

  return request;
};

const refreshToken = () => {
  const requestData = {
    refreshToken: inMemoryRefreshToken,
    platform: env.deviceInfo.platform,
    appVersion: env.appInfo.version,
    distVersion: env.versionNumber,
  };
  const request = axios({
    method: 'POST',
    url: `${env.apiRoot}identity/refreshtoken`,
    data: requestData,
  }).then(async (response) => {
    if (response.data) {
      await setAccessToken(response.data.accessToken, response.data.refreshToken, true);
    }
  });

  return request;
};

const requestResetPasswordBySms = (payload) => axios({
  method: 'POST',
  url: `${env.apiRoot}account/requestresetpasswordbysms`,
  data: payload,
});

const resetPasswordBySms = (payload) => axios({
  method: 'POST',
  url: `${env.apiRoot}account/resetpasswordbysms`,
  data: payload,
});

const isAuthorized = () => inMemoryAccessToken;

export {
  loadTokens,
  isAuthorized,
  login,
  logout,
  refreshToken,
  getAccessToken,
  getRefreshToken,
  setAccessToken,
  requestResetPasswordBySms,
  resetPasswordBySms,
};
