<template>
  <q-page class="layout-padding">
    <profile-video ref="profileVideo" />
  </q-page>
</template>

<script>
import ProfileVideo from 'components/ProfileVideo';

export default {
  name: 'PageAccountProfileVideo',
  components: { ProfileVideo },
  data() {
    return {
    };
  },
  computed: {
  },
  methods: {
  },
  beforeRouteLeave(to, from, next) {
    if (this.$refs.profileVideo.isUploading) {
      this.$refs.profileVideo.confirmExit(() => {
        next();
      }, () => {
        next(false);
      });
    } else {
      next();
    }
  },
};
</script>
