<template>
  <q-page class="post-one-off-job-step-5">
    <div class="layout-padding">
      <q-field
        borderless
        readonly
        hide-bottom-space
      >
        <template v-slot:prepend>
          <q-icon name="date_range" />
        </template>
        <template v-slot:control>
          {{getJobDateText()}}
        </template>
      </q-field>

      <q-field
        borderless
        readonly
        hide-bottom-space
      >
        <template v-slot:prepend>
          <q-icon name="home" />
        </template>
        <template v-slot:control>
          <div>
            <span v-if="addressLabel" class="text-primary text-bold">{{addressLabel}}<br/></span>
            {{getAddress()}}
          </div>
        </template>
      </q-field>

      <q-field
        borderless
        readonly
        hide-bottom-space
      >
        <template v-slot:prepend>
          <q-icon name="access_time" />
        </template>
        <template v-slot:control>
          <div>
            <div>
              {{getPaymentText()}}
            </div>
            <div v-if="isBusiness">
              <small v-if="country === 'au'">Excludes Superannuation and other statutory entitlements. </small>
              <small v-if="country === 'nz'">Excludes any government statutory entitlements.</small>
            </div>
          </div>
        </template>
      </q-field>

      <q-field
        borderless
        readonly
        hide-bottom-space
        v-if="job.willPayOvertime"
      >
        <template v-slot:prepend>
          <q-icon name="timelapse" />
        </template>
        <template v-slot:control>
          {{getOvertimeText()}}
        </template>
      </q-field>

      <q-field
        label="Job Description"
        borderless
        readonly
        hide-bottom-space
      >
        <template v-slot:prepend>
          <q-icon name="assignment" />
        </template>
        <template v-slot:control>
          <div class="full-width q-mt-md">
            <p class="no-margin" v-for="(text, index) in job.specialInstructions.split('\n')" :key="index">
              {{text}}&nbsp;
            </p>
          </div>
        </template>
      </q-field>

      <filters-panel mode="jobDetails" filterType="postOneOffJob" :jobType="job.jobType" v-if="job.managedBy === types.managedBy.system" />

      <q-field
        borderless
        readonly
        hide-bottom-space
        label="Payment Method">
        <template v-slot:prepend>
          <q-icon name="monetization_on" />
        </template>
        <template v-slot:control>
          <div class="full-width q-mt-md">
            <span>{{getHelperPaymentMethod()}}</span>
          </div>
        </template>
      </q-field>

      <q-field
        v-if="selectedApplicants.length > 0"
        label="Invited Candidates"
        borderless
        readonly
      >
        <template v-slot:prepend>
          <q-icon name="people" />
        </template>
      </q-field>
      <div class="full-width invited-carers" style="margin-bottom: 50px">
        <helper-profile-card
          v-for="helper in selectedApplicants"
          :key="helper.id"
          :helper="helper"
          :displayDistance="true"
          mode="read-only"
        />
      </div>

      <q-btn
        big
        color="accent"
        class="full-width fixed-bottom"
        @click="handlePostJob">
          Post Job
      </q-btn>
    </div>
  </q-page>
</template>

<script>
import { mapState } from 'vuex';
import HelperProfileCard from 'components/HelperProfileCard';
import FiltersPanel from 'components/FiltersPanel';
import { beforeRouteEnter, beforeLeaveStep, postJob } from './multistep-helper';
import c from '../../utils/constants';
import {
  formatJobDate, getJobPaymentMethodText, getFormattedAddress, formatNumber, getTimeDifference,
} from '../../utils/helpers';

export default {
  name: 'PageOneOffJobStep5',
  components: { HelperProfileCard, FiltersPanel },
  data() {
    return {
      types: {
        managedBy: c.managedBy,
      },
    };
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
      job: (state) => state.postOneOffJob.jobDetails.data,
      meta: (state) => state.postOneOffJob.jobDetails.meta,
    }),
    storeModuleName() {
      return 'postOneOffJob.jobDetails';
    },
    selectedApplicants() {
      return this.meta.selectedApplicants;
    },
    addressLabel() {
      const address = this.userInfo.addresses.find((a) => a.id === this.job.addressId);
      if (!address) return '';

      return address.addressLabel;
    },
  },
  methods: {
    validate() {
      return true;
    },
    getJobDateText() {
      return formatJobDate(this.job);
    },
    getPaymentText() {
      const {
        price, paymentType, jobStartTime, jobEndTime,
      } = this.job;

      const hours = getTimeDifference(jobStartTime, jobEndTime);
      if (paymentType === c.jobPaymentType.hourly) {
        const jobPrice = hours * price;
        return `Total job = $${formatNumber(jobPrice)} at $${price} per hour`;
      }

      const avgRate = (price / hours).toLocaleString('en-AU');
      return `Total job = $${formatNumber(price)} fixed price for ${hours} hours Avg. ${avgRate} p/hr`;
    },
    getAddress() {
      const address = this.userInfo.addresses.find((a) => a.id === this.job.addressId);
      return getFormattedAddress(address);
    },
    getOvertimeText() {
      const { overtimeRate } = this.job;
      return `Extra hours rate: $${formatNumber(overtimeRate)} per hour`;
    },
    getHelperPaymentMethod() {
      return getJobPaymentMethodText(this.job.helperPaymentMethod);
    },
    handlePostJob() {
      postJob(this.$router);
    },
  },
  beforeRouteEnter(to, from, next) {
    beforeRouteEnter(to, from, next);
  },
  beforeRouteLeave(to, from, next) {
    beforeLeaveStep(this, to, from, next);
  },
};
</script>
