<template>
  <q-page class="">
    <div ref="chatArea">
      <div v-if="meta && meta.lastFetched">
        <div class="layout-padding messages-container" ref="messagesContainer">
          <div class="message-container q-my-sm"
            v-for="msg in formattedMessages"
            :key="msg.value.id">
            <div v-if="msg.type === 'title'" class="text-center text-bold">
              {{msg.value}}
            </div>
            <q-chat-message
              v-if="msg.type === 'message'"
              :bg-color="getBackgroundColor(msg)"
              :text-color="getTextColor(msg)"
              :text="getChatMessage(msg)"
              :stamp="getStamp(msg)"
              :sent="getSent(msg)"
              :name="getName(msg)"
            >
              <template v-slot:avatar>
                <img
                  @click="handleMessageClick(msg)"
                  :class="getAvatarClass(msg)"
                  :src="getAvatar(msg)"
                >
              </template>
            </q-chat-message>
          </div>
          <q-chat-message v-if="meta.isTyping"
            :avatar="getChatWithAvatar()"
            text-color="white"
            bg-color="secondary">
            <q-spinner-dots size="2rem" />
          </q-chat-message>
        </div>
        <q-footer class="bg-white q-px-md">
          <q-separator />
          <q-field v-if="canChat" borderless>
            <template v-slot:control>
              <div class="full-width row items-center">
                <div class="col-10">
                  <q-input placeholder="Message" :disable="pageInfo.isProcessing" v-model="newMessage" rows="3" :max-height="150" type="textarea"/>
                </div>
                <div class="col-2">
                  <q-btn :loading="pageInfo.isProcessing" color="accent" @click="handleSendMessage">Send</q-btn>
                </div>
              </div>
            </template>
          </q-field>
          <div v-else class="text-center">
            <div v-if="isFamily" class="text-black q-my-xs">
              <div v-if="hasUnlimitedChat">
                <span>You need an active job post to chat with this helper.</span>
                <br/>
                <q-btn class="q-my-xs" color="accent" @click="handlePostJob">Post Job</q-btn>
              </div>
              <div v-else>
                <span>You need to subscribe and post a job to chat with helpers.</span>
                <br/>
                <q-btn class="q-my-xs" color="accent" @click="handleUpgrade">Upgrade</q-btn>
              </div>
            </div>
            <div v-else-if="isHelper" class="text-black q-my-sm">
              <span v-if="userInfo.membershipType === types.membershipType.free">
                This account is interested in your profile, but you need an active membership to chat with Juggle St parents.
                <br/>
                <q-btn class="q-my-xs" color="accent" @click="handleUpgrade">Upgrade</q-btn>
              </span>
              <span v-if="userInfo.membershipType === types.membershipType.subscription">
                In-app chat is available when a parent gets in touch or invites you to a job.
              </span>
            </div>
          </div>
        </q-footer>
      </div>
    </div>
  </q-page>
</template>

<script>
import { date } from 'quasar';
import { mapState, mapGetters } from 'vuex';
import { env } from '../utils/env';
import { getMessageDeliveryStatusText, delay } from '../utils/helpers';
import store from '../store';
import c from '../utils/constants';
import NotificationService from '../utils/notification-service';
import errorHandler from '../utils/error-handler';

export default {
  name: 'PageAppInfo',
  data() {
    return {
      types: {
        membershipType: c.membershipType,
      },
    };
  },
  computed: {
    ...mapGetters('realtime', ['chat']),
    ...mapState({
      pageInfo: (state) => state.pageInfo,
      userInfo: (state) => state.userInfo.data,
      connection: (state) => state.realtime.connection,
      conversation: (state) => state.realtime.chat.data,
      meta: (state) => state.realtime.chat.meta,
      adminMode: (state) => state.appInfo.adminMode,
    }),
    canChat() {
      return this.conversation.isEnabled;

      // if (this.isFamily) {
      //   return this.conversation.isEnabled;
      // }

      // if (this.isHelper) {
      //   if (this.userInfo.membershipType === c.membershipType.subscription) {
      //     if (this.conversation.hasPendingJobs) return true;

      //     // Allow one message to be sent.
      //     return this.conversation.messages.length !== 1;
      //   }
      // }

      // return false;
    },
    hasUnlimitedChat() {
      return this.userInfo.permissions.hasUnlimitedChatWithProviders;
    },
    storeModuleName() {
      return 'realtime.chat';
    },
    newMessage: {
      get() {
        return this.getMetaProperty('newMessage');
      },
      set(value) {
        // this.$store.dispatch('realtime/sendTypingMessage');
        this.setMetaProperty('newMessage', value);
      },
    },
    formattedMessages() {
      const formattedMessages = [];
      const { messages } = this.conversation;

      const displayInsuranceNote = true;
      if (this.isFamily && displayInsuranceNote) {
        const insuranceNote = 'Parents with an active subscription receive automatic Public Liability Insurance when the job is posted, applied and awarded on Juggle Street. Insurance is excluded if a job is arranged and awarded using this online chat. Please visit our Insurance page for more details.';
        const insuranceMessage = {
          body: insuranceNote,
          deliveryStatus: null,
          id: 'insurance-note',
          senderId: 'js',
          sentAt: null,
        };

        formattedMessages.push({
          type: 'message',
          value: insuranceMessage,
        });
      } else if (this.isHelper) {
        const note1 = 'REMINDER - only jobs posted & awarded can be rated, reviewed and added to your job counter. If a parent or a business offers you a job over chat, please ask them to POST the job and award it to you.';
        formattedMessages.push({
          type: 'message',
          value: {
            body: note1,
            deliveryStatus: null,
            id: 'job-note-1',
            senderId: 'js',
            sentAt: null,
          },
        });

        const addSecurityNote = this.userInfo.appSettings && this.userInfo.appSettings.enableSecurityNotes;
        if (addSecurityNote) {
          const note2 = 'Your safety & security is our priority. Never share your Juggle St login details and be aware of messages and emails trying to trick you into clicking on suspicious links. Do not provide your credit card or debit card details to third parties. If you have any concerns, please contact Juggle St directly. ';
          formattedMessages.push({
            type: 'message',
            value: {
              body: note2,
              deliveryStatus: null,
              id: 'job-note-2',
              senderId: 'js',
              sentAt: null,
            },
          });
        }
      }

      if (messages.length === 0) {
        return formattedMessages;
      }

      let lastMessageDate;
      messages.forEach((message) => {
        const messageDate = date.formatDate(message.sentAt, 'ddd DD MMM YY');
        if (lastMessageDate !== messageDate) {
          formattedMessages.push({
            type: 'title',
            value: messageDate,
          });

          lastMessageDate = messageDate;
        }

        formattedMessages.push({
          type: 'message',
          value: message,
        });
      });

      return formattedMessages;
    },
  },
  methods: {
    getName(msg) {
      if (msg.type === 'label') {
        return '';
      }

      // let part = this.getMessageParticipant(msg.value);
      // return part.firstName;
      const time = date.formatDate(msg.value.sentAt, 'hh:mm a');
      return time;
    },
    getLabel(msg) {
      if (msg.type === 'message') {
        return '';
      }

      return msg.value;
    },
    handleUpgrade() {
      if (this.isFamily) {
        this.routerPush('/account/pricing');
      } else {
        this.routerReplace(`/account/helper-membership?upgradeNow=1&redirect=${this.$route.fullPath}`);
      }
    },
    handlePostJob() {
      this.routerReplace('/post-job?reset=1');
    },
    getSent(msg) {
      if (msg.type === 'label') {
        return '';
      }

      return msg.value.senderId === this.userInfo.id;
    },
    getBackgroundColor(msg) {
      if (msg.type === 'label') {
        return '';
      }

      if (msg.value.senderId === 'js') {
        return 'primary';
      }

      return msg.value.senderId === this.userInfo.id ? 'grey-7' : 'green-6';
    },
    getTextColor(msg) {
      if (msg.type === 'label') {
        return '';
      }

      if (msg.value.senderId === 'js') {
        return 'white';
      }

      return msg.value.senderId === this.userInfo.id ? 'white' : 'white';
    },
    getStamp(msg) {
      if (msg.type === 'label') {
        return '';
      }

      if (msg.value.senderId === 'js') {
        return 'Juggle St';
      }

      if (msg.value.senderId !== this.userInfo.id) {
        return '';
      }

      const deliveryStatusText = getMessageDeliveryStatusText(msg.value.deliveryStatus);
      return deliveryStatusText;
    },
    getChatMessage(msg) {
      if (msg.type === 'label') {
        return [];
      }

      const chatMessage = [];
      chatMessage.push(msg.value.body);
      return chatMessage;
    },
    getAvatar(msg) {
      if (msg.type === 'label') {
        return '';
      }

      if (msg.value.senderId === 'js') {
        return this.isApp ? 'img/js-logo.png' : 'icons/favicon-128x128.png';
      }

      const part = this.getMessageParticipant(msg.value);
      return this.getImageUrl(part);
    },
    getAvatarClass(msg) {
      return msg.value.senderId === this.userInfo.id ? 'q-message-avatar q-message-avatar--sent' : 'q-message-avatar q-message-avatar--received';
    },
    getMessageParticipant(msg) {
      return this.conversation.participants.find((participant) => msg.senderId === participant.participantId);
    },
    getParticipant(mine) {
      const currentUserId = this.userInfo.id;
      let participant;
      for (let i = 0; i < this.conversation.participants.length; i++) {
        if ((mine && this.conversation.participants[i].participantId === currentUserId)
        || (!mine && this.conversation.participants[i].participantId !== currentUserId)) {
          participant = this.conversation.participants[i];
          break;
        }
      }

      return participant;
    },
    getChatWithName() {
      const part = this.getParticipant(false);
      return part.firstName;
    },
    getChatWithAvatar() {
      const part = this.getParticipant(false);
      return this.getImageUrl(part);
    },
    getImageUrl(participant) {
      return `${env.storageUrl}/images/${participant.imageUrl}`;
    },
    async handleSendMessage() {
      // this.$store.dispatch('realtime/sendChatMessage');
      await this.$store.dispatch('realtime/sendChatMessageDirect').catch((error) => {
        NotificationService.negative(error.message);
      });

      this.scrollContainer();
    },
    handleMessageClick(msg) {
      if (msg.value.senderId === this.userInfo.id || msg.value.senderId === 'js') return;

      const part = this.getMessageParticipant(msg.value);
      if (part.jugglerTypes === c.jugglerType.client) {
        this.routerPush(`/account/client-profile/${part.participantId}`);
      } else {
        this.routerPush(`/account/helper-profile/${part.participantId}`);
      }
    },
    async scrollContainer() {
      await delay(1000);

      this.doubleRaf(() => {
        // window.scrollTo(0, document.body.scrollHeight);
        window.scrollTo({
          top: document.body.scrollHeight,
          left: 0,
          behavior: 'smooth',
        });
      });
    },
    async fetchChatMessages(route) {
      store.dispatch('realtime/resetChatMessages');

      const payload = {};
      if (route.params.id) {
        payload.id = route.params.id;
      } else if (route.params.userId) {
        payload.userId = route.params.userId;
      } else {
        // Throw error.
      }

      try {
        await this.$store.dispatch('realtime/fetchChatMessages', payload);
        this.scrollContainer();
      } catch (error) {
        console.log(error);
        errorHandler.captureException(error);
        NotificationService.negative('This user is not able to receive messages at this time.');
        this.routerReplace('/conversations');
      }
    },
  },
  beforeRouteEnter(to, from, next) {
    next(async (vm) => {
      try {
        await vm.fetchChatMessages(to);
      } catch (error) {
        NotificationService.negative(error.message);
        next({ path: from.path });
      }
    });
  },
  async beforeRouteUpdate(to, from, next) {
    const existingMessage = this.newMessage;
    await this.fetchChatMessages(to);
    this.newMessage = existingMessage;

    next();
  },
  beforeRouteLeave(to, from, next) {
    this.$store.dispatch('realtime/resetChatMessages');
    next();
  },
};
</script>
