<template>
  <q-dialog
      v-model="isVisible"
      @show="onShow"
      @hide="onHide"
    >
    <q-card>
        <q-card-section>
          <div class="text-h6">{{userName}}</div>
        </q-card-section>

        <q-card-section class="q-pt-none">
          <span>Do you want to add {{userName}} to your list of Favourites or Hide?</span>
        </q-card-section>

        <q-card-actions align="right">
          <!-- <q-btn flat label="Cancel" color="accent" v-close-popup /> -->
          <!-- <q-btn flat label="Turn on Wifi" color="primary" v-close-popup />                   -->
          <q-btn color="tertiary" label="Cancel" @click="handleCancel" outline />
          <q-btn color="secondary" label="Hide" @click="handleHide" />
          <q-btn
            color="accent"
            icon="add"
            label=""
            @click="handleConnect">
              <q-icon name="favorite_border" color="white" size="24px" />
            </q-btn>
        </q-card-actions>
      </q-card>
    </q-dialog>
</template>

<script>
import { mapState, mapActions } from 'vuex';
import c from '../utils/constants';
import NotificationService from '../utils/notification-service';

export default {
  name: 'ConnectionManagementModal',
  data() {
    return {
      name: '',
      isVisible: false,
      callback: null,
    };
  },
  props: {
    user: {
      default: undefined,
    },
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
    }),
    userName() {
      if (!this.user) return '';

      return this.user.firstName;
    },
  },
  methods: {
    ...mapActions(['sendFriendRequest']),
    showDialog(cb) {
      this.isVisible = true;
      this.callback = cb;
    },
    hideDialog() {
      this.isVisible = false;
    },
    handleConnect() {
      if (this.user.friendStatus === c.friendStatus.none) {
        this.processFriendRequest(c.friendStatus.pending);
      } else if (this.user.friendStatus === c.friendStatus.pending && this.user.actionRequired) {
        this.processFriendRequest(c.friendStatus.approved);
      }

      this.hideDialog();
    },
    handleHide() {
      this.$q.dialog({
        title: `Hide ${this.user.publicName}`,
        message: `Are you sure you want to permanently hide ${this.user.publicName}?`,
        cancel: {
          label: 'Cancel',
          color: 'secondary',
          outline: true,
        },
        ok: {
          label: 'Hide',
          color: 'primary',
          outline: true,
        },
      }).onOk(() => {
        this.hideUser();
      }).onCancel(() => {
        this.hideDialog();
      });
    },
    hideUser() {
      let id;
      let status;
      if (this.user.requestId && this.user.requestId > 0) {
        id = this.user.requestId;
        status = c.friendStatus.rejected;
      } else if (this.user.friendshipId && this.user.friendshipId > 0) {
        id = this.user.friendshipId;
        status = c.friendStatus.rejected;
      } else {
        status = c.friendStatus.hidden;
      }

      this.processFriendRequest(status, id);

      this.hideDialog();
    },
    handleCancel() {
      console.log('cancel');
      this.hideDialog();
    },
    onShow() {
    },
    onHide() {
    },
    processFriendRequest(status, requestId) {
      const payload = {
        friendId: this.user.id,
        status,
      };

      if (requestId) {
        payload.id = requestId;
      }

      if (this.user.requestId && this.user.requestId > 0) {
        payload.id = this.user.requestId;
      } else if (this.user.friendshipId && this.user.friendshipId > 0) {
        payload.id = this.user.friendshipId;
      }

      this.sendFriendRequest(payload).then(() => {

      }).catch((error) => {
        NotificationService.info(error.message);
      });
    },
    pendingAccountNotice() {
      this.$q.dialog({
        title: `Invite ${this.user.firstName}`,
        message: `Your profile must be 100% complete before you can connect with ${this.user.firstName}`,
        cancel: {
          label: 'Later',
          color: 'secondary',
          outline: true,
        },
        ok: {
          label: 'Complete Profile',
          color: 'primary',
          outline: true,
        },
      }).onOk(() => {
        if (this.isFamily) {
          this.routerReplace('/client-profile-wizard');
        } else {
          this.routerReplace('/helper-profile-wizard');
        }
      }).onCancel(() => {});
    },
    resetConnection() {
      this.processFriendRequest(c.friendStatus.none);
    },
    reject() {
      this.$q.dialog({
        title: `Hide ${this.user.firstName}`,
        message: `Are you sure you want to permanently hide ${this.user.firstName}?`,
        cancel: {
          label: 'Cancel',
          color: 'secondary',
          outline: true,
        },
        ok: {
          label: 'Hide',
          color: 'primary',
          outline: true,
        },
      }).onOk(() => {
        this.processFriendRequest(c.friendStatus.rejected);
      }).onCancel(() => {});
    },
  },
};
</script>
