<template>
  <q-card
    :style="cardStyle"
    :class="cardClass">
    <q-card-section @click="handleClick" style="padding: 4px 16px; height: 25px">
      <div class="text-subtitle1 ellipsis">
        {{publicName}}
      </div>
    </q-card-section>
    <q-card-section align="center" @click="handleClick" style="padding: 8px 16px">
      <div class="img-container row justify-center relative-position">
        <span class="video-container" v-if="helper.metadata.hasVideo && this.mode === 'view'">
          <q-icon name="play_circle_outline" class="overlay-video-icon"/>
        </span>
        <img :src="getImageUrl()"/>
      </div>
    </q-card-section>
    <q-card-section style="padding: 0 16px">
      <div :style="featuresSectionStyles">
        <div class="ellipsis">
          <!-- <q-icon name="place" size="18px" /> <span v-if="displayDistance">{{helper.distanceInKm}}km - </span>{{helper.suburb}} -->
          <q-icon name="location_on" size="18px" style="vertical-align: middle" :class="isSelected ? 'text-secondary-lighter' : 'text-primary'" /> {{location}}
        </div>
        <div v-if="isSuperHelper">
          <q-icon name="star" size="18px" class="text-brand-gold q-mr-xs" />
          <span>Super Helper</span>
        </div>
        <div class="ellipsis" v-if="helper.jobsCompleted > 0">
            <!-- <span :class="'icon-juggle-street ' + (isSelected ? 'text-white' : 'text-primary')" style="marginRight: 5px; vertical-align: middle"></span> -->
            <q-icon name="receipt_long" size="18px" style="vertical-align: middle" class="text-secondary-lighter q-mr-xs" />
            <span :class="isSelected ? 'text-white' : 'text-black'">{{helper.jobsCompleted}}</span> Job{{helper.jobsCompleted == 1 ? "" : "s"}}

            <!-- <span style="marginLeft: 5px" v-if="helper.metadata.reviewsCount > 0">
              <span :class="isSelected ? 'text-white' : 'text-black'">{{helper.metadata.reviewsCount}}</span> Review{{helper.metadata.reviewsCount == 1 ? "" : "s"}}
            </span> -->
            <span style="marginLeft: 5px" v-if="helper.ratingsCount > 0">
              <span :class="isSelected ? 'text-white' : 'text-black'">{{helper.ratingsCount}}</span> Rating{{helper.ratingsCount == 1 ? "" : "s"}}
            </span>
            <span style="marginLeft: 5px" v-else-if="helper.ratingsAvg > 0">
              <q-icon name="star_border" size="18px" class="text-brand-orange" style="vertical-align: text-bottom"/>
              <span :class="isSelected ? 'text-white' : 'text-black'">{{helper.ratingsAvg}}</span> Rating
            </span>
        </div>
        <div style="" v-if="hasResponseRate">
          <!-- <span :class="'icon-juggle-street ' + (isSelected ? 'text-white' : 'text-primary')" style="marginRight: 5px; vertical-align: middle"></span> -->
          <q-icon name="reply" size="18px" style="vertical-align: middle" class="text-secondary-lighter q-mr-xs" />
          <span :class="isSelected ? 'text-white' : 'text-black'">{{responseTime}}</span> Response
        </div>
        <div style="" class="ellipsis" v-if="helper.metadata.hasSpecialNeedsExperience">
          <q-icon name="accessibility_new" size="18px" style="vertical-align: middle" class="text-secondary-lighter" /> Special needs
        </div>
        <div style="" class="ellipsis" v-if="helper.metadata.qualifications.length > 0 || helper.metadata.firstAid.length > 0">
          <q-icon name="local_hospital" size="18px" style="vertical-align: middle" class="text-secondary-lighter" /> Qualifications
        </div>
        <div style="" class="ellipsis" v-if="languagesList">
          <q-icon name="language" size="18px" style="vertical-align: middle" class="text-secondary-lighter" /> {{languagesList}}
        </div>
        <div style="" class="ellipsis" v-if="helper.metadata.hasDrivingLicence">
          <q-icon name="drive_eta" size="18px" style="vertical-align: middle" class="text-secondary-lighter" /> Drivers Licence
        </div>
        <div style="" class="ellipsis" v-if="helper.metadata.nationality">
          <q-icon name="flag" size="18px" style="vertical-align: middle" class="text-secondary-lighter" /> {{citizenship}}
        </div>
        <div style="" class="ellipsis" v-if="helper.metadata.certificates.wwcc">
          <q-icon name="verified" size="18px" style="vertical-align: middle" class="text-secondary-lighter" /> WWCC
        </div>
        <!-- <div v-if="hasResponseRate" style="text-align: center">
            <span :class="isSelected ? 'text-white' : 'text-primary'">{{responseTime}}</span> Response Rate
        </div>
        <div class="ellipsis" style="height: 15px" v-else>
          "{{helper.aboutMe25}}"
        </div> -->
      </div>
    </q-card-section>
    <q-card-actions align="center" v-if="this.mode === 'post-job'" style="padding-top: 0; height: 44px">
      <q-btn v-if="!isDisabled" :color="selectStyle" :icon="selectIcon" @click="toggleSelection" :label="selectLabel" />
      <span v-if="isDisabled" class="text-italic">Already Selected</span>
    </q-card-actions>
    <q-card-actions vertical v-if="this.mode === 'view'" style="padding-top: 0">
      <!-- <j-user-actions :user="helper" :allowChat="allowChat" :allowAccept="false" /> -->
      <!-- <j-hide-user-action :user="helper" display-mode="icon"/> -->
      <favourite-actions :user="helper" default-class="absolute-position q-mr-md"/>
    </q-card-actions>
  </q-card>
</template>

<script>
import FavouriteActions from 'components/FavouriteActions';
import { env } from '../utils/env';
import c from '../utils/constants';
import datasets from '../utils/datasets';
import { getHelperProfileDefaultTab, getLastSeenNoteFromNumOfDays } from '../utils/helpers';

export default {
  name: 'HelperProfileCard',
  components: { FavouriteActions },
  data() {
    return {};
  },
  props: ['helper', 'mode', 'isSelected', 'click', 'isDisabled', 'allowChat', 'displayDistance', 'filterKey'],
  computed: {
    featuresSectionStyles() {
      return (this.platform === 'android' ? 'height: 87px; overflow: hidden' : 'height: 85px; overflow: hidden');
    },
    publicName() {
      if (this.helper.publicName.length < 15) {
        return this.helper.publicName;
      }

      return `${this.helper.publicName.substring(0, 12)}...`;
    },
    isSuperHelper() {
      return this.helper.metadata.isSuperProvider;
    },
    location() {
      if (this.displayDistance) return this.helper.suburb;

      if (this.country === 'nz') return this.helper.suburb;

      return `${this.helper.suburb}, ${this.helper.state.toUpperCase()}`;
    },
    colorStyle() {
      return this.isSelected ? 'primary' : this.isDisabled ? 'secondary' : '';
    },
    selectStyle() {
      return !this.isSelected ? 'accent' : 'secondary';
    },
    selectLabel() {
      return !this.isSelected ? 'Select' : 'Unselect';
    },
    selectIcon() {
      return !this.isSelected ? 'add' : 'remove';
    },
    hasSpace() {
      return !this.hasResponseRate || this.helper.jobsCompleted === 0;
    },
    hasResponseRate() {
      return this.helper.metadata.responseRate >= 75 && (this.helper.metadata.responseTime === 1 || this.helper.metadata.responseTime === 24);
    },
    ratingStyle() {
      return this.isSelected ? 'secondary' : 'primary';
    },
    cardStyle() {
      return (this.mode === 'view' || this.mode === 'read-only') ? 'height: 285px; display: inline-block' : 'height: 325px; display: inline-block';
    },
    cardClass() {
      let style = 'helper-profile-card';
      if (this.isDisabled) style += ' disabled bg-secondary';
      if (this.isSelected) style += ' bg-primary text-white';

      return style;
    },
    userRating() {
      return Math.round(this.helper.ratingsAvg);
    },
    responseTime() {
      if (this.helper.metadata.responseTime === 1) {
        return '1 hr';
      }

      if (this.helper.metadata.responseTime === 24) {
        return '1 day';
      }

      return '';
    },
    citizenship() {
      let { nationality } = this.helper.metadata;

      if (!nationality) return null;

      nationality = nationality.toLowerCase();

      const match = datasets.countriesIso.find((country) => country.value === nationality || country.alpha2 === nationality);

      if (!match) return null;

      return match.label;
    },
    languagesList() {
      if (!this.helper) return '';

      const sep = ', ';
      const selectedList = [];
      const list = this.helper.metadata.languages;
      if (list.length === 0) return '';

      for (let i = 0; i < list.length; i++) {
        const match = datasets.languages.find((lang) => lang.value === list[i]);
        if (match) {
          selectedList.push(match.label);
        }
      }

      if (selectedList.length > 0) {
        return selectedList.join(sep);
      }

      return '';
    },
  },
  methods: {
    getImageUrl() {
      return env.demoMode ? '/statics/images/emily.png' : `${env.storageUrl}/images/${this.helper.imageSrc}`;
    },
    toggleSelection() {
      if (this.mode === c.helperProfileCardMode.readOnly || this.isDisabled) {
        return;
      }

      if (this.click) {
        this.click(this.helper);
      }
    },
    handleClick() {
      if (this.mode === c.helperProfileCardMode.readOnly || this.isDisabled) {
        return;
      }

      if (this.filterKey) {
        this.$store.dispatch('toggleRightDrawer', false);
        const defaultTab = (this.mode === 'post-job') ? `&${getHelperProfileDefaultTab(this.filterKey)}` : '';
        const query = `?mode=${this.mode}${defaultTab}`;
        this.routerPush(`/account/helper-profile/${this.helper.id}${query}`);
      }
    },
    getLastSeenText(days) {
      return getLastSeenNoteFromNumOfDays(days);
    },
  },
};
</script>

<style lang="sass">
.helper-profile-card
  height: 285px
  position: relative
  margin: 2px 0
  width: 50%
  border: 1px solid white
  .img-container
    overflow: hidden
    img
      height: 150px
      max-width: 150px
  .q-card-section
    padding: 8px
  .disabled
    opacity: 0.5
  .video-container
    position: absolute
    top: 2px
    left: 2px
    width: 150px
    height: 150px
    color: white
    display: block
    text-align: left
    .overlay-video-icon
      background-color: transparent
      font-size: 30px
      width: 35x
      height: 35px
      display: inline-block
      margin-top: 5px
      margin-left: 5px
</style>
