<template>
  <q-page class="">
    <div class="">
      <dashboard-actions/>
      <div class="layout-padding" v-if="meta.lastFetched != null && conversations.length === 0">
        <div v-if="isApproved">
          <p class="text-center" v-if="isFamily">
            You don't have any conversations yet.
            <br />
            <q-btn color="accent" rounded class="full-width" @click="routerReplace(mainRoute)">Find helpers near you</q-btn>
          </p>
          <p class="text-center" v-if="isHelper">
            You don't have any conversations yet.
            <br />
            <q-btn color="accent" rounded class="full-width" @click="routerReplace(mainRoute)">Connect with families near you</q-btn>
          </p>
        </div>
        <div v-else>
          <pending-profile-banner/>
        </div>
      </div>
      <div v-if="meta.lastFetched != null && conversations.length > 0">
        <!-- <div class="q-my-xs">
          <q-chip
            clickable
            v-for="(item, index) in filterGroups" :key="item.id"
            :icon="groupFilter === item.id ? 'check_circle' : 'radio_button_unchecked'"
            :color="groupFilter === item.id ? 'primary' : 'white'"
            :text-color="groupFilter === item.id ? 'white' : 'black'"
            @click="handleFilterGroupClick(index)"
            class="q-mx-xs q-my-xs q-caption profile-tab-margin"
          >
            {{item.text}}
          </q-chip>
        </div> -->
        <div>
          <conversation-card
            v-for="conversation in filteredConversations"
            :key="conversation.id"
            :conversation="conversation">
          </conversation-card>
        </div>
        <div class="q-my-sm text-center" v-if="hasMore">
          <q-btn rounded outline label="Load More" color="primary" @click="loadMore"/>
        </div>
      </div>
    </div>
  </q-page>
</template>

<script>
import DashboardActions from 'components/DashboardActions';
import ConversationCard from 'components/ConversationCard';
import PendingProfileBanner from 'components/PendingProfileBanner';
import { mapState, mapGetters } from 'vuex';
import c from '../utils/constants';
import store from '../store';

export default {
  name: 'PageConversations',
  components: { DashboardActions, ConversationCard, PendingProfileBanner },
  data() {
    const familyFilterGroups = [
      {
        id: 'all',
        text: 'All',
      },
      {
        id: 'mine',
        text: 'Mine',
      },
      {
        id: 'initiatedByHelpers',
        text: 'New Helpers',
      },
    ];

    const helperFilterGroups = [
      {
        id: 'all',
        text: 'All',
      },
      {
        id: 'mine',
        text: 'Mine',
      },
      {
        id: 'initiatedByParents',
        text: 'Parents',
      },
    ];

    return {
      familyFilterGroups,
      helperFilterGroups,
      groupFilter: 'all',
    };
  },
  computed: {
    ...mapGetters(
      'realtime',
      ['conversations'],
    ),
    ...mapState({
      userInfo: (state) => state.userInfo.data,
      meta: (state) => state.realtime.conversations.meta,
    }),
    hasMore() {
      return (this.meta.pageIndex * this.meta.pageSize) < this.meta.total;
    },
    filterGroups() {
      return this.isFamily ? this.familyFilterGroups : this.helperFilterGroups;
    },
    filteredConversations() {
      if (this.groupFilter === 'mine') {
        return this.conversations.filter((conversation) => conversation.initiatedBy === this.userInfo.jugglerTypes);
      }

      if (this.groupFilter === 'initiatedByParents') {
        return this.conversations.filter((conversation) => conversation.initiatedBy === c.jugglerType.client);
      }

      if (this.groupFilter === 'initiatedByHelpers') {
        return this.conversations.filter((conversation) => conversation.initiatedBy === c.jugglerType.provider);
      }

      return this.conversations;
    },
  },
  methods: {
    handleFilterGroupClick(index) {
      const group = this.filterGroups[index];
      if (this.groupFilter === group.id) return;

      this.groupFilter = group.id;
    },
    async loadMore() {
      const searchCriteria = {
        pageIndex: this.meta.pageIndex + 1,
        pageSize: this.meta.pageSize,
      };
      await this.$store.dispatch('realtime/setSearchCriteria', searchCriteria);
      await this.$store.dispatch('realtime/searchConversations');
    },
  },
  beforeRouteEnter(to, from, next) {
    store.dispatch('realtime/resetConversations');
    next(async (vm) => {
      await vm.$store.dispatch('realtime/searchConversations');
    });
  },
  beforeRouteLeave(to, from, next) {
    this.$store.dispatch('realtime/resetConversations');
    next();
  },
};
</script>
