<template>
  <vue-cropper
    ref='cropper'
    :guides="true"
    :view-mode="1"
    :aspect-ratio="1"
    drag-mode="crop"
    :auto-crop-area="0.9"
    :min-container-width="250"
    :min-container-height="250"
    :min-crop-box-width="150"
    :min-crop-box-height="150"
    :background="false"
    :rotatable="false"
    :modal="false"
    :movable="false"
    :zoomable="false"
    :scalable="false"
    :zoom-on-touch="false"
    :zoom-on-wheel="false"
    :src="imageUrl"
    alt="Profile Image"
    :img-style="{ width: '400px', 'height': '400px' }"
    :cropmove="handlecropImageChange"
    :ready="handlecropImageChange"
  >
  </vue-cropper>
</template>

<script>
import VueCropper from 'vue-cropperjs';
import 'cropperjs/dist/cropper.css';

export default {
  name: 'AppInfo',
  components: {
    'vue-cropper': VueCropper,
  },
  emits: ['cropMove'],
  props: ['imageUrl'],
  data() {
    return {
    };
  },
  methods: {
    handlecropImageChange() {
      if (!this.$refs.cropper) {
        return;
      }
      const cropDimensions = this.$refs.cropper.getData(true);
      this.$emit('cropMove', cropDimensions);
    },
  },
};
</script>
