<template>
  <div class="phone-verification">
    <div v-show="currentStep === steps.requestCode">
      <p>Step 1 of 2</p>
      <p v-if="isPending">
        At Juggle Street, trust and security are paramount, that's why we need to verify each person. Your mobile number is used to verify your identity. This is fast, secure and ensures each person is who they claim to be.
      </p>
      <p v-if="isApproved">
        At Juggle Street trust and security are paramount. We need to verify your mobile number if you are going to change it.
      </p>
      <q-field
        borderless
        class="q-my-sm q-mx-md"
        :error="v$.newPhoneNumber.$error"
        :error-message="country === 'au' ? 'Enter a valid mobile number (04xxxxxxxx).' : 'Enter a valid mobile number (02xxxxxxxx).'"
        >
        <template v-slot:control>
          <div class="full-width">
            <q-input
              max-length="12"
              v-model="newPhoneNumber"
              placeholder="New Phone Number"
            />
          </div>
        </template>
      </q-field>
      <p>
        Tapping GET CODE will SMS a verification code to your mobile.
      </p>
      <div class="text-center">
        <q-btn color="accent" @click="getVerificationCode">
          Get Code
        </q-btn>
      </div>
    </div>
    <div v-show="currentStep === steps.submitCode">
      <p>Step 2 of 2</p>
      <p>To complete your identity verification please enter the six digit code that was sent by SMS to your mobile phone.</p>
      <q-field
        borderless
        class="q-my-sm q-mx-md"
        :error="v$.verificationCode.$error"
        error-message="Enter 6 digit verification code.">
        <template v-slot:control>
          <div class="full-width">
            <q-input
              class="full-width"
              align="center"
              max-length="7"
              v-model="verificationCode"
              placeholder="Verification Code"
            />
          </div>
        </template>
      </q-field>
      <div class="text-center">
        <q-btn color="accent" @click="submitVerificationCode">
          Submit
        </q-btn>
        <a @click="requestCodeAgain" class="request-again-link" v-if="canRequestCodeAgain">
          Didn't receive the code? Request again...
        </a>
      </div>
    </div>
  </div>
</template>

<script>
import useVuelidate from '@vuelidate/core';
import {
  required,
  // numeric,
  maxLength,
  minLength,
} from '@vuelidate/validators';
import { mapGetters, mapActions, mapState } from 'vuex';
import NotificationService from '../utils/notification-service';
import { validatePhoneNumber } from '../utils/helpers';

export default {
  name: 'PhoneVerification',
  props: ['phoneNumber'],
  emits: ['verificationSuccess', 'verificationFailure'],
  data() {
    return {
      canRequestCodeAgain: false,
    };
  },
  setup() {
    return {
      v$: useVuelidate(),
    };
  },
  validations: {
    // newPhoneNumber: {
    //   required,
    //   numeric,
    //   maxLength: maxLength(10),
    //   minLength: minLength(10),
    // },
    newPhoneNumber: {
      validate(value) {
        return validatePhoneNumber(value);
      },
    },
    verificationCode: {
      required,
      maxLength: maxLength(7),
      minLength: minLength(6),
    },
  },
  computed: {
    ...mapState({
      data: (state) => state.phoneVerification.data,
      meta: (state) => state.phoneVerification.meta,
    }),
    ...mapGetters('phoneVerification', ['steps']),
    storeModuleName() {
      return 'phoneVerification';
    },
    newPhoneNumber: {
      get() {
        return this.data.newPhoneNumber;
      },
      set(value) {
        this.setProperty('newPhoneNumber', value);
      },
    },
    verificationCode: {
      get() {
        return this.data.verificationCode;
      },
      set(value) {
        this.setProperty('verificationCode', value);
      },
    },
    currentStep: {
      get() {
        return this.meta.currentStep;
      },
      set(value) {
        this.setMetaProperty('currentStep', value);
      },
    },
  },
  methods: {
    ...mapActions({
      getPhoneVerificationCode: 'phoneVerification/getPhoneVerificationCode',
      submitPhoneVerificationCode: 'phoneVerification/submitPhoneVerificationCode',
    }),
    getVerificationCode() {
      this.v$.newPhoneNumber.$touch();
      if (this.v$.newPhoneNumber.$error) {
        return;
      }

      this.getPhoneVerificationCode()
        .then(() => {
          this.currentStep = this.steps.submitCode;
          this.canRequestCodeAgain = false;

          setTimeout(() => {
            this.canRequestCodeAgain = true;
          }, 30000);
        })
        .catch((error) => {
          NotificationService.negative(error.message);
        });
    },
    submitVerificationCode() {
      this.v$.verificationCode.$touch();
      if (this.v$.verificationCode.$error) {
        return;
      }

      this.submitPhoneVerificationCode()
        .then(() => {
          this.currentStep = this.steps.requestCode;
          this.$emit('verificationSuccess');
        })
        .catch((error) => {
          this.$emit('verificationFailure', error);
        });
    },
    requestCodeAgain() {
      this.currentStep = this.steps.requestCode;
      this.verificationCode = '';
    },
  },
  mounted() {
    if (this.phoneNumber) {
      this.newPhoneNumber = this.phoneNumber;
    }
  },
};
</script>
<style lang="sass">
.phone-verification
  .container
    text-align: center
  .request-again-link
    margin-top: 1rem
    display: block
    text-decoration: underline
</style>
