<template>
  <q-layout view="lHh Lpr lFf">
    <q-header elevated>
      <q-toolbar class="bg-red text-center" v-if="$store.state.appInfo.adminMode">
        <q-toolbar-title>
          Admin Mode
        </q-toolbar-title>
      </q-toolbar>

      <q-toolbar
        v-show="headerShown"
      >
        <q-btn flat @click="handlePrevious" :disabled="userMeta.isProcessing || meta.currentStep === 0">
          <div>
            <q-icon name="keyboard_arrow_left" />
            <small>PREV</small>
          </div>
        </q-btn>
        <q-toolbar-title class="text-center">
          <span>{{pageTitle}}</span>
        </q-toolbar-title>
        <q-btn flat @click="handleNext" :disabled="userMeta.isProcessing">
          <div>
            <small>{{ nextLabel }}</small>
            <q-icon name="keyboard_arrow_right" />
          </div>
        </q-btn>
      </q-toolbar>
    </q-header>

    <q-page-container>
      <router-view />
    </q-page-container>
  </q-layout>
</template>

<script>
import { mapState, mapActions } from 'vuex';
import c from '../../utils/constants';

export default {
  name: 'ProfileWizardLayout',
  computed: {
    ...mapState({
      meta: (state) => state.profileWizard.meta,
      userInfo: (state) => state.userInfo.data,
      userMeta: (state) => state.userInfo.meta,
      headerShown: (state) => {
        const { meta } = state.profileWizard;
        if (!meta.currentStep) return true;

        const currentStep = meta.steps[meta.currentStep];

        return currentStep.path !== 'welcome' && currentStep.path !== 'success';
      },
    }),
    nextLabel() {
      const currentStep = this.meta.steps[this.meta.currentStep];
      if (!currentStep) return 'NEXT';

      return currentStep.complete || !currentStep.meta.canSkip ? 'NEXT' : 'SKIP';
    },
  },
  methods: {
    ...mapActions('profileWizard', [
      'calculateSteps', 'getStep',
    ]),
    handleNext() {
      if (this.meta.currentStep === this.meta.lastStep) {
        return;
      }

      this.$store.dispatch('profileWizard/getStep', { forward: true }).then((nextStep) => {
        if (nextStep !== null) {
          this.routerReplace({ path: `/${this.baseRoute}/${nextStep.path}` });
          return;
        }

        this.routerReplace('/dashboard');
      });
    },
    handlePrevious() {
      if (this.meta.currentStep === 0) {
        return;
      }

      this.$store.dispatch('profileWizard/getStep', { forward: false }).then((nextStep) => {
        if (nextStep !== null) {
          this.routerReplace({ path: `/${this.baseRoute}/${nextStep.path}` });
          return;
        }

        this.routerReplace('/dashboard');
      });
    },
    getJugglerType($route) {
      if ($route.matched.some((record) => record.meta.jugglerType === c.jugglerType.client)) {
        return c.jugglerType.client;
      }

      return c.jugglerType.provider;
    },
  },
  async created() {
    if (this.isFamily) {
      this.baseRoute = 'client-profile-wizard';
    } else {
      this.baseRoute = 'helper-profile-wizard';
    }

    await this.$store.dispatch('profileWizard/calculateSteps', this.userInfo);

    this.routerReplace(`/${this.baseRoute}/${this.meta.steps[0].path}`);
  },
};
</script>
