<template>
  <div class="layout-padding">
    <helper-tutoring-high-school-preferences ref="tutoringPrefs" :displayCategory="false" v-on:change="handleChange"/>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex';
import HelperTutoringHighSchoolPreferences from 'components/HelperTutoringHighSchoolPreferences';
import beforeLeaveStep from '../before-leave-step';
import * as types from '../../../store/mutation-types';

export default {
  name: 'ProfileWizardHighSchoolTutoringExperience',
  components: { HelperTutoringHighSchoolPreferences },
  data() {
    return {};
  },
  methods: {
    ...mapActions([
      'updateUserInfo',
    ]),
    handleChange({ valid }) {
      this.$store.commit(`profileWizard/${types.UPDATE_STEP_STATUS}`, { complete: valid });
      this.$store.commit(`profileWizard/${types.UPDATE_STEP_STATUS}`, { canSkip: false });
    },
    validate() {
      if (this.currentStep.meta.canSkip) {
        return true;
      }

      return this.$refs.tutoringPrefs.validate(true);
    },
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
      currentStep: (state) => {
        const { meta } = state.profileWizard;
        const currentStep = meta.steps[meta.currentStep];
        return currentStep;
      },
      stepComplete: (state) => {
        const { meta } = state.profileWizard;
        const currentStep = meta.steps[meta.currentStep];
        return currentStep.complete;
      },
    }),
    storeModuleName() {
      return 'userInfo';
    },
  },
  beforeRouteLeave(to, from, next) {
    // Update user info before proceeding to the next step if step is complete
    if (this.stepComplete) {
      this.updateUserInfo().then(() => {
        beforeLeaveStep(this, to, from, next);
      });
    } else {
      beforeLeaveStep(this, to, from, next);
    }
  },
};
</script>
