<template>
  <q-page class="">
    <div>
      <job-actions/>
      <div class="layout-padding">
        <!-- <div v-if="pageInfo.isProcessing">
          <helper-job-summary-skeleton v-for="index in 6" :key="index"/>
        </div> -->
      <div v-if="!pageInfo.isProcessing">
        <q-field v-if="pendingJobs.length > 0"
          borderless
          readonly
          hide-bottom-space
          label="Open Jobs">
          <template v-slot:prepend>
            <q-icon name="assignment" />
          </template>
        </q-field>
        <div class="q-mx-sm">
          <div v-for="(job, index) in pendingJobs" :key="index">
            <helper-job-summary
              :key="job.id"
              :job="job">
            </helper-job-summary>
          </div>
        </div>

        <q-field v-if="upcomingJobs.length > 0"
          readonly
          borderless
          label="Upcoming Jobs">
            <template v-slot:prepend>
              <q-icon name="assignment" />
            </template>
          </q-field>
          <div class="q-mx-sm">
            <div v-for="(job, index) in upcomingJobs" :key="index">
              <helper-job-summary
                :key="job.id"
                :job="job">
              </helper-job-summary>
            </div>
          </div>
          <!-- <div class="text-faded" style="padding: 1rem;" v-if="jobs.length === 0">
            No jobs match the specified criteria.
          </div> -->
        </div>
      </div>
    </div>
  </q-page>
</template>

<script>
import HelperJobSummary from 'components/HelperJobSummary';
// import HelperJobSummarySkeleton from 'components/HelperJobSummarySkeleton';
import JobActions from 'components/JobActions';
import { mapState } from 'vuex';
import c from '../../utils/constants';

export default {
  name: 'PageHelpersUpcomingJobs',
  components: { HelperJobSummary, JobActions },
  data() {
    return {
    };
  },
  computed: {
    ...mapState({
      meta: (state) => state.helpers.upcomingJobs.meta,
      jobs: (state) => state.helpers.upcomingJobs.data,
      pageInfo: (state) => state.pageInfo,
    }),
    pendingJobs() {
      const jobs = this.jobs.filter((job) => job.jobStatus === c.jobStatus.pending);
      const responseRequired = [];
      const pendingResponseFromFamily = [];
      jobs.forEach((job) => {
        if (job.applicationStatus === c.jobApplicationStatus.pending || job.applicationStatus === c.jobApplicationStatus.viewed) {
          responseRequired.push(job);
        } else {
          pendingResponseFromFamily.push(job);
        }
      });

      return responseRequired.concat(pendingResponseFromFamily);
    },
    upcomingJobs() {
      return this.jobs.filter((job) => job.jobStatus === c.jobStatus.awarded);
    },
  },
  methods: {
    async fetch() {
      await this.$store.dispatch('setPageProcessing', { isProcessing: true });

      await this.$store.dispatch('helpers/fetchUpcomingJobs');

      await this.$store.dispatch('setPageProcessing', { isProcessing: false });
    },
  },
  beforeRouteEnter(to, from, next) {
    next(async (vm) => {
      await vm.fetch();
    });
  },
  async beforeRouteUpdate(to, from, next) {
    await this.fetch();
    next();
  },
};
</script>
