<template>
    <div class="">
      <profile-name :helper="helper" :isGuardianApprovalMode="isGuardianApprovalMode" :mode="mode"/>

      <div v-if="displayProfileImage" class="q-mx-sm">
        <q-tabs
          v-model="mediaShown"
          class="image-video-tabs q-mb-sm"
          color="secondary"
          v-if="hasProfileVideo()"
        >
          <q-tab class="q-caption" name="video" label="Video" />
          <q-tab class="q-caption" name="image" label="Image" />
        </q-tabs>
        <div class="image-video-container">
          <div v-if="mediaShown === 'video'">
            <div v-html="getVideo(helper.profileIntroVideo)"></div>
          </div>
          <div class="q-my-sm text-center" v-if="mediaShown === 'image'">
            <img :src="helper.imageSrc" />
          </div>
        </div>
      </div>
      <q-card-section align="center" class="q-mx-sm" style="padding: 4px 8px">
        <div class="row justify-between">
            <div class="column" style="text-align: left;">
              <table>
                <tr>
                  <td>
                    <q-icon name="location_on" color="primary" size="18px" />
                    <span v-if="displayDistance">{{helper.distanceInKiloMetersRounded}}km,</span>
                    {{helper.suburb}}<span v-if="!displayDistance">, {{state}}</span>
                  </td>
                </tr>
                <tr v-if="helper.nationality && displayCitizenship">
                  <td>
                      <q-icon name="language" class="text-secondary-lighter" size="18px" />
                      {{citizenship}}
                    </td>
                </tr>

                <tr v-if="displayRatings && helper.providerRatingsAvg > 0">
                  <td>
                    <q-icon name="star_border" class="text-brand-orange" size="18px"/>
                    {{helper.providerRatingsAvg.toFixed(2)}} Avg Rating
                  </td>
                </tr>
                <tr v-if="displayAuPairExperience && isExperiencedAuPair">
                  <td>
                    <q-icon name="check" class="text-secondary-lighter" size="18px"/>
                    <span>Has worked as an Au Pair before</span>
                  </td>
                </tr>
                <tr v-if="displayAuPairExperience && !isExperiencedAuPair">
                  <td>
                    <q-icon name="clear" class="text-secondary-lighter" size="18px"/>
                    <span>Has not worked as an Au Pair before</span>
                  </td>
                </tr>
                <tr v-if="hasProfileVideo()" @click="navigateToProfile">
                  <td>
                    <q-icon name="play_circle_outline" class="text-secondary-lighter" size="18px"/>
                    <span class="text-underline q-mx-xs">Profile Video</span>
                  </td>
                </tr>
                <tr v-if="helper.responseRate >= 75 && (helper.responseTime === 1 || helper.responseTime === 24)">
                  <td>
                    <q-icon name="reply" class="text-secondary-lighter" size="18px"/>
                    <span class="text-black" v-if="helper.responseRate >= 75 && helper.responseTime === 1"> 1 hr</span>
                    <span class="text-black" v-if="helper.responseRate >= 75 && helper.responseTime === 24">1 day</span>
                    Response Rate
                  </td>
                </tr>
              </table>
            </div>
            <div class="column" style="text-align: left">
              <table>
                <tr v-if="displayJobStats">
                  <td class="text-black text-right">
                    {{helper.providerJobsCompleted}}
                  </td>
                  <td>
                    Jobs Completed
                  </td>
                </tr>
                <tr v-if="displayJobStats">
                  <td class="text-red text-right">
                    {{helper.providerJobsCancelled}}
                  </td>
                  <td>
                    Jobs Cancelled
                  </td>
                </tr>
                <tr v-if="displayReviews && helper.providerRatingsCount > 0" @click="scrollToProfileReviews">
                  <td class="text-black text-right">
                    {{helper.providerRatingsCount}}
                  </td>
                  <td>
                    <span class="text-underline">&nbsp;Reviews</span>
                  </td>
                </tr>
              </table>
            </div>
        </div>
      </q-card-section>
      <!-- <q-card-actions align="center" v-if="!isGuardianApprovalMode && mode === 'view'">
        <helper-actions :user="helper" :allowHideAction="false" :allowChat="true"/>
      </q-card-actions> -->
    </div>
</template>

<script>
import ProfileName from 'components/helper-profile/ProfileName';
import c from '../../utils/constants';
import datasets from '../../utils/datasets';

export default {
  name: 'ProfileOverview',
  components: { ProfileName },
  props: ['helper', 'mode', 'isGuardianApprovalMode', 'displayProfileImage', 'displayDistance', 'displayCitizenship', 'displayAuPairExperience', 'displayJobStats', 'displayRatings', 'displayReviews', 'displayVideo'],
  emits: ['profileTabChange', 'scrollToReviews'],
  data() {
    let mediaShown = 'image';
    if (this.helper) {
      mediaShown = this.hasVideo(this.helper.profileIntroVideo) ? 'video' : 'image';
    }
    return {
      mediaShown,
    };
  },
  computed: {
    isExperiencedAuPair() {
      const { providerAuPairCategory } = this.helper;

      return providerAuPairCategory === c.auPairCategory.experiencedAuPair;
    },
    state() {
      return this.helper.state;
    },
    countriesList() {
      return datasets.countriesIso;
    },
    citizenship() {
      let { nationality } = this.helper;

      if (!nationality) return null;

      nationality = nationality.toLowerCase();

      const match = this.countriesList.find((country) => country.value === nationality || country.alpha2 === nationality);

      if (!match) return null;

      return match.label;
    },
  },
  methods: {
    navigateToProfile() {
      const tabInfo = { id: 'profile' };
      this.$emit('profileTabChange', tabInfo);
    },
    hasProfileVideo() {
      return this.displayVideo && this.hasVideo(this.helper.profileIntroVideo);
    },
    scrollToProfileReviews() {
      this.$emit('scrollToReviews');
    },
    hasVideo(video) {
      return video && video.externalHashedId && video.mediaReviewStatus === c.mediaReviewStatus.approved;
    },
    getVideo(video) {
      if (this.hasVideo(video)) {
        return `
            <iframe src="https://fast.wistia.net/embed/iframe/${video.externalHashedId}?playerColor=299C4A"
              width="100%" height="250px" allowtransparency="true" frameborder="0" scrolling="no"
              class="wistia_embed" name="wistia_embed" allowfullscreen mozallowfullscreen webkitallowfullscreen oallowfullscreen msallowfullscreen>
            </iframe>
          `;
      }

      return '';
    },
  },
};
</script>
