import { env } from './env';

const FacebookPixelManager = {
  trackViewEvent: () => {
    window.fbq('track', 'ViewContent');
  },

  trackCompleteRegistration: (isClient) => {
    const contentName = isClient ? 'Family' : 'Helper';
    window.fbq('track', 'CompleteRegistration', { content_name: contentName });
  },

  trackPurchase: (value, currency) => {
    window.fbq('track', 'Purchase', { value, currency });
  },

  trackAppDownload: () => {
    window.fbq('trackCustom', 'FirstOpen',
      { os: env.deviceInfo.operatingSystem === 'android' ? 'Android' : 'iOS' });
  },
};

export default FacebookPixelManager;
