<template>
  <div class="layout-padding">
    <p>
      Families look for specific skills when posting jobs, select all options below that apply to you.
    </p>

    <!-- <div class="group">
      <q-field
        icon="accessibility_new"
        label="Do you have special needs training?"
      >
        <q-option-group
          type="radio"
          v-model="hasSpecialNeedsTraining"
          :options="[
              { label: 'No', value: 0 },
              { label: 'Yes', value: 1 },
          ]"
        />
        <q-input v-if="hasSpecialNeedsTraining"
          v-model="myExperience2"
          type="textarea"
          placeholder="Describe your special needs training & experience."
          rows="3"
        />
      </q-field>
    </div> -->

    <div class="group">
      <q-field
        stack-label
        borderless
        label="Driving">
        <template v-slot:prepend>
          <q-icon name="directions_car" />
        </template>
        <template v-slot:control>
          <q-chip
            clickable
            outline
            :icon="item.selected ? 'check' : 'clear'"
            :color="item.selected ? 'primary' : 'black'"
            :text-color="item.selected ? 'white' : 'black'"
            v-for="(item, index) in userInfo.provider.transport" :key="item.optionId"
            @click="handleItemClick('transport', index)"
            class="q-mx-xs q-my-xs q-caption profile-tab-margin"
          >
              {{item.text}}
          </q-chip>
        </template>
      </q-field>
    </div>

    <div class="group">
      <q-field
        stack-label
        borderless
        label="Spoken Languages">
        <template v-slot:prepend>
          <q-icon name="class" />
        </template>
        <template v-slot:control>
          <q-chip
            clickable
            outline
            :icon="item.selected ? 'check' : 'clear'"
            :color="item.selected ? 'primary' : 'black'"
            :text-color="item.selected ? 'white' : 'black'"
            v-for="(item, index) in userInfo.provider.spokenLanguages" :key="item.optionId"
            @click="handleItemClick('spokenLanguages', index)"
            class="q-mx-xs q-my-xs q-caption profile-tab-margin"
          >
              {{item.text}}
          </q-chip>
        </template>
      </q-field>
    </div>

    <div class="group">
      <q-field
        stack-label
        borderless
        label="First Aid">
        <template v-slot:prepend>
          <q-icon name="local_hospital" />
        </template>
        <template v-slot:control>
          <q-chip
            clickable
            outline
            :icon="item.selected ? 'check' : 'clear'"
            :color="item.selected ? 'primary' : 'black'"
            :text-color="item.selected ? 'white' : 'black'"
            v-for="(item, index) in userInfo.provider.firstAid" :key="item.optionId"
            @click="handleItemClick('firstAid', index)"
            class="q-mx-xs q-my-xs q-caption profile-tab-margin"
          >
            {{item.text}}
          </q-chip>
        </template>
      </q-field>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex';
import beforeLeaveStep from '../before-leave-step';
import * as mutationTypes from '../../../store/mutation-types';

export default {
  name: 'ProfileWizardSkillsAndLanguages',
  data() {
    return {
      hasSpecialNeedsTraining: 0,
    };
  },
  methods: {
    ...mapActions([
      'updateUserInfo',
    ]),
    validate() {
      if (this.currentStep.meta.canSkip) {
        return true;
      }

      return true;
    },
    handleItemClick(section, index) {
      const options = this.userInfo.provider[section];
      const currentOption = options[index];

      const selectedOptions = [];
      options.forEach((option) => {
        let selected = false;
        if (option.optionId === currentOption.optionId) {
          selected = !option.selected;
        } else {
          // eslint-disable-next-line
          selected = option.selected;
        }

        if (selected) {
          selectedOptions.push(option.optionId);
        }
      });

      const payload = {
        propPath: `provider.${section}`,
        value: selectedOptions,
      };

      this.$store.commit(mutationTypes.UPDATE_USER_OPTIONS, payload);
      this.$store.commit(`profileWizard/${mutationTypes.UPDATE_STEP_STATUS}`, { complete: true });
      this.triggerChange();
    },
    triggerChange() {
      this.$store.commit(`profileWizard/${mutationTypes.UPDATE_STEP_STATUS}`, { canSkip: false });
    },
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
      currentStep: (state) => {
        const { meta } = state.profileWizard;
        const currentStep = meta.steps[meta.currentStep];
        return currentStep;
      },
      stepComplete: (state) => {
        const { meta } = state.profileWizard;
        const currentStep = meta.steps[meta.currentStep];
        return currentStep.complete;
      },
    }),
    storeModuleName() {
      return 'userInfo';
    },
    myExperience2: {
      get() {
        return this.userInfo.provider.myExperience2;
      },
      set(value) {
        this.setProperty('provider.myExperience2', value);
      },
    },
  },
  beforeRouteLeave(to, from, next) {
    // Update user info before proceeding to the next step if step is complete
    if (this.stepComplete) {
      this.updateUserInfo().then(() => {
        beforeLeaveStep(this, to, from, next);
      });
    } else {
      beforeLeaveStep(this, to, from, next);
    }
  },
};
</script>
