<template>
  <div class="pending-profile-banner notice">
    <div class="completeness-msg text-bold" v-if="completeness < 100">
      Complete your profile to activate your account
    </div>
    <div class="completeness-bar round-borders">
      <div class="completed round-borders shadow-transition" :style="{ width: completeness + '%' }"/>
    </div>
    <div class="completeness-msg" v-if="completeness < 100">
      Your profile is {{completeness}}% complete
    </div>
    <div class="completeness-msg" v-if="completeness === 100 && userInfo.provider.requiresGuardianApproval && !userInfo.defaultGuardian">
      Send a guardian approval request to activate your account.
    </div>
    <div class="completeness-msg" v-if="completeness === 100 && userInfo.provider.requiresGuardianApproval && userInfo.defaultGuardian">
      Your profile will be activated once your guardian approves your account.
    </div>
    <div class="completeness-msg" v-if="completeness === 100 && !userInfo.provider.requiresGuardianApproval">
      Your account is being reviewed and will be approved shortly.
    </div>
    <!-- <div class="completeness-msg" v-if="completeness === 100 && !userInfo.provider.requiresGuardianApproval && userInfo.certificates.length > 0 && userInfo.certificates[0].certificateStatus === certificateStatus.pendingVerification">
      Your Working with Children Check is being reviewed and will be approved shortly.
    </div> -->
    <div class="q-mt-sm">
      <q-btn color="accent" rounded @click="handleCompleteProfile" v-if="completeness < 100 || (isHelper && userInfo.provider.requiresGuardianApproval && !userInfo.defaultGuardian)">Continue</q-btn>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import c from '../utils/constants';

export default {
  name: 'PendingProfileBanner',
  data() {
    return {
      flaggedForReviewType: c.flaggedForReviewType,
      certificateStatus: c.certificateStatus,
    };
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
      completeness: (state) => state.userInfo.data.profileCompleteness,
      jugglerType: (state) => state.userInfo.data.jugglerTypes,
    }),
  },
  props: [],
  methods: {
    handleCompleteProfile() {
      if (this.isFamily) {
        this.routerReplace('/client-profile-wizard');
      } else {
        this.routerReplace('/helper-profile-wizard');
      }
    },
  },
};
</script>

<style lang="sass">
.pending-profile-banner
  margin-top: 5px
  padding: 0.5rem
  .completeness-msg
    padding-top: 0
  .completeness-bar
    width: 100%
    height: 1.5rem
    background: $blue-grey-1
    border-radius: 20px
    border: 1px solid $blue-grey-4
    .completed
      height: 100%
      border-radius: 20px
      background: $brand-blue
</style>
