import c from '../../../utils/constants';

import welcome from './Welcome';
import identityCheck from '../IdentityCheck';
import children from './Children';
import intro from './IntroduceFamily';
import introduceBusiness from './IntroduceBusiness';
import photo from '../ProfilePhoto';
import success from './Success';

export const steps = [
  {
    profileItem: null,
    path: 'welcome',
    title: 'Welcome',
    shouldInclude: () => true,
    component: welcome,
    complete: true,
  },
  {
    profileItem: c.profileItems.trustVerification,
    title: 'Identity Check',
    path: 'identity-check',
    component: identityCheck,
    complete: false,
    canSkip: false,
  },
  {
    profileItem: c.profileItems.myChildren,
    title: 'Your Children',
    path: 'your-children',
    component: children,
    complete: false,
    canSkip: true,
    shouldInclude: (userInfo) => userInfo.client.clientType === c.clientType.individual,
  },
  {
    profileItem: c.profileItems.clientAboutMe,
    title: 'Introduce Family',
    path: 'introduce-family',
    component: intro,
    complete: false,
    canSkip: true,
    shouldInclude: (userInfo) => userInfo.client.clientType === c.clientType.individual,
  },
  {
    profileItem: c.profileItems.clientAboutMe,
    title: 'Your Business',
    path: 'introduce-business',
    component: introduceBusiness,
    complete: false,
    canSkip: false,
    shouldInclude: (userInfo) => userInfo.client.clientType === c.clientType.business,
  },
  {
    profileItem: c.profileItems.clientPhoto,
    title: (userInfo) => {
      const title = userInfo.client.clientType === c.clientType.individual || userInfo.provider.providerType === c.providerType.individual ? 'Profile Photo' : 'Company Logo';
      return title;
    },
    path: 'profile-photo',
    component: photo,
    complete: false,
    canSkip: true,
  },
  {
    title: 'Success',
    path: 'success',
    shouldInclude: () => true,
    component: success,
    complete: true,
  },
];

export const getRoutes = () => {
  const mappedSteps = steps.map((step, index) => ({
    path: step.path,
    name: `client-profile-wizard-${step.path}`,
    component: step.component,
    meta: { stepIndex: index, title: step.title },
  }));

  return mappedSteps;
};
