<template>
  <q-page class="layout-padding">
    <p style="margin-top: 10px">
      <span class="text-subtitle2">Family Introduction</span>
      <br/>
      Please describe your family, but do not include any info that will need keeping up-to-date, such as the age of your children. <br/> <br/>Do <strong>NOT</strong> include job details here, you do this when you Post a Job.
    </p>
    <q-field style="margin-top: 0"
      icon="border color"
      borderless
      :error="v$.aboutMe.$error"
      error-message="required">
      <template v-slot:control>
        <q-input
          class="full-width"
          placeholder="Example - Hi there, we are a busy family of four. We are looking for local babysitters to help us, as my husband and I both often travel for work."
          v-model="aboutMe" type="textarea" :max-height="100" rows="3" />
      </template>
    </q-field>
    <p style="margin-top: 10px; margin-bottom: 0">
      <span class="text-subtitle2">Profile Photo</span>
      <br/>
      Families and helpers are required to have a profile photo, choose one that represents your whole family.
    </p>
    <q-field style="margin-top: 0" label=""
      stack-label borderless>
      <template v-slot:control>
        <div class="self-center full-width no-outline" tabindex="0">
          <img :src="userInfo.defaultImage.scale1ImageUrl" style="height: 150px"/>
          <br/>
          <a @click="handleSelectPhoto" class="q-ml-md button-link text-primary">Select Image</a>
          <image-uploader
            ref="image-uploader"
            @uploadSuccess="handleUploadSuccess"
            @uploadFailure="handleUploadFailure"
          />
          <a @click="handleCropPhoto" class="q-ml-md button-link text-primary">Crop</a>
          <small class="text-faded" v-if="!hasImage"><br/>required</small>
        </div>
      </template>
    </q-field>
    <q-field
      stack-label
      borderless
      label="Outline your general expectations for anybody looking after your children.">
      <template v-slot:control>
        <q-input class="full-width" v-model="jobDescription" type="textarea" :max-height="150" rows="3" />
      </template>
    </q-field>
    <q-field
      stack-label
      borderless
      label="Tell helpers about any house rules they need to know.">
      <template v-slot:control>
        <q-input
          class="full-width"
          placeholder=""
          v-model="jobRules" type="textarea" :max-height="150" rows="3" />
      </template>
    </q-field>

    <q-field label="Perks for Babysitters" stack-label>
      <template v-slot:control>
        <q-option-group type="checkbox" color="primary" inline v-model="selectedBenefitsForProviders" :options="benefitsForProvidersList" />
      </template>
    </q-field>
    <q-field label="Pets" stack-label>
      <template v-slot:control>
        <q-option-group inline color="primary" type="checkbox" v-model="selectedPets" :options="petsList" />
      </template>
    </q-field>
  </q-page>
</template>

<script>
import { mapState, mapActions } from 'vuex';
import useVuelidate from '@vuelidate/core';
import { required } from '@vuelidate/validators';
import ImageUploader from 'components/ImageUploader';
import NotificationService from '../../utils/notification-service';
import * as mutationTypes from '../../store/mutation-types';

export default {
  name: 'PageAccountMyFamily',
  components: { ImageUploader },
  data() {
    const userData = this.$store.state.userInfo.data;

    const pets = userData.client.pets.filter((option) => option.selected);

    const selectedPets = pets.map((option) => option.optionId);

    const benefits = userData.client.benefitsForProviders.filter((option) => option.selected);
    const selectedBenefitsForProviders = benefits.map((option) => option.optionId);

    return {
      selectedPets, selectedBenefitsForProviders,
    };
  },
  methods: {
    handleSelectPhoto() {
      this.$refs['image-uploader'].open();
    },
    handleCropPhoto() {
      this.routerPush('/account/modal/crop-image');
    },
    handleUploadSuccess() {
      this.routerPush('/account/modal/crop-image');
    },
    handleUploadFailure() {
      NotificationService.negative('An error occurred. Please try again later.');
    },
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
    }),
    ...mapActions([
      'updateUserInfo',
    ]),
    storeModuleName() {
      return 'userInfo';
    },
    hasImage() {
      return this.userInfo.defaultImage && this.userInfo.defaultImage.scale1ImageUrl;
    },
    aboutMe: {
      get() {
        return this.getProperty('aboutMe');
      },
      set(value) {
        this.setProperty('aboutMe', value);
      },
    },
    jobRules: {
      get() {
        return this.getProperty('client.jobRules');
      },
      set(value) {
        this.setProperty('client.jobRules', value);
      },
    },
    jobDescription: {
      get() {
        return this.userInfo.client.jobDescription;
      },
      set(value) {
        this.setProperty('client.jobDescription', value);
      },
    },
    benefitsForProvidersList() {
      const list = this.userInfo.client.benefitsForProviders.map((option) => ({ label: option.text, value: option.optionId }));

      return list;
    },
    petsList() {
      return this.userInfo.client.pets.map((option) => ({ label: option.text, value: option.optionId }));
    },
  },
  setup() {
    return {
      v$: useVuelidate(),
    };
  },
  validations: {
    aboutMe: {
      required,
    },
  },
  watch: {
    selectedBenefitsForProviders(updatedBenefits) {
      const payload = {
        propPath: 'client.benefitsForProviders',
        value: updatedBenefits,
      };
      this.$store.commit(mutationTypes.UPDATE_USER_OPTIONS, payload);
    },
    selectedPets(updatedPets) {
      const payload = {
        propPath: 'client.pets',
        value: updatedPets,
      };
      this.$store.commit(mutationTypes.UPDATE_USER_OPTIONS, payload);
    },
  },
  beforeRouteLeave(to, from, next) {
    this.v$.$touch();
    if (this.v$.$error) {
      NotificationService.negative('Please fix all highlighted errors.');
      next(false);
      return;
    }

    this.$store.dispatch('updateUserInfo').then(() => {
      next();
    }).catch((error) => {
      NotificationService.negative(error.message);
      next(false);
    });
  },
};
</script>
