<template>
  <q-page class="post-au-pair-job-step-1">
    <div class="layout-padding">
      <q-field
        borderless
        readonly
        :error="v$.durationToStart.$error"
        hide-bottom-space
        label="Select start date">
        <template v-slot:prepend>
          <q-icon name="date_range" />
        </template>
        <template v-slot:control>
          <div class="full-width row items-center">
            <q-radio
              v-model="jobStartType"
              :val="2"
              label="Job to start within:"
            />
            <!-- <q-input :disable="jobStartType !== 2"
              v-model="durationToStart"
              :error="v$.durationToStart.$error"
              error-message="required"
              type="number"
              placeholder="00"
              :min="1"
              style="width: 25px;margin: 0 10px"
              class="inline duration-to-start"
            /> -->
            <q-select
              :disable="jobStartType !== 2"
              v-model="durationToStart"
              :options="numberList"
              option-value="value"
              option-label="label"
              emit-value
              map-options
              placeholder="00"
              style="width: 3rem"
              class="inline q-mx-sm"
            />
            <q-select
              :disable="jobStartType !== 2"
              v-model="jobStartDurationType"
              :options="durationTypes"
              option-value="value"
              option-label="label"
              emit-value
              map-options
              class="inline"
            />
          </div>
        </template>
      </q-field>
      <p class="q-my-xs q-mx-xl">OR</p>
      <q-field
        :error="v$.jobDate.$error"
        hide-bottom-space
        borderless>
        <template v-slot:prepend>
          <q-icon name="date_range" />
        </template>
        <template v-slot:control>
          <div class="full-width row items-start">
            <q-radio
              v-model="jobStartType"
              :val="1"
              label="Job to start on:"/>
            <date-picker v-if="jobStartType === 1" :autoSelect="true" :show-hint="false" :date="jobDate" v-on:change="handleJobDateChange" :min-date-today="true" />
          </div>
        </template>
      </q-field>

      <q-field
        :error="v$.duration.$error"
        borderless
        hide-bottom-space
        readonly
        label="Expected job duration"
      >
        <template v-slot:prepend>
          <q-icon name="timelapse" />
        </template>
        <template v-slot:control>
          <div class="full-width row items-center">
            <!-- <q-input
              :error="v$.duration.$error"
              error-message="required"
              v-model="duration"
              type="number"
              placeholder="00"
              :min="1"
              class="inline duration-to-start"
              style="width: 30px; margin: 0 30px"
            /> -->
            <q-select
              v-model="duration"
              :options="numberList"
              placeholder="00"
              style="width: 3rem"
              class="inline q-mx-sm"
              option-value="value"
              option-label="label"
              emit-value
              map-options
            />
            <q-select
              v-model="durationType"
              radio
              :options="durationTypes"
              class="inline"
              option-value="value"
              option-label="label"
              emit-value
              map-options
            />
          </div>
        </template>
      </q-field>

      <q-field
        borderless
        hide-bottom-space
        label="Working Days per week"
        :error="v$.daysPerWeek.$error"
      >
        <template v-slot:prepend>
          <q-icon name="timelapse" />
        </template>
        <template v-slot:control>
          <div class="full-width">
            <q-select
              v-model="daysPerWeek"
              :options="daysPerWeekTypes"
              class="inline"
              style="width: 150px"
              option-value="value"
              option-label="label"
              emit-value
              map-options
            />
            <div class="" v-if="daysPerWeek > 0">
              Days off per week: {{daysOff}}
            </div>
          </div>
        </template>
      </q-field>

      <q-field
        borderless
        readonly
        hide-bottom-space
        label="Hours worked each week"
        :error="v$.hoursPerWeek.$error">
        <template v-slot:prepend>
          <q-icon name="hourglass_empty" />
        </template>
        <template v-slot:control>
          <div class="full-width q-mt-md">
            <q-option-group
              type="radio"
              v-model="hoursPerWeek"
              :options="allHoursPerWeek"
            />
          </div>
        </template>
      </q-field>
    </div>
  </q-page>
</template>

<script>
import useVuelidate from '@vuelidate/core';
import { required, requiredIf } from '@vuelidate/validators';
import { mapState } from 'vuex';
import DatePicker from 'components/DatePicker';
import { beforeRouteEnter, beforeLeaveStep } from './multistep-helper';
import NotificationService from '../../utils/notification-service';
import c from '../../utils/constants';
import { getHoursPerWeekText } from '../../utils/helpers';

export default {
  name: 'PageAuPairJobStep1',
  components: { DatePicker },
  data() {
    return {
      durationTypes: [
        { label: 'Week(s)', value: 1 },
        { label: 'Month(s)', value: 2 },
      ],
      negotiableTypes: [
        { label: 'Yes', value: true },
        { label: 'No', value: false },
      ],
      numberList: c.numberList,
      daysPerWeekTypes: [
        { label: '1 day', value: 1 },
        { label: '2 days', value: 2 },
        { label: '3 days', value: 3 },
        { label: '4 days', value: 4 },
        { label: '5 days', value: 5 },
        { label: '6 days', value: 6 },
      ],
    };
  },
  setup() {
    return {
      v$: useVuelidate(),
    };
  },
  validations() {
    return {
      hoursPerWeek: {
        required,
      },
      daysPerWeek: {
        required,
      },
      durationToStart: {
        requiredIf: requiredIf(this.jobStartType === 2),
      },
      jobDate: {
        required(value) {
          if (this.jobStartType === 1) {
            // return required(this.job.jobDate);
            if (!value) {
              return false;
            }
            const jobDate = new Date(value);
            const today = new Date();
            today.setHours(0, 0, 0, 0);
            return jobDate.getTime() >= today.getTime();
          }

          return true;
        },
      },
      duration: {
        required,
      },
      // specialInstructions: {
      //   required,
      // },
      // hourlyPrice: {
      //   required,
      // },
    };
  },
  computed: {
    ...mapState({
      job: (state) => state.postAuPairJob.jobDetails.data,
    }),
    storeModuleName() {
      return 'postAuPairJob.jobDetails';
    },
    allHoursPerWeek() {
      const list = [
        { value: 10, label: getHoursPerWeekText(10) },
        { value: 20, label: getHoursPerWeekText(20) },
        { value: 30, label: getHoursPerWeekText(30) },
        { value: 40, label: getHoursPerWeekText(40) },
      ];

      return list;
    },
    hoursPerWeek: {
      get() {
        return this.job.jobSettings.hoursPerWeek;
      },
      set(value) {
        this.setProperty('jobSettings.hoursPerWeek', value);
      },
    },
    daysPerWeek: {
      get() {
        return this.job.jobSettings.daysPerWeek;
      },
      set(value) {
        this.setProperty('jobSettings.daysPerWeek', value);
      },
    },
    daysOff() {
      return this.daysPerWeek ? 7 - this.daysPerWeek : '';
    },
    jobStartType: {
      get() {
        return this.job.jobStartType;
      },
      set(value) {
        this.setProperty('jobStartType', value);
      },
    },
    durationToStart: {
      get() {
        return this.job.durationToStart;
      },
      set(value) {
        this.setProperty('durationToStart', value);
      },
    },
    jobStartDurationType: {
      get() {
        return this.job.jobStartDurationType;
      },
      set(value) {
        this.setProperty('jobStartDurationType', value);
      },
    },
    jobDate: {
      get() {
        return this.job.jobDate;
      },
      set(value) {
        // const parsedValue = value ? new Date(value) : null;
        // this.setProperty('jobDate', parsedValue);
        this.setProperty('jobDate', value);

        if (value && value.getDate) {
          this.setProperty('jobDateDay', value.getDate());
          this.setProperty('jobDateMonth', value.getMonth() + 1);
          this.setProperty('jobDateYear', value.getFullYear());
        }
      },
    },
    duration: {
      get() {
        return this.job.duration;
      },
      set(value) {
        this.setProperty('duration', value);
      },
    },
    durationType: {
      get() {
        return this.job.durationType;
      },
      set(value) {
        this.setProperty('durationType', value);
      },
    },
    specialInstructions: {
      get() {
        return this.job.specialInstructions;
      },
      set(value) {
        this.setProperty('specialInstructions', value);
      },
    },
    isPriceNegotiable: {
      get() {
        return this.job.isPriceNegotiable;
      },
      set(value) {
        this.setProperty('isPriceNegotiable', value);
      },
    },
    hourlyPrice: {
      get() {
        return this.job.hourlyPrice;
      },
      set(value) {
        this.setProperty('hourlyPrice', value);
      },
    },
  },
  methods: {
    validate() {
      this.v$.$touch();
      const hasError = this.v$.$error;
      if (hasError) {
        NotificationService.negative('Please review highlighted fields.');
      }

      return !hasError;
    },
    getDayLabel(day) {
      return getHoursPerWeekText(day);
    },
    handleJobDateChange(value) {
      this.jobDate = value.date;
    },
  },
  watch: {
    jobStartType(value) {
      if (value === 1) this.durationToStart = '';
      else this.jobDate = null;
    },
  },
  beforeRouteEnter(to, from, next) {
    beforeRouteEnter(to, from, next);
  },
  beforeRouteLeave(to, from, next) {
    beforeLeaveStep(this, to, from, next);
  },
};
</script>
<style lang="sass">
.post-au-pair-job-step-1
  .duration-to-start
    width: 10rem
</style>
