import * as types from '../mutation-types';
import * as http from '../../api/http';
import NotificationService from '../../utils/notification-service';
import { env } from '../../utils/env';
import { setAccessToken } from '../../api/auth';
import loader from '../../utils/loader';
import c from '../../utils/constants';
import ConversionTrackingService from '../../utils/conversion-tracking-service';

export default {
  state: {
    userDetails: {},
    userDetailsTemplate: {
      data: {
        accountType: c.accountType.unspecified,
        clientType: c.clientType.unspecified,
        clientCategory: c.clientCategory.unspecified,
        providerType: c.providerType.unspecified,
        interestedInJobTypes: 0,
        interestedInChildcareJobs: false,
        interestedInAuPairJobs: false,
        interestedInTutoringJobs: false,
        address: {
          addressLine1: '',
          addressLine2: '',
          suburb: '',
          state: '',
          postCode: '',
        },
        marketingInfo: {
          utmSource: '',
          utmMedium: '',
          utmCampaign: '',
          utmTerm: '',
          utmContent: '',
          identifier: '',
          referrer: '',
        },
        firstName: '',
        lastName: '',
        email: '',
        password: '',
        confirmPassword: '',
        gender: '',
        dateOfBirth: null,
        dateOfBirthDay: null,
        dateOfBirthMonth: null,
        dateOfBirthYear: null,
        phoneNumber: '',
        affiliateCode: '',
        country: '',
        businessName: '',
        tradingName: '',
        businessNumber: '',
        businessTaxNumber: '',
      },
      meta: {
        dirty: false,
        isProcessing: false,
        lookingFor: c.lookingFor.unspecified,
        completeAddress: '',
      },
    },
  },
  actions: {
    signupUser({ commit, state, dispatch }) {
      loader.show();
      commit(types.SIGNUP_REQUEST);
      return http
        .post('account/register', state.userDetails.data)
        .then(async (response) => {
          ConversionTrackingService.trackRegistration(state.userDetails.data.accountType === c.accountType.client);
          commit(types.SIGNUP_SUCCESS, response.data);
          await setAccessToken(response.data.accessToken, response.data.refreshToken, true);

          await dispatch('fetchCoreData');
          loader.hide();

          return response;
        })
        .catch((error) => {
          commit(types.SIGNUP_FAILURE);
          loader.hide();
          NotificationService.negative(error.message);

          return error;
        });
    },
    async prefillSignupForm({ commit }, currentRoute) {
      const marketingInfo = {
        utmSource: currentRoute.value.query.utm_source ? currentRoute.value.query.utm_source : '',
        utmMedium: currentRoute.value.query.utm_medium ? currentRoute.value.query.utm_medium : '',
        utmCampaign: currentRoute.value.query.utm_campaign ? currentRoute.value.query.utm_campaign : '',
        utmTerm: currentRoute.value.query.utm_term ? currentRoute.value.query.utm_term : '',
        utmContent: currentRoute.value.query.utm_content ? currentRoute.value.query.utm_content : '',
        identifier: currentRoute.value.query.gclid ? currentRoute.value.query.gclid : '',
        referrer: currentRoute.value.query.referer ? currentRoute.value.query.referer : document.referrer,
      };

      const address = {
        addressLine1: '',
        addressLine2: '',
        suburb: '',
        state: '',
        postCode: '',
      };

      if (currentRoute.value.query.gclid) {
        marketingInfo.utmSource = 'google';
        marketingInfo.utmMedium = 'cpc';
      }

      const firstName = '';
      let email = '';
      let tradingName = '';
      let accountType = c.accountType.unspecified;
      let clientType = c.clientType.unspecified;
      let lookingFor = c.lookingFor.unspecified;
      let completeAddress = '';

      if (marketingInfo.utmSource === 'js-pipeline' && marketingInfo.utmTerm) {
        const payload = {
          leadId: marketingInfo.utmTerm,
        };

        const leadRes = await http.post('account/leadInfo', payload);
        if (leadRes.status === 200) {
          const leadInfo = leadRes.data;
          lookingFor = c.lookingFor.businessClient;

          tradingName = leadInfo.name;
          email = leadInfo.email;
          accountType = c.accountType.client;
          clientType = c.clientType.business;

          address.addressLine1 = leadInfo.addressLine1;
          address.addressLine2 = leadInfo.addressLine2;
          address.formattedAddress = leadInfo.formattedAddress;
          address.suburb = leadInfo.formattedSuburb;
          address.state = leadInfo.state.toLowerCase();
          address.postCode = leadInfo.postCode;

          if (address.postCode && address.state && address.suburb) {
            if (env.country === 'au') {
              completeAddress = `${address.suburb}, ${address.state.toUpperCase()}`;
            } else {
              completeAddress = address.formattedAddress;
            }
          }
        }
      }

      const payload = {
        data: {
          firstName,
          email,
          accountType,
          clientType,
          tradingName,
          lastName: '',
          password: '',
          confirmPassword: '',
          affiliateCode: currentRoute.value.query.ac ? currentRoute.value.query.ac : '',
          country: env.country,
          deviceType: env.deviceType,
          platform: env.deviceInfo.platform,
          appVersion: env.appInfo.version,
          distVersion: env.versionNumber,
          marketingInfo,
          address,
        },
        meta: {
          lookingFor,
          completeAddress,
        },
      };

      commit(types.SIGNUP_PREFILL_FORM, payload);
    },
  },
  mutations: {
    [types.SIGNUP_REQUEST]: (state) => {
      state.userDetails.meta.isProcessing = true;
    },
    [types.SIGNUP_SUCCESS]: (state) => {
      state.userDetails.meta.isProcessing = false;
    },
    [types.SIGNUP_FAILURE]: (state) => {
      state.userDetails.meta.isProcessing = false;
    },
    [types.SIGNUP_PREFILL_FORM]: (state, payload) => {
      Object.assign(state.userDetails.data, payload.data);
      Object.assign(state.userDetails.meta, payload.meta);
    },
  },
  getters: {},
};
