// import { store } from 'quasar/wrappers';
import { createStore } from 'vuex';
import { date } from 'quasar';
import axios from 'axios';
import c from '../utils/constants';
import * as types from './mutation-types';
import * as http from '../api/http';
import PaymentManager from '../utils/payment-manager';

// modules.
import realtime from './modules/realtime';
import signup from './modules/signup';
import postJob from './modules/post-job';
import misc from './modules/misc';
import postOneOffJob from './modules/post-one-off-job';
import postRecurringJob from './modules/post-recurring-job';
import postAuPairJob from './modules/post-au-pair-job';
import postTutoringJob from './modules/post-tutoring-job';
import phoneVerification from './modules/phone-verification';
import payments from './modules/payments';
import filters from './modules/filters';
import profiles from './modules/profiles';
import families from './modules/families';
import helpers from './modules/helpers';
import profileWizard from './modules/profile-wizard';
import guardian from './modules/guardian';
import settings from './modules/settings';

import loader from '../utils/loader';
import intercomService from '../utils/intercom-service';
import { deepClone, flattenOptions, enableAppFeatures } from '../utils/helpers';
import errorHandler from '../utils/error-handler';
import { env, setCulture } from '../utils/env';
import analyticsService from '../utils/analytics-service';
import pushNotificationService from '../utils/push-notification-service';
import appUpdateManager from '../utils/app-update-manager';
import appRatingManager from '../utils/app-rating-manager';
import appManager from '../utils/app-manager';
import { logout } from '../api/auth';

// import example from './module-example'

/*
 * If not building with SSR mode, you can
 * directly export the Store instantiation;
 *
 * The function below can be async too; either use
 * async/await or return a Promise which resolves
 * with the Store instance.
 */

export default createStore({
  modules: {
    realtime,
    signup,
    postJob,
    misc,
    postOneOffJob,
    postRecurringJob,
    postAuPairJob,
    postTutoringJob,
    filters,
    phoneVerification,
    payments,
    profiles,
    profileWizard,
    families,
    helpers,
    guardian,
    settings,
  },

  // enable strict mode (adds overhead!)
  // for dev mode and --debug builds only
  strict: process.env.DEBUGGING,
  state: {
    pageInfo: {
      title: '',
      subtitle: 'subtitle',
      leftDrawerOpen: false,
      rightDrawerOpen: false,
      isProcessing: false,
    },
    userInfo: {
      data: {},
      meta: {
        isFetching: false,
        isProcessing: false,
        lastFetched: null,
        dirty: false,
      },
    },
    pages: {},
    activities: {
      data: [],
      meta: {
        isFetching: false,
        lastFetched: null,
      },
    },
    pushNotifications: {
      permission: null,
      pushChannel: null,
      enabled: null,
    },
    notifications: {
      data: [],
      meta: {
        isFetching: false,
        lastFetched: null,
      },
    },
    notificationChannels: {
      data: [],
      meta: {
        isFetching: false,
        lastFetched: null,
      },
    },
    appInfo: {
      adminMode: false,
      hasCodeUpdates: false,
      hasAppUpdates: false,
    },
  },
  actions: {
    setPageInfo({ commit }, payload) {
      commit(types.SET_PAGE_INFO, payload);
    },
    setPageProcessing({ commit }, payload) {
      commit(types.SET_PAGE_PROCESSING, payload);
    },
    toggleLeftDrawer({ commit }, value) {
      commit(types.TOGGLE_LEFT_DRAWER, value);
    },
    toggleRightDrawer({ commit }, value) {
      commit(types.TOGGLE_RIGHT_DRAWER, value);
    },
    resetStoreStructure({ commit }, payload) {
      commit(types.RESET_STORE_STRUCTURE, payload);
    },
    async updatePushSettings({ commit }, pushSettings) {
      if (typeof pushSettings === 'undefined') pushSettings = {};

      if (typeof pushSettings.enabled === 'undefined') {
        pushSettings.enabled = await pushNotificationService.checkEnabled();
      }

      commit(types.UPDATE_PUSH_SETTINGS, pushSettings);
    },
    fetchUserInfo({ commit, state, dispatch }) {
      if (state.userInfo.meta.lastFetched) {
        return Promise.resolve();
      }

      commit(types.USER_INFO_REQUEST);
      loader.show();
      return http
        .get('account/sessioninfo')
        .then((response) => {
          // commit(types.USER_INFO_SUCCESS, response.data);
          commit(types.UPDATE_USER_INFO_SUCCESS, response.data);
          const userInfo = state.userInfo.data;
          if (userInfo.accountStatus === c.accountStatus.pending) {
            dispatch('profileWizard/calculateSteps', userInfo);
          }

          pushNotificationService.configure();
          pushNotificationService.register();
          setCulture(state.userInfo.data.country);

          errorHandler.setUser(userInfo);
          analyticsService.setUser(userInfo);
          intercomService.init(userInfo, state.appInfo.adminMode);
          loader.hide();

          if (userInfo.accountStatus === c.accountStatus.approved && userInfo.appSettings.enableAppRating && enableAppFeatures(state.userInfo)) {
            const longTimeUser = userInfo.activationDate !== null && date.getDateDiff(new Date(), new Date(userInfo.activationDate), 'years') >= 1;
            const completedManyJobs = userInfo.client.jobsCompleted >= 2 || userInfo.provider.jobsCompleted >= 2;
            appRatingManager.configure(longTimeUser || completedManyJobs);
            appRatingManager.promptForRating(false);
          }

          PaymentManager.initialize();

          if (env.deviceInfo.platform !== 'web') {
            const requiresAppStoreUpdate = appUpdateManager.requiresAppStoreUpdate(state.userInfo);
            commit(types.UPDATE_APP_INFO, { hasAppUpdates: requiresAppStoreUpdate });
          }

          return response;
        })
        .catch(async (error) => {
          await logout();
          commit(types.USER_INFO_FAILURE, error);
          loader.hide();
          return Promise.reject(error);
        });
    },
    refreshUserInfo({ commit }) {
      commit(types.USER_INFO_REQUEST);
      return http
        .get('account/sessioninfo')
        .then((response) => {
          // commit(types.USER_INFO_SUCCESS, response.data);
          commit(types.UPDATE_USER_INFO_SUCCESS, response.data);
          return response;
        })
        .catch((error) => {
          commit(types.USER_INFO_FAILURE, error);
          return Promise.reject(error);
        });
    },
    setDefaultAddress({ commit }, index) {
      commit(types.SET_DEFAULT_ADDRESS, index);
    },
    updateStoreData({ commit }, payload) {
      commit(types.UPDATE_STORE_DATA, payload);
    },
    updateUserInfo({ commit, state }) {
      if (!state.userInfo.meta.dirty) {
        return { success: true };
      }
      loader.show();
      commit(types.UPDATE_USER_INFO_REQUEST);
      return http
        .patch('account/update', state.userInfo.data)
        .then((response) => {
          commit(types.UPDATE_USER_INFO_SUCCESS, response.data);
          loader.hide();
          return response;
        })
        .catch((error) => {
          commit(types.UPDATE_USER_INFO_FAILURE, error);
          loader.hide();
          return Promise.reject(error);
        });
    },
    submitTransactionInfo({ state }, trnData) {
      try {
        analyticsService.logTransaction(state.userInfo.data, trnData.paymentType);
      } catch (e) {
        errorHandler.captureException(e);
      }
    },
    fetchActivities({ commit, state }) {
      if (state.activities.meta.lastFetched) {
        return Promise.resolve();
      }
      loader.show();
      commit(types.FETCH_ACTIVITIES_REQUEST);
      return http
        .get('graph/activity/100/2500')
        .then((response) => {
          commit(types.FETCH_ACTIVITEIS_SUCCESS, response.data);
          loader.hide();
          return response;
        })
        .catch((error) => {
          commit(types.FETCH_ACTIVITIES_FAILURE, error);
          loader.hide();
          return Promise.reject(error);
        });
    },
    fetchNotifications({ commit, state }) {
      if (state.notifications.meta.lastFetched) {
        return Promise.resolve();
      }
      loader.show();
      commit(types.FETCH_NOTIFICATIONS_REQUEST);
      return http
        .get('notifications/recent')
        .then((response) => {
          commit(types.FETCH_NOTIFICATIONS_SUCCESS, response.data);
          loader.hide();
          return response;
        })
        .catch((error) => {
          commit(types.FETCH_NOTIFICATIONS_FAILURE, error);
          loader.hide();
          return Promise.reject(error);
        });
    },
    fetchNotificationChannels({ commit, state }) {
      if (state.notificationChannels.meta.lastFetched) {
        return Promise.resolve();
      }
      loader.show();
      commit(types.FETCH_NOTIFICATION_CHANNELS_REQUEST);
      return http
        .get('notifications/channels')
        .then((response) => {
          commit(types.FETCH_NOTIFICATION_CHANNELS_SUCCESS, response.data);
          loader.hide();
          return response;
        })
        .catch((error) => {
          commit(types.FETCH_NOTIFICATION_CHANNELS_FAILURE, error);
          loader.hide();
          return Promise.reject(error);
        });
    },
    suburbSearch(context, payload) {
      const term = encodeURIComponent(payload.term.trim().replace(/[^a-zA-Z0-9-_ ]/g, ''));
      const country = payload.country ? payload.country : env.country;
      const url = `https://api2.jugglestreet.com/api/geo/suburbsearch/${term}/${country}`;
      return axios.get(url)
        .then((response) => response.data)
        .catch((error) => Promise.reject(error));
    },
    addressSearch(context, payload) {
      const term = payload.term.trim();
      const country = payload.country ? payload.country : 'nz';
      const req = { term, country };
      return axios.post(`${env.apiRoot}geo/addresssearch`, req)
        .then((response) => response.data)
        .catch((error) => Promise.reject(error));
    },
    geocodeAddress(context, payload) {
      const term = payload.term.trim();
      const country = payload.country ? payload.country : env.country;
      const req = { term, country };
      return axios.post(`${env.apiRoot}geo/geocodeaddress`, req)
        .then((response) => response.data)
        .catch((error) => Promise.reject(error));
    },
    updateStoreValue({ commit }, payload) {
      commit(types.UPDATE_STORE_VALUE, payload);
    },
    pushMessageReceieved(context, notification) {
      const message = {
        id: notification.data.messageId,
        deliveryStatus: c.messageDeliveryStatus.seen,
      };

      return http
        .patch(`notifications/setdeliverystatus/${message.id}`, message)
        .catch((error) => Promise.reject(error));
    },
    async checkForCodeUpdates({ commit, state }) {
      if (!enableAppFeatures(state.userInfo)) return;

      const hasUpdate = await appUpdateManager.checkForCodeUpdatesBrief();
      commit(types.UPDATE_APP_INFO, { hasCodeUpdates: hasUpdate });

      if (hasUpdate) {
        appUpdateManager.showCodeUpdateDialog(false);
      }
    },
    installCodeUpdate() {
      appUpdateManager.showCodeUpdateDialog(true);
    },
    navigateToAppStore() {
      appUpdateManager.navigateToAppStore();
    },
    registerPushNotifications() {
      return pushNotificationService.register();
    },
    async configureApp({ state }) {
      await appUpdateManager.configure();

      const configuration = {
        handleAppStateChange: async (status) => {
          if (!status.isActive) return;

          if (enableAppFeatures(state.userInfo)) {
            await appUpdateManager.installUpdate(false);
          }
        },
      };

      appManager.configure(configuration);
      appManager.reportAppDownload();
    },
    initApp({ dispatch }) {
      try {
        dispatch('configureApp');
      } catch (e) {
        errorHandler.captureException(e);
      }
    },
    async fetchCoreData({ dispatch }) {
      try {
        await dispatch('fetchUserInfo');
      } catch (e) {
        errorHandler.captureException(e);
      }
    },
    sendFriendRequest: ({ commit, dispatch }, payload) => {
      loader.show();
      let url = 'connections';
      let request;
      if (payload.id && payload.id > 0) {
        url += `/${payload.id}`;
        request = http.patch;
      } else {
        request = http.post;
      }
      return request(url, payload)
        .then((response) => {
          const result = {
            friendId: payload.friendId,
            status: payload.status,
            actionRequired: false,
            id: response.data.id,
          };
          commit(types.FRIEND_REQUEST_SUCCESS, result);
          commit(`profiles/${types.FRIEND_REQUEST_SUCCESS}`, result);
          // dispatch('filters/refreshFilter');
          dispatch('realtime/fetchPendingStatsDirect');
          loader.hide();
          return response;
        })
        .catch((error) => {
          loader.hide();
          // TODO: Users that are deleted but still cached by the client causes various APIs to return user not found errors that neet to be gracefully handled.
          // const response = {
          //   status: c.friendStatus.hidden,
          //   actionRequired: false,
          //   friendId: payload.friendId
          // };
          // commit(`profiles/${types.FRIEND_REQUEST_SUCCESS}`, response);
          return Promise.reject(error);
        });
    },
  },
  mutations: {
    [types.SET_PAGE_INFO]: (state, payload) => {
      state.pageInfo.title = payload.title;
      state.pageInfo.subtitle = payload.subtitle;
      window.document.title = payload.title;
    },
    [types.SET_PAGE_PROCESSING]: (state, payload) => {
      state.pageInfo.isProcessing = payload.isProcessing;
    },
    [types.TOGGLE_LEFT_DRAWER]: (state, value) => {
      state.pageInfo.leftDrawerOpen = value;
    },
    [types.TOGGLE_RIGHT_DRAWER]: (state, value) => {
      state.pageInfo.rightDrawerOpen = value;
    },
    [types.USER_INFO_REQUEST]: (state) => {
      state.userInfo.meta.isFetching = true;
    },
    [types.SET_DEFAULT_ADDRESS]: (state, defaultAddressIndex) => {
      const defaultAddress = state.userInfo.data.addresses[defaultAddressIndex];
      const updatedAddresses = [];
      updatedAddresses.push(defaultAddress);

      state.userInfo.data.addresses.forEach((a, index) => {
        a.isPrimaryAddress = false;
        if (index !== defaultAddressIndex) {
          updatedAddresses.push(a);
        }
      });
      defaultAddress.isPrimaryAddress = true;

      state.userInfo.data.addresses = updatedAddresses;

      state.userInfo.meta.dirty = true;
    },
    [types.UPDATE_USER_INFO_SUCCESS]: (state, payload) => {
      state.userInfo.data = payload;
      state.userInfo.meta.isFetching = false;
      state.userInfo.meta.isProcessing = false;
      state.userInfo.meta.dirty = false;
      state.userInfo.meta.lastFetched = new Date();
    },
    [types.USER_INFO_FAILURE]: (state) => {
      state.userInfo.data = {};
      state.userInfo.meta.isFetching = false;
    },
    [types.UPDATE_USER_INFO_REQUEST]: (state) => {
      state.userInfo.meta.isProcessing = true;
    },
    // [types.UPDATE_USER_INFO_SUCCESS]: (state, payload) => {
    //   state.userInfo.data = Object.assign({}, state.userInfo.data, payload);
    //   state.userInfo.meta.isProcessing = false;
    //   state.userInfo.meta.dirty = false;
    //   state.userInfo.meta.lastFetched = new Date();
    // },
    [types.UPDATE_USER_INFO_FAILURE]: (state) => {
      state.userInfo.meta.isProcessing = false;
    },
    [types.FETCH_ACTIVITIES_REQUEST]: (state) => {
      state.activities.meta.isFetching = true;
    },
    [types.UPDATE_PUSH_SETTINGS]: (state, pushSettings) => {
      if (typeof pushSettings === 'undefined') return;

      if (typeof pushSettings.enabled !== 'undefined') state.pushNotifications.enabled = pushSettings.enabled;
      if (typeof pushSettings.permission !== 'undefined') state.pushNotifications.permission = pushSettings.permission;
      if (typeof pushSettings.pushChannel !== 'undefined') state.pushNotifications.pushChannel = pushSettings.pushChannel;
    },
    [types.FETCH_ACTIVITEIS_SUCCESS]: (state, payload) => {
      state.activities.data = payload;
      state.activities.meta.isFetching = false;
      state.activities.meta.lastFetched = new Date();
    },
    [types.FETCH_ACTIVITIES_FAILURE]: (state) => {
      state.notifications.data = [];
      state.notifications.meta.isFetching = false;
    },
    [types.FETCH_NOTIFICATIONS_REQUEST]: (state) => {
      state.notifications.meta.isFetching = true;
    },
    [types.FETCH_NOTIFICATIONS_SUCCESS]: (state, payload) => {
      state.notifications.data = payload;
      state.notifications.meta.isFetching = false;
      state.notifications.meta.lastFetched = new Date();
    },
    [types.FETCH_NOTIFICATIONS_FAILURE]: (state) => {
      state.notifications.data = [];
      state.notifications.meta.isFetching = false;
    },
    [types.FETCH_NOTIFICATION_CHANNELS_REQUEST]: (state) => {
      state.notificationChannels.meta.isFetching = true;
    },
    [types.FETCH_NOTIFICATION_CHANNELS_SUCCESS]: (state, payload) => {
      state.notificationChannels.data = payload;
      state.notificationChannels.meta.isFetching = false;
      state.notificationChannels.meta.lastFetched = new Date();
    },
    [types.FETCH_NOTIFICATION_CHANNELS_FAILURE]: (state) => {
      state.notificationChannels.data = [];
      state.notificationChannels.meta.isFetching = false;
    },
    [types.UPDATE_USER_OPTIONS]: (state, payload) => {
      const optionContainer = Object.resolve(state.userInfo.data, payload.propPath);
      const selectedValues = payload.value;
      const allOptions = flattenOptions(optionContainer);

      allOptions.forEach((option) => {
        const isSelected = selectedValues.indexOf(option.optionId) !== -1;
        if (option.selected !== isSelected) {
          option.selected = isSelected;
          state.userInfo.meta.dirty = true;
        }
      });
    },
    [types.RESET_STORE_STRUCTURE]: (state, payload) => {
      const sepIndex = payload.propPath.lastIndexOf('.');
      const propName = sepIndex === -1 ? payload.propPath : payload.propPath.substring(sepIndex + 1);
      const container = sepIndex === -1 ? state : Object.resolve(state, payload.propPath.substring(0, sepIndex));

      let { value } = payload;
      if (!value) {
        const templatePropName = `${propName}Template`;
        value = deepClone(container[templatePropName]);
      }

      const mutation = {};
      mutation[propName] = value;

      Object.assign(
        container,
        mutation,
      );
    },
    [types.UPDATE_STORE_VALUE]: (state, payload) => {
      const module = Object.resolve(state, payload.module);
      const sepIndex = payload.propPath.lastIndexOf('.');
      const propName = sepIndex === -1 ? payload.propPath : payload.propPath.substring(sepIndex + 1);
      const container = sepIndex === -1 ? module : Object.resolve(module, payload.propPath.substring(0, sepIndex));

      const mutation = {};
      mutation[propName] = payload.value;

      Object.assign(
        container,
        mutation,
      );
    },
    [types.UPDATE_STORE_DATA]: (state, payload) => {
      // TODO: Centralise store update logic.
      // let module = state[payload.module];
      const module = Object.resolve(state, payload.module);
      const sepIndex = payload.propPath.lastIndexOf('.');
      const propName = sepIndex === -1 ? payload.propPath : payload.propPath.substring(sepIndex + 1);
      const container = sepIndex === -1 ? module.data : Object.resolve(module.data, payload.propPath.substring(0, sepIndex));

      const mutation = {};
      mutation[propName] = payload.value;

      Object.assign(
        container,
        mutation,
      );

      module.meta.dirty = true;
    },
    [types.UPDATE_STORE_META]: (state, payload) => {
      // TODO: Centralise store update logic.
      // let module = state[payload.module];
      const module = Object.resolve(state, payload.module);
      const sepIndex = payload.propPath.lastIndexOf('.');
      const propName = sepIndex === -1 ? payload.propPath : payload.propPath.substring(sepIndex + 1);
      const container = sepIndex === -1 ? module.meta : Object.resolve(module.meta, payload.propPath.substring(0, sepIndex));

      const mutation = {};
      mutation[propName] = payload.value;

      Object.assign(
        container,
        mutation,
      );

      module.meta.dirty = true;
    },
    [types.FRIEND_REQUEST_SUCCESS]: (state, payload) => {
      if (!state.filters.data || state.filters.data.length === 0) {
        return;
      }

      const user = state.filters.data.find((u) => u.id === payload.friendId);
      if (!user) {
        return;
      }
      if (payload.status === c.friendStatus.hidden || payload.status === c.friendStatus.rejected) {
        const idx = state.filters.data.indexOf(user);
        state.filters.data.splice(idx, 1);
      } else {
        user.friendStatus = payload.status;
        user.actionRequired = payload.actionRequired;
        user.requestId = payload.id;
      }
    },
    [types.UPDATE_APP_INFO]: (state, payload) => {
      Object.assign(state.appInfo, payload);
    },
  },
  getters: {
    userInfo(state) {
      return state.userInfo.data;
    },
    pageInfo(state) {
      return state.pageInfo;
    },
    appInfo(state) {
      return state.appInfo;
    },
    isTutoringBusiness(state) {
      return state.userInfo.meta.lastFetched
        && state.userInfo.data.client.clientType === c.clientType.business
        && state.userInfo.data.interestedInTutoringJobs;
    },
  },
});
