<template>
  <div class="profile-wizard-helper-success-screen fullscreen relative-position">
    <div class="welcome-message">
      <p>
        <b>{{title}}</b>
      </p>
      <p class="subtitle">
        {{ successMessage }}
      </p>
      <div class="start-now-button">
        <q-btn color="accent" @click="handleFinish">Okay</q-btn>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import beforeLeaveStep from '../before-leave-step';
import store from '../../../store';
import c from '../../../utils/constants';

export default {
  name: 'ProfileWizardHelperSuccess',
  data() {
    return {
    };
  },
  methods: {
    handleFinish() {
      this.routerReplace({ path: '/dashboard' });
    },
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
      title: (state) => {
        const isPending = state.userInfo.data.accountStatus === c.accountStatus.pending;
        return isPending
          ? `${state.userInfo.data.firstName}!`
          : 'Congratulations your profile is now complete!';
      },
      successMessage: (state) => {
        const hasGuardian = state.userInfo.data.defaultGuardian;
        if (hasGuardian) {
          return 'An SMS has been sent to your guardian, your profile will be visible on the network as soon as it is approved.';
        }

        if (state.userInfo.data.accountStatus === c.accountStatus.pending) {
          return 'Your account is being reviewed and will be approved shortly.';
        }

        return 'View profiles of families and businesses in your neighbourhood, and start receiving job invitations.';
      },
    }),
  },
  beforeRouteLeave(to, from, next) {
    beforeLeaveStep(this, to, from, next);
  },
  beforeRouteEnter(to, from, next) {
    const profileCompletenss = store.getters.userInfo.profileCompleteness;

    if (profileCompletenss === 100) {
      next();
    } else {
      next({ path: '/dashboard', replace: true });
    }
  },
};
</script>
<style lang="sass">
.profile-wizard-helper-success-screen
  background: url('/images/welcome-screen-background.jpg') no-repeat
  background-size: cover
  .welcome-message
    text-align: center
    position: absolute
    height: 175px
    top: 6%
    left: 0
    width: 100vw
    background: rgb(238,238,238)
    opacity: 0.9
    padding: 1rem 0.5rem
    .title
      font-size: 1.2rem
</style>
