<template>
  <div class="">
    <q-field
      class="q-my-sm"
      label="Citizenship and Right to Work"
      borderless
      readonly
    >
      <template v-slot:prepend>
        <q-icon name="location_city" />
      </template>
      <template v-slot:control>
        <div class="full-width">
          <vevo-details ref="vevoDetails" v-on:change="handleChange" v-on:complete="handleComplete" />
        </div>
      </template>
    </q-field>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex';
import VevoDetails from 'components/VevoDetails';
import beforeLeaveStep from '../before-leave-step';
import * as types from '../../../store/mutation-types';

export default {
  name: 'ProfileWizardVevoDeta129ils',
  components: { VevoDetails },
  data() {
    return {};
  },
  methods: {
    ...mapActions([
      'updateUserInfo',
    ]),
    handleChange() {
      this.$store.commit(`profileWizard/${types.UPDATE_STEP_STATUS}`, { canSkip: false });
    },
    handleComplete() {
      this.$store.commit(`profileWizard/${types.UPDATE_STEP_STATUS}`, { complete: true });
    },
    validate() {
      if (this.currentStep.meta.canSkip) {
        return true;
      }

      return this.$refs.vevoDetails.validate(true);
    },
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
      currentStep: (state) => {
        const { meta } = state.profileWizard;
        const currentStep = meta.steps[meta.currentStep];
        return currentStep;
      },
      stepComplete: (state) => {
        const { meta } = state.profileWizard;
        const currentStep = meta.steps[meta.currentStep];
        return currentStep.complete;
      },
    }),
    storeModuleName() {
      return 'userInfo';
    },
  },
  beforeRouteLeave(to, from, next) {
    // Update user info before proceeding to the next step if step is complete
    if (this.stepComplete) {
      this.updateUserInfo().then(() => {
        beforeLeaveStep(this, to, from, next);
      });
    } else {
      beforeLeaveStep(this, to, from, next);
    }
  },
};
</script>
