<template>
  <q-page class="">
    <div>
      <job-actions/>
      <div class="layout-padding">
        <div v-if="meta.lastFetched != null">
          <helper-unrated-job-summary
            v-for="job in this.unratedJobs"
            :key="job.id"
            :job="job">
          </helper-unrated-job-summary>
        </div>
      </div>
    </div>
  </q-page>
</template>

<script>
import JobActions from 'components/JobActions';
import HelperUnratedJobSummary from 'components/HelperUnratedJobSummary';
import { mapState } from 'vuex';

export default {
  name: 'PageUnratedJobs',
  components: { JobActions, HelperUnratedJobSummary },
  data() {
    return {
    };
  },
  computed: {
    ...mapState({
      unratedJobs: (state) => state.helpers.unratedJobs.data,
      meta: (state) => state.helpers.unratedJobs.meta,
    }),
  },
  methods: {
  },
  beforeRouteEnter(to, from, next) {
    next(async (vm) => {
      await vm.$store.dispatch('helpers/fetchUnratedJobs');
    });
  },
};
</script>
