<template>
  <div>
    <q-field
      label-slot
      stack-label
      readonly
      hide-bottom-space
      borderless
      helper="Your mobile number will be used for notification messages to confirm jobs."
      :error-message="signupCountry === 'au' ? 'Enter a valid mobile number (04xxxxxxxx).' : 'Enter a valid mobile number (02xxxxxxxx).'"
    >
      <template v-slot:label>
        <h5 class="text-primary text-wrap no-margin">
          <span v-if="isBusiness">Details for Juggle St jobs</span>
          <span v-else>Juggle Street job details...</span>
        </h5>
      </template>
      <template v-slot:control>
        <div class="full-width">
          <q-input v-model="phoneNumber" max-length="12" :placeholder="isBusiness ? 'Account Holder Mobile Number' : 'Mobile Number'" type="tel" name="mobile" autocomplete:="tel" hide-bottom-space :error="v$.phoneNumber.$error"/>
        </div>
      </template>
    </q-field>
    <div class="q-mt-md">
      <span>{{isBusiness ? 'Business Street Address' : 'Street Address'}}</span>
      <q-input v-model="addressLine1" placeholder="Street Address e.g. 3/48 Northbourne Avenue" :error="v$.addressLine1.$error" name="address1" autocomplete="billing address-line1" autocapitalize="off" autocorrect="off" spellcheck="false" />
    </div>
    <div v-if="signupCountry === 'au'">
      <div v-if="!showAddressDetails">
        <div>
          <span>{{formattedSuburb}}</span>
          <q-btn flat color="secondary" class="q-mr-sm text-underline" @click="showAddressDetails = true">Change</q-btn>
        </div>
      </div>
      <div v-if="showAddressDetails">
        <!-- <q-select
          v-model="suburb"
          placeholder="Suburb"
          :error="v$.suburb.$error"
          autocomplete="billing address-level2"
          autocapitalize="off"
          autocorrect="off"
          option-value="value"
          option-label="label"
          use-input
          hide-selected
          fill-input
          :options="matchingSuburbs"
          @filter="suburbSearch"
          :min-characters="3"
          :max-results="10"
          spellcheck="false">
        </q-select> -->
        <q-input
          v-model="suburb"
          label="Suburb"
          placeholder="Suburb"
          :error="v$.suburb.$error"
          autoComplete="billing address-level2"
          autocapitalize="off"
          autocorrect="off"
          spellcheck="false" />

        <q-select
          placeholder="State"
          v-model="state"
          :error="v$.state.$error"
          :options="states"
          autoComplete="billing address-level1"
          option-value="value"
          option-label="label"
          emit-value
          map-options
        />
        <q-input v-model="postCode" placeholder="Post Code" :error="v$.postCode.$error" autocomplete="billing postal-code" />
      </div>
    </div>
    <div v-else-if="signupCountry === 'nz'">
      <q-input v-model="suburb" label="Suburb" placeholder="Suburb" :error="v$.suburb.$error" autocomplete="none" autocapitalize="off" autocorrect="off" spellcheck="false" />
      <q-input v-model="state" label="Region/City" placeholder="Region/City" :error="v$.state.$error" autocomplete="none" />
      <q-input v-model="postCode" label="Post Code" placeholder="Post Code" :error="v$.postCode.$error" autocomplete="none" />
    </div>
    <div>
      <div v-if="isBusiness" class="q-mt-sm">
        Your mobile number and address are only shared with Juggle St helpers after you award a job.
      </div>
      <div v-else class="q-mt-sm">
        <div v-if="isClient">
          Your mobile number and home address are only shared with Juggle St helpers after you award a job.
        </div>
        <div v-if="isProvider">
          Your mobile number will only be shared with a Juggle Street family once you are awarded a job.
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex';
import useVuelidate from '@vuelidate/core';
import {
  required, numeric, maxLength, minLength,
} from '@vuelidate/validators';
import store from '../../store';
import { validatePhoneNumber } from '../../utils/helpers';
import c from '../../utils/constants';
import errorHandler from '../../utils/error-handler';

export default {
  name: 'SignupStep1',
  props: ['currentStep'],
  data() {
    const signup = store.state.signup.userDetails.data;
    return {
      states: c.states,
      showAddressDetails: (!signup.address.suburb || !signup.address.postCode),
      matchingSuburbs: [],
    };
  },
  setup() {
    return {
      v$: useVuelidate(),
    };
  },
  validations() {
    return {
      phoneNumber: {
        validate(value) {
          return validatePhoneNumber(value, this.signupCountry);
        },
      },
      addressLine1: {
        validate(value) {
          if (!value) return false;

          return (value.indexOf(' ') !== -1);
        },
      },
      suburb: {
        required,
      },
      state: {
        required,
      },
      postCode: {
        required,
        numeric,
        maxLength: maxLength(4),
        minLength: minLength(4),
      },
    };
  },
  computed: {
    ...mapState({
      accountInfo: (state) => state.signup.userDetails.data,
    }),
    storeModuleName() {
      return 'signup.userDetails';
    },
    isBusiness() {
      return this.accountInfo.clientType === c.clientType.business || this.accountInfo.providerType === c.providerType.business;
    },
    formattedSuburb() {
      return `${this.suburb}, ${this.state.toUpperCase()} ${this.postCode}`;
    },
    isClient() {
      return this.accountInfo.accountType === c.accountType.client;
    },
    isProvider() {
      return this.accountInfo.accountType === c.accountType.provider;
    },
    signupCountry() {
      return this.accountInfo.country;
    },
    phoneNumber: {
      get() {
        return this.accountInfo.phoneNumber;
      },
      set(value) {
        this.setProperty('phoneNumber', value);
      },
    },
    addressLine1: {
      get() {
        return this.accountInfo.address.addressLine1;
      },
      set(value) {
        this.setProperty('address.addressLine1', value);
      },
    },
    addressLine2: {
      get() {
        return this.accountInfo.address.addressLine2;
      },
      set(value) {
        this.setProperty('address.addressLine2', value);
      },
    },
    suburb: {
      get() {
        return this.accountInfo.address.suburb;
      },
      set(value) {
        if (value && value.meta) {
          this.setProperty('address.suburb', value.meta.name);
          this.suburbSelected(value);
        } else {
          this.setProperty('address.suburb', value);
        }
      },
    },
    postCode: {
      get() {
        return this.accountInfo.address.postCode;
      },
      set(value) {
        this.setProperty('address.postCode', value);
      },
    },
    state: {
      get() {
        return this.accountInfo.address.state;
      },
      set(value) {
        this.setProperty('address.state', value);
      },
    },
  },
  methods: {
    ...mapActions({
      suburbSearchService: 'suburbSearch',
    }),
    validate() {
      this.v$.$touch();
      const hasError = this.v$.$error;
      return hasError;
    },
    async suburbSearch(term, update, abort) {
      if (term.length < 3) {
        abort();
        return;
      }

      let results = [];
      try {
        const suburbs = await this.suburbSearchService({ term });
        results = suburbs.map((suburb) => ({
          label: `${suburb.name}, ${suburb.state.replace('-', ' ').toUpperCase()}`,
          value: suburb.name,
          meta: suburb,
        }));
      } catch (err) {
        errorHandler.captureException(err);
      }

      update(() => {
        this.matchingSuburbs = results;
      });
    },
    suburbSelected(item) {
      if (item.meta) {
        this.state = item.meta.state;
        this.postCode = item.meta.postcode;
      }
    },
  },
};
</script>
<style lang="sass">

</style>
