import c from '../../../utils/constants';

import welcome from './Welcome';
import identityCheck from '../IdentityCheck';
import intro from './IntroduceYourself';
import skills from './SkillsAndLanguages';
import photo from '../ProfilePhoto';
import guardian from './GuardianApproval';
import success from './Success';
import childcareExperience from './ChildcareExperience';
import tutorPrimarySchoolExperience from './PrimarySchoolTutoringExperience';
import tutorHighSchoolExperience from './HighSchoolTutoringExperience';
import tutoringCategory from './TutoringCategory';
import auPairExperience from './AuPairExperience';
import vevoDetails from './ProfileWizardVevoDetails';
import profileVisibility from './ProfileVisibility';
import helperResponsibilities from './HelperResponsibilities';
import certificatesManager from './CertificatesManager';
import personalReferences from './PersonalReferences';
import paymentMethods from './PaymentMethods';
import profileVideo from './ProfileVideo';
import helperMembership from './HelperMembership';

export const steps = [
  {
    profileItem: null,
    path: 'welcome',
    title: 'Welcome',
    shouldInclude: () => true,
    component: welcome,
    complete: true,
  },
  {
    profileItem: null,
    path: 'helper-responsibilities',
    title: 'Responsibilities',
    shouldInclude: (userInfo) => userInfo.networkResponsibilitiesAcceptanceDate === null,
    component: helperResponsibilities,
    complete: false,
    canSkip: false,
  },
  {
    profileItem: c.profileItems.trustVerification,
    title: 'Identity Check',
    path: 'identity-check',
    component: identityCheck,
    complete: false,
  },
  {
    profileItem: c.profileItems.providerAboutMe | c.profileItems.providerJobCategories,
    title: 'Introduction',
    path: 'introduce-yourself',
    component: intro,
    shouldInclude: () => true,
    complete: false,
    canSkip: true,
  },
  {
    profileItem: c.profileItems.providerCertificates,
    title: 'WWCC Details',
    path: 'certificates-manager',
    component: certificatesManager,
    complete: false,
    shouldInclude: (userInfo) => userInfo.country === 'au' && !userInfo.provider.requiresGuardianApproval,
    canSkip: true,
  },
  {
    profileItem: c.profileItems.providerJobCategories,
    title: 'Childcare',
    path: 'childcare-experience',
    component: childcareExperience,
    complete: false,
    shouldInclude: (userInfo) => userInfo.interestedInChildcareJobs && (userInfo.interestedInJobTypes > 0 || (userInfo.interestedInJobTypes & 15) === 0),
    canSkip: true,
  },
  {
    profileItem: c.profileItems.providerJobCategories,
    title: 'Au Pair',
    path: 'au-pair-experience',
    component: auPairExperience,
    complete: false,
    shouldInclude: (userInfo) => userInfo.interestedInAuPairJobs && (userInfo.interestedInJobTypes > 0 || (userInfo.interestedInJobTypes & c.jobType.auPair) === 0),
    canSkip: false,
  },
  {
    profileItem: c.profileItems.providerJobCategories,
    title: 'References',
    path: 'personal-references',
    component: personalReferences,
    complete: false,
    shouldInclude: (userInfo) => {
      let nonJsRefs = 0;

      userInfo.vouches.forEach((v) => {
        if (v.referenceType !== c.vouchReferenceType.juggleStreet) {
          nonJsRefs++;
        }
      });

      const result = userInfo.interestedInAuPairJobs && (userInfo.interestedInJobTypes > 0 || (userInfo.interestedInJobTypes & c.jobType.auPair) === 0) && nonJsRefs < 1;
      return result;
    },
    canSkip: true,
  },
  {
    profileItem: c.profileItems.providerJobCategories,
    title: 'Tutoring',
    path: 'tutoring-category',
    component: tutoringCategory,
    complete: false,
    shouldInclude: (userInfo) => userInfo.interestedInTutoringJobs,
    canSkip: false,
  },
  {
    profileItem: c.profileItems.providerJobCategories,
    title: 'Primary School',
    path: 'primary-school-tutoring-experience',
    component: tutorPrimarySchoolExperience,
    complete: false,
    shouldInclude: (userInfo) => userInfo.interestedInTutoringJobs && userInfo.interestedInJobTypes > 0 && (userInfo.interestedInJobTypes & c.jobType.primarySchoolTutoring) !== 0,
    canSkip: true,
  },
  {
    profileItem: c.profileItems.providerJobCategories,
    title: 'High School',
    path: 'high-school-tutoring-experience',
    component: tutorHighSchoolExperience,
    complete: false,
    shouldInclude: (userInfo) => userInfo.interestedInTutoringJobs && userInfo.interestedInJobTypes > 0 && (userInfo.interestedInJobTypes & c.jobType.highSchoolTutoring) !== 0,
    canSkip: true,
  },
  {
    profileItem: c.profileItems.providerPhoto,
    title: 'Profile Photo',
    path: 'profile-photo',
    component: photo,
    complete: false,
    canSkip: true,
  },
  {
    profileItem: c.profileItems.providerJobCategories,
    title: 'Profile Video',
    path: 'profile-video',
    component: profileVideo,
    complete: false,
    shouldInclude: (userInfo) => {
      const noVideo = userInfo.medias.length === 0;
      // const requiredForTutoring = userInfo.interestedInTutoringJobs && userInfo.provider.interestedInOnlineTutoring;
      // const requiredForAuPairs = userInfo.interestedInAuPairJobs;

      // const result = noVideo && (requiredForTutoring || requiredForAuPairs);
      // const result = noVideo && requiredForTutoring;
      return noVideo;
    },
    canSkip: true,
  },
  {
    profileItem: c.profileItems.citizenship,
    title: 'Citizenship',
    path: 'vevo-details',
    component: vevoDetails,
    complete: false,
    canSkip: true,
  },
  {
    profileItem: c.profileItems.paymentMethods,
    title: 'Payment Methods',
    path: 'payment-methods',
    component: paymentMethods,
    complete: false,
    canSkip: false,
  },
  {
    profileItem: null,
    title: 'Other Skills',
    path: 'skills-and-languages',
    shouldInclude: (userInfo) => {
      let shouldInclude = true;

      const hasSelectedOptions = (option) => option.selected;

      shouldInclude = userInfo.provider.transport.filter(hasSelectedOptions).length === 0
        // userInfo.provider.extraSkills.filter(hasSelectedOptions).length === 0 &&
        && userInfo.provider.spokenLanguages.filter(hasSelectedOptions).length === 0;
      return shouldInclude;
    },
    component: skills,
    complete: false,
    canSkip: true,
  },
  {
    profileItem: c.profileItems.profileVisibility,
    title: 'Privacy Settings',
    path: 'profile-visibility',
    shouldInclude: (userInfo) => !userInfo.provider.requiresGuardianApproval,
    component: profileVisibility,
    complete: false,
    canSkip: true,
  },
  {
    profileItem: c.profileItems.providerMembership,
    path: 'helper-membership',
    title: 'Membership',
    shouldInclude: (userInfo) => {
      if (userInfo.provider.requiresGuardianApproval) return false;
      return true;
    },
    component: helperMembership,
    complete: false,
    canSkip: true,
  },
  {
    title: 'Guardian Details',
    path: 'guardian-approval',
    shouldInclude: (userInfo) => userInfo.provider.requiresGuardianApproval,
    component: guardian,
    complete: false,
  },
  {
    title: 'Success',
    path: 'success',
    component: success,
    complete: true,
  },
];

export const getRoutes = () => {
  const mappedSteps = steps.map((step, index) => ({
    path: step.path,
    name: `helper-profile-wizard-${step.path}`,
    component: step.component,
    meta: { stepIndex: index, title: step.title },
  }));
  return mappedSteps;
};
