<template>
  <q-page class="">
    <div class="layout-padding">
      <q-field
        label="Payment Methods"
        borderless
        readonly
      >
        <template v-slot:prepend>
          <q-icon name="monetization_on" />
        </template>
        <template v-slot:control>
          <div class="full-width q-mt-md">
            <payment-methods ref="paymentMethods" />
          </div>
        </template>
      </q-field>
    </div>
  </q-page>
</template>

<script>
import PaymentMethods from 'components/PaymentMethods';
import NotificationService from '../../utils/notification-service';

export default {
  name: 'PageAccountPaymentMethods',
  components: { PaymentMethods },
  data() {
    return {};
  },
  methods: {},
  computed: {},
  beforeRouteLeave(to, from, next) {
    if (!this.$refs.paymentMethods.validate(true)) {
      next(false);
      return;
    }

    this.$store.dispatch('updateUserInfo').then(() => {
      next();
    }).catch((error) => {
      NotificationService.negative(error.message);
      next(false);
    });
  },
};
</script>
