import axios from 'axios';
import { env } from '../utils/env';
import { getAccessToken } from './auth';

const get = (path) => {
  console.log('Get called::', path);

  return axios({
    method: 'GET',
    url: env.apiRoot + path,
    headers: {
      Authorization: `Bearer ${getAccessToken()}`,
    },
  });
};

const post = (path, payload) => {
  console.log('Post called::', path);

  return axios({
    method: 'POST',
    url: env.apiRoot + path,
    data: payload,
    headers: {
      Authorization: `Bearer ${getAccessToken()}`,
    },
  });
};

const patch = (path, payload) => {
  console.log('Patch called::', path);

  return axios({
    method: 'PATCH',
    url: env.apiRoot + path,
    data: payload,
    headers: {
      Authorization: `Bearer ${getAccessToken()}`,
    },
  });
};

const deleteResource = (path, payload) => {
  console.log('Delete called::', path);

  return axios({
    method: 'DELETE',
    url: env.apiRoot + path,
    data: payload,
    headers: {
      Authorization: `Bearer ${getAccessToken()}`,
    },
  });
};

export {
  get, post, patch, deleteResource,
};
