import { AppRate } from '@ionic-enterprise/app-rate';
import intercomService from './intercom-service';
import { env } from './env';
import c from './constants';
import conversionTrackingService from './conversion-tracking-service';

const iosUrl = 'itms-apps://itunes.apple.com/app/viewContentsUserReviews/id1586742271?action=write-review';
const androidUrl = 'market://details?id=com.jugglestreet.app';

let configured = false;
let canRate = false;

const AppRatingManager = {
  canRateNow() {
    return canRate;
  },
  configure(promptNow) {
    if (configured) return Promise.resolve();

    configured = true;
    canRate = promptNow;

    if (env.deviceInfo.platform === 'web') return Promise.resolve();

    if (!AppRate || !AppRate.setPreferences) return Promise.resolve();

    AppRate.setPreferences({
      usesUntilPrompt: promptNow ? 3 : 20,
      simpleMode: true,
      customLocale: {
        title: 'Would you mind rating %@?',
        message: 'It won’t take more than a minute and helps to promote our app. Thanks for your support!',
        cancelButtonLabel: 'No, Thanks',
        laterButtonLabel: 'Remind Me Later',
        rateButtonLabel: 'Rate It Now',
        yesButtonLabel: 'Yes!',
        noButtonLabel: 'Not really',
        appRatePromptTitle: 'Do you like using %@',
        feedbackPromptTitle: 'Mind giving us some feedback?',
      },
      reviewType: {
        ios: 'InAppReview',
        android: 'InAppBrowser',
      },
      storeAppURL: {
        ios: '1586742271',
        android: 'market://details?id=com.jugglestreet.app',
      },
      callbacks: {
        handleNegativeFeedback: () => {
          intercomService.show();
        },
        // onRateDialogShow: (callback) => {
        //   console.log('onRateDialogShow');
        //   callback(1); // Cause immediate click on 'Rate Now' button.
        // },
        onButtonClicked: (buttonIndex) => {
          // Rate It Now: 3
          console.log(`App Rating:: onButtonClicked ${buttonIndex}`);
          if (buttonIndex === 3) {
            conversionTrackingService.logAppEvent(c.appEventType.appRating);
          }
        },
      },
      openUrl: (url) => {
        let safariAvailable = false;

        if (window.SafariViewController) {
          window.SafariViewController.isAvailable((available) => {
            safariAvailable = available;
          });
        }

        if (!safariAvailable) {
          window.open(url, '_blank', 'location=yes');
        } else {
          window.SafariViewController.show(
            {
              url,
              barColor: '#299C4A',
              controlTintColor: '#00ffff',
              tintColor: '#00ffff',
            },

            (result) => {
              console.log(result.event);
            },

            (msg) => {
              console.log(`Error: ${msg}`);
            },
          );
        }
      },
    });

    return Promise.resolve();
  },
  promptForRating(immediately) {
    if (env.deviceInfo.platform === 'web') return;

    if (!AppRate || !AppRate.promptForRating) return;

    AppRate.promptForRating(immediately);
  },
  promptForAppStoreRating() {
    if (env.deviceInfo.platform === 'web') return;
    conversionTrackingService.logAppEvent(c.appEventType.appRating);

    const url = env.deviceInfo.operatingSystem === 'android' ? androidUrl : iosUrl;
    window.open(url, '_blank', 'location=yes');
  },
};

export default AppRatingManager;
