<template>
  <q-page class="post-tutoring-job-step-9">
    <div class="layout-padding">
      <q-field
        borderless
        readonly
        hide-bottom-space
      >
        <template v-slot:prepend>
          <q-icon name="date_range" />
        </template>
        <template v-slot:control>
          {{getJobDateText()}}
        </template>
      </q-field>

      <q-field
        borderless
        readonly
        hide-bottom-space
      >
        <template v-slot:prepend>
          <q-icon name="home" />
        </template>
        <template v-slot:control>
          <div>
            <span v-if="addressLabel" class="text-primary text-bold">{{addressLabel}}<br/></span>
            {{getAddress()}}
          </div>
        </template>
      </q-field>

      <weekly-schedule-summary :job="job" :meta="meta" :minHours="0"/>

      <q-field
        borderless
        readonly
        hide-bottom-space
        label="Payment Method">
        <template v-slot:prepend>
          <q-icon name="monetization_on" />
        </template>
        <template v-slot:control>
          <span class="q-mt-md">{{getHelperPaymentMethod()}}</span>
        </template>
      </q-field>

      <q-field
        borderless
        readonly
        hide-bottom-space
        label="Tutoring Requirements">
          <template v-slot:prepend>
            <q-icon name="supervised_user_circle" />
          </template>
          <template v-slot:control>
            <div class="q-mt-md">
              <div>
                Type: <span class="text-grey">{{getTutoringType()}}</span>
              </div>
              <div v-if="getSchoolSubject()">
                Subject(s): <span class="text-grey">{{getSchoolSubject()}}</span>
              </div>
              <div v-if="getSchoolYears()">
                Year(s): <span class="text-grey">{{getSchoolYears()}}</span>
              </div>
            </div>
          </template>
      </q-field>
      <!-- <q-field
        borderless
        readonly
        hide-bottom-space
        label="School Subject/Year">
          <template v-slot:prepend>
            <q-icon name="school" />
          </template>
          <template v-slot:control>
            <span class="q-mt-md">{{getSchoolSubject()}}{{getSchoolYears()}}</span>
          </template>
      </q-field> -->

      <q-field
        label="Job Description"
        borderless
        readonly
        hide-bottom-space
      >
        <template v-slot:prepend>
          <q-icon name="assignment" />
        </template>
        <template v-slot:control>
          <div class="full-width q-mt-md">
            <div v-for="(text, index) in job.specialInstructions.split('\n')" :key="index">
              {{text}}&nbsp;
            </div>
          </div>
        </template>
      </q-field>

      <filters-panel mode="jobDetails" :filterType="filterKey" :jobType="job.jobType" v-if="job.managedBy === types.managedBy.system" />

      <q-field
        v-if="selectedApplicants.length > 0"
        label="Invited Tutors"
        borderless
        readonly
      >
        <template v-slot:prepend>
          <q-icon name="people" />
        </template>
      </q-field>
      <div class="full-width invited-carers" style="margin-bottom: 50px">
        <helper-profile-card
          v-for="helper in selectedApplicants"
          :key="helper.id"
          :helper="helper"
          :displayDistance="false"
          mode="read-only"
        />
      </div>

      <q-btn
        big
        color="accent"
        class="full-width fixed-bottom"
        @click="handlePostJob">
          Post Job
      </q-btn>
    </div>
  </q-page>
</template>

<script>
import { mapState } from 'vuex';
import { date } from 'quasar';
import HelperProfileCard from 'components/HelperProfileCard';
import FiltersPanel from 'components/FiltersPanel';
import WeeklyScheduleSummary from 'components/WeeklyScheduleSummary';
import { beforeRouteEnter, beforeLeaveStep, postJob } from './multistep-helper';
import {
  getJobDeliveryMethodText, getOption, getJobPaymentMethodText, getDayOfWeekText, formatTime, getFormattedAddress,
} from '../../utils/helpers';
import c from '../../utils/constants';

export default {
  name: 'PageTutoringJobStep9',
  components: { HelperProfileCard, WeeklyScheduleSummary, FiltersPanel },
  data() {
    return {
      types: {
        managedBy: c.managedBy,
      },
    };
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
      job: (state) => state.postTutoringJob.jobDetails.data,
      meta: (state) => state.postTutoringJob.jobDetails.meta,
    }),
    filterKey() {
      const filterType = this.job.jobDeliveryMethod === c.jobDeliveryMethod.faceToFace ? 'postHomeTutoringJob' : 'postOnlineTutoringJob';

      return filterType;
    },
    selectedApplicants() {
      return this.meta.selectedApplicants;
    },
    addressLabel() {
      const address = this.userInfo.addresses.find((a) => a.id === this.job.addressId);
      if (!address) return '';

      return address.addressLabel;
    },
  },
  methods: {
    validate() {
      return true;
    },
    formatTime(time) {
      return formatTime(time);
    },
    getAddress() {
      const address = this.userInfo.addresses.find((a) => a.id === this.job.addressId);
      return getFormattedAddress(address);
    },
    getJobDateText() {
      const {
        jobType,
        jobDate,
        jobStartType,
        jobStartDurationType,
        durationToStart,
        durationType,
        duration,
      } = this.job;

      let tutorType;
      if (jobType === c.jobType.primarySchoolTutoring) {
        tutorType = 'Primary School';
      } else if (this.country === 'au') {
        tutorType = 'High School';
      } else {
        tutorType = 'Secondary School';
      }

      let text = `You are looking for a ${tutorType} Tutor starting `;

      if (Number(jobStartType) === c.jobStartType.duration) {
        text += `within ${durationToStart} `;
        if (Number(jobStartDurationType) === c.durationType.weeks) {
          text += 'weeks';
        } else {
          text += 'months';
        }
      } else {
        text += `on ${date.formatDate(jobDate, 'ddd D MMM YY')}`;
      }

      text += `, for ${duration} `;
      if (durationType === c.durationType.weeks) {
        text += 'weeks.';
      } else {
        text += 'months.';
      }

      return text;
    },
    getDayLabel(day) {
      return getDayOfWeekText(day);
    },
    getSchoolSubject() {
      if (!this.job.jobSettings.schoolSubjects || this.job.jobSettings.schoolSubjects.length === 0) {
        return 'All';
      }

      const schoolSubjects = this.job.jobType === c.jobType.primarySchoolTutoring ? this.userInfo.client.primarySchoolSubjects : this.userInfo.client.highSchoolSubjects;
      const option = getOption(this.job.jobSettings.schoolSubjects[0], schoolSubjects);

      return option.text;
    },
    getSchoolYears() {
      if (!this.job.jobSettings.schoolYears || this.job.jobSettings.schoolYears.length === 0) {
        return '';
      }

      const schoolYears = this.job.jobType === c.jobType.primarySchoolTutoring ? this.userInfo.client.primarySchoolYears : this.userInfo.client.highSchoolYears;

      let text = '';
      this.job.jobSettings.schoolYears.forEach((schoolYear) => {
        const option = getOption(schoolYear, schoolYears);
        text += `${option.text}/`;
      });

      return ` - ${text.substring(0, text.length - 1)}`;
    },
    getWeeklyTotal() {
      const { weeklyScheduleEntries } = this.meta;

      const weeklyTotal = weeklyScheduleEntries.reduce((totalAcc, entry) => {
        const shiftTotal = entry.shifts.reduce((shiftAcc, shift) => {
          shiftAcc += (shift.hours * this.job.price);
          return shiftAcc;
        }, 0);
        totalAcc += shiftTotal;
        return totalAcc;
      }, 0);
      return weeklyTotal;
    },
    getHelperPaymentMethod() {
      return getJobPaymentMethodText(this.job.helperPaymentMethod);
    },
    getTutoringType() {
      return getJobDeliveryMethodText(this.job.jobDeliveryMethod);
    },
    handlePostJob() {
      postJob(this.$router);
    },
  },
  beforeRouteEnter(to, from, next) {
    beforeRouteEnter(to, from, next);
  },
  beforeRouteLeave(to, from, next) {
    beforeLeaveStep(this, to, from, next);
  },
};
</script>
<style lang="sass">
.post-tutoring-job-step-9
  .schedule-table
    margin-bottom: 1rem
</style>
