<template>
  <div>
    <q-dialog v-model="opened">
      <q-layout view="Lhh lpR fff" container class="bg-white" style="max-height: 450px">
        <q-toolbar>
          <q-toolbar-title>
            {{ index >= 0 ? 'Edit Reference' : 'Add Reference' }}
          </q-toolbar-title>
          <q-btn flat>
            <q-icon name="clear" v-close-popup />
          </q-btn>
        </q-toolbar>

        <q-page-container>
          <div class="layout-padding">
            <q-field
              borderless
              stack-label
            >
              <template v-slot:prepend>
                <q-icon :name="icon" />
              </template>
              <template v-slot:control>
                <div class="full-width">
                  <q-input
                    class="q-my-xs"
                    v-model="fullName"
                    :error="v$.fullName.$error"
                    hide-bottom-space
                    placeholder="First name and surname"
                  />
                  <q-input
                    class="q-my-xs"
                    :error="v$.phoneNumber.$error"
                    v-model="phoneNumber"
                    hide-bottom-space
                    placeholder="Telephone #"
                  />
                  <q-input
                    class="q-my-xs"
                    v-model="email"
                    :error="v$.email.$error"
                    hide-bottom-space
                    placeholder="Email address"
                  />
                  <q-select
                    class="q-my-xs"
                    :error="v$.referenceType.$error"
                    v-model="referenceType"
                    :options="referenceList"
                    hide-bottom-space
                    label="Reference Type"
                    option-value="value"
                    option-label="label"
                    emit-value
                    map-options
                    use-input
                    hide-selected
                    fill-input
                  />
                  <q-input
                    v-if="referenceType === 4"
                    class="q-my-xs"
                    v-model="otherReferenceType"
                    :error="v$.otherReferenceType.$error"
                    placeholder="Other - please specify"
                  />
                </div>
              </template>
            </q-field>
          </div>
        </q-page-container>

        <q-footer class="bg-white">
          <q-toolbar>
            <q-btn color="accent" @click="handleSave">
              <q-icon name="save"/>
              Save
            </q-btn>
          </q-toolbar>
        </q-footer>
      </q-layout>
    </q-dialog>
  </div>
</template>

<script>
import useVuelidate from '@vuelidate/core';
import { required, email, requiredIf } from '@vuelidate/validators';
import { validateGenericPhoneNumber } from '../utils/helpers';
import NotificationService from '../utils/notification-service';

export default {
  name: 'PersonalReference',
  props: ['icon'],
  emits: ['change', 'complete', 'success'],
  data() {
    return {
      reference: {
      },
      index: -1,
      opened: false,
    };
  },
  methods: {
    isValid() {
      return !this.v$.$invalid;
    },
    validate() {
      const isValid = this.isValid();
      if (isValid) return true;

      this.v$.$touch();
      return !this.v$.$error;
    },
    triggerChange() {
      const isValid = this.isValid();
      this.$emit('change', { valid: isValid });

      if (isValid) {
        this.triggerComplete();
      }
    },
    triggerComplete() {
      this.$emit('complete');
    },
    open(reference, index) {
      this.v$.$reset();

      this.reference = reference;
      this.index = index;

      this.opened = true;
    },
    handleSave() {
      this.v$.$touch();
      if (this.v$.$error) {
        NotificationService.negative('Please review highlighted fields.');
        return;
      }

      // if (!this.reference.certificateStatus) {
      //   this.reference.certificateStatus = c.certificateStatus.pendingVerification;
      // }
      // if (!this.reference.certificateTypeId) {
      //   this.reference.certificateTypeId = 1;
      // }
      // if (!this.reference.category) {
      //   this.reference.category = 1;
      // }

      this.$emit('success', {
        reference: this.reference,
        index: this.index,
      });
      this.opened = false;
    },
  },
  computed: {
    storeModuleName() {
      return 'userInfo';
    },
    referenceList() {
      return [
        { label: 'Family where I was an Au Pair', value: 1 },
        { label: 'Other childcare employment', value: 2 },
        { label: 'Non-childcare employment', value: 3 },
        { label: 'Other - please specify', value: 4 },
      ];
    },
    otherReferenceType: {
      get() {
        return this.reference.otherReferenceType;
      },
      set(value) {
        if (this.index === -1) {
          this.reference.otherReferenceType = value;
        } else {
          this.setProperty(`vouches.${this.index}.otherReferenceType`, value);
        }
        this.triggerChange();
      },
    },
    referenceType: {
      get() {
        return this.reference.referenceType;
      },
      set(value) {
        if (this.index === -1) {
          this.reference.referenceType = value;
        } else {
          this.setProperty(`vouches.${this.index}.referenceType`, value);
        }
        this.triggerChange();
      },
    },
    fullName: {
      get() {
        return this.reference.fullName;
      },
      set(value) {
        if (this.index === -1) {
          this.reference.fullName = value;
        } else {
          this.setProperty(`vouches.${this.index}.fullName`, value);
        }
        this.triggerChange();
      },
    },
    phoneNumber: {
      get() {
        return this.reference.phoneNumber;
      },
      set(value) {
        if (this.index === -1) {
          this.reference.phoneNumber = value;
        } else {
          this.setProperty(`vouches.${this.index}.phoneNumber`, value);
        }
        this.triggerChange();
      },
    },
    email: {
      get() {
        return this.reference.email;
      },
      set(value) {
        if (this.index === -1) {
          this.reference.email = value;
        } else {
          this.setProperty(`vouches.${this.index}.email`, value);
        }
        this.triggerChange();
      },
    },
  },
  setup() {
    return {
      v$: useVuelidate(),
    };
  },
  validations() {
    return {
      fullName: {
        required,
      },
      phoneNumber: {
        validate(value) {
          return validateGenericPhoneNumber(value);
        },
      },
      email: {
        required,
        email,
      },
      referenceType: {
        required(value) {
          return value > 0;
        },
      },
      otherReferenceType: {
        requiredIf: requiredIf(this.referenceType === 4),
      },
    };
  },
};
</script>
