import * as types from '../mutation-types';
import * as http from '../../api/http';
import c from '../../utils/constants';
import { env } from '../../utils/env';
import NotificationService from '../../utils/notification-service';
import loader from '../../utils/loader';
import { getTimeDifference, getJuggleAssistFilters } from '../../utils/helpers';

export default {
  namespaced: true,
  state: {
    jobDetails: {},
    jobDetailsTemplate: {
      data: {
        actionType: c.jobActionType.create,
        jobType: 0,
        helperPaymentMethod: 0,
        managedBy: c.managedBy.user,
        jobDate: null,
        jobDateDay: null,
        jobDateMonth: null,
        jobDateYear: null,
        jobStartType: c.jobStartType.duration,
        jobStartDurationType: c.durationType.weeks,
        durationToStart: '',
        jobFinishType: c.jobFinishType.duration,
        durationType: c.durationType.weeks,
        duration: '',
        specialInstructions: '',
        hourlyPrice: '',
        isPriceNegotiable: '',
        applicants: [],
        weeklySchedule: {
          isActive: true,
          scheduleName: 'default',
          weeklyScheduleEntries: [],
        },
        userPaymentType: c.userPaymentType.annualChildcareSubscription180,
        tokenId: undefined,
        addressId: -1,
      },
      meta: {
        dirty: false,
        isProcessing: false,
        currentStep: 1,
        lastStep: 8,
        helpersPageIndex: 1,
        displayFilterNotice: false,
        selectedApplicants: [],
        previousApplicants: [],
        selectedDays: [],
        weeklyScheduleEntries: [],
      },
      currentStep: 1,
      lastStep: 7,
      filterData: {
        searchText: '',
      },
    },
  },
  actions: {
    toggleApplicant({ commit }, helper) {
      commit(types.TOGGLE_APPLICANT, helper);
      // dispatch('setPageTitle');
    },
    resetApplicants({ commit }) {
      commit(types.RESET_APPLICANTS);
    },
    setPageTitle({ commit, state }) {
      const title = state.jobDetails.meta.selectedApplicants.length > 0 ? `${state.jobDetails.meta.selectedApplicants.length} Candidate${state.jobDetails.meta.selectedApplicants.length > 1 ? 's' : ''} Selected` : 'Select Candidates';
      const subtitle = '';

      commit(types.SET_PAGE_INFO, { title, subtitle }, { root: true });
    },
    postRecurringJob({
      commit, state, getters, dispatch, rootState,
    }, router) {
      loader.show();
      commit(types.POST_RECURRING_JOB_REQUEST);
      const { isPaymentRequired } = getters;

      commit(types.SET_DEVICE_TYPE);
      commit(types.POST_JOB_SET_APPLICANT_FILTERS, rootState.filters.filterInfo.postRecurringJob);

      return http
        .post('jobs/client', state.jobDetails.data)
        .then(async (response) => {
          if (isPaymentRequired) {
            const trnData = {
              paymentType: state.jobDetails.data.userPaymentType,
            };
            dispatch('submitTransactionInfo', trnData, { root: true });
          }

          await dispatch('refreshUserInfo', null, { root: true });
          dispatch('resetStoreStructure', { propPath: 'postRecurringJob.jobDetails' }, { root: true });
          dispatch('resetApplicants');
          loader.hide();
          router.replace(`/families/job-details/${response.data.id}`);
        })
        .catch((error) => {
          commit(types.POST_RECURRING_JOB_FAILURE);
          loader.hide();
          NotificationService.negative(error.message);

          return Promise.reject(error);
        });
    },
  },
  mutations: {
    [types.POST_JOB_SET_APPLICANT_FILTERS]: (state, filters) => {
      const processedFilters = getJuggleAssistFilters(filters);
      state.jobDetails.data.applicantFilters = processedFilters;
    },
    [types.SET_DEVICE_TYPE]: (state) => {
      state.jobDetails.data.deviceType = env.deviceType;
    },
    [types.POST_RECURRING_JOB_REQUEST]: (state) => {
      state.jobDetails.meta.isProcessing = true;

      // Update weekly schedule shifts.
      const entries = [];
      state.jobDetails.meta.weeklyScheduleEntries.forEach((entry) => {
        entry.shifts.forEach((shift) => {
          // const updatedShift = Object.assign({}, shift);
          const updatedShift = { ...shift };
          updatedShift.dayOfWeek = entry.dayOfWeek;
          updatedShift.isRequired = true;
          entries.push(updatedShift);
        });
      });
      state.jobDetails.data.weeklySchedule.weeklyScheduleEntries = entries;
    },
    [types.POST_RECURRING_JOB_SUCCESS]: (state) => {
      state.jobDetails.meta.isProcessing = false;
    },
    [types.POST_RECURRING_JOB_FAILURE]: (state) => {
      state.jobDetails.meta.isProcessing = false;
    },
    [types.UPDATE_STEP]: (state, payload) => {
      state.jobDetails.meta.currentStep = payload.toStep;
    },
    [types.TOGGLE_SHIFT]: (state, payload) => {
      const entry = state.jobDetails.meta.weeklyScheduleEntries.find((e) => e.dayOfWeek === payload.dayOfWeek);

      if (payload.shiftIndex !== -1) {
        // Remove shift.
        entry.shifts.splice(payload.shiftIndex, 1);
      } else {
        // Add shift.
        entry.shifts.push(payload.value);
      }
    },
    [types.UPDATE_SHIFT_DATA]: (state, payload) => {
      const entry = state.jobDetails.meta.weeklyScheduleEntries.find((e) => e.dayOfWeek === payload.dayOfWeek);
      const shift = entry.shifts[payload.shiftIndex];
      shift[payload.propName] = payload.value;

      if (payload.propName === 'jobStartTime' || payload.propName === 'jobEndTime') {
        shift.hours = getTimeDifference(shift.jobStartTime, shift.jobEndTime);
      }

      shift.price = shift.hourlyPrice * shift.hours;
    },
    [types.TOGGLE_APPLICANT]: (state, payload) => {
      const helper = payload;
      let helperIndex = -1;

      for (let i = 0; i < state.jobDetails.data.applicants.length; i++) {
        if (state.jobDetails.data.applicants[i].applicantId === helper.id) {
          helperIndex = i;
          break;
        }
      }

      if (helperIndex >= 0) {
        state.jobDetails.data.applicants.splice(helperIndex, 1);
        state.jobDetails.meta.selectedApplicants.splice(helperIndex, 1);
      } else {
        const applicant = { applicantId: helper.id };
        state.jobDetails.data.applicants.push(applicant);
        state.jobDetails.meta.selectedApplicants.push(helper);
      }
    },
    [types.RESET_APPLICANTS]: (state) => {
      state.jobDetails.data.applicants = [];
      state.jobDetails.meta.selectedApplicants = [];
    },
  },
  getters: {
    filters: (state, getters, rootState, rootGetters) => rootGetters['filters/filters'],
    currentStep: (state) => state.jobDetails.meta.currentStep,
    jobDetails: (state) => state.jobDetails.data,
    isPaymentRequired: (state, getters, rootState) => {
      if (state.jobDetails.data.actionType === c.jobActionType.edit) {
        return false;
      }

      const user = rootState.userInfo.data;
      const isPaymentRequired = !user.permissions.canPostUnlimitedRecurringJobs;

      return isPaymentRequired;
    },
  },
};
