import JsMixins from '../utils/js-mixins';
import { initEnv, env } from '../utils/env';
import { queryStringParams } from '../utils/helpers';
import * as auth from '../api/auth';
import * as types from '../store/mutation-types';
import errorHandler from '../utils/error-handler';
import pushNotificationService from '../utils/push-notification-service';
import appUpdateManager from '../utils/app-update-manager';

/* eslint-disable */
Object.resolve = function (obj, path) {
  return path.split('.').reduce(function(prev, curr) {
    return prev ? prev[curr] : undefined;
  }, obj || self);
};
/* eslint-enable */

export default async ({ app, store, router }) => {
  console.log('boot::init');

  const checkAdminMode = async () => {
    const params = queryStringParams();
    if (params.admin_mode && params.admin_mode === '1') {
      store.commit(types.UPDATE_APP_INFO, { adminMode: true });
      await auth.setAccessToken(params.access_token, false);
    }
  };

  await initEnv();
  errorHandler.init(app, router, store);

  await auth.loadTokens();

  JsMixins.configureMixins(app);
  JsMixins.importComponents(app);
  pushNotificationService.init(store, router);
  appUpdateManager.init(store, router);

  checkAdminMode();
  const params = queryStringParams();
  if (params.demoMode) env.demoMode = true;
  if (params.testMode) env.isTest = true;

  env.initialised = true;
};
