<template>
  <q-page class="">
    <div class="layout-padding extend-payg-job">
      <div class="text-small text-center">
        <q-icon name="info" color="secondary" style="margin: 0 2px; font-size: 20px"/>
        <small>Extend the job for a further 15 days or upgrade to a subscription for unlimited Juggle St access.</small>
      </div>
      <q-list link no-border>
        <q-item tag="label">
          <q-item-section side top>
            <q-radio v-model="userPaymentType" val="1005" />
          </q-item-section>
          <q-item-section>
            <q-item-label>Extend this job  $15.00</q-item-label>
            <q-item-label caption>Extend this job for 15 days</q-item-label>
          </q-item-section>
        </q-item>
      </q-list>

      <!-- <div class="card-details" v-if="!hasExistingCard">
        <j-card-details ref="cardDetails" @useExistingCard="handleUseExistingCard"/>
      </div> -->

      <div class="payment-summary">
        <payment-summary :paymentType="userPaymentType"/>
        <small class="row text-center" v-if="isSubscription">* You can turn off your auto-renew in the payment section of your account at anytime.</small>
      </div>

      <q-btn
        big
        color="primary"
        class="full-width fixed-bottom"
        @click="handleExtendJob">
          Submit
      </q-btn>
    </div>
  </q-page>
</template>

<script>
import { mapState } from 'vuex';
import PaymentSummary from 'components/PaymentSummary';
import NotificationService from '../../utils/notification-service';
import c from '../../utils/constants';
import store from '../../store';

export default {
  name: 'PageFamiliesExtendPaygJob',
  components: { PaymentSummary },
  data() {
    return {
      userPaymentType: `${c.userPaymentType.extendRecurringJob}`,
    };
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
      job: (state) => state.families.pages.jobDetails.data,
    }),
    storeModuleName() {
      return 'families.pages.jobDetails';
    },
    isSubscription() {
      return this.userPaymentType !== `${c.userPaymentType.extendRecurringJob}`;
    },
  },
  methods: {
    handleUseExistingCard() {

    },
    handleExtendJob() {
      this.setProperty('userPaymentType', parseInt(this.userPaymentType, 10));
      this.setProperty('deviceType', this.deviceType);
      this.setProperty('extendExpirationDateBy', 15);
      this.$store.dispatch('families/updateJobDetails').then(async () => {
        await this.$store.dispatch('refreshUserInfo', null, { root: true });
        NotificationService.positive('Job extended succesfully.');
        this.routerReplace(`/families/job-details/${this.job.id}`);
      });
    },
    async loadJobDetails(id) {
      this.loader.show();
      await this.$store.dispatch('families/fetchJobDetails', { id });
      this.loader.hide();
    },
  },
  beforeRouteEnter(to, from, next) {
    store.dispatch('resetStoreStructure', { propPath: 'families.pages.jobDetails' });

    next(async (vm) => {
      await vm.loadJobDetails(to.params.id);
    });
  },
  async beforeRouteUpdate(to, from, next) {
    this.$store.dispatch('resetStoreStructure', { propPath: 'families.pages.jobDetails' });
    await this.loadJobDetails(to.params.id);
    next();
  },
};
</script>
<style lang="sass">
.extend-payg-job
  .plan-info
    display: inline-block
    margin: 5px 25px
  .card-details
    margin: 10px 0
  .payment-summary
    margin: 10px
</style>
