import BingManager from './bing-manager';
import FacebookPixelManager from './facebook-pixel-manager';
import AdwordsManager from './adwords-manager';
import AnalyticsService from './analytics-service';
import { env } from './env';
import errorHandler from './error-handler';
import * as http from '../api/http';

const ConversionTrackingService = {
  trackRegistration: (isClient) => {
    if (env.isTest) {
      console.log('Test Mode - Skip Tracking');
      return;
    }

    try {
      AnalyticsService.logConversion(isClient);
      AdwordsManager.reportConversion(isClient);
      FacebookPixelManager.trackCompleteRegistration(isClient);
      BingManager.trackCompleteRegistration(isClient);
    } catch (error) {
      errorHandler.captureException(error);
    }
  },
  trackAppDownload() {
    if (env.isTest) {
      console.log('Test Mode - Skip Tracking');
      return;
    }

    try {
      console.log('Track App Download');
      const req = {
        platform: env.deviceInfo.platform,
        appVersion: env.appInfo.version,
        distVersion: env.versionNumber,
        country: env.country,
      };
      http.post('account/registerappdownload', req)
        .catch((error) => console.log(error));
      AnalyticsService.logAppDownload();
      FacebookPixelManager.trackAppDownload();
      AdwordsManager.reportAppDownload();
    } catch (error) {
      errorHandler.captureException(error);
    }
  },
  logAppEvent(eventType) {
    if (env.isTest) {
      console.log('Test Mode - Skip Tracking');
      return Promise.resolve();
    }

    try {
      console.log('Track App Event');
      const req = {
        eventType,
        platform: env.deviceInfo.platform,
        appVersion: env.appInfo.version,
        distVersion: env.versionNumber,
        country: env.country,
      };
      return http.post('account/logappevent', req)
        .catch((error) => console.log(error));
    } catch (error) {
      errorHandler.captureException(error);
      return Promise.reject();
    }
  },
};

export default ConversionTrackingService;
