<template>
  <q-page class="">
    <q-stepper flat v-model="currentStep" animated contracted class="q-pb-lg">
      <q-step
        title="Let's Get Started!"
        color="primary"
        :name="1"
        :error="step1Error"
        :done="currentStep > 1"
      >
          <step1 :currentStep="currentStep" ref="step1" />
        <q-stepper-navigation>
          <q-btn class="float-right" color="accent" @click="handleNext">Continue</q-btn>
        </q-stepper-navigation>
      </q-step>

      <q-step
        title="Your Details"
        color="primary"
        :name="2"
        :error="step2Error"
        :done="currentStep > 2"
      >
        <step2 :currentStep="currentStep" ref="step2" />
        <q-stepper-navigation>
          <q-btn outline color="secondary" class="q-mr-sm" @click="handlePrevious">Back</q-btn>
          <q-btn class="float-right" color="accent" @click="handleNext">Continue</q-btn>
        </q-stepper-navigation>
      </q-step>

      <q-step
        title="Account Details"
        color="primary"
        :name="3"
        :error="step3Error"
        :done="currentStep > 3"
      >
        <step3 :currentStep="currentStep" ref="step3" />
        <q-stepper-navigation class="q-my-sm" >
          <q-btn outline color="secondary" class="q-mr-sm" @click="handlePrevious">Back</q-btn>
          <q-btn class="float-right" color="accent" @click="handleNext">Continue</q-btn>
        </q-stepper-navigation>
      </q-step>

      <q-step
        title="Your Location"
        color="primary"
        :name="4"
        :error="step4Error"
        :done="currentStep > 4"
      >
        <step4 :currentStep="currentStep" ref="step4" />
        <q-stepper-navigation>
          <q-btn outline color="secondary" class="q-mr-sm" @click="handlePrevious">Back</q-btn>
          <q-btn class="float-right" color="accent" @click="handleSubmit">Submit</q-btn>
        </q-stepper-navigation>
      </q-step>
    </q-stepper>
  </q-page>
</template>

<script>
import Step1 from 'components/signup/Step1';
import Step2 from 'components/signup/Step2';
import Step3 from 'components/signup/Step3';
import Step4 from 'components/signup/Step4';
import { mapState, mapActions } from 'vuex';
import NotificationService from '../utils/notification-service';
import c from '../utils/constants';
import store from '../store';
import { isAuthorized } from '../api/auth';
import errorHandler from '../utils/error-handler';

export default {
  name: 'PageSignup',
  components: {
    Step1, Step2, Step3, Step4,
  },
  data() {
    return {
      step1Error: false,
      step2Error: false,
      step3Error: false,
      step4Error: false,

      currentStep: 1,
      helpWith: [],

      // completeAddress: '',
      // showAddressDetails: false,
      // matchingSuburbs: [],
      // matchingAddresses: [],
    };
  },
  methods: {
    ...mapActions({
      signupUser: 'signupUser',
      prefillSignupForm: 'prefillSignupForm',
    }),
    handlePrevious() {
      this.currentStep--;
    },
    handleNext() {
      if (this.hasErrorInCurrentStep()) {
        return;
      }

      this.currentStep++;
    },
    async handleSubmit() {
      if (this.hasErrorInCurrentStep()) {
        NotificationService.negative('Please fix all highlighted errors.');
        return;
      }

      try {
        const { accountType } = this;
        const response = await this.signupUser();
        if (response.success) {
          if (accountType === c.accountType.client) {
            this.routerReplace('/client-profile-wizard/welcome?success=1');
          } else {
            this.routerReplace('/helper-profile-wizard/welcome?success=1');
          }
        }
      } catch (err) {
        errorHandler.captureException(err);
      }
    },
    hasErrorInCurrentStep() {
      this[`step${this.currentStep}Error`] = this.$refs[`step${this.currentStep}`].validate();
      return this[`step${this.currentStep}Error`];
    },
  },
  async created() {
    await this.prefillSignupForm(this.$router.currentRoute);
  },
  computed: {
    ...mapState({
      accountInfo: (state) => state.signup.userDetails.data,
    }),
    storeModuleName() {
      return 'signup.userDetails';
    },
  },
  watch: {
    helpWith(value) {
      const interestedInJobTypes = value.reduce((acc, item) => {
        acc += item;
        return acc;
      }, 0);
      this.setProperty('interestedInJobTypes', interestedInJobTypes);
    },
  },
  beforeRouteEnter(to, from, next) {
    if (isAuthorized()) {
      next({ path: '/dashboard', replace: true });
      return;
    }

    store.dispatch('resetStoreStructure', { propPath: 'signup.userDetails' });
    next();
  },
};
</script>
<style lang="sass">
.q-stepper__header
  display: none
</style>
