export const getImageUrl = (user) => `${env.storageUrl}/images/${user.imageSrc}`;

export const getTimeObj = (time) => ({
  hour: Number(time.split(':')[0]),
  minute: Number(time.split(':')[1].substring(0, 2)),
  type: time.split(' ')[1],
});

export const formatTimeObj = (timeObj) => {
  if (timeObj.type === 'PM') {
    timeObj.hour += 12;
  }
  return timeObj;
};

export const formatTime = (timeSpan) => {
  const parts = timeSpan.split(':');
  const hours = parseInt(parts[0], 10);
  const minutes = parseInt(parts[1], 10);

  if (hours === 0 && minutes === 0) return 'Midnight';

  return `${hours === 12 ? 12 : hours % 12}:${minutes === 0 ? '00' : minutes} ${hours > 11 ? 'PM' : 'AM'}`;
};

export const formatJobDate = (job) => {
  const dateString = date.formatDate(job.jobDate, 'ddd D MMM YY');
  if (job.jobType === c.jobType.babysitting) {
    return `${dateString}, ${formatTime(job.jobStartTime)} - ${formatTime(job.jobEndTime)}`;
  }
  let jobStartText;
  let jobFinishText;

  const start = new Date() > new Date(job.jobDate) && (job.jobStatus === c.jobStatus.awarded || job.jobStatus === c.jobStatus.completed) ? 'Started' : 'Starting';
  if (new Date() > job.jobDate || job.jobStatus === c.jobStatus.pending) {
    if (job.jobStartType === c.jobStartType.date) {
      jobStartText = `${start} on ${dateString}`;
    } else {
      jobStartText = `${start} within ${job.durationToStart} ${job.jobStartDurationType === c.durationType.weeks ? 'week' : 'month'}${job.durationToStart > 1 ? 's' : ''}`;
    }
  } else if (job.jobStartType === c.jobStartType.date) {
    jobStartText = `${start} on ${dateString}`;
  } else {
    jobStartText = `${start} around ${dateString}`;
  }

  if (job.jobFinishType === c.jobFinishType.date) {
    jobFinishText = ` till ${date.formatDate(job.jobFinishDate, 'ddd D MMM YY')}`;
  } else {
    jobFinishText = `, for ${job.duration} ${job.durationType === c.durationType.weeks ? 'week' : 'month'}${job.duration > 1 ? 's' : ''}`;
  }

  return `${jobStartText + jobFinishText}`;
};

export const isSameDate = (d1, d2) => d1.getFullYear() === d2.getFullYear() && d1.getMonth() === d2.getMonth() && d1.getDate() === d2.getDate();

export const getTimeDifference = (startTime, endTime) => {
  const startTimeObj = formatTimeObj(getTimeObj(startTime));
  const endTimeObj = formatTimeObj(getTimeObj(endTime));

  if (endTimeObj.hour === 0) endTimeObj.hour = 24; // Midnight.
  if (startTimeObj.hour >= 12 && endTimeObj.hour < 12) endTimeObj.hour += 24; // PM to AM event.

  const hoursDiff = endTimeObj.hour - startTimeObj.hour;
  const minutesDiff = (endTimeObj.minute - startTimeObj.minute) / 60;

  return hoursDiff + minutesDiff;
};

export const getHourlyRate = (job) => {
  if (!job.id) throw new Error('This function can only be used after a job has been posted.');

  let hourlyRate;
  if (job.jobType === c.jobType.babysitting) {
    if (job.paymentType === c.jobPaymentType.hourly) {
      hourlyRate = job.price;
    } else {
      const hours = getTimeDifference(job.jobStartTime, job.jobEndTime);
      hourlyRate = job.price / hours;
    }
  } else if (job.jobType === c.jobType.auPair) {
    hourlyRate = 0;
  } else {
    hourlyRate = 1000;
    job.weeklySchedule.weeklyScheduleEntries.forEach((entry) => {
      const hours = getTimeDifference(entry.jobStartTime, entry.jobEndTime);
      const shiftRate = entry.price / hours;
      if (shiftRate < hourlyRate) {
        hourlyRate = shiftRate;
      }
    });
  }

  return hourlyRate;
};

