export const getPricingOptions = (userInfo, jobType) => {
  let subscriptionTypes;
  if (userInfo.client.clientType === c.clientType.individual) {
    subscriptionTypes = individualSubscriptionTypes;
  } else if (userInfo.client.clientCategory === c.clientCategory.businessSingleSite) {
    subscriptionTypes = businessSingleSiteSubscriptionTypes;
  } else {
    subscriptionTypes = businessMultiSiteSubscriptionTypes;
  }

  const list = subscriptionTypes[jobType];

  return list;
};

export const getUpsellOptions = (userInfo, jobType) => {
  let basePrice = 0;
  if (userInfo.membershipType === c.membershipType.subscription) {
    basePrice = getAmountForPaymentType(userInfo.paymentInfo.paymentType);
  }

  const list = getPricingOptions(userInfo, jobType);
  const options = [];
  for (let i = 0; i < list.length; i++) {
    const optionId = list[i];
    const optionPrice = getAmountForPaymentType(optionId);
    if (optionPrice > basePrice) {
      options.push(optionId);
    }

    if (options.length === 3) break;
  }

  return options;
};

export const getMessageDeliveryStatusText = (deliveryStatus) => {
  switch (deliveryStatus) {
    case 0:
      return 'failed';
    case 1:
      return 'sending...';
    case 2:
      return 'unseen';
    case 3:
      return 'delivered';
    case 4:
      return 'seen';
    default:
      throw new Error(`Unsupported option: ${deliveryStatus}`);
  }
};

export const formatNumber = (value) => {
  if (!value) return 0;

  return value.toLocaleString('en-AU');
};

export const formatCurrency = (value) => `$${formatNumber(value)}`;

export const formatDate = (targetDate) => date.formatDate(targetDate, 'ddd D MMM YY');

export const formatDateTime = (targetDate) => date.formatDate(targetDate, 'D MMM YY @ HH:mm a');

export const getScheduledJobDate = (job) => {
  let jobStartText;
  let jobFinishText;

  if (job.jobStartType === c.jobStartType.date) {
    jobStartText = `Starting on ${formatDate(job.jobDate)}`;
  } else {
    jobStartText = `Starting within ${job.durationToStart} ${job.jobStartDurationType === c.durationType.weeks ? 'week' : 'month'}${job.durationToStart > 1 ? 's' : ''}`;
  }

  if (job.jobFinishType === c.jobFinishType.date) {
    jobFinishText = ` till ${formatDate(job.jobFinishDate)}`;
  } else {
    jobFinishText = `, for ${job.duration} ${job.durationType === c.durationType.weeks ? 'week' : 'month'}${job.duration > 1 ? 's' : ''}`;
  }

  return `${jobStartText + jobFinishText}`;
};

export const queryStringParams = () => {
  const params = {};
  const queryString = window.location.search.substring(1);
  if (!queryString) {
    return params;
  }

  const queries = queryString.split('&');

  for (let i = 0; i < queries.length; i++) {
    const query = queries[i].split('=');
    const key = query[0];
    const value = query[1];
    params[key] = value;
  }

  return params;
};

export const parseQueryStringParams = (url) => {
  const params = {};
  const queryIdx = url.indexOf('?');
  if (queryIdx === -1) return params;

  const queryString = url.substring(queryIdx + 1);
  if (!queryString) {
    return params;
  }

  const queries = queryString.split('&');

  for (let i = 0; i < queries.length; i++) {
    const query = queries[i].split('=');
    const key = query[0];
    const value = query[1];
    params[key] = value;
  }

  return params;
};

export const getLastSeenNoteFromNumOfDays = (numOfDays) => {
  if (numOfDays === 0) return 'Active today';

  if (numOfDays === 1) return 'Active yesterday';

  if (numOfDays <= 28) return `Active ${numOfDays} days ago`;

  if (numOfDays > 28 && numOfDays <= 56) {
    return 'Active last month';
  }

  return 'Active 2 mths ago';
};

export const waitForRender = () => new Promise((resolve) => {
  requestAnimationFrame(() => {
    requestAnimationFrame(resolve);
  });
});

export const delay = (value) => new Promise((resolve) => {
  setTimeout(() => {
    resolve();
  }, value);
});

export const validatePhoneNumberLoose = (value) => {
  let valid = value.startsWith('04') || value.startsWith('+614') || value.startsWith('614');
  if (valid) {
    const phoneRegex = /^\+?[\d\s]{10,15}$/;
    valid = phoneRegex.test(value);
  }

  return valid;
};

export const getJobTypes = (userInfo) => {
  const { interestedInJobTypes } = userInfo;
  const jobTypes = c.childcareJobTypes.reduce((acc, item) => {
    if (item.value & interestedInJobTypes) {
      acc.push(item.value);
    }
    return acc;
  }, []);

  return jobTypes;
};

export const registerJobType = (interestedInJobTypes, jobType) => {
  interestedInJobTypes |= jobType;

  return interestedInJobTypes;
};

export const unregisterJobType = (interestedInJobTypes, jobType) => {
  interestedInJobTypes &= (~jobType);

  return interestedInJobTypes;
};

export const getFormattedAddress = (address) => {
  if (!address || !address.addressLine1 || !address.suburb) return '';

  let formattedAddress = address.addressLine1;
  if (address.addressLine2 && address.addressLine2.length > 0) {
    formattedAddress += `, ${address.addressLine2}`;
  }

  formattedAddress += `, ${address.suburb}`;

  return formattedAddress;
};

export const getFormattedAddressWithCountry = (address) => {
  const baseAddress = getFormattedAddress(address);

  return `${baseAddress} (${address.country.toUpperCase()})`;
};

export const toggleHomeTutoring = (userInfo) => {
  let { interestedInJobTypes } = userInfo;

  const {
    canTeachStudySkills,
    canTeachExamPreparation,
    year12GraduationYear,
    myExperience3,
    primarySchoolSubjects,
    highSchoolSubjects,
  } = userInfo.provider;

  const hasSelectedPrimarySchoolYears = userInfo.provider.primarySchoolYears.filter((option) => option.selected).length > 0;
  const selectedPrimarySchoolSubjects = getSelectedOptions(primarySchoolSubjects);

  const hasSelectedHighSchoolYears = userInfo.provider.highSchoolYears.filter((option) => option.selected).length > 0;
  const selectedHighSchoolSubjects = getSelectedOptions(highSchoolSubjects);

  const validPrimarySchool = hasSelectedPrimarySchoolYears
    && selectedPrimarySchoolSubjects.length > 0
    && myExperience3 && myExperience3.length >= 100;

  const validHighSchool = hasSelectedHighSchoolYears
    && selectedHighSchoolSubjects.length > 0
    && myExperience3 && myExperience3.length >= 100
    && typeof canTeachStudySkills === 'boolean'
    && typeof canTeachExamPreparation === 'boolean'
    && year12GraduationYear !== null;

  if (validPrimarySchool) {
    interestedInJobTypes |= c.jobType.primarySchoolTutoring;
  } else {
    interestedInJobTypes &= (~c.jobType.primarySchoolTutoring);
  }

  if (validHighSchool) {
    interestedInJobTypes |= c.jobType.highSchoolTutoring;
  } else {
    interestedInJobTypes &= (~c.jobType.highSchoolTutoring);
  }

  return interestedInJobTypes;
};

const PHONE_REG_EXP = /^[+]?\d{8,14}/;
const AU_PHONE_REG_EXP = /^04\d{8}/;
const NZ_PHONE_REG_EXP = /^02\d{7,9}/;
// const AU_PHONE_REG_EXP_2 = /^[+]?614\d{8}/;
// const NZ_PHONE_REG_EXP_2 = /^[+]?642\d{7,9}/;

export const validateGenericPhoneNumber = (v) => {
  v = v.replace(/\s/gi, '');

  if (!PHONE_REG_EXP.test(v)) {
    return false;
  }

  return v.length <= 15;
};

export const validatePhoneNumber = (v, country) => {
  if (!country) country = env.country;

  v = v.replace(/\s/gi, '');
  if (/\D/.test(v)) {
    return false;
  }

  if (country === 'au' && !AU_PHONE_REG_EXP.test(v)) {
    return false;
  }

  if (country === 'nz' && !NZ_PHONE_REG_EXP.test(v)) {
    return false;
  }

  return v.length <= 12;
};

export const getHelperProfileDefaultTab = (filterKey) => {
  let query = 'defaultTab=';
  switch (filterKey) {
    case 'babysittersNearMe':
    case 'nanniesNearMe':
      query += 'childcare';
      break;
    case 'auPairsNearMe':
      query += 'auPair';
      break;
    case 'tutorsNearMe':
    case 'onlineTutors':
      query += 'tutor';
      break;
    default:
      query += 'profile';
      break;
  }

  return query;
};
