<template>
  <div>
    <q-tabs v-model="selectedTab" class="section-groups text-white shadow-2">
      <q-tab v-for="(action, index) in actions" :class="action.className" :key="index" @click="handleClick(action.url)" :alert="action.count > 0 ? 'light-red' : false" :icon="action.icon" :label="action.label" :name="action.name">
        <!-- <q-badge v-if="action.count > 0" color="red" floating>
          {{action.count}}
        </q-badge> -->
      </q-tab>
    </q-tabs>
  </div>
</template>

<script>
import { mapState } from 'vuex';

export default {
  name: 'DashboardActions',
  data() {
    return {
    };
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
      realtime: (state) => state.realtime,
    }),
    actions() {
      const { isClient } = this.userInfo;
      const { alerts } = this.realtime;
      let actions = [];

      if (isClient) {
        if (this.canAccessChildcareJobs) {
          actions.push({
            url: '/dashboard',
            label: 'Childcare',
            icon: 'child_care',
            // count: alerts.invitationAlerts,
            count: 0,
            className: 'col-4',
            name: 'dashboard',
          });
        }

        if (this.canAccessTutoringJobs) {
          actions.push({
            url: '/dashboard-tutors',
            label: 'Tutors',
            icon: 'school',
            count: 0,
            className: 'col-4',
            name: 'online-tutors',
          });
        }

        const { jobsPlaced } = this.userInfo.client;
        if (jobsPlaced > 0) {
          actions.push({
            url: '/families/all-jobs',
            url2: '/conversations',
            label: 'Jobs & Chat',
            icon: 'playlist_add_check',
            count: alerts.jobAlerts + alerts.chatAlerts,
            className: 'col-4',
            name: 'allJobs',
          });
        } else {
          actions.push({
            url: '/post-job?reset=1',
            label: 'Post a Job',
            icon: 'add_circle',
            count: 0,
            className: 'col-4',
            name: 'post-job',
          });
        }

        if (actions.length < 3) {
          actions.push({
            url: '/conversations',
            label: 'Chat',
            icon: 'chat_bubble',
            count: alerts.chatAlerts,
            className: 'col-4',
            name: 'chat',
          });
        }

        // if (jobsPlaced > 0) {
        //   actions.push({
        //     url: '/conversations',
        //     label: 'Chat',
        //     icon: 'chat bubble',
        //     count: alerts.chatAlerts,
        //     className: 'col-4',
        //     name: 'chat',
        //   });
        // } else {
        //   actions.push({
        //     url: '/onboarding',
        //     label: 'Get Started',
        //     icon: 'touch app',
        //     count: 0,
        //     className: 'col-4 getting-started-tab',
        //     name: 'getStarted',
        //   });
        // }
      } else {
        actions = [
          {
            url: '/dashboard',
            label: 'Near Me',
            icon: 'near_me',
            // count: alerts.invitationAlerts,
            count: 0,
            className: 'col-4',
            name: 'dashboard',
          },
          {
            url: '/helpers/upcoming-jobs',
            label: 'Jobs',
            icon: 'playlist_add_check',
            count: alerts.jobAlerts,
            className: 'col-4',
            name: 'upcomingJobs',
          },
          {
            url: '/conversations',
            label: 'Chat',
            icon: 'chat_bubble',
            count: alerts.chatAlerts,
            className: 'col-4',
            name: 'chat',
          },
        ];
      }

      return actions;
    },
    selectedTab: {
      get() {
        let action = this.actions.find((a) => a.url === this.$route.path);
        if (!action) {
          action = this.actions.find((a) => a.url2 && a.url2 === this.$route.path);
        }

        if (!action) return null;

        return action.name;
      },
      set() {

      },
    },
  },
  props: [],
  methods: {
    handleClick(url) {
      if ((url.startsWith('/post-job') || url.startsWith('/conversations')) && this.isPending) {
        this.$q.dialog({
          title: 'Complete Profile',
          message: 'Your account must be 100% complete before proceeding.',
          ok: {
            label: 'Ok',
            outline: true,
          },
        });
        return;
      }

      this.routerPush(url);
    },
  },
};
</script>
