<template>
  <q-field
    :error-message="errorMessage"
    :error="error"
    :label="label"
    borderless
    hide-bottom-space
    readonly
  >
    <template v-slot:prepend v-if="icon">
      <q-icon :name="icon" />
    </template>
    <template v-slot:control>
      <div class="full-width">
        <q-select
          class="inline hours"
          v-model="hourSelected"
          option-value="value"
          option-label="label"
          emit-value
          map-options
          :options="hours"
        />
        <q-select
          class="inline minutes"
          v-model="minuteSelected"
          option-value="value"
          option-label="label"
          emit-value
          map-options
          :options="minutes"
        />
        <q-select
          class="inline types"
          v-model="typeSelected"
          option-value="value"
          option-label="label"
          emit-value
          map-options
          :options="types"
        />
      </div>
    </template>
  </q-field>
</template>

<script>
export default {
  name: 'TimePicker',
  props: ['defaultValue', 'change', 'error', 'error-message', 'label', 'icon'],
  data() {
    const defaultTime = this.defaultValue.split(':');
    let defaultHour;
    let defaultType;
    defaultHour = parseInt(defaultTime[0], 10);
    if (defaultHour > 12) {
      defaultHour -= 12;
      defaultType = 'PM';
    } else if (defaultHour === 12) {
      defaultType = 'PM';
    } else {
      defaultType = 'AM';
    }
    const defaultMinute = parseInt(defaultTime[1], 10);

    return {
      hours: [
        { label: '00', value: 0 },
        { label: '1', value: 1 },
        { label: '2', value: 2 },
        { label: '3', value: 3 },
        { label: '4', value: 4 },
        { label: '5', value: 5 },
        { label: '6', value: 6 },
        { label: '7', value: 7 },
        { label: '8', value: 8 },
        { label: '9', value: 9 },
        { label: '10', value: 10 },
        { label: '11', value: 11 },
        { label: '12', value: 12 },
      ],
      minutes: [
        { label: '00', value: 0 },
        { label: '15', value: 15 },
        { label: '30', value: 30 },
        { label: '45', value: 45 },
      ],
      types: [
        { label: 'AM', value: 'AM' },
        { label: 'PM', value: 'PM' },
      ],
      hourSelected: defaultHour,
      minuteSelected: defaultMinute,
      typeSelected: defaultType,
    };
  },
  computed: {
  },
  watch: {
    hourSelected() {
      this.handleChange();
    },
    minuteSelected() {
      this.handleChange();
    },
    typeSelected() {
      this.handleChange();
    },
  },
  methods: {
    handleChange() {
      let hour = this.typeSelected === 'PM' && this.hourSelected < 12 ? this.hourSelected + 12 : this.hourSelected;
      if (this.typeSelected === 'AM' && this.hourSelected === 12) hour = 0;
      const val = `${hour}:${this.minuteSelected}`;
      this.change(val);
    },
  },
};
</script>
<style lang="sass">
.time-picker
  .hours, .minutes
    width: 50px
    margin-right: 10px
</style>
