<template>
  <div class="month-year-picker">
    <q-field
      :error-message="errorLabel"
      :error="error"
      borderless
      class="no-padding no-margin"
    >
      <template v-slot:control>
        <q-select
          :error="error"
          class="inline months"
          v-model="monthSelected"
          :options="months"
          option-value="value"
          option-label="label"
          emit-value
          map-options
        />
        <q-select
          :error="error"
          class="inline years"
          v-model="yearSelected"
          :options="years"
          option-value="value"
          option-label="label"
          emit-value
          map-options
        />
      </template>
    </q-field>
  </div>
</template>

<script>

export default {
  name: 'MonthYearPicker',
  data() {
    const yearsCount = 20;

    return {
      defaultDay: 15,
      months: [
        { label: 'Jan', value: 0 },
        { label: 'Feb', value: 1 },
        { label: 'Mar', value: 2 },
        { label: 'Apr', value: 3 },
        { label: 'May', value: 4 },
        { label: 'Jun', value: 5 },
        { label: 'Jul', value: 6 },
        { label: 'Aug', value: 7 },
        { label: 'Sep', value: 8 },
        { label: 'Oct', value: 9 },
        { label: 'Nov', value: 10 },
        { label: 'Dec', value: 11 },
      ],
      years: new Array(yearsCount).fill().reduce((acc) => {
        if (acc.length === 0) {
          const currentYear = new Date().getFullYear();
          acc.push({ value: currentYear, label: currentYear.toString() });
        } else {
          const year = acc[acc.length - 1].value;
          acc.push({ value: year - 1, label: (year - 1).toString() });
        }

        return acc;
      }, []),
    };
  },
  computed: {
    yearSelected: {
      get() {
        return new Date(this.value).getFullYear();
      },
      set(year) {
        const value = new Date(year, this.monthSelected, this.defaultDay);
        this.change(value);
      },
    },
    monthSelected: {
      get() {
        return new Date(this.value).getMonth();
      },
      set(month) {
        const value = new Date(this.yearSelected, month, this.defaultDay);
        this.change(value);
      },
    },
  },
  props: ['value', 'change', 'error', 'error-label'],
  methods: {
    handleChange() {
      const val = new Date(this.yearSelected, this.monthSelected, this.defaultDay);
      this.change(val);
    },
  },
};
</script>
<style lang="sass">
.month-year-picker
  .months, .years
    width: 75px
    margin-right: 10px
</style>
