<template>
  <q-page class="">
    <div>
      <p style="margin: 10px" v-if="meta.failed">
        This job is no longer available.
        <br/>
        <q-btn color="accent" @click="routerReplace('/helpers/upcoming-jobs')">Back to My Jobs</q-btn>
      </p>
      <div v-if="job.id && job.id">
        <div style="margin: 10px">
          <div v-if="job.applicationStatus === types.jobApplicationStatus.pending || job.applicationStatus === types.jobApplicationStatus.viewed">
            <small style="padding: 5px; border-radius: 5px;" class="bg-light-red text-white">response required</small>
          </div>
          <div v-if="job.applicationStatus === types.jobApplicationStatus.applied || job.applicationStatus === types.jobApplicationStatus.shortlistedBySystem || job.applicationStatus === types.jobApplicationStatus.excludedByClient">
            <small style="padding: 5px; border-radius: 5px;" class="bg-green text-white">pending response from parent</small>
          </div>
        </div>
        <q-card flat v-if="job.jobType === types.jobType.babysitting">
          <q-card-section>
            <div class="text-bold">
              <span>{{jobTypeText}}</span>
              <div class="float-right text-grey">
                <q-icon :name="helperPaymentMethodIcon" size="18px" /> {{helperPaymentMethodText}}
              </div>
            </div>
            <div>
              <router-link class="text-black" :to="'/account/client-profile/' + job.jobOwnerId">
                <img class="round-user-image" :src="job.ownerImageSrc"/> {{job.ownerPublicName}}
                <span v-if="isPending">has invited you to a job.</span>
              </router-link>
              <!-- <q-btn style="margin-left: 25px" color="accent" v-if="canChat" @click="handleChat">Chat</q-btn> -->
            </div>
            <span>{{jobDate}}</span>
            <div v-if="job.ownerPhoneNumber">
              Phone Number: <a :href="phoneNumberLink">{{job.ownerPhoneNumber}}</a>
            </div>
            <div>
              Location: {{jobLocation}}
            </div>
            <div>
              Job Description:
              <br/><br/>
              <p style="margin: 0 5px" v-for="(text, index) in jobSpecialInstructions.split('\n')" :key="index">
                {{text}}&nbsp;
              </p>
              <br/>
            </div>
            <div v-if="job.awardedApplicantGettingHome && job.jobStatus == types.jobStatus.awarded">
              Getting Home: <span> {{gettingHomeText}}</span>
            </div>
            <div>
              Price: <span>{{formatCurrency(job.price)}}</span> <span>{{getPaymentTypeText}}</span>
              <!-- <br/> Will Pay Overtime? <span>{{willPayOvertimeText}}</span> -->
            </div>
            <div v-if="job.willPayOvertime">
              {{getOvertimeText}}
              <!-- Overtime Payment Type: <span>{{overtimePaymentType}}</span>
              <br/>
              Overtime Rate: <span>{{formatCurrency(job.overtimeRate)}}</span> -->
            </div>
            <div v-if="isBusinessJob">
              <small v-if="country === 'au'">Excludes Superannuation and other statutory entitlements. </small>
              <small v-if="country === 'nz'">Excludes any government statutory entitlements.</small>
            </div>
            <div v-if="job.applicationStatus === types.jobApplicationStatus.cancelled">
              This job has been cancelled.
            </div>
            <div v-if="job.applicationStatus === types.jobApplicationStatus.withdrawn">
              Your application has been withdrawn.
            </div>
            <div v-if="job.applicationStatus === types.jobApplicationStatus.notAvailable">
              You have declined this job offer.
            </div>
            <div v-if="isPendingResponse">
              <span class="text-green">Pending Response from Parent</span>
            </div>
          </q-card-section>
          <q-separator />
          <q-card-actions>
            <q-btn color="accent" v-if="canApply" @click="handleApply">Apply</q-btn>
            <q-btn color="secondary" outline v-if="canApply" @click="handleNotAvailable">Decline</q-btn>
            <q-btn color="secondary" outline v-if="canCancel" @click="handleCancelJob">Cancel Job</q-btn>
            <q-btn color="accent" v-if="canRate" @click="handleRateParent">Rate Parent</q-btn>
            <q-btn color="secondary" outline v-if="isPendingResponse" @click="handleWithdraw">Withdraw Application</q-btn>
            <q-btn color="secondary" v-if="canChat" @click="handleChat">Chat</q-btn>
          </q-card-actions>
        </q-card>
        <q-card flat v-else-if="job.jobType === types.jobType.auPair">
          <q-card-section>
            <div class="text-bold">
              <span>{{jobTypeText}}</span>
              <div class="float-right text-grey">
                <q-icon :name="helperPaymentMethodIcon" size="18px" /> {{helperPaymentMethodText}}
              </div>
            </div>
            <div>
              <router-link class="text-black" :to="'/account/client-profile/' + job.jobOwnerId">
                <img class="round-user-image" :src="job.ownerImageSrc"/> {{job.ownerPublicName}}
                <span v-if="isPending">has invited you to a job.</span>
              </router-link>
            </div>
            <span>{{jobDate}}</span>
            <div v-if="job.ownerPhoneNumber">
              Phone Number: <a :href="phoneNumberLink">{{job.ownerPhoneNumber}}</a>
            </div>
            <div>
              Location: {{auPairJobLocation}}
            </div>
            <div>
              Job Description:
              <br/><br/>
              <p style="margin: 0 5px" v-for="(text, index) in jobSpecialInstructions.split('\n')" :key="index">
                {{text}}&nbsp;
              </p>
              <br/>
            </div>
            <div>
              Price: <span>{{formatCurrency(job.price)}}</span> <span>{{getPaymentTypeText}}</span>
            </div>
            <div class="q-my-sm">
              Working week: {{job.jobSettings.hoursPerWeek}} hours p/week
              <br/>
              Working days per week: {{job.jobSettings.daysPerWeek}} days
            </div>
            <div class="q-my-sm">
              Food & Accomodation
              <div class="q-mx-sm">
                Meals provided: {{mealOptions}}
                <br/>
                Bedroom: {{bedroomOption}}
                <br/>
                Facilities: {{facilityOptions}}
                <br/>
                Bathroom: {{bathroomOption}}
              </div>
            </div>
            <div v-if="job.applicationStatus === types.jobApplicationStatus.cancelled">
              This job has been cancelled.
            </div>
            <div v-if="job.applicationStatus === types.jobApplicationStatus.withdrawn">
              Your application has been withdrawn.
            </div>
            <div v-if="job.applicationStatus === types.jobApplicationStatus.notAvailable">
              You have declined this job offer.
            </div>
            <div v-if="isPendingResponse">
              <span class="text-green">Pending Response from Parent</span>
            </div>
          </q-card-section>
          <q-separator />
          <q-card-actions>
            <q-btn color="accent" v-if="canApply" @click="handleApplyForAuPairJob">Apply</q-btn>
            <q-btn color="secondary" outline v-if="canApply" @click="handleNotAvailable">Decline</q-btn>
            <q-btn color="secondary" outline v-if="canCancel" @click="handleCancelJob">Cancel Job</q-btn>
            <q-btn color="accent" v-if="canRate" @click="handleRateParent">Rate Parent</q-btn>
            <q-btn color="secondary" outline v-if="isPendingResponse" @click="handleWithdraw">Withdraw Application</q-btn>
            <q-btn color="secondary" v-if="canChat" @click="handleChat">Chat</q-btn>
          </q-card-actions>
        </q-card>
        <q-card flat v-else-if="job.id && job.id > 0">
          <q-card-section>
            <div class="text-bold">
              <span>{{jobTypeText}}</span>
              <div class="float-right text-grey">
                <q-icon :name="helperPaymentMethodIcon" size="18px" /> {{helperPaymentMethodText}}
              </div>
            </div>
            <div>
              <router-link class="text-black" :to="'/account/client-profile/' + job.jobOwnerId">
                <img class="round-user-image" :src="job.ownerImageSrc"/> {{job.ownerPublicName}}
                <span v-if="isPending">has invited you to a job.</span>
              </router-link>
              <!-- <q-btn style="margin-left: 25px" color="secondary" v-if="canChat" @click="handleChat">Chat</q-btn> -->
            </div>
            <span>{{scheduledJobDate}}</span>
            <div>
              Required Days: {{getDaysOfTheWeek}}
            </div>
            <div v-if="job.jobType === types.jobType.primarySchoolTutoring || job.jobType === types.jobType.highSchoolTutoring">
              <div v-if="getJobDeliveryMethodText">
                Tutoring Type: {{getJobDeliveryMethodText}}
              </div>
            </div>
            <div v-if="job.jobType === types.jobType.primarySchoolTutoring || job.jobType === types.jobType.highSchoolTutoring">
              <div v-if="getSchoolYears">
                Required School Year(s): {{getSchoolYears}}
              </div>
            </div>
            <div v-if="job.jobType === types.jobType.primarySchoolTutoring || job.jobType === types.jobType.highSchoolTutoring">
              <div v-if="getSchoolSubjects">
                Required Subject: {{getSchoolSubjects}}
              </div>
            </div>
            <div>
              {{recurringJobPaymentText}}
            </div>
            <div>
              <span v-if="isBusinessJob" class="q-mr-xs">*</span>
              Total Weekly Payment: {{formatCurrency(job.pricePerWeek)}}
            </div>
            <div v-if="job.ownerPhoneNumber">
              Phone Number: <a :href="phoneNumberLink">{{job.ownerPhoneNumber}}</a>
            </div>
            <div>
              Location: {{jobLocation}}
            </div>
            <div>
              Job Description:
              <br/>
              <p style="margin: 0 5px" v-for="(text, index) in jobSpecialInstructions.split('\n')" :key="index">
                {{text}}&nbsp;
              </p>
            </div>
            <q-field v-if="canApply" label="Apply for one or more days below." readonly borderless>
              <template v-slot:control>
                <div class="full-width q-mt-sm">
                  <q-option-group
                    color="primary"
                    type="checkbox"
                    v-model="availabilitySelection"
                    :options="availabilityList"
                  />
                </div>
              </template>
            </q-field>

            <q-field label="Your Application" v-if="isPendingResponse" borderless readonly>
              <template v-slot:control>
                <div class="full-width q-mt-md">
                  <table class="horizontal-separator">
                    <tbody>
                      <tr v-for="shift in availabilityList" :key="shift.id">
                        <td><q-icon :name="shift.icon"/></td>
                        <td>{{shift.label}}</td>
                      </tr>
                    </tbody>
                  </table>
                  <!-- <q-field
                    borderless
                    readonly
                    hide-bottom-space
                    v-for="shift in availabilityList" :key="shift.id">
                    <template v-slot:prepend>
                      <q-icon :name="shift.icon" />
                    </template>
                    <template v-slot:control>
                      <span>{{shift.label}}</span>
                    </template>
                  </q-field> -->
                  <!-- <p
                    v-for="shift in availabilityList" :key="shift.id"
                    class="no-margin">
                    {{shift.label}}
                  </p> -->
                </div>
              </template>
            </q-field>

          <q-card-actions>
            <q-btn color="accent" :disable="availabilitySelection.length === 0" v-if="canApply" @click="handleApplyForRecurringJob">Apply</q-btn>
            <q-btn color="secondary" outline :disable="availabilitySelection.length > 0" v-if="canApply" @click="handleApplyForRecurringJob">Decline</q-btn>
            <q-btn color="secondary" outline v-if="canCancel" @click="handleCancelJob">Cancel Job</q-btn>
            <q-btn color="accent" v-if="canRate" @click="handleRateParent">Rate Parent</q-btn>
            <q-btn color="secondary" outline v-if="isPendingResponse" @click="handleWithdraw">Withdraw Application</q-btn>
            <q-btn color="secondary" v-if="canChat" @click="handleChat">Chat</q-btn>
          </q-card-actions>
          <div v-if="isBusinessJob">
            <small class="q-mr-xs">*</small>
            <small v-if="country === 'au'">Excludes Superannuation and other statutory entitlements. </small>
            <small v-if="country === 'nz'">Excludes any government statutory entitlements.</small>
          </div>
        </q-card-section>
        </q-card>
      </div>
      <covid-19-modal ref='covidPopup' v-on:covidNoticeCompleted="processCovidNoticeCompletion"/>
    </div>
  </q-page>
</template>

<script>
import { mapState } from 'vuex';
import Covid19Modal from 'components/Covid19Modal';
import c from '../../utils/constants';
import {
  getJobDeliveryMethodText, getJobPaymentMethodIcon, getJobPaymentMethodText, getSelectedOptions, getGettingHomeTextForHelpers, formatJobDate, formatCurrency, getScheduledJobDate, getDayOfWeekText, formatTime, jobTypeText, getOptionTextValues, getOption, formatNumber,
} from '../../utils/helpers';
import store from '../../store';
import NotificationService from '../../utils/notification-service';
import errorHandler from '../../utils/error-handler';

export default {
  name: 'PageHelpersJobDetails',
  components: { Covid19Modal },
  data() {
    return {
      acceptedCovidStatement: true,
      confirmedSubscriptionNotice: false,
      availabilitySelection: [],
      types: {
        jobType: c.jobType,
        jobStatus: c.jobStatus,
        jobApplicationStatus: c.jobApplicationStatus,
        clientType: c.clientType,
      },
    };
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
      job: (state) => state.helpers.pages.jobDetails.data,
      meta: (state) => state.helpers.pages.jobDetails.meta,
    }),
    storeModuleName() {
      return 'helpers.pages.jobDetails';
    },
    availabilityList() {
      const requiredDays = this.job.applicantAvailability.filter((item) => item.isRequired);

      const options = requiredDays.map((requiredDay, index) => {
        const label = `${getDayOfWeekText(requiredDay.dayOfWeek)}, ${formatTime(requiredDay.jobStartTime)} - ${formatTime(requiredDay.jobEndTime)} for ${formatCurrency(requiredDay.price)}`;
        const icon = requiredDay.availabilityStatus === c.applicantAvailabilityStatus.available ? 'check' : 'close';

        return {
          label, value: index, status: requiredDay.availabilityStatus, icon,
        };
      });

      return options;
    },
    jobTypeText() {
      return jobTypeText(this.job.jobType, this.clientType);
    },
    jobSpecialInstructions() {
      if (!this.job || !this.job.specialInstructions) return '';

      return this.job.specialInstructions;
    },
    getDaysOfTheWeek() {
      const days = [];
      const { applicantAvailability } = this.job;

      applicantAvailability.forEach((item) => {
        if (item.isRequired) {
          days.push(getDayOfWeekText(item.dayOfWeek));
        }
      });

      const uniqueDays = days.filter((value, index, self) => self.indexOf(value) === index);

      return uniqueDays.join(', ');
    },
    getJobDeliveryMethodText() {
      return getJobDeliveryMethodText(this.job.jobDeliveryMethod);
    },
    getSchoolYears() {
      if (!this.job.jobSettings.schoolYears || this.job.jobSettings.schoolYears.length === 0) {
        return '';
      }

      const schoolYears = this.job.jobType === c.jobType.primarySchoolTutoring ? this.userInfo.provider.primarySchoolYears : this.userInfo.provider.highSchoolYears;
      let text = '';
      this.job.jobSettings.schoolYears.forEach((schoolYear) => {
        const option = getOption(schoolYear, schoolYears);
        text += `${option.text}/`;
      });

      return text.substring(0, text.length - 1);
    },
    getSchoolSubjects() {
      if (!this.job.jobSettings.schoolSubjects || this.job.jobSettings.schoolSubjects.length === 0) {
        return 'All';
      }

      const schoolSubjects = this.job.jobType === c.jobType.primarySchoolTutoring ? this.userInfo.provider.primarySchoolSubjects : this.userInfo.provider.highSchoolSubjects;
      const option = getOption(this.job.jobSettings.schoolSubjects[0], schoolSubjects);

      return option.text;
    },
    helperPaymentMethodIcon() {
      return getJobPaymentMethodIcon(this.job.helperPaymentMethod);
    },
    helperPaymentMethodText() {
      return getJobPaymentMethodText(this.job.helperPaymentMethod);
    },
    jobDate() {
      return formatJobDate(this.job);
    },
    scheduledJobDate() {
      return getScheduledJobDate(this.job);
    },
    phoneNumberLink() {
      return `tel:${this.job.ownerPhoneNumber}`;
    },
    gettingHomeText() {
      return getGettingHomeTextForHelpers(this.job.awardedApplicantGettingHome);
    },
    jobLocation() {
      return this.job.formattedAddress ? this.job.formattedAddress : this.job.suburb;
    },
    auPairJobLocation() {
      if (this.job.formattedAddress) return this.job.formattedAddress;

      if (this.job.state) return `${this.job.suburb}, ${this.job.state}`;

      return this.job.suburb;
    },
    getPaymentTypeText() {
      const negotiable = this.job.isPriceNegotiable ? ' (Negotiable)' : ' (Not negotiable)';
      return this.job.jobType === c.jobType.auPair ? `p/week${negotiable}` : this.job.paymentType === 1 ? 'total' : 'p/h';
    },
    recurringJobPaymentText() {
      const { price } = this.job;
      const isNegotiable = this.job.isPriceNegotiable;

      if (price === 0) {
        return '';
      }

      if (!price) {
        return `Payment: ${isNegotiable ? 'Negotiable' : 'Not negotiable'}`;
      }

      return `Payment: ${`$${price} p/h`} (${isNegotiable ? 'Negotiable' : 'Not negotiable'})`;
    },
    willPayOvertimeText() {
      return this.job.willPayOvertime ? 'Yes' : 'No';
    },
    overtimePaymentType() {
      return this.job.overtimePaymentType === 1 ? 'Fixed' : 'Hourly';
    },
    getOvertimeText() {
      const { overtimeRate } = this.job;
      return `Extra hours rate: $${formatNumber(overtimeRate)} per hour`;
    },
    canChat() {
      return true;
    },
    canApply() {
      return this.job.jobStatus === c.jobStatus.pending && (this.job.applicationStatus === c.jobApplicationStatus.pending || this.job.applicationStatus === c.jobApplicationStatus.viewed);
    },
    canCancel() {
      return this.job.jobStatus === c.jobStatus.awarded;
    },
    canRate() {
      return !this.job.isRatedByProvider && this.job.jobStatus === c.jobStatus.completed && (this.job.awardedApplicantId === this.userInfo.id);
    },
    isPending() {
      return this.job.jobStatus === c.jobStatus.pending;
    },
    isPendingResponse() {
      return this.job.jobStatus === c.jobStatus.pending && (this.job.applicationStatus === c.jobApplicationStatus.applied || this.job.applicationStatus === c.jobApplicationStatus.shortlistedByClient || this.job.applicationStatus === c.jobApplicationStatus.shortlistedBySystem);
    },
    mealOptions() {
      return getOptionTextValues(this.job.jobSettings.mealOptions, c.allMeals);
    },
    bedroomOption() {
      return getOptionTextValues(this.job.jobSettings.bedroomOption, c.bedroomOptions);
    },
    facilityOptions() {
      return getOptionTextValues(this.job.jobSettings.facilityOptions, c.facilities);
    },
    bathroomOption() {
      return getOptionTextValues(this.job.jobSettings.bathroomOption, c.bathroomOptions);
    },
    isBusinessJob() {
      return this.job.ownerClientType === c.clientType.business;
    },
  },
  methods: {
    formatCurrency(value) {
      return formatCurrency(value);
    },
    validPaymentDetails() {
      if (this.job.helperPaymentMethod === c.helperPaymentMethod.bankTransfer) {
        const { interestedInBankTransfers, bankAccountVerificationStatus } = this.userInfo.paymentInfo;
        if (!interestedInBankTransfers || bankAccountVerificationStatus !== c.bankAccountVerificationStatus.approved) {
          this.$q.dialog({
            title: 'Missing Bank Details',
            message: 'Update your profile to receive payments by bank transfer.',
            cancel: {
              label: 'Cancel',
              color: 'secondary',
              outline: true,
            },
            ok: {
              label: 'Update',
              preventClose: true,
              outline: true,
            },
          }).onOk(() => {
            this.routerReplace('/account/payment-methods');
          }).onCancel(() => {});

          return false;
        }
      }

      return true;
    },
    validCheck() {
      // const invalidCheck = (this.userInfo.certificates.length === 0 || this.userInfo.certificates[0].certificateStatus === c.certificateStatus.invalid || this.userInfo.certificates[0].certificateStatus === c.certificateStatus.expired)
      //   && this.userInfo.country === 'au' && this.userInfo.ageInYears >= 18;
      const invalidCheck = (this.userInfo.certificates.length === 0 || this.userInfo.certificates[0].certificateStatus !== c.certificateStatus.active)
        && this.userInfo.country === 'au' && this.userInfo.ageInYears >= 18;
      if (invalidCheck) {
        this.$q.dialog({
          title: 'WWCC Alert!',
          message: 'A valid Working with Children Check (WWCC) is mandatry on Juggle Street. Go to the Certificates section of My Account and upload WWCC.',
          ok: {
            label: 'Upload',
            outline: true,
          },
          cancel: {
            label: 'Later',
            color: 'secondary',
            outline: true,
          },
        }).onOk(() => {
          this.routerPush('/account/my-certificates');
        }).onCancel(() => {});

        return false;
      }

      return true;
    },
    validSubscription() {
      if (this.userInfo.membershipType === c.membershipType.free && this.userInfo.ageInYears >= 18) {
        this.$q.dialog({
          title: 'Membership Required',
          message: 'Activate your membership to apply for this job.',
          cancel: {
            label: 'Later',
            color: 'secondary',
            outline: true,
          },
          ok: {
            label: 'Subscribe',
            preventClose: true,
            outline: true,
          },
        }).onOk(() => {
          this.routerReplace(`/account/helper-membership?redirect=${this.$route.fullPath}`);
        }).onCancel(() => {});

        return false;
      }

      return true;
    },
    handleApply() {
      if (!this.validCheck()) {
        return;
      }

      if (!this.validSubscription()) {
        return;
      }

      if (!this.validPaymentDetails()) {
        return;
      }

      if (!this.acceptedCovidStatement) {
        this.$refs.covidPopup.showDialog(this.handleApply);
        return;
      }

      if (this.isBusinessJob) {
        this.applyForJob(c.gettingHome.unspecified);
        return;
      }

      this.$q.dialog({
        title: 'Getting Home',
        message: 'How are you getting home after this job?',
        options: {
          type: 'radio',
          model: '',
          isValid: (val) => val !== '',
          items: [
            { label: 'I need taking home', value: '1', color: 'secondary' },
            { label: 'Walking', value: '2' },
            { label: 'Public transport', value: '3' },
            { label: 'Getting picked up', value: '4' },
            { label: 'Driving myself', value: '5' },
            { label: 'Uber/Taxi', value: '6' },
          ],
        },
        cancel: {
          label: 'Cancel',
          color: 'secondary',
          outline: true,
        },
        ok: {
          label: 'Apply',
          preventClose: true,
          outline: true,
        },
      }).onOk((data) => {
        if (!data) {
          NotificationService.info('Please select an option');
          return;
        }

        this.applyForJob(parseInt(data, 10));
      }).onCancel(() => {});
    },
    applyForJob(gettingHome) {
      this.setProperty('applicationStatus', c.jobApplicationStatus.applied);
      this.setProperty('gettingHome', gettingHome);
      this.setProperty('awardedApplicantGettingHome', gettingHome);

      this.$store.dispatch('helpers/updateJobDetails').catch(this.handleJobUpdateError);
    },
    handleApplyForAuPairJob() {
      if (!this.validCheck()) {
        return;
      }

      if (!this.validSubscription()) {
        return;
      }

      if (!this.validPaymentDetails()) {
        return;
      }

      if (!this.acceptedCovidStatement) {
        this.$refs.covidPopup.showDialog(this.handleApplyForAuPairJob);
        return;
      }

      this.$q.dialog({
        title: 'Confirm',
        message: 'Are you sure you would like to apply for this Au Pair job?',
        cancel: {
          label: 'Cancel',
          color: 'secondary',
          outline: true,
        },
        ok: {
          label: 'Apply',
          preventClose: true,
          outline: true,
        },
      }).onOk(() => {
        this.applyForJob(0);
      }).onCancel(() => {});
    },
    handleNotAvailable() {
      this.$q.dialog({
        title: 'Tell Us Why',
        message: 'Please tell us the main reason you declined this job.',
        options: {
          type: 'radio',
          model: '',
          isValid: (val) => val !== '',
          items: [
            { label: 'Hourly rate too low', value: '3' },
            { label: 'Job too short', value: '6' },
            { label: 'Calendar clash', value: '1' },
            // { label: 'Not Enough Notice', value: '2' },
            { label: 'Too far away', value: '5' },
            { label: 'Other', value: '4' },
          ],
        },
        cancel: {
          label: 'Cancel',
          color: 'secondary',
          flat: true,
        },
        ok: {
          label: 'Submit',
          preventClose: true,
          flat: true,
        },
      }).onOk((data) => {
        if (!data) {
          NotificationService.info('Please select an option');
          return;
        }

        this.submitUnavailable(parseInt(data, 10));
      }).onCancel(() => {});
    },
    async submitUnavailable(reason) {
      this.setProperty('applicationStatus', c.jobApplicationStatus.notAvailable);
      this.setProperty('unavailableReason', reason);

      await this.$store.dispatch('helpers/updateJobDetails').catch(this.handleJobUpdateError);
      this.routerReplace('/helpers/upcoming-jobs');
    },
    handleChat() {
      const url = `/chat/with/${this.job.jobOwnerId}`;
      this.routerPush(url);
    },
    handleWithdraw() {
      this.$q.dialog({
        title: 'Confirmation',
        message: 'You are about to withdraw your job application and you will not be able to re-apply for this job. The parent will be notified of your withdrawal, but this will not affect your job cancellation counter. Click OK to continue.',
        cancel: {
          label: 'Cancel',
          color: 'secondary',
          outline: true,
        },
        ok: {
          label: 'Ok',
          outline: true,
        },
      }).onOk(() => {
        this.withdrawApplication();
      }).onCancel(() => {});
    },
    withdrawApplication() {
      this.setProperty('applicationStatus', c.jobApplicationStatus.withdrawn);
      // Workaround an API issue.
      const requiredDays = this.job.applicantAvailability.filter((item) => item.isRequired);
      this.setProperty('applicantAvailability', requiredDays);

      this.$store.dispatch('helpers/updateJobDetails').catch(this.handleJobUpdateError);
      this.routerReplace('/helpers/upcoming-jobs');
    },
    handleCancelJob() {
      this.$q.dialog({
        title: 'Confirm Cancellation',
        message: 'Cancelling a job will be shown on your Jobs Cancelled section of your profile, are you sure you want to continue?',
        cancel: {
          label: 'Don`t Cancel',
          color: 'secondary',
        },
        ok: {
          label: 'Cancel Job',
        },
      }).onOk(() => {
        this.cancelJob();
      }).onCancel(() => {});
    },
    cancelJob() {
      this.setProperty('jobStatus', c.jobStatus.cancelled);
      this.setProperty('applicationStatus', c.jobApplicationStatus.cancelled);

      this.$store.dispatch('helpers/updateJobDetails').catch(this.handleJobUpdateError);
    },
    handleRateParent() {
      this.routerPush(`/helpers/rate-family/${this.job.id}`);
    },
    processCovidNoticeCompletion(args) {
      this.acceptedCovidStatement = true;

      if (args.callback) {
        args.callback();
      }
    },
    handleApplyForRecurringJob() {
      if (this.availabilitySelection.length === 0) {
        this.$q.dialog({
          title: 'Tell Us Why',
          message: 'Please tell us the main reason you declined this job.',
          options: {
            type: 'radio',
            model: '',
            isValid: (val) => val !== '',
            items: [
              { label: 'Hourly rate too low', value: '3' },
              { label: 'Shifts too short', value: '6' },
              { label: 'Calendar clash', value: '1' },
              // { label: 'Not Enough Notice', value: '2' },
              { label: 'Too far away', value: '5' },
              { label: 'Other', value: '4' },
            ],
          },
          cancel: {
            label: 'Cancel',
            color: 'secondary',
            flat: true,
          },
          ok: {
            label: 'Submit',
            preventClose: true,
            flat: true,
          },
        }).onOk((data) => {
          if (!data) {
            NotificationService.info('Please select an option');
            return;
          }

          this.applyForRecurringJob(parseInt(data, 10));
        }).onCancel(() => {});
      } else {
        if (!this.validCheck()) {
          return;
        }

        if (!this.validSubscription()) {
          return;
        }

        if (!this.validPaymentDetails()) {
          return;
        }

        if (this.job.jobType === c.jobType.highSchoolTutoring && this.job.jobSettings.schoolSubjects.length > 0) {
          const schoolSubjects = this.userInfo.provider.highSchoolSubjects;
          const selectedHighSchoolSubjects = getSelectedOptions(schoolSubjects);
          const selectedSubject = selectedHighSchoolSubjects.indexOf(this.job.jobSettings.schoolSubjects[0]) !== -1;
          if (!selectedSubject) {
            this.$q.dialog({
              title: `${this.getSchoolSubjects}`,
              message: `You need to teach ${this.getSchoolSubjects} to apply for this job - update all of your tutor subjects now.`,
              cancel: {
                label: 'Cancel',
                color: 'secondary',
                outline: true,
              },
              ok: {
                label: 'Update',
                preventClose: true,
                outline: true,
              },
            }).onOk(() => {
              this.routerReplace('/account/tutoring-preferences');
            }).onCancel(() => {});
            return;
          }
        }

        if (!this.acceptedCovidStatement) {
          this.$refs.covidPopup.showDialog(this.handleApplyForRecurringJob);
          return;
        }

        this.applyForRecurringJob();
      }
    },
    async handleJobUpdateError(error) {
      NotificationService.negative(error.message);
      // Refresh job details.
      await this.loadJobDetails(this.job.id);
    },
    async applyForRecurringJob(unavailableReason) {
      const requiredDays = this.job.applicantAvailability.filter((item) => item.isRequired);
      const applicantAvailability = [];
      requiredDays.forEach((requiredDay, index) => {
        // const entry = Object.assign({}, requiredDay);
        const entry = { ...requiredDay };
        entry.availabilityStatus = this.availabilitySelection.indexOf(index) !== -1
          ? c.applicantAvailabilityStatus.available : c.applicantAvailabilityStatus.notAvailable;
        applicantAvailability.push(entry);
      });

      this.setProperty('applicationStatus', c.jobApplicationStatus.applied);
      this.setProperty('applicantAvailability', applicantAvailability);
      if (unavailableReason) {
        this.setProperty('unavailableReason', unavailableReason);
      }

      await this.$store.dispatch('helpers/updateJobDetails').catch(this.handleJobUpdateError);

      if (unavailableReason) {
        this.routerReplace('/helpers/upcoming-jobs');
      }
    },
    async loadJobDetails(id) {
      this.loader.show();
      await this.$store.dispatch('helpers/fetchJobDetails', { id }).catch(errorHandler.captureException);
      this.loader.hide();
    },
  },
  beforeRouteEnter(to, from, next) {
    store.dispatch('resetStoreStructure', { propPath: 'helpers.pages.jobDetails' });
    next(async (vm) => {
      await vm.loadJobDetails(to.params.id);
    });
  },
  async beforeRouteUpdate(to, from, next) {
    this.$store.dispatch('resetStoreStructure', { propPath: 'helpers.pages.jobDetails' });
    await this.loadJobDetails(to.params.id);
    // TODO: availabilitySelection doesn't reset in route update events.
    next();
  },
};
</script>
