<template>
  <q-field label="VEVO - Right to Work Check for Visa holders" v-if="!isLocalNationality && helper.providerVisaCheckStatus === 2" borderless readonly>
    <template v-slot:prepend>
      <q-icon name="check_circle_outline" />
    </template>
    <template v-slot:control>
      <div class="full-width q-mt-md">
        <span style="font-weight: bold" class="text-primary"><q-icon name="check" color="primary" size="18px" style="margin-right: 5px"  />Verified</span><br/>
        <span>Verified On: {{getDate(helper.providerVisaCheckDate)}}</span><br/>
        <span>Visa class / subclass: {{helper.providerVisaClass + ' / ' + helper.providerVisaSubclass}}</span><br/>
        <span>Visa grant date: {{getDate(helper.providerVisaGrantDate)}}</span><br/>
        <span>Visa expiry date: {{getDate(helper.providerVisaExpiry)}}</span><br/>
      </div>
    </template>
  </q-field>
</template>

<script>
import { date } from 'quasar';

export default {
  name: 'ProfileVevoChecks',
  props: ['helper'],
  data() {
    return {

    };
  },
  computed: {
    isLocalNationality() {
      const { nationality } = this.helper;
      return nationality === 'aus' || nationality === 'nzl'
        || nationality === 'AU' || nationality === 'NZ';
    },
  },
  methods: {
    getDate(value) {
      return date.formatDate(new Date(value), 'DD/MM/YYYY');
    },
  },
};
</script>
