<template>
  <div class="layout-padding">
    <payment-methods ref="paymentMethods" v-on:change="handleChange" v-on:complete="handleComplete" />
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex';
import PaymentMethods from 'components/PaymentMethods';
import beforeLeaveStep from '../before-leave-step';
import * as types from '../../../store/mutation-types';

export default {
  name: 'ProfileWizardPaymentMethods',
  components: { PaymentMethods },
  data() {
    return {};
  },
  methods: {
    ...mapActions([
      'updateUserInfo',
    ]),
    handleChange() {
      this.$store.commit(`profileWizard/${types.UPDATE_STEP_STATUS}`, { canSkip: false });
    },
    handleComplete() {
      this.$store.commit(`profileWizard/${types.UPDATE_STEP_STATUS}`, { complete: true });
    },
    validate() {
      if (this.currentStep.meta.canSkip) {
        return true;
      }

      return this.$refs.paymentMethods.validate(true);
    },
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
      currentStep: (state) => {
        const { meta } = state.profileWizard;
        const currentStep = meta.steps[meta.currentStep];
        return currentStep;
      },
      stepComplete: (state) => {
        const { meta } = state.profileWizard;
        const currentStep = meta.steps[meta.currentStep];
        return currentStep.complete;
      },
    }),
    storeModuleName() {
      return 'userInfo';
    },
  },
  mounted() {
    if (!this.userInfo.paymentInfo.interestedInBankTransfers) {
      this.handleComplete();
    }
  },
  beforeRouteLeave(to, from, next) {
    // Update user info before proceeding to the next step if step is complete
    if (this.stepComplete) {
      this.updateUserInfo().then(() => {
        beforeLeaveStep(this, to, from, next);
      });
    } else {
      beforeLeaveStep(this, to, from, next);
    }
  },
};
</script>
