<template>
  <div class="helper-reviews">
    <q-field label="Reference Checks" v-if="vouches.length > 0" borderless readonly>
      <template v-slot:prepend>
        <q-icon name="group" />
      </template>
      <template v-slot:control>
        <div class="full-width q-my-sm">
          <q-field
            borderless readonly hide-bottom-space
            v-for="review in vouches" :key="review.id"
            class="q-my-sm helper-review">
            <template v-slot:control>
              <div class="full-width">
                {{review.whatWouldYouLikeToSay}}
              </div>
            </template>
            <template v-slot:hint>
              <div class="full-width">
                {{getReviewFromText(review)}}
              </div>
            </template>
          </q-field>
        </div>
      </template>
    </q-field>
  </div>
</template>

<script>
import { date } from 'quasar';
import c from '../../utils/constants';

export default {
  name: 'HelperReferences',
  props: ['helper'],
  data() {
    return {
    };
  },
  computed: {
    vouches() {
      const checks = [];
      this.helper.vouches.forEach((v) => {
        if (v.referenceType !== c.vouchReferenceType.juggleStreet) {
          checks.push(v);
        }
      });

      return checks;
    },
  },
  methods: {
    getReviewFromText(review) {
      return `From ${review.publicName} - ${date.formatDate((review.submissionDate), 'DD/MM/YYYY')}`;
    },
  },
};
</script>
