<template>
  <q-card
    class="helper-unrated-job-summary">
    <q-card-section>
      <div>
        <img class="round-user-image" :src="job.ownerImageSrc"/> {{job.ownerPublicName}}
        <q-btn class="float-right" color="accent" @click="handleRateFamily" size="sm">{{rateButtonText}}</q-btn>
      </div>
      <div>
        <span>{{jobTypeText}}</span>
        <br/>
        <span>{{jobDate}}</span>
      </div>
    </q-card-section>
  </q-card>
</template>

<script>
import c from '../utils/constants';
import { getGettingHomeTextForHelpers, formatJobDate } from '../utils/helpers';

export default {
  name: 'HelperUnratedJobSummary',
  data() {
    return {
      types: {
        jobType: c.jobType,
        jobStatus: c.jobStatus,
        jobApplicationStatus: c.jobApplicationStatus,
      },
    };
  },
  props: ['job'],
  methods: {
    handleRateFamily() {
      this.routerPush(`/helpers/rate-family/${this.job.id}`);
    },
  },
  computed: {
    jobTypeText() {
      switch (this.job.jobType) {
        case c.jobType.babysitting:
          return 'Babysitting Job';
        case c.jobType.nannying:
          return 'Daytime Care';
        case c.jobType.beforeSchoolCare:
          return 'Before School Care';
        case c.jobType.afterSchoolCare:
          return 'After School Care';
        case c.jobType.beforeAndAfterSchoolCare:
          return 'Before & After School Care';
        case c.jobType.auPair:
          return 'Au Pair Job';
        case c.jobType.primarySchoolTutoring:
          return 'Primary School Tutoring';
        case c.jobType.highSchoolTutoring:
          return this.country === 'au' ? 'High School Tutoring' : 'Secondary School Tutoring';
        default:
          return 'Job';
      }
    },
    jobRating() {
      return Math.round(this.job.ratingProvided);
    },
    gettingHomeText() {
      return getGettingHomeTextForHelpers(this.job.awardedApplicantGettingHome);
    },
    jobDate() {
      return formatJobDate(this.job);
    },
    rateButtonText() {
      return this.job.ownerClientType === c.clientType.individual ? 'Rate Parent' : 'Rate Company';
    },
  },
};
</script>
<style lang="sass">
.helper-unrated-job-summary
  margin: 0.5rem 0
  border: 1px solid white
  line-height: 1.3rem
  .q-card-actions
    padding: 0 8px
</style>
