<template>
  <div class="">
    <div class="group">
      <q-field
        label="Select the jobs you would like to apply for"
        :error="v$.tutoringJobCategories.$error"
        borderless
        stack-label
      >
        <template v-slot:prepend>
          <q-icon name="flag" />
        </template>
        <template v-slot:control>
          <q-checkbox v-model="primarySchoolTutoring"  label="Primary School" />
          <br/>
          <q-checkbox v-model="highSchoolTutoring" :label="country === 'au' ? 'High School' : 'Secondary School'" />
        </template>
      </q-field>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import useVuelidate from '@vuelidate/core';
import { sameAs } from '@vuelidate/validators';
import c from '../utils/constants';
import * as mutationTypes from '../store/mutation-types';
import NotificationService from '../utils/notification-service';

export default {
  name: 'HelperTutoringJobs',
  emits: ['change'],
  data() {
    return {

    };
  },
  props: ['isMandatory'],
  setup() {
    return {
      v$: useVuelidate(),
    };
  },
  validations: {
    tutoringJobCategories: {
      sameAsRawValue: sameAs(true),
    },
  },
  methods: {
    isValid() {
      if (!this.isMandatory) {
        return true;
      }

      return this.primarySchoolTutoring || this.highSchoolTutoring;
    },
    validate(notify) {
      if (!this.isMandatory) {
        return true;
      }

      if (!notify) {
        return this.isValid();
      }

      this.v$.$touch();
      if (this.v$.$error) {
        NotificationService.negative('Please review highlighted fields.');
      }

      return !this.v$.$error;
    },
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
      userMeta: (state) => state.userInfo.meta,
    }),
    storeModuleName() {
      return 'userInfo';
    },
    tutoringJobCategories() {
      return this.isMandatory ? this.primarySchoolTutoring || this.highSchoolTutoring : true;
    },
    primarySchoolTutoring: {
      get() {
        return (this.userInfo.interestedInJobTypes & c.jobType.primarySchoolTutoring) === c.jobType.primarySchoolTutoring;
      },
      set(value) {
        // Remove primary school selections.
        if (!value) {
          this.selectedPrimarySchoolSubjects = [];
          const payload = {
            propPath: 'provider.primarySchoolSubjects',
            value: [],
          };
          this.$store.commit(mutationTypes.UPDATE_USER_OPTIONS, payload);

          payload.propPath = 'provider.primarySchoolYears';
          this.$store.commit(mutationTypes.UPDATE_USER_OPTIONS, payload);
        }

        let { interestedInJobTypes } = this.userInfo;
        if (value) {
          interestedInJobTypes |= c.jobType.primarySchoolTutoring;
        } else {
          interestedInJobTypes &= (~c.jobType.primarySchoolTutoring);
        }

        if (this.userInfo.interestedInJobTypes !== interestedInJobTypes) {
          this.setProperty('interestedInJobTypes', interestedInJobTypes);
        }

        this.$emit('change', { valid: value || this.highSchoolTutoring });
      },
    },
    highSchoolTutoring: {
      get() {
        return (this.userInfo.interestedInJobTypes & c.jobType.highSchoolTutoring) === c.jobType.highSchoolTutoring;
      },
      set(value) {
        if (!value) {
          this.selectedHighSchoolSubjects = [];
          const payload = {
            propPath: 'provider.highSchoolSubjects',
            value: [],
          };
          this.$store.commit(mutationTypes.UPDATE_USER_OPTIONS, payload);

          payload.propPath = 'provider.highSchoolYears';
          this.$store.commit(mutationTypes.UPDATE_USER_OPTIONS, payload);
        }

        let { interestedInJobTypes } = this.userInfo;
        if (value) {
          interestedInJobTypes |= c.jobType.highSchoolTutoring;
        } else {
          interestedInJobTypes &= (~c.jobType.highSchoolTutoring);
        }

        if (this.userInfo.interestedInJobTypes !== interestedInJobTypes) {
          this.setProperty('interestedInJobTypes', interestedInJobTypes);
        }

        this.$emit('change', { valid: value || this.primarySchoolTutoring });
      },
    },
  },
};
</script>
