<template>
  <q-page class="layout-padding payments-page">
    <div v-if="!paymentInfo.requiresPayment">
      <p class="text-center">Juggle Street is currently free in your area</p>
      <p class="text-center">
        Enjoy posting as many jobs as you like!
      </p>
    </div>
    <div v-else-if="membershipType === membershipTypes.subscription">
      <q-card flat>
        <q-card-section>
          <!-- <div v-if="isHelper">
            <div class="text-center text-h6 text-bold text-primary q-mb-sm">{{helperPaymentFrequency}}</div>
            <div class="pricing-section">
              <div class="text-h4 q-pr-sm">{{helperMembershipPrice}}</div>
              <div class="">
                <span class="text-bold">per month</span>
                <br/>
                <span v-if="hasBillingNote" class="text-grey">billed annually</span>
              </div>
            </div>
            <div>
              Parents pay you directly at the end of each job, either in cash or via bank transfer.
              Juggle Street does NOT take a cut, you keep 100% of your earnings.
              Other job platforms typically take 15-30% of every dollar you earn.
              <q-separator inset class="q-my-sm" />
            </div>
          </div> -->
          <div class="text-subtitle2 q-mb-sm">
            Current Subscription
          </div>
          <div>
            <subscription-text />
            <span v-if="!paymentInfo.autoRenew" class="bold">
              Your membership will expire on {{ activeUntil }}.
              <br/><br/>
              <!-- <q-btn color="secondary" @click="setAutoRenew(true)" v-if="hasExistingCard">
                Enable Auto Renew
              </q-btn> -->
              <a @click="setAutoRenew(true)" class="button-link text-primary" v-if="hasExistingCard">Enable Auto Renew</a>
            </span>
            <span v-if="paymentInfo.autoRenew" class="bold">
              Your credit card will be billed on {{ activeUntil }}.
              <br/> <br/>
              <!-- <q-btn color="secondary" @click="confirmAutoRenew(false)" flat>
                Cancel Subscription
              </q-btn> -->
              <a v-if="canUpgrade" @click="upgradeSubscription" class="button-link text-primary q-mr-lg">Upgrade to Annual</a>
              <a @click="confirmAutoRenew(false)" class="button-link text-secondary">Cancel Subscription</a>
            </span>
          </div>
        </q-card-section>
      </q-card>
      <q-separator inset />
    </div>
    <div class="q-my-sm" v-if="hasExistingCard">
      <update-credit-card-modal ref="updateCreditCardDetailsModal" v-on:updateCreditCardSuccess="handleUpdateCreditCardSuccess" />
      <q-card flat>
        <q-card-section>
          <div class="text-subtitle2 q-mb-sm">
            Payment Details
          </div>
          <div>
            Credit card on file
            <br/>
            XXXX XXXX XXXX {{existingCardText}}
            <br/><br/>
            <a @click="handleUpdateCreditCard" class="button-link text-secondary">Update Credit Card</a>
          </div>
        </q-card-section>
      </q-card>
      <q-separator inset />
    </div>

    <div class="q-mb-xl" v-if="paymentInfo.requiresPayment && invoices && invoices.length > 0">
      <q-card flat>
        <q-card-section>
          <div class="text-subtitle2 q-mb-sm">Invoices</div>
          <!-- <span>Recent invoices may take few hours to show up.</span> -->
          <table class="q-table horizontal-separator invoices-table">
            <thead>
              <tr>
                <th class="text-center">Date</th>
                <th class="text-center">Description</th>
                <th class="text-center">Total</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="invoice in invoices" :key="invoice.id">
                <td class="text-center">{{ formatDate(invoice.date) }}</td>
                <td class="text-center" @click="displayFailureMessage(invoice)">
                  <!-- <q-icon v-if="invoice.paid" name="done" color="primary" size="xs"/> -->
                  <q-icon v-if="!invoice.paid" name="error" size="xs" color="red"/>
                  {{ invoice.description }}
                </td>
                <td class="text-center">{{ formatCurrency(invoice.amountDue) }}</td>
              </tr>
            </tbody>
          </table>
        </q-card-section>
      </q-card>
    </div>
    <div class="q-my-sm" v-if="meta.lastFetched !== null && invoices.length === 0 && !hasExistingCard && isFamily">
      No payments have been made yet, check our pricing page by clicking <router-link :to="'/account/pricing'">here</router-link>
    </div>
    <div class="q-my-sm" v-if="meta.lastFetched !== null && invoices.length === 0 && !hasExistingCard && isHelper">
      No payments have been made yet, check our membership page by clicking <router-link :to="'/account/helper-membership'">here</router-link>
    </div>
  </q-page>
</template>

<script>
import {
  mapGetters,
  mapState,
} from 'vuex';
import { date } from 'quasar';
import SubscriptionText from 'components/SubscriptionText';
import UpdateCreditCardModal from 'components/UpdateCreditCardModal';
import {
  formatCurrency,
} from '../../utils/helpers';
import NotificationService from '../../utils/notification-service';
import c from '../../utils/constants';

export default {
  name: 'PageAccountPayments',
  components: { UpdateCreditCardModal, SubscriptionText },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
      paymentInfo: (state) => state.userInfo.data.paymentInfo,
      membershipType: (state) => state.userInfo.data.membershipType,
      walletBalance: (state) => state.userInfo.data.walletBalance,
      activeUntil: (state) => date.formatDate(state.userInfo.data.activeUntil, 'DD/MM/YYYY'),
      invoices: (state) => state.payments.data.invoices,
      meta: (state) => state.payments.meta.invoices,
    }),
    ...mapGetters(
      'payments',
      ['hasExistingCard', 'existingCardText', 'userPaymentTypes', 'membershipTypes'],
    ),
    canUpgrade() {
      switch (this.paymentInfo.paymentType) {
        case c.userPaymentType.monthlyHelperStandardSubscription5:
        case c.userPaymentType.quarterlyHelperStandardSubscription15:
          return true;
        case c.userPaymentType.quarterlyBusinessTier1Subscription500:
        case c.userPaymentType.quarterlyBusinessTier2Subscription500:
          return true;
        default:
          return false;
      }
    },
    helperPaymentFrequency() {
      // return `Helper ${this.paymentInfo.paymentType === c.userPaymentType.monthlyHelperStandardSubscription5 ? 'Monthly' : 'Annual'} Membership`;
      return `Helper ${this.paymentInfo.paymentType === c.userPaymentType.quarterlyHelperStandardSubscription15 ? 'Quarterly' : 'Annual'} Membership`;
    },
    helperMembershipPrice() {
      let monthlyPrice;
      switch (this.paymentInfo.paymentType) {
        case c.userPaymentType.annualHelperStandardSubscription24:
          monthlyPrice = 2;
          break;
        case c.userPaymentType.annualHelperStandardSubscription30:
          monthlyPrice = 2.5;
          break;
        default:
          monthlyPrice = 5;
          break;
      }

      return `$${monthlyPrice}`;
    },
    paymentFrequency() {
      return 'per month';
    },
    hasBillingNote() {
      return this.paymentInfo.paymentType === c.userPaymentType.annualHelperStandardSubscription24 || this.paymentInfo.paymentType === c.userPaymentType.annualHelperStandardSubscription30;
    },
  },
  data() {
    return {

    };
  },
  methods: {
    upgradeSubscription() {
      if (this.isClient) {
        const upgradeType = this.paymentInfo.paymentType === c.userPaymentType.quarterlyBusinessTier1Subscription500
          ? c.userPaymentType.annualBusinessTier1Subscription1500
          : c.userPaymentType.annualBusinessTier2Subscription1500;
        this.routerReplace(`/account/modal/upgrade-client-account/${upgradeType}`);
      } else {
        const upgradeType = c.userPaymentType.annualHelperStandardSubscription30;
        this.routerReplace(`/account/modal/upgrade-provider-account/${upgradeType}`);
      }
    },
    handleUpdateCreditCard() {
      this.$refs.updateCreditCardDetailsModal.open();
    },
    displayFailureMessage(invoice) {
      if (!invoice.failureMessage) return;

      NotificationService.negative(invoice.failureMessage);
    },
    handleUpdateCreditCardSuccess() {

    },
    formatDate(invoiceDate) {
      return date.formatDate(invoiceDate, 'DD/MM/YY');
    },
    formatCurrency(amount) {
      return formatCurrency(amount);
    },
    confirmAutoRenew(value) {
      let message;
      let confirmButton;
      let cancelButton;
      let title;

      if (value) {
        title = 'Confirm';
        message = 'Are you sure you want to enable your subscription?';
        confirmButton = 'Yes';
        cancelButton = 'No';
      } else {
        title = 'Are you sure?';
        // message = `Your current subscription will be cancelled on ${this.activeUntil}. Please note that you will only be covered by our Public Liability Insurance for the duration of the membership, would you like to proceed?`;

        if (this.isFamily) {
          message = `Your subscription will be cancelled on ${this.activeUntil}. Please note that you will NOT be covered by Juggle Street’s Public Liability Insurance once the subscription has been cancelled, would you like to proceed?`;
        } else {
          message = `Your subscription will be cancelled on ${this.activeUntil}. Would you like to proceed?`;
        }

        confirmButton = 'Yes';
        cancelButton = 'No';
      }

      const autoRenewAction = this.setAutoRenew;
      this.$q.dialog({
        title,
        message,
        ok: {
          label: confirmButton,
          outline: true,
        },
        cancel: {
          label: cancelButton,
          color: 'secondary',
          outline: true,
        },
      }).onOk(() => {
        autoRenewAction(value);
      }).onCancel(() => {});
    },
    setAutoRenew(value) {
      this.loader.show();
      this.$store.dispatch('payments/setAutoRenew', value)
        .then(() => {
          this.$store.dispatch('refreshUserInfo')
            .then(() => {
              this.loader.hide();
            })
            .catch((error) => {
              this.loader.hide();
              NotificationService.negative(error.message);
            });
        })
        .catch((error) => {
          this.loader.hide();
          NotificationService.negative(error.message || 'Failed to update your payment information.');
        });
    },
  },
  created() {
    this.$store.dispatch('payments/fetchInvoices');
  },
  beforeRouteEnter(to, from, next) {
    next((vm) => {
      if (vm.$route.query.updatePaymentInfo) {
        vm.$refs.updateCreditCardDetailsModal.open();
      }
    });
  },
};
</script>
<style lang="sass">
.payments-page
  .pricing-section
    display: flex
    justify-content: center
    align-items: center
  .benefits-list
    list-style: none
    margin: 0 0 0 20px
    padding: 0
    i
      font-size: 20px
    li
      margin: 2px 0
  .pricing-section
    margin: 10px 0
  .pricing-card-container
    display: flex
    flex-wrap: wrap
    justify-content: center
    align-items: flex-start
  .pricing-card
    margin: 10px
    width: 95%
  .pricing-card-title
    padding: 0
  .pricing-card-main
    line-height: 1.5
    padding: 10px
  .pricing-card-insurance-logo
    width: 40px
    vertical-align: middle
  .pricing-card-pricing-point
    font-size: 125%
  .pricing-plan-description
    padding: 5px 0
    border-bottom: 1px solid $grey-4
    >span
      float: right
      margin-right: 50px
</style>
