<template>
  <div class="one-off-job-details" v-if="job.id && job.id > 0">
    <div class="bg-grey-2 row justify-center items-center" style="padding: 5px 0;" v-if="job.jobStatus === types.jobStatus.pending">
      <span style="margin-right: 5px;">Job post will expire in <strong class="text-red">{{job.expiresInDays}}</strong> days</span>
    </div>
    <q-card flat>
      <q-card-section>
        <q-field
          borderless
          readonly
          hide-bottom-space
        >
          <template v-slot:prepend>
            <q-icon name="date_range" />
          </template>
          <template v-slot:control>
            <div class="full-width q-mt-md">
              <div>
                <span>{{jobTypeText}}</span>
                <br/>
                <span>{{jobDate}}</span>
              </div>
              <div>
                <span v-if="job.addressLabel" class="text-primary text-bold">{{job.addressLabel}}<br/></span>
                {{job.formattedAddress}}
              </div>
            </div>
          </template>
        </q-field>
        <q-field
          label="Job Description"
          borderless
          readonly
          hide-bottom-space
        >
          <template v-slot:prepend>
            <q-icon name="assignment" />
          </template>
          <template v-slot:control>
            <div class="full-width q-mt-md">
              <p style="margin: 0 5px" v-for="(text, index) in specialInstructions.split('\n')" :key="index">
                {{text}}&nbsp;
              </p>
              <span v-if="job.specialInstructions && job.specialInstructions.length > 30 && job.specialInstructions.indexOf('\n') === -1" style="text-decoration: underline"
                @click="displayAllJobDetails = !displayAllJobDetails">{{displayAllJobDetails ? 'less' : 'more'}}</span>
            </div>
          </template>
        </q-field>
        <div v-if="job.managedBy === types.managedBy.system">
          <filters-panel mode="jobDetails" filterType="postOneOffJob" :jobType="job.jobType" />
        </div>
        <q-field
          borderless
          readonly
          hide-bottom-space
          label="Payment Method">
          <template v-slot:prepend>
            <q-icon name="monetization_on" />
          </template>
          <template v-slot:control>
            <div class="full-width q-mt-md">
              <div>
                Price: <span>{{formatCurrency(job.price)}}</span> <span>{{getPaymentTypeText}}</span>
              </div>
              <div v-if="job.willPayOvertime">
                {{getOvertimeText}}
                <!-- Overtime Payment Type: <span>{{overtimePaymentType}}</span>
                <br/>
                Overtime Rate: <span>{{formatCurrency(job.overtimeRate)}}</span> -->
              </div>
              <div>
                <span>{{getHelperPaymentMethod()}}</span>
              </div>
              <div v-if="isBusiness">
                <small v-if="country === 'au'">Excludes Superannuation and other statutory entitlements. </small>
                <small v-if="country === 'nz'">Excludes any government statutory entitlements.</small>
              </div>
            </div>
          </template>
        </q-field>
        <juggle-assist-job-analysis :job="job" />
      </q-card-section>
      <q-card-section v-if="job.jobStatus === types.jobStatus.awarded || job.jobStatus === types.jobStatus.completed">
        <span class="text-bold">Awarded Candidate</span>
        <div class="q-my-sm">
          <img class="round-user-image" :src="job.awardedImageSrc"/>
          <router-link class="q-mx-sm text-black" :to="'/account/helper-profile/' + job.awardedApplicantId">{{job.awardedFirstName}} {{job.awardedLastInitial}}</router-link>
          <!-- <q-btn v-if="canChat" class="q-ml-sm" color="accent" @click="handleChatWithAwardedHelper">Chat</q-btn> -->
          <q-icon v-if="canChat" color="black" size="36px" name="chat" @click="handleChatWithAwardedHelper" />
        </div>
        <div v-if="job.awardedPhoneNumber">
          Phone Number: <a :href="phoneNumberLink">{{job.awardedPhoneNumber}}</a>
        </div>
        <div>
          Getting Home: {{awardedHelperGettingHomeText}}
        </div>
        <div v-if="job.helperPaymentMethod === types.helperPaymentMethod.bankTransfer">
          <div>
            <q-field
              class="q-my-sm"
              borderless
              readonly
              label="Bank Transfer Details">
              <template v-slot:prepend>
                <q-icon name="account_balance" />
              </template>
              <template v-slot:control>
                <div class="full-width q-mt-md">
                  <div>
                    <q-input label="Name" :model-value="job.awardedBankAccountName" hide-underline class="bank-account-input" borderless readonly/>
                  </div>
                  <div v-if="userInfo.country === 'au'">
                    <q-input label="BSB" :model-value="job.awardedBankAccountBsb" hide-underline class="bank-account-input" borderless readonly/>
                  </div>
                  <div>
                    <q-input label="Acc #" :model-value="job.awardedBankAccountNumber" hide-underline class="bank-account-input" borderless readonly/>
                  </div>
                </div>
              </template>
            </q-field>
          </div>
        </div>
      </q-card-section>
      <div style="margin: 5px 0; text-align: center;" v-if="job.jobStatus === types.jobStatus.pending && job.managedBy === types.managedBy.user">
        <div style="display: inline-block; width: 30%;">Applied: <span class="text-green">{{job.applicantsApplied}}</span></div>
        <div style="display: inline-block; width: 30%;">Declines: <span class="text-red">{{job.applicantsNotInterested + job.applicantsNotAvailable }}</span></div>
        <div style="display: inline-block; width: 30%;">Viewed: <span class="">{{job.applicantsViewed}}</span></div>
      </div>
      <div v-if="job.jobStatus === types.jobStatus.pending">
        <q-tabs v-model="applicantsTabsModel" class="applicant-groups bg-secondary text-white shadow-2">
          <q-tab
            v-for="(key, index) in Object.keys(applicants)"
            :key="key"
            :name="'applicant-tab-' + index"
            :icon="applicants[key].icon"
            :label="applicants[key].label"
            class="col-4"
            >
              <q-badge v-if="applicants[key].list.length > 0" color="red" floating>
                {{applicants[key].list.length}}
              </q-badge>
            </q-tab>
        </q-tabs>
        <q-tab-panels v-model="applicantsTabsModel" class="bg-transparent">
          <q-tab-panel
            class="applicants-tab-pane"
            v-for="(key, index) in Object.keys(applicants)"
            :key="key"
            :name="'applicant-tab-' + index"
          >
            <div v-for="(item, index) in applicants[key].list" :key="index">
              <applicant-card :applicant="item" :mode="key" v-on:awardJob="handleAward(item)"/>
              <q-separator />
            </div>
            <div class="text-faded" style="padding: 1rem;" v-if="applicants[key].list.length === 0">
              No carers match this criteria.
            </div>
          </q-tab-panel>
        </q-tab-panels>
      </div>
      <q-card-section>
        <div v-if="job.jobStatus === types.jobStatus.cancelled">
          This job has been archived.
        </div>
      </q-card-section>
      <div class="job-actions" v-if="job.jobStatus === types.jobStatus.pending">
        <q-btn class="cancel-job" @click="handleCancelJob" color="red" icon="close">Cancel</q-btn>
        <q-btn @click="handleEditJob" class="bg-grey text-white" icon="mode_edit">Edit</q-btn>
        <q-btn v-if="job.managedBy === types.managedBy.user" class="invite-more" @click="handleInviteMore" color="primary" icon="person_add" label="Invite More" stack size="12px"></q-btn>
        <q-btn v-if="job.managedBy === types.managedBy.system" @click="handleChangeCandidateCriteria" color="primary" icon="group_add" label="Edit Criteria" stack size="12px"></q-btn>
      </div>
      <div class="job-actions" v-if="job.jobStatus === types.jobStatus.awarded">
        <q-btn class="cancel-job" @click="handleCancelJob" color="red" icon="close">Cancel</q-btn>
        <q-btn @click="handleDuplicateJob" color="secondary" icon="file copy">Duplicate</q-btn>
      </div>
      <div class="job-actions" v-if="job.jobStatus === types.jobStatus.cancelled || job.jobStatus === types.jobStatus.completed">
        <q-btn @click="handleDuplicateJob" color="secondary" icon="file_copy">Duplicate</q-btn>
      </div>
    </q-card>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import c from '../../utils/constants';
import {
  getJobTypeArray, getJobPaymentMethodText, deepClone, gettGettingHomeTextForFamilies, getUnavailableReasonText, formatJobDate, formatCurrency, jobTypeText, formatNumber,
} from '../../utils/helpers';
import ApplicantCard from './ApplicantCard';
import FiltersPanel from '../../components/FiltersPanel';
import JuggleAssistJobAnalysis from '../../components/JuggleAssistJobAnalysis';
import NotificationService from '../../utils/notification-service';
import * as mutationTypes from '../../store/mutation-types';

export default {
  name: 'OneOffJobDetails',
  components: { ApplicantCard, FiltersPanel, JuggleAssistJobAnalysis },
  data() {
    return {
      selectedTab: '',
      displayAllJobDetails: false,
      types: {
        jobType: c.jobType,
        jobStatus: c.jobStatus,
        jobApplicationStatus: c.jobApplicationStatus,
        unavailableReason: c.unavailableReason,
        helperPaymentMethod: c.helperPaymentMethod,
        managedBy: c.managedBy,
      },
    };
  },
  props: ['job'],
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
      // users: (state) => state.users.data,
    }),
    getOvertimeText() {
      const { overtimeRate } = this.job;
      return `Extra hours rate: $${formatNumber(overtimeRate)} per hour`;
    },
    jobTypeText() {
      return jobTypeText(this.job.jobType, this.clientType);
    },
    canChat() {
      return this.job.awardedPhoneNumber || this.userInfo.permissions.hasUnlimitedChatWithProviders;
    },
    specialInstructions() {
      if (!this.job.specialInstructions || this.job.specialInstructions.length === 0) {
        return 'None specified';
      }

      if (this.job.specialInstructions.length < 40 || this.job.specialInstructions.indexOf('\n') !== -1 || this.displayAllJobDetails) {
        return this.job.specialInstructions;
      }

      const idx = this.job.specialInstructions.indexOf(' ', 25);
      if (idx === -1) return this.job.specialInstructions;

      return `${this.job.specialInstructions.substring(0, idx)}...`;
    },
    applicantsTabsModel: {
      get() {
        /* eslint-disable */
        if (!this.selectedTab) {
          const { applicants } = this;
          let index;
          if (applicants.shortlisted.list.length > 0 || this.job.managedBy === c.managedBy.system) index = 0;
          else if (applicants.waiting.list.length > 0) index = 2;
          else index = 1;

          this.selectedTab = 'applicant-tab-' + index;
        }

        return this.selectedTab;
        /* eslint-enable */
      },
      set(value) {
        this.selectedTab = value;
      },
    },
    applicants() {
      if (this.job.managedBy === c.managedBy.user) {
        return {
          shortlisted: {
            icon: 'stars',
            label: 'Applied',
            list: this.job.applicants.filter((applicant) => applicant.applicationStatus === c.jobApplicationStatus.applied),
          },
          declined: {
            icon: 'clear',
            label: 'Declined',
            list: this.job.applicants.filter((applicant) => applicant.applicationStatus === c.jobApplicationStatus.withdrawn
              || applicant.applicationStatus === c.jobApplicationStatus.notAvailable
              || applicant.applicationStatus === c.jobApplicationStatus.notInterested),
          },
          waiting: {
            icon: 'hourglass_empty',
            label: 'Waiting',
            list: this.job.applicants.filter((applicant) => applicant.applicationStatus === c.jobApplicationStatus.pending
              || applicant.applicationStatus === c.jobApplicationStatus.viewed),
          },
        };
      }

      return {
        shortlisted: {
          icon: 'stars',
          label: 'Shortlisted Candidates',
          list: this.job.applicants.filter((applicant) => applicant.applicationStatus === c.jobApplicationStatus.applied
            || applicant.applicationStatus === c.jobApplicationStatus.shortlistedByClient
            || applicant.applicationStatus === c.jobApplicationStatus.shortlistedBySystem),
        },
      };
    },
    storeModuleName() {
      return 'families.pages.jobDetails';
    },
    awardedHelperGettingHomeText() {
      return gettGettingHomeTextForFamilies(this.job.awardedApplicantGettingHome);
    },
    jobDate() {
      return formatJobDate(this.job);
    },
    getPaymentTypeText() {
      return this.job.paymentType === 1 ? 'total' : 'p/h';
    },
    willPayOvertimeText() {
      return this.job.willPayOvertime ? 'Yes' : 'No';
    },
    overtimePaymentType() {
      return this.job.overtimePaymentType === 1 ? 'Fixed' : 'Hourly';
    },
    phoneNumberLink() {
      return `tel:${this.job.awardedPhoneNumber}`;
    },
  },
  methods: {
    formatCurrency(value) {
      return formatCurrency(value);
    },
    getHelperPaymentMethod() {
      return `Payment Method: ${getJobPaymentMethodText(this.job.helperPaymentMethod)}`;
    },
    handleChat(applicant) {
      const url = `/chat/with/${applicant.applicantId}`;
      this.routerPush(url);
    },
    handleChatWithAwardedHelper() {
      this.routerPush(`/chat/with/${this.job.awardedApplicantId}`);
    },
    handleEditJob() {
      const message = 'When you edit the job details it will be reposted, and you will lose your shortlist. Candidates who have already applied will be asked to re-apply.';
      this.$q.dialog({
        title: 'Edit Job',
        message,
        cancel: {
          label: 'Cancel',
          color: 'secondary',
          outline: true,
        },
        ok: {
          label: 'Edit Job',
          outline: true,
        },
      }).onOk(() => {
        this.editJob(false);
      }).onCancel(() => {});
    },
    handleDuplicateJob() {
      const message = 'Use this feature to post a new job based on the current job details, all applicable job settings are copied.';
      this.$q.dialog({
        title: 'Duplicate Job',
        message,
        cancel: {
          label: 'No',
          color: 'secondary',
          outline: true,
        },
        ok: {
          label: 'Yes',
          outline: true,
        },
      }).onOk(() => {
        this.editJob(true);
      }).onCancel(() => {});
    },
    editJob(duplicate) {
      const existingJob = deepClone(this.job);

      const newJob = deepClone(this.$store.state.postOneOffJob.jobDetailsTemplate);
      Object.keys(newJob.data).forEach((key) => {
        if (existingJob[key]) {
          newJob.data[key] = existingJob[key];
        }
      });

      if (duplicate) {
        newJob.data.actionType = c.jobActionType.create;
        newJob.data.jobDate = null;
        delete newJob.data.id;
      } else {
        newJob.data.actionType = c.jobActionType.edit;
        newJob.data.id = this.job.id;

        if (newJob.data.jobDate) {
          const jobDate = new Date(newJob.data.jobDate);
          newJob.jobDate = jobDate;
          newJob.jobDateDay = jobDate.getDate();
          newJob.jobDateMonth = jobDate.getMonth() + 1;
          newJob.jobDateYear = jobDate.getFullYear();
        }
      }

      newJob.data.applicants = [];
      newJob.data.previousApplicants = [];
      newJob.data.selectedApplicants = [];
      existingJob.applicants.forEach((applicant) => {
        newJob.meta.previousApplicants.push(applicant.applicantId);
      });

      this.$store.dispatch('resetStoreStructure', { propPath: 'postOneOffJob.jobDetails', value: newJob });

      const jobTypeArray = getJobTypeArray(this.job.jobType);
      const jobTypePayload = {
        filterType: 'postOneOffJob',
        filterKey: 'jobTypes',
        value: jobTypeArray,
      };
      this.$store.commit(`filters/${mutationTypes.UPDATE_FILTER_OPTION}`, jobTypePayload);

      // const filterPayload = {
      //   filterKey: 'postOneOffJob',
      // };
      // this.$store.dispatch('filters/applyFilter', filterPayload);

      // this.routerReplace('/post-one-off-job');
      this.routerReplace(`/post-job?mode=edit&jobType=${this.job.jobType}`);
    },
    handleRateCarer() {

    },
    handleInviteMore() {
      this.routerReplace(`/families/invite-more-helpers/${this.job.id}`);
    },
    handleChangeCandidateCriteria() {
      this.routerReplace(`/families/change-candidate-criteria/${this.job.id}`);
    },
    applicantStatusText(applicant) {
      switch (applicant.applicationStatus) {
        case this.types.jobApplicationStatus.pending:
          return 'Unseen';
        case this.types.jobApplicationStatus.viewed:
          return 'Viewed';
        case this.types.jobApplicationStatus.applied:
          return 'Interested';
        case this.types.jobApplicationStatus.awarded:
          return 'Awarded';
        case this.types.jobApplicationStatus.notInterested:
          return 'Not Interested';
        case this.types.jobApplicationStatus.notAvailable:
          return 'Not Available';
        case this.types.jobApplicationStatus.withdrawn:
          return 'Withdrawn';
        default:
          return '';
      }
    },
    applicantStatusClass(applicant) {
      switch (applicant.applicationStatus) {
        case this.types.jobApplicationStatus.applied:
          return 'text-green';
        case this.types.jobApplicationStatus.notAvailable:
          return 'text-red';
        case this.types.jobApplicationStatus.notInterested:
          return 'text-red';
        default:
          return '';
      }
    },
    gettingHomeText(applicant) {
      return gettGettingHomeTextForFamilies(applicant.gettingHome);
    },
    unvailableReasonText(applicant) {
      return getUnavailableReasonText(applicant.unavailableReason);
    },
    handleAward(applicant) {
      const message = `Would you like to award this job to ${applicant.applicantFirstName}?`;
      this.$q.dialog({
        title: 'Confirm Award',
        message,
        cancel: {
          label: 'No',
          color: 'secondary',
          outline: true,
        },
        ok: {
          label: 'Yes',
          outline: true,
        },
      }).onOk(() => {
        this.setProperty('awardedApplicantId', applicant.applicantId);
        this.setProperty('awardedApplicantGettingHome', applicant.gettingHome);

        this.$store.dispatch('families/updateJobDetails');
      }).onCancel(() => {});
    },
    handleCancelJob() {
      let message = 'If you cancel this job all applicants will be notified and the cancelled job will be moved to the "Unawarded / Cancelled Jobs" section of "My Jobs". ';
      message += 'Are you sure you want to continue?';
      this.$q.dialog({
        title: 'Confirm Cancellation',
        message,
        cancel: {
          label: 'Don`t Cancel',
          color: 'secondary',
          outline: true,
        },
        ok: {
          label: 'Cancel Job',
          outline: true,
        },
      }).onOk(() => {
        this.cancelJob();
      }).onCancel(() => {});
    },
    cancelJob() {
      this.setProperty('jobStatus', c.jobStatus.cancelled);

      this.$store.dispatch('families/updateJobDetails').then(() => {
        NotificationService.positive('Job cancelled succesfully.');
        this.routerPush('/families/all-jobs');
      });
    },
  },
};
</script>
<style lang="sass">
.one-off-job-details
  .bank-account-input
      width: 200px
      margin-left: 10px
      margin-bottom: 5px
  .q-card-container
    padding: 5px
  .applicant-groups
    .q-tab-label
      font-size: 0.75rem
    .q-chip
      font-size: 75%
      min-height: 10px
      right: 10px
      font-weight: bold
    .q-tab:not(:last-child)
      border-right: 1px solid #fff
  .applicants-tab-pane
    margin-bottom: 25px
    padding: 0
  .job-actions
    position: fixed
    bottom: 0
    left: 0
    right: 0
    display: flex
    .q-btn
      flex: 1
      border-radius: 0
    .cancel-job
      flex-grow: 1.1
    .invite-more
      flex-grow: 1.4
</style>
