<template>
  <div class="">
    <profile-tabs :helper="helper" :selectedTab="selectedTab" v-on:profileTabChange="handleProfileTabChange" />
    <!-- <helper-profile-brief :helper="helper"  v-on:scrollToReviews="scrollToProfileReviews" /> -->
    <profile-overview :displayCitizenship="true" :displayDistance="false" :helper="helper" :mode="mode" :isGuardianApprovalMode="isGuardianApprovalMode" :displayRatings="false" :displayReviews="false" :displayAuPairExperience="true"  v-on:profileTabChange="handleProfileTabChange" />

    <q-card-section style="padding: 8px" class="">
      <q-field label="Au Pair Experience" v-if="helper.providerMyExperience4" borderless readonly hide-bottom-space>
        <template v-slot:prepend>
          <q-icon name="child_care" />
        </template>
        <template v-slot:control>
          <div class="full-width q-mt-md">
            {{providerMyExperience4}}
            <span v-if="helper.providerMyExperience4 && helper.providerMyExperience4.length > 300" style="text-decoration: underline"
                @click="displayAllExperience = !displayAllExperience">{{displayAllExperience ? 'less' : 'more'}}</span>
          </div>
        </template>
      </q-field>

      <q-field v-if="juggleStRefTitle"
        :label="juggleStRefTitle"
        borderless
        hide-bottom-space
        readonly>
        <template v-slot:prepend>
          <q-icon name="icon-juggle-street" />
        </template>
        <template v-slot:control>
          <div class="full-width q-mt-md">
            {{juggleStRefBody}}
          </div>
        </template>
      </q-field>

      <profile-checks :helper="helper" />

      <profile-vevo-check :helper="helper" />

      <!-- <profile-review :helper="helper" ref="profileReviews"/> -->

      <helper-references :helper="helper" ref="profileReviews"/>

      <q-field
        label="Host Family Responsibilities"
        borderless
        hide-bottom-space
        readonly>
        <template v-slot:prepend>
          <q-icon name="warning" />
        </template>
        <template v-slot:control>
          <div class="full-width q-mt-md">
            Interview Au Pair and check references
            <br />
            Sight and verify citizenship & visa documents
            <br />
            Comply with all applicable government regulations
            <br />
          </div>
        </template>
      </q-field>
    </q-card-section>
  </div>
</template>

<script>
import { date } from 'quasar';
import ProfileTabs from 'components/helper-profile/ProfileTabs';
import ProfileOverview from 'components/helper-profile/ProfileOverview';
import ProfileChecks from 'components/helper-profile/ProfileChecks';
import ProfileVevoCheck from 'components/helper-profile/ProfileVevoCheck';
import HelperReferences from 'components/helper-profile/HelperReferences';
import c from '../../utils/constants';
import { scrollToElement } from '../../utils/helpers';

export default {
  name: 'AupairTab',
  components: {
    ProfileTabs, ProfileOverview, ProfileChecks, ProfileVevoCheck, HelperReferences,
  },
  props: ['helper', 'mode', 'selectedTab', 'isGuardianApprovalMode'],
  emits: ['profileTabChange'],
  data() {
    return {
      displayAllExperience: false,
    };
  },
  computed: {
    juggleStRefTitle() {
      const ref = this.helper.vouches.find((v) => v.referenceType === c.vouchReferenceType.juggleStreet);
      if (!ref) return '';

      return `Au Pair Screening - ${date.formatDate(ref.submissionDate, 'DD/MM/YYYY')}`;
    },
    juggleStRefBody() {
      const ref = this.helper.vouches.find((v) => v.referenceType === c.vouchReferenceType.juggleStreet);
      if (!ref) return '';

      return ref.whatWouldYouLikeToSay;
    },
    isNewAuPair() {
      const { providerAuPairCategory } = this.helper;

      return providerAuPairCategory === c.auPairCategory.newAuPair;
    },
    providerMyExperience4() {
      if (!this.helper.providerMyExperience4) return '';

      if (this.helper.providerMyExperience4.length < 300 || this.displayAllExperience) {
        return this.helper.providerMyExperience4;
      }

      const idx = this.helper.providerMyExperience4.indexOf(' ', 300);
      if (idx === -1) return this.helper.providerMyExperience4;

      return `${this.helper.providerMyExperience4.substring(0, idx)}...`;
    },
    isExperiencedAuPair() {
      const { providerAuPairCategory } = this.helper;

      return providerAuPairCategory === c.auPairCategory.experiencedAuPair;
    },
  },
  methods: {
    getDate(value) {
      return date.formatDate(new Date(value), 'DD/MM/YYYY');
    },
    handleProfileTabChange(args) {
      this.$emit('profileTabChange', args);
    },
    scrollToProfileReviews() {
      scrollToElement(this.$refs.profileReviews.$el, -50);
    },
    getList(groupName, sep) {
      if (!this.helper) {
        return '';
      }

      const selectedList = [];
      const list = this.helper[groupName];
      for (let i = 0; i < list.length; i++) {
        if (list[i].selected) {
          selectedList.push(list[i].text);
        }
      }

      if (selectedList.length > 0) {
        return selectedList.join(sep);
      }

      return '';
    },
  },
};
</script>
