<template>
  <div>
    <q-card v-for="(reference, index) in vouches" :key="index" class="q-mx-sm q-my-sm">
      <q-card-section>
        <q-field label="Name" borderless readonly stack-label hide-bottom-space>
          {{reference.fullName}}
        </q-field>
        <!-- <q-field label="Status" borderless readonly stack-label hide-bottom-space>
          {{getStatusText(reference.status)}}
        </q-field> -->
        <q-field label="Contact Details" borderless readonly stack-label hide-bottom-space>
          <div>
            <div v-if="reference.phoneNumber">Phone: {{reference.phoneNumber}}</div>
            <div v-if="reference.email">Email: {{reference.email}}</div>
          </div>
        </q-field>
      </q-card-section>
      <q-card-actions v-if="canEditReference(reference) || canDeleteReference(reference)">
        <q-btn
          v-if="canEditReference(reference)"
          color="accent"
          @click="handleEditReference(reference, index)"
          icon="edit"
          small
        >
          Edit
        </q-btn>
        <q-btn
          outline
          v-if="canDeleteReference(reference)"
          color="secondary"
          @click="handleDeleteReference(index)"
          icon="close"
          small
        >
          Delete
        </q-btn>
      </q-card-actions>
    </q-card>
    <div class="q-my-sm">
      <q-btn v-if="!requestedConfirmation" @click="handleConfirm" color="accent" icon="add">
        Add
      </q-btn>
      <q-btn v-if="requestedConfirmation" :disabled="!confirmedRequirements" @click="handleAddReference" color="accent" icon="add">
        Add
      </q-btn>
    </div>
    <div v-if="requestedConfirmation && !confirmedRequirements">
      <p>
        By ticking the box below you acknowledge and agree with the following:
      </p>
      <ul>
        <li>This Reference is independent, ie. not a member of your family, not your partner etc. </li>
        <li class="q-my-sm">Information provided is true and correct</li>
        <li>You give Juggle Street permission to provide referee details to other registered Juggle Street users.</li>
      </ul>
      <p>
        <q-checkbox v-model="confirmedRequirements" label="Tick to acknowledge and agree" />
      </p>
    </div>
    <div>
      <span class="text-red text-bold">Note</span><br/>
      People you work for on Juggle St will be asked to provide a 5 Star Rating and a written recommendation and these are automatically uploaded to your profile. Providing a referee enables additional, external reference checking.
    </div>
    <personal-reference
      ref="addReferenceModal"
      v-on:success="handleAddReferenceSuccess"
    />
  </div>
</template>

<script>
import { mapState } from 'vuex';
import PersonalReference from 'components/PersonalReference';
import { date } from 'quasar';
import c from '../utils/constants';

export default {
  name: 'PersonalReferences',
  components: { PersonalReference },
  props: {
    minRefs: {
      default: 0,
    },
  },
  emits: ['complete'],
  data() {
    // const { vouches } = this.$store.state.userInfo.data;

    return {
      confirmedRequirements: false,
      requestedConfirmation: false,
    };
  },
  methods: {
    validate() {
      return true;
    },
    getNewReference() {
      return {
        fullName: '',
        phoneNumber: '',
        email: '',
        referenceType: 0,
        otherReferenceType: '',
        status: c.vouchStatus.pendingVerification,
      };
    },
    handleAddReferenceSuccess({ reference, index }) {
      if (index === -1) {
        const refs = [...this.userInfo.vouches];
        refs.push(reference);
        this.setProperty('vouches', refs);
      }

      if (this.userInfo.vouches.length >= this.minRefs) {
        this.$emit('complete');
      }

      this.confirmedRequirements = false;
      this.requestedConfirmation = false;
    },
    handleEditReference(reference, index) {
      this.$refs.addReferenceModal.open(reference, index);
    },
    handleConfirm() {
      this.requestedConfirmation = true;
    },
    handleAddReference() {
      this.$refs.addReferenceModal.open(this.getNewReference(), -1);
    },
    getStatusText(status) {
      switch (status) {
        case c.vouchStatus.approved:
          return 'Verified';
        case c.vouchStatus.pendingVerification:
          return 'Pending';
        case c.vouchStatus.invalid:
          return 'Invalid';
        case c.vouchStatus.expired:
          return 'Expired';
        default:
          throw new Error(`Unsupported option: ${status}`);
      }
    },
    formatDate(d) {
      return date.formatDate(d, 'DD/MM/YYYY');
    },
    handleDeleteReference(index) {
      this.$q.dialog({
        title: 'Confirm',
        message: 'Are you sure you want to delete this reference?',
        cancel: {
          label: 'No',
          color: 'secondary',
          outline: true,
        },
        ok: {
          label: 'Yes',
          outline: true,
        },
      }).onOk(() => {
        const refs = [...this.userInfo.vouches];
        refs.splice(index, 1);
        this.setProperty('vouches', refs);
      }).onCancel(() => {});
    },
    canEditReference(reference) {
      return reference.status !== c.vouchStatus.approved && reference.referenceType !== c.vouchReferenceType.juggleStreet;
    },
    canDeleteReference(reference) {
      return reference.status !== c.vouchStatus.approved && reference.referenceType !== c.vouchReferenceType.juggleStreet;
    },
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
      canAddWWC: (state) => {
        const { certificates } = state.userInfo.data;
        return certificates && certificates.length === 0;
      },
      wwc: (state) => {
        const { certificates } = state.userInfo.data;
        return certificates && certificates.length > 0
          ? certificates[0]
          : {};
      },
      updateNotice() {
        return this.isApproved ? 'UPDATE REQUIRED' : 'NOTE';
      },
    }),
    storeModuleName() {
      return 'userInfo';
    },
    vouches() {
      const vouches = this.userInfo.vouches.filter((r) => r.referenceType !== c.vouchReferenceType.juggleStreet);
      return vouches;
    },
  },
  watch: {
    confirmedRequirements(value) {
      if (value) {
        this.handleAddReference();
      }
    },
  },
};
</script>
