const AdwordsManager = (() => {
  const setGooggleSnippetVars = (convId, convLabel) => {
    const w = window;
    w.google_conversion_id = convId;
    w.google_conversion_label = convLabel;
    // w.google_conversion_value = 13.00;
    // w.google_conversion_currency = "USD";
    w.google_remarketing_only = false;
    w.google_conversion_language = 'en';
    w.google_conversion_format = '3';
    w.google_conversion_color = 'ffffff';
  };

  return {
    reportConversion(isClient) {
      setGooggleSnippetVars(944090999, isClient ? 'JYrxCNKBz18Q996WwgM' : 'qc58CMDC1V8Q996WwgM');

      const opt = {};
      const convHandler = window.google_trackConversion;
      if (typeof convHandler === 'function') {
        convHandler(opt);
      }
    },
    reportAppDownload() {
      setGooggleSnippetVars(944090999, 'DAB0CK_C54UDEPfelsID');

      const opt = {};
      const convHandler = window.google_trackConversion;
      if (typeof convHandler === 'function') {
        convHandler(opt);
      }
    },
  };
})();

export default AdwordsManager;
