import * as http from '../../api/http';
import loader from '../../utils/loader';

export default {
  namespaced: true,
  state: {

  },
  actions: {
    updateSettings: (context, payload) => {
      loader.show();
      return http
        .post('account/settings', payload)
        .then((response) => {
          loader.hide();
          return response;
        })
        .catch((error) => {
          loader.hide();
          return Promise.reject(error);
        });
    },
    hideAccount: () => {
      loader.show();
      return http
        .get('account/hideaccount')
        .then((response) => {
          loader.hide();
          return response;
        })
        .catch((error) => {
          loader.hide();
          return Promise.reject(error);
        });
    },
    deleteAccount: () => {
      loader.show();
      return http
        .get('account/requestdeleteaccount')
        .then((response) => {
          loader.hide();
          return response;
        })
        .catch((error) => {
          loader.hide();
          return Promise.reject(error);
        });
    },
    changePassword: (_, payload) => {
      loader.show();
      return http
        .post('account/changepassword', payload)
        .then((response) => {
          loader.hide();
          return response;
        })
        .catch((error) => {
          loader.hide();
          return Promise.reject(error);
        });
    },
    updateProfileVisibility: ({ dispatch }, payload) => {
      loader.show();
      return http
        .post('account/profilevisibliity', payload)
        .then((response) => {
          console.log(response);
          return dispatch('refreshUserInfo', null, { root: true }).then(() => {
            loader.hide();
            return response;
          }).catch((error) => {
            loader.hide();
            return Promise.reject(error);
          });
        })
        .catch((error) => {
          loader.hide();
          return Promise.reject(error);
        });
    },
  },
  mutations: {

  },
  getters: {

  },
};
