import * as http from '../../api/http';
import c from '../../utils/constants';
import * as types from '../mutation-types';

export default {
  namespaced: true,
  state: {
    data: {
      fullName: '',
      balance: null,
      invoices: [],
      transfers: [],
      payouts: [],
    },
    meta: {
      userPaymentTypes: c.userPaymentType,
      membershipTypes: c.membershipType,
      invoices: {
        isFetching: false,
        lastFetched: null,
      },
      transfers: {
        isFetching: false,
        lastFetched: null,
      },
      payouts: {
        isFetching: false,
        lastFetched: null,
      },
    },
  },
  actions: {
    makePayment(context, payload) {
      return http
        .patch('payments', payload)
        .then((response) => response)
        .catch((error) => Promise.reject(error));
    },
    updatePaymentInfo(context, payload) {
      return http
        .patch('payments', payload)
        .then((response) => response)
        .catch((error) => Promise.reject(error));
    },
    upgradeAccount(context, payload) {
      return http
        .patch('payments', payload)
        .then((response) => response)
        .catch((error) => Promise.reject(error));
    },
    setAutoRenew(context, value) {
      const payload = { actionType: value ? 8 : 2 };
      return http
        .patch('payments', payload)
        .then((response) => response)
        .catch((error) => Promise.reject(error));
    },
    previewProration(context, paymentType) {
      return http
        .get(`payments/previewproration/${paymentType}`)
        .then((response) => response.data)
        .catch((error) => Promise.reject(error));
    },
    fetchInvoices({ commit }) {
      commit(types.INVOICES_REQUEST);
      return http
        .get('payments/invoices')
        .then((response) => {
          commit(types.INVOICES_SUCCESS, response.data);
          return response;
        })
        .catch((error) => {
          commit(types.INVOICES_FAILURE);
          return Promise.reject(error);
        });
    },
    fetchTransfers({ commit }) {
      commit(types.TRANSFERS_REQUEST);
      return http
        .get('payments/transfers')
        .then((response) => {
          commit(types.TRANSFERS_SUCCESS, response.data);
          return response;
        })
        .catch((error) => {
          commit(types.TRANSFERS_FAILURE);
          return Promise.reject(error);
        });
    },
    fetchPayouts({ commit }) {
      commit(types.PAYOUTS_REQUEST);
      return http
        .get('payments/payouts')
        .then((response) => {
          commit(types.PAYOUTS_SUCCESS, response.data);
          return response;
        })
        .catch((error) => {
          commit(types.PAYOUTS_FAILURE);
          return Promise.reject(error);
        });
    },
  },
  mutations: {
    [types.INVOICES_REQUEST]: (state) => {
      state.meta.invoices.isFetching = true;
    },
    [types.INVOICES_SUCCESS]: (state, payload) => {
      state.data.invoices = payload;
      state.meta.invoices.isFetching = false;
      state.meta.invoices.lastFetched = new Date();
    },
    [types.INVOICES_FAILURE]: (state, payload) => {
      state.data.invoices = payload;
      state.meta.invoices.isFetching = false;
      state.meta.invoices.lastFetched = undefined;
    },
    [types.TRANSFERS_REQUEST]: (state) => {
      state.meta.transfers.isFetching = true;
    },
    [types.TRANSFERS_SUCCESS]: (state, payload) => {
      state.data.transfers = payload;
      state.meta.transfers.isFetching = false;
      state.meta.transfers.lastFetched = new Date();
    },
    [types.TRANSFERS_FAILURE]: (state, payload) => {
      state.data.transfers = payload;
      state.meta.transfers.isFetching = false;
      state.meta.transfers.lastFetched = undefined;
    },
    [types.PAYOUTS_REQUEST]: (state) => {
      state.meta.payouts.isFetching = true;
    },
    [types.PAYOUTS_SUCCESS]: (state, payload) => {
      state.data.payouts = payload;
      state.meta.payouts.isFetching = false;
      state.meta.payouts.lastFetched = new Date();
    },
    [types.PAYOUTS_FAILURE]: (state, payload) => {
      state.data.payouts = payload;
      state.meta.payouts.isFetching = false;
      state.meta.payouts.lastFetched = undefined;
    },
  },
  getters: {
    userPaymentTypes: (state) => state.meta.userPaymentTypes,
    membershipTypes: (state) => state.meta.membershipTypes,
    hasExistingCard: (state, getters, rootState) => {
      const { paymentInfo } = rootState.userInfo.data;

      const hasExistingCard = paymentInfo.cardLast4Digits !== '' && paymentInfo.cardLast4Digits !== null && paymentInfo.cardLast4Digits !== undefined;

      return hasExistingCard;
    },
    existingCardText: (state, getters, rootState) => rootState.userInfo.data.paymentInfo.cardLast4Digits,
  },
};
