import loader from '../../utils/loader';
import * as http from '../../api/http';

const steps = {
  requestCode: 1,
  submitCode: 2,
};

export default {
  namespaced: true,
  state: {
    data: {
      newPhoneNumber: '',
      verificationCode: '',
    },
    meta: {
      currentStep: steps.requestCode,
      steps,
    },
  },
  actions: {
    getPhoneVerificationCode({ state }) {
      const number = encodeURIComponent(state.data.newPhoneNumber.trim().replace(/[^0-9]/g, ''));
      const url = `account/sendverificationcode/${number}`;
      loader.show();
      return http
        .get(url)
        .then((response) => {
          loader.hide();
          return response;
        })
        .catch((error) => {
          loader.hide();
          return Promise.reject(error);
        });
    },
    submitPhoneVerificationCode({ state, dispatch }) {
      const code = encodeURIComponent(state.data.verificationCode.trim().replace(/[^0-9]/g, ''));
      const url = `account/verifycode/${code}`;
      loader.show();
      return http
        .get(url)
        .then(async (response) => {
          await dispatch('refreshUserInfo', null, { root: true });
          loader.hide();
          return response;
        })
        .catch((error) => {
          loader.hide();
          return Promise.reject(error);
        });
    },
  },
  mutations: {},
  getters: {
    steps: (state, getters, rootState) => rootState.phoneVerification.meta.steps,
  },
};
