<template>
  <q-page class="layout-padding">
    <div class="text-center q-pa-md flex flex-center">
      <div class="error-container">
          <p class="error-message">
              Sorry...it looks like our system is juggling too many balls at the moment.
          </p>
          <p class="error-message retry-container">
              <q-btn color="secondary" @click="handleTryAgain" label="Try Again" />
          </p>
          <p class="error-message">
              If that doesn't work please click the help icon below and one of our Jugglers will get you up and running again. You can also email us at <a href="mailto:team@jugglestreet.com">team@jugglestreet.com</a>
          </p>
      </div>
    </div>
  </q-page>
</template>

<script>
import errorHandler from '../utils/error-handler';

export default {
  name: 'PageError',
  data() {
    return {
      fromRoute: null,
    };
  },
  methods: {
    handleTryAgain() {
      if (this.platform === 'web') {
        window.location = '/';
      } else {
        window.location = '/';
      }
    },
  },
  beforeRouteEnter(to, from, next) {
    next((vm) => {
      vm.fromRoute = from;

      try {
        throw new Error('Accessed Error Page');
      } catch (err) {
        const data = {
          tags: {
            referrer: from.fullPath,
          },
        };
        errorHandler.captureException(err, data);
      }
    });
  },
};
</script>
