<template>
  <q-page class="">
    <div class="layout-padding">
      <q-stepper ref="scheduleEntries" animated vertical v-model="currentStep">
        <q-step
          v-for="entry in weeklyScheduleEntries"
          :key="entry.dayOfWeek"
          :done="currentStep > entry.index"
          :name="entry.index"
          color="primary"
          :title="getDayLabel(entry.dayOfWeek)">

          <ValidateEach
            v-for="(shift, shiftIndex) in entry.shifts"
            :key="shiftIndex"
            :state="shift"
            :rules="shiftRules"
          >
            <template #default="{ v }">
              <div>
                <time-picker
                  label="Start Time"
                  icon="access_time"
                  :default-value="shift.jobStartTime"
                  :change="handleStartTimeChange(entry.dayOfWeek, shiftIndex, weeklyScheduleEntries)"
                  :error="v.hours.$error"
                  error-message="End time must be greater than start time"
                />

                <time-picker
                  label="End Time"
                  icon="timelapse"
                  :default-value="shift.jobEndTime"
                  :change="handleEndTimeChange(entry.dayOfWeek, shiftIndex, weeklyScheduleEntries)"
                  :error="v.hours.$error"
                  error-message="End time must be greater than start time"
                />

                <q-field
                  class="q-py-sm"
                  label-slot
                  borderless
                  hide-bottom-space
                  readonly
                  v-if="shift.hours > 0"
                >
                  <template v-slot:prepend>
                    <q-icon name="timer" />
                  </template>
                  <template v-slot:label>
                    <div class="full-width">
                      Total Hours: <b>{{shift.hours}}</b>
                      <span class="text-red q-ml-xs" v-if="lowHours(shift)">*</span>
                    </div>
                  </template>
                  <template v-slot:control>
                    <div class="q-mt-md">
                      <span v-if="lowHours(shift)">
                        <span class="text-red" v-if="lowHours(shift)">*</span>
                        Shifts under 3 hours can be difficult to fill. Consider increasing the hours and/or increasing the hourly rate.
                      </span>
                    </div>
                  </template>
                </q-field>
              </div>
            </template>
          </ValidateEach>
          <div class="text-left q-py-xs">
            <q-btn color="secondary"
              flat
              :icon="(entry.shifts.length === 1) ? 'add_circle_outline' : 'remove_circle_outline'"
              @click="handleToggleSecondShift(entry)">
                <span class="text-underline q-mx-sm">
                  {{ entry.shifts.length === 1 ? 'Add second shift' : 'Remove second shift' }}
                </span>
            </q-btn>
          </div>

          <q-stepper-navigation>
            <div class="full-width row justify-center">
              <q-btn
                color="secondary"
                @click="handlePrevious"
                v-show="entry.index !== 0"
                class="q-mx-sm"
                outline
              >
                Back
              </q-btn>
              <q-btn
                color="accent"
                @click="handleNext"
              >
                Next
              </q-btn>
            </div>
          </q-stepper-navigation>
        </q-step>
      </q-stepper>
    </div>
  </q-page>
</template>

<script>
import { mapState } from 'vuex';
import useVuelidate from '@vuelidate/core';
import { minValue } from '@vuelidate/validators';
import { ValidateEach } from '@vuelidate/components';
import TimePicker from 'components/TimePicker';
import { beforeRouteEnter, beforeLeaveStep } from './multistep-helper';
import { getDayOfWeekText, getTimeDifference } from '../../utils/helpers';
import NotificationService from '../../utils/notification-service';
import * as types from '../../store/mutation-types';

export default {
  name: 'PageRecurringJobStep2',
  components: { TimePicker, ValidateEach },
  data() {
    return {
      currentStep: 0,
    };
  },
  setup() {
    return {
      v$: useVuelidate(),
      shiftRules: {
        // hourlyPrice: {
        //   required,
        //   between: between(1, 1000),
        // },
        // jobStartTime: {
        //   required: validators.timeRange('jobStartTime', 'jobEndTime'),
        // },
        // jobEndTime: {
        //   required: validators.timeRange('jobStartTime', 'jobEndTime'),
        // },
        hours: {
          minValue: minValue(0.25),
        },
      },
    };
  },
  computed: {
    ...mapState({
      meta: (state) => state.postRecurringJob.jobDetails.meta,
      job: (state) => state.postRecurringJob.jobDetails.data,
      weeklyScheduleEntries: (state) => state.postRecurringJob.jobDetails.meta.weeklyScheduleEntries,
    }),
    storeModuleName() {
      return 'postRecurringJob.jobDetails';
    },
  },
  methods: {
    handlePrevious() {
      // this.$refs.scheduleEntries.previous();
      this.currentStep--;
    },
    handleNext() {
      if (this.hasErrorInStep()) {
        return;
      }
      if (this.currentStep !== this.weeklyScheduleEntries.length - 1) {
        // this.$refs.scheduleEntries.next();
        this.currentStep++;
      } else {
        this.routerReplace({ path: `/post-recurring-job/step/${this.meta.currentStep + 1}` });
      }
    },
    hasErrorInStep() {
      this.v$.$touch();
      return this.v$.$error;
    },
    validate() {
      this.v$.$touch();
      const hasError = this.v$.$error;
      if (hasError) {
        NotificationService.negative('Please set shift paramaters.');
        return false;
      }

      if (this.currentStep !== this.weeklyScheduleEntries.length - 1) {
        // this.$refs.scheduleEntries.next();
        this.currentStep++;
        return false;
      }

      return true;
    },
    getDayLabel(day) {
      return getDayOfWeekText(day);
    },
    lowHours(shift) {
      return shift.hours <= 2;
    },
    handleStartTimeChange(day, shiftIndex) {
      const self = this;
      return (value) => {
        self.updateTime('jobStartTime', day, shiftIndex, value);
      };
    },
    handleHourlyPriceChange(value, day, shiftIndex) {
      const mutation = {
        dayOfWeek: day,
        shiftIndex,
        propName: 'hourlyPrice',
        value,
      };
      this.$store.commit(`postRecurringJob/${types.UPDATE_SHIFT_DATA}`, mutation);
    },
    handleEndTimeChange(day, shiftIndex) {
      const self = this;
      return (value) => {
        self.updateTime('jobEndTime', day, shiftIndex, value);
      };
    },
    updateTime(timeField, day, shiftIndex, value) {
      const mutation = {
        dayOfWeek: day,
        shiftIndex,
        propName: timeField,
        value,
      };
      this.$store.commit(`postRecurringJob/${types.UPDATE_SHIFT_DATA}`, mutation);
    },
    getTotalPriceForShiftLabel(day, shiftIndex) {
      let label = '';
      for (let i = 0; i < this.weeklyScheduleEntries.length; i++) {
        const entry = this.weeklyScheduleEntries[i];

        if (entry.dayOfWeek === day) {
          const shift = entry.shifts[shiftIndex];

          const hours = getTimeDifference(shift.jobStartTime, shift.jobEndTime);
          const price = shift.hours * shift.hourlyPrice;

          label = `Total $${price} for ${hours} hours`;
          break;
        }
      }
      return label;
    },
    handleToggleSecondShift(day) {
      let value;
      let shiftIndex;
      if (day.shifts.length === 1) {
        value = {
          isRequired: true,
          jobStartTime: '15:00',
          jobEndTime: '18:00',
          hourlyPrice: this.job.hourlyPrice,
          price: this.job.hourlyPrice * 3,
          hours: 3,
        };
        shiftIndex = -1;
      } else {
        shiftIndex = 1;
      }

      const mutation = {
        dayOfWeek: day.dayOfWeek,
        shiftIndex,
        value,
      };
      this.$store.commit(`postRecurringJob/${types.TOGGLE_SHIFT}`, mutation);
    },
  },
  beforeRouteEnter(to, from, next) {
    beforeRouteEnter(to, from, next);
  },
  beforeRouteLeave(to, from, next) {
    beforeLeaveStep(this, to, from, next);
  },
};
</script>
