<template>
  <div class="profile-wizard-helper-welcome-screen fullscreen relative-position">
    <div class="welcome-message">
      <p class="text-dark text-title">{{userInfo.firstName}}, you're almost there!</p>
      <p class="text-dark text-subtitle2">
        We just need a few more details before your profile will become active.
      </p>
      <!-- <img src="https://secure.adnxs.com/seg?add=10333449&t=2" width="1" height="1" v-if="submitConversion"/>
      <img src="https://secure.adnxs.com/px?id=965223&t=2" width="1" height="1" v-if="submitConversion"/> -->
      <div class="start-now-button">
        <q-btn color="secondary" outline class="q-mx-sm" @click="handleLater">Later</q-btn>
        <q-btn color="accent" @click="handleStart">Complete</q-btn>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex';
import beforeLeaveStep from '../before-leave-step';

export default {
  name: 'ProfileWizardHelperWelcome',
  data() {
    return {
    };
  },
  methods: {
    ...mapActions('profileWizard', [
      'getStep',
    ]),
    handleLater() {
      this.routerReplace('/dashboard');
    },
    handleStart() {
      // const nextStep = this.meta.steps[this.meta.currentStep + 1];
      this.$store.dispatch('profileWizard/getStep', { forward: true }).then((nextStep) => {
        this.routerReplace({ path: `/helper-profile-wizard/${nextStep.path}` });
      });
    },
  },
  computed: {
    ...mapState({
      meta: (state) => state.profileWizard.meta,
      userInfo: (state) => state.userInfo.data,
    }),
    submitConversion() {
      return this.$route.query.success;
    },
  },
  beforeRouteLeave(to, from, next) {
    beforeLeaveStep(this, to, from, next);
  },
};
</script>
<style lang="sass">
.profile-wizard-helper-welcome-screen
  background: url('/images/welcome-screen-background.jpg') no-repeat
  background-size: cover
  .welcome-message
    text-align: center
    position: absolute
    top: 6%
    left: 0
    width: 100vw
    background: rgb(238,238,238)
    opacity: 0.9
    padding: 1rem 0.5rem
    .title
      font-size: 1.2rem
</style>
