<template>
  <div class="hide-user-action q-mb-md">
    <div
      v-if="!isOwnProfile && isApproved && canHide"
      v-bind:class="getClassName()"
    >
      <q-btn v-if="isIconMode() && newConnection" color="transparent" @click="handleHide" small flat>
        <q-icon name="clear" color="grey"></q-icon>
      </q-btn>
      <q-btn v-else-if="isIconMode() && existingConnection" color="transparent" @click="handleFavorite" small flat>
        <q-icon name="favorite_border" class="text-brand-pink"></q-icon>
      </q-btn>
      <q-btn v-else-if="!isIconMode()" color="secondary" @click="handleHide" icon="remove_red_eye" size="10px" label="Hide" />
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex';
import c from '../utils/constants';
import NotificationService from '../utils/notification-service';

const DISPLAY_MODES = {
  ICON: 'icon',
  BUTTON: 'button',
};

export default {
  name: 'HideUserAction',
  data() {
    return {
    };
  },
  props: {
    user: {
      default: undefined,
    },
    displayMode: {
      default: DISPLAY_MODES.ICON,
    },
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
    }),
    canHide() {
      return true;
    },
    newConnection() {
      return this.user.friendStatus === c.friendStatus.none;
    },
    existingConnection() {
      return this.user.friendStatus === c.friendStatus.approved
        || (this.user.friendStatus === c.friendStatus.pending && !this.user.actionRequired);
    },
    isOwnProfile() {
      return this.userInfo.id === this.user.id;
    },
  },
  methods: {
    ...mapActions(['sendFriendRequest']),
    hide() {
      const payload = {
        friendId: this.user.id,
      };

      if (this.user.requestId && this.user.requestId > 0) {
        payload.id = this.user.requestId;
      } else if (this.user.friendshipId && this.user.friendshipId > 0) {
        payload.id = this.user.friendshipId;
      }

      // Pending -> Rejected
      // Approved -> Unfriended
      if (payload.id) {
        payload.status = this.user.friendStatus === c.friendStatus.approved ? c.friendStatus.unfriended : c.friendStatus.rejected;
      } else {
        payload.status = c.friendStatus.hidden;
      }

      this.sendFriendRequest(payload).then(() => {
        if (this.isIconMode()) return;

        this.routerBack();
      }).catch((error) => {
        NotificationService.info(error.message);
      });
    },
    handleFavorite() {
      // Do nothing.
    },
    handleHide() {
      this.$q.dialog({
        title: `Hide ${this.user.publicName}`,
        message: `Are you sure you want to permanently hide ${this.user.publicName}?`,
        cancel: {
          label: 'Cancel',
          color: 'secondary',
          outline: true,
        },
        ok: {
          label: 'Hide',
          color: 'primary',
          outline: true,
        },
      }).onOk(() => {
        this.hide();
      }).onCancel(() => {});
    },
    getClassName() {
      return this.displayMode === DISPLAY_MODES.ICON
        ? 'icon-mode'
        : 'button-mode';
    },
    isIconMode() {
      return this.displayMode === DISPLAY_MODES.ICON;
    },
  },
};
</script>
<style lang="sass">
.hide-user-action
  .icon-mode
    position: absolute
    top: 0
    right: 0
</style>
