<template>
  <q-page class="">
    <div class="layout-padding">
      <q-field
        :error="v$.price.$error"
        borderless
        readonly
        hide-bottom-space
        label-slot
        error-slot
      >
        <template v-slot:prepend>
          <q-icon name="add_box" />
        </template>
        <template v-slot:label>
          Price Guide
        </template>
        <template v-slot:control>
          <div class="full-width q-mt-md">
            <div v-if="isIndividual">
              <div>
                When selecting the hourly rate please consider overall cost of job, ie. take into account travel time and prep & follow-up work carried out by tutor
              </div>
              <table class="price-guide-table q-mt-sm">
                <tr>
                  <td>{{country === 'au' ? 'High School' : 'Secondary School'}} Student</td>
                  <td>$20 - $30</td>
                </tr>
                <tr>
                  <td>University Student</td>
                  <td>$30 - $60</td>
                </tr>
                <tr>
                  <td>Certified Teacher</td>
                  <td>$60 or higher</td>
                </tr>
              </table>
            </div>
            <div v-else>
              <div v-if="country === 'au'">
                For details of the childcare wages stipulated in the Children Services Award please refer to the government’s Fair Work Commission website.
              </div>
              <div v-else-if="country === 'nz'">
                For information on childcare employment and salaries please refer to the government’s Employment New Zealand website.
              </div>
            </div>
            <div>
              <b class="q-mr-xs" v-if="isBusiness">*</b>
              <b>Base Rate $</b><q-input
                type="number"
                dense
                hide-bottom-space
                bottom-slots
                v-model="price"
                placeholder="00"
                style="width: 40px; margin: 5px 5px"
                class="inline"
              />per hour
            </div>
            <div v-if="isBusiness" class="text-grey q-mt-sm">
              <span class="q-mr-xs">*</span>
              <span v-if="country === 'au'">Excludes Superannuation and other statutory entitlements. </span>
              <span v-if="country === 'nz'">Excludes any government statutory entitlements.</span>
            </div>
          </div>
        </template>
        <template v-slot:error>
          <div class="q-ml-xl">
            required, min $20
          </div>
        </template>
      </q-field>

      <weekly-schedule-summary :job="job" :meta="meta" v-if="job.price > 0" :minHours="0" ref="weeklySummary" />

      <div class="text-center q-my-sm" v-if="job.price > 0">
        <q-btn
          color="accent"
          @click="handleNext"
        >
          Confirm
        </q-btn>
        <div v-if="isIndividual" class="q-my-sm text-bold">
          Please consider overall job value
        </div>
      </div>
    </div>
  </q-page>
</template>

<script>
import useVuelidate from '@vuelidate/core';
import { required, between } from '@vuelidate/validators';
import { mapState } from 'vuex';
import WeeklyScheduleSummary from 'components/WeeklyScheduleSummary';
import { beforeRouteEnter, beforeLeaveStep } from './multistep-helper';
import NotificationService from '../../utils/notification-service';
import c from '../../utils/constants';
import * as types from '../../store/mutation-types';

export default {
  name: 'PageTutoringJobStep5',
  components: { WeeklyScheduleSummary },
  data() {
    return {
    };
  },
  setup() {
    return {
      v$: useVuelidate(),
    };
  },
  validations: {
    price: { // TODO: Juggle St Pro shouldn't have a minimum rate.
      required,
      between: between(20, 250),
    },
  },
  computed: {
    ...mapState({
      job: (state) => state.postTutoringJob.jobDetails.data,
      meta: (state) => state.postTutoringJob.jobDetails.meta,
    }),
    storeModuleName() {
      return 'postTutoringJob.jobDetails';
    },
    price: {
      get() {
        return this.job.price;
      },
      set(value) {
        this.setProperty('price', value);

        // Update shift hourly rates.
        if (this.meta.weeklyScheduleEntries.length === 0) return;

        this.meta.weeklyScheduleEntries.forEach((entry) => {
          if (entry.shifts.length === 0) return;

          entry.shifts.forEach((shift, shiftIndex) => {
            const mutation = {
              dayOfWeek: entry.dayOfWeek,
              shiftIndex,
              propName: 'hourlyPrice',
              value,
            };
            this.$store.commit(`postTutoringJob/${types.UPDATE_SHIFT_DATA}`, mutation);
          });
        });
      },
    },
  },
  methods: {
    validate() {
      this.v$.$touch();
      const hasError = this.v$.$error;
      if (hasError) {
        NotificationService.negative('Please review highlighted fields.');
      }

      return !hasError;
    },
    handleNext() {
      this.routerReplace({ path: `/post-tutoring-job/step/${this.meta.currentStep + 1}` });
    },
  },
  beforeRouteEnter(to, from, next) {
    beforeRouteEnter(to, from, next);
  },
  beforeRouteLeave(to, from, next) {
    // Set minimum hourly rate.
    let minRate = 1000;
    this.meta.weeklyScheduleEntries.forEach((entry) => {
      entry.shifts.forEach((shift) => {
        const shiftRate = parseFloat(shift.hourlyPrice);
        if (shiftRate < minRate) {
          minRate = shiftRate;
        }
      });
    });

    const filterType = this.job.jobDeliveryMethod === c.jobDeliveryMethod.online ? 'postOnlineTutoringJob' : 'postHomeTutoringJob';
    const filterPayload = {
      filterType,
      filterKey: 'hourlyRate',
      value: minRate,
    };
    this.$store.commit(`filters/${types.UPDATE_FILTER_OPTION}`, filterPayload);

    beforeLeaveStep(this, to, from, next);
  },
};
</script>
