<template>
  <q-page class="">
    <div class="layout-padding">
      <q-field
        borderless
        readonly
        hide-bottom-space
        label-slot
        :label="isIndividual ? 'Price Guide' : 'Wage Rates'"
        :error="v$.hourlyPrice.$error"
        error-message="required"
      >
        <template v-slot:prepend>
          <q-icon name="add_box" />
        </template>
        <template v-slot:control>
          <div class="full-width q-mt-md">
            <div v-if="isIndividual">
              <div>
                When selecting the hourly rate please consider:
                <ul class="no-margin">
                  <li>
                    Overall job requirements incl. number & age of children, driving, extra duties etc.</li>
                  <li>Experience & qualifications of invited helpers.</li>
                  <li>Shift duration - short shifts require higher rates.</li>
                </ul>
              </div>
              <table class="price-guide-table q-mt-sm">
                <tr>
                  <th>
                    Shift duration
                  </th>
                  <th>
                    Base Rate
                  </th>
                </tr>
                <tr>
                  <td>2 Hours</td>
                  <td>Min. $30 p/h</td>
                </tr>
                <tr>
                  <td>3+ Hours</td>
                  <td>Av. $25-$35 p/h</td>
                </tr>
              </table>
            </div>
            <div v-else>
              <div v-if="country === 'au'">
                For details of the childcare wages stipulated in the Children Services Award please refer to the government’s Fair Work Commission website.
              </div>
              <div v-else-if="country === 'nz'">
                For information on childcare employment and salaries please refer to the government’s Employment New Zealand website.
              </div>
            </div>
            <div class="">
              <b class="q-mr-xs" v-if="hourlyPrice > 0 && isBusiness">*</b>
              <b>Base rate $</b><q-input
                type="number"
                dense
                hide-bottom-space
                bottom-slots
                v-model="hourlyPrice"
                placeholder="00"
                style="width: 30px; margin: 0 5px; padding: 0"
                input-style="padding: 0"
                class="inline"
              />per hour<span v-if="isLowRate" class="text-red"> *</span>
            </div>
            <div v-if="isLowRate" class="text-grey q-mt-sm">
              <span class="text-red">*</span>
              Jobs priced below the Price Guide can be difficult to fill.
            </div>
            <!-- <div v-if="hourlyPrice > 0 && isBusiness" class="text-grey q-mt-sm">
              <span class="q-mr-xs">*</span>
              <span v-if="country === 'au'">Excludes Superannuation and other statutory entitlements. </span>
              <span v-if="country === 'nz'">Excludes any government statutory entitlements.</span>
            </div> -->
          </div>
        </template>
      </q-field>

      <weekly-schedule-summary :job="job" :meta="meta" v-if="hourlyPrice > 0" :editable="true" storeModuleName="postRecurringJob" :minHours="3" ref="weeklySummary" />

      <div class="text-center q-my-sm" v-if="hourlyPrice > 0">
        <q-btn
          color="accent"
          @click="handleNext"
        >
          Confirm
        </q-btn>
        <div v-if="isIndividual" class="q-my-sm text-bold">
          Please consider overall job value
        </div>
      </div>
    </div>
  </q-page>
</template>

<script>
import useVuelidate from '@vuelidate/core';
import { required, minValue } from '@vuelidate/validators';
import { mapState } from 'vuex';
import WeeklyScheduleSummary from 'components/WeeklyScheduleSummary';
import { beforeRouteEnter, beforeLeaveStep } from './multistep-helper';
import NotificationService from '../../utils/notification-service';
import * as types from '../../store/mutation-types';

export default {
  name: 'PageRecurringJobStep3',
  components: { WeeklyScheduleSummary },
  data() {
    return {};
  },
  setup() {
    return {
      v$: useVuelidate(),
    };
  },
  validations() {
    return {
      hourlyPrice: {
        required,
        minValue: minValue(1),
      },
    };
  },
  computed: {
    ...mapState({
      job: (state) => state.postRecurringJob.jobDetails.data,
      meta: (state) => state.postRecurringJob.jobDetails.meta,
    }),
    storeModuleName() {
      return 'postRecurringJob.jobDetails';
    },
    isLowRate() {
      if (this.isBusiness) return false;

      return this.hourlyPrice > 0 && this.hourlyPrice < 25;
    },
    hourlyPrice: {
      get() {
        return this.job.hourlyPrice;
      },
      set(value) {
        this.setProperty('hourlyPrice', value);

        // Update shift hourly rates.
        if (this.meta.weeklyScheduleEntries.length === 0) return;

        this.meta.weeklyScheduleEntries.forEach((entry) => {
          if (entry.shifts.length === 0) return;

          entry.shifts.forEach((shift, shiftIndex) => {
            const mutation = {
              dayOfWeek: entry.dayOfWeek,
              shiftIndex,
              propName: 'hourlyPrice',
              value,
            };
            this.$store.commit(`postRecurringJob/${types.UPDATE_SHIFT_DATA}`, mutation);
          });
        });
      },
    },
  },
  methods: {
    validate() {
      this.v$.$touch();
      const hasError = this.v$.$error || (this.$refs.weeklySummary && !this.$refs.weeklySummary.validate());
      if (hasError) {
        NotificationService.negative('Please check hourly rates.');
      }

      return !hasError;
    },
    handleNext() {
      this.routerReplace({ path: `/post-recurring-job/step/${this.meta.currentStep + 1}` });
    },
  },
  beforeRouteEnter(to, from, next) {
    beforeRouteEnter(to, from, next);
  },
  beforeRouteLeave(to, from, next) {
    // Set minimum hourly rate.
    let minRate = 1000;
    this.meta.weeklyScheduleEntries.forEach((entry) => {
      entry.shifts.forEach((shift) => {
        const shiftRate = parseFloat(shift.hourlyPrice);
        if (shiftRate < minRate) {
          minRate = shiftRate;
        }
      });
    });

    const filterPayload = {
      filterType: 'postRecurringJob',
      filterKey: 'hourlyRate',
      value: minRate,
    };
    this.$store.commit(`filters/${types.UPDATE_FILTER_OPTION}`, filterPayload);

    beforeLeaveStep(this, to, from, next);
  },
};
</script>
