import * as http from '../../api/http';
import * as types from '../mutation-types';

export default {
  namespaced: true,
  state: {

  },
  actions: {
    checkProfile: ({ commit }, { id }) => {
      commit(types.PROFILE_CHECK, { id });
    },
    fetchHelperProfile: ({ commit, state }, { id, guardianApprovalKey }) => {
      if (state[id].lastFetched != null) {
        return Promise.resolve();
      }

      const query = guardianApprovalKey ? `?guardianApprovalKey=${guardianApprovalKey}` : '';
      const endpoint = guardianApprovalKey ? 'guardians/provider' : 'profiles/provider';

      commit(types.PROFILE_REQUEST, { id });
      return http
        .get(`${endpoint}/${id}${query}`)
        .then((response) => {
          const profile = response.data;
          commit(types.PROFILE_SUCCESS, profile);

          const title = `${profile.firstName} ${profile.lastInitial}`;
          commit(types.SET_PAGE_INFO, { title }, { root: true });

          return response;
        })
        .catch((error) => {
          commit(types.PROFILE_FAILURE, { id });
          return Promise.reject(error);
        });
    },
    fetchClientProfile: ({ commit, state }, id) => {
      if (state[id].lastFetched != null) {
        return Promise.resolve();
      }

      commit(types.PROFILE_REQUEST, { id });
      return http
        .get(`profiles/client/${id}`)
        .then((response) => {
          const profile = response.data;
          commit(types.PROFILE_SUCCESS, profile);

          commit(types.SET_PAGE_INFO, { title: profile.publicName }, { root: true });

          return response;
        })
        .catch((error) => {
          commit(types.PROFILE_FAILURE, { id });
          return Promise.reject(error);
        });
    },
  },
  mutations: {
    [types.PROFILE_CHECK]: (state, payload) => {
      if (state[payload.id]) {
        return;
      }

      state[payload.id] = {
        data: {},
        meta: {
          isFetching: false,
          lastFetched: null,
          dirty: false,
          selectedTab: 'profile',
        },
      };
      // Vue.set(
      //   state, payload.id,
      //   {
      //     data: {},
      //     meta: {
      //       isFetching: false,
      //       lastFetched: null,
      //       dirty: false,
      //       selectedTab: 'profile',
      //     },
      //   },
      // );
    },
    [types.PROFILE_REQUEST]: (state, payload) => {
      state[payload.id].meta.isFetching = true;
      state[payload.id].meta.lastFetched = null;
    },
    [types.PROFILE_SUCCESS]: (state, payload) => {
      state[payload.id].data = payload;
      state[payload.id].meta.isFetching = false;
      state[payload.id].meta.lastFetched = new Date();
    },
    [types.PROFILE_FAILURE]: (state, payload) => {
      if (state[payload.id]) {
        state[payload.id].data = {};
        state[payload.id].meta.isFetching = false;
        state[payload.id].meta.lastFetched = new Date();
      }
    },
    [types.FRIEND_REQUEST_SUCCESS]: (state, payload) => {
      const profile = state[payload.friendId];
      if (!profile) {
        return;
      }

      profile.data.friendStatus = payload.status;
      profile.data.friendshipId = payload.id;
    },
  },
  getters: {
    getHelperProfile: (state) => (id) => state[id],
    getClientProfile: (state) => (id) => state[id],
  },
};
