<template>
  <div class="profile-wizard-children layout-padding">
    <p style="margin-top: 10px">
      It is essential Juggle Street helpers know the number of children you have and how old they are. We ask for the birth month and year so your child’s age is always up-to-date. Only your child’s age is displayed, not the birth month.
    </p>
    <q-list v-if="children.length > 0">
      <q-item
        v-for="(child, index) in children"
        :key="index"
      >
        <q-item-section top avatar>
          <q-avatar icon="child_care" />
        </q-item-section>
        <q-item-section>
          <q-item-label>
            {{child.firstName}} - {{ageInYears(child)}} yr{{ageInYears(child) === 1 ? '' : 's'}}
          </q-item-label>
        </q-item-section>
        <q-item-section>
          <q-item-label>
            <q-btn
              size="sm"
              round
              color="accent"
              icon="create"
              class="q-mx-sm"
              @click="handleEditChild(child, index)"
            />
            <q-btn
              size="sm"
              round
              color="secondary"
              icon="close"
              outline
              @click="handleDeleteChild(index)"
            />
          </q-item-label>
        </q-item-section>
      </q-item>
    </q-list>

    <div class="row add-child-button">
      <q-btn @click="handleAddChild" color="accent">
        Add Child
      </q-btn>
    </div>
    <!-- <small class="text-faded" v-if="children.length === 0">* required</small> -->

    <add-child-modal ref="addChildModal" v-on:addChildSuccess="handleAddChildSuccess" />
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex';
import AddChildModal from 'components/AddChildModal';
import * as types from '../../../store/mutation-types';
import beforeLeaveStep from '../before-leave-step';
import NotificationService from '../../../utils/notification-service';

export default {
  name: 'ProfileWizardChildrenStep',
  components: { AddChildModal },
  data() {
    return {};
  },
  methods: {
    ...mapActions([
      'updateUserInfo',
    ]),
    ageInYears(child) {
      return new Date().getFullYear() - new Date(child.dateOfBirth).getFullYear();
    },
    handleAddChild() {
      this.$refs.addChildModal.open({ dateOfBirth: new Date() }, -1);
      this.updateStepStatus();
    },
    handleAddChildSuccess({ child, index }) {
      const newChildren = [...this.children];

      if (index >= 0) {
        newChildren[index] = child;
      } else {
        newChildren.push(child);
      }

      this.setProperty('children', newChildren);
      this.updateStepStatus();
    },
    handleEditChild(child, index) {
      this.$refs.addChildModal.open({ ...child }, index);
    },
    handleDeleteChild(index) {
      const newChildren = [...this.children];

      if (index >= 0) {
        newChildren.splice(index, 1);
      }

      this.setProperty('children', newChildren);
      this.updateStepStatus();
    },
    updateStepStatus() {
      const complete = this.children.length > 0;
      this.$store.commit(`profileWizard/${types.UPDATE_STEP_STATUS}`, { complete });
    },
    validate() {
      if (this.currentStep.meta.canSkip) {
        return true;
      }

      if (this.stepComplete) {
        return true;
      }

      NotificationService.info('Please add your children details.');
      return false;
    },
  },
  computed: {
    ...mapState({
      children: (state) => state.userInfo.data.children,
      currentStep: (state) => {
        const { meta } = state.profileWizard;
        const currentStep = meta.steps[meta.currentStep];
        return currentStep;
      },
      stepComplete: (state) => {
        const { meta } = state.profileWizard;
        const currentStep = meta.steps[meta.currentStep];
        return currentStep.complete;
      },
    }),
    storeModuleName() {
      return 'userInfo';
    },
  },
  beforeRouteLeave(to, from, next) {
    // Update user info before proceeding to the next step if step is complete
    if (this.stepComplete) {
      this.updateUserInfo().then(() => {
        beforeLeaveStep(this, to, from, next);
      });
    } else {
      beforeLeaveStep(this, to, from, next);
    }
  },
  beforeRouteEnter(to, from, next) {
    next((vm) => {
      vm.updateStepStatus();
    });
  },
};
</script>
<style lang="sass">
.profile-wizard-children
  .add-child-button
    margin: 1rem
  .icons
    display: block
</style>
