<template>
  <q-dialog v-model="opened" @hide="handleHide">
    <q-layout view="Lhh lpR fff" container class="bg-white">
      <q-header>
        <q-toolbar>
          <q-toolbar-title>
            Rate Your Helper
          </q-toolbar-title>
          <q-btn flat>
            <q-icon name="clear" v-close-popup />
          </q-btn>
        </q-toolbar>
      </q-header>

      <q-page-container>
        <div class="layout-padding">
          <rate-helper ref="rateHelper" v-on:rateHelperSuccess="handleRateHelperSuccess"/>
        </div>
      </q-page-container>
    </q-layout>
  </q-dialog>
</template>

<script>
import RateHelper from 'components/RateHelper';

export default {
  name: 'RateHelperModal',
  components: { RateHelper },
  emits: ['hide', 'rateHelperSuccess'],
  data() {
    return {
      opened: false,
    };
  },
  methods: {
    open() {
      this.opened = true;
    },
    handleHide() {
      this.$emit('hide');
    },
    handleRateHelperSuccess() {
      this.opened = false;
      this.$emit('rateHelperSuccess');
    },
  },
};
</script>
