<template>
  <div>
    <div v-if="userInfo.guardian">
      <span>
        The guardian request has been sent.
      </span>
    </div>
    <div v-else>
      <q-field
        label="First Name"
        :error="v$.firstName.$error"
        error-message="required"
        borderless
        :readonly="!canEdit"
        hide-bottom-space
      >
        <template v-slot:control>
          <div class="full-width">
            <q-input v-model="firstName" :disable="!canEdit"/>
          </div>
        </template>
      </q-field>

      <q-field
        label="Last Name"
        :error="v$.lastName.$error"
        error-message="required"
        borderless
        :readonly="!canEdit"
        hide-bottom-space
      >
        <template v-slot:control>
          <div class="full-width">
            <q-input v-model="lastName" :disable="!canEdit"/>
          </div>
        </template>
      </q-field>

      <q-field
        label="Phone Number"
        :error="v$.phoneNumber.$error"
        error-message="required"
        borderless
        :readonly="!canEdit"
        hide-bottom-space
      >
        <template v-slot:control>
          <div class="full-width">
            <q-input v-model="phoneNumber" :disable="!canEdit"/>
          </div>
        </template>
      </q-field>

      <q-field
        label="Repeat Phone Number"
        :error="v$.phoneNumberRepeat.$error"
        :error-message="phoneNumberErrorMessage()"
        v-if="canEdit"
        borderless
        hide-bottom-space
        :readonly="!canEdit"
      >
        <template v-slot:control>
          <div class="full-width">
            <q-input v-model="phoneNumberRepeat" :disable="!canEdit"/>
          </div>
        </template>
      </q-field>

      <q-field
        label="Relationship"
        :error="v$.relationship.$error"
        error-message="Select one option"
        borderless
        :readonly="!canEdit"
      >
        <template v-slot:control>
          <div class="full-width q-mt-md">
            <q-option-group
              type="radio"
              v-model="relationship"
              :options="relationships"
              :disable="!canEdit"
            />
          </div>
        </template>
      </q-field>
      <div v-if="canEdit">
        <q-btn color="accent" @click="handleSubmit">Submit</q-btn>
        <p class="q-my-sm q-mx-sm">
          When you press Submit, your parent or guardian will be sent an SMS with a link asking them to review and approve your Juggle Street.
        </p>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex';
import useVuelidate from '@vuelidate/core';
import {
  required,
  sameAs,
} from '@vuelidate/validators';
import { validatePhoneNumber } from '../utils/helpers';

export default {
  name: 'AddGuardian',
  emits: ['addGuardianSuccess', 'addGuardianFailure'],
  data() {
    return {};
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
      newGuardian: (state) => state.guardian.data,
      relationships: (state) => state.guardian.meta.relationships,
    }),
    storeModuleName() {
      return 'guardian';
    },
    guardian() {
      return this.userInfo.defaultGuardian ? this.userInfo.defaultGuardian : this.newGuardian;
    },
    canEdit() {
      return !this.userInfo.defaultGuardian;
    },
    firstName: {
      get() {
        return this.guardian.firstName;
      },
      set(value) {
        this.setProperty('firstName', value);
      },
    },
    lastName: {
      get() {
        return this.guardian.lastName;
      },
      set(value) {
        this.setProperty('lastName', value);
      },
    },
    phoneNumber: {
      get() {
        return this.guardian.phoneNumber;
      },
      set(value) {
        this.setProperty('phoneNumber', value);
      },
    },
    phoneNumberRepeat: {
      get() {
        return this.guardian.phoneNumberRepeat;
      },
      set(value) {
        this.setProperty('phoneNumberRepeat', value);
      },
    },
    relationship: {
      get() {
        return this.guardian.relationship;
      },
      set(value) {
        this.setProperty('relationship', value);
      },
    },
  },
  methods: {
    ...mapActions('guardian', [
      'submitGuardian',
      'fetchGuardian',
    ]),
    handleSubmit() {
      this.v$.$touch();
      if (this.v$.$error) {
        return;
      }
      this.submitGuardian()
        .then(() => {
          this.$emit('addGuardianSuccess');
        })
        .catch((res) => {
          this.$emit('addGuardianFailure', res);
        });
    },
    phoneNumberErrorMessage() {
      if (this.v$.phoneNumberRepeat.$error) {
        return this.v$.phoneNumberRepeat.sameAsPhoneNumber ? 'required' : 'Phone number does not match.';
      }

      return '';
    },
  },
  created() {
    // There is no need to make a separate call to fetch guardian details as the data is available under usenInfo.defaultGuardian.
    // this.fetchGuardian();
  },
  setup() {
    return {
      v$: useVuelidate(),
    };
  },
  validations() {
    return {
      firstName: {
        required,
      },
      lastName: {
        required,
      },
      // phoneNumber: {
      //   required,
      //   numeric,
      //   maxLength: maxLength(10),
      //   minLength: minLength(10),
      // },
      phoneNumber: {
        validate(value) {
          return validatePhoneNumber(value);
        },
      },
      phoneNumberRepeat: {
        required,
        sameAsPhoneNumber: sameAs(this.phoneNumber),
      },
      relationship: {
        required,
      },
    };
  },
};
</script>
