<template>
  <q-page class="">
    <div>
      <div v-if="job.id && job.id > 0 && hasProcessedDetails">
        <one-off-job-details :job="job" v-if="job.jobType === types.jobType.babysitting" />
        <au-pair-job-details :job="job" v-else-if="job.jobType === types.jobType.auPair" />
        <tutoring-job-details :job="job" v-else-if="job.jobType === types.jobType.primarySchoolTutoring || job.jobType === types.jobType.highSchoolTutoring" />
        <recurring-job-details :job="job" v-else />
      </div>
    </div>
  </q-page>
</template>

<script>
import { mapState } from 'vuex';
import OneOffJobDetails from 'components/one-off-job-details/Main';
import RecurringJobDetails from 'components/recurring-job-details/Main';
import AuPairJobDetails from 'components/au-pair-job-details/Main';
import TutoringJobDetails from 'components/tutoring-job-details/Main';
import * as mutationTypes from '../../store/mutation-types';
import c from '../../utils/constants';
import store from '../../store';
import { getJobTypeArray, getJobModuleName } from '../../utils/helpers';

export default {
  name: 'PageFamiliesJobDetails',
  components: {
    OneOffJobDetails, RecurringJobDetails, AuPairJobDetails, TutoringJobDetails,
  },
  data() {
    return {
      hasProcessedDetails: false,
      types: {
        jobType: c.jobType,
      },
    };
  },
  computed: {
    ...mapState({
      job: (state) => state.families.pages.jobDetails.data,
    }),
    filterKey() {
      if (this.job.jobType === c.jobType.primarySchoolTutoring || this.job.jobType === c.jobType.highSchoolTutoring) {
        if (this.job.jobDeliveryMethod === c.jobDeliveryMethod.online) {
          return 'postOnlineTutoringJob';
        }

        return 'postHomeTutoringJob';
      }
      return getJobModuleName(this.job.jobType);
    },
  },
  methods: {
    async loadJobDetails(id) {
      this.loader.show();

      await this.$store.dispatch('families/fetchJobDetails', { id });
      this.setJobFilters();

      this.loader.hide();
    },
    setJobFilters() {
      if (this.job.managedBy === c.managedBy.user) {
        this.hasProcessedDetails = true;
        return;
      }

      const jobTypeArray = getJobTypeArray(this.job.jobType);
      const jobTypePayload = {
        filterType: this.filterKey,
        filterKey: 'jobTypes',
        value: jobTypeArray,
      };
      this.$store.commit(`filters/${mutationTypes.UPDATE_FILTER_OPTION}`, jobTypePayload);
      this.job.applicantFilters.forEach((filter) => {
        const filterMutation = {
          filterType: this.filterKey,
          filterKey: filter.field,
          value: filter.value,
        };
        this.$store.commit(`filters/${mutationTypes.UPDATE_FILTER_OPTION}`, filterMutation);
      });
      this.hasProcessedDetails = true;
    },
  },
  beforeRouteEnter(to, from, next) {
    store.dispatch('resetStoreStructure', { propPath: 'families.pages.jobDetails' });

    next(async (vm) => {
      await vm.loadJobDetails(to.params.id);
    });
  },
  async beforeRouteUpdate(to, from, next) {
    this.$store.dispatch('resetStoreStructure', { propPath: 'families.pages.jobDetails' });
    await this.loadJobDetails(to.params.id);
    next();
  },
};
</script>
