<template>
  <div>
    <q-field
      :error="v$.localNationality.$error"
      borderless
      stack-label
      hide-bottom-space
      readonly
      dense
    >
      <template v-slot:control>
        <div class="full-width">
          <p class="no-margin">
            {{localNationalityQuestion}}
          </p>
          <q-radio v-model="localNationality" val="AU" label="Australian Citizen" />
          <br/>
          <q-radio v-model="localNationality" val="NZ" label="New Zealand Citizen" />
          <br/>
          <q-radio v-model="localNationality" val="OTHER" label="Other Nationality" />
        </div>
      </template>
    </q-field>

    <div v-if="localNationality === 'OTHER'">
      <q-field
        class=""
        :error="v$.nationality.$error"
        borderless
        hide-bottom-space
        readonly
        dense
      >
        <template v-slot:control>
          <div class="full-width">
            <q-select
              style="width: 200px"
              :error="v$.nationality.$error"
              v-model="nationality"
              :options="countriesList"
              label="Select Nationality"
              option-value="value"
              option-label="label"
              emit-value
              map-options
            />
          </div>
        </template>
      </q-field>

      <q-field
        :error="v$.workingRights.$error"
        borderless
        stack-label
        hide-bottom-space
        readonly
      >
        <template v-slot:control>
          <div class="full-width">
            <p class="no-margin">
              {{workingRightsQuestion}}
            </p>
            <q-radio class="q-mx-sm" v-model="workingRights" :val="2" label="Yes" />
            <q-radio class="q-mx-sm" v-model="workingRights" :val="1" label="No" />
          </div>
        </template>
      </q-field>

      <q-field
        v-if="workingRights === 2"
        class=""
        icon="card travel"
        label="Passport Details"
        borderless
        stack-label
        hide-bottom-space
        readonly
      >
        <template v-slot:control>
          <div class="full-width">
            <q-input
              style="width: 250px"
              v-model="officialFirstName"
              :error="v$.officialFirstName.$error"
              hide-bottom-space
              label="First Name - as shown on passport"
            />
            <q-input
              style="width: 250px"
              v-model="officialMiddleName"
              label="Middle Name - as shown on passport"
            />
            <q-input
              style="width: 250px"
              v-model="officialLastName"
              :error="v$.officialLastName.$error"
              hide-bottom-space
              label="Last Name - as shown on passort"
            />
            <q-input
              style="width: 250px"
              v-model="passportNumber"
              :error="v$.passportNumber.$error"
              label="Passport Number"
            />
            <q-select
              style="width: 250px"
              :error="v$.passportCountry.$error"
              v-model="passportCountry"
              :options="countriesList"
              label="Country of Issue"
              option-value="value"
              option-label="label"
              emit-value
              map-options
            />
            <span class="text-red text-bold">Note</span>
            Above passport information is not displayed on your Juggle Street profile. It is stored to allow families to check your right to work in {{country === 'au' ? 'Australia' : 'New Zealand'}}.
          </div>
        </template>
      </q-field>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import useVuelidate from '@vuelidate/core';
import { required, requiredIf } from '@vuelidate/validators';
import datasets from '../utils/datasets';

export default {
  name: 'VevoDetails',
  emits: ['change', 'complete'],
  data() {
    const userInfo = this.$store.state.userInfo.data;

    let localNationality;
    if (userInfo.nationality === 'AU' || userInfo.nationality === 'NZ') {
      localNationality = userInfo.nationality;
    } else if (userInfo.nationality !== null) {
      localNationality = 'OTHER';
    } else {
      localNationality = null;
    }

    const data = {
      localNationality,
    };

    return data;
  },
  methods: {
    isValid() {
      const mandatory = this.nationality !== null;
      const optional = this.nationality === 'AU' || this.nationality === 'NZ'
        || (this.workingRights > 1 && this.passportNumber && this.passportNumber.length > 0
        && this.passportCountry && this.passportCountry.length > 0
        && this.officialFirstName && this.officialFirstName.length > 0
        && this.officialLastName && this.officialLastName.length > 0);

      return mandatory && optional;
    },
    validate() {
      const isValid = this.isValid();
      if (isValid) return true;

      this.v$.$touch();
      return !this.v$.$error;
    },
    triggerChange() {
      const isValid = this.isValid();
      this.$emit('change', { valid: isValid });

      if (isValid) {
        this.$emit('complete');
      }
    },
    triggerComplete() {
      this.$emit('complete');
    },
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
      currentStep: (state) => {
        const { meta } = state.profileWizard;
        const currentStep = meta.steps[meta.currentStep];
        return currentStep;
      },
    }),
    storeModuleName() {
      return 'userInfo';
    },
    localNationalityQuestion() {
      return 'Are you an Australian or a New Zealand Citizen?';
    },
    workingRightsQuestion() {
      return `Do you have the right to work in ${this.country === 'au' ? 'Australia' : 'New Zealand'}?`;
    },
    countriesList() {
      return datasets.countriesIso;
    },
    workingRights: {
      get() {
        return this.userInfo.provider.workingRights;
      },
      set(value) {
        this.setProperty('provider.workingRights', value);
        this.triggerChange();
      },
    },
    nationality: {
      get() {
        return this.userInfo.nationality;
      },
      set(value) {
        this.setProperty('nationality', value);
        this.triggerChange();
      },
    },
    passportNumber: {
      get() {
        return this.userInfo.passportNumber;
      },
      set(value) {
        this.setProperty('passportNumber', value);
        this.triggerChange();
      },
    },
    passportCountry: {
      get() {
        return this.userInfo.passportCountry;
      },
      set(value) {
        this.setProperty('passportCountry', value);
        this.triggerChange();
      },
    },
    officialFirstName: {
      get() {
        return this.userInfo.officialFirstName;
      },
      set(value) {
        this.setProperty('officialFirstName', value);
        this.triggerChange();
      },
    },
    officialMiddleName: {
      get() {
        return this.userInfo.officialMiddleName;
      },
      set(value) {
        this.setProperty('officialMiddleName', value);
        this.triggerChange();
      },
    },
    officialLastName: {
      get() {
        return this.userInfo.officialLastName;
      },
      set(value) {
        this.setProperty('officialLastName', value);
        this.triggerChange();
      },
    },
  },
  watch: {
    localNationality(value) {
      if (value !== 'OTHER') {
        this.nationality = value;
      } else {
        this.nationality = null;
      }

      this.triggerChange();
    },
  },
  setup() {
    return {
      v$: useVuelidate(),
    };
  },
  validations() {
    return {
      localNationality: {
        required,
      },
      nationality: {
        required,
      },
      passportNumber: {
        requiredIf: requiredIf(this.localNationality === 'OTHER'),
      },
      passportCountry: {
        requiredIf: requiredIf(this.localNationality === 'OTHER'),
      },
      officialFirstName: {
        requiredIf: requiredIf(this.localNationality === 'OTHER'),
      },
      officialLastName: {
        requiredIf: requiredIf(this.localNationality === 'OTHER'),
      },
      workingRights: {
        requiredIf: requiredIf(this.localNationality === 'OTHER'),
      },
    };
  },
};
</script>
