import store from '../../store';
import * as types from '../../store/mutation-types';

const beforeLeaveStep = (context, to, from, next) => {
  // Step indices from the route meta to identify whether the user is
  // going forward or backward
  const toStep = to.meta.stepIndex;
  const fromStep = from.meta.stepIndex;

  if (toStep > fromStep) {
    // Check validation.
    if (context.validate && !context.validate(true)) {
      next(false);
      return;
    }
  } else {
    // Skip validation.
  }

  store.commit(`profileWizard/${types.SET_STEP}`, { step: toStep });

  next();
};

export default beforeLeaveStep;
