<template>
  <q-page class="crop-image layout-padding">
    <crop-image :imageUrl="userInfo.defaultImage.scale1ImageUrl" v-on:cropMove="handleCropImageChange"/>
    <div class="btn-crop-image">
      <a @click="finishCropImage" class="button-link text-primary">Crop Image</a>
    </div>
    <!-- <div v-if="platform !== 'web'" class="text-center q-mt-sm">
      DEV ORIGIN: {{href}}
    </div> -->
  </q-page>
</template>

<script>
import CropImage from 'components/CropImage';
import { mapState, mapActions } from 'vuex';
import NotificationService from '../../utils/notification-service';

export default {
  name: 'PageAccountCropImage',
  components: { CropImage },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
    }),
    ...mapActions([
      'updateUserInfo',
    ]),
    storeModuleName() {
      return 'userInfo';
    },
  },
  data() {
    return {
      href: window.location.origin,
    };
  },
  methods: {
    handleCropImageChange(cropDimensions) {
      this.setProperty('defaultImage.cropDimensions', cropDimensions);
    },
    finishCropImage() {
      this.updateUserInfo.then(() => {
        this.routerBack();
      }).catch(() => {
        NotificationService.negative('An error occurred. Please try again later.');
      });
    },
  },
};
</script>
<style lang="sass">
.crop-image
  .btn-crop-image
    text-align: center
    margin-top: 1rem
</style>
