<template>
  <q-page class="">
    <div class="layout-padding">
      <q-field
        borderless
        readonly
        hide-bottom-space
        label="Weekly Summary"
      >
        <template v-slot:prepend>
          <q-icon name="date_range" />
        </template>
        <template v-slot:control>
          <div class="full-width q-mt-md">
            <table class="job-summary-table">
              <tr>
                <th>Days</th>
                <th>Shifts</th>
                <th>Hours</th>
                <th>Pay</th>
              </tr>
              <tr>
                <td>
                  <span class="text-bold">{{getDaysTotal()}}</span>
                </td>
                <td>
                  <span class="text-bold">{{getShiftsTotal()}}</span>
                </td>
                <td>
                  <span class="text-bold">{{getHourlyTotal()}}</span>
                </td>
                <td>
                  <span class="text-bold">${{getWeeklyTotal()}}</span>
                </td>
              </tr>
            </table>
          </div>
        </template>
      </q-field>
      <q-field
        borderless
        readonly
        hide-bottom-space
        label="Job Payment"
      >
        <template v-slot:prepend>
          <q-icon name="monetization_on" />
        </template>
        <template v-slot:control>
          <div class="full-width q-mt-sm">
            <q-field
              borderless
              label="Is this price negotiable?"
              hide-bottom-space
              :error="v$.isPriceNegotiable.$error"
            >
              <template v-slot:control>
                <div class="full-width q-my-sm">
                  <q-option-group
                      type="radio"
                      v-model="isPriceNegotiable"
                      :options="negotiableTypes"
                      inline
                    />
                </div>
              </template>
            </q-field>
            <job-payment-method ref="jobPaymentMethod" :job="job" :storeModuleName="storeModuleName" :hideIcon="true" />
          </div>
        </template>
      </q-field>

      <!-- <q-field
        class="q-my-sm"
        icon="assignment"
        :error="v$.specialInstructions.$error"
        label="Describe the job"
        borderless
        no-error-icon
      >
        <template v-slot:prepend>
          <q-icon name="assignment" />
        </template>
        <template v-slot:control>
          <div class="full-width">
            <q-input
              :error="v$.specialInstructions.$error"
              error-message="required"
              v-model="specialInstructions"
              type="textarea"
              rows="4"
              :max-height="200"
              placeholder="What does your tutor need to know about this job?"
            />
          </div>
        </template>
      </q-field> -->
    </div>
  </q-page>
</template>

<script>
import useVuelidate from '@vuelidate/core';
// import { required } from '@vuelidate/validators';
import { mapState } from 'vuex';
import JobPaymentMethod from 'components/JobPaymentMethod';
import { beforeRouteEnter, beforeLeaveStep } from './multistep-helper';
import NotificationService from '../../utils/notification-service';

export default {
  name: 'PageTutoringJobStep6',
  components: { JobPaymentMethod },
  data() {
    return {
      negotiableTypes: [
        { label: 'Yes', value: true },
        { label: 'No', value: false },
      ],
    };
  },
  setup() {
    return {
      v$: useVuelidate(),
    };
  },
  validations: {
    // specialInstructions: {
    //   required,
    // },
    isPriceNegotiable: {
      required(value) {
        return value !== '';
      },
    },
  },
  computed: {
    ...mapState({
      job: (state) => state.postTutoringJob.jobDetails.data,
      meta: (state) => state.postTutoringJob.jobDetails.meta,
    }),
    storeModuleName() {
      return 'postTutoringJob.jobDetails';
    },
    specialInstructions: {
      get() {
        return this.job.specialInstructions;
      },
      set(value) {
        this.setProperty('specialInstructions', value);
      },
    },
    isPriceNegotiable: {
      get() {
        return this.job.isPriceNegotiable;
      },
      set(value) {
        this.setProperty('isPriceNegotiable', value);
      },
    },
  },
  watch: {

  },
  methods: {
    validate() {
      this.v$.$touch();
      const hasPaymentMethoodError = !this.$refs.jobPaymentMethod.validate();
      const hasError = this.v$.$error || hasPaymentMethoodError;
      if (hasError) {
        NotificationService.negative('Please review highlighted fields.');
      }

      return !hasError;
    },
    getHourlyTotal() {
      const { weeklyScheduleEntries } = this.meta;

      const weeklyTotal = weeklyScheduleEntries.reduce((totalAcc, entry) => {
        const shiftTotal = entry.shifts.reduce((shiftAcc, shift) => {
          shiftAcc += shift.hours;
          return shiftAcc;
        }, 0);
        totalAcc += shiftTotal;
        return totalAcc;
      }, 0);
      return weeklyTotal;
    },
    getDaysTotal() {
      return this.meta.weeklyScheduleEntries.length;
    },
    getShiftsTotal() {
      const { weeklyScheduleEntries } = this.meta;

      let total = 0;
      weeklyScheduleEntries.forEach((entry) => {
        total += entry.shifts.length;
      });

      return total;
    },
    getWeeklyTotal() {
      const { weeklyScheduleEntries } = this.meta;

      const weeklyTotal = weeklyScheduleEntries.reduce((totalAcc, entry) => {
        const shiftTotal = entry.shifts.reduce((shiftAcc, shift) => {
          shiftAcc += shift.price;
          return shiftAcc;
        }, 0);
        totalAcc += shiftTotal;
        return totalAcc;
      }, 0);
      return weeklyTotal;
    },
  },
  beforeRouteEnter(to, from, next) {
    beforeRouteEnter(to, from, next);
  },
  beforeRouteLeave(to, from, next) {
    beforeLeaveStep(this, to, from, next);
  },
};
</script>
