<template>
  <q-dialog v-model="opened">
    <q-layout view="Lhh lpR fff" container class="bg-white">
      <q-header>
        <q-toolbar>
          <q-toolbar-title>
            {{ index >= 0 ? 'Edit Child' : 'Add Child' }}
          </q-toolbar-title>
          <q-btn flat>
            <q-icon name="clear" v-close-popup />
          </q-btn>
        </q-toolbar>
      </q-header>

      <q-page-container>
        <div class="layout-padding">
          <q-field
            :error="v$.child.firstName.$error"
            error-message="required"
            borderless
            stack-label
          >
            <template v-slot:control>
              <q-input
                v-model="child.firstName"
                float-label="First Name"
                placeholder="First Name"
              />
            </template>
          </q-field>

          <q-field
            label="Gender"
            :error="v$.child.gender.$error"
            error-message="Select at least one option"
            borderless
            stack-label
            hide-bottom-space
          >
            <template v-slot:control>
              <q-option-group
                  type="radio"
                  v-model="child.gender"
                  :options="[
                      { label: 'Male', value: 1 },
                      { label: 'Female', value: 2 },
                  ]"
              />
            </template>
          </q-field>

          <q-field
            label="Birth Month & Year"
            helper="(Only your child's age will be visible, not their birth month)"
            borderless
            stack-label
            hide-bottom-space
          >
            <template v-slot:control>
              <month-year-picker
                :error="v$.child.dateOfBirth.$error"
                error-message="required"
                :value="child.dateOfBirth"
                :change="handleDateOfBirthChange"
              />
            </template>
          </q-field>

          <q-field
            label="Allergies"
            borderless
            stack-label
            hide-bottom-space
          >
            <template v-slot:control>
              <div class="full-width">
                <q-option-group
                    type="radio"
                    v-model="child.hasAllergies"
                    :options="[
                        { label: 'No', value: false },
                        { label: 'Yes', value: true },
                    ]"
                />
                <q-input v-if="child.hasAllergies"
                  class="q-mx-md"
                  v-model="child.allergyDetails"
                  type="textarea"
                  :max-height="100"
                  rows="3"
                  placeholder="What does your helper need to know about your child's allergies?"
                />
              </div>
            </template>
          </q-field>

          <q-field
            label="Anything else?"
            borderless
            stack-label
          >
            <template v-slot:control>
              <div class="full-width">
                <q-input
                  v-model="child.aboutMe"
                  type="textarea"
                  :max-height="100"
                  rows="3"
                  placeholder="Every child is special. Tell helpers more about your child."
                />
              </div>
            </template>
          </q-field>
        </div>
      </q-page-container>

      <q-footer class="bg-white">
        <q-toolbar>
          <q-btn color="accent" @click="handleSave">
            <q-icon name="save"/>
            Save
          </q-btn>
        </q-toolbar>
      </q-footer>
    </q-layout>
  </q-dialog>
</template>

<script>
import useVuelidate from '@vuelidate/core';
import { required } from '@vuelidate/validators';
import MonthYearPicker from 'components/MonthYearPicker';
import NotificationService from '../utils/notification-service';

export default {
  name: 'AddChildModal',
  components: { MonthYearPicker },
  emits: ['addChildSuccess'],
  data() {
    return {
      index: -1,
      child: {},
      opened: false,
    };
  },
  methods: {
    open(child, index) {
      this.v$.$reset();

      this.child = child;
      this.index = index;

      this.opened = true;
    },
    handleSave() {
      this.v$.$touch();
      if (this.v$.$error) {
        NotificationService.negative('Please review highlighted fields.');
        return;
      }
      this.$emit('addChildSuccess', { child: this.child, index: this.index });
      this.opened = false;
    },
    handleDateOfBirthChange(val) {
      this.child.dateOfBirth = val;
    },
  },
  computed: {
  },
  setup() {
    return {
      v$: useVuelidate(),
    };
  },
  validations: {
    child: {
      firstName: {
        required,
      },
      dateOfBirth: {
        required,
      },
      gender: {
        required,
      },
    },
  },
};
</script>
