import { loadStripe } from '@stripe/stripe-js';
import { env } from './env';

let stripe;

const PaymentManager = {
  async initialize() {
    if (!stripe) {
      // stripe = window.Stripe(env.stripeKey);
      stripe = await loadStripe(env.stripeKey);
    }
    return stripe;
  },
  createToken(card, extraDetails) {
    return stripe.createToken(card, extraDetails);
  },
};

export default PaymentManager;
