<template>
  <q-page class="">
    <q-list bordered separator>
      <q-item-label v-if="isHelper" header>Juggle Street Profile</q-item-label>
      <EssentialLink v-if="isApproved && isHelper" title="Profile Strength" icon="check" iconColor="" link="/account/profile-ranking"/>
      <EssentialLink v-if="isHelper" title="About Me" icon="person" iconColor="" link="/account/about-me" :note="requiresAboutMeUpdate ? 'UPDATE REQUIRED': ''"/>
      <EssentialLink v-if="isHelper" title="Profile Video" icon="videocam" iconColor="" link="/account/profile-video" :note="requiresProfileVideoUpdate ? 'UPDATE REQUIRED': ''"/>
      <EssentialLink v-if="isApproved && isHelper" title="Response Stats" icon="reply" iconColor="" link="/helpers/response-stats" />
      <EssentialLink v-if="isApproved && isHelper" title="View Profile" icon="account_box" iconColor="" :link="`/account/${isHelper ? 'helper' : 'client'}-profile/${id}`" />

      <q-item-label header>My Account</q-item-label>
      <EssentialLink v-if="isClient && isIndividual" title="About Me" icon="person" iconColor="" link="/account/about-me" :note="requiresAboutMeUpdate ? 'UPDATE REQUIRED': ''"/>
      <EssentialLink v-if="isClient && isBusiness" title="Account Holder" icon="person" iconColor="" link="/account/about-me" :note="requiresAboutMeUpdate ? 'UPDATE REQUIRED': ''"/>
      <EssentialLink v-if="isClient && isIndividual" title="My Family" icon="people" iconColor="" link="/account/my-family" />
      <EssentialLink v-if="isClient && isIndividual" title="My Children" icon="child_care" iconColor="" link="/account/my-children" />
      <EssentialLink v-if="isClient && isBusiness" title="My Business" icon="business" iconColor="" link="/account/my-business" />
      <EssentialLink v-if="isClient && hasMultipleAddresses" title="My Addresses" icon="house" iconColor="" link="/account/my-addresses" />
      <EssentialLink v-if="isClient" title="Payments" icon="paid" iconColor="" link="/account/payments" />
      <EssentialLink v-if="isClient" title="Pricing" icon="attach_money" iconColor="" link="/account/pricing" />
      <EssentialLink v-if="isApproved && isClient" title="View Profile" icon="account_box" iconColor="" :link="`/account/${isHelper ? 'helper' : 'client'}-profile/${id}`" />

      <EssentialLink v-if="isHelper && isSuperHelper" title="Super Helper Settings" icon="stars" iconColor="" link="/account/advanced-helper-settings"/>
      <EssentialLink v-if="isHelper" title="Childcare" icon="child_care" iconColor="" link="/account/my-experience" :note="requiresJobTypeUpdate ? 'UPDATE REQUIRED': ''"/>
      <EssentialLink v-if="isHelper" title="Au Pair" icon="supervised_user_circle" iconColor="" link="/account/au-pair-preferences" :note="requiresAuPairUpdate ? 'UPDATE REQUIRED': ''"/>
      <EssentialLink v-if="isHelper" title="Tutoring" icon="school" iconColor="" link="/account/tutoring-preferences" :note="requiresTutoringUpdate ? 'UPDATE REQUIRED': ''"/>
      <EssentialLink v-if="isHelper" title="Citizenship" icon="location_city" iconColor="" link="/account/vevo-details" :note="requiresVevoUpdate ? 'UPDATE REQUIRED' : ''"/>
      <EssentialLink v-if="isHelper" title="Certificates" icon="feed" iconColor="" link="/account/my-certificates" :note="requiresCertificateUpdate ? 'UPDATE REQUIRED' : ''"/>
      <EssentialLink v-if="isHelper" title="References" icon="grading" iconColor="" link="/account/personal-references" :note="requiresReferencesUpdate ? 'UPDATE REQUIRED' : ''"/>
      <EssentialLink v-if="isHelper" title="Driving & Language" icon="directions_car" iconColor="" link="/account/skills-and-languages" />

      <q-separator spaced />
      <q-item-label header>Settings</q-item-label>
      <EssentialLink v-if="isHelper" title="My Addresses" icon="house" iconColor="" link="/account/my-addresses" />
      <EssentialLink v-if="isHelper && !hasSubscription" title="Membership" icon="card_membership" iconColor="" link="/account/helper-membership" :note="requiredHelperMembershipUpdate ? 'UPDATE REQUIRED': ''"/>
      <EssentialLink v-if="isHelper && hasSubscription" title="Membership" icon="card_membership" iconColor="" link="/account/payments" />
      <EssentialLink v-if="isHelper" title="Payment Methods" icon="monetization_on" iconColor="" link="/account/payment-methods" :note="requiresPaymentMethodsUpdate ? 'UPDATE REQUIRED' : ''" />
      <EssentialLink v-if="isHelper" title="Privacy settings" icon="visibility" iconColor="" link="/account/profile-visibility" :note="requiresVevoUpdate ? 'UPDATE REQUIRED' : ''"/>
      <EssentialLink title="General Settings" icon="settings" iconColor="" link="/account/settings" />
      <EssentialLink v-if="enableNotificationPage" title="Notifications" icon="notifications" iconColor="" link="/account/notifications" />
      <EssentialLink v-if="canRate" title="Rate Juggle St App" icon="star" iconColor="" @click="handleRateNow" />
      <EssentialLink :title="'App Version: ' + this.appVersion" icon="info" iconColor=""/>
      <EssentialLink :title="'App Language: ' + this.appCulture" icon="language" iconColor=""/>
      <EssentialLink v-if="enableAppUpdates && isApp" title="Check for Updates" icon="system_update" iconColor="" @click="handleCheckForUpdates" />
      <EssentialLink title="Logout" icon="logout" iconColor="" @click="handleLogout" />
    </q-list>
  </q-page>
</template>

<script>
import EssentialLink from 'components/EssentialLink';
import { mapState } from 'vuex';
import c from '../utils/constants';
import appUpdateManager from '../utils/app-update-manager';
import appRatingManager from '../utils/app-rating-manager';
import { enableAppFeatures } from '../utils/helpers';

export default {
  name: 'PageAccountSummary',
  components: { EssentialLink },
  data() {
    return {

    };
  },
  computed: {
    ...mapState({
      id: (state) => state.userInfo.data.id,
      userInfo: (state) => state.userInfo.data,
      appMode: (state) => {
        const { appType } = state.appInfo;
        return appType === c.applicationType.webApp ? 'App Mode' : 'Site Mode';
      },
      enableNotificationPage() {
        return this.userInfo.appSettings.enableAppNotificationChecks && this.isApp && this.isApproved;
      },
      frameworkVersion() {
        return this.$q.version;
      },
      browserDetails: (state) => {
        const browserName = state.appInfo.userAgent.browser.name;
        const browserVersion = state.appInfo.userAgent.browser.version;
        return `${browserName} ${browserVersion}`;
      },
      isSuperHelper() {
        return this.userInfo.provider.isSuperProvider;
      },
    }),
    hasMultipleAddresses() {
      return this.isIndividual || this.userInfo.client.clientCategory === c.clientCategory.businessMultiSite;
    },
    hasSubscription() {
      return this.userInfo.membershipType === c.membershipType.subscription;
    },
    canRate() {
      return this.isApp && appRatingManager.canRateNow();
    },
    requiresPaymentMethodsUpdate() {
      return this.isHelper && this.isApproved
        && (this.userInfo.paymentInfo.interestedInCashPayments === null
        || this.userInfo.paymentInfo.interestedInBankTransfers === null);
    },
    requiresAboutMeUpdate() {
      return this.isHelper && this.isApproved && (!this.userInfo.aboutMe || this.userInfo.aboutMe.length < 100);
    },
    requiresCertificateUpdate() {
      return this.isHelper && this.isApproved
        && (this.userInfo.certificates.length === 0 || this.userInfo.certificates[0].certificateStatus === c.certificateStatus.invalid || this.userInfo.certificates[0].certificateStatus === c.certificateStatus.expired)
        && this.userInfo.country === 'au' && this.userInfo.ageInYears >= 18;
    },
    requiresVevoUpdate() {
      return this.isHelper && this.isApproved && !this.userInfo.nationality;
    },
    requiresJobTypeUpdate() {
      return this.userInfo.interestedInJobTypes === 0;
    },
    requiresAuPairUpdate() {
      // const noVideo = this.userInfo.medias.length === 0;
      return this.isHelper && this.isApproved && (this.userInfo.interestedInAuPairJobs || (this.userInfo.interestedInJobTypes & c.jobType.auPair) === c.jobType.auPair) && (this.userInfo.provider.auPairCategory === 0);
    },
    requiresReferencesUpdate() {
      let nonJsRefs = 0;

      this.userInfo.vouches.forEach((v) => {
        if (v.referenceType !== c.vouchReferenceType.juggleStreet) {
          nonJsRefs++;
        }
      });

      return this.isHelper && this.isApproved && (this.userInfo.interestedInAuPairJobs || (this.userInfo.interestedInJobTypes & c.jobType.auPair) === c.jobType.auPair) && nonJsRefs < 1;
    },
    requiredHelperMembershipUpdate() {
      return (this.userInfo.ageInYears >= 18 && !this.hasSubscription && this.isApproved);
    },
    requiresProfileVideoUpdate() {
      return false;
      // const interestedInOnlineTutoring = this.userInfo.provider.interestedInOnlineTutoring &&
      // (
      //   (this.userInfo.interestedInJobTypes & c.jobType.primarySchoolTutoring) === c.jobType.primarySchoolTutoring
      //   ||
      //   (this.userInfo.interestedInJobTypes & c.jobType.highSchoolTutoring) === c.jobType.highSchoolTutoring
      // );

      // const interestedInAuPairJobs = this.userInfo.interestedInJobTypes & c.jobType.auPair;

      // return noVideo && (interestedInOnlineTutoring || interestedInAuPairJobs);
    },
    requiresTutoringUpdate() {
      return this.isHelper && this.isApproved
        && (this.userInfo.interestedInTutoringJobs
        || (this.userInfo.interestedInJobTypes & c.jobType.primarySchoolTutoring) === c.jobType.primarySchoolTutoring
        || (this.userInfo.interestedInJobTypes & c.jobType.highSchoolTutoring) === c.jobType.highSchoolTutoring)
        && (this.userInfo.provider.tutoringCategory === 0
        || this.userInfo.provider.interestedInFaceToFaceTutoring === null
        || this.userInfo.provider.interestedInOnlineTutoring === null);
    },
    enableAppUpdates() {
      return this.userInfo.appSettings.enableAppUpdates && enableAppFeatures(this.$store.state.userInfo);
    },
  },
  methods: {
    async handleCheckForUpdates() {
      await appUpdateManager.checkForCodeUpdates();
    },
    handleRateNow() {
      appRatingManager.promptForAppStoreRating();
    },
  },
};
</script>
