<template>
  <q-page class="">
    <q-card flat v-if="job.id && job.id > 0">
      <q-card-section>
        <div>
          <img class="round-user-image" :src="job.ownerImageSrc"/> {{job.ownerPublicName}}
          <br/>
          <span>{{jobTypeText}}</span>
          <br/>
          <span>{{jobDate}}</span>
        </div>
        <q-field :error="v$.category1.$error" error-message="required" borderless hide-bottom-space>
          <template v-slot:control>
            <div class="full-width q-my-sm">
              <p class="no-margin">
                Were you told everything you needed to know (children and duties)?
              </p>
              <q-rating size="2rem" v-model="category1" :max="5" color="accent" />
            </div>
          </template>
        </q-field>
        <q-field :error="v$.category2.$error" error-message="required" borderless hide-bottom-space>
          <template v-slot:control>
            <div class="full-width q-my-sm">
              <p class="no-margin">
                End of Job – punctuality and communication
              </p>
              <q-rating size="2rem" v-model="category2" :max="5" color="accent" />
            </div>
          </template>
        </q-field>
        <q-field :error="v$.category3.$error" error-message="required" borderless hide-bottom-space>
          <template v-slot:control>
            <div class="full-width q-my-sm">
              <p class="no-margin">
                How happy are you with the amount you were paid?
              </p>
              <q-rating size="2rem" v-model="category3" :max="5" color="accent" />
            </div>
          </template>
        </q-field>
        <q-field :error="v$.category4.$error" error-message="required" borderless hide-bottom-space>
          <template v-slot:control>
            <div class="full-width q-my-sm">
              <p class="no-margin">
                How keen would you be to apply for another job?
              </p>
              <q-rating size="2rem" v-model="category4" :max="5" color="accent" />
            </div>
          </template>
        </q-field>
        <div class="full-width q-my-sm text-center">
          <q-btn color="accent" @click="handleSubmitRating" label="Submit" />
        </div>
      </q-card-section>
    </q-card>
  </q-page>
</template>

<script>
import { mapState, mapActions } from 'vuex';
import useVuelidate from '@vuelidate/core';
import { required, minValue, numeric } from '@vuelidate/validators';
import { formatJobDate, jobTypeText } from '../../utils/helpers';
import NotificationService from '../../utils/notification-service';
import store from '../../store';

export default {
  name: 'PageHelpersRateFamily',
  data() {
    return {

    };
  },
  setup() {
    return {
      v$: useVuelidate(),
    };
  },
  validations: {
    category1: {
      required,
      minValue: minValue(1),
      numeric,
    },
    category2: {
      required,
      minValue: minValue(1),
      numeric,
    },
    category3: {
      required,
      minValue: minValue(1),
      numeric,
    },
    category4: {
      required,
      minValue: minValue(1),
      numeric,
    },
  },
  computed: {
    ...mapState({
      rating: (state) => state.helpers.pages.rateFamily.data,
      job: (state) => state.helpers.pages.rateFamily.meta.job,
    }),
    storeModuleName() {
      return 'helpers.pages.rateFamily';
    },
    jobDate() {
      return formatJobDate(this.job);
    },
    jobTypeText() {
      return jobTypeText(this.job.jobType, this.clientType);
    },
    category1: {
      get() {
        return this.rating.category1;
      },
      set(value) {
        this.setProperty('category1', value);
      },
    },
    category2: {
      get() {
        return this.rating.category2;
      },
      set(value) {
        this.setProperty('category2', value);
      },
    },
    category3: {
      get() {
        return this.rating.category3;
      },
      set(value) {
        this.setProperty('category3', value);
      },
    },
    category4: {
      get() {
        return this.rating.category4;
      },
      set(value) {
        this.setProperty('category4', value);
      },
    },
  },
  methods: {
    ...mapActions({
      postFamilyRating: 'helpers/postFamilyRating',
    }),
    handleSubmitRating() {
      this.v$.$touch();
      const hasError = this.v$.$error;
      if (hasError) {
        NotificationService.negative('Please review highlighted fields.');
        return;
      }

      this.postFamilyRating(this.$router);
    },
  },
  async beforeRouteEnter(to, from, next) {
    store.dispatch('resetStoreStructure', { propPath: 'helpers.pages.rateFamily' });

    next(async (vm) => {
      const { jobId } = vm.$route.params;
      vm.loader.show();
      await vm.$store.dispatch('helpers/fetchJobForRating', { id: jobId });
      vm.loader.hide();
    });
  },
};
</script>
