<template>
  <q-page class="">
    <div class="layout-padding">
      <div class="group">
        <q-field
          label="Would you like to work as a Tutor?"
          stack-label
          borderless
        >
          <template v-slot:prepend>
            <q-icon name="school" />
          </template>
          <template v-slot:control>
            <q-radio class="q-mx-sm" v-model="interestedInTutoringJobs" :val="true" label="Yes" />
            <q-radio class="q-mx-sm" v-model="interestedInTutoringJobs" :val="false" label="No" />
          </template>
        </q-field>
      </div>
      <div v-if="interestedInTutoringJobs">
        <helper-tutoring-job-methods ref="tutoringMethods" :isMandatory="true" />
      </div>
      <div v-if="interestedInTutoringJobs && suppliedTutoringMethods">
        <helper-tutoring-jobs ref="tutoringJobs" :isMandatory="true" />
        <helper-tutoring-category ref="tutoringCategory"/>
        <helper-tutoring-primary-school-preferences ref="primarySchoolPrefs" v-if="primarySchoolTutoring"/>
        <helper-tutoring-high-school-preferences ref="highSchoolPrefs" v-if="highSchoolTutoring"/>
      </div>
    </div>
  </q-page>
</template>

<script>
import { mapState } from 'vuex';
import HelperTutoringJobs from 'components/HelperTutoringJobs';
import HelperTutoringJobMethods from 'components/HelperTutoringJobMethods';
import HelperTutoringCategory from 'components/HelperTutoringCategory';
import HelperTutoringPrimarySchoolPreferences from 'components/HelperTutoringPrimarySchoolPreferences';
import HelperTutoringHighSchoolPreferences from 'components/HelperTutoringHighSchoolPreferences';
import NotificationService from '../../utils/notification-service';
import { scrollToElement, unregisterJobType } from '../../utils/helpers';
import c from '../../utils/constants';

export default {
  name: 'PageAccountTutoringPreferences',
  components: {
    HelperTutoringJobs,
    HelperTutoringCategory,
    HelperTutoringJobMethods,
    HelperTutoringPrimarySchoolPreferences,
    HelperTutoringHighSchoolPreferences,
  },
  data() {
    const userInfo = this.$store.state.userInfo.data;
    const { interestedInJobTypes } = userInfo;

    const interestedInTutoringJobs = (interestedInJobTypes & c.jobType.primarySchoolTutoring) > 0 || (interestedInJobTypes & c.jobType.highSchoolTutoring) > 0;
    return {
      acceptedPopup: false,
      interestedInTutoringJobs,
    };
  },
  methods: {},
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
    }),
    storeModuleName() {
      return 'userInfo';
    },
    suppliedTutoringMethods() {
      return this.userInfo.provider.interestedInFaceToFaceTutoring || this.userInfo.provider.interestedInOnlineTutoring;
    },
    primarySchoolTutoring() {
      return (this.userInfo.interestedInJobTypes & c.jobType.primarySchoolTutoring) === c.jobType.primarySchoolTutoring;
    },
    highSchoolTutoring() {
      return (this.userInfo.interestedInJobTypes & c.jobType.highSchoolTutoring) === c.jobType.highSchoolTutoring;
    },
  },
  watch: {
    interestedInTutoringJobs(value) {
      if (!value) {
        let { interestedInJobTypes } = this.userInfo;

        interestedInJobTypes = unregisterJobType(interestedInJobTypes, c.jobType.primarySchoolTutoring);
        interestedInJobTypes = unregisterJobType(interestedInJobTypes, c.jobType.highSchoolTutoring);

        if (this.userInfo.interestedInJobTypes !== interestedInJobTypes) {
          this.setProperty('interestedInJobTypes', interestedInJobTypes);
        }
      }
    },
  },
  beforeRouteLeave(to, from, next) {
    if (this.interestedInTutoringJobs) {
      if (!this.$refs.tutoringMethods.validate(true)) {
        scrollToElement(this.$refs.tutoringMethods.$el);
        next(false);
        return;
      }

      if (!this.$refs.tutoringJobs.validate(true)) {
        scrollToElement(this.$refs.tutoringJobs.$el);
        next(false);
        return;
      }

      if (!this.$refs.tutoringCategory.validate(true)) {
        scrollToElement(this.$refs.tutoringCategory.$el);
        next(false);
        return;
      }

      if (this.$refs.primarySchoolPrefs && !this.$refs.primarySchoolPrefs.validate(true)) {
        scrollToElement(this.$refs.primarySchoolPrefs.$el);
        next(false);
        return;
      }

      if (this.$refs.highSchoolPrefs && !this.$refs.highSchoolPrefs.validate(true)) {
        scrollToElement(this.$refs.highSchoolPrefs.$el);
        next(false);
        return;
      }
    }

    this.$store.dispatch('updateUserInfo').then(() => {
      // const interestedInOnlineTutoring = this.userInfo.provider.interestedInOnlineTutoring
      // && (
      //   (this.userInfo.interestedInJobTypes & c.jobType.primarySchoolTutoring) === c.jobType.primarySchoolTutoring
      //   || (this.userInfo.interestedInJobTypes & c.jobType.highSchoolTutoring) === c.jobType.highSchoolTutoring
      // );
      // const displayPopup = interestedInOnlineTutoring && this.userInfo.medias.length === 0 && !this.acceptedPopup;
      const displayPopup = false;

      if (displayPopup) {
        this.$q.dialog({
          title: 'Profile Video',
          message: 'You need to upload a short personal Profile Video to be an Online Tutor. Your Online Tutoring account will not be activated until you upload your Profile Video. You will not receive Online Tutoring job invitations until you upload a Profile Video.',
          ok: {
            label: 'Ok, Got It',
            color: 'primary',
            outline: true,
          },
        }).onOk(() => {
          this.acceptedPopup = true;
          next({ path: '/account/profile-video', replace: true });
        });

        next(false);
      } else {
        next();
      }
    }).catch((error) => {
      NotificationService.negative(error.message);
      next(false);
    });
  },
};
</script>
