<template>
  <q-page class="post-tutoring-job-step-2">
    <div class="layout-padding">
      <q-field
        borderless
        readonly
        label="Select School Subject"
        :error="v$.isValid.$error"
        ref="schoolTree">
        <template v-slot:prepend>
          <q-icon name="school" />
        </template>
        <template v-slot:control>
          <div class="full-width q-mt-sm">
            <q-radio class="" v-model="subjectType" val="all" label="All" />
            <br/>
            <q-radio class="" v-model="subjectType" val="specific" label="Select Subject" />
            <div style="margin-left: 50px">
              <q-input
                v-if="enableSearch && subjectType === 'specific'"
                v-model="subjectFilter"
                label="Search for subject..."
                clearable
                class="q-mb-sm"
              />
              <label v-if="schoolSubjects.length > 0">
                Selected Subject: {{getSchoolSubjectText}}
              </label>
              <q-tree
                :nodes="schoolSubjects"
                :filter="subjectFilter"
                v-model:ticked="selectedSubjects"
                v-model:expanded="expandedSubjects"
                color="primary"
                node-key="optionId"
                tick-strategy="leaf"
              />
            </div>
          </div>
        </template>
      </q-field>
    </div>
  </q-page>
</template>

<script>
import { mapState } from 'vuex';
import useVuelidate from '@vuelidate/core';
import { sameAs } from '@vuelidate/validators';
import { beforeRouteEnter, beforeLeaveStep } from './multistep-helper';
import NotificationService from '../../utils/notification-service';
import c from '../../utils/constants';
import {
  processOptionsTree, getOption, getOptionParent, getExpandedOptions,
} from '../../utils/helpers';
import * as mutationTypes from '../../store/mutation-types';

export default {
  name: 'PageTutoringJobStep2',
  data() {
    const job = this.$store.state.postTutoringJob.jobDetails.data;
    const userInfo = this.$store.state.userInfo.data;
    const subjects = job.jobType === c.jobType.primarySchoolTutoring ? userInfo.client.primarySchoolSubjects : userInfo.client.highSchoolSubjects;
    const expandedSubjects = getExpandedOptions(subjects);

    const academicTree = job.jobType === c.jobType.primarySchoolTutoring ? 4015 : 4019;
    if (expandedSubjects.indexOf(academicTree) === -1) {
      expandedSubjects.push(academicTree);
    }

    return {
      subjectType: job.jobSettings.schoolSubjects.length === 0 ? '' : 'specific',
      subjectFilter: '',
      expandedSubjects,
    };
  },
  setup() {
    return {
      v$: useVuelidate(),
    };
  },
  validations: {
    isValid: {
      sameAsRawValue: sameAs(true),
    },
  },
  computed: {
    ...mapState({
      job: (state) => state.postTutoringJob.jobDetails.data,
      userInfo: (state) => state.userInfo.data,
    }),
    storeModuleName() {
      return 'postTutoringJob.jobDetails';
    },
    enableSearch() {
      return this.job.jobType === c.jobType.highSchoolTutoring;
    },
    filterKey() {
      if (this.job.jobDeliveryMethod === c.jobDeliveryMethod.online) {
        return 'postOnlineTutoringJob';
      }

      return 'postHomeTutoringJob';
    },
    selectedSubjects: {
      get() {
        return this.job.jobSettings.schoolSubjects;
      },
      set(value) {
        if (value.length > 1) {
          value.splice(0, 1);
        }

        if (value.length > 0) {
          this.subjectType = 'specific';
        } else {
          this.subjectType = 'all';
        }

        this.setProperty('jobSettings.schoolSubjects', value);
      },
    },
    ageGroupText() {
      return this.job.jobType === c.jobType.primarySchoolTutoring ? 'Primary School' : 'High School';
    },
    getSchoolSubjectText() {
      if (this.job.jobSettings.schoolSubjects.length === 0) return 'None selected';

      const schoolSubjects = this.job.jobType === c.jobType.primarySchoolTutoring ? this.userInfo.client.primarySchoolSubjects : this.userInfo.client.highSchoolSubjects;
      const option = getOption(this.job.jobSettings.schoolSubjects[0], schoolSubjects);

      return option.text;
    },
    schoolSubjects() {
      const subjects = this.job.jobType === c.jobType.primarySchoolTutoring ? this.userInfo.client.primarySchoolSubjects : this.userInfo.client.highSchoolSubjects;

      const tree = processOptionsTree(subjects);
      return tree;
    },
    isValid() {
      return (this.subjectType === 'all' || (this.subjectType === 'specific'
        && this.selectedSubjects.length > 0));
    },
  },
  methods: {
    validate() {
      this.v$.$touch();
      const hasError = this.v$.$error;

      if (hasError) {
        NotificationService.negative('Please select school subject.');
      }

      return !hasError;
    },
  },
  watch: {
    subjectType(val) {
      if (val === 'all') {
        this.selectedSubjects = [];
      }
    },
  },
  beforeRouteEnter(to, from, next) {
    beforeRouteEnter(to, from, next);
  },
  beforeRouteLeave(to, from, next) {
    const toStep = parseInt(to.path.split('step/')[1], 10) || 1;
    if (toStep === 3) {
      // Add secondary subject.
      let secondarySubjects = [];
      if (this.selectedSubjects.length > 0 && this.job.jobType === c.jobType.highSchoolTutoring) {
        const subjectList = this.userInfo.client.highSchoolSubjects;
        const subject = getOption(this.selectedSubjects[0], subjectList);

        if (subject !== null && subject.children === null) {
          const subjectParent = getOptionParent(this.selectedSubjects[0], subjectList);
          secondarySubjects = [subjectParent.optionId];
        }
      }

      const filterPayload = {
        filterType: this.filterKey,
        filterKey: 'schoolSubjects',
        value: this.selectedSubjects,
      };
      this.$store.commit(`filters/${mutationTypes.UPDATE_FILTER_OPTION}`, filterPayload);

      console.log(secondarySubjects);
      // filterPayload = {
      //   filterType: this.filterKey,
      //   filterKey: 'secondarySubjects',
      //   value: secondarySubjects,
      // };
      // this.$store.commit(`filters/${mutationTypes.UPDATE_FILTER_OPTION}`, filterPayload);

      // filterPayload = {
      //   filterKey: this.filterKey,
      // };
      // this.$store.dispatch('filters/applyFilter', filterPayload);

      this.$store.dispatch('postTutoringJob/resetApplicants');
      this.$store.dispatch('filters/resetFilters');
    }
    beforeLeaveStep(this, to, from, next);
  },
};
</script>
<style lang="sass">
.post-tutoring-job-step-2
  .duration-to-start
    width: 10rem
</style>
