<template>
  <q-page class="">
    <div class="layout-padding">
      <q-field
        borderless
        readonly
        label="Food & Accomodation">
          <template v-slot:prepend>
            <q-icon name="home" />
          </template>
          <template v-slot:control>
            <div class="full-width">
              <q-field
                borderless
                hide-bottom-space
                readonly
                :error="v$.mealOptions.$error"
                label="Meals provided">
                  <template v-slot:control>
                    <div class="full-width q-mt-sm">
                      <q-option-group
                        type="checkbox"
                        v-model="mealOptions"
                        :options="allMeals"
                      />
                    </div>
                </template>
              </q-field>

              <q-field
                borderless
                readonly
                hide-bottom-space
                :error="v$.bedroomOption.$error"
                label="Bedroom">
                  <template v-slot:control>
                    <div class="full-width q-mt-sm">
                      <q-option-group
                        type="radio"
                        v-model="bedroomOption"
                        :options="bedroomOptions"
                      />
                      <q-option-group
                        type="checkbox"
                        v-model="facilityOptions"
                        :options="facilities"
                      />
                    </div>
                </template>
              </q-field>

              <q-field
                borderless
                readonly
                hide-bottom-space
                :error="v$.bathroomOption.$error"
                label="Bathroom">
                  <template v-slot:control>
                    <div class="full-width q-mt-sm">
                      <q-option-group
                        type="radio"
                        v-model="bathroomOption"
                        :options="bathroomOptions"
                      />
                    </div>
                </template>
              </q-field>
            </div>
        </template>
      </q-field>

      <q-field
        class="q-my-sm"
        borderless
        :error="v$.specialInstructions.$error"
        label="Describe the job"
        no-error-icon
        v-if="job.managedBy === managedBy.user">
        <template v-slot:prepend>
          <q-icon name="assignment" />
        </template>
        <template v-slot:control>
          <div class="full-width">
            <q-input
            :error="v$.specialInstructions.$error"
            error-message="required"
            v-model="specialInstructions"
            type="textarea"
            :rows="4"
            :max-height="200"
            placeholder="What does your Au Pair need to know about this job and working for your family?" />
          </div>
        </template>
      </q-field>
    </div>
  </q-page>
</template>

<script>
import useVuelidate from '@vuelidate/core';
import { required, requiredIf } from '@vuelidate/validators';
import { mapState } from 'vuex';
import { beforeRouteEnter, beforeLeaveStep } from './multistep-helper';
import { getHoursPerWeekText } from '../../utils/helpers';
import NotificationService from '../../utils/notification-service';
import c from '../../utils/constants';

export default {
  name: 'PageAuPairJobStep3',
  data() {
    return {
      allMeals: c.allMeals,
      facilities: c.facilities,
      bedroomOptions: c.bedroomOptions,
      bathroomOptions: c.bathroomOptions,
      managedBy: c.managedBy,
    };
  },
  setup() {
    return {
      v$: useVuelidate(),
    };
  },
  validations() {
    return {
      mealOptions: {
        required,
      },
      bedroomOption: {
        required,
      },
      bathroomOption: {
        required,
      },
      specialInstructions: {
        requiredIf: requiredIf(this.job.managedBy === c.managedBy.user),
      },
    };
  },
  computed: {
    ...mapState({
      job: (state) => state.postAuPairJob.jobDetails.data,
    }),
    storeModuleName() {
      return 'postAuPairJob.jobDetails';
    },
    bedroomOption: {
      get() {
        return this.job.jobSettings.bedroomOption;
      },
      set(value) {
        this.setProperty('jobSettings.bedroomOption', value);
      },
    },
    bathroomOption: {
      get() {
        return this.job.jobSettings.bathroomOption;
      },
      set(value) {
        this.setProperty('jobSettings.bathroomOption', value);
      },
    },
    mealOptions: {
      get() {
        return this.job.jobSettings.mealOptions;
      },
      set(value) {
        this.setProperty('jobSettings.mealOptions', value);
      },
    },
    facilityOptions: {
      get() {
        return this.job.jobSettings.facilityOptions;
      },
      set(value) {
        this.setProperty('jobSettings.facilityOptions', value);
      },
    },
    specialInstructions: {
      get() {
        return this.job.specialInstructions;
      },
      set(value) {
        this.setProperty('specialInstructions', value);
      },
    },
  },
  methods: {
    validate() {
      this.v$.$touch();
      const hasError = this.v$.$error;
      if (hasError) {
        NotificationService.negative('Please review highlighted fields.');
      }

      return !hasError;
    },
    getDayLabel(day) {
      return getHoursPerWeekText(day);
    },
  },
  beforeRouteEnter(to, from, next) {
    beforeRouteEnter(to, from, next);
  },
  beforeRouteLeave(to, from, next) {
    beforeLeaveStep(this, to, from, next);
  },
};
</script>
