<template>
  <q-field
    helper="Choose one"
    :error="v$.lookingFor.$error"
    error-message="Select one option"
    label-slot
    stack-label
    readonly
    hide-bottom-space
    borderless
  >
    <template v-slot:label>
      <h5 class="text-primary text-wrap no-margin">
        What are you looking for on Juggle Street?
      </h5>
    </template>
    <template v-slot:control>
      <div class="full-width q-mt-md">
        <q-radio v-model="lookingFor" label="I am a Parent looking for Helpers" :val="types.lookingFor.individualClient" checked-icon="check" unchecked-icon="check_box_outline_blank" />
        <q-radio v-if="enableBusinessAccounts" v-model="lookingFor" label="I am a Business looking for Helpers" :val="types.lookingFor.businessClient" checked-icon="check" unchecked-icon="check_box_outline_blank" />
        <q-radio v-model="lookingFor" label="I am a Helper (babysitter, nanny, au pair, tutor) looking for work" :val="types.lookingFor.individualProvider" checked-icon="check" unchecked-icon="check_box_outline_blank" />
      </div>
    </template>
  </q-field>
  <q-field
    stack-label
    borderless
    hide-bottom-space
    label-slot
    :error="v$.clientCategory.$error"
    v-if="lookingFor === types.lookingFor.businessClient"
  >
    <template v-slot:label>
      <h5 class="text-primary text-wrap no-margin">
        Juggle St Pro - Childcare
      </h5>
    </template>
    <template v-slot:control>
      <div class="full-width">
        <q-radio v-model="clientCategory" label="Single Site Business" class="q-mr-sm" :val="types.clientCategory.businessSingleSite" checked-icon="check" unchecked-icon="check_box_outline_blank"/>
        <div class="text-faded" style="margin-left: 40px">
          Hiring staff to work in your premises
        </div>
        <q-radio v-model="clientCategory" label="Agency Business" class="q-mr-sm q-mt-sm" :val="types.clientCategory.businessMultiSite" checked-icon="check" unchecked-icon="check_box_outline_blank"/>
        <div class="text-faded" style="margin-left: 40px">
          Hiring staff to work at your clients premises
        </div>
      </div>
    </template>
  </q-field>
  <q-field
    helper="Choose one"
    :error-message="signupCountry === 'au' ? 'Enter your suburb' : 'Enter your address'"
    label-slot
    stack-label
    readonly
    hide-bottom-space
    borderless
    v-if="lookingFor !== types.lookingFor.unspecified"
  >
    <template v-slot:label>
      <h5 v-if="lookingFor === types.lookingFor.businessClient" class="text-primary text-wrap no-margin">
        Primary Business Location
      </h5>
      <h5 v-else class="text-primary text-wrap no-margin">
        Where do you live?
      </h5>
    </template>
    <template v-slot:control>
      <div class="full-width q-mt-md">
        <q-select
          class="q-mb-sm"
          v-model="signupCountry"
          :options="types.countryOptions"
          option-value="value"
          option-label="label"
          behavior="menu"
          emit-value
          map-options
          label="Country"
        />

        <q-select
          v-if="signupCountry === 'au'"
          v-model="completeAddress"
          label="Enter your Suburb or Postcode"
          hide-bottom-space
          :error="v$.completeAddress.$error"
          placeholder=""
          autocomplete="none"
          autocapitalize="off"
          autocorrect="off"
          option-value="value"
          option-label="label"
          use-input
          hide-selected
          fill-input
          :options="matchingSuburbs"
          @filter="suburbSearch"
          :min-characters="3"
          :max-results="10"
          spellcheck="false">
        </q-select>

        <div class="q-my-sm">
          <small v-if="signupCountry === 'au' && suburbHealth">
            {{suburbHealth}}
          </small>
        </div>

        <q-select
          v-if="signupCountry === 'nz'"
          v-model="completeAddress"
          :error="v$.completeAddress.$error"
          label="Start typing your address..."
          hide-bottom-space
          placeholder=""
          autocomplete="none"
          autocapitalize="off"
          autocorrect="off"
          option-value="value"
          option-label="label"
          use-input
          hide-selected
          fill-input
          :options="matchingAddresses"
          @filter="addressSearch"
          :min-characters="3"
          :max-results="10"
          spellcheck="false">
        </q-select>
      </div>
    </template>
  </q-field>
</template>

<script>
import { mapState, mapActions } from 'vuex';
import useVuelidate from '@vuelidate/core';
import { minValue, required } from '@vuelidate/validators';
import c from '../../utils/constants';
import errorHandler from '../../utils/error-handler';

export default {
  name: 'SignupStep1',
  props: ['currentStep'],
  data() {
    return {
      types: {
        lookingFor: c.lookingFor,
        countryOptions: c.countryOptions,
        clientCategory: c.clientCategory,
      },
      showAddressDetails: false,
      matchingSuburbs: [],
      matchingAddresses: [],
      suburbHealth: '',
    };
  },
  setup() {
    return {
      v$: useVuelidate(),
    };
  },
  validations() {
    return {
      completeAddress: {
        required,
      },
      lookingFor: {
        minValue: minValue(1),
      },
      clientCategory: {
        required() {
          if (this.lookingFor !== c.lookingFor.businessClient) return true;

          return this.clientCategory > 1;
        },
      },
      state: {
        required(value) {
          if (this.signupCountry === 'nz') return true;

          const valid = value !== 'tas' && value !== 'nt';
          console.log(valid);
          return valid;
        },
      },
    };
  },
  computed: {
    ...mapState({
      accountInfo: (state) => state.signup.userDetails.data,
      meta: (state) => state.signup.userDetails.meta,
    }),
    storeModuleName() {
      return 'signup.userDetails';
    },
    lookingFor: {
      get() {
        return this.meta.lookingFor;
      },
      set(value) {
        this.setMetaProperty('lookingFor', value);

        if (value === c.lookingFor.individualClient) {
          this.accountType = c.accountType.client;
          this.clientType = c.clientType.individual;
          this.providerType = c.providerType.unspecified;
          this.clientCategory = c.clientCategory.family;
        } else if (value === c.lookingFor.individualProvider) {
          this.accountType = c.accountType.provider;
          this.clientType = c.clientType.unspecified;
          this.providerType = c.providerType.individual;
          this.clientCategory = c.clientCategory.unspecified;
        } else if (value === c.lookingFor.businessClient) {
          this.accountType = c.accountType.client;
          this.clientType = c.clientType.business;
          this.providerType = c.providerType.unspecified;
          this.clientCategory = c.clientCategory.unspecified;
        } else if (value === c.lookingFor.businessProvider) {
          this.accountType = c.accountType.provider;
          this.clientType = c.clientType.unspecified;
          this.providerType = c.providerType.business;
          this.clientCategory = c.clientCategory.unspecified;
        }
      },
    },
    completeAddress: {
      get() {
        return this.meta.completeAddress;
      },
      set(value) {
        this.setMetaProperty('completeAddress', value);
      },
    },
    clientCategory: {
      get() {
        return this.accountInfo.clientCategory;
      },
      set(value) {
        this.setProperty('clientCategory', value);
      },
    },
    addressLine1: {
      get() {
        return this.accountInfo.address.addressLine1;
      },
      set(value) {
        this.setProperty('address.addressLine1', value);
      },
    },
    addressLine2: {
      get() {
        return this.accountInfo.address.addressLine2;
      },
      set(value) {
        this.setProperty('address.addressLine2', value);
      },
    },
    suburb: {
      get() {
        return this.accountInfo.address.suburb;
      },
      set(value) {
        if (value && value.meta) {
          this.setProperty('address.suburb', value.meta.name);
        } else {
          this.setProperty('address.suburb', value);
        }
      },
    },
    postCode: {
      get() {
        return this.accountInfo.address.postCode;
      },
      set(value) {
        this.setProperty('address.postCode', value);
      },
    },
    state: {
      get() {
        return this.accountInfo.address.state;
      },
      set(value) {
        this.setProperty('address.state', value);
      },
    },
    signupCountry: {
      get() {
        return this.accountInfo.country;
      },
      set(value) {
        this.setProperty('country', value);
        this.completeAddress = '';
      },
    },
    accountType: {
      get() {
        return this.accountInfo.accountType;
      },
      set(value) {
        this.setProperty('accountType', value);
      },
    },
    clientType: {
      get() {
        return this.accountInfo.clientType;
      },
      set(value) {
        this.setProperty('clientType', value);
      },
    },
    providerType: {
      get() {
        return this.accountInfo.providerType;
      },
      set(value) {
        this.setProperty('providerType', value);
      },
    },
  },
  methods: {
    ...mapActions({
      suburbSearchService: 'suburbSearch',
      addressSearchService: 'addressSearch',
      geocodeAddresService: 'geocodeAddress',
    }),
    validate() {
      this.v$.$touch();
      const hasError = this.v$.$error;
      return hasError;
    },
    async suburbSearch(term, update, abort) {
      if (term.length < 3) {
        abort();
        return;
      }

      let results = [];
      try {
        const suburbs = await this.suburbSearchService({ term, country: this.signupCountry });
        results = suburbs.map((suburb) => ({
          label: `${suburb.name}, ${suburb.state.replace('-', ' ').toUpperCase()}`,
          value: suburb.name,
          meta: suburb,
        }));
      } catch (err) {
        errorHandler.captureException(err);
      }

      update(() => {
        this.matchingSuburbs = results;
      });
    },
    async addressSearch(term, update, abort) {
      if (term.length < 3) {
        abort();
        return;
      }

      let results = [];
      try {
        const addresses = await this.addressSearchService({ term, country: this.signupCountry });
        results = addresses.map((result) => ({
          label: result,
          value: result,
        }));
      } catch (err) {
        errorHandler.captureException(err);
      }

      update(() => {
        this.matchingAddresses = results;
      });
    },
    auAddressSelected(item) {
      if (!item.value) return;

      this.suburb = item.value;
      this.suburbSelected(item);
    },
    suburbSelected(item) {
      if (item.meta) {
        this.state = item.meta.state;
        this.postCode = item.meta.postcode;

        if (this.state === 'tas' || this.state === 'nt') {
          this.suburbHealth = 'Juggle Street doesn\'t operate in Tasmania or Northern Territory, apologies for the inconvenience.';
        }
      }
    },
    async nzAddressSelected(term) {
      try {
        if (term.value) {
          const address = await this.geocodeAddresService({ term: term.value, country: this.signupCountry });
          this.showAddressDetails = true;
          this.addressLine1 = address.unitNumber ? `${address.unitNumber}/` : '';
          this.addressLine1 += address.streetNumber ? address.streetNumber : '';
          this.addressLine1 += address.streetName ? ` ${address.streetName}` : '';
          this.postCode = address.postCode;
          this.suburb = address.suburb;
          this.state = address.state;
        }
      } catch (err) {
        errorHandler.captureException(err);
      }
    },
  },
  watch: {
    completeAddress(value) {
      if (this.signupCountry === 'au') {
        this.auAddressSelected(value);
        return;
      }

      this.nzAddressSelected(value);
    },
  },
};
</script>
