<template>
  <q-page>
    <q-carousel
      v-model="stepIndex"
      no-arrows
      no-navigation
      swipeable
      :style="containerStyle"
    >
      <q-carousel-slide :name="1" class="no-padding">
        <div class="onboarding-intro">
        </div>
      </q-carousel-slide>
      <q-carousel-slide class="no-padding" v-for="(step, index) in stepsInfo" :key="index" :name="step.index" style="overflow: hidden">
        <div class="q-mx-sm q-pt-md text-center">
          <q-icon name="clear" color="grey" size="22px" class="absolute-position q-mr-md q-mt-md" @click="handleClose"/>
          <div class="onboarding-header">
            <q-icon :name="step.icon" class="onboarding-header-icon" color="primary" size="sm"/>
            <span class="q-mx-sm q-my-xs text-subtitle1 text-bold">{{step.label}}</span>
          </div>
          <div class="q-mt-xs q-mx-md text-subtitle1 text-left">
            {{step.description}}
          </div>
        </div>
        <div v-if="step.image" class="text-center">
          <img draggable="false" class="onboarding-image" :src="'/images/' + step.image" />
        </div>
        <div v-if="step.items" class="q-px-md">
          <table style="border-collapse: collapse;">
            <tr v-for="(item, index) in step.items" :key="index" style="min-height: 100px; border-bottom: 1px solid #9e9e9e">
              <td class="q-pr-md">
                <q-icon :name="item.icon" class="onboarding-header-icon" color="primary" size="sm"/>
              </td>
              <td class="q-py-sm">
                <span class="text-subtitle1">{{item.description}}</span>
              </td>
            </tr>
          </table>
        </div>
      </q-carousel-slide>
    </q-carousel>
    <q-footer elevated class="bg-white q-py-xs">
      <q-toolbar color="secondary">
        <q-btn outline v-if="stepIndex === 1" :style="isLoggedIn ? 'visibility: hidden' : 'visibility: visible'" @click="handleClose" color="accent" label="Login" />
        <q-btn outline v-else @click="handlePrevious" color="accent" label="Back" />
        <q-toolbar-title>
          <div style="margin: 0 auto; width: 152px">
            <q-icon v-for="index in stepsCount" :key="index" class="q-mr-xs" :color="stepIndex === index ? 'accent' : 'grey'" name="circle" size="xs" @click="setIndex(index)" />
          </div>
        </q-toolbar-title>
        <q-btn @click="handleNext" color="accent" :label="nextButtonText" />
      </q-toolbar>
    </q-footer>
  </q-page>
</template>

<style lang="sass">
.onboarding-header
  display: flex
  align-items: center
  justify-content: center
.onboarding-header-icon
  border-radius: 30px
  padding: 5px
  border: 2px solid $primary
.onboarding-image
  pointer-events: none
  height: calc(100vh - 180px)
.onboarding-intro
  background: url('/images/onboarding-intro.jpg') no-repeat
  background-size: cover
  width: 100%
  height: calc(100vh - 58px)
.onboarding-intro-img
  object-fit: cover
  width: 100%
  height: 100vh
.onboarding-action
  position: fixed
  left: 0
  right: 0
</style>
<script>
import * as storageManager from '../utils/storage-manager';
import errorHandler from '../utils/error-handler';

const displayedOnboardingPageKey = 'jugglestreet-displayed-onboarding-key';

export default {
  name: 'PageOnboarding',
  data() {
    return {
      stepIndex: 1,
      stepsCount: 7,
      stepsInfo: [
        {
          index: 2, icon: 'person_pin', image: 'onboarding-step-1.jpg', label: 'Near Me', description: 'Toggle between Childcare and Tutors and use filters to narrow your search.',
        },
        {
          index: 3, icon: 'edit', image: 'onboarding-step-2.jpg', label: 'Post in Minutes', description: 'Create your job post, invite helpers or let Juggle Street create a shortlist!',
        },
        {
          index: 4, icon: 'event_available', image: 'onboarding-step-3.jpg', label: 'Real-time Hiring Process', description: 'Helpers receive invites in real-time, review job details and tap to apply, decline, or chat.',
        },
        {
          index: 5, icon: 'work_outline', image: 'onboarding-step-4.jpg', label: 'Parent Awards Job', description: 'Parent reviews applicants, chats to ask questions, then simply taps to award the job.',
        },
        {
          index: 6, icon: 'star_border', image: 'onboarding-step-5.jpg', label: 'Ratings & Reviews', description: 'Parents depend on local recommendations from other Juggle St families to choose new helpers.',
        },
        {
          index: 7,
          icon: 'local_offer',
          label: 'Pricing',
          description: 'Juggle Street’s unique model ensures fair pricing for Parents, Businesses and Helpers.',
          items: [
            {
              icon: 'check_circle',
              description: 'Parents & Businesses set the price of every job post. Helpers decide to apply or decline.',
            },
            {
              icon: 'paid',
              description: 'Parents & Businesses pay Helpers directly at the end of every job.',
            },
            {
              icon: 'percent',
              description: 'Juggle St does not take a commission or fee per job, so helpers keep 100% of their earnings.',
            },
            // {
            //   icon: 'verified_user',
            //   description: 'Annual Family Membership includes unlimited job posts, chat with helpers and AON Family Public Liability Insurance.',
            // },
            {
              icon: 'card_membership',
              description: 'Families, Businesses and Helpers pay a subscription to post jobs, receive job invites, and chat with other users.',
            },
          ],
        },
      ],
    };
  },
  computed: {
    containerStyle() {
      // const fullScreenStyle = 'width: 100%; height: calc(100vh); background-color: #f5f5f5';
      const footerStyle = 'width: 100%; height: calc(100vh - 58px); background-color: #f5f5f5; overflow: hidden';

      return footerStyle;
    },
    nextButtonText() {
      return this.stepIndex < this.stepsCount ? 'Next' : 'Got It';
    },
  },
  methods: {
    async handleNext() {
      if (this.stepIndex === this.stepsCount) {
        await this.handleClose();
        return;
      }

      if (this.stepIndex < this.stepsCount) {
        this.stepIndex++;
      }
    },
    handlePrevious() {
      if (this.stepIndex > 0) this.stepIndex--;
    },
    setIndex(index) {
      this.stepIndex = index;
    },
    async handleClose() {
      try {
        await storageManager.setValue(displayedOnboardingPageKey, true, true);
        this.routerReplace('/login');
      } catch (error) {
        errorHandler.captureException(error);
      }
    },
  },
};
</script>
