<template>
  <q-page class="post-one-off-job-step-2">
    <div class="layout-padding">
      <q-field
        :label="isIndividual ? 'Price Guide' : 'Is this job Fixed Price or Hourly Rate?'"
        :error="v$.price.$error"
        error-message="Please enter the amount you are willing to pay for the job."
        borderless
        readonly
        no-error-icon
        hide-bottom-space
      >
        <template v-slot:prepend>
          <q-icon name="attach_money" />
        </template>
        <template v-slot:control>
          <div class="full-width q-mt-md">
            <div v-if="isIndividual">
              <div>
                When selecting the hourly rate please consider:
                <ul class="no-margin">
                  <li>
                    Overall job requirements incl. number & age of children, driving, extra duties etc.</li>
                  <li>Experience & qualifications of invited helpers.</li>
                  <li>Shift duration - short shifts require higher rates.</li>
                </ul>
              </div>
              <table class="price-guide-table q-mt-sm">
                <tr>
                  <th>
                    Shift duration
                  </th>
                  <th>
                    Base Rate
                  </th>
                </tr>
                <tr>
                  <td>2 Hours</td>
                  <td>Min. $30 p/h</td>
                </tr>
                <tr>
                  <td>3+ Hours</td>
                  <td>Av. $25-$35 p/h</td>
                </tr>
              </table>
            </div>
            <div v-if="isBusiness">
              <div v-if="country === 'au'">
                For details of the childcare wages stipulated in the Children Services Award please refer to the government’s Fair Work Commission website.
              </div>
              <div v-else-if="country === 'nz'">
                For information on childcare employment and salaries please refer to the government’s Employment New Zealand website.
              </div>
            </div>
            <div class="q-mt-sm">
              <div v-if="isIndividual" class="text-bold">Is this job Fixed Price or Hourly Rate?</div>
              <q-option-group
                type="radio"
                v-model="paymentType"
                :options="[
                { label: 'Hourly rate', value: 2 },
                  { label: 'Fixed Price', value: 1 },
                ]"
              />
            </div>
            <div v-if="paymentType === 1">
              <div>
                <b class="q-mr-xs" v-if="isBusiness">*</b>
                <b>Total price $</b>
                <q-input
                  type="number"
                  :error="v$.price.$error"
                  v-model="price"
                  placeholder="00"
                  prefix="$"
                  class="price-input"
                  dense
                /> for {{jobHours}} Hour Job<span v-if="isLowRate" class="text-red"> *</span>
              </div>
            </div>
            <div v-if="paymentType === 2">
              <div>
                <b class="q-mr-xs" v-if="price && isBusiness">*</b>
                <b>Base rate $</b>
                <q-input
                  type="number"
                  :error="v$.price.$error"
                  v-model="price"
                  placeholder="00"
                  prefix="$"
                  class="price-input"
                  dense
                />per hour<span v-if="isLowRate" class="text-red"> *</span>
              </div>
              <div v-if="price">
                <b>Total price = </b>
                <span>${{jobPrice}} for {{jobHours}} Hour job</span>
              </div>
            </div>
            <div v-if="isLowRate" class="text-grey">
              <span class="text-red">*</span>
              Jobs priced below the Price Guide can be difficult to fill.
            </div>
            <div v-if="price && isBusiness" class="text-grey">
              <small class="q-mr-xs">*</small>
              <small v-if="country === 'au'">Excludes Superannuation and other statutory entitlements. </small>
              <small v-if="country === 'nz'">Excludes any government statutory entitlements.</small>
            </div>
          </div>
        </template>
      </q-field>
      <div class="text-center q-my-sm" v-if="price > 0">
          <q-btn
            color="accent"
            @click="handleNext"
          >
            Confirm
          </q-btn>
          <div v-if="isIndividual" class="q-my-sm text-bold">
            Please consider overall job value
          </div>
        </div>
    </div>
  </q-page>
</template>

<script>
import useVuelidate from '@vuelidate/core';
import { required, minLength, numeric } from '@vuelidate/validators';
import { mapState } from 'vuex';
import { beforeRouteEnter, beforeLeaveStep } from './multistep-helper';
import * as mutationTypes from '../../store/mutation-types';
import c from '../../utils/constants';
import NotificationService from '../../utils/notification-service';
import { getTimeDifference, formatNumber } from '../../utils/helpers';

export default {
  name: 'PageOneOffJobStep2',
  data() {
    return {};
  },
  setup() {
    return {
      v$: useVuelidate(),
    };
  },
  validations: {
    price: {
      required,
      minLength: minLength(1),
      numeric,
    },
  },
  computed: {
    ...mapState({
      job: (state) => state.postOneOffJob.jobDetails.data,
      meta: (state) => state.postOneOffJob.jobDetails.meta,
    }),
    storeModuleName() {
      return 'postOneOffJob.jobDetails';
    },
    isLowRate() {
      if (this.isBusiness) return false;

      if (!this.price) return false;

      const jobPrice = parseFloat(this.price);

      if (this.paymentType === c.jobPaymentType.hourly) {
        return jobPrice < 25;
      }

      return (jobPrice / this.jobHours) < 25;
    },
    jobHours() {
      const hours = getTimeDifference(this.job.jobStartTime, this.job.jobEndTime);
      return hours;
    },
    jobPrice() {
      let totalValue;
      if (this.paymentType === c.jobPaymentType.hourly) {
        totalValue = this.jobHours * this.price;
      } else {
        totalValue = this.price;
      }

      return formatNumber(totalValue);
    },
    price: {
      get() {
        return this.job.price;
      },
      set(value) {
        this.setProperty('price', value);
      },
    },
    paymentType: {
      get() {
        return this.job.paymentType;
      },
      set(value) {
        this.setProperty('paymentType', value);
      },
    },
  },
  watch: {
    price(value) {
      if (this.paymentType === c.jobPaymentType.hourly) {
        this.setProperty('overtimeRate', value);
      }
    },
    paymentType(value) {
      if (value === c.jobPaymentType.fixed) {
        this.setProperty('overtimeRate', null);
      }
    },
  },
  methods: {
    handleNext() {
      this.routerReplace({ path: `/post-one-off-job/step/${this.meta.currentStep + 1}` });
    },
    validate() {
      this.v$.$touch();
      const hasError = this.v$.$error;
      if (hasError) {
        NotificationService.negative('Please review highlighted fields.');
      }

      return !hasError;
    },
  },
  beforeRouteEnter(to, from, next) {
    beforeRouteEnter(to, from, next);
  },
  beforeRouteLeave(to, from, next) {
    const jobPrice = parseFloat(this.job.price);
    let hourlyRate;
    if (this.job.paymentType === c.jobPaymentType.hourly) {
      hourlyRate = jobPrice;
    } else {
      const hours = getTimeDifference(this.job.jobStartTime, this.job.jobEndTime);
      hourlyRate = jobPrice / hours;
    }

    const filterPayload = {
      filterType: 'postOneOffJob',
      filterKey: 'hourlyRate',
      value: hourlyRate,
    };
    this.$store.commit(`filters/${mutationTypes.UPDATE_FILTER_OPTION}`, filterPayload);

    beforeLeaveStep(this, to, from, next);
  },
};
</script>
<style lang="sass">
  .post-one-off-job-step-2
    .price-input
      display: inline-block
      width: 75px
    .overtime-rate-input
      width: 125px
</style>
