<template>
  <q-page class="my-addresses layout-padding">
    <q-list v-if="addresses.length > 0">
      <div
        :key="index"
        v-for="(address, index) in addresses">
        <q-item>
          <q-item-section top avatar>
            <q-avatar :color="addressColor(address)" size="xl" :text-color="addressTextColor(address)" :icon="addressIcon(address)"/>
          </q-item-section>
          <q-item-section>
            <q-item-label>
              {{formattedAddress(address)}}
              <br/>
              <span v-if="address.isPrimaryAddress" class="text-bold text-primary">Default Address</span>
              <q-btn
                v-if="!address.isPrimaryAddress"
                class="text-underline no-padding"
                color="black"
                label="Set to Default"
                flat
                @click="handleDefaultAddress(index)"
              />
            </q-item-label>
          </q-item-section>
          <q-item-section top side>
            <q-item-label class="q-mt-sm">
              <q-btn
                size="sm"
                round
                color="primary"
                icon="create"
                @click="handleEditAddress(address, index)"
              />
              <q-btn
                :disable="address.isPrimaryAddress"
                size="sm"
                class="q-mx-sm bg-secondary-lighter text-white"
                round
                icon="close"
                @click="handleDeleteAddress(index)"
              />
            </q-item-label>
          </q-item-section>
        </q-item>
        <q-separator spaced />
      </div>
    </q-list>

    <div class="text-center">
      <q-btn @click="handleAddAddress" color="accent">
        Add Address
      </q-btn>
    </div>

    <add-address-modal ref="AddAddressModal" v-on:addAddressSuccess="handleAddAddressSuccess" />
  </q-page>
</template>

<script>
import AddAddressModal from 'components/AddAddressModal';
import { mapState, mapActions } from 'vuex';
import { getFormattedAddress } from '../../utils/helpers';
import NotificationService from '../../utils/notification-service';

export default {
  name: 'PageAccountMyAddresses',
  components: { AddAddressModal },
  data() {
    return {};
  },
  methods: {
    ...mapActions([
      'updateUserInfo',
      'setDefaultAddress',
    ]),
    addressColor(address) {
      return address.isPrimaryAddress ? 'primary' : 'transparent';
    },
    addressIcon(address) {
      return address.isPrimaryAddress ? 'house' : 'house';
    },
    addressTextColor(address) {
      return address.isPrimaryAddress ? 'white' : 'black';
    },
    handleEditAddress(address, index) {
      this.$refs.AddAddressModal.open({ ...address }, index);
    },
    handleAddAddress() {
      this.$refs.AddAddressModal.open({
        addressLine1: '',
        addressLine2: '',
        suburb: '',
        state: '',
        postCode: '',
        addressLabel: '',
        country: this.country,
      }, -1);
    },
    handleDefaultAddress(index) {
      this.setDefaultAddress(index);
      this.updateUserInfo().catch((error) => {
        NotificationService.negative(error.message);
      });
    },
    handleDeleteAddress(index) {
      this.$q.dialog({
        title: 'Delete Address',
        message: 'Are you sure you want to delete this address?',
        cancel: {
          label: 'No',
          color: 'secondary',
          outline: true,
        },
        ok: {
          label: 'Yes',
          outline: true,
        },
      }).onOk(() => {
        const newAddresses = [...this.addresses];

        if (index >= 0) {
          newAddresses.splice(index, 1);
        }

        this.setProperty('addresses', newAddresses);
        this.updateUserInfo().catch((error) => {
          NotificationService.negative(error.message);
        });
      }).onCancel(() => {});
    },
    handleAddAddressSuccess({ address, index }) {
      const newAddresses = [...this.addresses];

      if (index >= 0) {
        newAddresses[index] = address;
      } else {
        newAddresses.push(address);
      }

      this.setProperty('addresses', newAddresses);
      this.updateUserInfo().catch((error) => {
        NotificationService.negative(error.message);
      });
    },
    formattedAddress(address) {
      return getFormattedAddress(address);
    },
  },
  computed: {
    ...mapState({
      addresses: (state) => state.userInfo.data.addresses,
    }),
    storeModuleName() {
      return 'userInfo';
    },
  },
};
</script>
<style lang="sass">
.my-addresses
  .add-address-button
    margin-top: 1rem
  .icons
    display: block
</style>
