// components
// import AppInfo from 'components/AppInfo';

import loader from './loader';
import intercomService from './intercom-service';
import analyticsService from './analytics-service';
import { getRouteMetaProperty } from './helpers';
import * as mutationTypes from '../store/mutation-types';
import store from '../store';
import c from './constants';
import { env } from './env';
import { logout, isAuthorized } from '../api/auth';
import errorHandler from './error-handler';

const JsMixins = {
  importComponents(app) {
    app.config.globalProperties.loader = loader;
  },
  configureMixins(app) {
    app.mixin({
      computed: {
        pageTitle() {
          // Make sure all routes have a valid meta title, they are used by the main vue layout and Google Analytics.
          return store.getters.pageInfo.title;
        },
        pageSubtitle() {
          return store.getters.pageInfo.subtitle;
        },
        isProcessing() {
          return store.getters.pageInfo.isProcessing;
        },
        isFamily() {
          if (!store.getters.userInfo) {
            return false;
          }

          return store.getters.userInfo.jugglerTypes === c.jugglerType.client;
        },
        isClient() {
          if (!store.getters.userInfo) {
            return false;
          }

          return store.getters.userInfo.jugglerTypes === c.jugglerType.client;
        },
        isHelper() {
          if (!store.getters.userInfo) {
            return false;
          }

          return store.getters.userInfo.jugglerTypes === c.jugglerType.provider;
        },
        isPending() {
          if (!store.getters.userInfo) {
            return false;
          }

          return store.getters.userInfo.accountStatus === c.accountStatus.pending;
        },
        isApproved() {
          if (!store.getters.userInfo) {
            return false;
          }

          return store.getters.userInfo.accountStatus === c.accountStatus.approved;
        },
        clientType() {
          const userInfo = store.getters.userInfo;
          return userInfo.client.clientType;
        },
        isIndividual() {
          if (!store.getters.userInfo) {
            return false;
          }

          const userInfo = store.getters.userInfo;
          return userInfo.client.clientType === c.clientType.individual || userInfo.provider.providerType === c.providerType.individual;
        },
        isBusiness() {
          if (!store.getters.userInfo) {
            return false;
          }

          const userInfo = store.getters.userInfo;
          return userInfo.client.clientType === c.clientType.business || userInfo.provider.providerType === c.providerType.business;
        },
        canAccessChildcareJobs() {
          const userInfo = store.getters.userInfo;
          if (!userInfo.id) return true;

          // We are only launching with Juggle St Pro Childcare at this point.
          // return userInfo.client.clientType === c.clientType.individual || userInfo.interestedInChildcareJobs;
          return true;
        },
        canAccessTutoringJobs() {
          const userInfo = store.getters.userInfo;
          if (!userInfo.id) return true;

          // We are only launching with Juggle St Pro Childcare at this point.
          return userInfo.client.clientType === c.clientType.individual && !userInfo.paymentInfo.isCorporateMembership;
        },
        isCorporateMembership() {
          const userInfo = store.getters.userInfo;
          if (!userInfo.id) return false;

          // We are only launching with Juggle St Pro Childcare at this point.
          return userInfo.paymentInfo.isCorporateMembership;
        },
        mainRoute() {
          if (this.isIndividual) return '/dashboard';

          if (this.isBusiness) {
            if (this.canAccessChildcareJobs) return '/dashboard';

            if (this.canAccessTutoringJobs) return '/dashboard-tutors';
          }

          return '/dashboard';
        },
        isTest() {
          return env.isTest;
        },
        isLive() {
          return !env.isTest;
        },
        enableBusinessAccounts() {
          return env.enableBusinessAccounts;
        },
        isLoggedIn() {
          return isAuthorized();
        },
        country() {
          return env.country;
        },
        appVersion() {
          if (this.isWeb) return env.appInfo.version;

          return `${env.appInfo.version}-${parseInt(env.versionCode, 10)}`;
        },
        appCulture() {
          return env.culture;
        },
        appTimestamp() {
          return env.appInfo.timestamp;
        },
        platform() {
          return env.deviceInfo.platform;
        },
        deviceType() {
          return env.deviceType;
        },
        isWeb() {
          return env.deviceInfo.platform === 'web';
        },
        isApp() {
          return env.deviceInfo.platform !== 'web';
        },
        isAndroidDevice() {
          return env.deviceInfo.operatingSystem === 'android';
        },
        isIosDevice() {
          return env.deviceInfo.operatingSystem === 'ios';
        },
        isTablet() {
          return env.platformInfo.ipad || env.platformInfo.desktop;
        },
        operatingSystem() {
          return env.deviceInfo.operatingSystem;
        },
        pageState() {
          const stateKey = this.initPageState();
          return this.$store.state.pages[stateKey];
        },
        isLoaderActive() {
          return this.loader.isActive();
        },
      },
      methods: {
        toggleLeftPanel() {
          this.$store.dispatch('toggleLeftDrawer');
        },
        toggleRightPanel() {
          this.$store.dispatch('toggleRightDrawer');
        },
        showLoader() {
          this.loader.show();
        },
        hideLoader() {
          this.loader.hide();
        },
        timedShowLoader(time) {
          this.loader.show();
          setTimeout(() => {
            this.loader.hide();
          }, time);
        },
        routerPush(route, hidePanel = false) {
          if (this.$router.currentRoute.value.fullPath === route) return;

          if (hidePanel) {
            this.$store.dispatch('toggleLeftDrawer');
            this.$router.push(route).catch(errorHandler.captureException);
          } else {
            this.$router.push(route).catch(errorHandler.captureException);
          }
        },
        routerReplace(route, hidePanel = false) {
          if (this.$router.currentRoute.value.fullPath === route) return;

          if (hidePanel) {
            this.$store.dispatch('toggleLeftDrawer');
            this.$router.replace(route).catch(errorHandler.captureException);
          } else {
            this.$router.replace(route).catch(errorHandler.captureException);
          }
        },
        routerBack() {
          let backRoute = this.getRouteMetaProp('backRoute');
          if (backRoute) {
            const hasParams = backRoute.indexOf(':') !== -1;
            if (hasParams) {
              Object.keys(this.$route.params).forEach((key) => {
                if (backRoute.indexOf(key) !== -1) {
                  backRoute = backRoute.replace(`:${key}`, this.$route.params[key]);
                }
              });
            }
            this.routerReplace(backRoute);
          } else if (window.history.length > 1) {
            this.$router.back();
          } else if (isAuthorized()) this.routerReplace('/dashboard');
          else this.routerReplace('/login');
        },
        getRouteMetaProp(propName) {
          return getRouteMetaProperty(this.$route, propName);
        },
        displayAction(targetAction) {
          const actions = this.getRouteMetaProp('actions');
          if (!actions) {
            return false;
          }

          return actions.indexOf(targetAction) !== -1;
        },
        async handleLogout(dontReload) {
          await logout();
          intercomService.shutdown();
          analyticsService.setUser(null);
          this.$router.push('/login?redirect=%2Fdashboard');

          if (!dontReload) {
            this.doubleRaf(() => {
              window.location.reload();
            });
          }
        },
        getStoreProperty(module, propPath) {
          return Object.resolve(this.$store.state, `${module}.data.${propPath}`);
        },
        getStoreMetaProperty(module, propPath) {
          return Object.resolve(this.$store.state, `${module}.meta.${propPath}`);
        },
        initPageState() {
          const key = this.$route.path.substring(1);
          let pageState = this.$store.state.pages[key];
          if (!pageState) {
            pageState = {};
            const payload = {
              module: 'pages',
              propPath: key,
              value: pageState,
            };
            this.$store.dispatch('updateStoreValue', payload);
          }

          return key;
        },
        setPageState(propPath, value) {
          const stateKey = this.initPageState();
          const module = `pages.${stateKey}`;
          const payload = {
            module,
            propPath,
            value,
          };
          this.$store.commit(mutationTypes.UPDATE_STORE_VALUE, payload);
        },
        setStoreData(module, propPath, value) {
          const payload = {
            module,
            propPath,
            value,
          };

          this.$store.commit(mutationTypes.UPDATE_STORE_DATA, payload);
        },
        setStoreMeta(module, propPath, value) {
          const payload = {
            module,
            propPath,
            value,
          };

          this.$store.commit(mutationTypes.UPDATE_STORE_META, payload);
        },
        getPageState(propPath) {
          const stateKey = this.initPageState();
          const module = `pages.${stateKey}`;
          return Object.resolve(this.$store.state, `${module}.${propPath}`);
        },
        getProperty(propPath) {
          // storeModuleName computed property must be provided.
          return this.getStoreProperty(this.storeModuleName, propPath);
        },
        setProperty(propPath, value) {
          // storeModuleName computed property must be provided.
          this.setStoreData(this.storeModuleName, propPath, value);
        },
        getMetaProperty(propPath) {
          // storeModuleName computed property must be provided.
          return this.getStoreMetaProperty(this.storeModuleName, propPath);
        },
        setMetaProperty(propPath, value) {
          // storeModuleName computed property must be provided.
          this.setStoreMeta(this.storeModuleName, propPath, value);
        },
        doubleRaf(callback) {
          requestAnimationFrame(() => {
            requestAnimationFrame(callback);
          });
        },
        nextTick(callback) {
          this.$nextTick(callback);
        },
      },
    });
  },
};

export default JsMixins;
