// import { Dialog, Notify } from 'quasar';
import { App } from '@capacitor/app';
import { Badge } from '@capawesome/capacitor-badge';
import { env } from './env';
import errorHandler from './error-handler';
import conversionTrackingService from './conversion-tracking-service';
import * as storageManager from './storage-manager';

const reportedDownloadKey = 'jugglestreet-reported-download';
let configured = false;

const AppManager = {
  configure(config) {
    if (configured) return Promise.resolve();

    configured = true;

    if (env.deviceInfo.platform === 'web') return Promise.resolve();

    if (config && config.handleAppStateChange) {
      App.addListener('appStateChange', (state) => {
        // state.isActive contains the active state
        console.log('AppManager::App state changed. Is active?', state.isActive);
        if (config.handleAppStateChange) {
          config.handleAppStateChange(state);
        }
      });
    }
    // Listen for serious plugin errors
    App.addListener('pluginError', (info, err) => {
      console.error('AppManager::There was a serious error with a plugin', err, info);
    });

    // App.addListener('appUrlOpen', (data) => {
    //   console.log(`AppManager::App opened with URL: ${data.url}`);
    // });

    // App.addListener('backButton', (data) => {
    //   console.log(`AppManager::App back button: ${data.url}`);
    // });

    App.addListener('appRestoredResult', (data) => {
      console.log('AppManager::Restored state:', data);
    });

    return Promise.resolve();
  },
  async setBadge(badgeNumber) {
    if (env.deviceInfo.platform === 'web') return Promise.resolve();

    try {
      const supportedStatus = await Badge.isSupported();
      if (!supportedStatus.isSupported) return Promise.resolve();

      let permissionState = await Badge.checkPermissions();
      if (permissionState.display !== 'granted') {
        permissionState = await Badge.requestPermissions();
        if (permissionState !== 'granted') return Promise.resolve();
      }

      await Badge.set({ count: badgeNumber });
    } catch (error) {
      errorHandler.captureException(error);
      return Promise.reject();
    }

    return Promise.resolve();
  },
  async reportAppDownload() {
    if (env.deviceInfo.platform === 'web') return Promise.resolve();

    try {
      const reportedDownload = await storageManager.getValue(reportedDownloadKey);
      if (reportedDownload) return Promise.resolve();

      conversionTrackingService.trackAppDownload();

      await storageManager.setValue(reportedDownloadKey, true, true);
    } catch (error) {
      errorHandler.captureException(error);
      return Promise.reject();
    }

    return Promise.resolve();
  },
};

export default AppManager;
