import { PushNotifications } from '@capacitor/push-notifications';
import { LocalNotifications } from '@capacitor/local-notifications';
import { Notify } from 'quasar';
import { env } from './env';
import * as http from '../api/http';
import errorHandler from './error-handler';
import { parseQueryStringParams, getFullRoute } from './helpers';

let initialised = false;
let router;
let store;

const pushNotificationService = {
  init(s, r) {
    store = s;
    router = r;
  },
  async checkEnabled() {
    try {
      const enabledResult = await LocalNotifications.areEnabled();
      return enabledResult.value;
    } catch {
      return true;
    }
  },
  removeAllDeliveredNotifications() {
    if (env.deviceInfo.platform === 'web') return;

    try {
      if (store.state.pushNotifications.permission !== 'granted') return;

      PushNotifications.removeAllDeliveredNotifications();
    } catch (error) {
      errorHandler.captureException(error);
    }
  },
  register() {
    if (env.deviceInfo.platform === 'web') return Promise.resolve();

    // Request permission to use push notifications.
    // iOS will prompt user and return if they granted permission or not.
    // Android will just grant without prompting.
    console.log('Push Notifications::Requesting Permission');
    return PushNotifications.requestPermissions().then((result) => {
      console.log('Push Notifications::Requested Permission');
      if (result.receive === 'granted') {
        console.log('Push Notifications::Permission Granted');
        // Register with Apple / Google to receive push via APNS/FCM.
        PushNotifications.register();
      } else {
        console.log('Push Notifications::Permission Denied');
        store.dispatch('updatePushSettings', { permission: result.receive });
        // Show some error.
      }
    });
  },
  configure() {
    if (env.deviceInfo.platform === 'web') return;

    if (initialised) return;
    initialised = true;

    // On success, we should be able to receive notifications.
    PushNotifications.addListener('registration',
      (token) => {
        // alert(`Push registration success, token: + ${token.value}`);
        const req = {
          platform: env.deviceInfo.platform,
          pushChannel: token.value,
          appVersion: env.appInfo.version,
          distVersion: env.versionNumber,
        };
        return http.post('notifications/register', req)
          .then((response) => {
            store.dispatch('updatePushSettings', { permission: 'granted', pushChannel: token.value });
            return response.data;
          })
          .catch((error) => {
            console.log(error);
            store.dispatch('updatePushSettings', { permission: 'granted-pending', pushChannel: token.value });
          });
      });

    // Some issue with our setup and push will not work.
    PushNotifications.addListener('registrationError',
      (error) => {
        Notify.create({
          type: 'positive',
          message: 'Registration Error',
          caption: `Error on registration: ${JSON.stringify(error)}`,
          position: 'top',
          icon: 'announcement',
        });
      });

    const ackMessageReceieved = (notification) => {
      store.dispatch('pushMessageReceieved', notification);
    };

    const isOwnedMessage = (notification) => {
      if (!notification.data || !notification.data.ownerId) return true;

      const userInfo = store.getters.userInfo;
      if (!userInfo || !userInfo.id) return true;

      const sameUser = userInfo.id === parseInt(notification.data.ownerId, 10);
      if (!sameUser) {
        Notify.create({
          type: 'positive',
          color: 'secondary',
          message: 'Notification',
          timeout: 0,
          caption: 'This message was meant for a different account. If you operate multiple accounts through the same device, please logout of the system then log back in using the relevant account.',
          position: 'bottom',
          icon: 'announcement',
          actions: [{
            label: 'Dismiss',
            color: 'accent',
            handler: () => { /* ... */ },
          }],
        });
      }

      return sameUser;
    };

    const getTargetRoute = (notification) => {
      if (!notification.data || !notification.data.route) return '';

      let targetRoute = notification.data.route;
      // Remove absolute path.
      const routeIdx = targetRoute.indexOf('#');
      if (routeIdx !== -1) {
        targetRoute = targetRoute.substring(routeIdx + 1);
      }

      // Expand quick navigation.
      if (targetRoute.indexOf('qn-') !== -1) {
        const query = parseQueryStringParams(targetRoute);
        const queryPosition = targetRoute.indexOf('?');
        const startIdx = targetRoute.indexOf('-');
        const typeLength = queryPosition !== -1 ? queryPosition : undefined;
        const type = targetRoute.substring(startIdx + 1, typeLength);
        try {
          targetRoute = getFullRoute(type, query);
        } catch (error) {
          errorHandler.captureException(error);
        }
      }

      return targetRoute;
    };

    const processNotification = (notification, replace) => {
      if (!notification.data || !notification.data.route) return;

      const targetRoute = getTargetRoute(notification);
      const query = parseQueryStringParams(targetRoute);
      query.notId = notification.id;
      const routeData = {
        path: targetRoute,
        query,
      };
      if (replace) {
        router.replace(routeData).catch(errorHandler.captureException);
      } else {
        router.push(routeData).catch(errorHandler.captureException);
      }
    };

    // Show us the notification payload if the app is open on our device.
    PushNotifications.addListener('pushNotificationReceived',
      (notification) => {
        if (!isOwnedMessage(notification)) {
          return;
        }

        let title = '';
        let message;
        if (env.deviceInfo.platform === 'android') {
          // Android -> .id .data .title .body.
          title += notification.title;
          message = notification.body;
        } else {
          title = notification.title;
          message = notification.body;
        }

        const actions = [];
        let samePage = false;
        if (notification.data) {
          if (notification.data.route) {
            const targetRoute = getTargetRoute(notification);
            const isSilent = targetRoute.indexOf('chat') !== -1;
            samePage = router.currentRoute.value.fullPath.indexOf(targetRoute) !== -1;

            if (samePage && isSilent) {
              // Same route.
              processNotification(notification, true);
              return;
            }

            actions.push({
              label: 'Go',
              color: 'accent',
              handler: () => {
                processNotification(notification);
              },
            });
          }
        }

        if (!samePage) {
          actions.push({
            label: 'Dismiss',
            color: 'accent',
            handler: () => { /* ... */ },
          });
        }

        Notify.create({
          type: 'positive',
          color: 'secondary',
          message: title,
          timeout: 0,
          caption: message,
          position: 'bottom',
          icon: 'announcement',
          actions,
        });

        ackMessageReceieved(notification);
      });

    // Method called when tapping on a notification.
    PushNotifications.addListener('pushNotificationActionPerformed',
      (notificationInfo) => {
        if (!isOwnedMessage(notificationInfo.notification)) {
          return;
        }
        ackMessageReceieved(notificationInfo.notification);
        processNotification(notificationInfo.notification, true);
      });
  },
};

export default pushNotificationService;
