<template>
  <q-page class="">
    <div class="layout-padding">
      <div class="">
        <q-toolbar color="secondary" class="filter-panel">
          <q-toolbar-title class="text-left" style="text-align: left; padding: 0">
            <small>{{filterText}}</small>
          </q-toolbar-title>
          <q-btn flat @click="handleSearch">
            <q-icon name="search" />
            <span>Filters</span>
          </q-btn>
        </q-toolbar>

        <div class="q-mb-xl">
          <helper-profile-card
            v-for="helper in users"
            :key="helper.id"
            :click="handleHelperClick"
            :helper="helper"
            mode="post-job"
            :filterKey="filterKey"
            :isSelected="isSelected(helper)"
            :isDisabled="alreadySelected(helper)"
          />
        </div>
        <div class="q-my-sm text-center" style="margin-bottom: 40px" v-if="hasMore">
          <q-btn rounded outline label="Load More" color="accent" @click="loadMore"/>
        </div>
      </div>
      <q-btn
        big
        color="accent"
        class="full-width fixed-bottom"
        @click="handleInviteCarers">
          Invite Helpers
      </q-btn>
    </div>
  </q-page>
</template>

<script>
import useVuelidate from '@vuelidate/core';
import { required } from '@vuelidate/validators';
import { mapState } from 'vuex';
import HelperProfileCard from 'components/HelperProfileCard';
import NotificationService from '../../utils/notification-service';
import * as mutationTypes from '../../store/mutation-types';
import {
  getJobModuleName, getJobTypeArray, formatNumber, getHelperType, getHourlyRate,
} from '../../utils/helpers';
import store from '../../store';
import c from '../../utils/constants';

export default {
  name: 'PageFamiliesInviteMoreHelpers',
  components: { HelperProfileCard },
  data() {
    return {
      searchText: '',
    };
  },
  setup() {
    return {
      v$: useVuelidate(),
    };
  },
  validations: {
    newApplicants: {
      required,
    },
  },
  computed: {
    ...mapState({
      job: (state) => state.families.pages.jobDetails.data,
      users: (state) => state.filters.data,
      usersMeta: (state) => state.filters.meta,
    }),
    storeModuleName() {
      return 'families.pages.jobDetails';
    },
    filterText() {
      if (this.isProcessing) return 'Searching...';

      const helperType = getHelperType(this.job.jobType);

      return `${formatNumber(this.usersMeta.total)} ${helperType}${this.usersMeta.total > 1 ? 's' : ''} meet your criteria`;
    },
    newApplicants() {
      return this.job.newApplicants;
    },
    hasMore() {
      return this.pageState.pageIndex * this.pageState.pageSize < this.usersMeta.total;
    },
    moduleName() {
      return getJobModuleName(this.job.jobType);
    },
    filterKey() {
      if (this.job.jobType === c.jobType.primarySchoolTutoring || this.job.jobType === c.jobType.highSchoolTutoring) {
        if (this.job.jobDeliveryMethod === c.jobDeliveryMethod.online) {
          return 'postOnlineTutoringJob';
        }

        return 'postHomeTutoringJob';
      }

      return this.moduleName;
    },
    helpers() {
      return this.filteredHelpers[this.filterKey];
    },
  },
  methods: {
    handleHelperClick(helper) {
      this.$store.dispatch('families/toggleApplicant', helper);
    },
    isSelected(helper) {
      if (!this.newApplicants || this.newApplicants.length === 0) {
        return '';
      }

      const isSelected = this.newApplicants
        .find((applicant) => applicant.applicantId === helper.id);
      if (isSelected) return 'primary';

      return '';
    },
    alreadySelected(helper) {
      const exists = this.job && this.job.applicants && this.job.applicants
        .find((applicant) => applicant.applicantId === helper.id && applicant.id);
      return exists;
    },
    handleFilterClick() {
      this.$store.dispatch('toggleLeftDrawer');
    },
    validate() {
      this.v$.$touch();
      const hasError = this.v$.$error;
      if (hasError) {
        NotificationService.negative('Please select at least one helper.');
      }

      return !hasError;
    },
    loadMore() {
      this.setPageState('pageIndex', this.pageState.pageIndex + 1);
      this.refreshFilters();
    },
    handleSearch() {
      this.$store.dispatch('toggleRightDrawer', true);
    },
    handleInviteCarers() {
      if (!this.validate()) {
        return;
      }

      this.$store.dispatch('families/updateJobDetails').then(() => {
        this.routerReplace(`/families/job-details/${this.$route.params.id}`);
      });
    },
    refreshFilters() {
      const filterPayload = {
        filterKey: this.filterKey,
        pageIndex: this.pageState.pageIndex,
        pageSize: this.pageState.pageSize,
        displayLoader: true,
      };
      return this.$store.dispatch('filters/applyFilter', filterPayload);
    },
  },
  beforeRouteEnter(to, from, next) {
    const job = store.state.families.pages.jobDetails.data;
    if (!job || !job.id) {
      const path = `/families/job-details/${to.params.id}`;
      next({ path });
      return;
    }

    next();
  },
  async mounted() {
    if (!this.newApplicants) {
      this.setProperty('newApplicants', []);
    }

    if (!this.pageState.pageSize || !this.pageState.pageIndex) {
      this.setPageState('pageIndex', 1);
      this.setPageState('pageSize', 50);
    }

    // TODO: Revise this mutation.
    const jobTypeArray = getJobTypeArray(this.job.jobType);
    const jobTypePayload = {
      filterType: this.filterKey,
      filterKey: 'jobTypes',
      value: jobTypeArray,
    };
    this.$store.commit(`filters/${mutationTypes.UPDATE_FILTER_OPTION}`, jobTypePayload);

    const addressFilter = {
      filterType: this.filterKey,
      filterKey: 'address',
      value: [this.job.longitude, this.job.latitude],
    };
    this.$store.commit(`filters/${mutationTypes.UPDATE_FILTER_OPTION}`, addressFilter);

    // const distancePayload = {
    //   filterType: this.moduleName,
    //   filterKey: 'distance',
    //   value: 2,
    // };
    // this.$store.commit(`filters/${mutationTypes.UPDATE_FILTER_OPTION}`, distancePayload);

    if (this.job.jobType === c.jobType.primarySchoolTutoring || this.job.jobType === c.jobType.highSchoolTutoring) {
      let filterPayload = {
        filterType: this.filterKey,
        filterKey: 'schoolYears',
        value: this.job.jobSettings.schoolYears,
      };
      this.$store.commit(`filters/${mutationTypes.UPDATE_FILTER_OPTION}`, filterPayload);

      filterPayload = {
        filterType: this.filterKey,
        filterKey: 'schoolSubjects',
        value: this.job.jobSettings.schoolSubjects,
      };
      this.$store.commit(`filters/${mutationTypes.UPDATE_FILTER_OPTION}`, filterPayload);
    }

    // Hourly rate filter.
    if (this.job.jobType !== c.jobType.auPair) {
      const filterPayload = {
        filterType: this.filterKey,
        filterKey: 'hourlyRate',
        value: getHourlyRate(this.job),
      };
      this.$store.commit(`filters/${mutationTypes.UPDATE_FILTER_OPTION}`, filterPayload);
    }

    // const filterPayload = {
    //   filterKey: this.filterKey,
    // };
    // await this.$store.dispatch('filters/applyFilter', filterPayload);
    this.$store.dispatch('filters/resetFilters');
    await this.refreshFilters();

    await this.$store.dispatch('families/setPageTitle');
  },
};
</script>
