<template>
  <div class="applicant-card">
    <div class="applicant-info">
      <img class="round-user-image applicant-info-image" :src="applicant.applicantImageSrc">
      <div class="col">
        <div class="applicant-info-name">
          <router-link :to="'/account/helper-profile/' + applicant.applicantId">{{applicant.applicantFirstName}} {{applicant.applicantLastInitial}}</router-link>
          <span v-if="mode === types.MODE.WAITING" v-html="getViewedStatus(applicant.applicationStatus)"/>
          <span v-if="mode === types.MODE.SHORTLISTED" v-html="getAwardedStatus(applicant.applicationStatus)"/>
          <span v-if="mode === types.MODE.DECLINED" v-html="getDeclinedStatus(applicant.applicationStatus, applicant.unavailableReason)"/>
        </div>
        <div class="applicant-info-other">
          <div>
            {{applicant.suburb}} - {{applicant.distanceInKiloMetersRounded}}km away
          </div>
          <div v-if="applicant.applicantRatingsAvg > 0">
            <q-rating
              :model-value="applicant.applicantRatingsAvg"
              :max="5"
              color="accent"
              readonly
            />
            - {{applicant.applicantRatingsCount}} Rating{{applicant.applicantRatingsCount !== 1 ? 's' : ''}}
          </div>
        </div>
      </div>
      <div class="chat-button" v-if="mode === types.MODE.SHORTLISTED">
        <q-icon color="black" size="36px" name="chat" @click="handleChat" />
      </div>
    </div>
    <q-expansion-item label="View application & award job" class="availability" v-if="mode === types.MODE.SHORTLISTED">
      <div v-for="key in Object.keys(availability)" :key="key" class="availability-item">
        <span>{{getDayLabel(key)}}</span>
        <div class="availability-item-timings">
          <div v-for="(item, index) in availability[key]" :key="index">
            <span>{{getFormattedTime(item.jobStartTime)}} - {{getFormattedTime(item.jobEndTime)}}</span>
            <q-icon class="availability-icon" :name="getIconName(item.availabilityStatus)" :color="getIconColor(item.availabilityStatus)"/>
          </div>
        </div>
      </div>
      <div class="actions-container">
        <q-btn v-if="applicant.applicantPhoneNumber" color="secondary" icon="phone"><a class="text-white" :href="'tel:'+applicant.applicantPhoneNumber">Call</a></q-btn>
        <q-btn v-if="applicant.applicantBankAccountNumber && applicant.applicationStatus === types.jobApplicationStatus.awarded" color="secondary" icon="account_balance" @click="handleDisplayBankDetails(applicant)">Bank Details</q-btn>
        <!-- <q-btn color="secondary" icon="chat" @click="handleChat">Chat</q-btn> -->
        <q-btn color="accent" icon="done" @click="handleAwardJob" v-if="showAwardButton(applicant.applicationStatus)">Award</q-btn>
      </div>
    </q-expansion-item>
    <bank-details-modal ref='bankDetailsPopup' :bankAccountName="applicant.applicantBankAccountName" />
  </div>
</template>

<script>
import BankDetailsModal from 'components/BankDetailsModal';
import { getDayOfWeekText, formatTime, getUnavailableReasonText } from '../../utils/helpers';
import c from '../../utils/constants';

const MODE = {
  SHORTLISTED: 'shortlisted',
  DECLINED: 'declined',
  WAITING: 'waiting',
};

export default {
  name: 'RecurringJobApplicantCard',
  components: { BankDetailsModal },
  props: ['applicant', 'mode'],
  emits: ['awardJob'],
  data() {
    return {
      types: {
        MODE,
        jobApplicationStatus: c.jobApplicationStatus,
      },
    };
  },
  computed: {
    availability() {
      return this.applicant.applicantAvailability.reduce((acc, item) => {
        if (!acc[item.dayOfWeek]) {
          acc[item.dayOfWeek] = [];
        }
        acc[item.dayOfWeek].push(item);

        return acc;
      }, {});
    },
  },
  methods: {
    getDayLabel(day) {
      return `${getDayOfWeekText(Number(day))}s`;
    },
    getIconColor(availabilityStatus) {
      return availabilityStatus === c.applicantAvailabilityStatus.available ? 'positive' : 'negative';
    },
    getIconName(availabilityStatus) {
      return availabilityStatus === c.applicantAvailabilityStatus.available ? 'done' : 'clear';
    },
    getFormattedTime(time) {
      return formatTime(time);
    },
    getViewedStatus(applicationStatus) {
      if (applicationStatus === c.jobApplicationStatus.viewed) {
        return '<span class="text-primary"> - <i class="material-icons">visibility</i> viewed</span>';
      }

      if (applicationStatus === c.jobApplicationStatus.pending) {
        return '<span class="text-faded"> - <i class="material-icons">visibility_off</i> unseen</span>';
      }

      return '';
    },
    getAwardedStatus(applicationStatus) {
      if (applicationStatus === c.jobApplicationStatus.awarded) {
        return '<span class="text-primary"> - <i class="material-icons">thumb_up</i> Job awarded</span>';
      }

      return '';
    },
    getDeclinedStatus(applicationStatus, reason) {
      if (applicationStatus === c.jobApplicationStatus.notAvailable) {
        return `<span class="text-secondary" style="vertical-align: middle"> - <i class="material-icons q-mr-xs" style="font-size: 16px">event_busy</i>${getUnavailableReasonText(reason)}</span>`;
      }

      return '';
    },
    showAwardButton(applicationStatus) {
      return applicationStatus === c.jobApplicationStatus.applied
        || applicationStatus === c.jobApplicationStatus.shortlistedBySystem
        || applicationStatus === c.jobApplicationStatus.shortlistedBySystem;
    },
    handleChat() {
      const url = `/chat/with/${this.applicant.applicantId}`;
      this.routerPush(url);
    },
    handleAwardJob() {
      this.$emit('awardJob', {
        newApplicant: this.applicant,
      });
    },
    handleDisplayBankDetails(applicant) {
      this.$refs.bankDetailsPopup.showDialog(applicant.applicantBankAccountName, applicant.applicantBankAccountNumber, applicant.applicantBankAccountBsb);
    },
  },
};
</script>
<style lang="sass">
.recurring-job-details
  .applicant-card
    border-bottom: 1px solid $grey-1
    margin-bottom: 1rem
    .applicant-info
      display: flex
      align-items: flex-start
      padding: 0.5rem
      &-image
        margin-right: 0.5rem
      &-other
        font-size: 0.9rem

  .availability
    background: $grey-1
    .availability-item
      display: flex
      justify-content: space-between
      padding: 1rem 0
      border-bottom: 1px solid $grey-4
      :last-child
        border: none
      &-timings
        display: flex
        flex-direction: column
        .availability-icon
          margin-left: 0.5rem
    .actions-container
      padding-top: 1rem
      display: flex
      justify-content: space-around
</style>
