<template>
  <q-page class="">
    <div class="layout-padding">
      <div class="q-ml-xs">
        <filters-section :filterType="filterKey" :jobType="jobType"/>
      </div>
      <!-- <div class="q-mt-md q-ml-sm">
        <b>Note</b><br/>
        <span class="text-grey-8">If you need more specific criteria go-back and select Do It Yourself job post.</span>
      </div> -->

      <!-- <q-field
        label="Candidate Selection"
        borderless
        readonly
        hide-bottom-space
        >
          <template v-slot:prepend>
            <q-icon name="group_add" />
          </template>
          <template v-slot:control>
            <div class="full-width q-mt-md">
              <q-radio class="" v-model="managedBy" :val="types.managedBy.user">
                <span class="q-option-label">
                  Managed by User<br/>
                  <small class="text-grey">
                    Let me select helpers to invite.
                  </small>
                </span>
              </q-radio>
              <q-radio class="" v-model="managedBy" :val="types.managedBy.system">
                <span class="q-option-label">
                  Managed by System<br/>
                  <small class="text-grey">
                    Let the system invite helpers and only let me know of successful applications.
                  </small>
                </span>
              </q-radio>
          </div>
        </template>
      </q-field> -->
    </div>
  </q-page>
</template>

<script>
import useVuelidate from '@vuelidate/core';
// import { required } from '@vuelidate/validators';
import { mapState } from 'vuex';
import FiltersSection from 'components/FiltersSection';
import { beforeRouteEnter, beforeLeaveStep } from './multistep-helper';
import NotificationService from '../../utils/notification-service';
import c from '../../utils/constants';

export default {
  name: 'PageOneOffJobStep4',
  components: { FiltersSection },
  data() {
    return {
      types: {
        managedBy: c.managedBy,
      },
    };
  },
  setup() {
    return {
      v$: useVuelidate(),
    };
  },
  validations: {
    // specialInstructions: {
    //   required,
    // },
  },
  computed: {
    ...mapState({
      job: (state) => state.postOneOffJob.jobDetails.data,
      meta: (state) => state.postOneOffJob.jobDetails.meta,
      helpersMeta: (state) => state.filters.meta,
    }),
    storeModuleName() {
      return 'postOneOffJob.jobDetails';
    },
    filterKey() {
      return 'postOneOffJob';
    },
    jobType() {
      return this.job.jobType;
    },
    specialInstructions: {
      get() {
        return this.job.specialInstructions;
      },
      set(value) {
        this.setProperty('specialInstructions', value);
      },
    },
    managedBy: {
      get() {
        return this.job.managedBy;
      },
      set(value) {
        this.setProperty('managedBy', value);
      },
    },
  },
  watch: {

  },
  methods: {
    validate() {
      this.v$.$touch();
      const hasError = this.v$.$error;
      if (hasError) {
        NotificationService.negative('Please review highlighted fields.');
        return false;
      }

      if (this.helpersMeta.total < 1) {
        NotificationService.negative('No helpers match specified criteria.');
        return false;
      }

      return true;
    },
  },
  beforeRouteEnter(to, from, next) {
    beforeRouteEnter(to, from, next);
  },
  beforeRouteLeave(to, from, next) {
    beforeLeaveStep(this, to, from, next);
  },
};
</script>
