<template>
  <q-dialog
      v-model="isVisible"
      prevent-close
      @ok="onOk"
      @cancel="onCancel"
      @show="onShow"
      @hide="onHide"
    >
    <q-layout view="Lhh lpR fff" container class="bg-white">
      <q-header>
        <q-toolbar>
          <q-toolbar-title>
            COVID-19 Notice
          </q-toolbar-title>
          <q-btn flat>
            <q-icon name="clear" v-close-popup />
          </q-btn>
        </q-toolbar>
      </q-header>

      <q-page-container>
        <div class="layout-padding">
          <p>
            <span v-if="isVictoria">
              While Victoria is under Stage 4 restrictions parents cannot post
              new childcare and in-home tutoring jobs.
              <br/>
              <br/>
              Parents Australia-wide can post online jobs and Victorian helpers can apply.
            </span>
            <span v-else>The following 4 questions are mandatory:</span>
          </p>

          <div v-if="isVictoria">
            <p>
              Are you applying for an online job?
            </p>
            <div class="text-center">
              <q-btn-toggle
                v-model="covidQuestion1"
                toggle-color="secondary"
                :options="[
                  {label: 'Yes', value: 'yes'},
                  {label: 'No', value: 'no'},
                ]"
              />
            </div>
            <div v-if="!covidValid" class="q-my-sm text-center">
              <span class="text-red">{{errorMessage}}</span>
            </div>
          </div>
          <div v-else>
            <p>
              Are you currently in a period of Government mandated quarantine or self-isolation?
            </p>
            <div v-if="!covidValid" class="q-my-sm text-center">
              <span class="text-red">{{errorMessage}}</span>
            </div>
            <div class="text-center">
              <q-btn-toggle
                v-model="covidQuestion1"
                toggle-color="secondary"
                :options="[
                  {label: 'Yes', value: 'yes'},
                  {label: 'No', value: 'no'},
                ]"
              />
            </div>

            <p>
              Are you living with anybody currently in a period of Government mandated quarantine or self-isolation?
            </p>
            <div class="text-center">
              <q-btn-toggle
                v-model="covidQuestion2"
                toggle-color="secondary"
                :options="[
                  {label: 'Yes', value: 'yes'},
                  {label: 'No', value: 'no'},
                ]"
              />
            </div>

            <p>
              Have you been in contact with a known or suspected case of COVID-19?
            </p>
            <div class="text-center">
              <q-btn-toggle
                v-model="covidQuestion3"
                toggle-color="secondary"
                :options="[
                  {label: 'Yes', value: 'yes'},
                  {label: 'No', value: 'no'},
                ]"
              />
            </div>

            <p>
              Are you exhibiting any symptoms of COVID-19, including high temperature, sore throat, coughing, runny nose, sneezing?
            </p>
            <div class="text-center">
              <q-btn-toggle
                v-model="covidQuestion4"
                toggle-color="secondary"
                :options="[
                  {label: 'Yes', value: 'yes'},
                  {label: 'No', value: 'no'},
                ]"
              />
            </div>
            <div v-if="!covidValid" class="q-my-sm text-center">
              <span class="text-red">{{errorMessage}}</span>
            </div>
          </div>
          <!-- v-if="covidQuestion1 === 'no' && covidQuestion2 === 'no' && covidQuestion3 === 'no' && covidQuestion4 === 'no'" -->
        </div>
        <div class="bg-white text-left q-my-lg">
          <q-btn color="secondary" label="Cancel" class="q-mx-sm" v-close-popup />
          <q-btn
            v-if="covidValid"
            color="accent"
            label="Submit"
            @click="onOk" />
        </div>
      </q-page-container>

      <!-- <q-footer class="bg-white">
        <q-toolbar>
          <q-btn color="secondary" label="Cancel" class="q-mx-sm" v-close-popup />
          <q-btn
            v-if="covidValid"
            color="accent"
            label="Submit"
            @click="onOk" />
          </q-toolbar>
        </q-footer> -->
      </q-layout>
    </q-dialog>
</template>

<script>
import { mapState } from 'vuex';

export default {
  name: 'Covid19Modal',
  emits: ['covidNoticeCompleted'],
  data() {
    return {
      name: '',
      isVisible: false,
      covidQuestion1: '',
      covidQuestion2: '',
      covidQuestion3: '',
      covidQuestion4: '',
      callback: null,
    };
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
    }),
    isVictoria() {
      // return this.userInfo.country === 'au' && this.userInfo.defaultAddress.state === 'vic';
      return false;
    },
    errorMessage() {
      let hasAnswered = false;

      if (this.isVictoria) {
        hasAnswered = this.covidQuestion1 !== '';
      } else {
        hasAnswered = this.covidQuestion1 !== '' && this.covidQuestion2 !== ''
          && this.covidQuestion3 !== '' && this.covidQuestion4 !== '';
      }

      if (!hasAnswered || this.covidValid) return '';

      return 'You are not eligible to apply for this job.';
    },
    covidValid() {
      if (this.isVictoria) {
        return this.covidQuestion1 === 'yes';
      }

      return this.covidQuestion1 === 'no' && this.covidQuestion2 === 'no'
        && this.covidQuestion3 === 'no' && this.covidQuestion4 === 'no';
    },
  },
  methods: {
    showDialog(cb) {
      this.isVisible = true;
      this.callback = cb;
    },
    onOk() {
      this.$emit('covidNoticeCompleted', { callback: this.callback });
      this.isVisible = false;
    },
    onCancel() {

    },
    onShow() {
    },
    onHide() {
    },
  },
};
</script>
