<template>
  <q-page class="post-recurring-job-step-7">
    <div class="layout-padding">
      <q-field
        borderless
        readonly
        hide-bottom-space
      >
        <template v-slot:prepend>
          <q-icon name="date_range" />
        </template>
        <template v-slot:control>
          {{getJobDateText()}}
        </template>
      </q-field>

      <q-field
        borderless
        readonly
        hide-bottom-space
      >
        <template v-slot:prepend>
          <q-icon name="home" />
        </template>
        <template v-slot:control>
          <div>
            <span v-if="addressLabel" class="text-primary text-bold">{{addressLabel}}<br/></span>
            {{getAddress()}}
          </div>
        </template>
      </q-field>

      <weekly-schedule-summary :job="job" :meta="meta" :minHours="0"/>

      <q-field
        label="Job Description"
        borderless
        readonly
        hide-bottom-space
      >
        <template v-slot:prepend>
          <q-icon name="assignment" />
        </template>
        <template v-slot:control>
          <div class="full-width q-mt-md">
            <p class="no-margin" v-for="(text, index) in job.specialInstructions.split('\n')" :key="index">
              {{text}}&nbsp;
            </p>
          </div>
        </template>
      </q-field>

      <filters-panel mode="jobDetails" filterType="postRecurringJob" :jobType="job.jobType" v-if="job.managedBy === types.managedBy.system" />

      <q-field
        borderless
        readonly
        hide-bottom-space
        label="Payment Method">
        <template v-slot:prepend>
          <q-icon name="monetization_on" />
        </template>
        <template v-slot:control>
          <div class="full-width q-mt-md">
            <span>{{getHelperPaymentMethod()}}</span>
          </div>
        </template>
      </q-field>

      <q-field
        v-if="selectedApplicants.length > 0"
        label="Invited Candidates"
        borderless
        readonly
      >
        <template v-slot:prepend>
          <q-icon name="people" />
        </template>
      </q-field>
      <div class="full-width invited-carers" style="margin-bottom: 50px">
        <helper-profile-card
          v-for="helper in selectedApplicants"
          :key="helper.id"
          :helper="helper"
          :displayDistance="true"
          mode="read-only"
        />
      </div>

      <q-btn
        big
        color="accent"
        class="full-width fixed-bottom"
        @click="handlePostJob">
          Post Job
      </q-btn>
    </div>
  </q-page>
</template>

<script>
import { date } from 'quasar';
import { mapState } from 'vuex';
import HelperProfileCard from 'components/HelperProfileCard';
import FiltersPanel from 'components/FiltersPanel';
import WeeklyScheduleSummary from 'components/WeeklyScheduleSummary';
import { beforeRouteEnter, beforeLeaveStep, postJob } from './multistep-helper';
import { getJobPaymentMethodText, getFormattedAddress } from '../../utils/helpers';
import c from '../../utils/constants';

export default {
  name: 'PageRecurringJobStep7',
  components: { HelperProfileCard, WeeklyScheduleSummary, FiltersPanel },
  data() {
    return {
      types: {
        managedBy: c.managedBy,
      },
    };
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
      job: (state) => state.postRecurringJob.jobDetails.data,
      meta: (state) => state.postRecurringJob.jobDetails.meta,
    }),
    selectedApplicants() {
      return this.meta.selectedApplicants;
    },
    addressLabel() {
      const address = this.userInfo.addresses.find((a) => a.id === this.job.addressId);
      if (!address) return '';

      return address.addressLabel;
    },
  },
  methods: {
    validate() {
      return true;
    },
    getAddress() {
      const address = this.userInfo.addresses.find((a) => a.id === this.job.addressId);
      return getFormattedAddress(address);
    },
    getJobDateText() {
      const {
        jobDate,
        jobStartType,
        jobStartDurationType,
        durationToStart,
        durationType,
        duration,
      } = this.job;

      let text = 'You are looking to fill a recurring job starting ';

      if (Number(jobStartType) === c.jobStartType.duration) {
        text += `within ${durationToStart} `;
        if (Number(jobStartDurationType) === c.durationType.weeks) {
          text += 'weeks';
        } else {
          text += 'months';
        }
      } else {
        text += `on ${date.formatDate(jobDate, 'ddd D MMM YY')}`;
      }

      text += `, for ${duration} `;
      if (durationType === c.durationType.weeks) {
        text += 'weeks.';
      } else {
        text += 'months.';
      }

      return text;
    },
    handlePostJob() {
      postJob(this.$router);
    },
    getHelperPaymentMethod() {
      return getJobPaymentMethodText(this.job.helperPaymentMethod);
    },
  },
  beforeRouteEnter(to, from, next) {
    beforeRouteEnter(to, from, next);
  },
  beforeRouteLeave(to, from, next) {
    beforeLeaveStep(this, to, from, next);
  },
};
</script>
<style lang="sass">
.post-recurring-job-step-7
  .schedule-table
    margin-bottom: 1rem
</style>
