<template>
  <div>
    <div>
      <p>
        You can receive payments from parents by cash and online bank transfer.
      </p>
      <p>
        To allow parents to pay you via Online Bank Transfer as well as Cash select the Online option below:
      </p>
      <p>
        <q-checkbox v-model="interestedInCashPayments" label="Cash" disable/>
        <br/>
        <span>Parents will pay you the agreement of cash directly at the end of each job</span>
      </p>
      <p>
        <q-checkbox v-model="interestedInBankTransfers" label="Online Bank Transfer" />
        <!-- <small class="text-italic q-mx-sm">(coming soon)</small> -->
        <br/>
        <span>Parents will transfer the agreement amount of money to your nominated bank account at the end of each job</span>
        <br/>
        <!-- <span class="text-red text-bold">Note</span> If you are not interested in online payments, please opt-out of this feature. -->
      </p>
    </div>

    <q-field
      v-if="interestedInBankTransfers"
      class="group"
      label="Bank Account Details"
      borderless
      readonly
    >
      <template v-slot:prepend>
        <q-icon name="account_balance" />
      </template>
      <template v-slot:control>
        <div class="full-width q-mt-md">
          <q-input
            borderless
            v-model="bankAccountName"
            :error="v$.bankAccountName.$error"
            label="Bank Account Name"
            hide-bottom-space
          />
          <q-input
            class="q-my-sm"
            borderless
            bottom-slots
            v-model="bankAccountBsb"
            :error="v$.bankAccountBsb.$error"
            error-message="6 digit BSB"
            label="BSB Number"
            v-if="userInfo.country === 'au'"
            hide-bottom-space
          />
          <q-input
            class="q-my-sm"
            borderless
            v-model="bankAccountNumber"
            :error="v$.bankAccountNumber.$error"
            label="Bank Account Number"
            error-message="Minimum 5 digits"
            hide-bottom-space
          />
          <span>{{getBankAccountVerificationText}}</span>
        </div>
      </template>
    </q-field>
    <div>
      <span class="text-red text-bold">Note</span>
      <br/>
      Parents decide the payment method for each job they post.
      <br/>
      Your bank details will only be released to parents when you are awarded a job with Online Bank Transfer.
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import useVuelidate from '@vuelidate/core';
import { required, minLength } from '@vuelidate/validators';
import { getBankAccountVerificationText } from '../utils/helpers';
import c from '../utils/constants';

export default {
  name: 'PaymentMethods',
  emits: ['change', 'complete'],
  data() {
    const data = {

    };

    return data;
  },
  methods: {
    isValid() {
      const cashOnly = !this.interestedInBankTransfers;
      if (cashOnly) return true;

      return !this.v$.$invalid;
    },
    validate() {
      const isValid = this.isValid();
      if (isValid) return true;

      this.v$.$touch();
      return !this.v$.$error;
    },
    triggerChange() {
      const isValid = this.isValid();
      this.$emit('change', { valid: isValid });

      if (isValid) {
        this.triggerComplete();
      }
    },
    triggerComplete() {
      this.$emit('complete');
    },
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
      paymentInfo: (state) => state.userInfo.data.paymentInfo,
      currentStep: (state) => {
        const { meta } = state.profileWizard;
        const currentStep = meta.steps[meta.currentStep];
        return currentStep;
      },
    }),
    storeModuleName() {
      return 'userInfo';
    },
    getBankAccountVerificationText() {
      if (this.paymentInfo.bankAccountVerificationStatus === c.bankAccountVerificationStatus.unspecified) return '';

      return `Verification Status: ${getBankAccountVerificationText(this.paymentInfo.bankAccountVerificationStatus, this.paymentInfo.bankAccountVerificationMessage)}`;
    },
    interestedInCashPayments: {
      get() {
        return this.paymentInfo.interestedInCashPayments;
      },
      set(value) {
        this.setProperty('paymentInfo.interestedInCashPayments', value);
        this.triggerChange();
      },
    },
    interestedInBankTransfers: {
      get() {
        return this.paymentInfo.interestedInBankTransfers;
      },
      set(value) {
        this.setProperty('paymentInfo.interestedInBankTransfers', value);
        this.triggerChange();
      },
    },
    bankAccountName: {
      get() {
        return this.paymentInfo.bankAccountName;
      },
      set(value) {
        this.setProperty('paymentInfo.bankAccountName', value);
        this.triggerChange();
      },
    },
    bankAccountBsb: {
      get() {
        return this.paymentInfo.bankAccountBsb;
      },
      set(value) {
        this.setProperty('paymentInfo.bankAccountBsb', value);
        this.triggerChange();
      },
    },
    bankAccountNumber: {
      get() {
        return this.paymentInfo.bankAccountNumber;
      },
      set(value) {
        this.setProperty('paymentInfo.bankAccountNumber', value);
        this.triggerChange();
      },
    },
  },
  setup() {
    return {
      v$: useVuelidate(),
    };
  },
  validations: {
    bankAccountName: {
      required,
    },
    bankAccountBsb: {
      required(value) {
        if (this.userInfo.country === 'au') {
          return value !== null && (value.length === 6 || value.length === 7);
        }

        return true;
      },
    },
    bankAccountNumber: {
      required,
      minLength: minLength(5),
    },
  },
  mounted() {
    const { paymentInfo } = this.userInfo;

    if (paymentInfo.interestedInCashPayments === null) {
      this.setProperty('paymentInfo.interestedInCashPayments', false);
    }

    if (paymentInfo.interestedInBankTransfers === null) {
      this.setProperty('paymentInfo.interestedInBankTransfers', false);
    }
  },
};
</script>
