<template>
  <div>
    <h6 class="no-margin text-center">{{country === 'au' ? 'High School' : 'Secondary School'}}</h6>
    <q-field
      borderless
      stack-label
      hide-bottom-space
      label="Select age groups you work with">
      <template v-slot:prepend>
        <q-icon name="class" />
      </template>
      <template v-slot:control>
        <q-chip
          clickable
          outline
          :icon="item.selected ? 'check' : 'clear'"
          :color="item.selected ? 'primary' : 'black'"
          :text-color="item.selected ? 'white' : 'black'"
          v-for="(item, index) in userInfo.provider.highSchoolYears" :key="item.optionId"
          @click="handleItemClick('highSchoolYears', index)"
          class="q-mx-xs q-my-xs q-caption profile-tab-margin"
        >
          {{item.text}}
        </q-chip>
      </template>
    </q-field>

    <q-field
      label="Do you teach Study Skills?"
      :error="v$.canTeachStudySkills.$error"
      borderless
      stack-label
      hide-bottom-space
      >
      <template v-slot:control>
        <q-option-group
            type="radio"
            v-model="canTeachStudySkills"
            class="no-margin"
            inline
            :options="[
                { label: 'Yes', value: true },
                { label: 'No', value: false },
            ]"
        />
      </template>
    </q-field>
    <q-field
      label="Do you teach Exam Preparation?"
      :error="v$.canTeachExamPreparation.$error"
      borderless
      stack-label
      hide-bottom-space
      >
      <template v-slot:control>
        <q-option-group
            type="radio"
            v-model="canTeachExamPreparation"
            class="no-margin"
            inline
            :options="[
                { label: 'Yes', value: true },
                { label: 'No', value: false },
            ]"
        />
      </template>
    </q-field>
    <q-field
      borderless
      stack-label
      hide-bottom-space
      :error="v$.year12GraduationYear.$error"
      :label="gradYearText">
      <template v-slot:control>
        <div class="q-mx-md">
          <q-select
            v-model="year12GraduationYear"
            placeholder="Select"
            toggle
            separator
            :options="years"
            option-value="value"
            option-label="label"
            emit-value
            map-options
            style="width: 150px"
          />
        </div>
      </template>
    </q-field>

    <q-field
      :label="highSchoolSubjectsText"
      borderless
      hide-bottom-space
      stack-label
      ref="highSchoolTree">
      <template v-slot:prepend>
        <q-icon name="school" />
      </template>
      <template v-slot:control>
        <q-tree
          :nodes="highSchoolSubjects"
          color="primary"
          v-model:ticked="selectedHighSchoolSubjects"
          v-model:expanded="expandedHighSchoolSubjects"
          node-key="optionId"
          accordion
          tick-strategy="leaf"
        />
      </template>
    </q-field>

    <h6 class="no-margin text-center">Tutoring Experience</h6>
    <q-field
      label="Describe your Tutoring experience"
      :error="v$.myExperience3.$error"
      ref="myExperience3"
      borderless
      stack-label
      class="q-mx-sm"
    >
      <!-- <template v-slot:prepend>
        <q-icon name="border_color" />
      </template> -->
      <template v-slot:control>
        <div class="full-width">
          <q-input
            v-model="myExperience3"
            type="textarea"
            placeholder="How long have you been tutoring? What subjects are you passionate about? What academic qualifications do you have? What formal teaching experience do you have? Etc."
            :max-height="100"
            rows="5"
          />
        </div>
      </template>
      <template v-slot:hint>
        {{myExperience3Helper}}
      </template>
      <template v-slot:error>
        {{myExperience3Helper}}
      </template>
    </q-field>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import useVuelidate from '@vuelidate/core';
import NotificationService from '../utils/notification-service';
import * as mutationTypes from '../store/mutation-types';
import {
  getSelectedOptions,
  processOptionsTree,
  getExpandedOptions,
  scrollToElement,
} from '../utils/helpers';

export default {
  name: 'HelperTutoringHighSchoolPreferences',
  props: ['displayCategory'],
  emits: ['change'],
  data() {
    const {
      highSchoolSubjects,
    } = this.$store.state.userInfo.data.provider;
    const selectedHighSchoolSubjects = getSelectedOptions(highSchoolSubjects);
    const expandedHighSchoolSubjects = getExpandedOptions(highSchoolSubjects);

    // const yearsCount = 65;
    // const years = new Array(yearsCount).fill().reduce((acc) => {
    //   if (acc.length === 0) {
    //     const year = new Date().getFullYear() + 4;
    //     acc.push({ value: year, label: year.toString() });
    //   } else {
    //     const year = acc[acc.length - 1].value;
    //     acc.push({ value: year - 1, label: (year - 1).toString() });
    //   }

    //   return acc;
    // }, []);

    const years = [];
    const startingYear = 1960;
    const maxYear = new Date().getFullYear();
    let year = startingYear;
    while (year <= maxYear) {
      years.push({ value: year, label: year.toString() });
      year++;
    }

    const data = {
      selectedHighSchoolSubjects,
      expandedHighSchoolSubjects,
      years,
    };

    return data;
  },
  methods: {
    handleItemClick(section, index) {
      const options = this.userInfo.provider[section];
      const currentOption = options[index];

      const selectedOptions = [];
      options.forEach((option) => {
        let selected = false;
        if (option.optionId === currentOption.optionId) {
          selected = !option.selected;
        } else {
          // eslint-disable-next-line
          selected = option.selected;
        }

        if (selected) {
          selectedOptions.push(option.optionId);
        }
      });

      const payload = {
        propPath: `provider.${section}`,
        value: selectedOptions,
      };

      this.$store.commit(mutationTypes.UPDATE_USER_OPTIONS, payload);

      this.triggerChange();
    },
    isValid() {
      const hasSelectedYearsAndSubjects = this.hasSelectedHighSchoolYears && this.selectedHighSchoolSubjects.length === 0;
      if (!hasSelectedYearsAndSubjects) return false;

      if (this.myExperience3.length <= 100) return false;

      const hasSetRequiredFields = typeof this.canTeachStudySkills === 'boolean' && typeof this.canTeachExamPreparation === 'boolean'
        && this.year12GraduationYear !== null && this.year12GraduationYear.length > 0;
      if (!hasSetRequiredFields) return false;

      return true;
    },
    validate(notify) {
      if (this.hasSelectedHighSchoolYears && this.selectedHighSchoolSubjects.length === 0) {
        if (notify) {
          NotificationService.negative(`You must select ${this.country === 'au' ? 'high school' : 'secondary school'} subjects as you have selected ${this.country === 'au' ? 'high school' : 'secondary school'} age groups.`);
          scrollToElement(this.$refs.highSchoolTree.$el);
        }
        return false;
      }

      const hasValidHighSchoolSelections = this.hasSelectedHighSchoolYears && this.selectedHighSchoolSubjects.length > 0;

      if (!hasValidHighSchoolSelections) {
        if (notify) {
          NotificationService.negative(`Please select relevant ${this.country === 'au' ? 'high school' : 'secondary school'} years and subjects.`);
        }
        return false;
      }

      this.v$.$touch();
      if (this.v$.$error) {
        if (notify) {
          NotificationService.negative('Please fix all highlighted errors.');
          if (this.myExperience3.length === 0) {
            scrollToElement(this.$refs.myExperience3.$el);
          }
        }
        return false;
      }

      return true;
    },
    triggerChange() {
      const isValid = this.validate(false);

      // let { interestedInJobTypes } = this.userInfo;
      // interestedInJobTypes = toggleHomeTutoring(this.userInfo);

      // if (this.userInfo.interestedInJobTypes !== interestedInJobTypes) {
      //   this.setProperty('interestedInJobTypes', interestedInJobTypes);
      // }

      this.$emit('change', { valid: isValid });
    },
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
    }),
    storeModuleName() {
      return 'userInfo';
    },
    gradYearText() {
      return `What year did you complete (or will complete) ${this.country === 'au' ? 'Year 12' : 'Year 13'}?`;
    },
    highSchoolSubjectsText() {
      return this.country === 'au' ? 'High School Subjects' : 'Secondary School Subjects';
    },
    hasSelectedHighSchoolYears() {
      return this.userInfo.provider.highSchoolYears.filter((option) => option.selected).length > 0;
    },
    highSchoolSubjects() {
      const subjects = this.userInfo.provider.highSchoolSubjects;
      const tree = processOptionsTree(subjects);
      return tree;
    },
    myExperience3: {
      get() {
        return this.userInfo.provider.myExperience3;
      },
      set(value) {
        this.setProperty('provider.myExperience3', value);
        this.triggerChange();
      },
    },
    myExperience3Helper() {
      if (this.v$.myExperience3.$error && this.myExperience3) {
        return `required - ${100 - this.myExperience3.length} characters left`;
      }

      return 'required - min 100 characters';
    },
    canTeachStudySkills: {
      get() {
        return this.userInfo.provider.canTeachStudySkills;
      },
      set(value) {
        this.setProperty('provider.canTeachStudySkills', value);
        this.triggerChange();
      },
    },
    canTeachExamPreparation: {
      get() {
        return this.userInfo.provider.canTeachExamPreparation;
      },
      set(value) {
        this.setProperty('provider.canTeachExamPreparation', value);
        this.triggerChange();
      },
    },
    year12GraduationYear: {
      get() {
        return this.userInfo.provider.year12GraduationYear;
      },
      set(value) {
        this.setProperty('provider.year12GraduationYear', value);
        this.triggerChange();
      },
    },
  },
  watch: {
    selectedHighSchoolSubjects(selectedOptions) {
      const section = 'highSchoolSubjects';
      const payload = {
        propPath: `provider.${section}`,
        value: selectedOptions,
      };

      this.$store.commit(mutationTypes.UPDATE_USER_OPTIONS, payload);
      this.triggerChange();
    },
  },
  setup() {
    return {
      v$: useVuelidate(),
    };
  },
  validations: {
    canTeachStudySkills: {
      required(value) {
        if (this.hasSelectedHighSchoolYears) {
          return typeof value === 'boolean';
        }

        return true;
      },
    },
    canTeachExamPreparation: {
      required(value) {
        if (this.hasSelectedHighSchoolYears) {
          return typeof value === 'boolean';
        }

        return true;
      },
    },
    year12GraduationYear: {
      required(value) {
        if (this.hasSelectedHighSchoolYears) {
          return value !== null;
        }

        return true;
      },
    },
    myExperience3: {
      required(value) {
        if (this.hasSelectedHighSchoolYears) {
          return value !== null && value.length >= 100;
        }

        return true;
      },
    },
  },
};
</script>
