<template>
  <input ref="image-uploader" type="file" name="image" accept="image/*"
    style="display: none;" @change="handleSetImage" />
</template>

<script>
import { mapState, mapActions } from 'vuex';
import NotificationService from '../utils/notification-service';
import c from '../utils/constants';

export default {
  name: 'ImageUploader',
  emits: ['uploadSuccess', 'uploadFailure'],
  data() {
    return {};
  },
  methods: {
    ...mapActions([
      'updateUserInfo',
    ]),
    open() {
      this.$refs['image-uploader'].click();
    },
    handleSetImage(eventData) {
      const { files } = eventData.target;
      if (files.length === 1) {
        const self = this;
        const fr = new FileReader();
        fr.onload = (file) => {
          const fileSize = file.total;
          if (fileSize > c.maxImageFileSize) {
            NotificationService.negative('The picture is above the allowed 20mb limit.');
            return;
          }
          const fileData = file.target.result;

          self.setProperty('defaultImage.imageBinary', fileData);

          self.updateUserInfo().then((update) => {
            if (update.success) {
              this.$emit('uploadSuccess');
            } else {
              this.$emit('uploadFailure');
            }
          }).catch((error) => {
            NotificationService.negative(error.message);
          });
        };
        fr.readAsDataURL(files[0]);
      }
    },
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
    }),
    storeModuleName() {
      return 'userInfo';
    },
  },
};
</script>
