<template>
  <div v-if="!isOwnProfile" :class="customClass">
    <q-btn size="10px" color="accent" v-if="canConnect && isApproved" @click="connect" icon="add" label="">
      Connect
    </q-btn>

    <q-btn size="10px" color="accent" v-if="canConnect && isPending" @click="pendingAccountNotice" icon="add">
      Connect
    </q-btn>

    <div v-if="canAccept" class="flex" style="align-items: center">
      <span style="font-size: 12px" class="text-primary" >
        Connection<br/>
        Invitation
      </span>
      <q-btn class="q-ml-xs" size="10px" color="accent" @click="accept" icon="mail" label="Accept" />
      <!-- <q-btn flat class="text-underline" color="accent" @click="accept" label="Accept" padding="none" /> -->
    </div>

    <q-btn size="14px" color="secondary" v-if="canReject" @click="handleReject" label="Unfriend" />

    <q-btn size="14px" color="accent" v-if="canChat" icon="chat" @click="handleChat" label="Chat" />

    <div class="q-px-sm box-primary-rounded q-py-xs" style="width: 100px; font-size: 10px" v-if="!canChat && displayConnectionStatus && (isConnectionApproved || isWaiting)">
      <q-icon name="check" size="18px" style="vertical-align: middle" class="q-mr-xs" /> CONNECTED
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex';
import c from '../utils/constants';
// import NotificationService from '../utils/notification-service';

export default {
  name: 'ClientActions',
  data() {
    return {
    };
  },
  props: {
    user: {
      default: undefined,
    },
    allowUnfriendAction: {
      default: false,
    },
    allowChat: {
      default: false,
    },
    allowAccept: {
      default: true,
    },
    allowConnect: {
      default: true,
    },
    customClass: {
      default: '',
    },
    displayConnectionStatus: {
      default: true,
    },
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
    }),
    canConnect() {
      return this.user.friendStatus === c.friendStatus.none && this.allowConnect;
    },
    canAccept() {
      return this.user.friendStatus === c.friendStatus.pending && this.user.actionRequired && this.allowAccept;
    },
    isWaiting() {
      return this.user.friendStatus === c.friendStatus.pending && !this.user.actionRequired;
    },
    canReject() {
      return this.user.friendStatus === c.friendStatus.approved && this.allowUnfriendAction;
    },
    canChat() {
      return false;
    },
    isConnectionApproved() {
      return this.user.friendStatus === c.friendStatus.approved;
    },
    isOwnProfile() {
      return this.userInfo.id === this.user.id;
    },
  },
  methods: {
    ...mapActions(['sendFriendRequest']),
    async handleChat() {
      if (this.isPending) {
        this.pendingAccountNotice();
      } else if (this.userInfo.membershipType === c.membershipType.free) {
        this.displayChatNotice();
      } else {
        this.navigateToChat();
      }
    },
    navigateToChat() {
      const url = `/chat/with/${this.user.id}`;
      this.routerPush(url);
    },
    displayChatNotice() {
      this.$q.dialog({
        title: `Chat with ${this.user.publicName}`,
        message: 'Chat is only available for helpers with an active membership.',
        ok: {
          label: 'Subscribe',
          outline: true,
        },
        cancel: {
          label: 'Later',
          color: 'secondary',
          outline: true,
        },
      }).onOk(() => {
        this.routerPush('/account/helper-membership');
      }).onCancel(() => {});
    },
    displayConnectionNotice() {
      this.$q.dialog({
        title: `Connect with ${this.user.publicName}`,
        message: 'Connecting with users is only available for helpers with an active membership.',
        ok: {
          label: 'Subscribe',
          outline: true,
        },
        cancel: {
          label: 'Later',
          color: 'secondary',
          outline: true,
        },
      }).onOk(() => {
        const query = this.userInfo.paymentInfo.renewalPaymentType !== c.userPaymentType.none ? '?upgradeNow=1' : '';
        this.routerPush(`/account/helper-membership${query}`);
      }).onCancel(() => {});
    },
    processFriendRequest(status) {
      const payload = {
        friendId: this.user.id,
        status,
      };

      if (this.user.requestId && this.user.requestId > 0) {
        payload.id = this.user.requestId;
      } else if (this.user.friendshipId && this.user.friendshipId > 0) {
        payload.id = this.user.friendshipId;
      }

      this.sendFriendRequest(payload);
    },
    pendingAccountNotice() {
      let message;
      let ok;
      let cancel;
      if (this.userInfo.profileCompleteness === 100) {
        message = 'Your account is being reviewed and will be approved shortly.';
        ok = {
          label: 'Ok',
          color: 'primary',
          outline: true,
        };
      } else {
        message = 'Connecting with users is only available for helpers with a complete profile and an active membership.';
        ok = {
          label: 'Complete Profile',
          color: 'primary',
          outline: true,
        };
        cancel = {
          label: 'Later',
          color: 'secondary',
          outline: true,
        };
      }

      this.$q.dialog({
        title: `Connect with ${this.user.publicName}`,
        message,
        cancel,
        ok,
      }).onOk(() => {
        if (this.userInfo.profileCompleteness < 100) {
          this.routerReplace('/helper-profile-wizard');
        }

        // Do nothing.
      }).onCancel(() => {});
    },
    connect() {
      if (this.userInfo.membershipType === c.membershipType.free && this.userInfo.ageInYears >= 18) {
        this.displayConnectionNotice();
        return;
      }

      const title = `Connect with ${this.user.publicName}`;
      const message = `${this.user.publicName} will be moved to your "My Connections" section`;

      this.$q.dialog({
        title,
        message,
        cancel: {
          label: 'Cancel',
          color: 'secondary',
          outline: true,
        },
        ok: {
          label: 'Invite',
          color: 'primary',
          outline: true,
        },
      }).onOk(() => {
        this.processFriendRequest(c.friendStatus.pending);
      }).onCancel(() => {});
    },
    accept() {
      if (this.userInfo.membershipType === c.membershipType.free && this.userInfo.ageInYears >= 18) {
        this.displayConnectionNotice();
        return;
      }

      this.processFriendRequest(c.friendStatus.approved);
      // NotificationService.positive(`${this.user.publicName} has been moved to your "${this.userInfo.jugglerTypes === c.jugglerType.client ? 'My Connections' : 'Connections'}" section.`);
    },
    resetConnection() {
      this.processFriendRequest(c.friendStatus.none);
    },
    handleReject() {
      this.$q.dialog({
        title: `Hide ${this.user.publicName}`,
        message: `Are you sure you want to permanently hide ${this.user.publicName}?`,
        cancel: {
          label: 'Cancel',
          color: 'secondary',
          outline: true,
        },
        ok: {
          label: 'Hide',
          color: 'primary',
          outline: true,
        },
      }).onOk(() => {
        this.processFriendRequest(c.friendStatus.rejected);
      }).onCancel(() => {});
    },
  },
};
</script>
