<template>
  <q-card class="helper-job-summary">
    <q-card-section @click="navigateToJobDetails">
      <div class="float-right">
        <div v-if="isIndividualJob" class="text-grey text-bold">
          <q-icon :name="helperPaymentMethodIcon" size="18px" /> {{helperPaymentMethodText}}
        </div>
        <div v-else class="q-px-md box-primary" style="width: 80px">Business</div>
      </div>
      <div>
        <div class="q-my-xs" v-if="job.applicationStatus === types.jobApplicationStatus.pending || job.applicationStatus === types.jobApplicationStatus.viewed">
          <small style="padding: 5px; border-radius: 5px;" class="bg-light-red text-white">response required</small>
        </div>
        <div class="q-my-xs" v-if="job.applicationStatus === types.jobApplicationStatus.applied || job.applicationStatus === types.jobApplicationStatus.shortlistedBySystem || job.applicationStatus === types.jobApplicationStatus.excludedByClient">
          <small style="padding: 5px; border-radius: 5px;" class="bg-green text-white">pending response from parent</small>
        </div>
        <span class="text-bold">{{jobTypeText}}</span>
        <br/>
        <span>{{jobDate}}</span>
      </div>
      <div v-if="job.applicationStatus == types.jobApplicationStatus.applied || job.applicationStatus == types.jobApplicationStatus.shortlistedByClient || job.applicationStatus == types.jobApplicationStatus.excludedByClient || job.applicationStatus == types.jobApplicationStatus.shortlistedBySystem || job.applicationStatus == types.jobApplicationStatus.awarded">
        <img class="round-user-image" :src="job.ownerImageSrc"/> {{job.ownerPublicName}}
      </div>
      <div v-else>
          <img class="round-user-image" :src="job.ownerImageSrc"/> {{job.ownerPublicName}} has invited you!
      </div>

      <div v-if="job.awardedApplicantGettingHome && job.applicationStatus == types.jobApplicationStatus.awarded && job.jobStatus == types.jobStatus.awarded && (job.jobType == job.jobType.babysitting || job.jobType == job.jobType.auPair)">
        Getting Home: <span> {{gettingHomeText}}</span>
      </div>
      <div v-else-if="job.applicationStatus == types.jobApplicationStatus.shortlistedByClient">
        <span class="text-green">Congratulations! You are shortlisted for this job.</span>
      </div>
      <div v-else-if="job.applicationStatus == types.jobApplicationStatus.excludedByClient">
        <span class="text-green">Bad luck! You were not shorlisted for this job.</span>
      </div>

      <div v-if="job.isRatedByProvider">
        You Rated:
        <q-rating
          v-model="jobRating"
          :max="5"
          color="accent"
          readonly />
      </div>
    </q-card-section>
    <q-card-actions>
      <div class="full-width text-center">
        <q-btn v-if="job.jobStatus == types.jobStatus.completed && (job.jobType == types.jobType.babysitting || job.jobType == types.jobType.auPair) && !job.isRatedByProvider" color="accent" @click="handleRateCarer" size="sm">Rate Parent</q-btn>
      </div>
    </q-card-actions>
  </q-card>
</template>

<script>
import c from '../utils/constants';
import {
  getJobPaymentMethodIcon, getJobPaymentMethodText, getGettingHomeTextForHelpers, formatJobDate, jobTypeText,
} from '../utils/helpers';

export default {
  name: 'HelperJobSummary',
  data() {
    return {
      types: {
        jobType: c.jobType,
        jobStatus: c.jobStatus,
        jobApplicationStatus: c.jobApplicationStatus,
      },
    };
  },
  props: ['job'],
  methods: {
    navigateToJobDetails() {
      this.routerPush(`/helpers/job-details/${this.job.id}`);
    },
    handleRateCarer() {
      this.routerPush(`/helpers/rate-family/${this.job.id}`);
    },
  },
  computed: {
    jobTypeText() {
      return jobTypeText(this.job.jobType, this.job.ownerClientType);
    },
    isIndividualJob() {
      return this.job.ownerClientType === c.clientType.individual;
    },
    jobRating() {
      return Math.round(this.job.ratingProvided);
    },
    gettingHomeText() {
      return getGettingHomeTextForHelpers(this.job.awardedApplicantGettingHome);
    },
    jobDate() {
      return formatJobDate(this.job);
    },
    helperPaymentMethodIcon() {
      return getJobPaymentMethodIcon(this.job.helperPaymentMethod);
    },
    helperPaymentMethodText() {
      return getJobPaymentMethodText(this.job.helperPaymentMethod);
    },
  },
};
</script>
<style lang="sass">
.helper-job-summary
  margin: 0.5rem 0
  border: 1px solid white
  line-height: 1.3rem
  .q-card-actions
    padding: 0 8px
</style>
