  <div :class="successClass">
    <div class="welcome-message">
      <p class="text-dark text-subtitle2" v-if="isIndividual">
        Congratulations {{userInfo.firstName}}, you're now part of the Juggle St community!
      </p>
      <p class="text-dark text-subtitle2" v-if="isBusiness">
        Congratulations {{userInfo.tradingName}}, you're now part of the Juggle St community!
      </p>
      <div class="start-now-button">
        <q-btn color="accent" @click="navigateToLearnMore">Get Started</q-btn>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import beforeLeaveStep from '../before-leave-step';
import store from '../../../store';
import c from '../../../utils/constants';

export default {
  name: 'ProfileWizardFamilySuccessStep',
  data() {
    return {
    };
  },
  methods: {
    navigateToLearnMore() {
      this.routerReplace({ path: '/dashboard' });
    },
    navigateToPostJob() {
      this.routerReplace({ path: '/post-job', query: { rest: 1 } });
    },
  },
  computed: {
    ...mapState({
      // meta: (state) => state.profileWizard.meta,
      userInfo: (state) => state.userInfo.data,
    }),
    welcomeMessageStyle() {
      return this.country === 'au' ? 'welcome-message welcome-message-au' : 'welcome-message welcome-message-nz';
    },
    successClass() {
      return `profile-wizard-success-screen ${this.isIndividual ? 'profile-wizard-family-success-screen' : 'profile-wizard-client-success-screen'} fullscreen relative-position`;
    },
  },
  beforeRouteLeave(to, from, next) {
    beforeLeaveStep(this, to, from, next);
  },
  beforeRouteEnter(to, from, next) {
    const { profileCompleteness, flaggedForReview } = store.getters.userInfo;

    if (profileCompleteness === 100 && flaggedForReview === c.flaggedForReviewType.approved) {
      next();
    } else {
      next({ path: '/dashboard', replace: true });
    }
  },
};
</script>
<style lang="sass">
.profile-wizard-family-success-screen
  background: url('/images/welcome-screen-background.jpg') no-repeat
  background-size: cover
.profile-wizard-client-success-screen
