  background-size: cover
.profile-wizard-success-screen
  .welcome-message
    text-align: center
    position: absolute
    height: 175px
    top: 6%
    left: 0
    width: 100vw
    background: rgb(238,238,238)
    opacity: 0.9
    padding: 1rem 0.5rem
    .title
      font-size: 1.2rem
