<template>
  <q-page class="">
    <dashboard-actions v-if="isApproved || isFamily"/>
    <pending-profile-banner v-if="isPending"/>

    <div v-if="isFamily && !pageInfo.isProcessing" class="q-my-xs">
      <q-chip
        clickable
        v-for="(item, index) in filterGroups" :key="item.id"
        :icon="groupFilter === item.id ? 'check_circle_outline' : 'radio_button_unchecked'"
        :color="groupFilter === item.id ? 'white' : 'white'"
        :text-color="groupFilter === item.id ? 'primary' : 'black'"
        @click="handleFamilyFilterGroupClick(index)"
        :class="'q-mx-xs q-my-xs q-caption profile-tab-margin' + (groupFilter === item.id ? ' text-bold' : '')"
      >
        {{item.text}}
      </q-chip>
      <div
        style="height: 40px; justify-content: space-between;"
        class="q-mx-sm row items-center">
        <span>
          {{nearMeSubtitle}}
        </span>
        <q-btn flat class="no-padding no-margin" @click="toggleRightPanel" v-if="displayAction('near-me-search-action')">
          <q-icon name="tune" class="text-secondary-lighter q-mr-xs"/>
          <span>Filters</span>
        </q-btn>
        <!-- <q-btn v-if="filterKey === 'allHelpersNearMe' && alerts.invitationAlerts > 0" flat class="no-padding q-mx-sm" @click="toggleConnections">
          <span>
            <q-icon name="favorite" color="primary" size="20px" />
            <q-chip class="" dense color="negative">{{alerts.invitationAlerts}}</q-chip>
          </span>
        </q-btn> -->
      </div>
    </div>

    <div v-if="isHelper" class="q-mx-sm">
      <div class="q-my-sm" v-if="isApproved">
        <div v-if="isSuperHelper" class="q-mb-sm">
          <q-icon name="star" size="24px" class="text-brand-gold q-mr-xs" />
          <span class="text-bold">Super Helper</span>
          <q-btn flat class="q-ml-xs text-underline" @click="handleHelperAdvancedSettings" label="Privileges" padding="none"></q-btn>
        </div>
        <response-stats v-if="userInfo.validApplicationsCount > 0" :displayDetails="false" />
        <profile-ranking v-else-if="!isSuperHelper" :displayDetails="false" />
      </div>

      <div class="">
        <div class="q-mb-sm">
          <q-icon name="home" size="24px" style="vertical-align: middle" class="text-tertiary" />
          {{address}}
          <q-btn v-if="isApproved" flat class="q-ml-xs text-underline" @click="handleChangeAddress" label="CHANGE" padding="none"></q-btn>
        </div>
        <div v-if="filterKey === 'familiesNearMe' && alerts.invitationAlerts > 0" class="q-mb-sm">
          <!-- <q-icon name="favorite_border" size="20px" style="vertical-align: middle" class="text-tertiary" /> -->
          <q-badge color="red">1+</q-badge>
          Connection Requests
          <q-btn flat class="q-ml-xs text-underline" @click="toggleConnections" label="REPLY" padding="none" />
        </div>
        <div class="q-mb-sm" v-if="displayAction('near-me-search-action')">
          <q-icon name="filter_list" size="24px" style="vertical-align: middle" class="text-tertiary" />
          {{nearMeSubtitle}}
          <q-btn flat class="q-ml-xs text-underline" @click="toggleRightPanel" label="EDIT" padding="none"></q-btn>
        </div>
      </div>
    </div>

    <p class="text-center" v-if="filterKey && filteredUsers.length === 0 && !pageInfo.isProcessing">
      <span>More {{isFamily ? 'helpers' : 'families'}} joining soon in your area.</span>
    </p>

    <div class="card-container" v-if="isFamily && pageInfo.isProcessing && pageState.pageIndex === 1">
      <helper-profile-card-skeleton v-for="index in 6" :key="index"/>
    </div>
    <div class="card-container" v-if="isFamily">
      <helper-profile-card
        v-for="helper in filteredUsers"
        :key="helper.id"
        mode="view"
        :filterKey="filterKey"
        :allowChat="true"
        :displayDistance="filterKey !== 'auPairsNearMe'"
        :helper="helper" />
    </div>

    <div class="card-container" v-if="isHelper && pageInfo.isProcessing && pageState.pageIndex === 1">
      <client-profile-card-skeleton v-for="index in 6" :key="index"/>
    </div>
    <div class="card-container" v-if="isHelper">
      <client-profile-card
        v-for="user in filteredUsers"
        :key="user.id"
        mode="view"
        defaultTab=""
        :client="user" />
    </div>
    <div class="q-my-sm text-center" v-if="hasMore">
      <q-btn rounded outline label="Load More" color="primary" @click="loadMore"/>
    </div>
    <!-- <q-page-scroller position="bottom-right" :scroll-offset="150" :offset="[18, 18]">
      <q-btn fab icon="keyboard_arrow_up" color="secondary" />
    </q-page-scroller> -->
  </q-page>
</template>

<script>
import HelperProfileCard from 'components/HelperProfileCard.vue';
import HelperProfileCardSkeleton from 'components/HelperProfileCardSkeleton.vue';
import ClientProfileCard from 'components/ClientProfileCard.vue';
import ClientProfileCardSkeleton from 'components/ClientProfileCardSkeleton.vue';
import PendingProfileBanner from 'components/PendingProfileBanner.vue';
import DashboardActions from 'components/DashboardActions.vue';
import ProfileRanking from 'components/ProfileRanking.vue';
import ResponseStats from 'components/ResponseStats.vue';
import { mapState, mapGetters, mapActions } from 'vuex';
import store from '../store';
import { formatNumber, getFormattedAddress } from '../utils/helpers';
import c from '../utils/constants';
import * as mutationTypes from '../store/mutation-types';

export default {
  name: 'PageDashboard',
  components: {
    HelperProfileCard,
    ClientProfileCard,
    PendingProfileBanner,
    DashboardActions,
    HelperProfileCardSkeleton,
    ClientProfileCardSkeleton,
    ProfileRanking,
    ResponseStats,
  },
  data() {
    return {
      groupFilter: null,
    };
  },
  methods: {
    ...mapActions({
      applyFilter: 'filters/applyFilter',
    }),
    loadMore() {
      this.setPageState('pageIndex', this.pageState.pageIndex + 1);
      this.refreshFilter();
    },
    navigateToMyJobs() {
      if (this.isFamily) {
        this.routerReplace({ path: '/families/all-jobs' });
      } else {
        this.routerReplace({ path: '/helpers/upcoming-jobs' });
      }
    },
    getAlert(id) {
      if (!id) return null;

      const alertValue = this.alerts[id];
      if (!alertValue || alertValue === 0) return null;

      return alertValue;
    },
    toggleConnections() {
      this.routerPush({ path: '/connections/invitations' });
    },
    handleChangeAddress() {
      this.routerPush({ path: '/account/my-addresses' });
    },
    handleHelperAdvancedSettings() {
      this.routerPush({ path: '/account/advanced-helper-settings' });
    },
    toggleRightPanel() {
      this.$store.dispatch('toggleRightDrawer', true);
    },
    handleFamilyFilterGroupClick(index) {
      const group = this.filterGroups[index];
      if (this.groupFilter === group.id) return;

      this.groupFilter = group.id;
      this.$store.dispatch('setPageProcessing', { isProcessing: true });
      this.doubleRaf(() => {
        this.setPageState('pageIndex', 1);
        this.setFilterType(group.filterKey);
        this.routerPush(`/dashboard?defaultTab=${group.filterKey}`);
      });
    },
    handleHelperFilterGroupClick(index) {
      const group = this.filterGroups[index];
      if (this.groupFilter === group.id) return;

      this.groupFilter = group.id;
      const newFilterKey = group.filterKey;

      const neighbourhoodPayload = {
        filterType: newFilterKey,
        filterKey: 'neighbourhood',
        value: this.filterGroups[index].filterParam,
      };
      this.$store.commit(`filters/${mutationTypes.UPDATE_FILTER_OPTION}`, neighbourhoodPayload);

      this.setPageState('pageIndex', 1);
      this.setFilterType(newFilterKey);
    },
    refreshFilter() {
      this.setFilterType(this.groupFilter);
    },
    setFilterType(filterKey) {
      const filterPayload = {
        filterKey,
        pageIndex: this.pageState.pageIndex,
        pageSize: this.pageState.pageSize,
        displayLoader: this.pageState.pageIndex > 1,
      };
      this.$store.dispatch('filters/applyFilter', filterPayload);
    },
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
      users: (state) => state.filters.data,
      usersMeta: (state) => state.filters.meta,
      pageInfo: (state) => state.pageInfo,
      alerts: (state) => state.realtime.alerts,
    }),
    ...mapGetters({
      filterKey: 'filters/filterKey',
    }),
    isSuperHelper() {
      return this.userInfo.provider.isSuperProvider;
    },
    familyFilterGroups() {
      const groups = [
        {
          id: 'nanniesNearMe',
          filterKey: 'nanniesNearMe',
          text: this.isIndividual ? 'Nannies' : 'Recurring Jobs',
        },
        {
          id: 'babysittersNearMe',
          filterKey: 'babysittersNearMe',
          text: this.isIndividual ? 'Babysitters' : 'One-Off Jobs',
        },
        // {
        //   id: 'auPairsNearMe',
        //   filterKey: 'auPairsNearMe',
        //   text: 'Au Pairs',
        // },
      ];

      return groups;
    },
    helperFilterGroups() {
      const groups = [
        {
          id: 'familiesNearMe',
          filterKey: 'familiesNearMe',
          filterParam: c.neighbourhood.any,
          text: 'All',
        },
        {
          id: 'newFamilies',
          filterKey: 'familiesNearMe',
          filterParam: c.neighbourhood.new,
          text: 'New',
        },
        {
          id: 'connections',
          filterKey: 'familiesNearMe',
          filterParam: c.neighbourhood.myConnections,
          text: 'Connections',
        },
        {
          id: 'invitations',
          filterKey: 'familiesNearMe',
          filterParam: c.neighbourhood.invitations,
          text: 'Requests',
          alert: 'invitationAlerts',
        },
      ];

      return groups;
    },
    nearMeSubtitle() {
      if (this.pageInfo.isProcessing) return 'Searching...';

      return `Filter displaying ${formatNumber(this.filteredUsers.length)} of ${formatNumber(this.filteredGroupLength)}`;
    },
    address() {
      return getFormattedAddress(this.userInfo.defaultAddress);
    },
    filteredUsers() {
      return this.users;
    },
    filteredGroupLength() {
      return this.usersMeta.total;
    },
    filterGroups() {
      return this.isFamily ? this.familyFilterGroups : this.helperFilterGroups;
    },
    hasMore() {
      return (this.pageState.pageIndex * this.pageState.pageSize) < this.filteredGroupLength;
    },
  },
  beforeRouteEnter(to, from, next) {
    if (store.getters.isTutoringBusiness) {
      next({ path: '/dashboard-tutors', replace: true });
      return;
    }

    next(async (vm) => {
      store.dispatch('setPageProcessing', { isProcessing: true });
      if (!vm.pageState.pageSize || !vm.pageState.pageIndex) {
        vm.setPageState('pageIndex', 1);
        vm.setPageState('pageSize', 50);
      }

      const queryDefaultTab = vm.$route.query.defaultTab;
      if (queryDefaultTab) {
        vm.groupFilter = queryDefaultTab;
      } else {
        vm.groupFilter = vm.isFamily ? 'nanniesNearMe' : 'familiesNearMe';
      }

      vm.doubleRaf(() => {
        if (queryDefaultTab) {
          vm.setFilterType(queryDefaultTab);
        } else {
          vm.setFilterType(vm.isFamily ? 'nanniesNearMe' : 'familiesNearMe');
        }

        store.dispatch('realtime/fetchPendingStatsDirect');
      });
    });
  },
};
</script>
