<template>
  <q-page class="layout-padding">
    <card-details-2 ref="cardDetails" :defaultPaymentMethod="paymentMethod" @paymentMethodChanged="handlePaymentMethodChanged"/>
    <div class="q-mb-md">
      <payment-summary
        :paymentType="userPaymentType"
      />
    </div>
    <div class="fixed-bottom">
      <q-btn class="full-width" @click="handleSubmit()" color="accent">
        Submit
      </q-btn>
    </div>
  </q-page>
</template>

<script>
import { mapActions } from 'vuex';
import CardDetails2 from 'components/CardDetails2';
import PaymentSummary from 'components/PaymentSummary';
import NotificationService from '../../utils/notification-service';
import c from '../../utils/constants';

export default {
  name: 'PageUpgradeClientAccount',
  components: { CardDetails2, PaymentSummary },
  data() {
    const hasExistingCard = this.$store.getters['payments/hasExistingCard'];
    return {
      paymentMethod: hasExistingCard ? c.paymentMethod.existingCard : c.paymentMethod.newCard,
      userPaymentType: this.$route.params.userPaymentType,
    };
  },
  methods: {
    ...mapActions('payments', [
      'makePayment',
    ]),
    ...mapActions([
      'refreshUserInfo',
      'submitTransactionInfo',
    ]),
    handlePaymentMethodChanged(value) {
      this.paymentMethod = value;
    },
    async handleSubmit() {
      await this.processPayment(this.paymentMethod === c.paymentMethod.newCard);
    },
    // handleUseExistingCard() {
    //   this.processPayment(false);
    // },
    async processPayment(useNewCard) {
      const { cardDetails } = this.$refs;
      if (useNewCard && !cardDetails.validate()) {
        NotificationService.negative('Please enter valid card details.');
        return;
      }
      this.loader.show();

      const paymentType = parseInt(this.$route.params.userPaymentType, 10);

      const payload = {
        deviceType: this.deviceType,
        actionType: c.membershipActionType.activateMembership,
        paymentType,
      };

      if (useNewCard) {
        const tokenId = await cardDetails.createToken();
        if (!tokenId) {
          NotificationService.negative('Invalid credit card.');
          this.loader.hide();
          return;
        }
        payload.tokenId = tokenId;
      }

      this.makePayment(payload)
        .then(() => {
          this.submitTransactionInfo({ paymentType });
          this.refreshUserInfo()
            .then(() => {
              NotificationService.positive('Your membership has been updated successfully.');
              this.routerReplace('/account/payments');
              this.loader.hide();
            })
            .catch((error) => {
              NotificationService.negative(error.message);
              this.loader.hide();
            });
        })
        .catch((error) => {
          this.loader.hide();
          NotificationService.negative(error.message || 'Failed to update your payment information.');
        });
    },
  },
  computed: {
    storeModuleName() {
      return 'payments';
    },
  },
};
</script>
<style lang="sass">
#card-errors
  color: red
  height: 1rem
  padding: 0.5rem
  text-align: center
#card-element
  margin-top: 0
</style>
