<template>
  <div class="layout-padding">
    <div class="group">
      <!-- <q-field
        label="Are you interested to work as a live-in Au pair?"
        v-if="allowOptOut"
        stack-label
        borderless
        hide-bottom-space
      >
        <template v-slot:prepend>
          <q-icon name="child_care" />
        </template>
        <template v-slot:control>
          <q-radio class="q-mx-sm" v-model="interestedInAuPairJobs" :val="true" label="Yes" />
          <q-radio class="q-mx-sm" v-model="interestedInAuPairJobs" :val="false" label="No" />
        </template>
      </q-field> -->

      <q-field
        label="Au Pair"
        borderless
        hide-bottom-space
        readonly>
        <template v-slot:prepend>
          <q-icon name="settings" />
        </template>
        <template v-slot:control>
          <div class="full-width q-mt-md">
            <p>
              Important notice, please read carefully
            </p>
            <div class="text-grey">
              <p>
                Au Pairs live-in with a host family 7 days a week and look after the family’s children. In return, Au Pairs receive a small weekly allowance plus lodging and meals.
              </p>
              <p>
                Availability – you need to be able to move in with a host family and start your Au Pair job within the next 4 weeks.
              </p>
            </div>
            <div>
              <q-radio class="q-mx-sm" v-model="acceptTerms" :val="true" label="I acknowledge & agree with all of the above" />
              <q-radio class="q-mx-sm" v-model="acceptTerms" :val="false" :label="notInterestedLabel" />
            </div>
          </div>
        </template>
      </q-field>

    </div>

    <div v-if="interestedInAuPairJobs && acceptTerms">
      <q-field
        label="Citizenship and Right to Work"
        borderless
        stack-label
        hide-bottom-space
      >
        <template v-slot:prepend>
          <q-icon name="location_city" />
        </template>
        <template v-slot:control>
          <div class="full-width">
            <vevo-details ref="vevoDetails" v-on:change="handleVevoChange" v-on:complete="handleVevoComplete" />
          </div>
        </template>
      </q-field>

      <q-field
        label="Au Pair Experience"
        borderless
        stack-label
        hide-bottom-space
      >
        <template v-slot:prepend>
          <q-icon name="flag" />
        </template>
        <template v-slot:control>
          <div class="full-width">
            <q-field
              :error="v$.auPairCategory.$error"
              borderless
            >
              <template v-slot:control>
                <div class="full-width">
                  <p class="no-margin">
                    Have you worked as an Au Pair before?
                  </p>
                  <q-radio class="q-mx-sm" v-model="auPairCategory" :val="2" label="Yes" />
                  <q-radio class="q-mx-sm" v-model="auPairCategory" :val="1" label="No" />
                </div>
              </template>
            </q-field>
          </div>
        </template>
      </q-field>
      <q-field
        :error="v$.myExperience4.$error"
        borderless
        stack-label
        hide-bottom-space
        label="Describe your Au Pair experience"
        class="q-mx-sm"
      >
        <!-- <template v-slot:prepend>
          <q-icon name="border_color" />
        </template> -->
        <template v-slot:control>
          <q-input
            class="full-width"
            v-model="myExperience4"
            type="textarea"
            :placeholder="auPairHelper"
            rows="5"
          />
        </template>
        <template v-slot:hint>
          {{auPairExperienceHelper}}
        </template>
        <template v-slot:error>
          {{auPairExperienceHelper}}
        </template>
      </q-field>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import useVuelidate from '@vuelidate/core';
import { required, minLength, between } from '@vuelidate/validators';
import VevoDetails from 'components/VevoDetails';
import NotificationService from '../utils/notification-service';
import c from '../utils/constants';

export default {
  name: 'HelperAuPairPreferences',
  components: { VevoDetails },
  data() {
    const userInfo = this.$store.state.userInfo.data;
    const { interestedInJobTypes } = userInfo;

    let optInDefault = (interestedInJobTypes & c.jobType.auPair) === c.jobType.auPair;
    if (!optInDefault) {
      optInDefault = null;
    }

    const data = {
      // interestedInAuPairJobs: !this.allowOptOut || (interestedInJobTypes & c.jobType.auPair) === c.jobType.auPair,
      acceptTerms: optInDefault,
    };

    return data;
  },
  props: ['allowOptOut'],
  emits: ['complete', 'change'],
  methods: {
    isValid() {
      const mandatory = this.auPairCategory > 0 && this.myExperience4 && this.myExperience4.length >= 100 && this.acceptTerms;
      const optional = !this.$refs.vevoDetails || this.$refs.vevoDetails.isValid();

      if (this.interestedInAuPairJobs) {
        return mandatory && optional;
      }

      return true;
    },
    validate() {
      if (!this.interestedInAuPairJobs) return true;

      const isValid = this.isValid();
      if (isValid) return true;

      const isValidVevo = !this.$refs.vevoDetails || this.$refs.vevoDetails.validate();
      this.v$.$touch();
      if (this.v$.$error || !isValidVevo) {
        NotificationService.negative('Please review highlighted fields.');
      }

      return !this.v$.$error && isValidVevo;
    },
    handleVevoChange() {
      this.triggerChange();
    },
    checkJuggleStreetInterview() {
      // No longer required.
      const { vouches } = this.userInfo;
      const validInterview = vouches.find((v) => v.referenceType === c.vouchReferenceType.juggleStreet);

      if (validInterview) return;

      const newRef = {
        fullName: 'Juggle Street',
        phoneNumber: '02 8001 6263',
        email: 'team@jugglestreet.com',
        otherReferenceType: '',
        status: c.vouchStatus.pendingVerification,
        referenceType: c.vouchReferenceType.juggleStreet,
      };

      const refs = [...vouches];
      refs.push(newRef);
      this.setProperty('vouches', refs);
    },
    handleVevoComplete() {

    },
    triggerChange() {
      const isValid = this.isValid();

      let { interestedInJobTypes } = this.userInfo;
      if (isValid) {
        interestedInJobTypes |= c.jobType.auPair;
        // this.checkJuggleStreetInterview();
        this.$emit('complete');
      } else {
        interestedInJobTypes &= (~c.jobType.auPair);
      }

      if (this.userInfo.interestedInJobTypes !== interestedInJobTypes) {
        this.setProperty('interestedInJobTypes', interestedInJobTypes);
      }

      this.$emit('change', { valid: isValid });
    },
    triggerComplete() {
      this.$emit('complete');
    },
    setAuPairValue() {
      let { interestedInJobTypes } = this.userInfo;
      if (this.interestedInAuPairJobs && this.isValid()) {
        interestedInJobTypes |= c.jobType.auPair;
        this.checkJuggleStreetInterview();
        this.setProperty('interestedInJobTypes', interestedInJobTypes);
      } else if (!this.interestedInAuPairJobs) {
        interestedInJobTypes &= (~c.jobType.auPair);
        this.setProperty('interestedInJobTypes', interestedInJobTypes);
      }
    },
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
      currentStep: (state) => {
        const { meta } = state.profileWizard;
        const currentStep = meta.steps[meta.currentStep];
        return currentStep;
      },
    }),
    storeModuleName() {
      return 'userInfo';
    },
    interestedInAuPairJobs: {
      get() {
        return this.userInfo.interestedInAuPairJobs;
      },
      set(value) {
        this.setProperty('interestedInAuPairJobs', value);
      },
    },
    notInterestedLabel() {
      if (this.allowOptOut) return 'Not interested to work as an Au Pair';

      return 'Remove Au Pair from my Juggle Street profile. Note - this does NOT affect your other Childcare or Tutoring profiles.';
    },
    myExperience4: {
      get() {
        return this.userInfo.provider.myExperience4;
      },
      set(value) {
        this.setProperty('provider.myExperience4', value);
        this.triggerChange();
      },
    },
    auPairCategory: {
      get() {
        return this.userInfo.provider.auPairCategory;
      },
      set(value) {
        this.setProperty('provider.auPairCategory', value);
        this.triggerChange();
      },
    },
    auPairHelper() {
      if (this.auPairCategory === c.auPairCategory.experiencedAuPair) {
        return 'Provide details of your previous Au Pair experience';
      }

      return 'Provide details of your other childcare experience and explain why you believe you would make a good Au Pair';
    },
    auPairExperienceHelper() {
      if (this.v$.myExperience4.$error && this.myExperience4) {
        return `required - ${100 - this.myExperience4.length} characters left`;
      }

      return 'required - min 100 characters';
    },
  },
  watch: {
    // interestedInAuPairJobs() {
    //   this.setAuPairValue();
    // },
    acceptTerms(value) {
      this.interestedInAuPairJobs = value;
      this.setAuPairValue();
    },
  },
  setup() {
    return {
      v$: useVuelidate(),
    };
  },
  validations: {
    myExperience4: {
      required,
      minLength: minLength(100),
    },
    auPairCategory: {
      between: between(1, 10),
    },
  },
};
</script>
