<template>
  <div class="recurring-job-details" v-if="job.id && job.id > 0">
    <!-- <div class="bg-grey-2 row justify-center items-center" style="padding: 5px 0;" v-if="job.jobStatus === types.jobStatus.pending">
      <span style="margin-right: 5px;">Job post will expire in <strong class="text-red">{{job.expiresInDays}}</strong> days</span>
      <q-btn color="accent" @click="handleExtendJob" v-if="canExtendJob">Extend Job</q-btn>
    </div> -->
    <q-card flat>
      <q-card-section>
        <q-field
          borderless
          readonly
          hide-bottom-space
        >
          <template v-slot:prepend>
            <q-icon name="date_range" />
          </template>
          <template v-slot:control>
            {{getJobDateText}}
          </template>
        </q-field>

        <q-field
          borderless
          readonly
          hide-bottom-space
        >
          <template v-slot:prepend>
            <q-icon name="home" />
          </template>
          <template v-slot:control>
            <div>
              <span v-if="job.addressLabel" class="text-primary text-bold">{{job.addressLabel}}<br/></span>
              {{formattedAddress}}
            </div>
          </template>
        </q-field>

        <weekly-schedule-summary :job="job" :minHours="0"/>

        <q-field
          label="Job Description"
          borderless
          readonly
          hide-bottom-space
        >
          <template v-slot:prepend>
            <q-icon name="assignment" />
          </template>
          <template v-slot:control>
            <div class="full-width q-mt-md">
              <p style="margin: 0 5px" v-for="(text, index) in specialInstructions.split('\n')" :key="index">
                {{text}}&nbsp;
              </p>
              <span class="q-mx-md" v-if="job.specialInstructions && job.specialInstructions.length > 30 && job.specialInstructions.indexOf('\n') === -1" style="text-decoration: underline"
                @click="displayAllJobDetails = !displayAllJobDetails">{{displayAllJobDetails ? 'less' : 'more'}}</span>
            </div>
          </template>
        </q-field>
        <!-- <div>
          <span>{{jobTypeText}}</span>
          <br/>
          <span>{{jobDate}}</span>
        </div>
        <div>
          {{recurringJobPaymentText}}
        </div>
        <div>
          Total Weekly Payment: {{formatCurrency(job.pricePerWeek)}}
        </div>
        <div>
          <span>{{getHelperPaymentMethod()}}</span>
        </div> -->
        <!-- <div class="q-my-sm">
          Job Details:
          <br/>
          <p style="margin: 0 5px" v-for="(text, index) in specialInstructions.split('\n')" :key="index">
            {{text}}&nbsp;
          </p>
          <span v-if="job.specialInstructions && job.specialInstructions.length > 30 && job.specialInstructions.indexOf('\n') === -1" style="text-decoration: underline"
            @click="displayAllJobDetails = !displayAllJobDetails">{{displayAllJobDetails ? 'less' : 'more'}}</span>
        </div> -->
        <div v-if="job.managedBy === types.managedBy.system">
          <filters-panel mode="jobDetails" filterType="postRecurringJob" :jobType="job.jobType" />
        </div>
        <q-field
          borderless
          readonly
          hide-bottom-space
          label="Payment Method">
          <template v-slot:prepend>
            <q-icon name="monetization_on" />
          </template>
          <template v-slot:control>
            <div class="full-width q-mt-md">
              <span>{{getHelperPaymentMethod}}</span>
            </div>
          </template>
        </q-field>
        <!-- <div>
          Address: {{job.formattedAddress}}
        </div> -->
        <juggle-assist-job-analysis :job="job" />
      </q-card-section>
    </q-card>
    <div class="layout-padding bg-grey-2" v-if="job.jobStatus === types.jobStatus.pending">
      <div class="text-small">
        <q-icon name="info" color="secondary" style="margin: 0 2px; font-size: 20px"/>
        <small>You can award this job to as many carers as needed to cover your required shifts.</small>
      </div>
    </div>
    <div class="layout-padding" v-if="job.jobStatus === types.jobStatus.cancelled">
      <div>{{scheduledJobDate}}</div>
    </div>
    <div class="layout-padding" style="margin-bottom: 4rem;">
      <div style="margin: 5px 0; text-align: center;" v-if="job.jobStatus === types.jobStatus.pending && job.managedBy === types.managedBy.user">
        <div style="display: inline-block; width: 30%;">Applied: <span class="text-green">{{job.applicantsApplied}}</span></div>
        <div style="display: inline-block; width: 30%;">Declines: <span class="text-red">{{job.applicantsNotInterested + job.applicantsNotAvailable }}</span></div>
        <div style="display: inline-block; width: 30%;">Viewed: <span class="">{{job.applicantsViewed}}</span></div>
      </div>
      <div v-if="job.jobStatus === types.jobStatus.pending">
        <q-tabs v-model="applicantsTabsModel" class="applicant-groups bg-secondary text-white shadow-2">
          <q-tab
            v-for="(key, index) in Object.keys(applicants)"
            :key="key"
            :name="'applicant-tab-' + index"
            :icon="applicants[key].icon"
            :label="applicants[key].label"
            class="col-4"
          >
            <q-badge v-if="applicants[key].list.length > 0" color="red" floating>
              {{applicants[key].list.length}}
            </q-badge>
          </q-tab>
        </q-tabs>
        <q-tab-panels v-model="applicantsTabsModel" class="bg-transparent">
          <q-tab-panel
            class="applicants-tab-pane"
            v-for="(key, index) in Object.keys(applicants)"
            :key="key"
            :name="'applicant-tab-' + index"
          >
            <div v-for="(item, index) in applicants[key].list" :key="index">
              <applicant-card :applicant="item" :mode="key" v-on:awardJob="handleAwardJob"/>
              <q-separator />
            </div>
            <div class="text-faded" style="padding: 1rem;" v-if="applicants[key].list.length === 0">
              <span v-if="job.managedBy === types.managedBy.user">No carers match this criteria.</span>
              <span v-else>Applications will show up in this area...</span>
            </div>
          </q-tab-panel>
        </q-tab-panels>
      </div>

      <div>
        <div class="q-my-sm" v-if="job.jobStatus === types.jobStatus.pending && shortlistedApplicants.length > 0">
          <span style="margin: 2rem 0 1rem;">View applicants by day of the week </span>
        </div>
        <div class="q-my-sm" v-if="job.jobStatus === types.jobStatus.awarded || job.jobStatus === types.jobStatus.completed">
          <span style="margin: 2rem 0 1rem;">Awarded applicants by day of the week</span>
        </div>
        <q-tabs class="applicant-groups bg-secondary text-white shadow-2" v-model="availabilityTabsModel" v-if="Object.keys(availability).length > 0" color="dark">
          <q-tab
            v-for="(key, index) in Object.keys(availability)"
            :key="key"
            :name="'availability-tab-' + index"
            :label="key"
          />
        </q-tabs>
        <q-tab-panels v-model="availabilityTabsModel" v-if="Object.keys(availability).length > 0" class="bg-transparent">
          <q-tab-panel
            class="applicants-tab-pane"
            v-for="(key, index) in Object.keys(availability)"
            :key="key"
            :name="'availability-tab-' + index"
          >
            <q-tabs v-model="availabilityShiftTabsModel" color="secondary" class="shadow-2">
              <q-tab
                v-for="(availabilityKey, index) in Object.keys(availability[key])"
                :key="availabilityKey"
                :name="'availability-shift-tab-' + index"
                :label="availabilityKey"
              />
            </q-tabs>
            <q-tab-panels v-model="availabilityShiftTabsModel" class="bg-transparent">
              <q-tab-panel
                class="applicants-tab-pane"
                v-for="(availabilityKey, index) in Object.keys(availability[key])"
                :key="availabilityKey"
                :name="'availability-shift-tab-' + index"
              >
                <div v-if="availability[key][availabilityKey].length > 0" >
                  <applicant-card :applicant="item" mode="shortlisted" v-on:awardJob="handleAwardJob" v-for="(item, index) in availability[key][availabilityKey]" :key="index"/>
                </div>
                <div v-else>
                  <p class="text-center">
                    No candidates have applied for this shift.
                  </p>
                </div>
              </q-tab-panel>
            </q-tab-panels>
          </q-tab-panel>
        </q-tab-panels>
      </div>
      <p v-if="job.jobStatus === types.jobStatus.cancelled" class="">
        This job has been archived.
      </p>
      <!-- <p v-else-if="job.jobStatus === types.jobStatus.pending" class="">
        No one has applied for the job yet, you will get notified once applications are available.
      </p> -->
    </div>
    <award-job-modal  ref="award-job-modal" />
    <div class="job-actions" v-if="job.jobStatus === types.jobStatus.pending">
      <q-btn class="cancel-job" @click="handleCancelJob" color="red" icon="close" label="Cancel" stack size="12px"></q-btn>
      <q-btn @click="handleEditJob" class="bg-grey text-white" icon="mode_edit" label="Edit Job" stack size="12px"></q-btn>
      <q-btn v-if="job.managedBy === types.managedBy.user" class="invite-more" @click="handleInviteMore" color="primary" icon="person_add" label="Invite More" stack size="12px"></q-btn>
      <q-btn v-if="job.managedBy === types.managedBy.system" @click="handleChangeCandidateCriteria" color="primary" icon="group_add" label="Edit Criteria" stack size="12px"></q-btn>
    </div>
    <div class="job-actions" v-if="job.jobStatus === types.jobStatus.awarded">
      <q-btn class="cancel-job" @click="handleCancelJob" color="red" icon="close" label="Cancel" stack size="12px"></q-btn>
      <q-btn @click="handleDuplicateJob" color="secondary" icon="file_copy" label="Duplicate" stack size="12px"></q-btn>
    </div>
    <div class="job-actions" v-if="job.jobStatus === types.jobStatus.cancelled || job.jobStatus === types.jobStatus.completed">
      <q-btn @click="handleDuplicateJob" color="secondary" icon="file_copy" label="Duplicate" stack size="12px"></q-btn>
    </div>
  </div>
</template>

<script>
import { date } from 'quasar';
import { mapState } from 'vuex';
import ApplicantCard from './ApplicantCard';
import AwardJobModal from './AwardJobModal';
import FiltersPanel from '../../components/FiltersPanel';
import JuggleAssistJobAnalysis from '../../components/JuggleAssistJobAnalysis';
import WeeklyScheduleSummary from '../../components/WeeklyScheduleSummary';
import c from '../../utils/constants';
import * as mutationTypes from '../../store/mutation-types';
import {
  getJobTypeArray, getJobPaymentMethodText, getDayOfWeekText, formatTime, formatDateTime, formatJobDate, formatCurrency, deepClone, getTimeDifference, jobTypeText, getScheduledJobDate,
} from '../../utils/helpers';
import NotificationService from '../../utils/notification-service';

export default {
  name: 'RecurringJobDetails',
  components: {
    ApplicantCard, AwardJobModal, FiltersPanel, WeeklyScheduleSummary, JuggleAssistJobAnalysis,
  },
  data() {
    return {
      selectedTab: '',
      displayAllJobDetails: false,
      types: {
        jobType: c.jobType,
        jobStatus: c.jobStatus,
        jobApplicationStatus: c.jobApplicationStatus,
        applicantAvailabilityStatus: c.applicantAvailabilityStatus,
        managedBy: c.managedBy,
      },
      availabilityTabsModel: 'availability-tab-0',
      availabilityShiftTabsModel: 'availability-shift-tab-0',
    };
  },
  props: ['job'],
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
      // users: (state) => state.users.data,
    }),
    storeModuleName() {
      return 'families.pages.jobDetails';
    },
    getHelperPaymentMethod() {
      return getJobPaymentMethodText(this.job.helperPaymentMethod);
    },
    formattedAddress() {
      return this.job.formattedAddress;
    },
    specialInstructions() {
      if (!this.job.specialInstructions || this.job.specialInstructions.length === 0) {
        return 'None specified';
      }

      if (this.job.specialInstructions.length < 40 || this.job.specialInstructions.indexOf('\n') !== -1 || this.displayAllJobDetails) {
        return this.job.specialInstructions;
      }

      const idx = this.job.specialInstructions.indexOf(' ', 25);
      if (idx === -1) return this.job.specialInstructions;

      return `${this.job.specialInstructions.substring(0, idx)}...`;
    },
    recurringJobPaymentText() {
      const { price } = this.job;
      const isNegotiable = this.job.isPriceNegotiable;

      if (price === 0) {
        return '';
      }

      if (!price) {
        return `Payment: ${isNegotiable ? 'Negotiable' : 'Not negotiable'}`;
      }

      return `Payment: ${`$${price} p/h`} (${isNegotiable ? 'Negotiable' : 'Not negotiable'})`;
    },
    jobDate() {
      return formatJobDate(this.job);
    },
    canExtendJob() {
      // return this.job.expirationExtensionCount === 0 && this.job.expiresInDays <= 7;
      return false;
    },
    scheduledJobDate() {
      return getScheduledJobDate(this.job);
    },
    jobTypeText() {
      return jobTypeText(this.job.jobType, this.clientType);
    },
    getJobDateText() {
      const {
        jobDate,
        jobStartType,
        jobStartDurationType,
        durationToStart,
        durationType,
        duration,
      } = this.job;

      let text = 'You are looking to fill a recurring job starting ';

      if (Number(jobStartType) === c.jobStartType.duration) {
        text += `within ${durationToStart} `;
        if (Number(jobStartDurationType) === c.durationType.weeks) {
          text += 'weeks';
        } else {
          text += 'months';
        }
      } else {
        text += `on ${date.formatDate(jobDate, 'ddd D MMM YY')}`;
      }

      text += `, for ${duration} `;
      if (durationType === c.durationType.weeks) {
        text += 'weeks.';
      } else {
        text += 'months.';
      }

      return text;
    },
    applicantsTabsModel: {
      get() {
        /* eslint-disable */
        if (!this.selectedTab) {
          const { applicants } = this;
          let index;
          if (applicants.shortlisted.list.length > 0 || this.job.managedBy === c.managedBy.system) index = 0;
          else if (applicants.waiting.list.length > 0) index = 2;
          else index = 1;

          this.selectedTab = 'applicant-tab-' + index;
        }

        return this.selectedTab;
        /* eslint-enable */
      },
      set(value) {
        this.selectedTab = value;
      },
    },
    applicants() {
      if (this.job.managedBy === c.managedBy.user) {
        return {
          shortlisted: {
            icon: 'stars',
            label: 'Shortlisted',
            list: this.job.applicants.filter((applicant) => applicant.applicationStatus === c.jobApplicationStatus.applied
              || applicant.applicationStatus === c.jobApplicationStatus.shortlistedByClient
              || applicant.applicationStatus === c.jobApplicationStatus.shortlistedBySystem),
          },
          declined: {
            icon: 'clear',
            label: 'Declined',
            list: this.job.applicants.filter((applicant) => applicant.applicationStatus === c.jobApplicationStatus.withdrawn
              || applicant.applicationStatus === c.jobApplicationStatus.notAvailable
              || applicant.applicationStatus === c.jobApplicationStatus.notInterested),
          },
          waiting: {
            icon: 'hourglass_empty',
            label: 'Waiting',
            list: this.job.applicants.filter((applicant) => applicant.applicationStatus === c.jobApplicationStatus.pending
              || applicant.applicationStatus === c.jobApplicationStatus.viewed),
          },
        };
      }

      return {
        shortlisted: {
          icon: 'stars',
          label: 'Shortlisted Candidates',
          list: this.job.applicants.filter((applicant) => applicant.applicationStatus === c.jobApplicationStatus.applied
            || applicant.applicationStatus === c.jobApplicationStatus.shortlistedByClient
            || applicant.applicationStatus === c.jobApplicationStatus.shortlistedBySystem),
        },
      };
    },
    availability() {
      const data = this.job.applicants.reduce((acc, applicant) => {
        // if (
        //   !(
        //     applicant.applicationStatus === c.jobApplicationStatus.awarded ||
        //     applicant.applicationStatus === c.jobApplicationStatus.applied ||
        //     applicant.applicationStatus ===
        //       c.jobApplicationStatus.shortlistedByClient ||
        //     applicant.applicationStatus ===
        //       c.jobApplicationStatus.shortlistedBySystem
        //   )
        // ) {
        //   return acc;
        // }

        applicant.applicantAvailability.forEach((item) => {
          const dayKey = getDayOfWeekText(item.dayOfWeek, true);
          if (!acc[dayKey]) {
            acc[dayKey] = {};
          }

          const shiftKey = `${formatTime(item.jobStartTime)} - ${formatTime(item.jobEndTime)}`;
          if (!acc[dayKey][shiftKey]) {
            acc[dayKey][shiftKey] = [];
          }

          if (item.availabilityStatus === c.applicantAvailabilityStatus.available) {
            acc[dayKey][shiftKey].push(applicant);
          }
        });
        return acc;
      }, {});

      return data;
    },
    shortlistedApplicants() {
      return this.job.applicants
        .filter((applicant) => applicant.applicationStatus === c.jobApplicationStatus.applied
            || applicant.applicationStatus === c.jobApplicationStatus.shortlistedByClient
            || applicant.applicationStatus === c.jobApplicationStatus.shortlistedBySystem);
    },
  },
  methods: {
    formatCurrency(value) {
      return formatCurrency(value);
    },
    formatDateTime(value) {
      return formatDateTime(value);
    },
    // getHelperPaymentMethod() {
    //   return `Payment Method: ${getJobPaymentMethodText(this.job.helperPaymentMethod)}`;
    // },
    handleAwardJob({ newApplicant }) {
      const mappedApplicants = this.shortlistedApplicants
        .map((applicant) => ({
          value: applicant.applicantId,
          label: `${applicant.applicantFirstName} ${applicant.applicantLastInitial}`,
        }));

      this.$refs['award-job-modal'].open({
        applicants: mappedApplicants,
        selected: [newApplicant.applicantId],
      });
    },
    handleExtendJob() {
      if (this.userInfo.membershipType === c.membershipType.payAsYouGo) {
        this.routerReplace(`/families/extend-payg-job/${this.job.id}`);
      } else {
        this.setProperty('extendExpirationDateBy', 30);
        this.$store.dispatch('families/updateJobDetails').then(() => {
          NotificationService.positive('Job extended succesfully.');
        });
      }
    },
    handleEditJob() {
      const message = 'When you edit the job details it will be reposted, and you will lose your shortlist. Candidates who have already applied will be asked to re-apply.';
      this.$q.dialog({
        title: 'Edit Job',
        message,
        cancel: {
          label: 'Cancel',
          color: 'secondary',
          outline: true,
        },
        ok: {
          label: 'Edit Job',
          outline: true,
        },
      }).onOk(() => {
        this.editJob(false);
      }).onCancel(() => {});
    },
    handleDuplicateJob() {
      const message = 'Use this feature to post a new job based on the current job details, all applicable job settings are copied.';
      this.$q.dialog({
        title: 'Duplicate Job',
        message,
        cancel: {
          label: 'No',
          color: 'secondary',
          outline: true,
        },
        ok: {
          label: 'Yes',
          outline: true,
        },
      }).onOk(() => {
        this.editJob(true);
      }).onCancel(() => {});
    },
    handleCancelJob() {
      this.$q.dialog({
        title: 'Cancel Job',
        message:
          'If you cancel this job all applicants will be notified instantly and the cancelled job will be moved to the Job History section of “My Jobs”. ',
        cancel: {
          label: 'Don\'t Cancel',
          color: 'secondary',
          outline: true,
        },
        ok: {
          label: 'Cancel Job',
          outline: true,
        },
      }).onOk(() => {
        this.cancelJob();
      }).onCancel(() => {});
    },
    handleInviteMore() {
      this.routerReplace(`/families/invite-more-helpers/${this.job.id}`);
    },
    handleChangeCandidateCriteria() {
      this.routerReplace(`/families/change-candidate-criteria/${this.job.id}`);
    },
    cancelJob() {
      this.setProperty('jobStatus', c.jobStatus.cancelled);
      this.$store.dispatch('families/updateJobDetails').then(() => {
        NotificationService.positive('Job cancelled succesfully.');
        this.routerPush('/families/all-jobs');
      });
    },
    editJob(duplicate) {
      const existingJob = deepClone(this.job);

      const newJob = deepClone(this.$store.state.postRecurringJob.jobDetailsTemplate);
      Object.keys(newJob.data).forEach((key) => {
        if (existingJob[key]) {
          newJob.data[key] = existingJob[key];
        }
      });

      newJob.meta.selectedDays = [];
      existingJob.weeklySchedule.weeklyScheduleEntries.forEach((entry) => {
        if (newJob.meta.selectedDays.indexOf(entry.dayOfWeek) !== -1) return;

        newJob.meta.selectedDays.push(entry.dayOfWeek);
      });

      newJob.meta.weeklyScheduleEntries = newJob.meta.selectedDays.map((selectedDay, index) => ({
        index,
        dayOfWeek: selectedDay,
        shifts: existingJob.weeklySchedule.weeklyScheduleEntries
          .filter((entry) => entry.dayOfWeek === selectedDay)
          .map((entry) => {
            const hours = getTimeDifference(entry.jobStartTime, entry.jobEndTime);
            const hourlyPrice = Math.round(entry.price / hours);
            return {
              isRequired: true,
              jobStartTime: entry.jobStartTime,
              jobEndTime: entry.jobEndTime,
              hourlyPrice,
              price: entry.price,
              hours,
            };
          }),
      }));

      if (duplicate) {
        newJob.data.actionType = c.jobActionType.create;
        delete newJob.data.id;
        newJob.data.jobDate = null;
      } else {
        newJob.data.actionType = c.jobActionType.edit;
        newJob.data.id = this.job.id;

        if (newJob.data.jobDate) {
          const jobDate = new Date(newJob.data.jobDate);
          newJob.jobDate = jobDate;
          newJob.jobDateDay = jobDate.getDate();
          newJob.jobDateMonth = jobDate.getMonth() + 1;
          newJob.jobDateYear = jobDate.getFullYear();
        }
      }

      if (newJob.data.jobStartType === c.jobStartType.duration) {
        newJob.data.jobDate = null;
      }

      newJob.data.applicants = [];
      newJob.data.previousApplicants = [];
      newJob.data.selectedApplicants = [];
      existingJob.applicants.forEach((applicant) => {
        newJob.meta.previousApplicants.push(applicant.applicantId);
      });

      this.$store.dispatch('resetStoreStructure', { propPath: 'postRecurringJob.jobDetails', value: newJob });

      const jobTypeArray = getJobTypeArray(this.job.jobType);
      const jobTypePayload = {
        filterType: 'postRecurringJob',
        filterKey: 'jobTypes',
        value: jobTypeArray,
      };
      this.$store.commit(`filters/${mutationTypes.UPDATE_FILTER_OPTION}`, jobTypePayload);

      this.routerReplace(`/post-job?mode=edit&jobType=${this.job.jobType}&managedBy=${this.job.managedBy}`);
    },
  },
};
</script>
<style lang="sass">
.recurring-job-details
  .applicant-groups
    .q-tab-label
      font-size: 0.75rem
    .q-chip
      font-size: 75%
      min-height: 10px
      right: 10px
      font-weight: bold
    .q-tab:not(:last-child)
      border-right: 1px solid #fff
  .applicants-tab-pane
    padding: 0
  .job-actions
    position: fixed
    bottom: 0
    left: 0
    right: 0
    display: flex
    .q-btn
      flex: 1
      border-radius: 0
    .cancel-job
      flex-grow: 1.1
    .invite-more
      flex-grow: 1.4
</style>
