import * as types from '../mutation-types';
import { steps as helperSteps } from '../../pages/profile-wizard/helper/multistep-helper';
import { steps as clientSteps } from '../../pages/profile-wizard/client/multistep-helper';
import c from '../../utils/constants';

export default {
  namespaced: true,
  state: {
    data: {
    },
    meta: {
      dirty: false,
      isProcessing: false,
      currentStep: 0,
      lastStep: 0,
      steps: [],
    },
  },
  actions: {
    calculateSteps({ commit }, { jugglerTypes }) {
      const steps = jugglerTypes === c.jugglerType.client ? clientSteps : helperSteps;

      const computedSteps = steps.reduce((acc, item) => {
        const clonedItem = { ...item };
        delete clonedItem.component;
        acc.push({
          stepIndex: acc.length,
          path: clonedItem.path,
          complete: clonedItem.complete,
          meta: clonedItem,
        });
        return acc;
      }, []);

      commit(types.SET_HELPER_PROFILE_COMPLETENESS_STEPS, { computedSteps });
    },
    getStep({ state, rootState }, { forward }) {
      const userProfileItemsValue = rootState.userInfo.data.profileItems;
      const stepper = forward ? 1 : -1;
      let nextStep = state.meta.currentStep + stepper;
      while (nextStep <= state.meta.lastStep && nextStep >= 0) {
        const step = state.meta.steps[nextStep];

        const include = (!step.meta.shouldInclude || step.meta.shouldInclude(rootState.userInfo.data, rootState.userInfo.meta))
          && (step.meta.profileItem === null || (userProfileItemsValue & step.meta.profileItem) !== step.meta.profileItem);

        if (include) {
          return step;
        }

        nextStep += stepper;
      }

      return null;
    },
  },
  mutations: {
    [types.SET_HELPER_PROFILE_COMPLETENESS_STEPS]: (state, { computedSteps }) => {
      state.meta.steps = computedSteps;
      state.meta.currentStep = 0;
      state.meta.lastStep = computedSteps.length - 1;
    },
    [types.SET_STEP]: (state, payload) => {
      state.meta.currentStep = payload.step;
    },
    [types.INCREMENT_STEP]: (state) => {
      if (state.meta.currentStep + 1 <= state.meta.lastStep) {
        state.meta.currentStep += 1;
      }
    },
    [types.DECREMENT_STEP]: (state) => {
      if (state.meta.currentStep - 1 >= 0) {
        state.meta.currentStep -= 1;
      }
    },
    [types.UPDATE_STEP_STATUS]: (state, { complete, canSkip }) => {
      const { steps } = state.meta;
      const currentStep = steps[state.meta.currentStep];
      if (currentStep && typeof canSkip === 'boolean') {
        currentStep.meta.canSkip = canSkip;
      }

      if (currentStep && typeof complete === 'boolean') {
        currentStep.complete = complete;
      }

      state.meta.steps = steps;
    },
  },
  getters: {
    currentStep: (state) => state.meta.currentStep,
  },
};
