<template>
  <div class="client-unrated-job-summary q-my-sm">
    <q-card class="job-summary q-px-sm q-py-sm">
      <q-card-section>
        <div>
          <img class="round-user-image" :src="job.awardedImageSrc"/> {{job.awardedFirstName}} {{job.awardedLastInitial}}
          <q-btn class="float-right" color="accent" @click="handleRateHelper" size="sm">Rate Helper</q-btn>
        </div>
        <div>
          <span>{{jobTypeText}}</span>
          <br/>
          <span>{{jobDate}}</span>
        </div>
      </q-card-section>
    </q-card>
  </div>
</template>

<script>
import { env } from '../utils/env';
import c from '../utils/constants';
import { gettGettingHomeTextForFamilies, formatJobDate, jobTypeText } from '../utils/helpers';

export default {
  name: 'ClientUnratedJobSummary',
  data() {
    return {
      types: {
        jobType: c.jobType,
        jobStatus: c.jobStatus,
      },
    };
  },
  props: ['job'],
  methods: {
    getImageUrl() {
      return `${env.storageUrl}/images/${this.job.imageSrc}`;
    },
    handleRateHelper() {
      this.routerPush(`/families/rate-helper/${this.job.id}?ratingFor=${this.job.awardedApplicantId}`);
    },
  },
  computed: {
    jobTypeText() {
      return jobTypeText(this.job.jobType, this.clientType);
    },
    jobRating() {
      return Math.round(this.job.ratingProvided);
    },
    gettingHomeText() {
      return gettGettingHomeTextForFamilies(this.job.awardedApplicantGettingHome);
    },
    jobDate() {
      return formatJobDate(this.job);
    },
  },
};
</script>
<style lang="sass">
.client-unrated-job-summary
  .job-summary
    border: 1px solid white
    line-height: 1.3rem
  .q-card-actions
    padding: 0 8px
</style>
