import * as http from '../../api/http';
import * as types from '../mutation-types';
import c from '../../utils/constants';
import NotificationService from '../../utils/notification-service';
import loader from '../../utils/loader';
import errorHandler from '../../utils/error-handler';
import pushNotificationService from '../../utils/push-notification-service';
import { truncateText } from '../../utils/helpers';
import appManager from '../../utils/app-manager';

export default {
  namespaced: true,
  state: {
    alerts: {
      jobAlerts: 0,
      invitationAlerts: 0,
      chatAlerts: 0,
      unratedJobs: 0,
    },
    connection: {
      status: c.realtimeConnectionStatus.disconnected,
    },
    conversations: {
    },
    chat: {
    },
  },
  actions: {
    initialiseRealtimeConnection({ state }) {
      try {
        if (state.connection.status === c.realtimeConnectionStatus.connected) return;

        // RealtimeService.initialise();
        // RealtimeService.start().then(() => {
        //   // dispatch('fetchPendingStats');
        // });
      } catch (e) {
        errorHandler.captureException(e);
      }
    },
    updateConnectionStatus({ commit }, payload) {
      commit(types.UPDATE_CONNECTION_STATUS, payload);
    },
    async fetchPendingStats(context) {
      const {
        commit, dispatch, state, rootState,
      } = context;
      if (state.connection.status !== c.realtimeConnectionStatus.connected) return;

      commit(types.PENDING_STATS_SUCCESS, {
        pendingJobs: 0,
        pendingInvitations: 0,
        pendingChatMessages: 0,
      });
      const message = {
        ownerId: rootState.userInfo.id,
        messageType: c.messageType.requestPendingStats,
        sentAt: new Date(),
      };
      const result = await dispatch('sendMessage', message);
      commit(types.PENDING_STATS_SUCCESS, result.messageBody);
    },
    fetchPendingStatsDirect({ state, commit }) {
      commit(types.PENDING_STATS_SUCCESS, {
        pendingJobs: 0,
        pendingInvitations: 0,
        pendingChatMessages: 0,
      });
      http
        .get('account/requestpendingstatspartial')
        .then((response) => {
          commit(types.PENDING_STATS_SUCCESS, response.data);
          const badgeCount = state.alerts.jobAlerts + state.alerts.chatAlerts;
          appManager.setBadge(badgeCount);
          if (badgeCount === 0) {
            pushNotificationService.removeAllDeliveredNotifications();
          }
        })
        .catch((error) => {
          errorHandler.captureException(error);
        });
    },
    resetConversations({ commit }) {
      commit(types.CONVERSATIONS_RESET);
    },
    fetchConversations({ commit }) {
      loader.show();
      commit(types.CONVERSATIONS_REQUEST);
      return http
        .get('conversations')
        .then((response) => {
          commit(types.CONVERSATIONS_SUCCESS, response.data);
          loader.hide();
          return response;
        })
        .catch((error) => {
          commit(types.CONVERSATIONS_FAILURE);
          loader.hide();
          return Promise.reject(error);
        });
    },
    setSearchCriteria({ commit }, searchCriteria) {
      commit(types.CONVERSATIONS_SET_SEARCH_CRITERIA, searchCriteria);
    },
    searchConversations({ commit, state }) {
      loader.show();
      const request = {
        pageindex: state.conversations.meta.pageIndex,
        pageSize: state.conversations.meta.pageSize,
      };

      commit(types.CONVERSATIONS_REQUEST);
      return http
        .post('conversations/search', request)
        .then((response) => {
          commit(types.CONVERSATIONS_SUCCESS, response.data);
          loader.hide();
          return response;
        })
        .catch((error) => {
          commit(types.CONVERSATIONS_FAILURE);
          loader.hide();
          return Promise.reject(error);
        });
    },
    canChatWithHelper(root, payload) {
      loader.show();

      return http
        .get(`conversations/canchatwithhelper/${payload.helperId}`)
        .then((response) => {
          loader.hide();
          return response.data;
        })
        .catch((error) => {
          loader.hide();
          return Promise.reject(error);
        });
    },
    fetchChatMessages({ commit, rootState }, payload) {
      loader.show();
      commit(types.CONVERSATION_MESSAGES_RESET);
      commit(types.CONVERSATION_MESSAGES_REQUEST);
      let url = 'conversations/';
      if (payload.id) {
        url += payload.id;
      } else if (payload.userId) {
        url += `with/${payload.userId}`;
      } else {
        // Throw error.
      }

      if (rootState.appInfo.adminMode) {
        url += '?admin_mode=1';
      }

      return http
        .get(url)
        .then((response) => {
          const chat = response.data[0];
          commit(types.CONVERSATION_MESSAGES_SUCCESS, chat);

          const part = chat.participants.find((p) => p.participantId !== rootState.userInfo.data.id);
          const title = part.publicName ? `Chat with ${truncateText(part.publicName, 10)}` : `Chat with ${part.firstName} ${part.lastInitial}`;
          commit(types.SET_PAGE_INFO, { title }, { root: true });
          loader.hide();
          return response;
        })
        .catch((error) => {
          commit(types.CONVERSATION_MESSAGES_FAILURE);
          loader.hide();
          return Promise.reject(error);
        });
    },
    requestOnlineStats(context) {
      const {
        dispatch, commit, state, rootState,
      } = context;
      if (state.connection.status !== c.realtimeConnectionStatus.connected) return;

      if (!state.chat) return;

      const chat = state.chat.data;
      const part = chat.participants.find((p) => p.participantId !== rootState.userInfo.data.id);
      const env = {
        ownerId: rootState.userInfo.data.id,
        messageType: c.messageType.requestUserOnlineStatus,
        messageBody: {
          args: {
            userId: part.participantId,
          },
        },
        sentAt: new Date(),
      };

      dispatch('sendMessage', env).then((result) => {
        const title = `Chat with ${part.firstName} ${part.lastInitial}`;
        commit(types.SET_PAGE_INFO, { title, subtitle: result.messageBody.lastSeenText }, { root: true });
      });
    },
    requestOnlineStatsDirect({ commit, state, rootState }) {
      if (!state.chat) return;

      const chat = state.chat.data;
      const part = chat.participants.find((p) => p.participantId !== rootState.userInfo.data.id);

      http
        .get(`account/requestonlinestats/${part.participantId}`)
        .then((response) => {
          const title = `Chat with ${part.firstName} ${part.lastInitial}`;
          commit(types.SET_PAGE_INFO, { title, subtitle: response.data.lastSeenText }, { root: true });
        })
        .catch((error) => {
          errorHandler.captureException(error);
        });
    },
    resetChatMessages({ commit }) {
      commit(types.CONVERSATION_MESSAGES_RESET);
    },
    // sendMessage(context, payload) {
    //   return RealtimeService.sendMessage(payload);
    // },
    // sendConversationMessage(context, payload) {
    //   return RealtimeService.sendConversationMessage(payload);
    // },
    sendTypingMessage({ dispatch, state, rootState }) {
      const env = {
        messageBody: {
          conversationId: state.chat.data.id,
        },
        ownerId: rootState.userInfo.data.id,
        messageType: state.chat.meta.newMessage.length > 0 ? c.messageType.jugglerTyping : c.messageType.jugglerNotTyping,
      };

      dispatch('sendConversationMessage', env);
    },
    sendChatMessageDirect({ commit, state, rootState }) {
      if (!state.chat.meta.newMessage) {
        NotificationService.info('Please type a message.');
        return Promise.resolve();
      }

      commit(types.SET_PAGE_PROCESSING, { isProcessing: true }, { root: true });
      const { id } = rootState.userInfo.data;

      const sentAt = new Date();
      const sentOn = new Date(sentAt.getFullYear(), sentAt.getMonth(), sentAt.getDate(), 0, 0, 0, 0);
      const newMessage = {
        id: `new-message-${sentAt.toJSON()}`,
        senderId: id,
        body: state.chat.meta.newMessage,
        sentAt,
        sentOn,
        deliveryStatus: c.messageDeliveryStatus.sending,
        conversationId: state.chat.data.id,
        retryAttempts: 0,
      };

      const env = {
        ownerId: id,
        messageType: c.messageType.newPrivateConversationMessage,
        sentAt,
        messageBody: newMessage,
      };

      // Send message to the server.
      commit(types.UPDATE_CONVERSATION_MESSAGE, newMessage);

      const url = `conversations/${state.chat.data.id}`;

      return http
        .patch(url, env)
        .then((response) => {
          commit(types.UPDATE_CONVERSATION_MESSAGE, response.data.messageBody);
          commit(types.SET_PAGE_PROCESSING, { isProcessing: false }, { root: true });
          // Reset new message.
          const messageMutation = {
            module: 'realtime.chat',
            propPath: 'newMessage',
            value: '',
          };
          commit(types.UPDATE_STORE_META, messageMutation, { root: true });
          return Promise.resolve();
        })
        .catch((error) => {
          const result = {
            messageId: newMessage.id,
            sentAt: newMessage.sentAt,
            deliveryStatus: c.messageDeliveryStatus.failed,
          };
          commit(types.UPDATE_CONVERSATION_MESSAGE, result);
          commit(types.SET_PAGE_PROCESSING, { isProcessing: false }, { root: true });
          return Promise.reject(error);
        });
    },
    sendChatMessage(context) {
      const {
        commit, dispatch, state, rootState,
      } = context;
      if (!state.chat.meta.newMessage) {
        NotificationService.info('Please type a message.');
        return;
      }

      const sentAt = new Date();
      const sentOn = new Date(sentAt.getFullYear(), sentAt.getMonth(), sentAt.getDate(), 0, 0, 0, 0);
      const newMessage = {
        id: `new-message-${sentAt.toJSON()}`,
        senderId: rootState.userInfo.data.id,
        body: state.chat.meta.newMessage,
        sentAt,
        sentOn,
        deliveryStatus: c.messageDeliveryStatus.sending,
        conversationId: state.chat.data.id,
        retryAttempts: 0,
      };

      const env = {
        ownerId: rootState.userInfo.data.id,
        messageType: c.messageType.newPrivateConversationMessage,
        sentAt,
        messageBody: newMessage,
      };

      // Reset new message.
      const messageMutation = {
        module: 'realtime.chat',
        propPath: 'newMessage',
        value: '',
      };
      commit(types.UPDATE_STORE_META, messageMutation, { root: true });

      // Send message to the server.
      commit(types.UPDATE_CONVERSATION_MESSAGE, newMessage);
      dispatch('sendConversationMessage', env).then((result) => {
        commit(types.UPDATE_CONVERSATION_MESSAGE, result.messageBody);
      }).catch((error) => {
        const result = {
          messageId: newMessage.id,
          sentAt: newMessage.sentAt,
          deliveryStatus: c.messageDeliveryStatus.failed,
        };
        commit(types.UPDATE_CONVERSATION_MESSAGE, result);
        console.log(error);
      });
    },
    processMessage({ dispatch, commit, state }, payload) {
      if (payload.messageType === c.messageType.jugglerTyping) {
        if (state.chat.data) {
          const typingPayload = {
            module: 'realtime.chat',
            propPath: 'isTyping',
            value: true,
          };
          commit(types.UPDATE_STORE_META, typingPayload, { root: true });

          const scroll = {
            module: 'realtime.chat',
            propPath: 'scrollToBottom',
            value: true,
          };
          commit(types.UPDATE_STORE_META, scroll, { root: true });

          setTimeout(() => {
            typingPayload.value = false;
            commit(types.UPDATE_STORE_META, typingPayload, { root: true });
          }, 5000);
        }
      } else if (payload.messageType === c.messageType.updateConversationMessageStatus) {
        commit(types.UPDATE_CONVERSATION_MESSAGE, payload.messageBody);
      } else if (payload.messageType === c.messageType.newPrivateConversationMessage) {
        const message = payload.messageBody;

        // Check if the current chat matches the new message.
        const conversation = state.chat.data;
        if (conversation && conversation.id && conversation.id === message.conversationId) {
          // Disable typing notification.
          const typingPayload = {
            module: 'realtime.chat',
            propPath: 'isTyping',
            value: false,
          };
          commit(types.UPDATE_STORE_META, typingPayload, { root: true });

          // Process the message.
          commit(types.UPDATE_CONVERSATION_MESSAGE, message);

          // Send a seen notification.
          const env = {
            messageBody: {
              conversationId: state.chat.data.id,
              messageId: message.messageId,
              sentAt: message.sentAt,
              deliveryStatus: c.messageDeliveryStatus.seen,
            },
            messageType: c.messageType.updateConversationMessageStatus,
          };
          dispatch('sendConversationMessage', env);
        } else if (state.conversations.data && state.conversations.data.length > 0) {
          //  Update conversation.
          commit(types.UPDATE_CONVERSATION, message);
        } else {
          // Display a Notify.
          NotificationService.positive({
            html: payload.messageSummary,
            timeout: 5000,
            button: {
              label: 'Chat',
              handler() {
                // router.replace(`/chat/${payload.messageBody.conversationId}`);
              },
            },
          });
        }
      }
    },
  },
  mutations: {
    [types.CONVERSATIONS_SET_SEARCH_CRITERIA]: (state, searchCriteria) => {
      state.conversations.meta.pageIndex = searchCriteria.pageIndex;
      state.conversations.meta.pageSize = searchCriteria.pageSize;
    },
    [types.UPDATE_CONNECTION_STATUS]: (state, payload) => {
      state.connection.status = payload.newState;
    },
    [types.PENDING_STATS_SUCCESS]: (state, message) => {
      state.alerts.jobAlerts = message.pendingJobs;
      state.alerts.invitationAlerts = message.pendingInvitations;

      state.alerts.chatAlerts = message.pendingChatMessages;
      state.alerts.unratedJobs = message.unratedJobs;
    },
    [types.CONVERSATIONS_RESET]: (state) => {
      const str = {
        data: [],
        meta: {
          isFetching: false,
          lastFetched: null,
          dirty: false,
          pageIndex: 1,
          pageSize: 50,
          total: 0,
        },
      };

      // Vue.set(state, 'conversations', str);
      state.conversations = str;
    },
    [types.CONVERSATIONS_REQUEST]: (state) => {
      state.conversations.meta.isFetching = true;
    },
    [types.CONVERSATIONS_SUCCESS]: (state, response) => {
      state.conversations.meta.total = response.total;
      if (state.conversations.meta.pageIndex === 1) {
        state.conversations.data = response.results;
      } else {
        state.conversations.data = state.conversations.data.concat(response.results);
      }

      state.conversations.meta.isFetching = false;
      state.conversations.meta.lastFetched = new Date();
    },
    [types.CONVERSATIONS_FAILURE]: (state, payload) => {
      state.conversations.data = payload;
      state.conversations.meta.isFetching = false;
      state.conversations.meta.lastFetched = undefined;
    },
    [types.CONVERSATION_MESSAGES_RESET]: (state) => {
      const str = {
        data: {
        },
        meta: {
          isFetching: false,
          lastFetched: null,
          dirty: false,
          isTyping: false,
          newMessage: '',
          scrollToBottom: true,
        },
      };

      state.chat = str;
    },
    [types.CONVERSATION_MESSAGES_REQUEST]: (state) => {
      state.chat.meta.isFetching = true;
    },
    [types.CONVERSATION_MESSAGES_SUCCESS]: (state, payload) => {
      state.chat.data = payload;
      state.chat.meta.isFetching = false;
      state.chat.meta.lastFetched = new Date();
    },
    [types.CONVERSATION_MESSAGES_FAILURE]: (state, payload) => {
      state.chat.data = payload;
      state.chat.meta.isFetching = false;
      state.chat.meta.lastFetched = undefined;
    },
    [types.UPDATE_CONVERSATION]: (state, message) => {
      const conversations = state.conversations.data;
      const conversation = conversations.find((conv) => conv.id === message.conversationId);
      if (conversation) {
        const part = conversation.participants.find((p) => p.participantId !== message.senderId);
        if (part) {
          part.unreadMessages++;
        }
      }
    },
    [types.UPDATE_CONVERSATION_MESSAGE]: (state, payload) => {
      // Locate a matching message.
      const conversation = state.chat.data;
      if (!conversation.messages) return;

      if (payload.deliveryStatus === c.messageDeliveryStatus.sending) {
        conversation.messages.push(payload);
        state.chat.meta.scrollToBottom = true;
        return;
      }

      const newId = `new-message-${payload.sentAt}`;
      let targetMessage = conversation.messages.find((message) => message.id === payload.messageId || message.id === newId);
      if (!targetMessage) {
        if (payload.senderId) { // New message.
          conversation.messages.push(payload);
          state.chat.meta.scrollToBottom = true;
          return;
        }
        targetMessage = conversation.messages[conversation.messages.length - 1];
      }

      if (payload.messageId && targetMessage.id !== payload.messageId) {
        targetMessage.id = payload.messageId;
      }

      targetMessage.deliveryStatus = payload.deliveryStatus;
    },
  },
  getters: {
    conversations: (state) => state.conversations.data,
    chat: (state) => state.chat.data,
  },
};
