<template>
  <q-page class="post-au-pair-job-step-4">
    <div>
      <q-toolbar color="secondary" class="filter-panel">
        <q-toolbar-title class="text-left text-body2" style="text-align: left; padding: 0">
          <span>{{filterText}}</span><span v-if="lowHelpers" class="text-red q-ml-xs">*</span>
          <span v-if="lowHelpers" class="text-red q-mr-xs"><br/>*</span>
          <span v-if="lowHelpers" class="text-body2 text-grey">
            <small>Use FILTERS to increase your list of Au Pairs</small>
          </span>
        </q-toolbar-title>
        <q-btn flat @click="handleSearch">
          <q-icon name="search" />
          <span>Filters</span>
        </q-btn>
      </q-toolbar>
      <div class="text-center" style="margin: 10px;">
        <q-btn color="accent" class="q-mx-md" @click="selectTopHelpers">{{selectTopHelpersText}}</q-btn>
        <q-btn color="secondary" @click="unselectAll">Unselect All</q-btn>
      </div>
      <div class="">
        <div class="card-container">
          <helper-profile-card
            v-for="helper in helpers"
            :key="helper.id"
            :click="handleHelperClick"
            :helper="helper"
            mode="post-job"
            filterKey="auPairsNearMe"
            :isSelected="isSelected(helper)"
          />
        </div>
        <div class="q-my-sm text-center" style="margin-bottom: 40px" v-if="hasMore">
          <q-btn rounded outline label="Load More" color="accent" @click="loadMore"/>
        </div>
      </div>
    </div>
  </q-page>
</template>

<script>
import useVuelidate from '@vuelidate/core';
import { required } from '@vuelidate/validators';
import { mapState } from 'vuex';
import HelperProfileCard from 'components/HelperProfileCard';
import { beforeRouteEnter, beforeLeaveStep } from './multistep-helper';
import NotificationService from '../../utils/notification-service';
import { formatNumber } from '../../utils/helpers';

export default {
  name: 'PageAuPairJobStep4',
  components: { HelperProfileCard },
  data() {
    return {
    };
  },
  setup() {
    return {
      v$: useVuelidate(),
    };
  },
  validations: {
    applicants: {
      required,
    },
  },
  computed: {
    ...mapState({
      job: (state) => state.postAuPairJob.jobDetails.data,
      meta: (state) => state.postAuPairJob.jobDetails.meta,
      helpers: (state) => state.filters.data,
      helpersMeta: (state) => state.filters.meta,
      userInfo: (state) => state.userInfo.data,
      pageInfo: (state) => state.pageInfo,
    }),
    storeModuleName() {
      return 'postAuPairJob.jobDetails';
    },
    filterText() {
      if (this.pageInfo.isProcessing) return 'Searching...';

      return `${formatNumber(this.helpersMeta.total)} Au Pairs meet your criteria`;
    },
    lowHelpers() {
      if (this.pageInfo.isProcessing) return false;

      return this.helpersMeta.total < 10;
    },
    applicants: {
      get() {
        return this.job.applicants;
      },
      set(value) {
        this.setProperty('applicants', value);
      },
    },
    selectTopHelpersText() {
      return this.helpers.length >= 25 ? 'Select Top 25' : 'Select All';
    },
    hasMore() {
      return this.pageState.pageIndex * this.pageState.pageSize < this.helpersMeta.total;
    },
  },
  methods: {
    selectTopHelpers() {
      this.loader.show({ delay: 0 });
      // TODO: Optimise performance.
      setTimeout(() => {
        const count = this.helpers.length >= 25 ? 25 : this.helpers.length;
        for (let i = 0; i < count; i++) {
          const helper = this.helpers[i];
          if (this.isSelected(helper)) continue;

          this.$store.dispatch('postAuPairJob/toggleApplicant', helper);
        }
        this.$store.dispatch('postAuPairJob/setPageTitle');

        this.loader.hide();
      }, 250);
    },
    unselectAll() {
      this.loader.show({ delay: 0 });
      setTimeout(() => {
        const { length } = this.meta.selectedApplicants;
        for (let i = 0; i < length; i++) {
          this.$store.dispatch('postAuPairJob/toggleApplicant', this.meta.selectedApplicants[0]);
        }
        this.$store.dispatch('postAuPairJob/setPageTitle');

        this.loader.hide();
      }, 250);
    },
    handleHelperClick(helper) {
      this.$store.dispatch('postAuPairJob/toggleApplicant', helper);
      this.$store.dispatch('postAuPairJob/setPageTitle');
    },
    isSelected(helper) {
      const isSelected = this.job.applicants
        .find((applicant) => applicant.applicantId === helper.id);

      if (isSelected) return 'primary';

      return '';
    },
    handleFilterClick() {
      // const layoutRef = this.$parent.$parent.$refs.layout;
      // layoutRef.toggleLeft();
      this.$store.dispatch('toggleLeftDrawer');
    },
    validate() {
      this.v$.$touch();
      const hasError = this.v$.$error;
      if (hasError) {
        NotificationService.negative('Please select at least one candidate.');
      }

      return !hasError;
    },
    loadMore() {
      this.setPageState('pageIndex', this.pageState.pageIndex + 1);
      this.refreshFilter();
    },
    async refreshFilter() {
      const filterPayload = {
        filterKey: 'postAuPairJob',
        pageIndex: this.pageState.pageIndex,
        pageSize: this.pageState.pageSize,
        displayLoader: true,
        includeUsers: this.meta.previousApplicants,
      };
      await this.$store.dispatch('filters/applyFilter', filterPayload);
    },
    handleSearch() {
      this.$store.dispatch('toggleRightDrawer', true);
    },
    displayFilterNotice() {
      this.$q.dialog({
        title: 'Filters',
        message: 'Apply the filters to narrow the search for your ideal candidate then select the ones who will receive your job invitation',
        ok: {
          label: 'Ok, Got It',
          color: 'primary',
          outline: true,
        },
        cancel: false,
      }).onOk(() => {}).onCancel(() => {});
    },
  },
  async mounted() {
    if (!this.pageState.pageSize || !this.pageState.pageIndex) {
      this.setPageState('pageIndex', 1);
      this.setPageState('pageSize', 50);
    }

    await this.refreshFilter();

    if (this.meta.selectedApplicants === 0 && this.meta.previousApplicants.length > 0) {
      // Select previous applicants.
      this.meta.previousApplicants.forEach((applicantId) => {
        const helper = this.helpers.find((user) => user.id === applicantId);
        if (helper) {
          this.$store.dispatch('postAuPairJob/toggleApplicant', helper);
        }
      });
    }

    this.$store.dispatch('postAuPairJob/setPageTitle');
    if (this.meta.displayFilterNotice) {
      if (this.userInfo.client.jobsPlaced < 2) {
        this.handleSearch();
        this.displayFilterNotice();
      }
      this.setMetaProperty('displayFilterNotice', false);
    }
  },
  beforeRouteEnter(to, from, next) {
    beforeRouteEnter(to, from, next);
  },
  beforeRouteLeave(to, from, next) {
    beforeLeaveStep(this, to, from, next);
  },
};
</script>
<style lang="sass">
.post-au-pair-job-step-4
  .helper-card-container
    display: inline-block
    width: 50%
    .helper-card
      width: 100%
</style>
