<template>
  <div>
    <div id="wistia_upload_drop_zone" class="wistia_upload_drop_zone" style="height:250px;width:100%;">
      <div class="wistia_upload_drop_zone_hover"></div>
      <div id="wistia_upload_button" alt="Upload Video" style="margin: 105px 0;"></div>
    </div>
  </div>
</template>

<script>
import NotificationService from '../utils/notification-service';
import { env } from '../utils/env';
import c from '../utils/constants';

export default {
  name: 'VideoUploader',
  props: ['target'],
  emits: ['uploadSuccess', 'uploadStart'],
  data() {
    return {
    };
  },
  methods: {
    saveMedia(file, media, embedCode, oembedResponse) {
      if (!this.isValidDuration(media.duration)) {
        return;
      }

      const mediaToSave = {
        id: 0,
        externalHashedId: media.id,
        name: media.name,
        description: media.description,
        externalUpdatedDate: file.lastModifiedDate,
        mediaStatus: c.mediaStatus.queued,
        mediaType: c.mediaType.profileIntro,
        playerColor: '#299C4A',
        duration: media.duration,
      };

      if (oembedResponse) {
        mediaToSave.thumbnailUrl = oembedResponse.thumbnail_url;
        mediaToSave.width = oembedResponse.width;
        mediaToSave.height = oembedResponse.height;
      }

      this.$emit('uploadSuccess', mediaToSave);
    },
    isValidDuration() {
      // Allow any duration.
      // if (duration < 20 || duration > 90) {
      //   NotificationService.negative('Video length should be between 20 to 90 seconds.');
      //   window.wistiaUploader.removePreview();
      //   return false;
      // }
      return true;
    },
  },
  computed: {
  },
  async created() {
    if (document.querySelectorAll('script[src*="wistia"]').length === 0) {
      const script = document.createElement('script');
      script.setAttribute('src', 'https://fast.wistia.com/assets/external/api.js');
      document.head.appendChild(script);
    }

    if (document.querySelectorAll('link[href*="wistia"]').length === 0) {
      const link = document.createElement('link');
      link.rel = 'stylesheet';
      link.href = 'https://fast.wistia.com/assets/external/uploader.css';
      link.type = 'text/css';
      document.head.appendChild(link);
    }

    /* eslint-disable */

    const self = this;
    window._wapiq = window._wapiq || [];
    window._wapiq.push(function(W) {
      window.wistiaUploader = new W.Uploader({
        accessToken: 'b95fc6a5bb75e12e61bf9574b068b795b807ff4b692692ed9ebec580c9362042',
        projectId: env.wistiaProjectId,
        playerColor: '#299C4A',
        button: "wistia_upload_button",
        dropZone: "wistia_upload_drop_zone",
        preview: "wistia_upload_preview",        
        onBeforeUnload: 'We are busy Queuing, Encoding and Uploading your video. If you Exit your video will be lost. Would you like to exit?',
        embedCodeOptions: {
          playerColor: "#299C4A",
          embedType: "async_popover"
        },        
      });

      window.wistiaUploader.bind('uploadembeddable', function(file, media, embedCode, oembedResponse) {
        // console.log('The video is now embeddable!');
        // console.log(`Here's the original file: `, file);
        // console.log(`Here's the media in Wistia: `, media);
        // console.log(`'Here's an embed code: `, embedCode);
        // console.log(`And here's the full oEmbed response (see http://wistia.com/doc/oembed): `, oembedResponse);
        // self.saveMedia(file, media, embedCode, oembedResponse);
      });

      window.wistiaUploader.bind('uploadfailed', function(file, errorResponse) {
        console.log('upload failed:', errorResponse.error);
        NotificationService.negative('Failed to upload the video.');
      });

      window.wistiaUploader.bind('uploadsuccess', function(file, media) {
        console.log('upload success');
        // console.log(media);
        self.saveMedia(file, media);
      });

      window.wistiaUploader.bind('uploadstart', function(file) {
        self.$emit('uploadStart');
      });
    });

    /* eslint-enable */
  },
};
</script>
