<template>
  <div class="">
    <profile-tabs :helper="helper" :selectedTab="selectedTab" v-on:profileTabChange="handleProfileTabChange" />
    <!-- <helper-profile-brief :helper="helper"  v-on:scrollToReviews="scrollToProfileReviews" /> -->
    <profile-overview :displayVideo="true" :displayRatings="true" :displayReviews="true" :displayJobStats="true" :displayDistance="true" :helper="helper" :mode="mode" :isGuardianApprovalMode="isGuardianApprovalMode" v-on:scrollToReviews="scrollToProfileReviews"  v-on:profileTabChange="handleProfileTabChange"/>

    <q-card-section style="padding: 8px" class="">
      <q-field
        label="Childcare Jobs"
        borderless
        readonly
      >
        <template v-slot:prepend>
          <q-icon name="work" />
        </template>
        <template v-slot:control>
          <div class="q-mt-md column">
              <div class="col">
                <q-checkbox v-model="interestedInBabysitting" readonly :label="isIndividual ? 'Babysitting' : 'One-Off'" unchecked-icon="check_box_outline_blank" />
              </div>
              <div class="col">
                <q-checkbox v-model="interestedInNannying" readonly :label="isIndividual ? 'Nannying' : 'Recurring'" unchecked-icon="check_box_outline_blank" />
              </div>
              <div class="col">
                <q-checkbox v-model="interestedInBeforeSchoolCare" readonly label="Before school care" unchecked-icon="check_box_outline_blank"/>
              </div>
              <div class="col">
                <q-checkbox v-model="interestedInAfterSchoolCare" readonly label="After school care" unchecked-icon="check_box_outline_blank"/>
              </div>
            </div>
        </template>
      </q-field>

      <q-field label="Childcare Experience"
        readonly
        borderless
        v-if="helper.providerMyExperience">
        <template v-slot:prepend>
          <q-icon name="child_care" />
        </template>
        <template v-slot:control>
          <div class="full-width q-mt-md">
            {{providerMyExperience}}
            <span v-if="helper.providerMyExperience && helper.providerMyExperience.length > 300" style="text-decoration: underline"
              @click="displayAllExperience = !displayAllExperience">{{displayAllExperience ? 'less' : 'more'}}</span>
          </div>
        </template>
      </q-field>

      <q-field
        label="Special Needs Experience"
        readonly
        borderless
        v-if="helper.providerMyExperience2">
        <template v-slot:prepend>
          <q-icon name="accessibility_new" />
        </template>
        <template v-slot:control>
          <div class="full-width q-mt-md">
            {{providerMyExperience2}}
            <span v-if="helper.providerMyExperience2 && helper.providerMyExperience2.length > 300" style="text-decoration: underline"
              @click="displayAllMyExperience2 = !displayAllMyExperience2">{{displayAllMyExperience2 ? 'less' : 'more'}}</span>
          </div>
        </template>
      </q-field>

      <profile-checks :helper="helper" />

      <q-field
        label="Qualifications"
        readonly
        borderless
        v-if="qualificationsList">
        <template v-slot:prepend>
          <q-icon name="local_hospital" />
        </template>
        <template v-slot:control>
           <div class="full-width q-mt-md">
            <div v-html="qualificationsList" />
          </div>
        </template>
      </q-field>

      <q-field
        label="Childcare Extras"
        readonly
        borderless
        v-if="childCareExtras">
        <template v-slot:prepend>
          <q-icon name="add_task" />
        </template>
        <template v-slot:control>
          <div class="full-width q-mt-md">
            <div v-html="childCareExtras" />
          </div>
        </template>
      </q-field>

      <helper-reviews :helper="helper" ref="profileReviews"/>
    </q-card-section>
  </div>
</template>

<script>
import ProfileTabs from 'components/helper-profile/ProfileTabs';
import ProfileOverview from 'components/helper-profile/ProfileOverview';
import ProfileChecks from 'components/helper-profile/ProfileChecks';
import HelperReviews from 'components/helper-profile/HelperReviews';
import c from '../../utils/constants';
import { scrollToElement } from '../../utils/helpers';

export default {
  name: 'ChildcareTab',
  emits: ['profileTabChange'],
  components: {
    ProfileTabs, ProfileOverview, ProfileChecks, HelperReviews,
  },
  props: ['helper', 'mode', 'selectedTab', 'isGuardianApprovalMode'],
  data() {
    return {
      displayAllExperience: false,
      displayAllMyExperience2: false,
    };
  },
  computed: {
    providerMyExperience() {
      if (!this.helper.providerMyExperience) return '';

      if (this.helper.providerMyExperience.length < 300 || this.displayAllExperience) {
        return this.helper.providerMyExperience;
      }

      const idx = this.helper.providerMyExperience.indexOf(' ', 300);
      if (idx === -1) return this.helper.providerMyExperience;

      return `${this.helper.providerMyExperience.substring(0, idx)}...`;
    },
    providerMyExperience2() {
      if (!this.helper.providerMyExperience2) return '';

      if (this.helper.providerMyExperience2.length < 300 || this.displayAllMyExperience2) {
        return this.helper.providerMyExperience2;
      }

      const idx = this.helper.providerMyExperience2.indexOf(' ', 300);
      if (idx === -1) return this.helper.providerMyExperience2;

      return `${this.helper.providerMyExperience2.substring(0, idx)}...`;
    },
    interestedInBabysitting() {
      const { interestedInJobTypes } = this.helper;

      return (interestedInJobTypes & c.jobType.babysitting) > 0;
    },
    interestedInNannying() {
      const { interestedInJobTypes } = this.helper;

      return (interestedInJobTypes & c.jobType.nannying) > 0;
    },
    interestedInBeforeSchoolCare() {
      const { interestedInJobTypes } = this.helper;

      return (interestedInJobTypes & c.jobType.beforeSchoolCare) > 0;
    },
    interestedInAfterSchoolCare() {
      const { interestedInJobTypes } = this.helper;

      return (interestedInJobTypes & c.jobType.afterSchoolCare) > 0;
    },
    qualificationsList() {
      return this.getList('qualifications', '<br />');
    },
    childCareExtras() {
      return this.getList('extraSkills', '<br />');
    },
  },
  methods: {
    handleProfileTabChange(args) {
      this.$emit('profileTabChange', args);
    },
    scrollToProfileReviews() {
      scrollToElement(this.$refs.profileReviews.$el, -50);
    },
    getList(groupName, sep) {
      if (!this.helper) {
        return '';
      }

      const selectedList = [];
      const list = this.helper[groupName];
      for (let i = 0; i < list.length; i++) {
        if (list[i].selected) {
          selectedList.push(list[i].text);
        }
      }

      if (selectedList.length > 0) {
        return selectedList.join(sep);
      }

      return '';
    },
  },
};
</script>

