<template>
  <q-page class="">
    <helper-au-pair-preferences ref="auPairPrefs" :allowOptOut="true"/>
  </q-page>
</template>

<script>
import { mapState } from 'vuex';
import HelperAuPairPreferences from 'components/HelperAuPairPreferences';
import NotificationService from '../../utils/notification-service';
import c from '../../utils/constants';

export default {
  name: 'PageAccountAuPairPreferences',
  components: { HelperAuPairPreferences },
  data() {
    return {
      acceptedPopup: false,
    };
  },
  methods: {},
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
    }),
  },
  beforeRouteLeave(to, from, next) {
    if (!this.$refs.auPairPrefs.validate(true)) {
      next(false);
      return;
    }

    this.$store.dispatch('updateUserInfo').then(() => {
      const interestedInAuPairJobs = this.userInfo.interestedInJobTypes & c.jobType.auPair;
      // const displayPopup = interestedInAuPairJobs && (this.userInfo.medias.length === 0 || this.userInfo.vouches.length < 2) && !this.acceptedPopup;
      const displayPopup = interestedInAuPairJobs && this.userInfo.vouches.length < 2 && !this.acceptedPopup;
      let popupTitle;
      let popupMessage;
      let popupDestination;

      // if (displayPopup) {
      //   if (this.userInfo.medias.length === 0) {
      //     popupTitle = 'Profile Video';
      //     popupMessage = 'You need to upload a short personal Profile Video to be an Au Pair. Your Au Pair account will not be activated until you upload your Profile Video.';
      //     popupDestination = '/account/profile-video';
      //   } else {
      //     popupTitle = 'References';
      //     popupMessage = 'A minimum of 2 references are required to be a Juggle Street Au Pair.';
      //     popupDestination = '/account/personal-references';
      //   }
      // }

      if (displayPopup) {
        popupTitle = 'References';
        popupMessage = 'A minimum of 2 references are required to be a Juggle Street Au Pair.';
        popupDestination = '/account/personal-references';
      }

      if (displayPopup) {
        this.$q.dialog({
          title: popupTitle,
          message: popupMessage,
          ok: {
            label: 'Ok, Got It',
            color: 'primary',
            outline: true,
          },
        }).onOk(() => {
          this.acceptedPopup = true;
          next({ path: popupDestination, replace: true });
        });
      } else {
        next();
      }
    }).catch((error) => {
      NotificationService.negative(error.message);
      next(false);
    });
  },
};
</script>
