<template>
  <q-page class="">
    <div class="layout-padding">
      <div class="q-ml-xs">
        <filters-section :filterType="filterKey" :jobType="jobType"/>
      </div>

      <q-field
        class="q-my-sm"
        :error="v$.specialInstructions.$error"
        borderless
        label="Describe the job"
        no-error-icon
      >
        <template v-slot:prepend>
          <q-icon name="assignment" />
        </template>
        <template v-slot:control>
          <div class="full-width">
            <q-input
              :error="v$.specialInstructions.$error"
              error-message="required"
              v-model="specialInstructions"
              type="textarea"
              rows="4"
              :max-height="200"
              placeholder="What does your Au Pair need to know about this job and working for your family?"
            />
          </div>
        </template>
      </q-field>
    </div>
  </q-page>
</template>

<script>
import useVuelidate from '@vuelidate/core';
import { required } from '@vuelidate/validators';
import { mapState } from 'vuex';
import FiltersSection from 'components/FiltersSection';
import { beforeRouteEnter, beforeLeaveStep } from './multistep-helper';
import NotificationService from '../../utils/notification-service';

export default {
  name: 'PageRecurringJobStep5',
  components: { FiltersSection },
  data() {
    return {
    };
  },
  setup() {
    return {
      v$: useVuelidate(),
    };
  },
  validations: {
    specialInstructions: {
      required,
    },
  },
  computed: {
    ...mapState({
      job: (state) => state.postAuPairJob.jobDetails.data,
      meta: (state) => state.postAuPairJob.jobDetails.meta,
    }),
    storeModuleName() {
      return 'postAuPairJob.jobDetails';
    },
    filterKey() {
      return 'postAuPairJob';
    },
    jobType() {
      return this.job.jobType;
    },
    specialInstructions: {
      get() {
        return this.job.specialInstructions;
      },
      set(value) {
        this.setProperty('specialInstructions', value);
      },
    },
  },
  watch: {

  },
  methods: {
    validate() {
      this.v$.$touch();
      const hasError = this.v$.$error;
      if (hasError) {
        NotificationService.negative('Please review highlighted fields.');
      }

      return !hasError;
    },
  },
  beforeRouteEnter(to, from, next) {
    beforeRouteEnter(to, from, next);
  },
  beforeRouteLeave(to, from, next) {
    beforeLeaveStep(this, to, from, next);
  },
};
</script>
