<template>
  <q-card class="bg-secondary text-white q-px-xs q-py-xs" v-if="paymentType && (!requiresProration || prorationInfo !== null)">
    <q-card-section>
      <div>
        <div class="text-h6 q-mb-xs">Payment Summary</div>
        <div v-if="prorationInfo === null">
          <div>
            {{ getPaymentTypeText() }}
          </div>
          <div>
            <span class="inline-block align-left" style="width: 75%">
              {{ getPaymentFrequencyForPaymentType() }}
            </span>
            <span class="inline-block bold text-right" style="width: 25%">
              {{ getAmountForPaymentType() }}
            </span>
          </div>
        </div>
        <div v-else>
          <div>
            <span class="text-bold">{{ getPaymentTypeText() }}</span>
          </div>
          <div>
            <span class="inline-block align-left" style="width: 75%">
              Pro-rata Annual Subscription
            </span>
            <span class="inline-block bold text-right" style="width: 25%">
              {{ getAmountForPaymentType() }}
            </span>
          </div>
          <div>
            <span class="text-bold">{{getCurrentPaymentTypeText()}}</span>
          </div>
          <div>
            <span class="inline-block align-left" style="width: 75%">Pro-rata discount</span>
            <span class="inline-block bold text-right" style="width: 25%">{{ getProrationRefund() }} </span>
          </div>
        </div>
       <div v-if="walletBalance > 0">
          <span class="inline-block align-left" style="width: 75%">Account Balance</span>
          <span class="inline-block bold text-right" style="width: 25%">{{ getWalletBalance() }} </span>
        </div>
       <div v-if="isBusiness">
          <span class="inline-block align-left" style="width: 75%">{{taxRateText}}</span>
          <span class="inline-block bold text-right" style="width: 25%">{{ getTaxAmountText() }} </span>
        </div>
        <hr/>
        <div>
          <span class="inline-block align-left text-bold" style="width: 75%">Amount Due inc. GST</span>
          <span class="inline-block bold text-right text-bold" style="width: 25%">
            {{ getAmountToBePaidText() }}
          </span>
        </div>
      </div>
    </q-card-section>
  </q-card>
</template>

<script>
import { mapState } from 'vuex';
import {
  getPaymentTypeText,
  getAmountForPaymentType,
  getPaymentFrequencyForPaymentType,
} from '../utils/helpers';
import c from '../utils/constants';

export default {
  name: 'PaymentSummary',
  data() {
    return {
      requiresProration: false,
      prorationInfo: null,
    };
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
      paymentInfo: (state) => state.userInfo.data.paymentInfo,
      walletBalance: (state) => state.userInfo.data.walletBalanceDecimal,
    }),
    taxRateText() {
      return `${this.userInfo.country === 'au' ? '10%' : '15%'} GST`;
    },
  },
  props: ['paymentType'],
  methods: {
    getPaymentTypeText() {
      return getPaymentTypeText(Number(this.paymentType));
    },
    getCurrentPaymentTypeText() {
      return getPaymentTypeText(this.paymentInfo.paymentType);
    },
    getAmountForPaymentType() {
      let amount;
      if (this.prorationInfo !== null) {
        amount = this.prorationInfo.payment;
      } else {
        amount = getAmountForPaymentType(Number(this.paymentType));
      }
      const cost = Math.abs(amount).toFixed(2);
      return `$${cost}`;
    },
    getPaymentFrequencyForPaymentType() {
      const paymentType = Number(this.paymentType);
      return getPaymentFrequencyForPaymentType(paymentType);
    },
    getWalletBalance() {
      return `$${Math.abs(this.walletBalance).toFixed(2)}`;
    },
    getProrationRefund() {
      return `$${Math.abs(this.prorationInfo.refund).toFixed(2)}`;
    },
    getAmountToBePaid() {
      const paymentType = Number(this.paymentType);
      let cost = getAmountForPaymentType(paymentType);
      if (this.prorationInfo !== null) {
        cost = this.prorationInfo.total;
      }

      return Math.abs(this.walletBalance - cost);
    },
    getTaxAmount() {
      if (this.isIndividual) return 0;

      const amount = this.getAmountToBePaid();
      const taxRate = this.userInfo.country === 'au' ? 0.1 : 0.15;

      return amount * taxRate;
    },
    getTaxAmountText() {
      const amount = this.getTaxAmount();

      return `$${amount.toFixed(2)}`;
    },
    getAmountToBePaidText() {
      const amount = this.getAmountToBePaid() + this.getTaxAmount();

      return `$${amount.toFixed(2)}`;
    },
    async processProration(value) {
      if (!value) return;

      if (this.requiresProration) {
        this.prorationInfo = null;

        this.loader.show();
        this.prorationInfo = await this.$store.dispatch('payments/previewProration', value);
        this.loader.hide();
      }
    },
  },
  watch: {
    async paymentType(value) {
      await this.processProration(value);
    },
  },
  async mounted() {
    this.requiresProration = this.userInfo.membershipType === c.membershipType.subscription
      && this.userInfo.paymentInfo.paymentType !== parseInt(this.paymentType, 10);
    await this.processProration(this.paymentType);
  },
};
</script>
