<template>
  <q-page class="post-tutoring-job-step-8">
    <div class="layout-padding upsell">
      <div>
        <payment-type :jobType="jobType" :paymentType="userPaymentType" @paymentTypeChanged="handlePaymentTypeChanged" />
      </div>

      <div class="card-details">
        <card-details-2 ref="cardDetails" :defaultPaymentMethod="paymentMethod" @paymentMethodChanged="handlePaymentMethodChanged"/>
      </div>

      <div class="payment-summary q-mb-xl">
        <payment-summary :paymentType="userPaymentType"/>
        <!-- <span class="row text-center" v-if="isSubscription">* You can turn off your auto-renew in the payment section of your account at anytime.</span> -->
      </div>
      <q-btn
        big
        color="accent"
        class="full-width fixed-bottom"
        @click="handleReviewAndPost">
          Review and Post
      </q-btn>
    </div>
  </q-page>
</template>

<script>
import { mapState } from 'vuex';
import CardDetails2 from 'components/CardDetails2';
import PaymentSummary from 'components/PaymentSummary';
import PaymentType from 'components/PaymentType';
import store from '../../store';
import { beforeRouteEnter, beforeLeaveStep } from './multistep-helper';
import NotificationService from '../../utils/notification-service';
import c from '../../utils/constants';
import { getUpsellOptions } from '../../utils/helpers';

const moduleName = 'postTutoringJob';

export default {
  name: 'PageTutoringJobStep8',
  components: { CardDetails2, PaymentSummary, PaymentType },
  data() {
    const hasExistingCard = this.$store.getters['payments/hasExistingCard'];
    const existingCardText = this.$store.getters['payments/existingCardText'];
    return {
      paymentMethod: hasExistingCard ? c.paymentMethod.existingCard : c.paymentMethod.newCard,
      existingCardText,
      jobTypes: c.jobType,
    };
  },
  computed: {
    ...mapState({
      job: (state) => state[moduleName].jobDetails.data,
      meta: (state) => state[moduleName].jobDetails.meta,
      userInfo: (state) => state.userInfo.data,
    }),
    storeModuleName() {
      return `${moduleName}.jobDetails`;
    },
    jobType() {
      return this.job.jobType;
    },
    userPaymentType: {
      get() {
        return this.job.userPaymentType;
      },
      set(value) {
        this.setProperty('userPaymentType', value);
      },
    },
    isSubscription() {
      return this.job.userPaymentType !== c.userPaymentType.singlePrimarySchoolTutoringJob
        && this.job.userPaymentType !== c.userPaymentType.singleHighSchoolTutoringJob;
    },
    isPayg() {
      return this.userInfo.membershipType === c.membershipType.free
      || this.userInfo.membershipType === c.membershipType.payAsYouGo;
    },
  },
  methods: {
    handlePaymentTypeChanged(value) {
      this.userPaymentType = value;
    },
    handleReviewAndPost() {
      this.routerReplace({ path: `/post-tutoring-job/step/${this.meta.currentStep + 1}` });
    },
    handlePaymentMethodChanged(value) {
      this.paymentMethod = value;
    },
    validate() {
      if (this.userPaymentType === null) {
        NotificationService.negative('Please choose a subscription option.');
        return false;
      }

      if (this.paymentMethod === c.paymentMethod.existingCard) {
        return true;
      }

      const { cardDetails } = this.$refs;
      if (!cardDetails.validate()) {
        NotificationService.negative('Please enter valid card details.');
        return false;
      }

      return true;
    },
    async createToken() {
      if (!this.validate()) {
        return false;
      }

      const { cardDetails } = this.$refs;
      this.loader.show();
      this.setMetaProperty('isProcessing', true);
      const tokenId = await cardDetails.createToken();
      this.setMetaProperty('isProcessing', false);
      this.loader.hide();
      if (!tokenId) {
        NotificationService.negative('Invalid credit card');
        return false;
      }
      this.setProperty('tokenId', tokenId);
      return true;
    },
  },
  beforeRouteEnter(to, from, next) {
    beforeRouteEnter(to, from, (params) => {
      // Skip page.
      if (params && params.path) {
        next(params);
        return;
      }

      const jobDetails = store.state[moduleName].jobDetails.data;
      if (!jobDetails) {
        next({ path: '/post-job', query: { reset: 1 }, replace: true });
        return;
      }

      const upsellOptions = getUpsellOptions(store.state.userInfo.data, jobDetails.jobType);
      if (upsellOptions.length === 0) next();

      if (upsellOptions.length > 1) {
        // Ask the user to make a selection.
        const payload = {
          module: `${moduleName}.jobDetails`,
          propPath: 'userPaymentType',
          value: null,
        };
        store.dispatch('updateStoreData', payload);
      } else {
        const alreadySelected = upsellOptions.indexOf(jobDetails.userPaymentType) !== -1;
        if (!alreadySelected) {
          const updatedPaymentType = upsellOptions[0];
          const payload = {
            module: `${moduleName}.jobDetails`,
            propPath: 'userPaymentType',
            value: updatedPaymentType,
          };
          store.dispatch('updateStoreData', payload);
        }
      }

      next();
    });
  },
  async beforeRouteLeave(to, from, next) {
    const toStep = parseInt(to.path.split('step/')[1], 10) || 1;
    if (toStep === 10) {
      if (this.paymentMethod === c.paymentMethod.newCard) {
        const success = await this.createToken();
        if (!success) {
          next(false);
          return;
        }
      }
    }
    beforeLeaveStep(this, to, from, next);
  },
};
</script>
<style lang="sass">
.post-tutoring-job-step-8
  .plan-info
    display: inline-block
    margin: 5px 25px
  .card-details
    margin: 10px 0
</style>
