<template>
  <q-page class="layout-padding">
    <div v-if="pageStatus == 'initial'">
      Please enter the 6 digit reset code along with the new password.
      <q-form class="q-mx-sm" v-on:submit.prevent="handleResetPassword">
        <q-input class="q-my-sm" v-model="code" max-length="7" label="6 Digit Code" :error="v$.code.$error" autocomplete="off" />
        <q-input class="q-my-sm" v-model="password" type="password" label="New Password" :error="v$.password.$error" autocomplete="off" />
        <q-input class="q-my-sm" v-model="confirmPassword" type="password" label="Confirm Password" :error="v$.confirmPassword.$error" autocomplete="off" />

        <q-btn loader @click="handleResetPassword" color="accent">
          Reset Password
        </q-btn>
      </q-form>
    </div>
    <div v-if="pageStatus == 'complete'">
      Your password was changed successfully.
      <q-btn class="q-my-sm" color="accent" @click="handleLogin">Login</q-btn>
    </div>
  </q-page>
</template>

<script>
import useVuelidate from '@vuelidate/core';
import { required, maxLength, minLength } from '@vuelidate/validators';
import { setAccessToken, resetPasswordBySms } from '../api/auth';
import NotificationService from '../utils/notification-service';

export default {
  name: 'PageResetPassword',
  data() {
    return {
      code: '',
      password: '',
      confirmPassword: '',
      pageStatus: 'initial',
    };
  },
  setup() {
    return {
      v$: useVuelidate(),
    };
  },
  validations: {
    code: {
      required,
      maxLength: maxLength(7),
      minLength: minLength(6),
    },
    password: {
      required,
    },
    confirmPassword: {
      required,
    },
  },
  methods: {
    handleLogin() {
      this.routerReplace('/login');
    },
    handleResetPassword() {
      this.setFieldsDirty();
      if (this.v$.code.$error || this.v$.password.$error || this.v$.confirmPassword.$error) {
        return;
      }

      this.loader.show();
      const payload = {
        code: this.code,
        password: this.password,
        confirmPassword: this.confirmPassword,
      };

      resetPasswordBySms(payload)
        .then(async (response) => {
          this.loader.hide();
          await setAccessToken(response.data.accessToken, response.data.refreshToken, true);

          await this.$store.dispatch('fetchCoreData');
          this.$router.replace('/dashboard');
        })
        .catch((error) => {
          this.loader.hide();
          NotificationService.negative(error.message);
        });
    },
    setFieldsDirty() {
      this.v$.code.$touch();
      this.v$.password.$touch();
      this.v$.confirmPassword.$touch();
    },
  },
};
</script>
