<template>
  <q-page class="helper-profile-page">
    <q-card flat v-if="helper.id && helper.id > 0 && meta.lastFetched !== null">
      <q-card-section style="padding: 6px 16px" v-if="isGuardianApprovalMode && guardianStatus === types.guardianStatus.pending">
        <p>
            Hi <span> {{guardian.firstName}}</span>,
            <br/>
            <br/>
            <span>{{helper.firstName}}</span> has created a helpers account on Juggle Street,
            and is requesting your permission to apply for jobs via the Juggle Street Platform.
        </p>
        <p>
            This is how <span>{{helper.firstName}}</span>'s profile appears:
        </p>
      </q-card-section>

      <profile-tab :helper="helper" :mode="mode" :selectedTab="selectedTab" v-on:profileTabChange="handleProfileTabChange" v-if="selectedTab === 'profile'"/>
      <childcare-tab :helper="helper" :mode="mode" :selectedTab="selectedTab" v-on:profileTabChange="handleProfileTabChange" v-if="selectedTab === 'childcare'"/>
      <aupair-tab :helper="helper" :mode="mode" :selectedTab="selectedTab" v-on:profileTabChange="handleProfileTabChange" v-if="selectedTab === 'auPair'"/>
      <tutor-tab :helper="helper" :mode="mode" :selectedTab="selectedTab" v-on:profileTabChange="handleProfileTabChange" v-if="selectedTab === 'tutor'"/>

      <q-card-section v-if="isGuardianApprovalMode && guardianStatus === types.guardianStatus.pending">
        <p>
            In consideration for, amongst other things, Juggle Street’s willingness to allow
            <span>{{helper.firstName}} {{helper.lastInitial}}</span>
            (Minor) to use the Juggle Street Platform: I represent and warrant that, I am the parent or legal
            guardian of Minor and I am authorised to act for, and on behalf of, Minor and I have read and understood
            these Terms and Conditions and explained these Terms and Conditions to Minor and Minor understands
            them; as the parent or legal guardian of Minor, I agree, on behalf of myself and Minor, to be bound
            by these Terms and Conditions; I confirm that I have read Minor’s Profile and that it is accurate
            and up-to-date; I acknowledge that I have access to Minor’s Profile and agree to immediately advise
            Juggle Street if I am, or become, aware that Minor’s Profile is no longer accurate or up-to-date
            or if Minor ceases to be eligible to use the Juggle Street Platform; and I agree to indemnify and
            hold harmless Juggle Street and its directors, officers, employees and agents, for any claims made
            against them as a result of any deficiency in my legal capacity or authority to act for, and on behalf
            of, Minor, as a result of the representations and warranties above not being, or ceasing to be, correct
            or as a result of my failure to comply with my obligations above.
        </p>
        <p>
          <q-checkbox v-model="doesApproveAccount" label="I approve my child's Account" />
          <q-checkbox v-model="doesAcceptTermsAndConditions" label="I accept terms and conditions" />
        </p>
        <q-btn color="accent" @click="handleApproveAccount">Approve Account</q-btn>
      </q-card-section>

      <q-card-actions align="center" v-if="!isGuardianApprovalMode && mode === 'view'">
        <!-- <j-user-actions class="q-mx-md" v-if="selectedTab !== 'profile'" :user="helper" :allowHideAction="false" :allowChat="true"/> -->
        <hide-user-action :user="helper" display-mode="button"/>
      </q-card-actions>

    </q-card>
    <p style="margin: 10px" v-else-if="!helper.id && meta.lastFetched !== null">
      This user is not accessible.
      <br/>
      <q-btn color="accent" @click="routerBack()">Back</q-btn>
    </p>
  </q-page>
</template>

<script>
import { mapState } from 'vuex';
import useVuelidate from '@vuelidate/core';
import { required } from '@vuelidate/validators';
import ProfileTab from 'components/helper-profile/ProfileTab';
import ChildcareTab from 'components/helper-profile/ChildcareTab';
import AupairTab from 'components/helper-profile/AupairTab';
import TutorTab from 'components/helper-profile/TutorTab';
import HideUserAction from 'components/HideUserAction';
import c from '../utils/constants';
import store from '../store';
import NotificationService from '../utils/notification-service';

export default {
  name: 'PageHelperProfile',
  components: {
    ProfileTab, ChildcareTab, AupairTab, TutorTab, HideUserAction,
  },
  data() {
    return {
      selectedTab: 'profile',
      mode: 'view',
      isGuardianApprovalMode: false,
      types: {
        jobType: c.jobType,
        guardianStatus: c.guardianStatus,
      },
    };
  },
  props: ['id'],
  computed: {
    jobTypesText() {
      const { interestedInJobTypes } = this.helper;

      let jobTypes = '';
      if (interestedInJobTypes & c.jobType.babysitting) jobTypes += 'Babysitting, ';
      if (interestedInJobTypes & c.jobType.nannying) jobTypes += 'Nannying, ';
      if (interestedInJobTypes & c.jobType.beforeSchoolCare) jobTypes += 'Before School Care, ';
      if (interestedInJobTypes & c.jobType.afterSchoolCare) jobTypes += 'After School Care, ';
      if (interestedInJobTypes & c.jobType.auPair) jobTypes += 'Au Pair, ';
      if (interestedInJobTypes & c.jobType.primarySchoolTutoring) jobTypes += 'Primary School Tutoring, ';
      if (interestedInJobTypes & c.jobType.highSchoolTutoring) jobTypes += 'High School Tutoring, ';

      if (jobTypes.length > 0) {
        jobTypes = jobTypes.substring(0, jobTypes.length - 2);
      }

      return jobTypes;
    },
    ...mapState({
      guardian: (state) => state.guardian.data,
    }),
    helper() {
      const helperInfo = this.$store.getters['profiles/getHelperProfile'](this.$route.params.id);
      return helperInfo.data;
    },
    meta() {
      const helperInfo = this.$store.getters['profiles/getHelperProfile'](this.$route.params.id);
      return helperInfo.meta;
    },
    storeModuleName() {
      return 'guardian';
    },
    userRating() {
      return Math.round(this.helper.providerRatingsAvg);
    },
    guardianStatus() {
      return this.guardian.status;
    },
    doesApproveAccount: {
      get() {
        return this.guardian.doesApproveAccount;
      },
      set(value) {
        this.setProperty('doesApproveAccount', value);
      },
    },
    doesAcceptTermsAndConditions: {
      get() {
        return this.guardian.doesAcceptTermsAndConditions;
      },
      set(value) {
        this.setProperty('doesAcceptTermsAndConditions', value);
      },
    },
  },
  methods: {
    handleProfileTabChange(args) {
      this.selectedTab = args.id;
    },
    getJobTypeIcon(jobType) {
      return jobType & this.helper.interestedInJobTypes ? 'check' : 'close';
    },
    handleApproveAccount() {
      this.v$.$touch();
      if (this.v$.$error) {
        NotificationService.negative('You must approve your child\'s account and accept terms & conditions.');
      } else {
        this.setProperty('status', c.guardianStatus.approved);
        const { id } = this.$route.params;
        this.$store.dispatch('guardian/approveAccount', { id }).then(() => {
          NotificationService.info('This profile has now been approved.');
        }).catch((error) => {
          NotificationService.negative(error.message);
          this.setProperty('status', c.guardianStatus.pending);
        });
      }
    },
  },
  async beforeRouteEnter(to, from, next) {
    await store.dispatch('profiles/checkProfile', { id: to.params.id });

    next(async (vm) => {
      const { isGuardianApprovalMode } = vm.$route.meta;
      const { id } = vm.$route.params;
      const { guardianApprovalKey } = vm.$route.query;

      vm.isGuardianApprovalMode = isGuardianApprovalMode;
      vm.guardianApprovalKey = guardianApprovalKey;

      if (vm.$route.query.defaultTab && vm.$route.query.defaultTab.length > 0) {
        vm.selectedTab = vm.$route.query.defaultTab;
      }

      if (vm.$route.query.mode && vm.$route.query.mode.length > 0) {
        vm.mode = vm.$route.query.mode;
      }

      vm.loader.show();
      await vm.$store.dispatch('profiles/fetchHelperProfile', { id, guardianApprovalKey }).catch(() => {
        vm.$store.dispatch('setPageInfo', { title: 'Not Accessible', subtitle: '' });
      });
      // vm.mediaShown = vm.hasProfileVideo() ? 'video' : 'image';

      if (isGuardianApprovalMode) {
        await vm.$store.dispatch('guardian/fetchGuardianForApproval', { guardianApprovalKey });
      }
      vm.loader.hide();
    });
  },
  setup() {
    return {
      v$: useVuelidate(),
    };
  },
  validations: {
    doesAcceptTermsAndConditions: {
      required,
    },
    doesApproveAccount: {
      required,
    },
  },
};
</script>
<style lang="sass">
  .helper-profile-page
    .profile-overview
      p
        margin: 4px
    .image-video-tabs
      .q-tabs-head
        min-height: 20px
        align-items: center
        height: 35px
        .q-tab.column:first-child
          border-right: 1px solid #b3b3b3

    .image-video-container
      img
        width: 80%
        height: 80%
        max-width: 300px
        max-height: 300px
        margin: 0 10px

    .job-type-container
      .q-field-content
        padding-top: 0

    .helper-review
      white-space: normal
</style>
