<template>
  <q-page class="">
    <q-card flat v-if="client.id && client.id > 0 && meta.lastFetched !== null">
      <q-card-section>
        {{`${client.firstName} ${client.lastInitial}`}}
        <div class="float-right">
          <q-icon name="location_pin" color="primary" size="18px" style="vertical-align: middle" />
          <span>{{client.distanceInKiloMetersRounded}}km, {{client.suburb}}</span>
        </div>
      </q-card-section>

      <div class="text-center">
        <img :src="client.imageSrc" />
      </div>

      <q-card-actions v-if="client.isIndividual" align="center" style="padding: 4px 8px">
        <client-actions :user="client" />
      </q-card-actions>

      <q-card-section align="center" style="padding: 4px 8px">
        <div class="row justify-between">
          <div class="column" style="text-align: left;">
            <span>{{client.clientJobsAwarded}} Jobs Awarded</span>
            <span>{{client.clientJobsCancelled}} Jobs Cancelled</span>
          </div>
          <div class="column" style="text-align: right;">
            <span>Avg Rating: {{client.clientRatingsAvg.toFixed(2)}}</span>
            <q-rating v-if="client.clientRatingsAvg >= 0" v-model="userRating" :max="5" color="accent" readonly />
          </div>
        </div>
      </q-card-section>

      <q-card-section align="center" style="padding: 4px 8px">
        "{{client.aboutMe}}"
      </q-card-section>

      <q-card-section style="padding: 4px 8px" v-if="client.isBusiness">
        <q-field borderless stack-label readonly
          label="Business Trading Name">
            <template v-slot:control>
              <div class="self-center full-width no-outline" tabindex="0">{{client.publicName}}</div>
            </template>
        </q-field>
        <q-field borderless stack-label readonly
          label="Year Company Established">
            <template v-slot:control>
              <div class="self-center full-width no-outline" tabindex="0">{{client.establishYear}}</div>
            </template>
        </q-field>
        <q-field borderless stack-label readonly
          label="Industry Sector">
            <template v-slot:control>
              <div class="self-center full-width no-outline" tabindex="0">{{industrySector}}</div>
            </template>
        </q-field>
      </q-card-section>

      <q-card-section style="padding: 4px 8px" v-if="client.isIndividual">
        <q-field
          :label="`${client.firstName}'s Family`"
          borderless
          readonly>
          <template v-slot:prepend>
            <q-icon name="group" />
          </template>
          <template v-slot:control>
            <div class="full-width q-mt-md">
              {{client.clientJobDescription}}
            </div>
          </template>
        </q-field>

        <q-field
          label="Children"
          borderless
          hide-bottom-space
          readonly>
          <template v-slot:prepend>
            <q-icon name="supervised_user_circle" />
          </template>
          <template v-slot:control>
            <div class="full-width q-mt-md">
              <ol class="children">
                <li v-for="(child, index) in client.children" :key="index">
                  <div>
                    {{child.firstName}} - {{child.ageInYears}} yr(s)
                    <br/>
                    Has allergies: <strong>{{`${child.hasAllergies ? 'Yes' : 'No'}`}}</strong>
                    <div>
                      {{child.allergyDetails}}
                    </div>
                    <div>
                      <em>{{child.aboutMe}}</em>
                    </div>
                  </div>
                </li>
              </ol>
            </div>
          </template>
        </q-field>

        <q-field
          :label="`${client.firstName}'s Home`"
          v-if="client.clientJobRules || client.benefitsProProvidersList"
          borderless
          readonly
        >
          <template v-slot:prepend>
            <q-icon name="home" />
          </template>
          <template v-slot:control>
            <div class="full-width q-mt-md">
              <p v-if="client.clientJobRules">
                {{client.clientJobRules}}
              </p>
              <p v-if="benefitsProProvidersList">
                {{benefitsProProvidersList}}
              </p>
            </div>
          </template>
        </q-field>

      </q-card-section>

      <q-card-actions align="center">
        <hide-user-action :user="client" display-mode="button"/>
      </q-card-actions>
    </q-card>
    <p style="margin: 10px" v-else-if="!client.id && meta.lastFetched !== null">
      This user is not accessible.
      <br/>
      <q-btn color="accent" @click="routerBack()">Back</q-btn>
    </p>
  </q-page>
</template>

<script>
import { mapState } from 'vuex';
import ClientActions from 'components/ClientActions';
import HideUserAction from 'components/HideUserAction';
import store from '../store';
import { getIndustrySectorText } from '../utils/helpers';

export default {
  name: 'PageClientProfile',
  components: { ClientActions, HideUserAction },
  props: ['id'],
  computed: {
    ...mapState({
    }),
    industrySector() {
      if (!this.client.industrySectorId) return '';

      if (this.client.industrySectorText) return this.client.industrySectorText;

      return getIndustrySectorText(this.client.industrySectorId);
    },
    client() {
      const clientInfo = this.$store.getters['profiles/getClientProfile'](this.$route.params.id);
      return clientInfo.data;
    },
    meta() {
      const clientInfo = this.$store.getters['profiles/getClientProfile'](this.$route.params.id);
      return clientInfo.meta;
    },
    userRating() {
      return Math.round(this.client.clientRatingsAvg);
    },
    benefitsProProvidersList() {
      const clientInfo = this.$store.getters['profiles/getClientProfile'](this.$route.params.id);
      if (!clientInfo.data) {
        return '';
      }

      const benefits = [];
      const list = clientInfo.data.benefitsForProviders;
      for (let i = 0; i < list.length; i++) {
        if (list[i].selected) {
          benefits.push(list[i].text);
        }
      }

      return benefits.join(', ');
    },
  },
  methods: {},
  async beforeRouteEnter(to, from, next) {
    await store.dispatch('profiles/checkProfile', { id: to.params.id });

    next(async (vm) => {
      const { id } = vm.$route.params;
      vm.loader.show();
      await vm.$store.dispatch('profiles/fetchClientProfile', id).catch(() => {
        vm.$store.dispatch('setPageInfo', { title: 'Not Accessible', subtitle: '' });
      });
      vm.loader.hide();
    });
  },
};
</script>
