<template>
  <div class="helper-reviews">
    <q-field label="References" v-if="helper.hasVouches" borderless readonly hide-bottom-space>
        <template v-slot:prepend>
          <q-icon name="grading" />
        </template>
        <template v-slot:control>
          <div class="full-width q-mt-md">
            Available on request. Please contact Customer Service to obtain referee details.
          </div>
        </template>
    </q-field>
    <q-field label="Reviews" v-if="helper.ratingsExtended && helper.ratingsExtended.length > 0" borderless readonly hide-bottom-space>
        <template v-slot:prepend>
          <q-icon name="reviews" />
        </template>
        <template v-slot:control>
          <div class="full-width q-mt-md">
            <div
              v-for="review in helper.ratingsExtended" :key="review.jobId"
              class="helper-review q-my-md">
                <div>
                  <span>{{getJobTypeText(review)}}</span> on {{getReviewDate(review)}}
                </div>
                <!-- <div class="q-mb-xs">
                  Rated by {{getReviewerName(review)}}
                </div> -->
                <div class="row items-center">
                  <span>Rated</span>
                  <q-icon name="star" class="text-brand-orange q-mx-xs" size="18px" />
                  <span>{{getReviewDetails(review)}}</span>
                </div>
                <div v-if="review.feedback">
                  {{review.feedback}}
                </div>
            </div>
          </div>
        </template>
    </q-field>
  </div>
</template>

<script>
import { date } from 'quasar';
import { jobTypeText } from '../../utils/helpers';

export default {
  name: 'HelperReviews',
  props: ['helper'],
  data() {
    return {
    };
  },
  computed: {
  },
  methods: {
    getReviewDetails(review) {
      return `${review.ratingAvg.toFixed(1)} by ${this.getReviewerName(review)}`;
    },
    getJobTypeText(review) {
      return jobTypeText(review.jobType, review.clientType);
    },
    getReviewerName(review) {
      return `${review.clientFirstName} ${review.clientLastName}`;
    },
    getReviewDate(review) {
      return `${date.formatDate(review.ratingDate, 'DD/MM/YYYY')}`;
    },
    getReviewFromText(review) {
      return `From ${this.getReviewerName(review)} - ${this.getReviewDate(review)}`;
    },
  },
};
</script>
