<template>
  <q-page class="layout-padding notifications-page">
    <div class="q-mb-sm">
      <div class="text-subtitle2 q-mb-sm">Push Notifications</div>
      <div class="q-mb-sm">
        To receive {{isClient ? 'Job Posts' : 'Job Invites'}} & Chat messages, your device needs to allow push notifications.
        <span v-if="!hasActiveChannel">To do this, turn on notifications in your app settings.</span>
      </div>
      <table>
        <tbody>
          <tr>
            <td class="text-left">
              <q-icon size="30px" class="q-mx-sm" :name="hasActiveChannel ? 'notifications_active' : 'notifications_off'" :color="hasActiveChannel ? 'primary' : 'secondary'" />
            </td>
            <td v-if="hasActiveChannel">
              Your device is configured to allow push notifications.
            </td>
            <td v-else>
              <div>Your device is <b>not</b> allowing push notifications. Please follow these instructions then tap Continue.</div>
            </td>
          </tr>
          <tr v-if="!hasActiveChannel">
            <td class="text-left">
            </td>
            <td>
              <div v-if="isAndroidDevice">
                <ol class="no-margin q-pl-md">
                  <li>Open your phone's Settings</li>
                  <li>Tap Apps and Notifications</li>
                  <li>Scroll to find the Juggle St app</li>
                  <li>Tap the app</li>
                  <li>Tap Notifications</li>
                  <li>Tap on 'All Juggle St Notifications' </li>
                </ol>
              </div>
              <div v-if="isIosDevice">
                <ol class="no-margin q-pl-md">
                  <li>Open your phone's Settings</li>
                  <li>Tap Notifications</li>
                  <li>Scroll to find the Juggle St app</li>
                  <li>Tap the app</li>
                  <li>Tap on “Allow Notifications”</li>
                </ol>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
      <div v-if="hasActiveChannel" class="text-center q-my-sm">
        <q-btn label="Continue" color="accent" @click="routerReplace('/dashboard')" />
      </div>
      <div v-else>
        <div class="text-center q-my-sm">
          <q-btn :loading="checkingPushRegistration" label="Continue" color="accent" @click="handleRegisterPushNotifications" />
        </div>
        <div class="">
          If you have any issues, please contact us using the Help icon below.
        </div>
      </div>
    </div>
    <div class="q-mb-xl" v-if="showNotifications && notifications && notifications.length > 0">
      <div class="text-subtitle2 q-mb-sm">Recent Notifications</div>
      <table class="q-table horizontal-separator notifications-table">
        <thead>
          <tr>
            <th class="text-center">Date</th>
            <th class="text-center">Message</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="notification in notifications" :key="notification.id">
            <td class="text-center">{{ formatDate(notification.date) }}</td>
            <td class="text-center" @click="navigateToRoute(notification)">
              {{ notification.body }}
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <div class="q-my-sm" v-if="notificationsMeta.lastFetched !== null && notifications.length === 0">
      No recent notifications
    </div>
  </q-page>
</template>

<script>
import {
  mapState,
} from 'vuex';
import { date } from 'quasar';
import NotificationService from '../../utils/notification-service';
import { delay } from '../../utils/helpers';

export default {
  name: 'PageAccountNotifications',
  computed: {
    ...mapState({
      notifications: (state) => state.notifications.data,
      notificationsMeta: (state) => state.notifications.meta,
      pushNotifications: (state) => state.pushNotifications,
    }),
    registeredForPushNotifications() {
      return this.pushNotifications.pushChannel !== null;
    },
    enabledPushNotifications() {
      return this.pushNotifications.enabled;
    },
    hasActiveChannel() {
      return this.registeredForPushNotifications && this.enabledPushNotifications;
    },
  },
  data() {
    return {
      showNotifications: false,
      checkingPushRegistration: false,
    };
  },
  methods: {
    formatDate(invoiceDate) {
      return date.formatDate(invoiceDate, 'DD/MM/YY');
    },
    async handleRegisterPushNotifications() {
      this.checkingPushRegistration = true;
      await this.$store.dispatch('registerPushNotifications');
      await delay(2000);
      this.checkingPushRegistration = false;

      if (!this.hasActiveChannel) {
        NotificationService.negative('Push notifications are still disabled. Please follow the instructions mentioned above and try again.');
      }
    },
    navigateToRoute(notification) {
      if (!notification || !notification.route) return;

      let route = notification.route;
      const routeIdx = route.indexOf('#');
      if (routeIdx !== -1) {
        route = route.substring(routeIdx + 1);
      }

      this.routerPush(route);
    },
  },
  beforeRouteEnter(to, from, next) {
    next(async (vm) => {
      // vm.$store.dispatch('fetchNotifications');
      vm.$store.dispatch('updatePushSettings');
    });
  },
};
</script>
<style lang="sass">
</style>
