<template>
  <div class="profile-wizard-introduce-family layout-padding">
    <p style="margin-top: 10px">
      Family Introduction
    </p>
    <p>
      Please describe your family, but do not include any info that will need keeping up-to-date, such as the age of your children. <br/> <br/>Do <strong>NOT</strong> include job details here, you do this when you Post a Job.
    </p>
    <q-field
      bottom-slots
      borderless
    >
      <template v-slot:prepend>
        <q-icon name="border_color" />
      </template>
      <template v-slot:control>
        <div class="full-width">
          <q-input
            v-model="aboutMe"
            type="textarea"
            placeholder="Example - Hi there, we are a busy family of four. We are looking for local babysitters to help us, as my husband and I both often travel for work."
            :max-height="100"
            rows="5"
          />
        </div>
      </template>
      <template v-slot:hint>
        required
      </template>
    </q-field>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex';
import beforeLeaveStep from '../before-leave-step';
import * as types from '../../../store/mutation-types';

export default {
  name: 'ProfileWizardIntroduceFamilyStep',
  data() {
    return {
      hasSpecialNeedsTraining: 0,
    };
  },
  methods: {
    ...mapActions([
      'updateUserInfo',
    ]),
    validate() {
      return true;
    },
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
      stepComplete: (state) => {
        const { meta } = state.profileWizard;
        const currentStep = meta.steps[meta.currentStep];
        return currentStep.complete;
      },
    }),
    storeModuleName() {
      return 'userInfo';
    },
    aboutMe: {
      get() {
        return this.userInfo.aboutMe;
      },
      set(value) {
        this.setProperty('aboutMe', value);
      },
    },
  },
  watch: {
    aboutMe(newAboutMe) {
      const complete = !!newAboutMe;
      this.$store.commit(`profileWizard/${types.UPDATE_STEP_STATUS}`, { complete });
    },
  },
  beforeRouteLeave(to, from, next) {
    // Update user info before proceeding to the next step if step is complete
    if (this.stepComplete) {
      this.updateUserInfo().then(() => {
        beforeLeaveStep(this, to, from, next);
      });
    } else {
      beforeLeaveStep(this, to, from, next);
    }
  },
};
</script>
