<template>
  <q-page class="">
    <div class="layout-padding">
      <div class="group">
        <q-field
          stack-label
          borderless
          label="Driving">
          <template v-slot:prepend>
            <q-icon name="directions" />
          </template>
          <template v-slot:control>
            <q-chip
              clickable
              outline
              :icon="item.selected ? 'check' : 'clear'"
              :color="item.selected ? 'primary' : 'black'"
              :text-color="item.selected ? 'white' : 'black'"
              v-for="(item, index) in userInfo.provider.transport" :key="item.optionId"
              @click="handleItemClick('transport', index)"
              class="q-mx-xs q-my-xs q-caption profile-tab-margin"
            >
              {{item.text}}
            </q-chip>
          </template>
        </q-field>
      </div>

      <div class="group">
        <q-field
        stack-label
        borderless
        label="Spoken Languages">
          <template v-slot:prepend>
            <q-icon name="class" />
          </template>
          <template v-slot:control>
            <q-chip
              clickable
              outline
              :icon="item.selected ? 'check' : 'clear'"
              :color="item.selected ? 'primary' : 'black'"
              :text-color="item.selected ? 'white' : 'black'"
              v-for="(item, index) in userInfo.provider.spokenLanguages" :key="item.optionId"
              @click="handleItemClick('spokenLanguages', index)"
              class="q-mx-xs q-my-xs q-caption profile-tab-margin"
            >
              {{item.text}}
            </q-chip>
          </template>
        </q-field>
      </div>
    </div>
  </q-page>
</template>

<script>
import { mapState } from 'vuex';
import NotificationService from '../../utils/notification-service';
import * as mutationTypes from '../../store/mutation-types';

export default {
  name: 'PageAccountSkills',
  data() {
    const specialNeedsExp = this.$store.state.userInfo.data.provider.myExperience2;
    return {
      hasSpecialNeedsTraining: specialNeedsExp && specialNeedsExp.length > 0 ? 1 : 0,
    };
  },
  methods: {
    handleItemClick(section, index) {
      const options = this.userInfo.provider[section];
      const currentOption = options[index];

      const selectedOptions = [];
      options.forEach((option) => {
        let selected = false;
        if (option.optionId === currentOption.optionId) {
          selected = !option.selected;
        } else {
          // eslint-disable-next-line
          selected = option.selected;
        }

        if (selected) {
          selectedOptions.push(option.optionId);
        }
      });

      const payload = {
        propPath: `provider.${section}`,
        value: selectedOptions,
      };

      this.$store.commit(mutationTypes.UPDATE_USER_OPTIONS, payload);
    },
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
    }),
    storeModuleName() {
      return 'userInfo';
    },
  },
  beforeRouteLeave(to, from, next) {
    this.$store.dispatch('updateUserInfo').then(() => {
      next();
    }).catch((error) => {
      NotificationService.negative(error.message);
      next(false);
    });
  },
};
</script>
