  </div>
</template>

<script>
import { mapState } from 'vuex';
import EssentialLink from 'components/EssentialLink';

export default {
  name: 'ProfilRanking',
  props: ['displayDetails'],
  components: { EssentialLink },
  methods: {
    getPointsSign(value) {
      if (value === 0) return '+ various';

      if (value < 0) return value;

      return `+${value}`;
    },
    entryIcon(rank) {
      return rank.entriesCount > 0 ? 'check_circle' : 'radio_button_unchecked';
    },
    entryIconColor(rank) {
      return rank.entriesCount > 0 && rank.pointsPerEntry > 0 ? 'primary' : 'black';
    },
    entryPercentage(rank) {
      if (rank.entriesCount > 0) return '';

      const perc = Math.round((rank.pointsPerEntry / this.pendingProfileTotalPoints) * 100);
      return `${perc > 0 ? '+' : ''}${perc}%`;
    },
    entryPercentageColor(rank) {
      if (rank.entriesCount > 0) return '';

      const perc = Math.round((rank.pointsPerEntry / this.pendingProfileTotalPoints) * 100);
      return perc >= 0 ? 'text-primary' : 'text-red';
    },
    getLink(rank) {
      switch (rank.rankingCategory) {
        case 'profile.video':
          return '/account/profile-video';
        case 'response.1hour':
        case 'response.1day':
          return '/helpers/response-stats';
        case 'profile.wwcc':
          return '/account/my-certificates';
        case 'profile.vouches':
          return '/account/personal-references';
        case 'profile.online-payments':
          return '/account/payment-methods';
        case 'profile.public':
          return '/account/profile-visibility';
        default:
          return '';
      }
    },
    getCaption(rank) {
      if (rank.entriesCount > 0) {
        switch (rank.rankingCategory) {
          case 'response.1hour':
            return 'Maintain your excellent Response Times!';
          case 'response.1day':
            return 'Improve to 1 Hour and double your points!';
          case 'response.slow':
            return 'This Response Time is below parent expectations. Respond quicker to all parent job invitations and in-app messages. ';
          default:
            return 'Completed!';
        }
      }

      switch (rank.rankingCategory) {
        case 'profile.photo':
          return 'Upload a recent portrait photo';
        case 'profile.active':
          return 'Job preferences & validated checks need to be completed before families can see your profile.';
        case 'profile.video':
          return 'Upload a short video, this is the best way to introduce yourself to new families. Helpers with a video receive more job invitations.';
        case 'response.1hour':
          return 'Receive maximum points by responding within 1 Hour to all parent job invitations and messages.';
        case 'response.1day':
          return 'Respond quicker to parents to receive more job invitations. ALWAYS respond to job invitations and in-app messages.';
        case 'response.slow':
          return 'Response Times slower than 1 Day are below parent expectations. Respond quicker to all parent job invitations and in-app messages. ';
        case 'profile.wwcc':
          return 'Commonly requested by parents.';
        case 'profile.vouches':
          return 'Add referees who can provide a personal reference about you. Very important for all new Juggle St helpers.';
        case 'profile.online-payments':
          return 'Give families the flexibility to pay you in cash or via bank transfer. Simply add your bank details. Juggle St does NOT take a fee or commission.';
        case 'profile.public':
          return 'Allow parents to view your profile on Juggle Street’s public website. No personal details are shared on public website.';
        case 'profile.references':
          return 'Add referees who can provide a personal reference about you. Very important for all new Juggle St helpers.';
        default:
          return '';
      }
    },
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
    }),
    storeModuleName() {
      return 'userInfo';
    },
    pendingProfileTotalPoints() {
      let score = 0;
      this.userInfo.rankingItems.forEach((item) => {
        if (item.userManageable) {
          score += item.pointsPerEntry;
        }
      });

      return score;
    },
    pendingProfilePoints() {
      let score = 0;
      this.userInfo.rankingItems.forEach((item) => {
        if (item.entriesCount > 0 && item.userManageable) {
          score += item.totalPoints;
        }
      });

      return score;
    },
    completenessPercentage() {
      return Math.round((this.pendingProfilePoints / this.pendingProfileTotalPoints) * 100);
    },
    // pendingPercentage() {
    //   return 100 - this.completenessPercentage;
    // },
    // rankingItems() {
    //   return this.userInfo.rankingItems;
    // },
    // activeItems() {
    //   return this.userInfo.rankingItems.filter((r) => r.entriesCount > 0);
    // },
    userManageableItems() {
      // const completed = this.userInfo.rankingItems.filter((r) => r.userManageable && r.entriesCount === 0);
      // const pending = this.userInfo.rankingItems.filter((r) => r.userManageable && r.entriesCount > 0);

      // return completed.concat(pending);

      return this.userInfo.rankingItems.filter((r) => r.userManageable && r.visible);
    },
    negativeItems() {
      const negativeItems = this.userInfo.rankingItems.filter((r) => r.entriesCount === 0 && r.pointsPerEntry < 0);
      negativeItems.push({ label: 'Low Response Rate', pointsPerEntry: -200 });

      return negativeItems;
    },
    profileRank() {
      return this.userInfo.accountScore2Capped.toLocaleString('en-AU');
    },
    calculatedProfileRank() {
      let score = 0;
      this.userInfo.rankingItems.forEach((item) => {
        score += item.totalPoints;
      });

      return score.toLocaleString('en-AU');
    },
  },
};
</script>
<style lang="sass">
.profile-ranking
  .ranking-bar
    .completeness-msg
      padding: 0.5rem 0
    .completeness-bar
      width: 100%
      height: 1.5rem
      background: $blue-grey-1
      border: 1px solid $blue-grey-4
      .completed
        height: 100%
        background: $blue-grey-6
      .pending
        height: 100%
</style>
