<template>
  <div class="filters-section">
    <div v-if="mode === 'summary'">
      <q-field
        label="Candidate Criteria"
        borderless
        readonly
        hide-bottom-space
        v-if="Object.keys(selectedFilters).length > 0"
      >
        <template v-slot:prepend>
          <q-icon name="people" />
        </template>
        <template v-slot:control>
          <div class="full-width q-mt-md">
            <div class="" v-for="(filter, key) in selectedFilters" :key="key">
              <span class="">
                {{filter.title}}:
              </span>
              <span>
                {{getFilterValue(filter)}}
              </span>
            </div>
          </div>
        </template>
      </q-field>
    </div>
    <div class="filters-container row" v-else>
      <q-field
        stack-label
        borderless
        hide-bottom-slots
      >
        <template v-slot:prepend>
          <q-icon name="assignment" />
        </template>
        <template v-slot:control>
          <div class="full-width q-mt-sm q-mx-md">
            <div v-if="isProcessing || !lowCandidates">
              Select the criteria candidates “must have” to apply for this job. You can view the detailed profile of all applicants before deciding to interview.
            </div>
            <div v-else>
              <span class="text-bold text-red">Warning</span>
               – your current criteria selection is restricting the total number of available candidates.
              <span class="text-red"> *</span>
            </div>
          </div>
        </template>
      </q-field>
      <div class="filters-item col-6" v-for="(filter, key) in filters" :key="key">
        <q-field class="" :label="filter.title" v-if="!filter.visible || filter.visible(jobType)" borderless readonly>
          <template v-slot:prepend>
            <q-icon :name="filter.icon" />
          </template>
          <template v-slot:control>
            <div class="full-width q-mt-md">
              <q-select v-if="filter.type === 'select'" :options="getFilterFields(filter.fields, jobType)" v-model="filter.selected" emit-value map-options />
              <q-option-group v-else size="sm" :inline="filter.inline" :options="getFilterFields(filter.fields, jobType)" :type="filter.type" v-model="filter.selected"/>
            </div>
          </template>
        </q-field>
      </div>
      <div v-if="!isProcessing && lowCandidates" class="q-ml-xs q-mt-sm">
        <span class="text-red">* </span> Only include “must have” criteria.
      </div>
    </div>
  </div>
</template>

