export default {
  name: 'FiltersSection',
  data() {
    return {
      filters: {},
      searchByNameMatches: [],
      watchers: [],
      userName: '',
    };
  },
  props: ['filterType', 'jobType', 'mode'],
  emits: ['filteredUsers'],
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
      helpersMeta: (state) => state.filters.meta,
    }),
    enableSearchByName() {
      return true;
    },
    lowCandidates() {
      if (this.filterType === 'postAuPairJob') return false;

      let hasMaxCriteria = true;
      if (this.filterType === 'postRecurringJob') {
        hasMaxCriteria = (!this.selectedFilters.distance || this.selectedFilters.distance.selected === c.distanceGroup.within10km)
          && (!this.selectedFilters.age || this.selectedFilters.age.selected.length === 4)
          && (!this.selectedFilters.driving || this.selectedFilters.driving.selected.length === 0)
          && (!this.selectedFilters.other || this.selectedFilters.other.selected.length === 0);
      } else if (this.filterType === 'postHomeTutoringJob') {
        hasMaxCriteria = (!this.selectedFilters.age || this.selectedFilters.age.selected.length === 4)
          && (!this.selectedFilters.tutoringCategory || this.selectedFilters.tutoringCategory.selected.length === 4);
      } else if (this.filterType === 'postOnlineTutoringJob') {
        hasMaxCriteria = (!this.selectedFilters.age || this.selectedFilters.age.selected.length === 4)
          && (!this.selectedFilters.tutoringCategory || this.selectedFilters.tutoringCategory.selected.length === 4);
      }

      return !hasMaxCriteria && this.helpersMeta.total < 20;
    },
    selectedFilters() {
      const selectedFilters = {};

      Object.keys(this.filters).forEach((key) => {
        const filterInfo = this.filters[key];
        if (filterInfo.type === 'checkbox') {
          if (filterInfo.selected.length > 0 && filterInfo.selected.length < 4) {
            selectedFilters[key] = filterInfo;
          }
        } else if (filterInfo.type === 'radio') {
          if (filterInfo.selected > 0) {
            selectedFilters[key] = filterInfo;
          }
        } else if (filterInfo.type === 'select') {
          if (filterInfo.selected > 0) {
            selectedFilters[key] = filterInfo;
          }
        }
      });

      return selectedFilters;
    },
    sortBy: {
      get() {
        return this.$store.state.filters.sortBy;
      },
      set(value) {
        this.$store.dispatch('filters/setSortBy', { sortBy: value });
        this.filterUsers();
      },
    },
  },
  watch: {
    filterType(value) {
      if (!value) return;

      this.configureFilters();
    },
  },
  methods: {
    ...mapMutations('filters', {
      updateFilterOption: types.UPDATE_FILTER_OPTION,
    }),
    ...mapActions({
      applyFilter: 'filters/applyFilter',
      searchByName: 'filters/searchByName',
    }),
    getFilterValue(filter) {
      if (filter.type === 'radio') {
        const field = filter.fields.find((f) => f.value === filter.selected);
        if (field) return field.label;
      }

      if (filter.type === 'checkbox') {
        const values = [];
        filter.selected.forEach((s) => {
          const field = filter.fields.find((f) => f.value === s);
          if (field) {
            values.push(field.label);
          }
        });

        return values.join(', ');
      }

      if (filter.type === 'select') {
        const field = filter.fields.find((f) => f.value === filter.selected);
        if (field) return field.label;
      }

      return '';
    },
    getFilterFields(fields, jobType) {
      const filteredFields = [];
      fields.forEach((field) => {
        if (!field.visible || field.visible(jobType)) {
          filteredFields.push(field);
        }
      });

      return filteredFields;
    },
    configureFilters() {
      let filters;

      this.watchers.forEach((unwatch) => {
        unwatch();
      });

      if (this.filterType === 'babysittersNearMe') {
        filters = babysittersNearMeFilters;
      } else if (this.filterType === 'nanniesNearMe') {
        filters = nanniesNearMeFilters;
      } else if (this.filterType === 'auPairsNearMe') {
        filters = auPairsNearMeFilters;
      } else if (this.filterType === 'tutorsNearMe') {
        // Workaround as env is not at the time of creating the script.
        // tutorsNearMeFilters.homeTutoringGroup.fields[1].label = env.country === 'au' ? 'High School' : 'Secondary School';
        tutorsNearMeFilters.jobTypes.fields[1].label = env.country === 'au' ? 'High School' : 'Secondary School';

        filters = tutorsNearMeFilters;
      } else if (this.filterType === 'onlineTutors') {
        // Workaround as env is not at the time of creating the script.
        // tutorsNearMeFilters.homeTutoringGroup.fields[1].label = env.country === 'au' ? 'High School' : 'Secondary School';
        onlineTutorFilters.jobTypes.fields[1].label = env.country === 'au' ? 'High School' : 'Secondary School';

        filters = onlineTutorFilters;
      } else if (this.filterType === 'postOneOffJob') {
        filters = postOneOffJobFilters;
      } else if (this.filterType === 'postRecurringJob') {
        filters = postRecurringJobFilters;
      } else if (this.filterType === 'postAuPairJob') {
        filters = postAuPairJobFilters;
      } else if (this.filterType === 'postHomeTutoringJob') {
        // postHomeTutoringJobFilters.primarySchoolYearGroups.fields[0].label = env.country === 'au' ? 'Kindy' : 'Year 1 - 2';
        // postHomeTutoringJobFilters.primarySchoolYearGroups.fields[1].label = env.country === 'au' ? 'Year 1 - 3' : 'Year 3 - 5';
        // postHomeTutoringJobFilters.primarySchoolYearGroups.fields[2].label = env.country === 'au' ? 'Year 4 - 6' : 'Year 6 - 8';

        // postHomeTutoringJobFilters.highSchoolYearGroups.fields[0].label = env.country === 'au' ? 'Year 7 - 8' : 'Year 9 - 10';
        // postHomeTutoringJobFilters.highSchoolYearGroups.fields[1].label = env.country === 'au' ? 'Year 9 - 10' : 'Year 11 - 12';
        // postHomeTutoringJobFilters.highSchoolYearGroups.fields[2].label = env.country === 'au' ? 'Year 11 - 12' : 'Year 13 - Uni Prep';

        if (postHomeTutoringJobFilters.year12GraduationYear) {
          postHomeTutoringJobFilters.year12GraduationYear.title = env.country === 'au' ? 'Graduated High School' : 'Graduated Secondary School';
        }

        filters = postHomeTutoringJobFilters;
      } else if (this.filterType === 'postOnlineTutoringJob') {
        // postOnlineTutoringJobFilters.primarySchoolYearGroups.fields[0].label = env.country === 'au' ? 'Kindy' : 'Year 1 - 2';
        // postOnlineTutoringJobFilters.primarySchoolYearGroups.fields[1].label = env.country === 'au' ? 'Year 1 - 3' : 'Year 3 - 5';
        // postOnlineTutoringJobFilters.primarySchoolYearGroups.fields[2].label = env.country === 'au' ? 'Year 4 - 6' : 'Year 6 - 8';

        // postOnlineTutoringJobFilters.highSchoolYearGroups.fields[0].label = env.country === 'au' ? 'Year 7 - 8' : 'Year 9 - 10';
        // postOnlineTutoringJobFilters.highSchoolYearGroups.fields[1].label = env.country === 'au' ? 'Year 9 - 10' : 'Year 11 - 12';
        // postOnlineTutoringJobFilters.highSchoolYearGroups.fields[2].label = env.country === 'au' ? 'Year 11 - 12' : 'Year 13 - Uni Prep';

        if (postOnlineTutoringJobFilters.year12GraduationYear) {
          postOnlineTutoringJobFilters.year12GraduationYear.title = env.country === 'au' ? 'Graduated High School' : 'Graduated Secondary School';
        }

        filters = postOnlineTutoringJobFilters;
      } else if (this.filterType === 'familiesNearMe') {
        filters = familiesNearMeFilters;
      } else if (this.filterType === 'helperInvitations') {
        filters = helperInvitationsFilter;
      } else if (this.filterType === 'familyInvitations') {
        filters = familyInvitationsFilter;
      } else {
        filters = {};
      }

      if (this.isFamily && (this.userInfo.defaultAddress.state === 'act' || this.userInfo.country === 'nz')) {
        if (filters.other && filters.other.fields.length === 3) {
          filters.other.fields.splice(0, 1);
        }
      }

      // Set store values.
      const filterValues = this.$store.state.filters.filterInfo[this.filterType];
      Object.keys(filters).forEach((key) => {
        const value = filterValues[key];
        filters[key].selected = value instanceof Array ? Object.assign([], value) : value;
      });

      // Set watchers.
      const watchers = [];
      Object.keys(filters).forEach((key) => {
        const path = `filters.${key}.selected`;

        const watcher = this.$watch(path, (value) => {
          const filterInfo = filters[key];
          if (filterInfo.singleSelection) {
            if (value.length > 1) {
              value = value.slice(value.length - 1, value.length);
              filters[key].selected = value;
            }
          }

          const payload = {
            filterType: this.filterType,
            filterKey: key,
            value,
          };

          this.updateFilterOption(payload);

          if (filterInfo.secondaryFilter) {
            const secondaryFilter = filterInfo.secondaryFilter(value, this.jobType, this.userInfo);
            secondaryFilter.filterType = this.filterType;
            this.updateFilterOption(secondaryFilter);
          }

          this.filterUsers(false);
        });

        watchers.push(watcher);
      });

      this.filters = filters;
      this.watchers = watchers;
    },
    filterUsers() {
      this.loader.show();
      this.setPageState('pageIndex', 1);
      if (!this.pageState.pageSize) {
        this.setPageState('pageSize', 50);
      }

      this.$store.dispatch('setPageProcessing', { isProcessing: true });
      this.doubleRaf(() => {
        const payload = {
          filterKey: this.filterType,
          pageIndex: this.pageState.pageIndex,
          pageSize: this.pageState.pageSize,
        };

        this.applyFilter(payload).then(() => {
          this.$emit('filteredUsers', { total: this.helpersMeta.total });
        });
      });
    },
    moduleName() {
      const { jobTypes } = this.$store.state.filters.filterInfo[this.filterType];
      // const moduleName = this.$route.path.indexOf('invite-more-helpers') !== -1 ? 'families' : jobTypes === c.jobType.babysitting ? 'postOneOffJob' : jobTypes === c.jobType.auPair ? 'postAuPairJob' : 'postRecurringJob';
      // return moduleName;

      if (this.$route.path.indexOf('invite-more-helpers') !== -1) return 'families';

      if (jobTypes && jobTypes.length > 0) {
        const moduleName = getJobModuleName(jobTypes[0]);
        return moduleName;
      }

      return '';
    },
    handleFilterButtonClick() {
      this.filterUsers();
    },
    togglePanel() {
      this.$store.dispatch('toggleRightDrawer');
    },
    async search(searchText, update, abort) {
      if (searchText.length < 2) {
        abort();
        return;
      }

      const results = await this.searchByName(searchText);

      update(() => {
        const searchResults = results.map((result) => ({
          label: result.publicName,
          value: result.id,
          avatar: getImageUrl(result),
          sublabel: result.suburb,
          stamp: (this.filterType === 'auPairsNearMe' || this.filterType === 'onlineTutors') ? '' : `${result.distanceInKm}km`,
          user: result,
        }));
        this.searchByNameMatches = searchResults;
      });
    },
    selected(item) {
      if (this.filterType.startsWith('post')) {
        const helper = item.user;
        const isAvailable = this.$store.state.filters.data.find((user) => user.id === item.value);
        if (!isAvailable) {
          // Add the user to to the filters list.
          this.$store.dispatch('filters/addUser', helper);
        }

        this.$store.dispatch(`${this.moduleName()}/toggleApplicant`, helper);
        this.userName = '';
        const { applicants } = this.$store.state[this.moduleName()].jobDetails.data;
        const isSelected = applicants.find((applicant) => applicant.applicantId === helper.id);
        if (isSelected) {
          NotificationService.positive(`${helper.publicName} has been added to your list`);
        } else {
          NotificationService.positive(`${helper.publicName} has been removed from your list`);
        }
      } else {
        // this.$parent.$parent.$refs.layout.hideCurrentSide(() => {
        //   this.userName = '';
        //   if (this.isFamily) {
        //     this.routerPush('/account/helper-profile/' + item.value);
        //   } else {
        //     this.routerPush('/account/client-profile/' + item.value);
        //   }
        // });
        this.$store.dispatch('toggleRightDrawer');
        this.userName = '';
        if (this.isFamily) {
          const query = getHelperProfileDefaultTab(this.filterType);
          this.routerPush(`/account/helper-profile/${item.value}?${query}`);
        } else {
          this.routerPush(`/account/client-profile/${item.value}`);
        }
      }
    },
  },
  mounted() {
    this.configureFilters();
  },
};
</script>
<style lang="sass">
.filters-section
  .q-field__native
    padding-bottom: 0
</style>
