<template>
  <div class="applicant-card">
    <div class="applicant-info">
      <img class="round-user-image applicant-info-image" :src="applicant.applicantImageSrc">
      <div class="col">
        <div class="applicant-info-name">
          <router-link :to="'/account/helper-profile/' + applicant.applicantId">{{applicant.applicantFirstName}} {{applicant.applicantLastInitial}}</router-link>
          <span v-html="getApplicationStatus()"/>
        </div>
        <div class="applicant-info-other">
          <div>
            {{applicant.suburb}} - {{applicant.distanceInKiloMetersRounded}}km away
          </div>
          <div v-if="applicant.applicantRatingsAvg > 0">
            <q-rating
              :model-value="applicant.applicantRatingsAvg"
              :max="5"
              color="accent"
              readonly
            />
            - {{applicant.applicantRatingsCount}} Rating{{applicant.applicantRatingsCount !== 1 ? 's' : ''}}
          </div>
          <div v-if="applicant.gettingHome">
            Getting Home: {{gettingHome()}}
          </div>
        </div>
      </div>
      <div class="chat-button" v-if="mode === types.MODE.SHORTLISTED">
        <q-icon color="black" size="36px" name="chat" @click="handleChat" />
      </div>
    </div>
      <div class="actions-container" v-if="mode === types.MODE.SHORTLISTED">
        <q-btn v-if="applicant.applicantPhoneNumber" color="secondary" icon="phone"><a class="text-white" :href="'tel:'+applicant.applicantPhoneNumber">Call</a></q-btn>
        <q-btn color="accent" icon="done" @click="handleAwardJob" v-if="showAwardButton(applicant.applicationStatus)">Award</q-btn>
      </div>
  </div>
</template>

<script>
import { getUnavailableReasonText, gettGettingHomeTextForFamilies } from '../../utils/helpers';
import c from '../../utils/constants';

const MODE = {
  SHORTLISTED: 'shortlisted',
  DECLINED: 'declined',
  WAITING: 'waiting',
};

export default {
  name: 'OneOffJobApplicantCard',
  props: ['applicant', 'mode'],
  emits: ['awardJob'],
  data() {
    return {
      types: {
        MODE,
      },
    };
  },
  computed: {
  },
  methods: {
    gettingHome() {
      return gettGettingHomeTextForFamilies(this.applicant.gettingHome);
    },
    getApplicationStatus() {
      let textColor;
      let icon;
      let title;
      const { applicationStatus } = this.applicant;
      if (applicationStatus === c.jobApplicationStatus.viewed) {
        textColor = 'primary';
        icon = 'visibility';
        title = 'viewed';
      } else if (applicationStatus === c.jobApplicationStatus.pending) {
        textColor = 'faded';
        icon = 'visibility_off';
        title = 'unseen';
      } else if (applicationStatus === c.jobApplicationStatus.awarded) {
        textColor = 'primary';
        icon = 'thumb_up';
        title = 'Job awarded';
      } else if (applicationStatus === c.jobApplicationStatus.notAvailable) {
        textColor = 'secondary';
        icon = 'event_busy';
        title = getUnavailableReasonText(this.applicant.unavailableReason);
      } else if (applicationStatus === c.jobApplicationStatus.applied) {
        textColor = 'primary';
        icon = 'event_available';
        title = 'Applied';
      }

      if (!title) return '';

      return `<span class="text-${textColor}"> - <i class="material-icons">${icon}</i> ${title}</span>`;
    },
    showAwardButton(applicationStatus) {
      return applicationStatus === c.jobApplicationStatus.applied;
    },
    handleChat() {
      const url = `/chat/with/${this.applicant.applicantId}`;
      this.routerPush(url);
    },
    handleAwardJob() {
      this.$emit('awardJob', {
        newApplicant: this.applicant,
      });
    },
  },
};
</script>
<style lang="sass">
.one-off-job-details
  .applicant-card
    border-bottom: 1px solid $grey-1
    margin-bottom: 5px
    .applicant-info
      display: flex
      align-items: flex-start
      padding: 0.5rem
      &-image
        margin-right: 0.5rem
      &-other
        font-size: 0.9rem
  .actions-container
    margin-left: 20px
    padding-top: 0
    display: flex
    justify-content: space-around
</style>
