<template>
  <div class="profile-wizard-introduce-family layout-padding">
    <p style="margin-top: 10px">
      <span class="text-subtitle2">Business Details</span>
    </p>

    <q-field
      borderless
      stack-label
      :error="v$.establishYear.$error"
      label="Your Company was established">
        <template v-slot:control>
          <div class="self-center no-outline" tabindex="0">
            <q-select
              class="q-my-xs"
              v-model="establishYear"
              :options="yearsList"
              hide-bottom-space
              option-value="value"
              option-label="label"
              emit-value
              map-options
              use-input
              hide-selected
              fill-input
              behavior="menu"
            />
          </div>
        </template>
    </q-field>

    <q-field borderless stack-label :error="v$.industrySectorId.$error"
      label="Industry Sector">
        <template v-slot:control>
          <div class="self-center no-outline" tabindex="0">
            <q-select
              class="q-my-xs"
              v-model="industrySectorId"
              :options="industrySectorList"
              hide-bottom-space
              option-value="value"
              option-label="label"
              emit-value
              map-options
              use-input
              hide-selected
              fill-input
              behavior="menu"
            />
            <q-input v-if="industrySectorId === 1001" v-model="industrySectorText" placeholder="Specify Industry" :error="v$.industrySectorText.$error" hide-bottom-space />
          </div>
        </template>
    </q-field>

    <q-field
      borderless
      stack-label
      :error="v$.numberOfMembers.$error"
      label="Number of Employees">
        <template v-slot:control>
          <div class="self-center no-outline" tabindex="0">
            <q-select
              class="q-my-xs"
              v-model="numberOfMembers"
              :options="numberOfMembersList"
              hide-bottom-space
              option-value="value"
              option-label="label"
              emit-value
              map-options
              use-input
              hide-selected
              fill-input
            />
          </div>
        </template>
    </q-field>

    <p style="margin-top: 10px">
      <span class="text-subtitle2">Business Introduction</span>
    </p>

    <q-field
      borderless
      :error="v$.aboutMe.$error"
      stack-label
      bottom-slots
    >
      <template v-slot:control>
        <div class="full-width">
          <p class="q-mt-xs">
            Please describe your business, but do not include any info that will need keeping up-to-date. <br/> <br/>Do <strong>NOT</strong> include job details here, you do this when you Post a Job.
          </p>
          <q-input
            v-model="aboutMe"
            type="textarea"
            placeholder="Example - ABC Pilates is a family owned and run business providing individual and group classes for women of all ages since 2014."
            :max-height="100"
            rows="5"
          />
        </div>
      </template>
      <template v-slot:hint>
        {{aboutMeHelper}}
      </template>
      <template v-slot:error>
        {{aboutMeHelper}}
      </template>
    </q-field>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex';
import useVuelidate from '@vuelidate/core';
import { required, requiredIf } from '@vuelidate/validators';
import beforeLeaveStep from '../before-leave-step';
import * as mutationTypes from '../../../store/mutation-types';
import NotificationService from '../../../utils/notification-service';
import c from '../../../utils/constants';

export default {
  name: 'ProfileWizardIntroduceBusinessStep',
  data() {
    return {

    };
  },
  methods: {
    ...mapActions([
      'updateUserInfo',
    ]),
    isValid() {
      const validAboutMe = this.aboutMe !== null && this.aboutMe.length >= 100;
      const validBusinessData = this.numberOfMembers && this.industrySectorId && this.establishYear;
      const validIndustry = this.industrySectorId !== 1001 || this.industrySectorText;

      const isValid = validAboutMe && validBusinessData && validIndustry;
      return isValid;
    },
    validate() {
      if (this.currentStep.meta.canSkip) {
        return true;
      }

      this.v$.$touch();
      if (this.v$.$error) {
        NotificationService.negative('Please review highlighted fields.');
      }

      return !this.v$.$error;
    },
    triggerChange() {
      if (this.isValid()) {
        this.$store.commit(`profileWizard/${mutationTypes.UPDATE_STEP_STATUS}`, { complete: true });
      }
    },
  },
  setup() {
    return {
      v$: useVuelidate(),
    };
  },
  validations() {
    return {
      aboutMe: {
        required(value) {
          return value !== null && value.length >= 100;
        },
      },
      numberOfMembers: {
        required,
      },
      industrySectorId: {
        required,
      },
      establishYear: {
        required,
      },
      industrySectorText: {
        requiredIf: requiredIf(this.industrySectorId === 1001),
      },
    };
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
      userMeta: (state) => state.userInfo.meta,
      currentStep: (state) => {
        const { meta } = state.profileWizard;
        const currentStep = meta.steps[meta.currentStep];
        return currentStep;
      },
      stepComplete: (state) => {
        const { meta } = state.profileWizard;
        const currentStep = meta.steps[meta.currentStep];
        return currentStep.complete;
      },
    }),
    storeModuleName() {
      return 'userInfo';
    },
    numberOfMembersList() {
      return c.numberOfMembersList;
    },
    industrySectorList() {
      return c.industrySectorsList;
    },
    yearsList() {
      return c.yearsList;
    },
    establishYear: {
      get() {
        return this.userInfo.establishYear;
      },
      set(value) {
        this.setProperty('establishYear', value);
        this.triggerChange();
      },
    },
    aboutMe: {
      get() {
        return this.userInfo.aboutMe;
      },
      set(value) {
        this.setProperty('aboutMe', value);
        this.triggerChange();
      },
    },
    numberOfMembers: {
      get() {
        return this.userInfo.numberOfMembers;
      },
      set(value) {
        this.setProperty('numberOfMembers', value);
        this.triggerChange();
      },
    },
    industrySectorId: {
      get() {
        return this.userInfo.industrySectorId;
      },
      set(value) {
        this.setProperty('industrySectorId', value);
        this.triggerChange();
      },
    },
    industrySectorText: {
      get() {
        return this.userInfo.industrySectorText;
      },
      set(value) {
        this.setProperty('industrySectorText', value);
        this.triggerChange();
      },
    },
    aboutMeHelper() {
      if (this.v$.aboutMe.$error && this.aboutMe) {
        return `required - ${100 - this.aboutMe.length} characters left`;
      }

      return 'required - min 100 characters';
    },
  },
  beforeRouteLeave(to, from, next) {
    // Update user info before proceeding to the next step if step is complete
    if (this.stepComplete) {
      this.updateUserInfo().then(() => {
        beforeLeaveStep(this, to, from, next);
      });
    } else {
      beforeLeaveStep(this, to, from, next);
    }
  },
};
</script>
