<template>
  <div class="">
    <q-field
      label="Select the category that best describes your tutoring skill-set"
      :error="v$.tutoringCategory.$error"
      borderless
      stack-label
    >
      <template v-slot:prepend>
        <q-icon name="class" />
      </template>
      <template v-slot:control>
        <q-list>
          <q-item tag="label">
            <q-item-section avatar>
              <q-radio v-model="tutoringCategory" :val="1"/>
            </q-item-section>
            <q-item-section>
              <q-item-label>Newbie</q-item-label>
              <q-item-label caption>
                <ul class="" style="margin: 5px 0; padding: 0 10px">
                  <li>
                    No tutoring experience
                  </li>
                </ul>
              </q-item-label>
            </q-item-section>
          </q-item>
          <q-item tag="label">
            <q-item-section avatar>
              <q-radio v-model="tutoringCategory" :val="2"/>
            </q-item-section>
            <q-item-section>
              <q-item-label>Apprentice</q-item-label>
              <q-item-label caption>
                <ul class="" style="margin: 5px 0; padding: 0 10px">
                  <li>
                    Some previous tutoring experience, mainly unpaid for family & friends
                  </li>
                </ul>
              </q-item-label>
            </q-item-section>
          </q-item>
          <q-item tag="label">
            <q-item-section avatar>
              <q-radio v-model="tutoringCategory" :val="3"/>
            </q-item-section>
            <q-item-section>
              <q-item-label>Experienced</q-item-label>
              <q-item-label caption>
                <ul class="" style="margin: 5px 0; padding: 0 10px">
                  <li>
                    Received payment for tutoring multiple times
                  </li>
                  <li>
                    Studying to be a teacher
                  </li>
                </ul>
              </q-item-label>
            </q-item-section>
          </q-item>
          <q-item tag="label">
            <q-item-section avatar>
              <q-radio v-model="tutoringCategory" :val="4"/>
            </q-item-section>
            <q-item-section>
              <q-item-label>Professional</q-item-label>
              <q-item-label caption>
                <ul class="" style="margin: 5px 0; padding: 0 10px">
                  <li>
                    Established tutor with deep academic understanding
                  </li>
                  <li>
                    Qualified teacher
                  </li>
                  <li>
                    Certified tutor
                  </li>
                </ul>
              </q-item-label>
            </q-item-section>
          </q-item>
        </q-list>
      </template>
    </q-field>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import useVuelidate from '@vuelidate/core';
import { between } from '@vuelidate/validators';
import NotificationService from '../utils/notification-service';

export default {
  name: 'HelperTutoringCategory',
  emits: ['change'],
  data() {
    return {
    };
  },
  setup() {
    return {
      v$: useVuelidate(),
    };
  },
  validations: {
    tutoringCategory: {
      between: between(1, 10),
    },
  },
  methods: {
    isValid() {
      return this.userInfo.provider.tutoringCategory > 0;
    },
    validate(notify) {
      if (!notify) {
        return this.isValid();
      }

      this.v$.$touch();
      if (this.v$.$error) {
        NotificationService.negative('Please review highlighted fields.');
      }

      return !this.v$.$error;
    },
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
    }),
    storeModuleName() {
      return 'userInfo';
    },
    tutoringCategory: {
      get() {
        return this.userInfo.provider.tutoringCategory;
      },
      set(value) {
        this.setProperty('provider.tutoringCategory', value);
        this.$emit('change', { valid: true });
      },
    },
  },
};
</script>
