<template>
  <div>
    <q-dialog v-model="opened">
      <q-layout style="max-height: 50vh" container class="bg-white">
        <q-header>
          <q-toolbar>
            <q-toolbar-title>
              Update Credit Card
            </q-toolbar-title>
            <q-btn flat>
              <q-icon name="clear" v-close-popup />
            </q-btn>
          </q-toolbar>
        </q-header>

        <q-page-container>
          <div class="layout-padding">
            <card-details-2 ref="cardDetails" :defaultPaymentMethod="1" :displayOptions="false" />
          </div>
        </q-page-container>

        <q-footer class="bg-white">
          <q-toolbar>
            <div class="full-width text-center">
              <q-btn color="accent" @click="handleSave">
                <q-icon name="save"/>
                Update
              </q-btn>
            </div>
          </q-toolbar>
        </q-footer>
      </q-layout>
    </q-dialog>
  </div>
</template>

<script>
import { mapActions } from 'vuex';
import CardDetails2 from 'components/CardDetails2';
import NotificationService from '../utils/notification-service';
import c from '../utils/constants';

export default {
  name: 'UpdateCreditCardModal',
  emits: ['updateCreditCardSuccess'],
  components: { CardDetails2 },
  data() {
    return {
      opened: false,
    };
  },
  methods: {
    ...mapActions('payments', [
      'updatePaymentInfo',
    ]),
    ...mapActions([
      'refreshUserInfo',
    ]),
    open() {
      this.opened = true;
    },
    async handleSave() {
      const { cardDetails } = this.$refs;
      if (!cardDetails.validate()) {
        NotificationService.negative('Please enter valid card details.');
        return;
      }

      this.loader.show();

      const tokenId = await cardDetails.createToken();

      if (!tokenId) {
        NotificationService.negative('Invalid credit card');
        this.loader.hide();
        return;
      }

      const payload = {
        actionType: c.membershipActionType.updatePaymentInfo,
        tokenId,
      };

      this.updatePaymentInfo(payload)
        .then(() => {
          this.refreshUserInfo()
            .then(() => {
              this.loader.hide();
              NotificationService.positive('Your payment details have been successfully changed.');
              this.$emit('updateCreditCardSuccess');
              this.opened = false;
            })
            .catch((error) => {
              this.loader.hide();
              NotificationService.negative(error.message);
            });
        })
        .catch((error) => {
          this.loader.hide();
          NotificationService.negative(error.message || 'Failed to update your payment details.');
        });
    },
  },
  computed: {
  },
};
</script>
