<template>
  <q-page class="">
    <rate-helper ref="rateHelper"/>
  </q-page>
</template>

<script>
import RateHelper from 'components/RateHelper';
import store from '../../store';
import c from '../../utils/constants';

export default {
  name: 'PageFamiliesRateHelper',
  components: { RateHelper },
  async beforeRouteEnter(to, from, next) {
    store.dispatch('resetStoreStructure', { propPath: 'families.pages.rateHelper' });

    next(async (vm) => {
      vm.loader.show();
      const ratingFor = parseInt(vm.$route.query.ratingFor, 10);
      const jobId = parseInt(vm.$route.params.jobId, 10);
      await vm.$store.dispatch('families/fetchJobForRating', { id: jobId, ratingFor, ratingMode: c.ratingMode.page });
      vm.loader.hide();
    });
  },
};
</script>
