<template>
  <q-card class="conversation-card q-py-sm q-my-sm q-mx-sm" @click="handleChat">
    <q-item>
      <q-item-section avatar>
        <q-avatar>
          <img :src="getChatWithAvatar()">
        <q-badge v-if="unreadMessages > 0" color="red" floating>
          {{unreadMessages}}
        </q-badge>
        </q-avatar>
      </q-item-section>

      <q-item-section>
        <q-item-label>
          {{getChatWithName()}}
          <span v-if="unreadMessages > 0"><q-icon name="fiber_new" color="primary" size="sm"/></span>
          <small class="float-right text-grey">{{lastMessageDate}}</small>
        </q-item-label>
        <q-item-label caption class="q-my-xs">
          {{ lastConversationMessage }}
        </q-item-label>
      </q-item-section>
    </q-item>

    <!-- <q-item>
      <q-item-section>
        <q-item-label caption>
          {{ lastConversationMessage }}
        </q-item-label>
      </q-item-section>
    </q-item> -->
  </q-card>
</template>

<script>
import { mapState } from 'vuex';
import { date } from 'quasar';
import { isSameDate } from '../utils/helpers';
import { env } from '../utils/env';

export default {
  name: 'ConversationCard',
  data() {
    return {};
  },
  props: ['conversation'],
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
    }),
    lastConversationMessage() {
      const maxLength = this.isTablet ? 200 : 75;
      if (this.conversation.lastMessage.length < maxLength) {
        return this.conversation.lastMessage;
      }

      const spacePos = this.conversation.lastMessage.indexOf(' ', maxLength);
      if (spacePos === -1) {
        return this.conversation.lastMessage;
      }

      return `${this.conversation.lastMessage.substring(0, spacePos)}...`;
    },
    lastMessageDate() {
      if (!this.conversation.lastMessageDate) {
        return '';
      }

      if (isSameDate(new Date(this.conversation.lastMessageDate), new Date())) {
        return date.formatDate(this.conversation.lastMessageDate, 'hh:mm a');
      }

      return date.formatDate(this.conversation.lastMessageDate, 'ddd D MMM YY');
    },
    unreadMessages() {
      const part = this.getParticipant(true);
      return part.unreadMessages;
    },
  },
  methods: {
    handleChat() {
      const url = `/chat/${this.conversation.id}`;
      this.routerPush(url);
    },
    getParticipant(mine) {
      const currentUserId = this.userInfo.id;
      let participant;
      for (let i = 0; i < this.conversation.participants.length; i++) {
        if ((mine && this.conversation.participants[i].participantId === currentUserId)
        || (!mine && this.conversation.participants[i].participantId !== currentUserId)) {
          participant = this.conversation.participants[i];
          break;
        }
      }

      return participant;
    },
    getChatWithName() {
      const part = this.getParticipant(false);

      if (part.publicName) return part.publicName;
      return `${part.firstName} ${part.lastInitial}`;
    },
    getChatWithAvatar() {
      const part = this.getParticipant(false);
      return this.getImageUrl(part);
    },
    getImageUrl(participant) {
      return `${env.storageUrl}/images/${participant.imageUrl}`;
    },
  },
};
</script>

<style lang="sass">
.conversation-card
  .q-item
    padding-top: 0
    padding-bottom: 0

</style>
