<template>
  <q-page class="layout-padding login-page">
    <q-form class="q-mx-sm" v-on:submit.prevent="handleLogin">
      <q-input stack-label v-model="username" autocomplete="username" type="email" label="Email" clearable :error="v$.username.$error" />
      <q-input stack-label v-model="password" type="password" autocomplete="current-password" label="Password" :error="v$.password.$error" />
      <div class="text-center">
        <q-btn loader @click="handleLogin" outline color="secondary" style="width: 100px">
          Login
        </q-btn>
      </div>
    </q-form>
    <div class="forgot-password-section">
      <q-btn flat color="secondary" class="text-underline" @click="handleForgotPassword" label="Forgot Password?"/>
    </div>
    <div class="join-section">
      <p class="text-bold">Don't have an account? </p>
      <q-btn color="accent" @click="handleSignup" label="Join Now" />
    </div>
  </q-page>
</template>

<style lang="sass">
  .login-page
    .forgot-password-section
      text-align: right
      margin-top: 30px
    .join-section
      margin-top: 50px
      padding: 1rem
      text-align: center
</style>

<script>
import { Screen } from 'quasar';
import useVuelidate from '@vuelidate/core';
import { required, email } from '@vuelidate/validators';
import { login, isAuthorized } from '../api/auth';
import NotificationService from '../utils/notification-service';
import loader from '../utils/loader';
import intercomService from '../utils/intercom-service';
import * as storageManager from '../utils/storage-manager';
import errorHandler from '../utils/error-handler';

const displayedOnboardingPageKey = 'jugglestreet-displayed-onboarding-key';
const enableOnboarding = true;

export default {
  name: 'PageLogin',
  data() {
    return {
      username: '',
      password: '',
    };
  },
  setup() {
    return {
      v$: useVuelidate(),
    };
  },
  validations() {
    return {
      username: {
        required,
        email,
      },
      password: {
        required,
      },
    };
  },
  methods: {
    handleForgotPassword() {
      this.routerReplace('/request-reset-password');
    },
    handleSignup() {
      this.routerPush('/signup');
    },
    handleLogin() {
      this.setFieldsDirty();
      if (this.v$.username.$error || this.v$.password.$error) {
        return;
      }

      loader.show();

      const payload = {
        username: this.username,
        password: this.password,
        grant_type: 'password',
      };
      intercomService.hideIcon();
      login(payload)
        .then(async () => {
          await this.$store.dispatch('fetchCoreData');
          loader.hide();
          if (this.$route.query.redirect) {
            this.routerReplace({ path: this.$route.query.redirect });
          } else {
            this.routerReplace({ path: '/dashboard' });
          }
        })
        .catch((error) => {
          try {
            loader.hide();
            intercomService.showIcon();
            if (error.status === 401) {
              NotificationService.info('Please login to access the platform.');
            } else if (error.status === 400) {
              NotificationService.negative('Invalid email or password.');
            } else if (error.message) {
              throw new Error(error.message);
            } else {
              throw new Error('An error occurred. Please try again.');
            }
          } catch (xcp) {
            errorHandler.captureException(xcp, error, true);
            NotificationService.negative(xcp.message);
          }
        });
    },
    setFieldsDirty() {
      this.v$.username.$touch();
      this.v$.password.$touch();
    },
  },
  async beforeRouteEnter(to, from, next) {
    if (isAuthorized()) {
      next({ path: '/dashboard', replace: true });
      return;
    }
    const validUrl = !to.query.redirect || to.query.redirect === '/dashboard';
    const triggerOnboarding = enableOnboarding && validUrl && Screen.width <= 500;
    if (triggerOnboarding) {
      try {
        const displayedOnboardingPage = await storageManager.getValue(displayedOnboardingPageKey);
        if (!displayedOnboardingPage) {
          next({ path: '/onboarding', replace: true });
          return;
        }
      } catch (error) {
        errorHandler.captureException(error);
      }
    }

    next();
  },
};
</script>
