<template>
  <div class="helper-membership">
    <div v-if="ageInYears >= 18">
      <div v-if="!selectedUserPaymentType">
        <table class="">
          <tbody>
            <tr>
              <td><q-icon size="sm" name="check" color="primary"/></td>
              <td>Receive unlimited job invitations</td>
            </tr>
            <tr>
              <td><q-icon size="sm" name="check" color="primary"/></td>
              <td>You get paid directly, by cach or bank transfer</td>
            </tr>
            <tr>
              <td><q-icon size="sm" name="check" color="primary"/></td>
              <td>Juggle St does not take a fee or a commission</td>
            </tr>
            <tr>
              <td><q-icon size="sm" name="check" color="primary"/></td>
              <td>You keep 100% of your earnings in Juggle St</td>
            </tr>
            <tr>
              <td><q-icon size="sm" name="check" color="primary"/></td>
              <td>Build your reputation with ratings & reviews</td>
            </tr>
          </tbody>
        </table>
      </div>
      <div class="text-center text-h6 text-bold text-primary q-my-sm">{{membershipText}}</div>
      <q-list no-border>
        <q-item-label header style="padding-bottom: 0">Select Membership</q-item-label>
        <q-item tag="label">
          <q-item-section side top>
            <q-radio v-model="userPaymentType" :val="3004" />
          </q-item-section>
          <q-item-section>
            <q-item-label>Quarterly Membership</q-item-label>
            <q-item-label class="text-bold">$15 per quarter</q-item-label>
          </q-item-section>
        </q-item>
        <q-item tag="label">
          <q-item-section side top>
            <q-radio v-model="userPaymentType" :val="3003" />
          </q-item-section>
          <q-item-section>
            <q-item-label>Annual Membership - SAVE 50%</q-item-label>
            <q-item-label class="text-bold">$30 per annum</q-item-label>
          </q-item-section>
        </q-item>
      </q-list>
      <div class="text-center q-my-sm" v-if="!selectedUserPaymentType">
        <q-btn class="" color="accent" @click="handleStart">Continue</q-btn>
      </div>

      <div v-if="selectedUserPaymentType">
        <div class="">
          <card-details-2 ref="cardDetails" :defaultPaymentMethod="paymentMethod" @paymentMethodChanged="handlePaymentMethodChanged"/>
        </div>

        <div class="payment-summary q-mb-md">
          <payment-summary :paymentType="userPaymentType"/>
        </div>

        <div v-if="isPending">
          <div class="text-center">
            <q-btn class="" color="accent" @click="handleUpdatePaymentInfo">Continue</q-btn>
          </div>

          <div class="q-my-md">
            <small>
              * Amount Due will be charged to your credit card once your profile is 100% complete.
            </small>
          </div>
        </div>

        <div v-if="isApproved">
          <div class="text-center">
            <q-btn class="" color="accent" @click="handleProcessMembership">Subscribe</q-btn>
          </div>
          <div class="q-my-md">
            <small v-if="userPaymentType === types.userPaymentTypes.monthlyHelperStandardSubscription5">
              * Your credit card will be debited $5 every month until such time that you cancel your subscription. There are no partial month refunds.
            </small>
            <small v-if="userPaymentType === types.userPaymentTypes.quarterlyHelperStandardSubscription15">
              * Your credit card will be debited $15 every 3 months until such time that you cancel your subscription. There are no partial-period refunds.
            </small>
            <small v-else-if="userPaymentType === types.userPaymentTypes.annualHelperStandardSubscription30">
              * Your credit card will be debited $30 every 12 months until such time that you cancel your subscription. There are no partial year refunds.
            </small>
          </div>
        </div>
      </div>
    </div>
    <div v-else>
      <p>
        Helpers under the age of 18 are not required to pay a membership fee.
      </p>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex';
import CardDetails2 from './CardDetails2';
import PaymentSummary from './PaymentSummary';
import c from '../utils/constants';
import NotificationService from '../utils/notification-service';

export default {
  name: 'HelperMembership',
  components: { CardDetails2, PaymentSummary },
  props: ['enableSubscription'],
  emits: ['paymentFailure', 'paymentInfoUpdated', 'membershipSuccess'],
  data() {
    const hasExistingCard = this.$store.getters['payments/hasExistingCard'];
    const settings = {
      selectedUserPaymentType: false,
      paymentMethod: hasExistingCard ? c.paymentMethod.existingCard : c.paymentMethod.newCard,
      userPaymentType: c.userPaymentType.none,
      types: {
        paymentMethods: c.paymentMethod,
        userPaymentTypes: c.userPaymentType,
      },
    };

    return settings;
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
      userMeta: (state) => state.userInfo.meta,
      paymentInfo: (state) => state.userInfo.data.paymentInfo,
    }),
    ageInYears() {
      return this.userInfo.ageInYears;
    },
    storeModuleName() {
      return 'userInfo';
    },
    membershipText() {
      switch (this.userPaymentType) {
        case c.userPaymentType.monthlyHelperStandardSubscription5:
          return 'Helper Monthly Membership';
        case c.userPaymentType.quarterlyHelperStandardSubscription15:
          return 'Helper Quarterly Membership';
        case c.userPaymentType.annualHelperStandardSubscription30:
          return 'Helper Annual Membership';
        case c.userPaymentType.none:
        default:
          return 'Helper Memberships';
      }
    },
  },
  methods: {
    ...mapActions('payments', [
      'makePayment',
      'upgradeAccount',
      'updatePaymentInfo',
    ]),
    ...mapActions([
      'updateUserInfo',
      'refreshUserInfo',
      'submitTransactionInfo',
    ]),
    handlePaymentMethodChanged(value) {
      this.paymentMethod = value;
    },
    validate() {
      const { cardDetails } = this.$refs;
      if (this.paymentMethod === c.paymentMethod.newCard && !cardDetails.validate()) {
        NotificationService.negative('Please enter valid card details.');
        return false;
      }

      return true;
    },
    handleStart() {
      this.selectedUserPaymentType = this.userPaymentType !== c.userPaymentType.none;

      if (!this.selectedUserPaymentType) {
        NotificationService.warning('Please select membership option.');
      }
    },
    async handleUpdatePaymentInfo() {
      await this.processCreditCard();
    },
    async handleProcessMembership() {
      await this.processCreditCard();
      await this.processMembership();
    },
    processMembership() {
      if (!this.validate()) return;

      this.processPayment();
    },
    handlePaymentProcessingError(error) {
      NotificationService.negative(error.message || 'Failed to update your details.');
      this.$emit('paymentFailure', error);
      this.loader.hide();
    },
    async processCreditCard() {
      if (!this.validate()) return;

      const useNewCard = this.paymentMethod === c.paymentMethod.newCard;
      if (!useNewCard) return;

      const { cardDetails } = this.$refs;

      this.loader.show();

      const tokenId = await cardDetails.createToken();
      if (!tokenId) {
        NotificationService.negative('Invalid credit card.');
        this.loader.hide();
        return;
      }

      try {
        const setPaymentInfo = {
          actionType: c.membershipActionType.updatePaymentInfo,
          paymentType: this.userPaymentType,
          tokenId,
        };
        await this.updatePaymentInfo(setPaymentInfo);
      } catch (e) {
        this.handlePaymentProcessingError(e);
      }

      // Trigger an update event to review profile's completeness.
      this.setMetaProperty('dirty', true);
      await this.updateUserInfo();
      this.$emit('paymentInfoUpdated');
      cardDetails.setPaymentMethod(c.paymentMethod.existingCard);
      this.loader.hide();
    },
    async processPayment() {
      // Credit card have been already validated.
      if (this.userInfo.accountStatus === c.accountStatus.approved) {
        this.loader.show();
        const payload = {
          deviceType: this.deviceType,
          actionType: c.membershipActionType.activateMembership,
          paymentType: this.userPaymentType,
          tokenId: null,
        };

        this.makePayment(payload)
          .then(() => {
            this.submitTransactionInfo({ paymentType: payload.paymentType });
            this.refreshUserInfo()
              .then(() => {
                this.$emit('membershipSuccess');
                this.loader.hide();
                if (this.isApproved) {
                  NotificationService.positive('Your membership has been updated successfully.');
                  this.routerReplace(this.$route.query.redirect ? this.$route.query.redirect : '/dashboard');
                }
              })
              .catch(this.handlePaymentProcessingError);
          })
          .catch((error) => {
            this.loader.hide();
            NotificationService.negative(error.message || 'Failed to update your payment information.');
          });
      } else if (this.userInfo.accountStatus === c.accountStatus.pending) {
        NotificationService.info('Your membership will be processed once your profile is 100% complete.');
      }
    },
  },
};
</script>
<style lang="sass">
.helper-membership
  .pricing-section
    display: flex
    justify-content: center
    align-items: center
</style>
