              </div>
              <div class="pricing-plan-description pricing-sep">
                  <span class="price-point">$1,500</span>
                  <div v-if="includeActions(userPaymentTypes.annualBusinessTier2Subscription1500)">
                    <q-radio v-model="paymentType" :val="userPaymentTypes.annualBusinessTier2Subscription1500">
                      <span class="q-option-label text-bold">
