  </q-page>
</template>

<script>
import {
  mapGetters,
  mapState,
} from 'vuex';
import c from '../../utils/constants';
import {
  formatCurrency,
  getPaymentTypeText,
  getAmountForPaymentType,
  getUpsellOptions,
  getPaymentFrequencyForPaymentType,
  getAnnualPaymentFrequencyForPaymentType,
  getPricingOptions,
} from '../../utils/helpers';
import NotificationService from '../../utils/notification-service';

export default {
  name: 'PageAccountPricing',
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
      paymentInfo: (state) => state.userInfo.data.paymentInfo,
      membershipType: (state) => state.userInfo.data.membershipType,
    }),
    ...mapGetters(
      'payments',
      ['userPaymentTypes', 'membershipTypes'],
    ),
    businessPaymentTypes() {
      if (this.userInfo.membershipType === c.membershipType.subscription) {
        return getPricingOptions(this.userInfo, c.jobType.babysitting);
      }

      return getUpsellOptions(this.userInfo, c.jobType.babysitting);
    },
    hasMultipleAddresses() {
      return this.isIndividual || this.userInfo.client.clientCategory === c.clientCategory.businessMultiSite;
    },
  },
  data() {
    return {
      paymentType: c.userPaymentType.none,
      jobType: c.jobType.unspecified,
      jobTypes: c.jobType,
      includePaygActions: true,
    };
  },
  methods: {
    getSpecialOfferClass(option) {
      switch (option) {
        case c.userPaymentType.annualBusinessTier1Subscription1500:
        case c.userPaymentType.annualBusinessTier2Subscription1500:
          return 'text-brand-gold';
        default:
          return '';
      }
    },
    displaySpecialOffer(option) {
      switch (option) {
        case c.userPaymentType.quarterlyBusinessTier1Subscription500:
        case c.userPaymentType.quarterlyBusinessTier2Subscription500:
        case c.userPaymentType.annualBusinessTier1Subscription1500:
        case c.userPaymentType.annualBusinessTier2Subscription1500:
          return true;
        default:
          return false;
      }
    },
    getSpecialOffer(option) {
      switch (option) {
        case c.userPaymentType.quarterlyBusinessTier1Subscription500:
        case c.userPaymentType.quarterlyBusinessTier2Subscription500:
          return `Annual Cost ${this.getAnnualAmountForPaymentType(option)}`;
        default:
          return 'SAVE 25% per annum';
      }
    },
    getPaymentTypeText(paymentType) {
      return getPaymentTypeText(paymentType);
    },
    getAmountForPaymentType(paymentType) {
      const amount = getAmountForPaymentType(paymentType);
      const cost = Math.abs(amount).toFixed(0);
      return `$${cost}`;
    },
    getAnnualAmountForPaymentType(paymentType) {
      const amount = getAmountForPaymentType(paymentType);
      const frequency = getAnnualPaymentFrequencyForPaymentType(paymentType);
      const cost = Math.abs(amount * frequency).toLocaleString();
      return `$${cost}`;
    },
    getPaymentFrequencyForPaymentType(option) {
      return getPaymentFrequencyForPaymentType(option);
    },
    handlePostJob() {
      if (this.jobType === c.jobType.unspecified) {
        NotificationService.info('Please select job type and try again.');
        return;
      }

      this.routerReplace(`/post-job?reset=1&jobType=${this.jobType}`);
    },
    includeActions() {
      // return this.paymentInfo.paymentType !== newPaymentType;
      return this.userInfo.membershipType !== c.membershipType.subscription;
    },
    pendingAccountNotice() {
      this.$q.dialog({
        title: 'Complete Profile',
        message: 'Your profile must be 100% complete before proceeding.',
        cancel: {
          label: 'Later',
          color: 'secondary',
          outline: true,
        },
        ok: {
          label: 'Complete Profile',
          color: 'primary',
          outline: true,
        },
      }).onOk(() => {
        if (this.isFamily) {
          this.routerReplace('/client-profile-wizard');
        } else {
          this.routerReplace('/helper-profile-wizard');
        }
      }).onCancel(() => {});
    },
    handleUpgradePayment() {
      if (this.isPending) {
        this.pendingAccountNotice();
        return;
      }

      if (this.paymentType === c.userPaymentType.none) {
        NotificationService.info('Please select subscription type and try again.');
        return;
      }

      this.routerReplace(`/account/modal/upgrade-client-account/${this.paymentType}`);
    },
    formatCurrency(amount) {
      return formatCurrency(amount);
    },
  },
};
</script>
