import * as Sentry from '@sentry/vue';
import { BrowserTracing } from '@sentry/tracing';
import * as SentryBrowser from '@sentry/browser';
import { env } from './env';

const ignoreList = [
  'ResizeObserver loop limit exceeded', // Quasar.
  'FirebaseAnalytics',
  '"FirebaseAnalytics" plugin is not implemented on ios',
  '"FirebaseAnalytics" plugin is not implemented on android',
  'userId property is missing',
  'Request timed out. The device maybe offline.',
  'Unable to connect, please make sure your phone has internet access then try again.',
  'Non-Error promise rejection captured with value: Request timed out. The device maybe offline.',
  'Non-Error promise rejection captured with value: The network connection was lost.',
  'Non-Error promise rejection captured with value: The request timed out.',
  'The device is offline.',
  'The network connection was lost.',
  'Non-Error promise rejection captured with value: Software caused connection abort',
  'The Internet connection appears to be offline.',
  'Load failed',
  'Failed to fetch',
  'The request timed out.',
  'Failed to connect to pro-deploy.ionicjs.com',
  'Software caused connection abort',

  // Random plugins/extensions
  'top.GLOBALS',
  // See: http://blog.errorception.com/2012/03/tale-of-unfindable-js-error.html
  'originalCreateNotification',
  'canvas.contentDocument',
  'MyApp_RemoveAllHighlights',
  'ComboSearch is not defined',
  'atomicFindClose',
  // Facebook borked
  'fb_xd_fragment',
  // ISP 'optimizing' proxy - `Cache-Control: no-transform` seems to
  // reduce this. (thanks @acdha)
  // See http://stackoverflow.com/questions/4113268
  'bmi_SafeAddOnload',
  'EBCallBackMessageReceived',
  // See http://toolbar.conduit.com/Developer/HtmlAndGadget/Methods/JSInjection.aspx
  'conduitPage',
];

const denyUrls = [
  // Facebook flakiness
  /graph\.facebook\.com/i,
  /pro-deploy\.ionicjs\.com/i,
  /api\.ionicjs\.com/i,
  // Facebook blocked
  /connect\.facebook\.net\/en_US\/all\.js/i,
  // Chrome extensions
  /extensions\//i,
  /^chrome:\/\//i,
  // Other plugins
  /127\.0\.0\.1:4001\/isrunning/i, // Cacaoweb
  /webappstoolbarba\.texthelp\.com\//i,
  /metrics\.itunes\.apple\.com\.edgesuite\.net\//i,
];

let app;
let store;
let router;
let customData = {};

const errorHandler = {
  ignoreList,
  denyUrls,
  init: (a, r, s) => {
    app = a;
    router = r;
    store = s;

    Sentry.init({
      app,
      release: `js-mobile-frontend@${env.appInfo.version}.${env.deviceInfo.platform === 'web' ? 'web' : 'app'}`,
      environment: env.deployment,
      dsn: 'https://c67dc13e632645168c643efebb90eec4@o458080.ingest.sentry.io/5455052',
      logErrors: env.isTest,
      ignoreErrors: errorHandler.ignoreList,
      denyUrls: errorHandler.denyUrls,
      beforeSend: errorHandler.beforeSend,
      beforeBreadcrumb: errorHandler.beforeBreadcrumb,
      integrations: [
        new BrowserTracing({
          routingInstrumentation: Sentry.vueRouterInstrumentation(router),
          tracingOrigins: ['localhost', 'sapi2.jugglestreet.com', 'api2.jugglestreet.com', /^\//],
        }),
      ],
      tracesSampleRate: env.isTest ? 1.0 : 0.2,
    });

    customData = window.appInfo || {};
    customData.standalone = window.navigator.standalone;

    SentryBrowser.setContext('deviceInfo', env.deviceInfo);
    // SentryBrowser.setContext('appInfo', env.appInfo);
  },
  // beforeSend: (event) => event,
  beforeSend: (event, hint) => {
    if (env.isTest) {
      console.log(hint);
    }

    if (event.level === 'error' && env.deviceInfo.platform !== 'web') {
      store.dispatch('checkForCodeUpdates');
    }

    return event;
  },
  beforeBreadcrumb: (breadcrumb, hint) => {
    if (breadcrumb.category === 'xhr') {
      if (hint && hint.xhr && hint.xhr.status >= 400) {
        if (breadcrumb.data && hint.xhr.response) {
          breadcrumb.data.response = hint.xhr.response;
        }
      }
    }

    return breadcrumb;
  },
  captureException: (err, data, mustCapture) => {
    if (!err) return;

    if (!mustCapture && err.url) {
      if (err.status >= 400 && err.status < 500) return;

      if (err.status === 503) return;
    }

    let errorData;

    if (data) {
      errorData = Object.assign(data, customData);
    } else {
      errorData = customData;
    }

    const context = {
      extra: errorData,
    };
    SentryBrowser.captureException(err, context);
  },
  setUser: (user) => {
    const accountDetails = `${user.isClient ? 'Client' : 'Helper'} - ${user.id} - ${user.firstName} ${user.lastInitial}`;
    SentryBrowser.setUser({
      id: user.id,
      // email: 'info@jugglestreet.com',
      username: accountDetails,
      accountType: user.isClient ? 'Client' : 'Helper',
      publicName: `${user.firstName} ${user.lastInitial}`,
    });
  },
};

export default errorHandler;
