<template>
  <div class="">
    <profile-tabs :helper="helper" :selectedTab="selectedTab" v-on:profileTabChange="handleProfileTabChange" />
    <profile-overview :displayRatings="true" :displayReviews="true" :displayJobStats="true" :displayDistance="true" :displayVideo="true" :displayProfileImage="true"
      :helper="helper" :mode="mode" :isGuardianApprovalMode="isGuardianApprovalMode" v-on:scrollToReviews="scrollToProfileReviews"/>

    <q-card-section align="center" style="padding: 8px">
      <span :style="displayAllAboutMe ? 'white-space: pre-wrap;': ''">"{{aboutMe}}"</span>
        <span class="q-ml-sm" v-if="helper.aboutMe && helper.aboutMe.length > 300" style="text-decoration: underline"
            @click="displayAllAboutMe = !displayAllAboutMe">{{displayAllAboutMe ? 'less' : 'more'}}</span>
    </q-card-section>

    <q-card-section style="padding: 8px" class="">
      <q-field label="Job Types" borderless readonly>
        <template v-slot:prepend>
          <q-icon name="work_outline" />
        </template>
        <template v-slot:control>
          <div class="full-width q-mt-md q-mx-sm row justify-between" style="align-items: center">
            <div class="">
              <q-checkbox v-model="interestedInChildcareJobs" readonly label="Childcare" unchecked-icon="check_box_outline_blank" />
              <br />
              <q-checkbox v-model="interestedInAuPairJobs" readonly label="Au Pair" unchecked-icon="check_box_outline_blank" />
              <br />
              <q-checkbox v-model="interestedInTutoringJobs" readonly label="Tutor" unchecked-icon="check_box_outline_blank" />
              <br />
            </div>
          </div>
        </template>
      </q-field>

      <q-field label="Special Needs Experience" borderless readonly v-if="helper.providerMyExperience2">
        <template v-slot:prepend>
          <q-icon name="accessibility_new" />
        </template>
        <template v-slot:control>
          <div class="full-width q-mt-md">
          {{providerMyExperience2}}
            <span v-if="helper.providerMyExperience2 && helper.providerMyExperience2.length > 300" style="text-decoration: underline"
                @click="displayAllMyExperience2 = !displayAllMyExperience2">{{displayAllMyExperience2 ? 'less' : 'more'}}</span>
          </div>
        </template>
      </q-field>

      <profile-checks :helper="helper" />

      <q-field label="Country of Citizenship" borderless readonly v-if="helper.nationality">
        <template v-slot:prepend>
          <q-icon name="flag" />
        </template>
        <template v-slot:control>
          <div class="full-width q-mt-md">
            {{citizenship}}
          </div>
        </template>
      </q-field>

      <profile-vevo-check :helper="helper" />

      <q-field label="First Aid & Accreditations" borderless readonly v-if="qualificationsList || firstAidList">
        <template v-slot:prepend>
          <q-icon name="local_hospital" />
        </template>
        <template v-slot:control>
          <div class="full-width q-mt-md">
            <div v-html="qualificationsList" />
            <div v-html="firstAidList" />
          </div>
        </template>
      </q-field>
      <q-field label="Transportation" borderless readonly v-if="transportList">
        <template v-slot:prepend>
          <q-icon name="directions_car" />
        </template>
        <template v-slot:control>
          <div class="full-width q-mt-md">
            {{transportList}}
          </div>
        </template>
      </q-field>
      <q-field label="Additional Languages" borderless readonly v-if="spokenLanguages">
        <template v-slot:prepend>
          <q-icon name="publci" />
        </template>
        <template v-slot:control>
          <div class="full-width q-mt-md">
            {{spokenLanguages}}
          </div>
        </template>
      </q-field>

      <helper-reviews :helper="helper" ref="profileReviews"/>
    </q-card-section>
  </div>
</template>

<script>
import ProfileTabs from 'components/helper-profile/ProfileTabs';
import ProfileOverview from 'components/helper-profile/ProfileOverview';
import ProfileChecks from 'components/helper-profile/ProfileChecks';
import ProfileVevoCheck from 'components/helper-profile/ProfileVevoCheck';
import HelperReviews from 'components/helper-profile/HelperReviews';
import { scrollToElement } from '../../utils/helpers';
import c from '../../utils/constants';
import datasets from '../../utils/datasets';

export default {
  name: 'ProfileTab',
  components: {
    ProfileTabs, HelperReviews, ProfileOverview, ProfileChecks, ProfileVevoCheck,
  },
  props: ['helper', 'mode', 'selectedTab', 'isGuardianApprovalMode'],
  emits: ['profileTabChange'],
  data() {
    return {
      displayAllAboutMe: false,
      displayAllMyExperience2: false,
    };
  },
  computed: {
    aboutMe() {
      if (!this.helper.aboutMe) return '';

      if (this.helper.aboutMe.length < 300 || this.displayAllAboutMe) {
        return this.helper.aboutMe;
      }

      const idx = this.helper.aboutMe.indexOf(' ', 300);
      if (idx === -1) return this.helper.aboutMe;

      return `${this.helper.aboutMe.substring(0, idx)}...`;
    },
    providerMyExperience2() {
      if (!this.helper.providerMyExperience2) return '';

      if (this.helper.providerMyExperience2.length < 300 || this.displayAllMyExperience2) {
        return this.helper.providerMyExperience2;
      }

      const idx = this.helper.providerMyExperience2.indexOf(' ', 300);
      if (idx === -1) return this.helper.providerMyExperience2;

      return `${this.helper.providerMyExperience2.substring(0, idx)}...`;
    },
    interestedInChildcareJobs() {
      const { interestedInJobTypes } = this.helper;

      return (interestedInJobTypes & c.jobType.babysitting
      || interestedInJobTypes & c.jobType.nannying
      || interestedInJobTypes & c.jobType.beforeSchoolCare
      || interestedInJobTypes & c.jobType.afterSchoolCare) > 0;
    },
    interestedInAuPairJobs() {
      const { interestedInJobTypes } = this.helper;

      return (interestedInJobTypes & c.jobType.auPair) > 0;
    },
    interestedInTutoringJobs() {
      const { interestedInJobTypes } = this.helper;

      return (interestedInJobTypes & c.jobType.primarySchoolTutoring
      || interestedInJobTypes & c.jobType.highSchoolTutoring) > 0;
    },
    spokenLanguages() {
      return this.getList('spokenLanguages', ', ');
    },
    qualificationsList() {
      return this.getList('qualifications', '<br />');
    },
    firstAidList() {
      return this.getList('firstAid', '<br />');
    },
    transportList() {
      return this.getList('transport', ', ');
    },
    countriesList() {
      return datasets.countriesIso;
    },
    citizenship() {
      let { nationality } = this.helper;

      if (!nationality) return null;

      nationality = nationality.toLowerCase();

      const match = this.countriesList.find((country) => country.value === nationality || country.alpha2 === nationality);

      if (!match) return null;

      return match.label;
    },
  },
  methods: {
    scrollToProfileReviews() {
      scrollToElement(this.$refs.profileReviews.$el, -50);
    },
    handleProfileTabChange(args) {
      this.$emit('profileTabChange', args);
    },
    getList(groupName, sep) {
      if (!this.helper) {
        return '';
      }

      const selectedList = [];
      const list = this.helper[groupName];
      for (let i = 0; i < list.length; i++) {
        if (list[i].selected) {
          selectedList.push(list[i].text);
        }
      }

      if (selectedList.length > 0) {
        return selectedList.join(sep);
      }

      return '';
    },
  },
};
</script>
