<template>
  <div>
    <q-field label="Checks" v-if="isCanberra" borderless readonly>
        <template v-slot:prepend>
          <q-icon name="check_circle_outline" />
        </template>
        <template v-slot:control>
          <div class="full-width q-mt-md">
            <strong>Working With Vulnerable People</strong>
            <br />
            Parents are responsible for verifying helper's WWVP registration.
            <br />
            Contact Access Canberra to verify.
          </div>
        </template>
    </q-field>
    <q-field label="Checks" v-else-if="hasCheck" borderless readonly>
      <template v-slot:prepend>
        <q-icon name="check_circle_outline" />
      </template>
      <template v-slot:control>
        <div class="full-width q-mt-md">
          <div>
            <span>Working with Children Check ({{type}})</span>
          </div>
          <div>
            <span style="font-weight: bold" class="text-primary"><q-icon name="verified" color="primary" size="18px" style="margin-right: 5px"  />Verified</span><br/>
            <span>Verified on: {{verifiedOn}}</span><br/>
            <span>Expires on: {{expiresOn}}</span>
          </div>
        </div>
      </template>
    </q-field>
  </div>
</template>

<script>
import { date } from 'quasar';
import c from '../../utils/constants';

export default {
  name: 'ProfileChecks',
  props: ['helper'],
  data() {
    return {

    };
  },
  computed: {
    isCanberra() {
      return this.country === 'au' && this.helper.state === 'ACT';
    },
    hasCheck() {
      const { certificates } = this.helper;

      return (certificates && certificates.length > 0 && certificates[0].certificateStatus === c.certificateStatus.active);
    },
    certificate() {
      if (!this.hasCheck) return null;

      const { certificates } = this.helper;
      return certificates[0];
    },
    type() {
      const cert = this.certificate;
      if (cert === null) return '';

      return cert.certificateNumericData1 === 1 ? 'Volunteer' : 'Paid';
    },
    verifiedOn() {
      const cert = this.certificate;
      if (cert === null) return '';

      return date.formatDate(new Date(cert.verificationDate), 'DD/MM/YYYY');
    },
    expiresOn() {
      const cert = this.certificate;
      if (cert === null) return '';

      return date.formatDate(new Date(cert.expiryDate), 'DD/MM/YYYY');
    },
  },
  methods: {
  },
};
</script>
