<template>
  <q-card
    class="family-job-summary">
    <q-card-section @click="navigateToJobDetails">
      <div class="float-right text-grey text-bold">
        {{managedByText}}
      </div>
      <div>
        <span class="text-bold text-grey">{{jobTypeText}}</span>
        <br/>
        <span v-if="addressLabel" class="text-primary text-bold">{{addressLabel}}<br/></span>
        <span>{{jobDate}}</span>
      </div>
      <div v-if="job.jobStatus === types.jobStatus.pending">
        <div v-if="job.managedBy === types.managedBy.user">
          Invited: <span class="pending">{{job.applicantsTotal}}</span><br/>
          <div v-if="job.jobType > 0">
            <div style="display: inline-block; width: 30%;">Applied: <span class="text-green">{{job.applicantsApplied}}</span></div>
            <div style="display: inline-block; width: 30%;">Declines: <span class="text-red">{{job.applicantsNotInterested + job.applicantsNotAvailable }}</span></div>
            <div style="display: inline-block; width: 30%;">Viewed: <span class="">{{job.applicantsViewed}}</span></div>
          </div>
          <div v-else>
            Viewed: <span class="pending">{{job.applicantsViewed}}</span><br/>
          </div>
        </div>

        <div v-if="job.managedBy === types.managedBy.system">
          <!-- <div>Status: <span class="">{{managementStatus}}</span></div> -->
          <div>Applied: <span class="text-green">{{job.applicantsApplied}}</span></div>
        </div>

        <div v-if="job.expiresInDays === 1">
            <div>
              Job expires <span class="text-red">tomorrow</span>
              <div class="float-right text-grey">
                <q-icon :name="helperPaymentMethodIcon" size="18px" /> {{helperPaymentMethodText}}
              </div>
            </div>
        </div>
        <div v-else>
            <div>
              Job expires in <span class="text-red"> {{job.expiresInDays}} </span> days
              <div class="float-right text-grey">
                <q-icon :name="helperPaymentMethodIcon" size="18px" /> {{helperPaymentMethodText}}
              </div>
            </div>
        </div>
      </div>
      <div v-if="(job.jobType === types.jobType.babysitting || job.jobType === types.jobType.auPair) && job.jobStatus === types.jobStatus.approved">
        <img class="round-user-image" :src="job.awardedImageSrc"/> {{job.awardedFirstName}} {{job.awardedLastInitial}}
        <br/>
        Getting Home: <span> {{gettingHomeText}}</span>
      </div>
      <div v-if="(job.jobType === types.jobType.babysitting || job.jobType === types.jobType.auPair) && (job.jobStatus === types.jobStatus.awarded || job.jobStatus === types.jobStatus.completed)">
        <div v-if="job.awardedApplicantId !== null && job.awardedIsHidden !== null && !job.awardedIsHidden">
          <img class="round-user-image" :src="job.awardedImageSrc"/> {{job.awardedFirstName}} {{job.awardedLastInitial}}
        </div>
        <div v-else>
          Helper is not accessible
        </div>
      </div>
      <div v-if="job.jobType === types.jobType.filled">
          Invited: <span class="pending">{{job.applicantsTotal}}</span><br/>
          Viewed: <span class="pending">{{job.applicantsViewed}}</span><br/>
      </div>
      <div v-if="job.isRatedByClient">
        You Rated:
        <q-rating
          v-model="jobRating"
          :max="5"
          color="accent"
          readonly />
      </div>
    </q-card-section>
    <!-- <q-card-actions>
      <q-btn class="q-my-sm" v-if="job.jobStatus == types.jobStatus.completed && (job.jobType == types.jobType.babysitting || job.jobType === types.jobType.auPair) && !job.isRatedByClient" color="primary" @click="handleRateHelper">Rate Helper</q-btn>
    </q-card-actions> -->
  </q-card>
</template>

<script>
import { env } from '../utils/env';
import c from '../utils/constants';
import {
  getJobPaymentMethodIcon, getJobPaymentMethodText, gettGettingHomeTextForFamilies, formatJobDate, jobTypeText, getJobManagementStatus, getManagedByText,
} from '../utils/helpers';

export default {
  name: 'ClientJobSummary',
  data() {
    return {
      types: {
        jobType: c.jobType,
        jobStatus: c.jobStatus,
        managedBy: c.managedBy,
      },
    };
  },
  props: ['job'],
  methods: {
    navigateToJobDetails() {
      this.routerPush(`/families/job-details/${this.job.id}`);
    },
    getImageUrl() {
      return `${env.storageUrl}/images/${this.job.imageSrc}`;
    },
    handleRateHelper() {
      this.routerPush(`/families/rate-helper/${this.job.id}?ratingFor=${this.job.awardedApplicantId}`);
    },
  },
  computed: {
    addressLabel() {
      if (!this.job.addressLabel) return '';

      return this.job.addressLabel;
    },
    managementStatus() {
      return getJobManagementStatus(this.job.managementStatus);
    },
    managedByText() {
      return getManagedByText(this.job.managedBy);
    },
    jobTypeText() {
      return jobTypeText(this.job.jobType, this.clientType);
    },
    jobRating() {
      return Math.round(this.job.ratingProvided);
    },
    gettingHomeText() {
      return gettGettingHomeTextForFamilies(this.job.awardedApplicantGettingHome);
    },
    jobDate() {
      return formatJobDate(this.job);
    },
    helperPaymentMethodIcon() {
      return getJobPaymentMethodIcon(this.job.helperPaymentMethod);
    },
    helperPaymentMethodText() {
      return getJobPaymentMethodText(this.job.helperPaymentMethod);
    },
  },
};
</script>

<style lang="sass">
.family-job-summary
  margin: 0.5rem 0
  border: 1px solid white
  line-height: 1.3rem
  .q-card-actions
    padding: 0 8px
</style>
