<template>
  <q-page class="layout-padding">
    <p style="margin-top: 10px">
      <span class="text-subtitle2">Business Details</span>
    </p>
    <q-field borderless stack-label readonly hide-bottom-space
      label="Business Trading Name *">
        <template v-slot:control>
          <div class="self-center full-width no-outline" tabindex="0">{{userInfo.tradingName}}</div>
        </template>
    </q-field>
    <q-field borderless stack-label readonly hide-bottom-space
      label="Registered Business Name *">
        <template v-slot:control>
          <div class="self-center full-width no-outline" tabindex="0">{{userInfo.businessName}}</div>
        </template>
    </q-field>
    <q-field v-if="isAddressReadonly" borderless stack-label readonly hide-bottom-space
      label="Business Addresss *">
        <template v-slot:control>
          <div class="self-center full-width no-outline" tabindex="0">{{userInfo.defaultAddress.formattedAddress}}</div>
        </template>
    </q-field>
    <q-field borderless stack-label hide-bottom-space
      :error="v$.establishYear.$error"
      :label="'Your Company Established' + (this.establishYear ? ' *' : '')">
        <template v-slot:control>
          <div v-if="establishYear" class="self-center full-width no-outline" tabindex="0">{{establishYear}}</div>
          <div v-else class="self-center no-outline">
            <q-select
              class="q-my-xs"
              v-model="establishYear"
              :options="yearsList"
              hide-bottom-space
              option-value="value"
              option-label="label"
              emit-value
              map-options
              use-input
              hide-selected
              fill-input
              behavior="menu"
            />
          </div>
        </template>
    </q-field>
    <q-field borderless stack-label :error="v$.industrySectorId.$error" hide-bottom-space
      label="Industry Sector">
        <template v-slot:control>
          <div class="self-center no-outline" tabindex="0">
            <q-select
              class="q-my-xs"
              v-model="industrySectorId"
              :options="industrySectorList"
              hide-bottom-space
              option-value="value"
              option-label="label"
              emit-value
              map-options
              use-input
              hide-selected
              fill-input
            />
            <q-input v-if="industrySectorId === 1001" v-model="industrySectorText" placeholder="Specify Industry" :error="v$.industrySectorText.$error" hide-bottom-space />
          </div>
        </template>
    </q-field>
    <q-field borderless stack-label :error="v$.numberOfMembers.$error" hide-bottom-space
      label="Number of Employees">
        <template v-slot:control>
          <div class="self-center no-outline" tabindex="0">
            <q-select
              class="q-my-xs"
              v-model="numberOfMembers"
              :options="numberOfMembersList"
              hide-bottom-space
              option-value="value"
              option-label="label"
              emit-value
              map-options
              use-input
              hide-selected
              fill-input
            />
          </div>
        </template>
    </q-field>
    <small>* This field is not editable. Contact Juggle Street to update.</small>
    <p style="margin-top: 10px">
      <span class="text-subtitle2">Business Introduction</span>
    </p>
    <q-field
      borderless
      bottom-slots
      :error="v$.aboutMe.$error">
      <template v-slot:control>
        <div>
          Please tell us a bit about your business…
        </div>
        <q-input
          class="full-width"
          placeholder="Example - ABC Pilates is a family owned and run business providing individual and group classes for women of all ages since 2014."
          v-model="aboutMe" type="textarea" :max-height="100" rows="3" />
      </template>
      <template v-slot:hint>
        {{aboutMeHelper}}
      </template>
      <template v-slot:error>
        {{aboutMeHelper}}
      </template>
    </q-field>
    <p style="margin-top: 10px; margin-bottom: 0">
      <span class="text-subtitle2">Business Logo</span>
    </p>
    <q-field style="margin-top: 0" label=""
      stack-label borderless>
      <template v-slot:control>
        <div class="self-center full-width no-outline" tabindex="0">
          <img :src="userInfo.defaultImage.scale1ImageUrl" style="height: 150px"/>
          <br/>
          <a @click="handleSelectPhoto" class="q-ml-md button-link text-primary">Select Image</a>
          <image-uploader
            ref="image-uploader"
            @uploadSuccess="handleUploadSuccess"
            @uploadFailure="handleUploadFailure"
          />
          <a @click="handleCropPhoto" class="q-ml-md button-link text-primary">Crop</a>
          <small class="text-faded" v-if="!hasImage"><br/>required</small>
        </div>
      </template>
    </q-field>
  </q-page>
</template>

<script>
import { mapState, mapActions } from 'vuex';
import useVuelidate from '@vuelidate/core';
import { required, requiredIf } from '@vuelidate/validators';
import ImageUploader from 'components/ImageUploader';
import NotificationService from '../../utils/notification-service';
import c from '../../utils/constants';

export default {
  name: 'PageAccountMyBusiness',
  components: { ImageUploader },
  data() {
    return {

    };
  },
  methods: {
    handleSelectPhoto() {
      this.$refs['image-uploader'].open();
    },
    handleCropPhoto() {
      this.routerPush('/account/modal/crop-image');
    },
    handleUploadSuccess() {
      this.routerPush('/account/modal/crop-image');
    },
    handleUploadFailure() {
      NotificationService.negative('An error occurred. Please try again later.');
    },
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
    }),
    ...mapActions([
      'updateUserInfo',
    ]),
    storeModuleName() {
      return 'userInfo';
    },
    isAddressReadonly() {
      return this.userInfo.client.clientCategory === c.clientCategory.businessSingleSite;
    },
    yearsList() {
      return c.yearsList;
    },
    numberOfMembersList() {
      return c.numberOfMembersList;
    },
    industrySectorList() {
      return c.industrySectorsList;
    },
    hasImage() {
      return this.userInfo.defaultImage && this.userInfo.defaultImage.scale1ImageUrl;
    },
    aboutMe: {
      get() {
        return this.getProperty('aboutMe');
      },
      set(value) {
        this.setProperty('aboutMe', value);
      },
    },
    aboutMeHelper() {
      if (this.v$.aboutMe.$error && this.aboutMe) {
        return `required - ${100 - this.aboutMe.length} characters left`;
      }

      return 'required - min 100 characters';
    },
    jobRules: {
      get() {
        return this.getProperty('client.jobRules');
      },
      set(value) {
        this.setProperty('client.jobRules', value);
      },
    },
    jobDescription: {
      get() {
        return this.userInfo.client.jobDescription;
      },
      set(value) {
        this.setProperty('client.jobDescription', value);
      },
    },
    numberOfMembers: {
      get() {
        return this.userInfo.numberOfMembers;
      },
      set(value) {
        this.setProperty('numberOfMembers', value);
      },
    },
    establishYear: {
      get() {
        return this.userInfo.establishYear;
      },
      set(value) {
        this.setProperty('establishYear', value);
      },
    },
    industrySectorId: {
      get() {
        return this.userInfo.industrySectorId;
      },
      set(value) {
        this.setProperty('industrySectorId', value);
      },
    },
    industrySectorText: {
      get() {
        return this.userInfo.industrySectorText;
      },
      set(value) {
        this.setProperty('industrySectorText', value);
      },
    },
  },
  setup() {
    return {
      v$: useVuelidate(),
    };
  },
  validations() {
    return {
      aboutMe: {
        required(value) {
          return value !== null && value.length >= 100;
        },
      },
      establishYear: {
        requiredIf: requiredIf(!this.establishYear),
      },
      numberOfMembers: {
        required,
      },
      industrySectorId: {
        required,
      },
      industrySectorText: {
        requiredIf: requiredIf(this.industrySectorId === 1001),
      },
    };
  },
  beforeRouteLeave(to, from, next) {
    this.v$.$touch();
    if (this.v$.$error) {
      NotificationService.negative('Please fix all highlighted errors.');
      next(false);
      return;
    }

    this.$store.dispatch('updateUserInfo').then(() => {
      next();
    }).catch((error) => {
      NotificationService.negative(error.message);
      next(false);
    });
  },
};
</script>
