import * as http from '../../api/http';
import * as types from '../mutation-types';
import NotificationService from '../../utils/notification-service';
import loader from '../../utils/loader';
import c from '../../utils/constants';
import appRatingManager from '../../utils/app-rating-manager';
import { env } from '../../utils/env';

export default {
  namespaced: true,
  state: {
    jobs: {
      data: [],
      meta: {
        isFetching: false,
        lastFetched: null,
        dirty: false,
      },
    },
    upcomingJobs: {
      data: [],
      meta: {
        isFetching: false,
        lastFetched: null,
        dirty: false,
      },
    },
    archivedJobs: {
      data: [],
      meta: {
        isFetching: false,
        lastFetched: null,
        dirty: false,
      },
    },
    unratedJobs: {
      data: [],
      meta: {
        isFetching: false,
        lastFetched: null,
        dirty: false,
      },
    },
    pages: {
      rateFamily: {},
      rateFamilyTemplate: {
        data: {
          jobId: undefined,
          ratingFor: undefined,
          ratingBy: undefined,
          category1: 0,
          category2: 0,
          category3: 0,
          category4: 0,
        },
        meta: {
          job: {},
          isFetching: false,
          lastFetched: null,
          dirty: false,
        },
      },
      jobDetails: {},
      jobDetailsTemplate: {
        data: {
        },
        meta: {
          isFetching: false,
          lastFetched: null,
          dirty: false,
          failed: false,
        },
      },
    },
  },
  actions: {
    fetchJobs: ({ commit, rootState }) => {
      loader.show();
      commit(types.JOBS_REQUEST);
      const url = `jobs/provider${rootState.appInfo.adminMode ? '?admin_mode=1' : ''}`;
      return http
        .get(url)
        .then((response) => {
          commit(types.JOBS_SUCCESS, response.data);
          loader.hide();
          return response;
        })
        .catch((error) => {
          commit(types.JOBS_FAILURE);
          loader.hide();
          return Promise.reject(error);
        });
    },
    fetchUpcomingJobs: ({ commit }) => {
      loader.show();
      commit(types.UPCOMING_JOBS_REQUEST);
      return http
        .get('jobs/provider/upcoming')
        .then((response) => {
          commit(types.UPCOMING_JOBS_SUCCESS, response.data);
          loader.hide();
          return response;
        })
        .catch((error) => {
          commit(types.UPCOMING_JOBS_FAILURE);
          loader.hide();
          return Promise.reject(error);
        });
    },
    fetchArchivedJobs: ({ commit }) => {
      loader.show();
      commit(types.ARCHIVED_JOBS_REQUEST);
      return http
        .get('jobs/provider/completed')
        .then((response) => {
          commit(types.ARCHIVED_JOBS_SUCCESS, response.data);
          loader.hide();
          return response;
        })
        .catch((error) => {
          commit(types.ARCHIVED_JOBS_FAILURE);
          loader.hide();
          return Promise.reject(error);
        });
    },
    fetchUnratedJobs: ({ commit }) => {
      loader.show();
      commit(types.UNRATED_JOBS_REQUEST);
      const url = 'jobs/provider/unratedjobs';
      return http
        .get(url)
        .then((response) => {
          commit(types.UNRATED_JOBS_SUCCESS, response.data);
          loader.hide();
          return response;
        })
        .catch((error) => {
          commit(types.UNRATED_JOBS_FAILURE);
          loader.hide();
          return Promise.reject(error);
        });
    },
    fetchJobForRating: ({ commit, rootState }, payload) => {
      loader.show();
      commit(types.JOB_FOR_RATING_REQUEST);
      return http
        .get(`jobs/provider/${payload.id}`)
        .then((response) => {
          if (!response.data.awardedApplicantId) {
            response.data.awardedApplicantId = rootState.userInfo.data.id;
          }
          commit(types.JOB_FOR_RATING_SUCCESS, response.data);
          loader.hide();
          return response;
        })
        .catch((error) => {
          commit(types.JOB_FOR_RATING_FAILURE);
          loader.hide();
          return Promise.reject(error);
        });
    },
    postFamilyRating({ state, rootState }, router) {
      loader.show();

      const rating = state.pages.rateFamily.data;
      const ratingSum = rating.category1 + rating.category2 + rating.category3 + rating.category4;

      http
        .post(`jobs/client/${rating.jobId}/rating`, state.pages.rateFamily.data)
        .then(async () => {
          loader.hide();
          if (env.deviceInfo.platform !== 'web' && ratingSum === 20) {
            appRatingManager.promptForRating();
          }

          if (rootState.realtime.alerts.unratedJobs > 1) {
            router.replace('/helpers/unrated-jobs');
          } else {
            router.replace('/helpers/upcoming-jobs');
          }
        })
        .catch((error) => {
          loader.hide();
          NotificationService.negative(error.message);

          return Promise.reject(error);
        });
    },
    fetchJobDetails: ({ commit, rootState }, payload) => {
      loader.show();
      commit(types.JOB_DETAILS_REQUEST);
      const url = `jobs/provider/${payload.id}${rootState.appInfo.adminMode ? '?admin_mode=1' : ''}`;
      return http
        .get(url)
        .then((response) => {
          commit(types.JOB_DETAILS_SUCCESS, response.data);
          loader.hide();
          return response;
        })
        .catch((error) => {
          commit(types.JOB_DETAILS_FAILURE);
          loader.hide();

          return Promise.reject(error);
        });
    },
    updateJobDetails: ({ rootState, state, dispatch }) => {
      loader.show();
      const userInfo = rootState.userInfo.data;
      const payload = state.pages.jobDetails.data;
      return http
        .patch(`jobs/provider/${payload.id}`, payload)
        .then(async (response) => {
          if (userInfo.paymentInfo.renewalPaymentType !== c.userPaymentType.none) {
            await dispatch('refreshUserInfo', null, { root: true });
          }
          loader.hide();
          return response;
        })
        .catch((error) => {
          loader.hide();
          return Promise.reject(error);
        });
    },
  },
  mutations: {
    [types.JOBS_REQUEST]: (state) => {
      state.jobs.meta.isFetching = true;
    },
    [types.JOBS_SUCCESS]: (state, payload) => {
      state.jobs.data = payload;
      state.jobs.meta.isFetching = false;
      state.jobs.meta.lastFetched = new Date();
    },
    [types.JOBS_FAILURE]: (state) => {
      if (state.jobs) {
        state.jobs.data = undefined;
        state.jobs.meta.isFetching = false;
        state.jobs.meta.lastFetched = new Date();
      }
    },
    [types.UPCOMING_JOBS_REQUEST]: (state) => {
      state.upcomingJobs.meta.isFetching = true;
    },
    [types.UPCOMING_JOBS_SUCCESS]: (state, payload) => {
      state.upcomingJobs.data = payload;
      state.upcomingJobs.meta.isFetching = false;
      state.upcomingJobs.meta.lastFetched = new Date();
    },
    [types.UPCOMING_JOBS_FAILURE]: (state) => {
      if (state.upcomingJobs) {
        state.upcomingJobs.data = undefined;
        state.upcomingJobs.meta.isFetching = false;
        state.upcomingJobs.meta.lastFetched = new Date();
      }
    },
    [types.ARCHIVED_JOBS_REQUEST]: (state) => {
      state.archivedJobs.meta.isFetching = true;
    },
    [types.ARCHIVED_JOBS_SUCCESS]: (state, payload) => {
      state.archivedJobs.data = payload;
      state.archivedJobs.meta.isFetching = false;
      state.archivedJobs.meta.lastFetched = new Date();
    },
    [types.ARCHIVED_JOBS_FAILURE]: (state) => {
      if (state.archivedJobs) {
        state.archivedJobs.data = undefined;
        state.archivedJobs.meta.isFetching = false;
        state.archivedJobs.meta.lastFetched = new Date();
      }
    },
    [types.UNRATED_JOBS_REQUEST]: (state) => {
      state.unratedJobs.meta.isFetching = true;
    },
    [types.UNRATED_JOBS_SUCCESS]: (state, payload) => {
      state.unratedJobs.data = payload;
      state.unratedJobs.meta.isFetching = false;
      state.unratedJobs.meta.lastFetched = new Date();
    },
    [types.UNRATED_JOBS_FAILURE]: (state) => {
      if (state.unratedJobs) {
        state.unratedJobs.data = undefined;
        state.unratedJobs.meta.isFetching = false;
        state.unratedJobs.meta.lastFetched = new Date();
      }
    },
    [types.JOB_FOR_RATING_REQUEST]: (state) => {
      state.pages.rateFamily.meta.isFetching = true;
    },
    [types.JOB_FOR_RATING_SUCCESS]: (state, payload) => {
      state.pages.rateFamily.data.jobId = payload.id;
      state.pages.rateFamily.data.ratingBy = payload.awardedApplicantId;
      state.pages.rateFamily.data.ratingFor = payload.jobOwnerId;

      state.pages.rateFamily.meta.job = payload;
      state.pages.rateFamily.meta.isFetching = false;
      state.pages.rateFamily.lastFetched = new Date();
    },
    [types.JOB_FOR_RATING_FAILURE]: (state) => {
      if (state.jobs) {
        state.pages.rateFamily.meta.job = undefined;
        state.pages.rateFamily.meta.isFetching = false;
        state.pages.rateFamily.meta.lastFetched = new Date();
      }
    },
    [types.JOB_DETAILS_REQUEST]: (state) => {
      state.pages.jobDetails.meta.isFetching = true;
    },
    [types.JOB_DETAILS_SUCCESS]: (state, payload) => {
      // state.pages.jobDetails.data = Object.assign({}, payload);
      state.pages.jobDetails.data = { ...payload };
      state.pages.jobDetails.meta.isFetching = false;
      state.pages.jobDetails.meta.dirty = false;
      state.pages.jobDetails.lastFetched = new Date();
    },
    [types.JOB_DETAILS_FAILURE]: (state) => {
      if (state.jobs) {
        state.pages.jobDetails.meta.job = undefined;
        state.pages.jobDetails.meta.isFetching = false;
        state.pages.jobDetails.meta.lastFetched = new Date();
        state.pages.jobDetails.meta.failed = true;
      }
    },
  },
  getters: {
    invitations: (state) => {
      const allJobs = state.jobs.data;
      const jobs = allJobs.filter((job) => job.jobStatus === c.jobStatus.pending);
      const responseRequired = [];
      const pendingResponseFromFamily = [];
      jobs.forEach((job) => {
        if (job.applicationStatus === c.jobApplicationStatus.pending || job.applicationStatus === c.jobApplicationStatus.viewed) {
          responseRequired.push(job);
        } else {
          pendingResponseFromFamily.push(job);
        }
      });

      return responseRequired.concat(pendingResponseFromFamily);
    },
    upcomingJobs: (state) => {
      const allJobs = state.jobs.data;
      const jobs = allJobs.filter((job) => job.jobStatus === c.jobStatus.awarded);
      return jobs;
    },
    jobHistory: (state) => {
      const allJobs = state.jobs.data;
      const jobs = allJobs.filter((job) => job.jobStatus === c.jobStatus.completed);
      return jobs;
    },
    jobById: (state) => (id) => {
      const allJobs = state.jobs.data;
      const job = allJobs.filter((j) => j.id === id);
      return job;
    },
  },
};
