<template>
  <q-layout view="lHh Lpr lFf">
    <q-header elevated>
      <q-toolbar class="bg-red text-center" v-if="$store.state.appInfo.adminMode">
        <q-toolbar-title>
          Admin Mode
        </q-toolbar-title>
      </q-toolbar>
      <q-toolbar>
        <q-btn
          flat
          dense
          round
           :disabled="isProcessing"
          icon="keyboard_arrow_left"
          @click="handlePrevious"
        />

        <q-toolbar-title class="text-center">
          <span>
              {{pageTitle}}
          </span>
          <span>
              {{pageSubtitle}}
          </span>
        </q-toolbar-title>

        <q-btn
          flat
          dense
          round
          icon="keyboard_arrow_right"
          :disabled="isProcessing"
          @click="handleNext"
        />
      </q-toolbar>
    </q-header>

    <q-drawer
      v-model="rightDrawerOpen"
      side="right"
      bordered
      overlay
      content-class="bg-grey-1"
      @before-show="handleDrawerBeforeShow"
    >
      <FiltersPanel :filterType="filterType" :jobType="job.jobType" ref="filtersPanel" />
    </q-drawer>

    <q-page-container>
      <router-view />
    </q-page-container>
  </q-layout>
</template>

<script>
import FiltersPanel from 'components/FiltersPanel';
import { mapState } from 'vuex';
import { postJob } from './multistep-helper';
import c from '../../utils/constants';

export default {
  name: 'TutoringJobLayout',
  components: { FiltersPanel },
  computed: {
    ...mapState({
      job: (state) => state.postTutoringJob.jobDetails.data,
      meta: (state) => state.postTutoringJob.jobDetails.meta,
    }),
    isProcessing() {
      if (!this.meta) return false;

      return this.meta.isProcessing;
    },
    nextButtonText() {
      return this.meta.currentStep === this.meta.lastStep ? 'Post' : 'Next';
    },
    filterType() {
      const filterType = this.job.jobDeliveryMethod === c.jobDeliveryMethod.faceToFace ? 'postHomeTutoringJob' : 'postOnlineTutoringJob';
      return filterType;
    },
    rightDrawerOpen: {
      get() {
        return this.$store.state.pageInfo.rightDrawerOpen;
      },
      set() {
        this.$store.dispatch('toggleRightDrawer');
      },
    },
  },
  methods: {
    handleDrawerBeforeShow() {
      this.$refs.filtersPanel.configureFilters();
    },
    handleNext() {
      if (this.meta.currentStep === this.meta.lastStep) {
        this.handlePostJob();
      } else {
        this.routerReplace({ path: `/post-tutoring-job/step/${this.meta.currentStep + 1}` });
      }
    },
    handlePrevious() {
      if (this.meta.currentStep === 1) {
        // this.routerBack();
        if (this.job.id && this.job.id > 0) {
          this.routerReplace(`/families/job-details/${this.job.id}`);
        } else {
          this.routerReplace('/post-job?reset=1');
        }
      } else {
        this.routerReplace({ path: `/post-tutoring-job/step/${this.meta.currentStep - 1}` });
      }
    },
    handlePostJob() {
      postJob(this.$router);
    },
  },
};
</script>
