<template>
  <q-dialog
      v-model="isVisible"
      prevent-close
      @ok="onOk"
      @cancel="onCancel"
      @show="onShow"
      @hide="onHide"
    >
    <q-layout view="Lhh lpR fff" container class="bg-white">
      <q-header>
        <q-toolbar>
          <q-toolbar-title>
            Bank Details
          </q-toolbar-title>
          <q-btn flat>
            <q-icon name="clear" v-close-popup />
          </q-btn>
        </q-toolbar>
      </q-header>
      <q-page-container>
        <div class="layout-padding">
          <q-input
            label="Bank Account Name"
            v-model="bankAccountName"
            borderless
            readonly
            style="width: 200px; margin: 5px 5px"
            class="inline"
            />
          <q-input
            v-if="country === 'au'"
            label="BSB"
            v-model="bankAccountBsb"
            readonly
            borderless
            style="width: 200px; margin: 5px 5px"
            class="inline"
            />
          <q-input
            label="Bank Account Number"
            v-model="bankAccountNumber"
            readonly
            borderless
            style="width: 200px; margin: 5px 5px"
            class="inline"
            />
        </div>
      </q-page-container>

      <q-footer class="bg-white">
        <q-toolbar>
          <q-btn
            color="accent"
            label="Got It"
            @click="onOk" />
          <!-- <q-btn color="secondary" label="Cancel" @click="props.cancel" /> -->
        </q-toolbar>
      </q-footer>
    </q-layout>
  </q-dialog>
</template>

<script>

export default {
  name: 'BankDetailsModal',
  emits: ['noticeConfirmed'],
  data() {
    return {
      name: '',
      isVisible: false,
      callback: null,
      bankAccountName: '',
      bankAccountNumber: '',
      bankAccountBsb: '',
    };
  },
  computed: {

  },
  methods: {
    showDialog(bankAccountName, bankAccountNumber, bankAccountBsb, cb) {
      this.isVisible = true;
      this.callback = cb;
      this.bankAccountName = bankAccountName;
      this.bankAccountNumber = bankAccountNumber;
      this.bankAccountBsb = bankAccountBsb;
    },
    onOk() {
      this.$emit('noticeConfirmed', { callback: this.callback });
    },
    onCancel() {

    },
    onShow() {
    },
    onHide() {
    },
  },
};
</script>
