import axios from 'axios';
import { Dialog } from 'quasar';
import errorHandler from '../utils/error-handler';
import { env } from '../utils/env';
import loader from '../utils/loader';
import { delay, doubleRaf, queryStringParams } from '../utils/helpers';
import {
  refreshToken, getAccessToken, getRefreshToken, logout,
} from '../api/auth';

const MAXRETRIES = 5;
const RETRYINTERVALS = [0, 2000, 2000, 2000, 2000]; // No need to extend the retry intervals as an offline dialog is utilised.
let offlineDialogActive = false;

const offlineDialog = () => {
  if (offlineDialogActive) {
    // Dialog was activate via another API, hold off further processing until the dialog has been dismissed.
    return new Promise((resolve) => {
      const checkDialogInterval = setInterval(() => {
        if (!offlineDialogActive) {
          clearInterval(checkDialogInterval);
          resolve();
        }
      }, 2000);
    });
  }

  offlineDialogActive = true;
  const options = {
    persistent: true,
    title: 'Offline',
    message:
      'Check your connection and try again.',
    ok: {
      label: 'Try Again',
      color: 'secondary',
      outline: true,
    },
  };

  const isLoaderActive = loader.isActive();
  return new Promise((resolve, reject) => {
    if (isLoaderActive) loader.hide();
    Dialog.create(options).onOk(() => {
      if (isLoaderActive) loader.show();
      offlineDialogActive = false;
      resolve();
    }).onCancel(() => {
      if (isLoaderActive) loader.hide();
      offlineDialogActive = false;
      reject();
    });
  });
};

const processResponse = (output) => {
  let { response } = output;
  if (!response) {
    response = {
      status: 503,
      data: {
        message: 'Unable to connect, please make sure your phone has internet access then try again.',
      },
    };
  }

  const { config } = output;
  const result = {
    method: config ? config.method : '',
    url: config ? config.url : '',
    status: response.status,
    success: response.status >= 200 && response.status < 300,
  };

  if (result.success) {
    result.data = response.data;
  } else {
    if (response.data) {
      if (response.data.message) result.message = response.data.message;
      else if (response.data.error_description) result.message = response.data.error_description;
      else if (response.data.exceptionMessage) result.exceptionMessage = response.data.exceptionMessage;
    }

    if (!result.message) result.message = 'An error occurred. Please try again later.';
  }

  return result;
};

const redirectToLogin = async (router) => {
  await logout();
  loader.hide();

  const params = queryStringParams();
  if (params.admin_mode && params.admin_mode === '1') {
    window.location = '/';
    return;
  }

  if (env.initialised) {
    router.push('/login?messageType=1001');
    doubleRaf(() => {
      window.location.reload();
    });
  } else {
    // Do nothing, the router will automatically redirect to login page.
    // window.location.href = 'statics/error.html';
  }
};

export default async ({ app, router }) => {
  console.log('boot::axios');

  app.config.globalProperties.$axios = axios;

  const axiosValidateStatus = (status) => status >= 200 && status < 300;

  axios.interceptors.request.use((config) => {
    config.validateStatus = axiosValidateStatus;
    if (!config.retries) config.retries = 0;

    return config;
  }, (error) => Promise.reject(error));

  axios.interceptors.response.use((response) => processResponse({ response, config: response.config }), async (error) => {
    if (!error.response && error.config.url !== '/app-info.json') {
      await offlineDialog();
      await delay(2000);
    }

    const result = processResponse({ response: error.response, config: error.config });
    if (result.url === '/app-info.json') return Promise.reject(result);

    if (!result.success && result.status === 503 && error.config && error.config.retries < MAXRETRIES) {
      // Only use safe to retry APIs.
      if (result.method === 'get' || result.url.endsWith('/search')) {
        await delay(RETRYINTERVALS[error.config.retries]);
        error.config.retries++;
        return axios.request(error.config);
      }
    }

    if (result.status === 401) {
      // https://github.com/axios/axios/issues/934

      if (error.config.retries < MAXRETRIES) {
        if (!getRefreshToken()) {
          redirectToLogin(router);
        } else {
          try {
            await refreshToken();
            error.config.headers.Authorization = `Bearer ${getAccessToken()}`;
            error.config.retries++;
            return axios.request(error.config);
          } catch (e) {
            redirectToLogin(router);
          }
        }
      } else {
        try {
          const message = 'Maximum retries reached.';
          throw new Error(message);
        } catch (e) {
          errorHandler.captureException(e);
        }
      }
    } else if (result.status > 401) {
      if (result.status !== 503 && result.status !== 404) {
        try {
          const message = `Status: ${result.status}, ${result.exceptionMessage ? result.exceptionMessage : result.message}${error.config.retries ? `. Retry Count: ${error.config.retries}` : ''}`;
          throw new Error(message);
        } catch (e) {
          errorHandler.captureException(e);
        }
      }

      if (result.status === 403) {
        // Invalid token.
        redirectToLogin(router);
      } else if (result.status === 404 || result.status === 405) {
        loader.hide();
        // The caller should handle this error.
      } else if (result.status !== 503 || error.config.retries === MAXRETRIES) {
        // Allow Raygun a few seconds to process the error.
        setTimeout(() => {
          loader.hide();
          router.push('/error');
        }, 3000);
      }
    }

    return Promise.reject(result);
  });
};
