<template>
  <div class="">
    <q-field
      borderless
      label-slot
      stack-label
      hide-bottom-space
      readonly
      class="q-mb-sm"
      v-if="isBusiness"
    >
    <template v-slot:label>
      <h5 class="text-primary text-wrap no-margin">
        Business Details
      </h5>
    </template>
      <template v-slot:control>
        <div class="full-width q-mt-sm">
          <q-input v-model="tradingName" placeholder="Business Trading Name" :error="v$.tradingName.$error" hide-bottom-space />
          <q-input v-model="businessName" placeholder="Registered Business Name" :error="v$.businessName.$error" hide-bottom-space />
          <q-input v-model="businessNumber" :placeholder="signupCountry === 'au' ? 'ABN' : 'NZBN'" :error="v$.businessNumber.$error" hide-bottom-space />
          <div v-if="signupCountry === 'nz'">
            <q-input v-model="businessTaxNumber" type="number" placeholder="GST Number" :error="v$.businessTaxNumber.$error" hide-bottom-space />
            <div class="q-mt-sm">
              If your business is registered for GST, please enter your GST Number above.
            </div>
          </div>
        </div>
      </template>
    </q-field>
    <q-field
      borderless
      label-slot
      stack-label
      hide-bottom-space
      readonly
    >
    <template v-slot:label>
      <h5 v-if="isBusiness" class="text-primary text-wrap no-margin">
        Account Holder Details
      </h5>
      <h5 v-else class="text-primary text-wrap no-margin">
        Please tell us a bit about yourself…
      </h5>
    </template>
      <template v-slot:control>
        <div class="full-width q-mt-sm">
          <div class="q-mt-sm" v-if="isBusiness">
            The person responsible for posting jobs and receiving responses on their mobile phone.
          </div>
          <q-input v-model="firstName" placeholder="First Name" :error="v$.firstName.$error" name="fname" autocomplete="given-name" autocapitalize="off" hide-bottom-space/>
          <q-input v-model="lastName" placeholder="Last Name" :error="v$.lastName.$error" name="lname" autocomplete="family-name" autocapitalize="off" hide-bottom-space />
          <div class="q-my-sm" v-if="isIndividual">
            Only the first initial of your last name will be shown on Juggle Street, e.g. Emily Smith will become Emily S.
          </div>
        </div>
      </template>
    </q-field>
    <q-field
      label="Gender"
      stack-label
      borderless
      hide-bottom-space
      :error="v$.gender.$error"
      error-message="Select your gender"
    >
      <template v-slot:control>
        <q-option-group
            type="radio"
            v-model="gender"
            :options="[
                { label: 'Male', value: 1 },
                { label: 'Female', value: 2 },
            ]"
        />
      </template>
    </q-field>
    <q-field
      v-if="isProvider"
      label="Date of Birth"
      stack-label
      borderless
      hide-bottom-space
      helper="Your date-of-birth is used to calculate your age, which is displayed on your profile."
      :error="v$.dateOfBirth.$error"
      error-message="Select your date of birth"
    >
      <template v-slot:control>
        <div class="full-width">
          <date-picker :autoSelect="true" :show-hint="true" :minimal="true" :date="dateOfBirth" v-on:change="handleDateOfBirthChange" default-view="Years" default-year-month="1980/01"/>
          <div class="q-my-sm">
            Your date of birth is never displayed, just your current age e.g. Emily S – 23 yrs.
          </div>
        </div>
      </template>
    </q-field>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import useVuelidate from '@vuelidate/core';
import {
  required, requiredIf, minLength, numeric,
} from '@vuelidate/validators';
import DatePicker from 'components/DatePicker';
import c from '../../utils/constants';

export default {
  name: 'SignupStep2',
  components: { DatePicker },
  props: ['currentStep'],
  data() {
    return {};
  },
  setup() {
    return {
      v$: useVuelidate(),
    };
  },
  validations() {
    return {
      firstName: {
        required,
        minLength: minLength(2),
      },
      lastName: {
        required,
        minLength: minLength(2),
      },
      gender: {
        required,
      },
      dateOfBirth: {
        requiredIf: requiredIf(this.isProvider),
      },
      businessName: {
        requiredIf: requiredIf(this.isBusiness),
        minLength: minLength(2),
      },
      tradingName: {
        requiredIf: requiredIf(this.isBusiness),
        minLength: minLength(2),
      },
      businessNumber: {
        validate(value) {
          if (!this.isBusiness) return true;

          if (!value) return false;
          if (!numeric.$validator(value)) return false;

          if (this.signupCountry === 'au') return value.length === 11;
          if (this.signupCountry === 'nz') return value.length === 13;

          return false;
        },
      },
      businessTaxNumber: {
        validate(value) {
          if (!value) return true;

          if (!numeric.$validator(value)) return false;

          return value.length === 9;
        },
      },
    };
  },
  computed: {
    ...mapState({
      accountInfo: (state) => state.signup.userDetails.data,
    }),
    storeModuleName() {
      return 'signup.userDetails';
    },
    isClient() {
      return this.accountInfo.accountType === c.accountType.client;
    },
    isIndividual() {
      return this.accountInfo.clientType === c.clientType.individual || this.accountInfo.providerType === c.providerType.individual;
    },
    isBusiness() {
      return this.accountInfo.clientType === c.clientType.business || this.accountInfo.providerType === c.providerType.business;
    },
    isProvider() {
      return this.accountInfo.accountType === c.accountType.provider;
    },
    signupCountry() {
      return this.accountInfo.country;
    },
    firstName: {
      get() {
        return this.accountInfo.firstName;
      },
      set(value) {
        this.setProperty('firstName', value);
      },
    },
    lastName: {
      get() {
        return this.accountInfo.lastName;
      },
      set(value) {
        this.setProperty('lastName', value);
      },
    },
    businessName: {
      get() {
        return this.accountInfo.businessName;
      },
      set(value) {
        this.setProperty('businessName', value);
      },
    },
    tradingName: {
      get() {
        return this.accountInfo.tradingName;
      },
      set(value) {
        this.setProperty('tradingName', value);
      },
    },
    businessNumber: {
      get() {
        return this.accountInfo.businessNumber;
      },
      set(value) {
        this.setProperty('businessNumber', value);
      },
    },
    businessTaxNumber: {
      get() {
        return this.accountInfo.businessTaxNumber;
      },
      set(value) {
        this.setProperty('businessTaxNumber', value);
      },
    },
    gender: {
      get() {
        return this.accountInfo.gender;
      },
      set(value) {
        this.setProperty('gender', value);
      },
    },
    dateOfBirth: {
      get() {
        return this.accountInfo.dateOfBirth;
      },
      set(value) {
        this.setProperty('dateOfBirth', value);

        if (value && value.getDate) {
          this.setProperty('dateOfBirthDay', value.getDate());
          this.setProperty('dateOfBirthMonth', value.getMonth() + 1);
          this.setProperty('dateOfBirthYear', value.getFullYear());
        }
      },
    },
  },
  methods: {
    validate() {
      this.v$.$touch();
      const hasError = this.v$.$error;
      return hasError;
    },
    handleDateOfBirthChange(value) {
      this.dateOfBirth = value.date;
    },
  },
  watch: {
  },
};
</script>
<style lang="sass">

</style>
