<template>
  <div class="profile-wizard-profile-photo layout-padding column justify-center">
    <div v-if="isIndividual">
      <p style="margin-top: 10px">
        {{getLabel()}}
      </p>
      <p>
        Your photo is only visible to other Juggle Street users. It cannot be found by somebody browsing the web.
      </p>
    </div>
    <div v-else>
      <p>
        Your logo will be visible to Juggle Street helpers and appear on your job posts.
      </p>
    </div>
    <q-field v-if="!cropperShown" borderless hide-bottom-space>
      <template v-slot:prepend>
        <q-icon name="error_outline" />
      </template>
      <template v-slot:control>
        <span v-if="isIndividual">Tap below to select a suitable photo from your device.</span>
        <span v-else>Tap below to select a suitable image from your device.</span>
      </template>
    </q-field>
    <div v-if="!cropperShown" class="column justify-center text-center">
      <image-uploader
        ref="profile-photo-uploader"
        @uploadSuccess="handleUploadSuccess"
        @uploadFailure="handleUploadFailure"
      />
      <q-btn color="secondary" icon="photo_camera" @click="handleUploadPhoto" />
    </div>
    <q-field v-if="cropperShown" borderless hide-bottom-space class="no-margin no-padding">
      <template v-slot:prepend>
        <q-icon name="error_outline" />
      </template>
      <template v-slot:control>
        Adjust the cropping box then tap Next to continue.
      </template>
    </q-field>
    <div v-if="cropperShown" class="">
      <crop-image
        :imageUrl="imageUrl"
        @cropMove="handleCropImageChange"
      />
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex';
import ImageUploader from 'components/ImageUploader';
import CropImage from 'components/CropImage';
import NotificationService from '../../utils/notification-service';
import * as types from '../../store/mutation-types';
import beforeLeaveStep from './before-leave-step';
import c from '../../utils/constants';

export default {
  name: 'ProfilePhoto',
  components: { ImageUploader, CropImage },
  data() {
    return {};
  },
  methods: {
    ...mapActions([
      'updateUserInfo',
    ]),
    handleUploadPhoto() {
      this.$refs['profile-photo-uploader'].open();
    },
    handleUploadSuccess() {
      this.$forceUpdate();
    },
    handleUploadFailure() {
      NotificationService.negative('An error occurred. Please try again later.');
    },
    handleCropImageChange(cropDimensions) {
      this.setProperty('defaultImage.cropDimensions', cropDimensions);
      this.$store.commit(`profileWizard/${types.UPDATE_STEP_STATUS}`, { complete: true });
    },
    getJugglerType($route) {
      if ($route.matched.some((record) => record.meta.jugglerType === c.jugglerType.client)) {
        return c.jugglerType.client;
      }

      return c.jugglerType.provider;
    },
    getLabel() {
      return this.getJugglerType(this.$route) === c.jugglerType.provider
        ? 'Families and helpers are required to have a profile photo, most helpers choose a photo of themselves doing the job they are good at.'
        : 'Families and helpers are required to have a profile photo, choose one that represents your whole family.';
    },
  },
  computed: {
    ...mapState({
      imageUrl: (state) => {
        const image = state.userInfo.data.defaultImage;
        return image.scale1ImageUrl;
      },
      cropperShown: (state) => {
        // Show cropper if the user has an image.
        const image = state.userInfo.data.defaultImage;
        return !!(image && image.scale1ImageUrl);
      },
      stepComplete: (state) => {
        const { meta } = state.profileWizard;
        const currentStep = meta.steps[meta.currentStep];
        return currentStep.complete;
      },
    }),
    storeModuleName() {
      return 'userInfo';
    },
  },
  beforeRouteLeave(to, from, next) {
    // Update user info before proceeding to the next step if step is complete
    if (this.stepComplete) {
      this.updateUserInfo().then(() => {
        beforeLeaveStep(this, to, from, next);
      });
    } else {
      beforeLeaveStep(this, to, from, next);
    }
  },
};
</script>
<style lang="sass">
.profile-wizard-profile-photo
  color: grey
  .photo-icon
    font-size: 12rem
</style>
