<template>
  <div>
    <q-card flat v-if="job.id && job.id > 0">
      <q-card-section>
        <div>
          <img class="round-user-image" :src="awardedImageSrc"/> {{awardedName}}
          <br/>
          <span>{{jobTypeText}}</span>
        </div>
        <span>{{jobDate}}</span>
        <q-field :error="v$.category1.$error" error-message="required" borderless hide-bottom-space>
          <template v-slot:control>
            <div class="full-width  q-my-sm">
              <p class="no-margin">
                Punctuality
              </p>
              <q-rating size="2rem" v-model="category1" :max="5" color="accent" />
            </div>
          </template>
        </q-field>
        <q-field :error="v$.category2.$error" error-message="required" borderless hide-bottom-space>
          <template v-slot:control>
            <div class="full-width  q-my-sm">
              <p class="no-margin">
                Performance of duties
              </p>
              <q-rating size="2rem" v-model="category2" :max="5" color="accent" />
            </div>
          </template>
        </q-field>
        <q-field :error="v$.category3.$error" error-message="required" borderless hide-bottom-space>
          <template v-slot:control>
            <div class="full-width  q-my-sm">
              <p class="no-margin">
                Communication with you
              </p>
              <q-rating size="2rem" v-model="category3" :max="5" color="accent" />
            </div>
          </template>
        </q-field>
        <q-field :error="v$.category4.$error" error-message="required" borderless hide-bottom-space>
          <template v-slot:control>
            <div class="full-width  q-my-sm">
              <p class="no-margin">
                What rating do you think your children would give?
              </p>
              <q-rating size="2rem" v-model="category4" :max="5" color="accent" />
            </div>
          </template>
        </q-field>
        <q-field error-message="required" borderless hide-bottom-space>
            <template v-slot:control>
              <div class="full-width">
              <p class="no-margin">
                Review (optional, will appear on helper’s profile)
              </p>
                <q-input v-model="feedback" type="textarea" :max-height="100" rows="3" placeholder="Write a quick review… This will appear on the helper's profile and helps other parents in your neighbourhood."/>
              </div>
            </template>
        </q-field>
        <div class="q-my-sm text-center">
          <q-btn color="accent" @click="handleSubmitRating" label="Submit" />
        </div>
      </q-card-section>
    </q-card>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex';
import useVuelidate from '@vuelidate/core';
import { required, minValue, numeric } from '@vuelidate/validators';
import { formatJobDate, jobTypeText } from '../utils/helpers';
import NotificationService from '../utils/notification-service';

export default {
  name: 'RateHelper',
  emits: ['rateHelperSuccess'],
  data() {
    return {

    };
  },
  setup() {
    return {
      v$: useVuelidate(),
    };
  },
  validations: {
    category1: {
      required,
      minValue: minValue(1),
      numeric,
    },
    category2: {
      required,
      minValue: minValue(1),
      numeric,
    },
    category3: {
      required,
      minValue: minValue(1),
      numeric,
    },
    category4: {
      required,
      minValue: minValue(1),
      numeric,
    },
  },
  computed: {
    ...mapState({
      rating: (state) => state.families.pages.rateHelper.data,
      job: (state) => state.families.pages.rateHelper.meta.job,
    }),
    storeModuleName() {
      return 'families.pages.rateHelper';
    },
    jobDate() {
      return formatJobDate(this.job);
    },
    jobTypeText() {
      return jobTypeText(this.job.jobType, this.clientType);
    },
    awardedName() {
      if (this.job.awardedFirstName) {
        return `${this.job.awardedFirstName} ${this.job.awardedLastInitial}`;
      }

      const applicant = this.job.applicants.find((a) => a.applicantId === this.ratingFor);
      return `${applicant.applicantFirstName} ${applicant.applicantLastInitial}`;
    },
    awardedImageSrc() {
      if (this.job.awardedImageSrc) {
        return this.job.awardedImageSrc;
      }

      const applicant = this.job.applicants.find((a) => a.applicantId === this.ratingFor);
      return applicant.applicantImageSrc;
    },
    ratingFor: {
      get() {
        return this.rating.ratingFor;
      },
      set(value) {
        this.setProperty('ratingFor', value);
      },
    },
    feedback: {
      get() {
        return this.rating.feedback;
      },
      set(value) {
        this.setProperty('feedback', value);
      },
    },
    category1: {
      get() {
        return this.rating.category1;
      },
      set(value) {
        this.setProperty('category1', value);
      },
    },
    category2: {
      get() {
        return this.rating.category2;
      },
      set(value) {
        this.setProperty('category2', value);
      },
    },
    category3: {
      get() {
        return this.rating.category3;
      },
      set(value) {
        this.setProperty('category3', value);
      },
    },
    category4: {
      get() {
        return this.rating.category4;
      },
      set(value) {
        this.setProperty('category4', value);
      },
    },
  },
  methods: {
    ...mapActions({
      postHelperRating: 'families/postHelperRating',
    }),
    validate() {
      this.v$.$touch();
      const hasError = this.v$.$error;
      if (hasError) {
        NotificationService.negative('Please review highlighted fields.');
        return false;
      }

      return true;
    },
    async handleSubmitRating() {
      if (!this.validate()) return;

      await this.postHelperRating(this.$router);
      this.$emit('rateHelperSuccess');
    },
  },
};
</script>
