<template>
  <q-page class="">
    <div class="gutter-y-sm">
      <q-field
        class="q-my-sm"
        label="Citizenship and Right to Work"
        borderless
        readonly
      >
        <template v-slot:prepend>
          <q-icon name="location_city" />
        </template>
        <template v-slot:control>
          <div class="full-width">
            <vevo-details ref="vevoDetails" />
          </div>
        </template>
      </q-field>
    </div>
  </q-page>
</template>

<script>
import VevoDetails from 'components/VevoDetails';
import NotificationService from '../../utils/notification-service';

export default {
  name: 'PageAccountVevoDetails',
  components: { VevoDetails },
  data() {
    return {};
  },
  methods: {},
  computed: {},
  beforeRouteLeave(to, from, next) {
    if (!this.$refs.vevoDetails.validate(true)) {
      next(false);
      return;
    }

    this.$store.dispatch('updateUserInfo').then(() => {
      next();
    }).catch((error) => {
      NotificationService.negative(error.message);
      next(false);
    });
  },
};
</script>
