<template>
  <div class="">
    <h6 class="no-margin text-center">Primary School</h6>
    <q-field
      borderless
      stack-label
      hide-bottom-space
      label="Select age groups you work with">
      <template v-slot:prepend>
        <q-icon name="class" />
      </template>
      <template v-slot:control>
        <q-chip
          clickable
          outline
          :icon="item.selected ? 'check' : 'clear'"
          :color="item.selected ? 'primary' : 'black'"
          :text-color="item.selected ? 'white' : 'black'"
          v-for="(item, index) in userInfo.provider.primarySchoolYears" :key="item.optionId"
          @click="handleItemClick('primarySchoolYears', index)"
          class="q-mx-xs q-my-xs q-caption profile-tab-margin"
        >
          {{item.text}}
        </q-chip>
      </template>
    </q-field>

    <q-field
      label="Primary School Subjects"
      borderless
      stack-label
      hide-bottom-space
      ref="primarySchoolTree">
      <template v-slot:prepend>
        <q-icon name="school" />
      </template>
      <template v-slot:control>
        <q-tree
          :nodes="primarySchoolSubjects"
          color="primary"
          v-model:ticked="selectedPrimarySchoolSubjects"
          v-model:expanded="expandedPrimarySchoolSubjects"
          node-key="optionId"
          tick-strategy="leaf"
        />
      </template>
    </q-field>

    <h6 class="no-margin text-center">Tutoring Experience</h6>
    <q-field
      label="Describe your Tutoring experience"
      :error="v$.myExperience3.$error"
      borderless
      stack-label
      ref="myExperience3"
      class="q-mx-sm"
    >
      <!-- <template v-slot:prepend>
        <q-icon name="border_color" />
      </template> -->
      <template v-slot:control>
        <div class="full-width">
          <q-input
            v-model="myExperience3"
            type="textarea"
            placeholder="How long have you been tutoring? What subjects are you passionate about? What academic qualifications do you have? What formal teaching experience do you have? Etc."
            :max-height="100"
            rows="5"
          />
        </div>
      </template>
      <template v-slot:hint>
        {{myExperience3Helper}}
      </template>
      <template v-slot:error>
        {{myExperience3Helper}}
      </template>
    </q-field>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import useVuelidate from '@vuelidate/core';
import NotificationService from '../utils/notification-service';
import * as mutationTypes from '../store/mutation-types';
import {
  getSelectedOptions,
  processOptionsTree,
  getExpandedOptions,
  scrollToElement,
} from '../utils/helpers';

export default {
  name: 'HelperTutoringPrimarySchoolPreferences',
  props: ['displayCategory'],
  emits: ['change'],
  data() {
    const {
      primarySchoolSubjects,
    } = this.$store.state.userInfo.data.provider;
    const selectedPrimarySchoolSubjects = getSelectedOptions(primarySchoolSubjects);
    const expandedPrimarySchoolSubjects = getExpandedOptions(primarySchoolSubjects);

    const data = {
      selectedPrimarySchoolSubjects,
      expandedPrimarySchoolSubjects,
    };

    return data;
  },
  methods: {
    handleItemClick(section, index) {
      const options = this.userInfo.provider[section];
      const currentOption = options[index];

      const selectedOptions = [];
      options.forEach((option) => {
        let selected = false;
        if (option.optionId === currentOption.optionId) {
          selected = !option.selected;
        } else {
          // eslint-disable-next-line
          selected = option.selected;
        }

        if (selected) {
          selectedOptions.push(option.optionId);
        }
      });

      const payload = {
        propPath: `provider.${section}`,
        value: selectedOptions,
      };

      this.$store.commit(mutationTypes.UPDATE_USER_OPTIONS, payload);

      this.triggerChange();
    },
    isValid() {
      const hasSelectedYearsAndSubjects = this.hasSelectedPrimarySchoolYears && this.selectedPrimarySchoolSubjects.length === 0;
      if (!hasSelectedYearsAndSubjects) return false;

      if (this.myExperience3 && this.myExperience3.length <= 100) return false;

      return true;
    },
    validate(notify) {
      if (this.hasSelectedPrimarySchoolYears && this.selectedPrimarySchoolSubjects.length === 0) {
        if (notify) {
          NotificationService.negative('You must select primary school subjects as you have selected primary school age groups.');
          scrollToElement(this.$refs.primarySchoolTree.$el);
        }
        return false;
      }

      const hasValidPrimarySchoolSelections = (this.hasSelectedPrimarySchoolYears && this.selectedPrimarySchoolSubjects.length > 0);

      if (!hasValidPrimarySchoolSelections) {
        if (notify) {
          NotificationService.negative('Please select relevant primary school years and subjects.');
        }
        return false;
      }

      this.v$.$touch();
      if (this.v$.$error) {
        if (notify) {
          NotificationService.negative('Please fix all highlighted errors.');
          if (this.myExperience3 && this.myExperience3.length === 0) {
            scrollToElement(this.$refs.myExperience3.$el);
          }
        }
        return false;
      }

      return true;
    },
    triggerChange() {
      const isValid = this.validate(false);

      // let { interestedInJobTypes } = this.userInfo;
      // interestedInJobTypes = toggleHomeTutoring(this.userInfo);

      // if (this.userInfo.interestedInJobTypes !== interestedInJobTypes) {
      //   this.setProperty('interestedInJobTypes', interestedInJobTypes);
      // }

      this.$emit('change', { valid: isValid });
    },
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
    }),
    storeModuleName() {
      return 'userInfo';
    },
    hasSelectedPrimarySchoolYears() {
      return this.userInfo.provider.primarySchoolYears.filter((option) => option.selected).length > 0;
    },
    primarySchoolSubjects() {
      const subjects = this.userInfo.provider.primarySchoolSubjects;
      const tree = processOptionsTree(subjects);
      return tree;
    },
    myExperience3: {
      get() {
        return this.userInfo.provider.myExperience3;
      },
      set(value) {
        this.setProperty('provider.myExperience3', value);
        this.triggerChange();
      },
    },
    myExperience3Helper() {
      if (this.v$.myExperience3.$error && this.myExperience3) {
        return `required - ${100 - this.myExperience3.length} characters left`;
      }

      return 'required - min 100 characters';
    },
  },
  watch: {
    selectedPrimarySchoolSubjects(selectedOptions) {
      const section = 'primarySchoolSubjects';
      const payload = {
        propPath: `provider.${section}`,
        value: selectedOptions,
      };

      this.$store.commit(mutationTypes.UPDATE_USER_OPTIONS, payload);
      this.triggerChange();
    },
  },
  setup() {
    return {
      v$: useVuelidate(),
    };
  },
  validations: {
    myExperience3: {
      required(value) {
        if (this.hasSelectedPrimarySchoolYears) {
          return value !== null && value.length >= 100;
        }

        return true;
      },
    },
  },
};
</script>
