<template>
  <q-page class="">
    <div class="layout-padding" v-if="isApproved">
      <q-stepper v-model="currentStep" animated vertical class="q-pb-lg">
        <q-step
          v-if="mode === 'add'"
          title="What kind of job are you posting?"
          color="primary"
          :name="1"
          :done="currentStep > 1"
        >
          <div class="full-width">
            <div v-if="canAccessChildcareJobs">
              <q-radio class="" v-model="jobType" :val="1">
                <span class="q-option-label">
                  {{isIndividual ? 'Babysitting' : 'One-off Job'}}
                </span>
              </q-radio>
              <br/>
              <q-radio class="" v-model="jobType" :val="2">
                <span class="q-option-label">
                  {{isIndividual ? 'Nannying' : 'Recurring Job'}}
                  <!-- Nannying <small v-if="isBusiness" class="text-grey q-ml-xs">Recurring jobs</small> -->
                </span>
              </q-radio>
              <br/>
              <q-radio class="" v-model="jobType" :val="12">
                <span class="q-option-label">
                  {{isIndividual ? 'Before & After School Care' : 'Outside School Hours Job'}}
                </span>
              </q-radio>
              <br/>
              <div class="q-mx-md" v-if="jobType === 12">
                <div class="">
                  <q-checkbox v-model="isBeforeSchoolCare" label="Before School Care" />
                  <br/>
                  <label class="q-mx-md">Monday to Friday AM shift</label>
                </div>
                <div class="">
                  <q-checkbox v-model="isAfterSchoolCare" label="After School Care" />
                  <br/>
                  <label class="q-mx-md">Monday to Friday PM shift</label>
                </div>
              </div>
              <!-- <q-radio class="" v-model="jobType" :val="16">
                <span class="q-option-label">
                  Au Pair
                </span>
              </q-radio>
              <br/> -->
            </div>
            <div v-if="canAccessTutoringJobs">
              <q-radio class="" v-model="jobType" :val="64">
                <span class="q-option-label">
                  Primary School Tutoring
                </span>
              </q-radio>
              <br/>
              <q-radio class="" v-model="jobType" :val="128">
                <span class="q-option-label">
                  {{highSchoolText}}
                </span>
              </q-radio>
            </div>
            <div v-if="isCorporateMembership">
              <small>* Please contact Customer Service if you are trying to post a Primary School or a High School Tutoring Job.</small>
            </div>
          </div>
          <q-stepper-navigation>
            <q-btn color="accent" @click="handleNextStep">Next</q-btn>
          </q-stepper-navigation>
        </q-step>
        <q-step
          title="Job Address"
          color="primary"
          :name="2"
          :done="currentStep > 2"
        >
          <div>
            <div v-for="(address) in userInfo.addresses" :key="address.id">
              <q-radio class="" v-model="addressId" :val="address.id">
                {{formattedAddress(address)}}
              </q-radio>
            </div>
            <div v-if="hasMultipleAddresses">
              <q-radio v-model="addressId" :val="-1" @update:model-value="handleAddAddress">Alternate Address</q-radio>
              <add-address-modal ref="AddAddressModal" v-on:addAddressSuccess="handleAddAddressSuccess" v-on:hide="handleHide"/>
            </div>
          </div>
          <q-stepper-navigation>
            <q-btn v-if="mode === 'add'" color="secondary" outline class="q-mr-sm" @click="handlePreviousStep">Back</q-btn>
            <q-btn color="accent" @click="handleNextStep">Next</q-btn>
          </q-stepper-navigation>
        </q-step>
        <q-step
          title="Job Posting & Candidate Selection"
          color="primary"
          :name="3"
          :done="currentStep > 3"
          v-if="mode === 'add'"
        >
          <div class="full-width">
            <div class="q-mb-sm">
              Once you have defined & described your job it is ready to post to Juggle Street helpers - you have 2 options
            </div>
            <q-radio class="" v-model="managedBy" :val="types.managedBy.system" label="Juggle Assist" dense />
            <div class="q-option-label q-ml-lg q-mb-sm">
              <small class="text-grey-8">
                Juggle Street’s algorithm matches carers to your requirements, invites the carers, and presents a shortlist of interested applicants.
              </small>
            </div>

            <q-radio class="" v-model="managedBy" :val="types.managedBy.user" label="Do It Yourself" dense />
            <div class="q-option-label q-ml-lg">
              <small class="text-grey-8">
                Browse profiles & use filters to match helpers to your requirements, send job invites to your selected helpers, receive all replies, create candidate shortlist.
              </small>
            </div>
        </div>
          <q-stepper-navigation>
            <q-btn color="secondary" outline class="q-mr-sm" @click="handlePreviousStep">Back</q-btn>
            <q-btn color="accent" @click="handleSubmit">Submit</q-btn>
          </q-stepper-navigation>
        </q-step>
      </q-stepper>
    </div>
    <div v-else>
      <pending-profile-banner/>
    </div>
    <!-- <div class="q-mx-sm q-my-sm">
      <p>
        <strong>COVID-19 Notice</strong>
        <br/>
          If you answer <strong>YES</strong> to any of the questions below you are <strong>NOT PERMITTED to Post a Job</strong>.
        <ol>
          <li>
            Are you currently living in a period of Government mandated quarantine or self-isolation?
          </li>
          <li class="q-my-md">
            Are you living with anybody currently in a period of Government mandated quarantine or self-isolation?
          </li>
          <li class="q-my-md">
            Have you been in contact with a known or suspected case of COVID-19?
          </li>
          <li>
            Are you exhibiting any symptoms of COVID-19, including high temperature, sore throat, coughing, runny nose, sneezing?
          </li>
        </ol>
      </p>
      <p>
        <strong>Juggle St Helpers</strong>
        <br/>
        All helpers receiving a job invite from a parent must first complete a mandatory questionnaire relating to their travel history, exposure to COVID-19 and their current health status before they can apply for the job. All helpers have also been provided a link to the Government COVID-19 Infection Control training portal.
      </p>
    </div> -->
  </q-page>
</template>

<script>
import { mapState, mapActions } from 'vuex';
import AddAddressModal from 'components/AddAddressModal';
import PendingProfileBanner from 'components/PendingProfileBanner';
import NotificationService from '../utils/notification-service';
import { getFormattedAddress } from '../utils/helpers';
import * as mutationTypes from '../store/mutation-types';
import store from '../store';
import c from '../utils/constants';

export default {
  name: 'PagePostJob',
  components: { AddAddressModal, PendingProfileBanner },
  data() {
    return {
      types: {
        managedBy: c.managedBy,
        jobType: c.jobType,
      },
      mode: 'add',
      newAddress: -1,
      currentStep: 1,
    };
  },
  computed: {
    ...mapState({
      job: (state) => state.postJob.jobDetails.data,
      userInfo: (state) => state.userInfo.data,
    }),
    storeModuleName() {
      return 'postJob.jobDetails';
    },
    hasMultipleAddresses() {
      return this.isIndividual || this.userInfo.client.clientCategory === c.clientCategory.businessMultiSite;
    },
    step1HasError() {
      return this.job.jobType === c.jobType.unspecified;
    },
    step2HasError() {
      return (!this.addressId || this.addressId === -1);
    },
    step3HasError() {
      return this.managedBy === c.managedBy.unspecified;
    },
    highSchoolText() {
      return this.country === 'au' ? 'High School Tutoring' : 'Secondary School Tutoring';
    },
    hasUnlimitedAccess() {
      return this.userInfo.hasUnlimitedAccess;
    },
    hasSubscription() {
      return this.userInfo.membershipType === c.membershipType.subscription;
    },
    hasBabysittingOnlySubscription() {
      return this.userInfo.paymentInfo.paymentType === c.userPaymentType.annualBabysittingSubscription60
        || this.userInfo.paymentInfo.paymentType === c.userPaymentType.annualBabysittingSubscription120;
    },
    hasChildcareSubscription() {
      return this.userInfo.paymentInfo.paymentType === c.userPaymentType.annualChildcareSubscription120
        || this.userInfo.paymentInfo.paymentType === c.userPaymentType.annualChildcareSubscription180;
    },
    hasTutoringSubscription() {
      return this.userInfo.paymentInfo.paymentType === c.userPaymentType.annualTutoringSubscription90;
    },
    isPayg() {
      return this.userInfo.membershipType === c.membershipType.free
      || this.userInfo.membershipType === c.membershipType.payAsYouGo;
    },
    cantPostBabysittingJob() {
      return this.hasSubscription && !this.userInfo.permissions.canPostUnlimitedOneOffJobs;
    },
    cantPostRecurringChildcareJob() {
      return this.hasSubscription && !this.userInfo.permissions.canPostUnlimitedRecurringJobs;
    },
    cantPostAuPairJob() {
      return this.hasSubscription && !this.userInfo.permissions.canPostUnlimitedAuPairJobs;
    },
    cantPostPrimarySchoolTutoringJob() {
      return this.hasSubscription && !this.userInfo.permissions.canPostUnlimitedPrimarySchoolTutoringJobs;
    },
    cantPostHighSchoolTutoringJob() {
      return this.hasSubscription && !this.userInfo.permissions.canPostUnlimitedHighSchoolTutoringJobs;
    },
    isBeforeSchoolCare: {
      get() {
        return this.job.isBeforeSchoolCare;
      },
      set(value) {
        this.setProperty('isBeforeSchoolCare', value);
      },
    },
    isAfterSchoolCare: {
      get() {
        return this.job.isAfterSchoolCare;
      },
      set(value) {
        this.setProperty('isAfterSchoolCare', value);
      },
    },
    jobType: {
      get() {
        return this.job.jobType;
      },
      set(value) {
        this.setProperty('jobType', value);

        this.managedBy = (value === c.jobType.babysitting) ? c.managedBy.unspecified : c.managedBy.system;
      },
    },
    managedBy: {
      get() {
        return this.job.managedBy;
      },
      set(value) {
        this.setProperty('managedBy', value);
      },
    },
    addressId: {
      get() {
        return this.job.addressId;
      },
      set(value) {
        this.setProperty('addressId', value);
      },
    },
  },
  methods: {
    ...mapActions([
      'updateUserInfo',
    ]),
    handleHide() {
      this.addressId = null;
    },
    handleAddAddress() {
      this.$refs.AddAddressModal.open({
        addressLine1: '',
        addressLine2: '',
        suburb: '',
        state: '',
        postCode: '',
      }, -1);
    },
    handleAddAddressSuccess({ address, index }) {
      const newAddresses = [...this.userInfo.addresses];

      if (index >= 0) {
        newAddresses[index] = address;
      } else {
        newAddresses.push(address);
      }

      this.setStoreData('userInfo', 'addresses', newAddresses);
      this.updateUserInfo().then(() => {
        this.addressId = this.userInfo.addresses[this.userInfo.addresses.length - 1].id;
      }).catch((error) => {
        NotificationService.negative(error.message);
      });
    },
    formattedAddress(address) {
      return getFormattedAddress(address);
    },
    // handleNewAddress() {
    //   this.routerPush('/account/my-addresses');
    // },
    getJobTypeSublabel(jobType) {
      if (this.isPayg || this.hasUnlimitedAccess) return '';

      if (jobType === c.jobType.babysitting) {
        if (this.cantPostBabysittingJob) return '+ $20 per job post';
      } else if (jobType === c.jobType.primarySchoolTutoring) {
        if (this.cantPostPrimarySchoolTutoringJob) return '+ $60 per job post';
      } else if (jobType === c.jobType.highSchoolTutoring) {
        if (this.cantPostHighSchoolTutoringJob) return '+ $95 per job post';
      } else if (jobType === c.jobType.auPair) {
        if (this.cantPostAuPairJob) return '+ $145 per job post';
      } else if (this.cantPostRecurringChildcareJob) return '+ $60 per job post';

      return '- unlimited posts';
    },
    hasErrorInCurrentStep() {
      const hasError = this[`step${this.currentStep}HasError`];
      if (hasError) {
        if (this.currentStep === 1) {
          NotificationService.warning('Please select job type.');

          if (this.job.jobType === c.jobType.beforeAndAfterSchoolCare) {
            if (!this.isBeforeSchoolCare && !this.isAfterSchoolCare) {
              NotificationService.warning('Please choose Before and/or After School Care.');
            }
          }
        } else if (this.currentStep === 2) {
          NotificationService.warning('Please select job address.');
        } else if (this.currentStep === 3) {
          NotificationService.warning('Please select candidate selection option.');
        }
      }
      return hasError;
    },
    handleNextStep() {
      if (this.hasErrorInCurrentStep()) {
        return;
      }

      if (this.currentStep === 1 && (this.jobType === c.jobType.beforeAndAfterSchoolCare)) {
        if (!this.isBeforeSchoolCare && !this.isAfterSchoolCare) {
          NotificationService.warning('Please choose Before and/or After School Care.');
          return;
        }
      }

      // if (this.currentStep === 2 && (this.jobType === c.jobType.babysitting)) {
      //   this.handleSubmit();
      //   return;
      // }

      if (this.currentStep === 2 && this.mode === 'edit') {
        this.handleSubmit();
        return;
      }

      this.currentStep++;
    },
    handlePreviousStep() {
      this.currentStep--;
    },
    handleSubmit() {
      if (this.hasErrorInCurrentStep()) {
        return;
      }

      this.next();
    },
    next() {
      if (!this.addressId || this.addressId === -1) {
        NotificationService.warning('Please select job address.');
        return;
      }

      if (this.mode === 'add') {
        this.nextAddJob();
        return;
      }

      this.nextEditJob();
    },
    nextEditJob() {
      const updatedJobType = this.job.jobType;
      let route;
      let moduleName;
      let filterKey;
      if (updatedJobType === c.jobType.babysitting) {
        moduleName = 'postOneOffJob';
        filterKey = 'postOneOffJob';
        route = '/post-one-off-job';
      } else if (updatedJobType === c.jobType.auPair) {
        moduleName = 'postAuPairJob';
        filterKey = 'postAuPairJob';
        route = '/post-au-pair-job';
      } else if (updatedJobType === c.jobType.primarySchoolTutoring || updatedJobType === c.jobType.highSchoolTutoring) {
        moduleName = 'postTutoringJob';
        filterKey = 'postHomeTutoringJob';
        route = '/post-tutoring-job';
      } else {
        moduleName = 'postRecurringJob';
        filterKey = 'postRecurringJob';
        route = '/post-recurring-job';
      }

      const addressPayload = {
        module: `${moduleName}.jobDetails`,
        propPath: 'addressId',
        value: this.addressId,
      };
      this.$store.commit(mutationTypes.UPDATE_STORE_DATA, addressPayload);

      const address = this.userInfo.addresses.find((a) => a.id === this.addressId);
      const filterPayload = {
        filterType: filterKey,
        filterKey: 'address',
        value: [address.longitude, address.latitude],
      };
      this.$store.commit(`filters/${mutationTypes.UPDATE_FILTER_OPTION}`, filterPayload);

      this.$store.dispatch('filters/resetFilters');

      this.routerReplace({ path: route });
    },
    nextAddJob() {
      let updatedJobType = this.job.jobType;
      let jobTypeFilter = [this.job.jobType];

      if (updatedJobType === c.jobType.unspecified) {
        NotificationService.warning('Please select job type.');
        return;
      }

      if (updatedJobType === c.jobType.beforeAndAfterSchoolCare) {
        if (!this.isBeforeSchoolCare && !this.isAfterSchoolCare) {
          NotificationService.warning('Please choose Before and/or After School Care.');
          return;
        }

        updatedJobType = 0;
        jobTypeFilter = [];
        if (this.isBeforeSchoolCare) {
          updatedJobType += c.jobType.beforeSchoolCare;
          jobTypeFilter.push(c.jobType.beforeSchoolCare);
        }

        if (this.isAfterSchoolCare) {
          updatedJobType += c.jobType.afterSchoolCare;
          jobTypeFilter.push(c.jobType.afterSchoolCare);
        }
      }

      // if (updatedJobType === c.jobType.homeTutoring) {
      //   NotificationService.warning('Please choose a School Group.');
      //   return;
      // }

      let route;
      let moduleName;
      let filterKey;
      if (updatedJobType === c.jobType.babysitting) {
        moduleName = 'postOneOffJob';
        filterKey = 'postOneOffJob';
        route = '/post-one-off-job';
      } else if (updatedJobType === c.jobType.auPair) {
        moduleName = 'postAuPairJob';
        filterKey = 'postAuPairJob';
        route = '/post-au-pair-job';
      } else if (updatedJobType === c.jobType.primarySchoolTutoring || updatedJobType === c.jobType.highSchoolTutoring) {
        moduleName = 'postTutoringJob';
        filterKey = 'postHomeTutoringJob';
        route = '/post-tutoring-job';

        // let updatedPaymentType;
        // if (updatedJobType === c.jobType.primarySchoolTutoring) {
        //   updatedPaymentType = c.userPaymentType.singlePrimarySchoolTutoringJob;
        // } else {
        //   updatedPaymentType = c.userPaymentType.singleHighSchoolTutoringJob;
        // }

        // const payload = {
        //   module: `${moduleName}.jobDetails`,
        //   propPath: 'userPaymentType',
        //   value: updatedPaymentType,
        // };
        // this.$store.commit(mutationTypes.UPDATE_STORE_DATA, payload);

        const jobTypePayload = {
          filterType: 'postOnlineTutoringJob',
          filterKey: 'jobTypes',
          value: jobTypeFilter,
        };
        this.$store.commit(`filters/${mutationTypes.UPDATE_FILTER_OPTION}`, jobTypePayload);
      } else {
        moduleName = 'postRecurringJob';
        filterKey = 'postRecurringJob';
        route = '/post-recurring-job';
      }

      const payload = {
        module: `${moduleName}.jobDetails`,
        propPath: 'jobType',
        value: updatedJobType,
      };
      this.$store.commit(mutationTypes.UPDATE_STORE_DATA, payload);

      const managedByPayload = {
        module: `${moduleName}.jobDetails`,
        propPath: 'managedBy',
        value: this.managedBy,
      };
      this.$store.commit(mutationTypes.UPDATE_STORE_DATA, managedByPayload);

      const addressPayload = {
        module: `${moduleName}.jobDetails`,
        propPath: 'addressId',
        value: this.addressId,
      };
      this.$store.commit(mutationTypes.UPDATE_STORE_DATA, addressPayload);

      const jobTypePayload = {
        filterType: filterKey,
        filterKey: 'jobTypes',
        value: jobTypeFilter,
      };
      this.$store.commit(`filters/${mutationTypes.UPDATE_FILTER_OPTION}`, jobTypePayload);

      const address = this.userInfo.addresses.find((a) => a.id === this.addressId);
      const filterPayload = {
        filterType: filterKey,
        filterKey: 'address',
        value: [address.longitude, address.latitude],
      };
      this.$store.commit(`filters/${mutationTypes.UPDATE_FILTER_OPTION}`, filterPayload);

      this.$store.dispatch('filters/resetFilters');

      this.routerReplace({ path: route });
    },
  },
  mounted() {
    if (this.$route.query.jobType) {
      this.jobType = parseInt(this.$route.query.jobType, 10);
    }

    if (this.$route.query.mode === 'edit') {
      this.currentStep = 2;
      this.mode = 'edit';
      this.addressId = null;
      if (this.$route.query.managedBy) {
        this.managedBy = parseInt(this.$route.query.managedBy, 10);
      }
    } else {
      this.addressId = this.userInfo.defaultAddress.id;
    }
  },
  beforeRouteEnter(to, from, next) {
    const undefinedState = typeof store.state.postJob.jobDetails.data === 'undefined';
    if (to.query.mode === 'edit') {
      store.dispatch('resetStoreStructure', { propPath: 'postJob.jobDetails' });
    } else if (to.query.reset || undefinedState) {
      store.dispatch('resetStoreStructure', { propPath: 'postJob.jobDetails' });
      store.dispatch('resetStoreStructure', { propPath: 'postOneOffJob.jobDetails' });
      store.dispatch('resetStoreStructure', { propPath: 'postRecurringJob.jobDetails' });
      store.dispatch('resetStoreStructure', { propPath: 'postAuPairJob.jobDetails' });
      store.dispatch('resetStoreStructure', { propPath: 'postTutoringJob.jobDetails' });
    }

    next();
  },
};
</script>
