<template>
  <tr>
    <td class="text-left">
      <span class="text-bold">{{`${getDayLabel(shift.dayOfWeek)}`}}</span>
    </td>
    <td class="text-center">
      {{`${formatTime(shift.jobStartTime)} to ${formatTime(shift.jobEndTime)}`}}
      <span class="text-red">{{lowHoursNote(shift)}}</span>
    </td>
    <td class="text-center">{{getShiftHours(shift)}}</td>
    <td class="text-center" v-if="editable">
      <q-input
        dense
        type="number"
        :model-value="shift.hourlyPrice"
        hide-bottom-space
        bottom-slots
        @update:model-value="(value) => handleHourlyPriceChange(value, index, shift.dayOfWeek, shift.shiftIndex)"
        :error="v$.hourlyPrice.$error"
        error-message=""
        placeholder="00"
        :style="v$.hourlyPrice.$error ? 'width: 60px; margin: 0 5px' : 'width: 30px; margin: 0 5px'"
        :suffix="lowHoursNote(shift) ? '*' : ''"
        class="hourly-price-editor"
      />
    </td>
    <td class="text-center" v-else>
      {{'$' + getHourlyPrice(shift)}}
    </td>
    <td class="text-center text-right">{{'$' + shift.price}}</td>
  </tr>
</template>

<script>
import useVuelidate from '@vuelidate/core';
import { required, between } from '@vuelidate/validators';
import { getDayOfWeekAbbrevText, formatTime, getTimeDifference } from '../utils/helpers';
import * as types from '../store/mutation-types';

export default {
  name: 'WeeklyScheduleSummaryRow',
  data() {
    return {
      hourlyPrice: this.shift.hourlyPrice,
    };
  },
  props: ['shift', 'storeModuleName', 'editable', 'minHours'],
  setup() {
    return {
      v$: useVuelidate(),
    };
  },
  validations() {
    return {
      hourlyPrice: {
        required,
        between: between(1, 1000),
      },
    };
  },
  methods: {
    getHourlyPrice(shift) {
      if (shift.hourlyPrice) return shift.hourlyPrice;

      const hours = this.getShiftHours(shift);

      return shift.price / hours;
    },
    getShiftHours(shift) {
      if (shift.hours) return shift.hours;

      const hours = getTimeDifference(shift.jobStartTime, shift.jobEndTime);
      return hours;
    },
    lowHoursNote(shift) {
      if (!this.minHours) return '';

      return shift.hours < this.minHours ? '*' : '';
    },
    handleHourlyPriceChange(value, index, day, shiftIndex) {
      this.hourlyPrice = value;

      const mutation = {
        dayOfWeek: day,
        shiftIndex,
        propName: 'hourlyPrice',
        value,
      };
      this.$store.commit(`${this.storeModuleName}/${types.UPDATE_SHIFT_DATA}`, mutation);

      this.v$.$touch();
    },
    formatTime(time) {
      return formatTime(time);
    },
    getDayLabel(day) {
      return getDayOfWeekAbbrevText(day);
    },
  },
  computed: {
    lowHoursNotice() {
      if (!this.minHours) return false;

      const hasLowHourShifts = this.weeklyScheduleEntries.find((entry) => entry.hours < this.minHours);

      return hasLowHourShifts;
    },
  },
};
</script>
<style lang="sass">
.hourly-price-editor
  .q-field__suffix
    color: red
</style>
