<template>
  <div class="layout-padding">
    <div class="text-center" v-if="hasGuardian">
      Your guardian request has been sent, tap Next to continue.
    </div>
    <add-guardian v-else
      v-on:addGuardianSuccess="handleAddGuardianSuccess"
      v-on:addGuardianFailure="handleAddGuardianFailure"
    />
  </div>
</template>

<script>
import { mapState } from 'vuex';
import AddGuardian from 'components/AddGuardian';
import NotificationService from '../../../utils/notification-service';
import beforeLeaveStep from '../before-leave-step';
import * as types from '../../../store/mutation-types';
import store from '../../../store';

export default {
  name: 'ProfileWizardGuardianApproval',
  components: { AddGuardian },
  data() {
    return {};
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
    }),
    hasGuardian() {
      return this.userInfo.defaultGuardian;
    },
  },
  methods: {
    handleAddGuardianSuccess() {
      this.$store.commit(`profileWizard/${types.UPDATE_STEP_STATUS}`, { complete: true });
    },
    handleAddGuardianFailure(err) {
      NotificationService.negative(err.message);
    },
  },
  beforeRouteLeave(to, from, next) {
    beforeLeaveStep(this, to, from, next);
  },
  beforeRouteEnter(to, from, next) {
    // Guardian approval step should only be shown if profile completeness is 100%.
    const profileCompletenss = store.getters.userInfo.profileCompleteness;

    if (profileCompletenss === 95) {
      next();
    } else {
      next({ path: '/dashboard', replace: true });
    }
  },
};
</script>
