<template>
  <q-layout view="hHh Lpr lFf">
    <q-header elevated>
      <q-toolbar class="bg-red text-center" v-if="$store.state.appInfo.adminMode">
        <q-toolbar-title>
          Admin Mode
        </q-toolbar-title>
      </q-toolbar>

      <q-toolbar class="bg-red text-white text-center text-bold" v-if="displayNotificationsBanner">
        <q-toolbar-title class="no-padding no-margin text-center">
          <q-btn label="Turn On" outline rounded @click="routerPush('/account/notifications')"/>
          <span v-if="isClient" class="q-mx-sm text-body2">Job Posts</span>
          <span v-if="isHelper" class="q-mx-sm text-body2">Job Invites</span>
          <!-- <router-link class="text-white text-body2 q-ml-xs" to="/account/notifications" replace>Job Invites</router-link> -->
        </q-toolbar-title>
      </q-toolbar>

      <!-- <q-toolbar class="bg-red text-white text-center text-bold" v-if="isApp && hasCodeUpdates">
        <q-toolbar-title class="no-padding no-margin text-center">
          <small>New Juggle St Update!</small>  <q-btn @click="installCodeUpdate" class="q-ml-md text-primary bg-white" label="Go" />
        </q-toolbar-title>
      </q-toolbar> -->

      <q-toolbar class="bg-red text-white text-center text-bold" v-if="isApp && hasAppUpdates">
        <q-toolbar-title>
          <q-btn outline rounded @click="navigateToAppStore" :no-ripple="true">
            Update Your App
          </q-btn>
        </q-toolbar-title>
      </q-toolbar>

      <q-banner class="bg-red text-white text-center text-bold" v-if="isWeb && displayAppStoreBanner">
        Download Juggle St App!
        <q-btn v-if="isSupportedCountry" type="a" :href="appStoreLink" target="_blank" class="q-ml-md text-primary bg-white" label="Go" />
        <q-btn v-else @click="handleDownloadApp" class="q-ml-md text-primary bg-white" label="Go" />
      </q-banner>

      <q-toolbar class="bg-red text-center" v-if="isHelper && isApproved && jobTypesUpdateRequired">
        <q-toolbar-title>
          <q-btn outline rounded @click="routerPush('/account/account-summary')"  :no-ripple="true">
            Select Job Types
          </q-btn>
        </q-toolbar-title>
      </q-toolbar>

      <q-toolbar style="background-color: #FF9900" v-if="displayBanner">
        <q-toolbar-title class="no-padding no-margin text-center">
          <span v-if="isFamily" class="text-body2">Going away? Add new addresses to your job posts!</span>
          <span v-if="isHelper" class="text-body2">Going away? Add new addresses</span>
          <router-link v-if="isHelper" class="text-white text-body2 q-ml-xs" to="/account/my-addresses" replace>here!</router-link>
        </q-toolbar-title>
      </q-toolbar>

      <q-toolbar class="bg-red text-center" v-if="notificationsPage && requiresCheck">
        <q-toolbar-title>
          <q-btn outline rounded @click="handleMissingCheck"  :no-ripple="true">
            WWCC Alert!
          </q-btn>
        </q-toolbar-title>
      </q-toolbar>
      <!-- <q-toolbar class="bg-red text-center" v-else-if="displayMembership && notificationsPage && requiresMembership">
        <q-toolbar-title>
          <q-btn outline rounded @click="routerPush('/account/helper-membership')"  :no-ripple="true">
            Membership Required
          </q-btn>
        </q-toolbar-title>
      </q-toolbar> -->
      <q-toolbar class="bg-red text-center" v-else-if="notificationsPage && isHelper && isApproved && profileUpdateRequired && !jobTypesUpdateRequired">
        <q-toolbar-title>
          <q-btn outline rounded @click="routerPush('/account/account-summary')"  :no-ripple="true">
            Profile Update Required
          </q-btn>
        </q-toolbar-title>
      </q-toolbar>
      <q-toolbar class="bg-red text-center" v-else-if="notificationsPage && requiresTutoringUpdate">
        <q-toolbar-title>
          <q-btn outline rounded @click="routerPush('/account/tutoring-preferences')" :no-ripple="true">
            Earn Money Tutoring Online
          </q-btn>
        </q-toolbar-title>
      </q-toolbar>

      <q-toolbar class="bg-primary">
        <q-btn
          flat
          dense
          round
          icon="menu"
          aria-label="Menu"
          @click="leftDrawerOpen = !leftDrawerOpen"
          v-if="displayAction('menu')"
        />

        <q-btn
          flat
          dense
          round
          icon="keyboard_arrow_left"
          aria-label="Back"
          @click="routerBack"
          v-if="displayAction('back')"
        />

        <q-toolbar-title class="text-center">
          <div>
            <span v-if="!dashboardRoute">
                {{pageTitle}}
            </span>
            <span v-if="dashboardRoute">
              <img src="~assets/js-logo-full-white.png" class="header-logo"/>
            </span>
          </div>
          <div v-if="pageSubtitle" class="text-subtitle2">
              {{pageSubtitle}}
          </div>
        </q-toolbar-title>

        <q-btn
          flat
          dense
          round
          icon="search"
          aria-label="Search"
          @click="rightDrawerOpen = !rightDrawerOpen"
          v-if="displayAction('search-action')"
        />

        <q-btn
          flat
          dense
          round
          icon="account_circle"
          aria-label="Account"
          @click="routerPush('/account')"
          v-if="displayAction('account')"
        />

        <q-btn
          rounded
          outline
          @click="routerBack"
          v-if="displayAction('save-action')"
          :no-ripple="true">
          Save
        </q-btn>
      </q-toolbar>
    </q-header>

    <q-drawer
      v-model="leftDrawerOpen"
      show-if-above
      :width="250"
      :breakpoint="1250"
      elevated
      class="bg-grey-1"
    >
      <q-list>
        <EssentialLink title="Home" icon="home" link="/dashboard" />
        <!-- <EssentialLink title="App Info" icon="settings" link="/app-info" /> -->
        <EssentialLink v-if="!isTablet" title="Get Started" icon="touch_app" link="/onboarding" />
        <EssentialLink title="How It Works" icon="info" link="/public/family-how-it-works" v-if="isFamily" />
        <EssentialLink title="How It Works" icon="info" link="/public/helper-how-it-works" v-if="isHelper" />
        <EssentialLink title="Pricing" icon="attach_money" link="/account/pricing" v-if="isFamily"/>
        <EssentialLink title="Pricing" icon="attach_money" link="/public/helper-pricing" v-if="isHelper"/>
        <EssentialLink title="Favourite Requests" icon="favorite_border" link="/connections/invitations"  v-if="isFamily && isApproved" />
        <EssentialLink title="Connection Requests" icon="link" link="/connections/invitations"  v-if="isHelper && isApproved" />
        <EssentialLink title="Post Job" icon="add" link="/post-job?reset=1"  v-if="isFamily && isApproved" />
        <EssentialLink title="My Jobs" icon="receipt_long" link="/families/all-jobs" v-if="isFamily && isApproved"/>
        <EssentialLink title="My Jobs" icon="receipt_long" link="/helpers/upcoming-jobs" v-if="isHelper && isApproved" />
        <!-- <EssentialLink title="Connection Requests" icon="mail" link="/connections/invitations" v-if="isHelper" /> -->
        <EssentialLink title="Chat" icon="chat_bubble_outline" link="/conversations" v-if="isApproved" />
        <EssentialLink title="Contact Us" icon="message" v-on:click="handleContactUs"/>
        <EssentialLink title="Learn More" icon="emoji_objects" link="/learn-more" />
      </q-list>
    </q-drawer>

    <q-drawer
      v-model="rightDrawerOpen"
      side="right"
      bordered
      overlay
      class="bg-grey-1"
    >
      <FiltersPanel :filterType="filterKey" :jobType="jobType"/>
    </q-drawer>

    <q-page-container class="">
      <router-view />
    </q-page-container>
  </q-layout>
</template>

<script>
import EssentialLink from 'components/EssentialLink';
import FiltersPanel from 'components/FiltersPanel';
import { openURL } from 'quasar';
import { mapState } from 'vuex';
// import * as http from '../api/http';
import intercomService from '../utils/intercom-service';
import c from '../utils/constants';
import errorHandler from '../utils/error-handler';

export default {
  name: 'MainLayout',
  components: { EssentialLink, FiltersPanel },
  data() {
    return {
      displayMembership: false,
      displayBanner: false,
    };
  },
  computed: {
    ...mapState({
      appInfo: (state) => state.appInfo,
      userInfo: (state) => state.userInfo.data,
      pushNotifications: (state) => state.pushNotifications,
    }),
    hasCodeUpdates() {
      return this.appInfo.hasCodeUpdates;
    },
    hasAppUpdates() {
      return this.appInfo.hasAppUpdates;
    },
    displayAppStoreBanner() {
      return this.userInfo.appSettings.enableAppBanner && this.userInfo.notificationMethod === c.notificationMethod.textMessage;
    },
    isSupportedCountry() {
      const languages = navigator.languages;
      const isSupported = languages.includes('en-AU') || languages.includes('en-NZ')
        || languages.includes('en-au') || languages.includes('en-nz');
      return isSupported;
    },
    displayNotificationsBanner() {
      if (this.$route.path === '/account/notifications') return false;

      let displayPushBanner = this.userInfo.appSettings && this.userInfo.appSettings.enableAppNotificationChecks && this.isApp && this.isApproved;
      if (!displayPushBanner) return false;

      if (this.pushNotifications.permission === null || this.pushNotifications.enabled === null) return false;

      displayPushBanner = this.pushNotifications.permission !== 'granted' || !this.pushNotifications.enabled;
      return displayPushBanner;
    },
    appStoreLink() {
      if (this.isAndroidDevice) {
        return 'market://details?id=com.jugglestreet.app';
      }
      return 'https://apps.apple.com/app/id1586742271';
    },
    requiresMembership() {
      return this.isHelper && this.isApproved && this.userInfo.membershipType === c.membershipType.free && this.userInfo.ageInYears >= 18;
    },
    invalidPaymentInfo() {
      return this.isApproved && this.userInfo.membershipType === c.membershipType.subscription && this.userInfo.paymentInfo.invalidPaymentInfo;
    },
    leftDrawerOpen: {
      get() {
        return this.$store.state.pageInfo.leftDrawerOpen;
      },
      set(value) {
        this.$store.dispatch('toggleLeftDrawer', value);
      },
    },
    rightDrawerOpen: {
      get() {
        return this.$store.state.pageInfo.rightDrawerOpen;
      },
      set(value) {
        this.$store.dispatch('toggleRightDrawer', value);
      },
    },
    dashboardRoute() {
      return this.$route.path === '/dashboard'
       || this.$route.path === '/'
       || this.$route.path === '/dashboard-tutors';
    },
    notificationsPage() {
      return this.$route.path.indexOf('account') === -1 || this.$route.path.indexOf('chat') !== -1;
    },
    isProfileIncomplete() {
      if (this.isFamily) {
        return this.isPending;
      }

      if (this.isHelper && !this.userInfo.requiresGuardianApproval) {
        return this.isPending;
      }

      return this.isPending && !this.userInfo.defaultGuardian;
    },
    filterKey() {
      return this.$store.getters['filters/filterKey'];
    },
    jobType() {
      const filterInfo = this.$store.getters['filters/filterInfo'];
      if (!filterInfo) return -1;

      const { jobTypes } = filterInfo;
      if (jobTypes && jobTypes.length > 0) {
        return jobTypes[0];
      }

      return -1;
    },
    profileUpdateRequired() {
      let nonJsRefs = 0;

      this.userInfo.vouches.forEach((v) => {
        if (v.referenceType !== c.vouchReferenceType.juggleStreet) {
          nonJsRefs++;
        }
      });

      return (!this.userInfo.aboutMe || this.userInfo.aboutMe.length < 100) || (!this.userInfo.nationality)
        || ((this.userInfo.certificates.length === 0 || this.userInfo.certificates[0].certificateStatus === c.certificateStatus.invalid || this.userInfo.certificates[0].certificateStatus === c.certificateStatus.expired)
          && this.userInfo.country === 'au' && this.userInfo.ageInYears >= 18)
        || ((this.userInfo.interestedInAuPairJobs || (this.userInfo.interestedInJobTypes & c.jobType.auPair) === c.jobType.auPair) && (this.userInfo.provider.auPairCategory === 0 || nonJsRefs.length < 1))
        || (this.userInfo.paymentInfo.interestedInCashPayments === null || this.userInfo.paymentInfo.interestedInBankTransfers === null)
        || ((this.userInfo.interestedInTutoringJobs || (this.userInfo.interestedInJobTypes & c.jobType.primarySchoolTutoring) === c.jobType.primarySchoolTutoring || (this.userInfo.interestedInJobTypes & c.jobType.highSchoolTutoring) === c.jobType.highSchoolTutoring)
        && (this.userInfo.provider.tutoringCategory === 0));
    },
    jobTypesUpdateRequired() {
      return this.userInfo.interestedInJobTypes === 0;
    },
    requiresTutoringUpdate() {
      return this.isHelper && this.isApproved
        && (this.userInfo.interestedInTutoringJobs
        || (this.userInfo.interestedInJobTypes & c.jobType.primarySchoolTutoring) === c.jobType.primarySchoolTutoring
        || (this.userInfo.interestedInJobTypes & c.jobType.highSchoolTutoring) === c.jobType.highSchoolTutoring)
        && (this.userInfo.provider.interestedInFaceToFaceTutoring === null
        || this.userInfo.provider.interestedInOnlineTutoring === null);
    },
    requiresCheck() {
      // Missing check or invalid or expired.
      return this.isHelper && this.isApproved
        && (this.userInfo.certificates.length === 0 || this.userInfo.certificates[0].certificateStatus === c.certificateStatus.invalid || this.userInfo.certificates[0].certificateStatus === c.certificateStatus.expired)
        && this.userInfo.country === 'au' && this.userInfo.ageInYears >= 18;
    },
  },
  methods: {
    openURL,
    navigate(url) {
      window.location = url;
    },
    handleMissingCheck() {
      this.$q.dialog({
        title: 'WWCC Alert!',
        message: 'A valid Working with Children Check (WWCC) is mandatry on Juggle Street. Go to the Certificates section of My Account and upload WWCC.',
        ok: {
          label: 'Upload',
          outline: true,
        },
        cancel: {
          label: 'Later',
          color: 'secondary',
          outline: true,
        },
      }).onOk(() => {
        this.routerPush('/account/my-certificates');
      }).onCancel(() => {});
    },
    handleDownloadApp() {
      const isSupported = this.isSupportedCountry;

      const logAppDownloads = false;
      if (logAppDownloads) {
        try {
          throw new Error('Handle Download App');
        } catch (err) {
          const data = {
            tags: {
              languages: navigator.languages.join(','),
              isSupportedCountry: isSupported,
            },
          };
          errorHandler.captureException(err, data);
        }
      }

      if (isSupported) {
        window.open(this.appStoreLink);
        return;
      }

      this.$q.dialog({
        title: 'Juggle St App Download',
        message: 'If you experience difficulties downloading the app, please ensure your App Store (Apple ID) or Play Store (Google Account) is set to Australia or New Zealand. Contact us for assistance.',
        ok: {
          label: 'Download',
          outline: true,
        },
        cancel: {
          label: 'Later',
          color: 'secondary',
          outline: true,
        },
      }).onOk(() => {
        window.open(this.appStoreLink);
      }).onCancel(() => {});
    },
    installCodeUpdate() {
      this.$store.dispatch('installCodeUpdate');
    },
    navigateToAppStore() {
      this.$store.dispatch('navigateToAppStore');
    },
    handleCompleteProfile() {
      if (this.isFamily) {
        this.routerPush('/client-profile-wizard');
      } else {
        this.routerPush('/helper-profile-wizard');
      }
    },
    routerPushAndHidePanel(route) {
      this.leftDrawerOpen = false;
      this.routerPush(route, false);
    },
    handleContactUs() {
      intercomService.show();
    },
    filterType() {
      const filterType = this.getRouteMetaProp('searchFilterType');
      if (filterType === 'nearMe') {
        return this.isFamily ? 'helpersNearMe' : 'familiesNearMe';
      }

      if (filterType === 'invitations') {
        return this.isFamily ? 'helperInvitations' : 'familyInvitations';
      }

      if (filterType === 'inviteMoreHelpers') {
        return '';
      }

      return '';
    },
  },
};
</script>
