<template>
  <div class="">
    <q-field
      :error="v$.email.$error"
      borderless
      label-slot
      stack-label
      hide-bottom-space
      readonly
      helper="You will use your email address to log in.">
      <template v-slot:label>
        <h5 v-if="isBusiness" class="text-primary text-wrap no-margin">
          Account Holder Setup
        </h5>
        <h5 v-else class="text-primary text-wrap no-margin">
          Account Setup
        </h5>
      </template>
      <template v-slot:control>
        <div class="full-width">
          <q-input v-model="email" placeholder="Email" type="email" />
        </div>
      </template>
    </q-field>
    <q-field
      borderless
      :error="v$.password.$error"
      hide-bottom-space
      error-message="Your password should contain at least 7 characters.">
      <template v-slot:control>
        <div class="full-width">
          <q-input v-model="password" placeholder="Password" :type="hidePassword ? 'password' : 'text'">
            <template v-slot:append>
              <q-icon
                :name="hidePassword ? 'visibility' : 'visibility_off'"
                class="cursor-pointer"
                @click="hidePassword = !hidePassword"
              />
            </template>
          </q-input>
        </div>
      </template>
    </q-field>
    <q-field
      borderless
      :error="v$.confirmPassword.$error"
      hide-bottom-space
      error-message="Passwords don't match.">
      <template v-slot:control>
        <div class="full-width">
          <q-input v-model="confirmPassword" placeholder="Confirm Password" :type="hidePassword ? 'password' : 'text'" >
            <template v-slot:append>
              <q-icon
                :name="hidePassword ? 'visibility' : 'visibility_off'"
                class="cursor-pointer"
                @click="hidePassword = !hidePassword"
              />
            </template>
          </q-input>
        </div>
      </template>
    </q-field>
    <q-field
      borderless
      hide-bottom-space
    >
      <template v-slot:control>
        <div class="">
          <q-input v-model="affiliateCode" placeholder="Join Code (Optional)"/>
          <div class="q-mt-sm">
            If you have a promotional or corporate code, enter it here.
          </div>
        </div>
      </template>
    </q-field>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import useVuelidate from '@vuelidate/core';
import {
  required, email, sameAs, minLength,
} from '@vuelidate/validators';
import c from '../../utils/constants';

export default {
  name: 'SignupStep3',
  props: ['currentStep'],
  data() {
    return {
      hidePassword: true,
    };
  },
  setup() {
    return {
      v$: useVuelidate(),
    };
  },
  validations() {
    return {
      email: {
        required,
        email,
      },
      password: {
        required,
        minLength: minLength(7),
      },
      confirmPassword: {
        required,
        sameAsPassword: sameAs(this.password),
      },
    };
  },
  computed: {
    ...mapState({
      accountInfo: (state) => state.signup.userDetails.data,
    }),
    storeModuleName() {
      return 'signup.userDetails';
    },
    isBusiness() {
      return this.accountInfo.clientType === c.clientType.business || this.accountInfo.providerType === c.providerType.business;
    },
    email: {
      get() {
        return this.accountInfo.email;
      },
      set(value) {
        this.setProperty('email', value);
      },
    },
    password: {
      get() {
        return this.accountInfo.password;
      },
      set(value) {
        this.setProperty('password', value);
      },
    },
    confirmPassword: {
      get() {
        return this.accountInfo.confirmPassword;
      },
      set(value) {
        this.setProperty('confirmPassword', value);
      },
    },
    affiliateCode: {
      get() {
        return this.accountInfo.affiliateCode;
      },
      set(value) {
        this.setProperty('affiliateCode', value);
      },
    },
  },
  methods: {
    validate() {
      this.v$.$touch();
      const hasError = this.v$.$error;
      return hasError;
    },
  },
  watch: {
  },
};
</script>
<style lang="sass">

</style>
