<template>
  <q-page class="post-au-pair-job-step-6">
    <div class="layout-padding">
      <q-field
        borderless
        readonly
        hide-bottom-space
      >
        <template v-slot:prepend>
          <q-icon name="date_range" />
        </template>
        <template v-slot:control>
          <div>
            <span v-if="addressLabel" class="text-primary text-bold">{{addressLabel}}<br/></span>
            {{getJobDateText()}}
          </div>
        </template>
      </q-field>

      <q-field
        borderless
        readonly
        hide-bottom-space
      >
        <template v-slot:prepend>
          <q-icon name="home" />
        </template>
        <template v-slot:control>
          <div class="">
            {{getAddress()}}
            <br/>
            Working {{job.jobSettings.daysPerWeek}} days per week – {{job.jobSettings.hoursPerWeek}} hours total per week
          </div>
        </template>
      </q-field>

      <q-field
        label="Job Description"
        borderless
        readonly
        hide-bottom-space
      >
        <template v-slot:prepend>
          <q-icon name="assignment" />
        </template>
        <template v-slot:control>
          <div class="full-width q-mt-md">
            <div v-for="(text, index) in job.specialInstructions.split('\n')" :key="index">
              {{text}}&nbsp;
            </div>
          </div>
        </template>
      </q-field>

      <filters-panel mode="jobDetails" filterType="postAuPairJob" :jobType="job.jobType" v-if="job.managedBy === types.managedBy.system" />

      <q-field
        borderless
        readonly
        label="Payment">
        <template v-slot:prepend>
          <q-icon name="monetization_on" />
        </template>
        <template v-slot:control>
          <div class="full-width q-mt-md">
            Weekly allowance: <span>${{job.price}}</span>
            <br/>
            Payment method: <span>{{getHelperPaymentMethod()}}</span>
          </div>
        </template>
      </q-field>

      <q-field
        label="Invited Au Pairs"
        borderless
        readonly
        v-if="selectedApplicants.length > 0"
      >
        <template v-slot:prepend>
          <q-icon name="people" />
        </template>
      </q-field>
      <div class="full-width invited-carers" style="margin-bottom: 50px">
        <helper-profile-card
          v-for="helper in selectedApplicants"
          :key="helper.id"
          :helper="helper"
          :displayDistance="true"
          mode="read-only"
        />
      </div>

      <q-btn
        big
        color="accent"
        class="full-width fixed-bottom"
        @click="handlePostJob">
          Post Job
      </q-btn>
    </div>
  </q-page>
</template>

<script>
import { date } from 'quasar';
import { mapState } from 'vuex';
import HelperProfileCard from 'components/HelperProfileCard';
import FiltersPanel from 'components/FiltersPanel';
import { beforeRouteEnter, beforeLeaveStep, postJob } from './multistep-helper';
import {
  getJobPaymentMethodText, getDayOfWeekText, formatTime, getFormattedAddress,
} from '../../utils/helpers';
import c from '../../utils/constants';

export default {
  name: 'PageAuPairJobStep6',
  components: { HelperProfileCard, FiltersPanel },
  data() {
    return {
      types: {
        managedBy: c.managedBy,
      },
    };
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
      job: (state) => state.postAuPairJob.jobDetails.data,
      meta: (state) => state.postAuPairJob.jobDetails.meta,
    }),
    selectedApplicants() {
      return this.meta.selectedApplicants;
    },
    addressLabel() {
      const address = this.userInfo.addresses.find((a) => a.id === this.job.addressId);
      if (!address) return '';

      return address.addressLabel;
    },
  },
  methods: {
    validate() {
      return true;
    },
    formatTime(time) {
      return formatTime(time);
    },
    getAddress() {
      const address = this.userInfo.addresses.find((a) => a.id === this.job.addressId);
      return getFormattedAddress(address);
    },
    getJobDateText() {
      const {
        jobDate,
        jobStartType,
        jobStartDurationType,
        durationToStart,
        durationType,
        duration,
      } = this.job;

      let text = 'You are looking to fill an Au Pair role starting ';

      if (Number(jobStartType) === c.jobStartType.duration) {
        text += `within ${durationToStart} `;
        if (Number(jobStartDurationType) === c.durationType.weeks) {
          text += 'weeks';
        } else {
          text += 'months';
        }
      } else {
        text += `on ${date.formatDate(jobDate, 'ddd D MMM YY')}`;
      }

      text += `, for ${duration} `;
      if (durationType === c.durationType.weeks) {
        text += 'weeks.';
      } else {
        text += 'months.';
      }

      return text;
    },
    getHelperPaymentMethod() {
      return getJobPaymentMethodText(this.job.helperPaymentMethod);
    },
    getDayLabel(day) {
      return getDayOfWeekText(day);
    },
    getWeeklyTotal() {
      const { weeklyScheduleEntries } = this.meta;

      const weeklyTotal = weeklyScheduleEntries.reduce((totalAcc, entry) => {
        const shiftTotal = entry.shifts.reduce((shiftAcc, shift) => {
          shiftAcc += shift.price;
          return shiftAcc;
        }, 0);
        totalAcc += shiftTotal;
        return totalAcc;
      }, 0);
      return weeklyTotal;
    },
    handlePostJob() {
      postJob(this.$router);
    },
  },
  beforeRouteEnter(to, from, next) {
    beforeRouteEnter(to, from, next);
  },
  beforeRouteLeave(to, from, next) {
    beforeLeaveStep(this, to, from, next);
  },
};
</script>
<style lang="sass">
.post-au-pair-job-step-6
  .schedule-table
    margin-bottom: 1rem
</style>
