import { Loading, QSpinnerGears } from 'quasar';
import errorHandler from './error-handler';

// const defaultOptions = {
//   spinner: QSpinnerGears,
//   messageColor: '#00ff00',
//   spinnerSize: 50,
//   spinnerColor: '#f034a3',
//   delay: 100,
//   textShadow: 'none',
// };

const logLoaderErrors = false;
const showDelay = 400;
const hideDelay = 0;

const defaultOptions = {
  spinner: QSpinnerGears,
  html: true,
  message: '<img src="/js-loading-animation.gif" style="height: 150px; mix-blend-mode: multiply;" />',
  delay: showDelay,
  textShadow: 'none',
  backgroundColor: 'white',
};

const loadingLimitInSeconds = 30;
const loadingLimitinMilliSecoonds = loadingLimitInSeconds * 1000;

const skipRoutes = ['/account/about-me',
  '/account/profile-video',
  '/account/my-family',
  '/account/my-business',
  '/helper-profile-wizard/profile-photo',
  '/client-profile-wizard/client-photo'];

let activeSince = null;
let timerId = null;

const checkLoader = () => {
  const route = window.location.hash.substring(1);
  if (skipRoutes.indexOf(route) !== -1) return;

  if (activeSince != null) {
    const seconds = (new Date().getTime() - activeSince.getTime()) / 1000;
    if (seconds >= loadingLimitInSeconds && logLoaderErrors) {
      try {
        const error = 'Loader is active for too long.';
        console.log(error);
        throw new Error(error);
      } catch (e) {
        errorHandler.captureException(e);
      }

      // setTimeout(() => {
      //   window.location.href = 'statics/error.html';
      // }, 5000);
    }
  }
};

const loader = {
  show: () => {
    if (Loading.isActive) return;
    activeSince = new Date();
    timerId = setTimeout(checkLoader, loadingLimitinMilliSecoonds);
    Loading.show({ ...defaultOptions });
  },
  hide: () => {
    if (!Loading.isActive) return;

    activeSince = null;
    if (timerId) {
      clearTimeout(timerId);
      timerId = null;
    }
    if (hideDelay === 0) {
      Loading.hide();
    } else {
      setTimeout(() => {
        Loading.hide();
      }, hideDelay);
    }
  },
  isActive: () => Loading.isActive,
  loadingLimit: loadingLimitInSeconds,
  checkLoader,
};

export default loader;
