<template>
  <div>
    <q-tabs v-model="selectedTab" class="section-groups bg-secondary text-white shadow-2">
      <q-tab v-for="(action, index) in actions" :class="action.className" :key="index" @click="handleClick(action.url)" :alert="action.count > 0 ? 'light-red' : false" :icon="action.icon" :label="action.label" :name="action.name">
        <!-- <q-badge v-if="action.count > 0" color="red" floating>
          {{action.count}}
        </q-badge> -->
      </q-tab>

    </q-tabs>
  </div>
</template>

<script>
import { mapState } from 'vuex';

export default {
  name: 'JobActions',
  data() {
    return {
    };
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
      realtime: (state) => state.realtime,
    }),
    actions() {
      const { isClient } = this.userInfo;
      const { alerts } = this.realtime;
      let actions = [];

      if (isClient) {
        actions.push({
          url: '/families/all-jobs',
          label: 'My Jobs',
          icon: 'assignment',
          count: alerts.jobAlerts,
          className: 'col-4',
          name: 'my-jobs',
        });

        // actions.push({
        //   url: '/families/unrated-jobs',
        //   label: 'Rate Helpers',
        //   icon: 'star',
        //   count: alerts.unratedJobs,
        //   name: 'unratedJobs',
        // });

        actions.push({
          url: '/post-job?reset=1',
          label: 'Post a Job',
          icon: 'add_circle',
          count: 0,
          className: 'col-4',
          name: 'post-job',
        });

        // actions.push({
        //   url: '/families/archived-jobs',
        //   label: 'Job History',
        //   icon: 'assignment turned in',
        //   count: 0,
        //   name: 'archivedJobs',
        // });

        actions.push({
          url: '/conversations',
          label: 'Chat',
          icon: 'chat_bubble',
          count: alerts.chatAlerts,
          className: 'col-4',
          name: 'chat',
        });
      } else {
        actions = [
          {
            url: '/helpers/upcoming-jobs',
            label: 'Upcoming Jobs',
            icon: 'assignment',
            count: alerts.jobAlerts,
            className: 'col-4',
            name: 'upcomingJobs',
          },
          {
            url: '/helpers/unrated-jobs',
            label: 'Rate Families',
            icon: 'star',
            count: alerts.unratedJobs,
            className: 'col-4',
            name: 'unratedJobs',
          },
          {
            url: '/helpers/archived-jobs',
            label: 'Completed Jobs',
            icon: 'assignment_turned_in',
            count: 0,
            className: 'col-4',
            name: 'archivedJobs',
          },
        ];
      }

      return actions;
    },
    selectedTab: {
      get() {
        const action = this.actions.find((a) => a.url === this.$route.path);
        return action.name;
      },
      set() {

      },
    },
  },
  props: [],
  methods: {
    handleClick(url) {
      this.routerPush(url);
    },
    handlePostJob() {
      this.routerPush('/post-job?reset=1');
    },
  },
  mounted() {
    // TODO: Optimise.
    this.$store.dispatch('realtime/fetchPendingStatsDirect');
  },
};
</script>
