<template>
  <div class="layout-padding">
    <q-field
      borderless
      :error="v$.acceptedTerms.$error">
      <template v-slot:prepend>
        <q-icon name="verified_user" />
      </template>
      <template v-slot:control>
        <p>
          <table class="horizontal-separator">
            <tbody>
              <!-- <tr>
                <td>As a Helper on Juggle St you keep 100% of the money you earn, Juggle Street does not take a commission.</td>
              </tr>
              <tr>
                <td>Other job platforms typically take 15-30% of every dollar you earn!</td>
              </tr>
              <tr>
                <td>Juggle Street parents pay you directly at the end of each job, either in cash or via bank transfer.</td>
              </tr>
              <tr>
                <td>Your membership options will be displayed in the next step.</td>
              </tr> -->
              <tr>
                <td>{{notice}}</td>
              </tr>
            </tbody>
          </table>
        </p>
        <q-checkbox class="q-my-sm" v-model="question1" label="Respond to all job invitations, either Apply or Decline as soon as possible" />
        <q-checkbox class="q-my-sm" v-model="question2" label="Respond to all messages from parents" />
        <q-checkbox class="q-my-sm" v-model="question3" label="Turn up on time for all jobs and complete the job to the best of your ability" />
        <q-checkbox class="q-my-sm" v-model="question4" label="If you need to cancel a job do so through the Juggle St app, always provide notice" />
        <q-checkbox class="q-my-sm" v-model="question5" label="Comply with Juggle St Terms & Conditions" />
      </template>
    </q-field>
    <div class="text-center q-my-sm">
      <q-btn color="accent" @click="handleContinue">Continue</q-btn>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex';
import useVuelidate from '@vuelidate/core';
import { sameAs } from '@vuelidate/validators';
import NotificationService from '../../../utils/notification-service';
import beforeLeaveStep from '../before-leave-step';
import * as mutationTypes from '../../../store/mutation-types';

export default {
  name: 'ProfileWizardHelperResponsibilites',
  data() {
    const userInfo = this.$store.state.userInfo.data;
    const optInDefault = userInfo.networkResponsibilitiesAcceptanceDate !== null;
    return {
      question1: optInDefault,
      question2: optInDefault,
      question3: optInDefault,
      question4: optInDefault,
      question5: optInDefault,
    };
  },
  methods: {
    ...mapActions('profileWizard', [
      'getStep',
    ]),
    ...mapActions([
      'updateUserInfo',
    ]),
    handleContinue() {
      // const nextStep = this.meta.steps[this.meta.currentStep + 1];
      this.$store.dispatch('profileWizard/getStep', { forward: true }).then((nextStep) => {
        this.routerReplace({ path: `/helper-profile-wizard/${nextStep.path}` });
      });
    },
    setTerms() {
      if (!this.acceptedTerms) return;

      this.setProperty('networkResponsibilitiesAcceptanceDate', new Date());
      this.$store.commit(`profileWizard/${mutationTypes.UPDATE_STEP_STATUS}`, { complete: true });
    },
    isValid() {
      return this.acceptedTerms;
    },
    validate() {
      if (this.currentStep.meta.canSkip) {
        return true;
      }

      this.v$.$touch();
      if (this.v$.$error) {
        NotificationService.negative('Please review highlighted fields.');
      }

      return !this.v$.$error;
    },
  },
  computed: {
    ...mapState({
      meta: (state) => state.profileWizard.meta,
      userInfo: (state) => state.userInfo.data,
      currentStep: (state) => {
        const { meta } = state.profileWizard;
        const currentStep = meta.steps[meta.currentStep];
        return currentStep;
      },
      stepComplete: (state) => {
        const { meta } = state.profileWizard;
        const currentStep = meta.steps[meta.currentStep];
        return currentStep.complete;
      },
    }),
    storeModuleName() {
      return 'userInfo';
    },
    acceptedTerms() {
      return this.question1 && this.question2 && this.question3 && this.question4 && this.question5;
    },
    notice() {
      // return 'As a Helper on Juggle St you keep 100% of the money you earn, Juggle Street does not take a commission. However, as a member of this community you need to acknowledge and accept the following responsibilites:';
      return 'As a member of the Juggle St community you need to acknowledge and accept the following responsibilites:';
    },
  },
  watch: {
    question1() {
      this.setTerms();
    },
    question2() {
      this.setTerms();
    },
    question3() {
      this.setTerms();
    },
    question4() {
      this.setTerms();
    },
    question5() {
      this.setTerms();
    },
  },
  setup() {
    return {
      v$: useVuelidate(),
    };
  },
  validations: {
    acceptedTerms: {
      sameAsRawValue: sameAs(true),
    },
  },
  beforeRouteLeave(to, from, next) {
    // Update user info before proceeding to the next step if step is complete
    if (this.stepComplete) {
      this.updateUserInfo().then(() => {
        beforeLeaveStep(this, to, from, next);
      });
    } else {
      beforeLeaveStep(this, to, from, next);
    }
  },
};
</script>
