<template>
  <q-page class="layout-padding">
    <div v-if="pageStatus == 'initial'">
      Please type your account email or phone number and we will send a verification code that can be used to reset your password.
      <q-form class="q-mx-sm" v-on:submit.prevent="handleResetPassword">
        <q-input v-model="accountIdentifier" label="Email or Phone Number" clearable :error="v$.accountIdentifier.$error" />
        <q-btn loader @click="handleResetPassword" color="accent">
          Reset Password
        </q-btn>
      </q-form>
    </div>
    <div v-if="pageStatus == 'complete'">
      An email and SMS have been sent with the verification code.
    </div>
  </q-page>
</template>

<script>
import useVuelidate from '@vuelidate/core';
import { required } from '@vuelidate/validators';
import { requestResetPasswordBySms } from '../api/auth';
import NotificationService from '../utils/notification-service';

export default {
  name: 'PageRequestResetPassword',
  data() {
    return {
      accountIdentifier: '',
      pageStatus: 'initial',
    };
  },
  setup() {
    return {
      v$: useVuelidate(),
    };
  },
  validations: {
    accountIdentifier: {
      required,
    },
  },
  methods: {
    handlePrevious() {
      this.routerReplace('/login');
    },
    handleResetPassword() {
      this.setFieldsDirty();

      if (this.v$.accountIdentifier.$error) {
        return;
      }

      const payload = {
        accountIdentifier: this.accountIdentifier,
      };

      this.loader.show();
      requestResetPasswordBySms(payload)
        .then(() => {
          this.loader.hide();
          this.routerReplace({ path: '/reset-password' });
        })
        .catch((error) => {
          this.loader.hide();
          if (error.message) {
            NotificationService.negative(error.message);
          } else {
            NotificationService.negative('An error occurred. Please try again.');
          }
        });
    },
    setFieldsDirty() {
      this.v$.accountIdentifier.$touch();
    },
  },
};
</script>
