<template>
  <div>
    <q-list link no-border v-if="hasExistingCard && displayOptions">
      <q-item-label header style="padding-bottom: 0">Select Payment Method</q-item-label>
      <q-item tag="label" v-ripple>
        <q-item-section side top>
          <q-radio v-model="paymentMethod" :val="2" />
        </q-item-section>

        <q-item-section>
          <q-item-label>XXXX XXXX XXXX {{this.existingCardText}}</q-item-label>
        </q-item-section>
      </q-item>
      <q-item tag="label" v-ripple>
        <q-item-section side top>
          <q-radio v-model="paymentMethod" :val="1" />
        </q-item-section>

        <q-item-section>
          <q-item-label>Add New Card</q-item-label>
        </q-item-section>
      </q-item>
    </q-list>
    <div class="q-mb-md q-px-md q-my-md" flat v-show="paymentMethod === 1">
      <div>
        <!-- <span v-if="hasExistingCard">Enter Card Details</span>
        <span v-else>Enter Credit Card Details</span> -->
        <small class="text-grey">
          Visa & Mastercard only
        </small>
        <form>
          <div>
            <q-input
              borderless
              v-model="fullName"
              :error="v$.fullName.$error"
              error-message="required"
              label="Name on card"
              hide-bottom-space
              tabindex="0"
            />
            <q-field
              label="Card details"
              borderless
              stack-label
              :error="v$.fullName.$error"
              error-message="required"
              hide-bottom-space
            >
              <template v-slot:control>
                <div class="full-width q-mt-xs">
                  <div id="card-element"></div>
                  <div id="card-errors" role="alert"></div>
                </div>
              </template>
            </q-field>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import useVuelidate from '@vuelidate/core';
import { required } from '@vuelidate/validators';
import PaymentManager from '../utils/payment-manager';
import c from '../utils/constants';

export default {
  name: 'CardDetails2',
  emits: ['paymentMethodChanged'],
  data() {
    return {
      paymentMethod: this.defaultPaymentMethod,
      fullName: `${this.$store.state.userInfo.data.firstName} ${this.$store.state.userInfo.data.lastName}`,
      cardNumber: undefined,
      carExpiry: undefined,
      cardCvc: undefined,
      hasCompletedCardEntry: false,
    };
  },
  setup() {
    return {
      v$: useVuelidate(),
    };
  },
  validations: {
    fullName: {
      required,
    },
  },
  props: {
    defaultPaymentMethod: {
      default: c.paymentMethod.existingCard,
    },
    displayOptions: {
      default: true,
    },
  },
  computed: {
    ...mapGetters(
      'payments',
      ['hasExistingCard', 'existingCardText'],
    ),
  },
  methods: {
    validate() {
      this.v$.$touch();
      const hasError = this.v$.$error;
      if (hasError || !this.hasCompletedCardEntry) {
        return false;
      }

      return true;
    },
    setPaymentMethod(value) {
      this.paymentMethod = value;
    },
    async createToken() {
      if (!this.validate()) {
        return null;
      }

      const { token, error } = await PaymentManager.createToken(this.card, { name: this.fullName });
      if (!error) {
        return token.id;
      }

      const errorElement = document.getElementById('card-errors');
      errorElement.textContent = error.message;
      return null;
    },
  },
  watch: {
    paymentMethod(value) {
      this.$emit('paymentMethodChanged', value);
    },
  },
  async mounted() {
    const vm = this;
    const stripe = await PaymentManager.initialize();
    const style = {
      base: {
        fontSize: '16px',
      },
    };

    this.card = stripe.elements().create('card', { style, hidePostalCode: true, hideIcon: true });
    this.card.mount('#card-element');

    // Stripe elements injects an iframe per element, breaking down the card details will cause Stripe in inject multiple iframes.
    // this.card = elements.create('cardNumber', {style, placeholder: 'Card number'});
    // this.card.mount('#card-number');

    // this.cardExpiry = elements.create('cardExpiry', {style, placeholder: 'MM/YY'});
    // this.cardExpiry.mount('#card-expiry');

    // this.cardCvc = elements.create('cardCvc', {style, placeholder: 'CVC'});
    // this.cardCvc.mount('#card-cvc');

    const validateCardDetails = (event) => {
      const displayError = document.getElementById('card-errors');

      if (event.error) {
        displayError.textContent = event.error.message;
      } else {
        displayError.textContent = '';
      }

      vm.hasCompletedCardEntry = event.complete;
    };

    this.card.addEventListener('change', validateCardDetails);
  },
};
</script>
<style lang="sass">
#card-errors
  color: red
  height: 1rem
  padding: 0.5rem
  text-align: center
#card-element
  margin-top: 0
</style>
