<template>
  <q-page class="">
    <profile-visibility :displayShareLinks="true" ref="profileVisibility" v-on:change="handleChange"/>
  </q-page>
</template>

<script>
import ProfileVisibility from 'components/ProfileVisibility';

export default {
  name: 'PageAccountProfileVisibility',
  components: { ProfileVisibility },
  data() {
    return {
      hasChanges: false,
    };
  },
  methods: {
    handleChange() {
      this.hasChanges = true;
    },
  },
  beforeRouteLeave(to, from, next) {
    if (this.hasChanges) {
      this.$refs.profileVisibility.updateProfile(true).then(() => {
        next();
      });
    } else {
      next();
    }
  },
};
</script>
