<template>
  <div
    v-if="!isOwnProfile && isApproved"
    :class="defaultClass"
  >
    <q-btn v-if="canConnect" color="transparent" @click="handleConnect" small flat class="no-padding">
      <q-icon name="favorite_border" color="black" size="22px" />
      <q-icon name="clear" color="black" size="22px" />
    </q-btn>
    <q-btn v-else-if="existingConnection" color="transparent" @click="handleFavorite" small flat class="no-padding">
      <q-icon name="favorite_border" class="text-brand-pink" size="22px"></q-icon>
    </q-btn>
    <q-btn v-else-if="canAccept" color="transparent" @click="handleConnect" small flat class="no-padding">
      <q-icon name="favorite_border" color="black" size="22px" />
      <q-icon name="clear" color="black" size="22px" />
    </q-btn>
    <connection-management-modal ref='connectionModal' :user="user" />
  </div>
</template>

<script>
import ConnectionManagementModal from 'components/ConnectionManagementModal';
import { mapState, mapActions } from 'vuex';
import c from '../utils/constants';
import NotificationService from '../utils/notification-service';

export default {
  name: 'FavouriteActions',
  components: { ConnectionManagementModal },
  data() {
    // New Connection - Hide.
    // Pending Connection (No action)
    // Pending Connection (Action Required)
    // Approved Connection - Reject.
    return {
    };
  },
  props: {
    user: {
      default: undefined,
    },
    defaultClass: {
      default: undefined,
    },
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.userInfo.data,
    }),
    canHide() {
      if (this.isFamily) return true;

      return (this.user.friendStatus === c.friendStatus.none || this.user.friendStatus === c.friendStatus.pending);
    },
    canConnect() {
      return this.user.friendStatus === c.friendStatus.none;
    },
    canAccept() {
      return this.user.friendStatus === c.friendStatus.pending && this.user.actionRequired;
    },
    isWaiting() {
      return this.user.friendStatus === c.friendStatus.pending && !this.user.actionRequired;
    },
    canReject() {
      return this.user.friendStatus === c.friendStatus.approved && this.allowUnfriendAction;
    },
    existingConnection() {
      return this.user.friendStatus === c.friendStatus.approved
        || (this.user.friendStatus === c.friendStatus.pending && !this.user.actionRequired);
    },
    isOwnProfile() {
      return this.userInfo.id === this.user.id;
    },
  },
  methods: {
    ...mapActions(['sendFriendRequest']),
    hide() {
      const payload = {
        friendId: this.user.id,
      };

      if (this.user.requestId && this.user.requestId > 0) {
        payload.id = this.user.requestId;
      } else if (this.user.friendshipId && this.user.friendshipId > 0) {
        payload.id = this.user.friendshipId;
      }

      payload.status = payload.id ? c.friendStatus.rejected : c.friendStatus.hidden;

      this.sendFriendRequest(payload).then(() => {
        if (this.isIconMode()) return;

        this.routerBack();
      }).catch((error) => {
        NotificationService.info(error.message);
      });
    },
    handleFavorite() {
      // Do nothing.
    },
    handleConnect() {
      this.$refs.connectionModal.showDialog(this.handleApply);
    },
  },
};
</script>
